import React, { useEffect, useRef, useState } from "react";
import { format } from "date-fns";
import { Box, Grid, ListItem, Typography } from "@material-ui/core";
import { AgreementListItem, Href } from "../../../utils/models";
import { useHistory } from "react-router";
import { agreementListToolTipStatusMap, paths } from "../../../utils/common";
import { StatusIndicator } from "../../atoms/StatusIndicator";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ListItemRippleDisabler } from "../../atoms/ListItemRippleDisabler";
import { ActionMenu } from "../../organisms/ActionMenu";
import clsx from "clsx";
import { AppDispatchEffect } from "../../../App.reducer";
import { postAgreementDuplicateEffect } from "../../pages/AgreementRegistration/effects";
import { isDevelopment, isElementOverflowed } from "../../../utils/helpers";
import { useAgreementsListStyles } from "../ListHeaderAgreements";
import { TooltipMap } from "../../atoms/TooltipMap";
import { Icon } from "../../atoms/Icon";

interface AgreementListItemProps {
  agreement: AgreementListItem;
  handleOnDelete: (guid: string, link?: Href) => void;
  handleOnLeave?: (guid: string, link?: Href) => void;
  hideColumn?: { status?: boolean };
  showDeleteOption?: boolean;
  className?: string;
  canDuplicateAgreement?: boolean;
  dispatchEffect?: AppDispatchEffect;
  windowWidth: number;
}

export const AgreementsListItem: React.FC<AgreementListItemProps> = ({
  agreement: {
    title,
    submitterAgreementNumbers,
    assignor,
    assignee,
    status,
    guid,
    created,
    updated,
    _links,
  },
  hideColumn,
  className,
  handleOnDelete,
  handleOnLeave,
  canDuplicateAgreement,
  dispatchEffect,
  windowWidth,
  showDeleteOption,
}) => {
  const classes = useAgreementsListStyles();
  const { push } = useHistory();
  const [disableRipple, setDisableRipple] = useState(false);
  const titleRef = useRef<HTMLSpanElement>();
  const [showTitleTooltip, setShowTitleTooltip] = useState(false);
  const stopPropagation = (e: React.MouseEvent<HTMLSpanElement>) =>
    e.stopPropagation();

  const handleDuplicate = (agreementGuid: string) => async () => {
    // const guid =
    if (dispatchEffect) {
      await dispatchEffect(postAgreementDuplicateEffect(agreementGuid));
    }
    // push(`/works/register/${guid}`) TODO:: redirect when BE is implemented
  };

  useEffect(() => {
    const isTitleOverflowed = isElementOverflowed(titleRef);
    setShowTitleTooltip(isTitleOverflowed);
  }, [windowWidth, titleRef]);

  return (
    <ListItem
      disableRipple={disableRipple}
      disableGutters
      button
      className={clsx(classes.listRow, className)}
      onClick={() => push(`${paths.AGREEMENTS_REGISTER}/${guid}`)}
    >
      <ContainerListItem className={classes.containerListItem}>
        {!hideColumn?.status && (
          <Grid item className={classes.statusColumn}>
            <StatusIndicator
              status={status}
              className={classes.status}
              mapStatusToTooltip={agreementListToolTipStatusMap}
            />
          </Grid>
        )}
        <Grid item className={classes.agreementColumn}>
          <Box display="flex" flexDirection="column">
            <Typography variant="subtitle1" innerRef={titleRef}>
              {showTitleTooltip ? (
                <TooltipMap variant="listItems" tooltipText={title}>
                  {title}
                </TooltipMap>
              ) : (
                title
              )}
            </Typography>
            {submitterAgreementNumbers?.map((el, index) => (
              <Typography
                key={`${index}${el}`}
                variant="body2"
                color="textSecondary"
              >
                {el}
              </Typography>
            ))}
          </Box>
        </Grid>
        <Grid item className={classes.assignorColumn}>
          {assignor && (
            <Box display="flex" flexDirection="column">
              <Typography variant="body1">{assignor.displayName}</Typography>
              <Typography variant="body2" color="textSecondary">
                IPI {assignor.ipiNameNumber}
              </Typography>
            </Box>
          )}
        </Grid>
        <Grid item className={classes.assigneeColumn}>
          {assignee && (
            <Box display="flex" flexDirection="column">
              <Typography variant="body1">{assignee.displayName}</Typography>
              <Typography variant="body2" color="textSecondary">
                IPI {assignee.ipiNameNumber}
              </Typography>
            </Box>
          )}
        </Grid>
        <Grid item className={classes.updatedColumn}>
          <Box display="flex" flexDirection="column">
            <Typography variant="subtitle1" color="textSecondary">
              {format(updated.timestamp * 1000, "LLLL dd, yyyy")}
            </Typography>
          </Box>
        </Grid>
        <Grid item className={classes.createdColumn}>
          <Box display="flex" flexDirection="column">
            <Typography variant="subtitle1" color="textSecondary">
              {format(created.timestamp * 1000, "LLLL dd, yyyy")}
            </Typography>
          </Box>
        </Grid>
        <Grid item className={classes.optionsColumn}>
          <ListItemRippleDisabler
            setDisableRipple={setDisableRipple}
            onClick={stopPropagation}
          >
            <ActionMenu
              items={[
                ...(isDevelopment && dispatchEffect && canDuplicateAgreement
                  ? [
                      {
                        label: "Duplicate",
                        onClick: handleDuplicate(guid),
                      },
                    ]
                  : []),
                ...(showDeleteOption
                  ? [
                      {
                        label: "Delete",
                        to: "",
                        onClick: () => handleOnDelete(guid),
                      },
                    ]
                  : []),
                ...(_links?.leaveAgreement && handleOnLeave
                  ? [
                      {
                        label: "Leave",
                        to: "",
                        onClick: () =>
                          handleOnLeave(guid, _links?.leaveAgreement),
                      },
                    ]
                  : []),
              ]}
            >
              <Icon name="listItemOptions" data-testid="options" />
            </ActionMenu>
          </ListItemRippleDisabler>
        </Grid>
      </ContainerListItem>
    </ListItem>
  );
};
