export const fetchBootstrapEffect =
  (
    href?: Href
  ): ThunkAction<
    Promise<void | FetchBootstrapResponse>,
    AppState,
    void,
    Action<string>
  > =>
  async (
    dispatch: ThunkDispatch<AppState, void, Action<string>>,
    getState: () => AppState
  ) => {
    dispatch(bootstrapAction());
    try {
      // const response = await axios.get<FetchBootstrapResponse>(
      //   href?.href || apiUrls.bootstrap,
      //   { withCredentials: true }
      // );
      const { account, company, _links, dlr } =
        fetchBootstrapResponseMockSignedIn.response;
      dispatch(
        bootstrapSuccessAction({
          account: account?.item,
          company: company?.item,
          _links,
          dlr,
        })
      );
      return fetchBootstrapResponseMockSignedIn;
    } catch (e) {
      dispatch(bootstrapFailureAction());
      dispatch(addAlertAction(AlertType.ERROR, toasts.error.common.GENERIC));
      throw e;
    }
  };

export const fetchAuthPolicyEffect =
  (): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchAuthPolicyAction());
    const link = getPasswordPolicyLinkSelector(getState());
    const {
      data: { response },
    } = await axios.request({
      url: link?.href,
      method: link?.method || "GET",
      withCredentials: true,
    });

    dispatch(
      fetchAuthPolicySuccessAction({
        passwordPolicy: response.passwordPolicy.item,
      })
    );
  };

export const patchProfileEffect =
  (data: {
    firstName: string;
    lastName: string;
  }): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(patchProfileAction(data));
    const link = getUpdateProfileLinkSelector(getState());
    try {
      const response = await axios.request<PatchAccountResponse>({
        url: link?.href,
        method: link?.method,
        data,
        withCredentials: true,
      });
      dispatch(patchProfileSuccessAction(response.data.response.profile.item));
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.auth.UPDATE_PROFILE)
      );
    } catch (e) {
      const error = handleError(e.response, "profile");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.auth.UPDATE_PROFILE} ${error.message}`
        )
      );
      dispatch(patchProfileFailAction());
    }
  };

export const changePasswordEffect =
  (
    currentPassword: string,
    newPassword: string
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(changePasswordAction());
    const link = getChangePasswordLinkSelector(getState());
    try {
      await axios.request<ChangePasswordResponse>({
        url: link?.href,
        method: link?.method,
        data: JSON.stringify({ currentPassword, newPassword }),
        withCredentials: true,
      });
      dispatch(changePasswordSuccessAction());
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.auth.CHANGE_PASSWORD)
      );
    } catch (e) {
      const error = handleError(e.response);
      dispatch(changePasswordFailAction());
      if (!error.field && error.code !== ApiErrorCodeTypes.INCORRECT_PASSWORD) {
        dispatch(
          addAlertAction(
            AlertType.ERROR,
            `${toasts.error.auth.CHANGE_PASSWORD} ${error.message}`
          )
        );
      } else {
        throw error;
      }
    }
  };

export const changeEmailRequestEffect =
  (
    email: string,
    password: string
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(changeEmailRequestAction());
    const link = getChangeEmailLinkSelector(getState());
    try {
      const response = await axios.request<ChangeEmailResponse>({
        url: link?.href,
        method: link?.method,
        data: JSON.stringify({ email, password }),
        withCredentials: true,
      });
      dispatch(
        changeEmailRequestSuccessAction(
          response.data.response._links.verifyEmail
        )
      );
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.auth.VERIFY_EMAIL)
      );
    } catch (e) {
      dispatch(changeEmailRequestFailAction());
      const error = handleError(e.response);
      if (
        error.code !== ApiErrorCodeTypes.BAD_CREDENTIALS &&
        error.status !== 409
      ) {
        dispatch(
          addAlertAction(
            AlertType.ERROR,
            `${toasts.error.auth.CHANGE_EMAIL} ${error.message}`
          )
        );
      } else {
        throw error;
      }
    }
  };

export const changeEmailConfirmEffect =
  (
    email: string,
    code: string
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(changeEmailConfirmAction());
    const link = getChangeEmailConfirmLinkSelector(getState());
    try {
      await axios.request<ChangeEmailResponse>({
        url: link?.href,
        method: link?.method,
        data: { code },
        withCredentials: true,
      });
      dispatch(changeEmailConfirmSuccessAction(email));
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.auth.CHANGE_EMAIL)
      );
    } catch (e) {
      dispatch(changeEmailConfirmFailAction());
      const error = handleError(e.response);
      const statusReason = e.response?.data.error?.statusReason;
      if (statusReason !== ApiErrorCodeTypes.ENTITY_NOT_FOUND) {
        dispatch(
          addAlertAction(
            AlertType.ERROR,
            `${toasts.error.auth.CHANGE_EMAIL} ${error.message}`
          )
        );
      } else {
        throw statusReason;
      }
    }
  };

export const changeAvatarEffect =
  (image: string): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>) => {
    //TODO:: implement backend when available
    const mockedPromise = new Promise((resolve) => {
      setTimeout(() => {
        resolve();
      }, 1000);
    });
    dispatch(changeAvatarAction());

    await mockedPromise;
    dispatch(changeAvatarSuccessAction(image));
    dispatch(
      addAlertAction(AlertType.SUCCESS, toasts.success.auth.CHANGE_AVATAR)
    );
  };

export const signUpEffect =
  (
    data: {
      email: string;
      firstName: string;
      lastName: string;
      displayName: string;
      password: string;
      inviteToken?: string;
    },
    link?: Href
  ): ThunkAction<Promise<SignUpResponse>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    const url = link || getBootstrapLinksSelector(getState())?.signUp;
    try {
      const response = await axios.request<SignUpResponse>({
        url: url?.href,
        method: url?.method,
        data: JSON.stringify(data),
        withCredentials: true,
      });
      return response.data;
    } catch (e) {
      if (e.response.status !== 409) {
        const error = handleError(e.response);
        dispatch(
          addAlertAction(
            AlertType.ERROR,
            `${toasts.error.auth.FINISH_SIGNUP} ${error.message}`
          )
        );
      }
      throw e;
    }
  };

export const signInEffect =
  (data: {
    email: string;
    password: string;
  }): ThunkAction<Promise<Authentication>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(signInAction());
    const link = getBootstrapLinksSelector(getState())?.signIn;
    try {
      // const response = await axios.request<SignInResponse>({
      //   url: link?.href,
      //   method: link?.method,
      //   data,
      //   withCredentials: true,
      // });
      dispatch(signInSuccessAction());
      return signInResponseMock.response.authentication.item;
    } catch (e) {
      dispatch(signInFailureAction());
      const error = handleError(e.response);
      if (e.response.status !== 400) {
        dispatch(
          addAlertAction(
            AlertType.ERROR,
            `${toasts.error.auth.SIGN_IN} ${error.message}`
          )
        );
      }
      throw e;
    }
  };

