export const resetPasswordEffect =
  (email: string): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(resetPasswordAction());
    const link = getBootstrapLinksSelector(getState())?.forgotPassword;
    try {
      const response = await axios.request<ResetPasswordResponse>({
        url: link?.href,
        method: link?.method,
        data: { email },
        withCredentials: true,
      });
      dispatch(resetPasswordSuccessAction(response.data.response.code.item));
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.success.auth.RESET_PASSWORD_REQUEST}`
        )
      );
    } catch (e) {
      dispatch(resetPasswordFailureAction());
      const error = handleError(e.response);
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.auth.RESET_PASSWORD} ${error.message}`
        )
      );
      throw e;
    }
  };

