import {
  Box,
  createStyles,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import React from "react";
import { Ipi } from "../../../utils/models";
import { IpiVerifiedIcon } from "../IpiVerifiedIcon";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      overflow: "hidden",
    },
    primaryText: {
      lineHeight: 1,
    },
    secondaryText: {
      paddingTop: theme.spacing(0.5),
      display: "inline-flex",
      alignItems: "center",
    },
  })
);

interface CardIpiProps {
  ipi: Ipi;
}

export const CardIpi: React.FC<CardIpiProps> = ({ ipi }) => {
  const classes = useStyles();
  const {
    profile: { displayName },
    ipiNameNumber,
    verified,
  } = ipi;
  return (
    <Box className={classes.root}>
      <Typography
        data-testid="display-name"
        variant="subtitle1"
        className={classes.primaryText}
        noWrap
      >
        {displayName}
      </Typography>
      {ipiNameNumber && (
        <Typography
          variant="body2"
          color="textSecondary"
          noWrap
          className={classes.secondaryText}
          data-testid="ipi-name-number"
        >
          {/*IPI {ipiNameNumber} ({copyrightSociety.acronym})*/}
          {/*TODO:: fix society name below when fixing works flow*/}
          IPI {ipiNameNumber}
          {verified && <IpiVerifiedIcon />}
        </Typography>
      )}
    </Box>
  );
};
