import { AgreementShare, AgreementTerm } from "../../../utils/models";
import { AgreementRightType } from "../../../types/enums";
import { ipiRightsPerforming } from "../../organisms/DialogIpiRights/utils";
import { getInfiniteDate } from "../../organisms/FormikDatePicker";

export const createTermWithShare = () => {
  const infinite = getInfiniteDate();
  const share: AgreementShare = {
    rightType: AgreementRightType.P,
    usageTypes: ipiRightsPerforming.map((el) => el.code),
    writerShareRetained: 100,
    publisherShareRetained: 0,
    assigneeCollectibleShare: 0,
  };
  const date = new Date(new Date().setHours(0, 0, 0, 0));
  const term: AgreementTerm = {
    territories: ["I2136"],
    startDate: date.toISOString(),
    endDate: infinite.toISOString(),
    shares: [share],
  };
  return term;
};
