import {
  DELETE_TEMPLATE_SUCCESS,
  FETCH_TEMPLATE,
  FETCH_TEMPLATE_FAILURE,
  FETCH_TEMPLATE_SUCCESS,
  FETCH_TEMPLATES,
  FETCH_TEMPLATES_FAILURE,
  FETCH_TEMPLATES_SUCCESS,
  FETCH_WORK_TEMPLATE_SCHEMA,
  FETCH_WORK_TEMPLATE_SCHEMA_FAILURE,
  FETCH_WORK_TEMPLATE_SCHEMA_SUCCESS,
  PATCH_TEMPLATE_SUCCESS,
  POST_TEMPLATE_SUCCESS,
  RESET_TEMPLATES_LOADING_STATES,
  TEMPLATE_CLONE,
  TEMPLATE_CLONE_FAILURE,
  TEMPLATE_CLONE_SUCCESS,
  TemplatesActionTypes,
  UPDATE_TEMPLATE,
  UPDATE_TEMPLATE_FAILURE,
  UPDATE_TEMPLATE_SUCCESS,
} from "./actions.types";
import {
  _TemplatesListLinks,
  Template,
  TemplateListItem,
} from "../../../utils/models";
import { LoadingState } from "../../../types/enums";
import { WorkSchemaJSON } from "../../../validationSchema/models";

export interface TemplatesState {
  templateListItems: TemplateListItem[];
  template?: Template;
  templateClone?: Template;
  templateSchema?: WorkSchemaJSON;
  templateCloneLoadingState: LoadingState;
  templateSchemaLoadingState: LoadingState;
  fetchTemplatesLoadingState: LoadingState;
  fetchTemplateLoadingState: LoadingState;
  updateTemplateLoadingState: LoadingState;
  _links?: _TemplatesListLinks;
}

export const templatesInitialState: TemplatesState = {
  templateListItems: [],
  templateCloneLoadingState: LoadingState.PRISTINE,
  templateSchemaLoadingState: LoadingState.PRISTINE,
  fetchTemplatesLoadingState: LoadingState.PRISTINE,
  fetchTemplateLoadingState: LoadingState.PRISTINE,
  updateTemplateLoadingState: LoadingState.PRISTINE,
};

export const templatesReducer = (
  state: TemplatesState = templatesInitialState,
  action: TemplatesActionTypes
): TemplatesState => {
  switch (action.type) {
    case FETCH_TEMPLATES: {
      return {
        ...state,
        fetchTemplatesLoadingState: action.next
          ? LoadingState.FETCHED
          : LoadingState.FETCHING,
      };
    }

    case FETCH_TEMPLATES_SUCCESS: {
      return {
        ...state,
        templateListItems: action.next
          ? [...state.templateListItems, ...action.templates]
          : action.templates,
        fetchTemplatesLoadingState: LoadingState.FETCHED,
        _links: action._links,
      };
    }

    case FETCH_TEMPLATES_FAILURE: {
      return { ...state, fetchTemplatesLoadingState: LoadingState.ERROR };
    }

    case FETCH_TEMPLATE: {
      return {
        ...state,
        template: undefined,
        fetchTemplateLoadingState: LoadingState.FETCHING,
      };
    }

    case FETCH_TEMPLATE_SUCCESS: {
      return {
        ...state,
        template: action.template,
        fetchTemplateLoadingState: LoadingState.FETCHED,
      };
    }

    case FETCH_TEMPLATE_FAILURE: {
      return {
        ...state,
        fetchTemplateLoadingState: LoadingState.ERROR,
      };
    }

    case DELETE_TEMPLATE_SUCCESS: {
      return {
        ...state,
        templateListItems: state.templateListItems.filter(
          ({ guid }) => guid !== action.guid
        ),
      };
    }

    case POST_TEMPLATE_SUCCESS: {
      return {
        ...state,
        template: action.template,
      };
    }

    case PATCH_TEMPLATE_SUCCESS: {
      return {
        ...state,
        template: action.template,
      };
    }

    case UPDATE_TEMPLATE: {
      return { ...state, updateTemplateLoadingState: LoadingState.FETCHING };
    }

    case UPDATE_TEMPLATE_SUCCESS: {
      return { ...state, updateTemplateLoadingState: LoadingState.FETCHED };
    }

    case UPDATE_TEMPLATE_FAILURE: {
      return { ...state, updateTemplateLoadingState: LoadingState.ERROR };
    }

    case RESET_TEMPLATES_LOADING_STATES: {
      return {
        ...state,
        fetchTemplatesLoadingState: LoadingState.PRISTINE,
        fetchTemplateLoadingState: LoadingState.PRISTINE,
        updateTemplateLoadingState: LoadingState.PRISTINE,
      };
    }

    case FETCH_WORK_TEMPLATE_SCHEMA: {
      return {
        ...state,
        templateSchemaLoadingState: LoadingState.FETCHING,
      };
    }

    case FETCH_WORK_TEMPLATE_SCHEMA_SUCCESS: {
      return {
        ...state,
        templateSchemaLoadingState: LoadingState.FETCHED,
        templateSchema: action.schema,
      };
    }

    case FETCH_WORK_TEMPLATE_SCHEMA_FAILURE: {
      return {
        ...state,
        templateSchemaLoadingState: LoadingState.ERROR,
      };
    }

    case TEMPLATE_CLONE: {
      return {
        ...state,
        templateCloneLoadingState: LoadingState.FETCHING,
      };
    }

    case TEMPLATE_CLONE_SUCCESS: {
      return {
        ...state,
        templateCloneLoadingState: LoadingState.FETCHED,
        templateClone: action.template,
      };
    }

    case TEMPLATE_CLONE_FAILURE: {
      return {
        ...state,
        templateCloneLoadingState: LoadingState.ERROR,
      };
    }

    default:
      return state;
  }
};
