import React from "react";
import {
  Box,
  createStyles,
  Grid,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { WorkCreator } from "../../../utils/models";
import { getSplitShareSum } from "../RowGridCreatorTotal/utils";
import { CellPrShareSummary } from "../../atoms/CellPrShareSummary";
import { RowCreatorListItem } from "../RowCreatorListItem";
import { CellMrShareSummary } from "../../atoms/CellMrShareSummary";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    summaryRow: {
      alignItems: "center",
      paddingLeft: theme.spacing(2),
    },
    errorRow: {
      paddingTop: 0,
      paddingBottom: 0,
    },
    roleCellSummary: {
      display: "flex",
      justifyContent: "flex-end",
      alignSelf: "start",
    },
    shareCell: {
      height: 52,
      position: "relative",
      borderTop: `1px solid ${theme.palette.divider}`,
      paddingTop: theme.spacing(3.5),
    },
    totalText: {
      position: "absolute",
      top: theme.spacing(3.5),
      left: theme.spacing(-10),
    },
    prShareText: {
      width: "60px",
      textAlign: "right",
    },
    mrShareCell: {
      textAlign: "right",
    },
  })
);

interface RowGridCreatorTotalProps {
  creators: WorkCreator[];
}

export const RowGridCreatorTotalSummary: React.FC<RowGridCreatorTotalProps> = ({
  creators,
}) => {
  const classes = useStyles();

  const mechanicalSum = getSplitShareSum(creators, "mrShare");
  const performanceSum = getSplitShareSum(creators, "prShare");

  return (
    <React.Fragment>
      <RowCreatorListItem className={classes.summaryRow}>
        <Grid container>
          <Grid item xs={8} />
          <CellPrShareSummary className={clsx(classes.shareCell)}>
            <Box display="inline-flex" alignItems="center">
              <Typography
                variant="subtitle1"
                color="textSecondary"
                className={classes.totalText}
              >
                Total
              </Typography>
              <Typography variant="subtitle1" className={classes.prShareText}>
                {performanceSum}%
              </Typography>
            </Box>
          </CellPrShareSummary>
          <CellMrShareSummary
            className={clsx(classes.shareCell, classes.mrShareCell)}
          >
            <Typography variant="subtitle1">{mechanicalSum}%</Typography>
          </CellMrShareSummary>
          <Grid item xs={1} />
        </Grid>
      </RowCreatorListItem>
    </React.Fragment>
  );
};
