import {
  CompanyActionTypes,
  DELETE_COMPANY_INVITE_SUCCESS,
  FETCH_COMPANY_DETAILS_SUCCESS,
  FETCH_COMPANY_INVITE,
  FETCH_COMPANY_INVITE_FAILURE,
  FETCH_COMPANY_INVITE_SUCCESS,
  FETCH_COUNTRIES,
  FETCH_COUNTRIES_SUCCESS,
  FETCH_EMPLOYEES,
  FETCH_EMPLOYEES_FAILURE,
  FETCH_EMPLOYEES_SUCCESS,
  PATCH_EMPLOYEE_SUCCESS,
  RESEND_COMPANY_INVITE_SUCCESS,
} from "./actions.types";
import { CompanyInviteType, LoadingState } from "../../../types/enums";
import {
  _EmployeesLinks,
  _InvitesLinks,
  CompanyDetails,
  CompanyInvite,
  Country,
  Employee,
} from "../../../utils/models";

export interface CompanyState {
  countries: Country[];
  employees: Employee[];
  invites: CompanyInvite[];
  countriesLoadingState: LoadingState;
  employeesLoadingState: LoadingState;
  invitesLoadingState: LoadingState;
  _linksEmployees?: _EmployeesLinks;
  _linksInvites?: _InvitesLinks;
  companyDetails?: CompanyDetails;
}

export const companyInitialState: CompanyState = {
  countries: [],
  employees: [],
  invites: [],
  countriesLoadingState: LoadingState.PRISTINE,
  employeesLoadingState: LoadingState.PRISTINE,
  invitesLoadingState: LoadingState.PRISTINE,
};

export const companyReducer = (
  state: CompanyState = companyInitialState,
  action: CompanyActionTypes
): CompanyState => {
  switch (action.type) {
    case FETCH_COUNTRIES: {
      return {
        ...state,
        countriesLoadingState: LoadingState.FETCHING,
      };
    }

    case FETCH_COUNTRIES_SUCCESS: {
      return {
        ...state,
        countriesLoadingState: LoadingState.FETCHED,
        countries: action.countries,
      };
    }
    case FETCH_EMPLOYEES: {
      return {
        ...state,
        employeesLoadingState: LoadingState.FETCHING,
      };
    }

    case FETCH_EMPLOYEES_SUCCESS: {
      return {
        ...state,
        employeesLoadingState: LoadingState.FETCHED,
        employees: action.employees.map((el) => ({
          ...el,
          groups: el.groups || [],
        })),
        _linksEmployees: action._links,
      };
    }

    case FETCH_EMPLOYEES_FAILURE: {
      return {
        ...state,
        employeesLoadingState: LoadingState.ERROR,
      };
    }

    case FETCH_COMPANY_INVITE: {
      return {
        ...state,
        invitesLoadingState: LoadingState.FETCHING,
      };
    }

    case FETCH_COMPANY_INVITE_SUCCESS: {
      return {
        ...state,
        invites: action.invites,
        invitesLoadingState: LoadingState.FETCHED,
        _linksInvites: action._links,
      };
    }

    case FETCH_COMPANY_INVITE_FAILURE: {
      return {
        ...state,
        invitesLoadingState: LoadingState.ERROR,
      };
    }

    case DELETE_COMPANY_INVITE_SUCCESS: {
      const filtered = state.invites.filter(
        (el) => el.inviteToken !== action.inviteToken
      );
      return {
        ...state,
        invites: [...filtered],
      };
    }

    case PATCH_EMPLOYEE_SUCCESS: {
      return {
        ...state,
        employees: state.employees.map((el) =>
          el.userGuid === action.employee.userGuid
            ? { ...el, ...action.employee }
            : el
        ),
      };
    }

    case RESEND_COMPANY_INVITE_SUCCESS: {
      return {
        ...state,
        invites: state.invites.map((el) =>
          el.inviteToken === action.inviteToken
            ? { ...el, status: CompanyInviteType.PENDING }
            : el
        ),
      };
    }

    case FETCH_COMPANY_DETAILS_SUCCESS: {
      return {
        ...state,
        companyDetails: action.companyDetails,
      };
    }

    default:
      return state;
  }
};
