import React, { ChangeEvent } from "react";
import {
  createStyles,
  FormControlLabel,
  makeStyles,
  Theme,
} from "@material-ui/core";
import { Field, FieldProps } from "formik";
import { Switch } from "formik-material-ui";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      margin: 0,
    },
    switch: {
      marginLeft: theme.spacing(3.5),
    },
  })
);

interface FormikSwitchProps {
  label: string;
  disabled?: boolean;
  className?: string;
  switchClassname?: string;
  labelPlacement?: "start" | "end";
  onChange?: (value: Boolean) => void;
}

export const FormikSwitch: React.FC<FormikSwitchProps & FieldProps> = ({
  field: { name, value, onChange },
  label,
  disabled,
  className,
  switchClassname,
  labelPlacement = "start",
  onChange: onChangeProp,
}) => {
  const classes = useStyles();

  //TODO:: https://github.com/stackworx/formik-material-ui/pull/171 start using this after it's merged
  return (
    <FormControlLabel
      className={clsx(classes.root, className)}
      disabled={disabled}
      value={value}
      checked={value}
      control={
        <Field
          className={clsx(classes.switch, switchClassname)}
          component={Switch}
          name={name}
          type="checkbox"
          color="primary"
        />
      }
      label={label}
      labelPlacement={labelPlacement}
      onChange={(e: ChangeEvent<{}>, checked) => {
        onChange(e);
        onChangeProp && onChangeProp(checked);
      }}
    />
  );
};
