import React, { useEffect, useState } from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { RouteComponentProps } from "react-router";
import { getAccountSelector, getCompanySelector } from "../SignIn/selectors";
import { fetchAgreementTemplateEffect } from "../AgreementTemplates/effects";
import { getAgreementTemplateSelector } from "../AgreementTemplates/selectors";
import { FormAgreementTemplate } from "../../organisms/FormAgreementTemplate";
import { PageAgreementLoading } from "../../molecules/PageAgreementLoading";
import { getAgreementsLinksSelector } from "../AgreementRegistration/selectors";
import { fetchAgreementsEffect } from "../AgreementRegistration/effects";

export const AgreementTemplate: React.FC<
  RouteComponentProps<{ guid: string }>
> = ({
  match: {
    params: { guid },
  },
}) => {
  const dispatch = useAppDispatch();
  const dispatchEffect = useAppDispatchEffect();
  const state = useAppSelector((state) => state);
  const account = getAccountSelector(state);
  const company = getCompanySelector(state);
  const agreementTemplate = getAgreementTemplateSelector(state);
  const [isFormDataLoading, setIsFormDataLoading] = useState(Boolean(guid));
  const agreementsListLinks = getAgreementsLinksSelector(state);

  useEffect(() => {
    const fetchAgreementTemplate = async (guid: string) => {
      await dispatchEffect(fetchAgreementTemplateEffect(guid));
      setIsFormDataLoading(false);
    };
    if (guid && agreementTemplate?.guid !== guid) {
      fetchAgreementTemplate(guid);
    } else {
      setIsFormDataLoading(false);
    }
  }, [guid, dispatchEffect, agreementTemplate]);

  //TODO:: This should be changed to agreement template fetch when its implemented on BE
  useEffect(() => {
    if (!agreementsListLinks) {
      dispatchEffect(fetchAgreementsEffect());
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [agreementsListLinks]);

  const breadCrumb = guid ? "Edit Template" : "New Template";
  return isFormDataLoading || !agreementsListLinks ? (
    <PageAgreementLoading dispatch={dispatch} breadCrumb={breadCrumb} />
  ) : account && company ? (
    <FormAgreementTemplate
      agreementsListLinks={agreementsListLinks} //TODO:: This should be changed to agreement template links when its implemented on BE
      dispatch={dispatch}
      dispatchEffect={dispatchEffect}
      breadCrumb={breadCrumb}
      agreement={guid ? agreementTemplate : undefined}
      account={account}
      company={company}
    />
  ) : null;
};
