import React, { useEffect, useState } from "react";
import { AppDispatchEffect } from "../../../App.reducer";
import { Dialog } from "../../molecules/Dialog";
import {
  Box,
  Button,
  createStyles,
  DialogActions,
  DialogContent,
  DialogTitle,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import {
  WorkRegistration,
  WorkRegistrationValidationError,
} from "../../../utils/models";
import { fetchWorkRegistrationValidationErrorsEffect } from "../../pages/Works/effects";
import { Skeleton } from "@material-ui/lab";
import { WorkValidationErrorPanel } from "../../molecules/WorkValidationErrorPanel";
import { format } from "date-fns";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dialog: {
      width: "100%",
      maxWidth: 880,
      height: 560,
    },
    skeletonTextLoader: {
      height: theme.spacing(2),
      marginBottom: theme.spacing(1),
      width: 150,
    },
    skeletonLoader: {
      marginBottom: theme.spacing(1),
      height: theme.spacing(8.5),
      width: "100%",
    },
    date: {
      paddingBottom: theme.spacing(1)
    }
  })
);

interface DialogCwrValidationErrorsProps {
  dispatchEffect: AppDispatchEffect;
  showDialog: boolean;
  hideDialog: () => void;
  workRegistration?: WorkRegistration;
}

export const DialogCwrValidationErrors: React.FC<DialogCwrValidationErrorsProps> = ({
  dispatchEffect,
  showDialog,
  hideDialog,
  workRegistration,
}) => {
  const classes = useStyles();
  const [loading, setLoading] = useState(false);
  const [validationErrors, setValidationErrors] = useState<
    WorkRegistrationValidationError[]
  >([]);

  const fullDate = workRegistration
    ? format(new Date(workRegistration.updated.iso), "MMMM d, yyyy")
    : "";

  const time = workRegistration
    ? format(new Date(workRegistration.updated.iso), "p")
    : "";

  const fetchValidationErrors = async (link: string) => {
    setLoading(true);
    try {
      const response: WorkRegistrationValidationError[] = await dispatchEffect(
        fetchWorkRegistrationValidationErrorsEffect(link)
      );
      setValidationErrors(response);
    } catch (e) {}
    setLoading(false);
  };

  const handleClose = () => {
    hideDialog();
    setValidationErrors([]);
  };

  useEffect(() => {
    const link = workRegistration?._links.validationErrors?.href;
    if (showDialog && link) {
      fetchValidationErrors(link);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [showDialog, workRegistration]);

  return (
    <Dialog
      variant="complex"
      open={showDialog}
      onClose={handleClose}
      PaperProps={{ className: classes.dialog }}
      scroll="paper"
    >
      <DialogTitle>Registration Errors</DialogTitle>
      <DialogContent>
        {loading ? (
          <Box pt={1}>
            <Skeleton className={classes.skeletonTextLoader} variant="rect" />
            <Skeleton className={classes.skeletonLoader} variant="rect" />
            <Skeleton className={classes.skeletonLoader} variant="rect" />
          </Box>
        ) : (
          <React.Fragment>
            {fullDate && (
              <Typography variant="body2" color="textSecondary" className={classes.date}>
                {fullDate}
              </Typography>
            )}
            {validationErrors.map((el) => (
              <WorkValidationErrorPanel key={el.code} error={el} time={time} />
            ))}
          </React.Fragment>
        )}
      </DialogContent>
      <DialogActions>
        <Button variant="contained" color="primary" onClick={hideDialog}>
          Close
        </Button>
      </DialogActions>
    </Dialog>
  );
};
