import React, { useState } from "react";
import { Dialog } from "../../molecules/Dialog";
import {
  Box,
  Button,
  createStyles,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  makeStyles,
  Theme,
} from "@material-ui/core";
import { LoadingState } from "../../../types/enums";
import {
  _IpiLinks,
  IpiSearchCreatorOption,
  Society,
  SocietyAffiliation,
} from "../../../utils/models";
import { AppDispatchEffect } from "../../../App.reducer";
import { notListedSocietyOptionMock } from "../AddIpiNumber";
import { Field, Form, Formik } from "formik";
import { FormikSwitch } from "../FormikSwitch";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import * as yup from "yup";
import { postAddIpiV2Effect, postIpiManually } from "../../pages/Ipi/effects";
import { FormikIpiNumberAutocomplete } from "../../atoms/FormikIpiNumberAutocomplete";
import { FormikSocietyAffiliationAutocomplete } from "../../atoms/FormikSocietyAffiliationAutocomplete";
import { FormikSocietyAutocomplete } from "../../atoms/FormikSocietyAutocomplete";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dialog: {
      width: 456,
      minHeight: 256,
    },
    noWrap: {
      whiteSpace: "nowrap",
    },
    makeDefaultSwitch: {
      marginBottom: theme.spacing(-0.5),
    },
    makeDefaultSwitchIcon: {
      margin: 0,
    },
    cancelButton: {
      marginLeft: "auto",
      marginRight: theme.spacing(2),
    },
    form: {
      display: "flex",
      flexDirection: "column",
      flexGrow: 1,
    },
  })
);

export interface SocietyAffiliationSelect {
  title: string;
  value: SocietyAffiliation;
  isSuggested: boolean;
}

interface FormValues {
  ipiNameNumber: IpiSearchCreatorOption | null;
  societyAffiliation: SocietyAffiliationSelect | null;
  makeDefault: boolean;
}

interface DialogAddIPIProps {
  open: boolean;
  onClose: () => void;
  societies: Society[];
  integratedSocieties: Society[];
  societiesLoadingState: LoadingState;
  integratedSocietiesLoadingState: LoadingState;
  dispatchEffect: AppDispatchEffect;
  ipiListLength: number;
  addManuallyEnabled: boolean;
  ipiListLinks: _IpiLinks;
}

const initialValues: FormValues = {
  ipiNameNumber: null,
  makeDefault: false,
  societyAffiliation: null,
};

export const DialogAddIPI: React.FC<DialogAddIPIProps> = ({
  open,
  onClose,
  societies,
  integratedSocieties,
  societiesLoadingState,
  integratedSocietiesLoadingState,
  dispatchEffect,
  ipiListLength,
  addManuallyEnabled,
  ipiListLinks: { ipiSearchCreators },
}) => {
  const classes = useStyles();
  const [selectedSociety, setSelectedSociety] = useState<Society | null>(null);

  const handleOnClose = () => {
    onClose();
    setSelectedSociety(null);
  };

  return (
    <Dialog
      variant="complex"
      open={open}
      classes={{ paper: classes.dialog }}
      onClose={handleOnClose}
    >
      <DialogTitle>Add IPI Number</DialogTitle>
      <Formik<FormValues>
        initialValues={initialValues}
        validationSchema={
          selectedSociety?.id !== notListedSocietyOptionMock.id
            ? undefined
            : yup.object().shape({
                ipiNameNumber: yup.object().typeError("IPI Number is required"),
                societyAffiliation: yup
                  .object()
                  .typeError("Society affiliation is required"),
              })
        }
        onSubmit={async (values) => {
          try {
            if (selectedSociety?.id === notListedSocietyOptionMock.id) {
              await dispatchEffect(
                postIpiManually({
                  ipiNameNumber: Number(values.ipiNameNumber?.ipiNameNumber),
                  makeDefault: values.makeDefault,
                  prSocietyAffiliation: Number(
                    values.societyAffiliation?.value?.societyId
                  ),
                })
              );
            } else {
              await dispatchEffect(
                postAddIpiV2Effect(selectedSociety?.id || "")
              );
            }
            handleOnClose();
          } catch (e) {}
        }}
      >
        {({ isSubmitting, values, resetForm }) => (
          <Form className={classes.form}>
            <DialogContent>
              <Grid container>
                <Grid item xs={12}>
                  <FormikSocietyAutocomplete
                    loadingState={integratedSocietiesLoadingState}
                    integratedSocieties={integratedSocieties}
                    addManuallyEnabled={addManuallyEnabled}
                    selectedSociety={selectedSociety}
                    setSelectedSociety={setSelectedSociety}
                    disabled={isSubmitting}
                  />
                </Grid>
                {selectedSociety?.id === notListedSocietyOptionMock.id && (
                  <React.Fragment>
                    <Grid item xs={12} component={Box} pt={3}>
                      <FormikIpiNumberAutocomplete
                        apiSource={ipiSearchCreators.href}
                        disabled={isSubmitting}
                      />
                    </Grid>
                    <Grid item xs={12} component={Box} pt={3}>
                      <FormikSocietyAffiliationAutocomplete
                        disabled={isSubmitting}
                        societies={societies}
                        loadingState={societiesLoadingState}
                        selectedIpiSearchOption={values.ipiNameNumber}
                      />
                    </Grid>
                    {ipiListLength > 0 && (
                      <Grid item xs={12} component={Box} pt={1}>
                        <Field
                          className={classes.makeDefaultSwitch}
                          switchClassname={classes.makeDefaultSwitchIcon}
                          component={FormikSwitch}
                          name="makeDefault"
                          label="Use this IPI by default"
                          labelPlacement="end"
                        />
                      </Grid>
                    )}
                  </React.Fragment>
                )}
              </Grid>
            </DialogContent>
            <DialogActions>
              <Button variant="text" onClick={handleOnClose}>
                Cancel
              </Button>
              <ButtonSubmit
                disabled={!selectedSociety}
                submitting={isSubmitting}
              >
                Add
              </ButtonSubmit>
            </DialogActions>
          </Form>
        )}
      </Formik>
    </Dialog>
  );
};
