import React, { MouseEvent } from "react";
import { Ipi, WorkCreator } from "../../../utils/models";
import {
  Box,
  ButtonBase,
  createStyles,
  makeStyles,
  Menu,
  MenuItem,
  Typography,
} from "@material-ui/core";
import { FieldProps, getIn } from "formik";
import { AvatarTitle } from "../../atoms/AvatarTitle";
import { IpiVerifiedIcon } from "../../atoms/IpiVerifiedIcon";
import { CardIpi } from "../../atoms/CardIpi";
import { Link } from "react-router-dom";
import { paths } from "../../../utils/common";
import { CaretDown16 } from "@carbon/icons-react";
import { Theme } from "@material-ui/core/styles";
import { WorkRegistrationErrorType } from "../../../types/enums";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    menu: {
      maxHeight: 350,
    },
    missingIpiText: {
      ...theme.typography.body2,
      textDecoration: "unset",
      display: "flex",
      alignItems: "center",
    },
    caretDownIcon: {
      color: theme.palette.primary.main,
    },
  })
);

interface SelectIpiDropdownProps extends FieldProps {
  ipiList: Ipi[];
  creatorIpi: string;
  disabled?: boolean;
}

export const FormikSelectIpiDropdown: React.FC<SelectIpiDropdownProps> = ({
  form: { values, setFieldValue, status, isSubmitting, errors },
  field: { name },
  ipiList,
  disabled,
}) => {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const handleOpen = (event: MouseEvent<HTMLElement>) => {
    if (!disabled && !isSubmitting) {
      setAnchorEl(event.currentTarget);
    }
  };
  const handleClose = () => setAnchorEl(null);

  const getSociety = (ipiNameNumber: number) =>
    ipiList.find((ipi) => Number(ipi.ipiNameNumber) === Number(ipiNameNumber));

  const handleIpiSelect = ({
    ipiNameNumber,
    profile,
    prSocietyAffiliation,
    mrSocietyAffiliation,
    ipiReference,
    verified,
  }: Ipi) => {
    handleClose();
    const creator: WorkCreator = getIn(values, name);
    const updatedCreator: WorkCreator = {
      ...creator,
      ipi: {
        prSocietyAffiliation: prSocietyAffiliation?.societyId,
        mrSocietyAffiliation: mrSocietyAffiliation?.societyId,
        ipiNameNumber,
        ipiReference,
        verified,
      },
      profile: {
        displayName: profile.displayName,
        initials: profile.initials,
      },
    };
    setFieldValue(name, updatedCreator);
  };

  const selectedIPI: Ipi = getIn(values, `${name}.ipi`) || {};
  const ipiError = status === WorkRegistrationErrorType.SAVE_ERROR_IPI;
  return ipiList.length > 0 ? (
    <React.Fragment>
      <Box
        minHeight={24}
        display="inline-flex"
        alignItems="center"
        onClick={handleOpen}
        data-testid={name}
      >
        <ButtonBase disabled={disabled || isSubmitting}>
          <Typography variant="body2" color="textSecondary" noWrap>
            IPI {selectedIPI.ipiNameNumber || ""}
          </Typography>
          {getSociety(selectedIPI.ipiNameNumber)?.verified && (
            <IpiVerifiedIcon />
          )}
          {!disabled && !isSubmitting && (
            <CaretDown16 className={classes.caretDownIcon} />
          )}
        </ButtonBase>
      </Box>

      <Menu
        open={Boolean(anchorEl)}
        anchorEl={anchorEl}
        getContentAnchorEl={null}
        onClose={handleClose}
        className={classes.menu}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        {ipiList.map((ipi: Ipi) => (
          <MenuItem
            data-testid={`ipi-list-item-${ipi.ipiNameNumber}`}
            key={ipi.ipiNameNumber}
            onClick={() => handleIpiSelect(ipi)}
            selected={
              Number(ipi.ipiNameNumber) === Number(selectedIPI.ipiNameNumber)
            }
          >
            <Box display="inline-flex" width={1}>
              <AvatarTitle
                avatar={ipi.profile.initials || ""}
                alignItems="flex-start"
              >
                <CardIpi ipi={ipi} />
              </AvatarTitle>
            </Box>
          </MenuItem>
        ))}
      </Menu>

      {ipiError && (
        <Typography color="error" variant="caption">
          IPI required
        </Typography>
      )}
    </React.Fragment>
  ) : (
    <Typography
      variant="body2"
      color={ipiError ? "error" : "textSecondary"}
      className={classes.missingIpiText}
      component={ipiError ? Link : Typography}
      to={ipiError ? paths.PROFILE_IPI : undefined}
    >
      IPI missing
    </Typography>
  );
};
