import React from "react";
import {
  Box,
  Container,
  createStyles,
  Grid,
  IconButton,
  ListItem,
  ListItemSecondaryAction,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { ContainerContent } from "../../atoms/ContainerContent";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { GridListHeaderItem } from "../../atoms/GridListHeaderItem";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { OverflowMenuVertical24 } from "@carbon/icons-react";
import { ActionMenu } from "../ActionMenu";
import { WorkAgreementListItem } from "../TabWorkAgreements";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    listContainer: {
      paddingTop: theme.spacing(3),
      padding: 0,
    },
    listContainerItem: {
      paddingLeft: theme.spacing(8.5),
      paddingBottom: theme.spacing(1.5),
      [theme.breakpoints.only("xs")]: {
        paddingLeft: theme.spacing(3),
      },
    },
    listRow: {
      minHeight: theme.spacing(9),
    },
    rowGrid: {
      paddingRight: 0,
      [theme.breakpoints.only("xs")]: {
        paddingLeft: theme.spacing(1),
      },
    },
    publisherColumn: {
      width: "100%",
      maxWidth: 320,
      minWidth: 150,
    },
    creatorColumn: {
      width: "100%",
      maxWidth: 295,
      minWidth: 150,
    },
    agreementNumberColumn: {
      width: "100%",
      minWidth: 150,
    },
    actionMenuIcon: {
      color: theme.palette.primary.main,
    },
    actionButton: {
      marginRight: theme.spacing(1.5),
    },
    actionMenuPaper: {
      minWidth: 160,
    },
  })
);

interface WorkAgreementsProps {
  workAgreements: WorkAgreementListItem[];
  sectionLabel: string;
  onEdit: (item: WorkAgreementListItem) => void;
  onDelete: (item: WorkAgreementListItem) => void;
  editable: boolean;
}

export const WorkAgreements: React.FC<WorkAgreementsProps> = ({
  workAgreements = [],
  sectionLabel,
  onEdit,
  onDelete,
  editable,
}) => {
  const classes = useStyles();

  return (
    <React.Fragment>
      {workAgreements.length > 0 && (
        <React.Fragment>
          <ContainerContent>
            <Box pt={6.5}>
              <Typography variant="h6">{sectionLabel}</Typography>
            </Box>
          </ContainerContent>
          <Container className={classes.listContainer}>
            <React.Fragment>
              <ContainerListItem className={classes.listContainerItem}>
                <GridListHeaderItem className={classes.publisherColumn}>
                  <ListColumnTitle>Publisher</ListColumnTitle>
                </GridListHeaderItem>
                <GridListHeaderItem className={classes.creatorColumn}>
                  <ListColumnTitle>Creator</ListColumnTitle>
                </GridListHeaderItem>
                <GridListHeaderItem className={classes.agreementNumberColumn}>
                  <ListColumnTitle>Agreement Number</ListColumnTitle>
                </GridListHeaderItem>
              </ContainerListItem>
              {workAgreements.map((el) => (
                <ListItem
                  key={`${el.creatorIpi}${el.publisherIpi}${el.agreementNumber}`}
                  className={classes.listRow}
                >
                  <ContainerListItem
                    leftOffset={3.5}
                    className={classes.rowGrid}
                  >
                    <Grid item className={classes.publisherColumn}>
                      {el.publisher}
                    </Grid>
                    <Grid item className={classes.creatorColumn}>
                      {el.creator}
                    </Grid>
                    <Grid item className={classes.agreementNumberColumn}>
                      {el.agreementNumber}
                    </Grid>
                    {editable && (
                      <ListItemSecondaryAction>
                        <ActionMenu
                          classes={{ paper: classes.actionMenuPaper }}
                          anchorOrigin={{
                            vertical: "bottom",
                            horizontal: "center",
                          }}
                          transformOrigin={{
                            vertical: "top",
                            horizontal: "right",
                          }}
                          items={[
                            {
                              label: "Edit",
                              onClick: () => onEdit(el),
                            },
                            {
                              label: "Delete",
                              onClick: () => onDelete(el),
                            },
                          ]}
                        >
                          <IconButton className={classes.actionButton}>
                            <OverflowMenuVertical24
                              className={classes.actionMenuIcon}
                            />
                          </IconButton>
                        </ActionMenu>
                      </ListItemSecondaryAction>
                    )}
                  </ContainerListItem>
                </ListItem>
              ))}
            </React.Fragment>
          </Container>
        </React.Fragment>
      )}
    </React.Fragment>
  );
};
