import { useEffect, useLayoutEffect, useState } from "react";
import debounce from "lodash/debounce";
import { useAppSelector } from "../App.reducer";

interface UseWindowSizeProps {
  subtractSidebarWidth?: boolean;
}

export const useWindowSize = (props?: UseWindowSizeProps) => {
  const [size, setSize] = useState([0, 0]);
  const { isSidebarOpen, sidebarWidth } = useAppSelector(
    (state) => state.sidebar
  );
  const updateSize = () => {
    const width =
      isSidebarOpen && props?.subtractSidebarWidth
        ? window.innerWidth - sidebarWidth
        : window.innerWidth;
    setSize([width, window.innerHeight]);
  };
  const delayedUpdateSize = debounce(updateSize, 300);

  useLayoutEffect(() => {
    window.addEventListener("resize", delayedUpdateSize);
    return () => {
      delayedUpdateSize.cancel();
      window.removeEventListener("resize", delayedUpdateSize);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    delayedUpdateSize();
    return () => delayedUpdateSize.cancel();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isSidebarOpen]);

  return size;
};
