import React from "react";
import {
  Avatar,
  Box,
  createStyles,
  makeStyles,
  Theme,
} from "@material-ui/core";
import { BoxProps } from "@material-ui/core/Box";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      padding: theme.spacing(1, 0),
      display: "inline-flex",
      alignItems: "center",
      overflow: "hidden",
    },
    avatar: {
      marginRight: theme.spacing(1),
      alignSelf: "start",
    },
  })
);

interface AvatarTitleProps extends BoxProps {
  avatar: string;
  avatarClassName?: string;
}

export const AvatarTitle: React.FC<AvatarTitleProps> = ({
  children,
  avatar,
  className,
  avatarClassName,
  ...props
}) => {
  const classes = useStyles();
  return (
    <Box className={clsx(classes.root, className)} {...props}>
      <Avatar className={clsx(avatarClassName, classes.avatar)}>
        {avatar}
      </Avatar>
      {children}
    </Box>
  );
};
