import React from "react";
import { Typography, TypographyProps } from "@material-ui/core";
import { FieldProps } from "formik";

interface AdditionalProps {
  formatValue: (value: any) => string;
}

export const FormikTypography: React.FC<
  FieldProps & TypographyProps & AdditionalProps
> = ({ field: { value }, form, meta, formatValue, ...props }) => (
  <Typography {...props}>{formatValue(value)}</Typography>
);
