import axios from "axios";
import { Action } from "redux";
import { ThunkAction, ThunkDispatch } from "redux-thunk";
import { WorkRegistrationResponse, WorkResponse } from "../../../api.types";
import { AppState } from "../../../App.reducer";
import { AlertType } from "../Alerts/actions.types";
import { addAlertAction } from "../Alerts/actions";
import {
  patchWorkAction,
  patchWorkSuccessAction,
  postWorkAction,
  postWorkFailureAction,
  postWorkSuccessAction,
  updateWorkAction,
  updateWorkFailureAction,
  updateWorkSuccessAction,
} from "../Works/actions";
import {
  AccountRoles,
  ApiErrorCodeMessages,
  ApiErrorCodeTypes,
  PartyRoleType,
  WorkRegistrationErrorType,
} from "../../../types/enums";
import { Ipi, Work, WorkPatch } from "../../../utils/models";
import { changeCreatorIpiEffect } from "../Works/effects";
import {
  registerWorkAction,
  registerWorkFailureAction,
  registerWorkSuccessAction,
} from "./actions";
import Promise from "promise-polyfill";
import {
  convertWorkCreatorSharesFromPercentage,
  convertWorkCreatorSharesToPercentage,
  doesUserHaveRoles,
} from "../../../utils/helpers";
import { handleError } from "../../../utils/errorHandler";
import { WorkFormValues } from "../../organisms/FormWorkRegistration";
import { creatorIpiHasChanged, extractDirtyWorkAttrs } from "./utils";
import { toasts } from "../../../utils/toasts";
import { getWorkLinksSelector } from "../ListWorks/selectors";
import { getIpiListSelector } from "../Ipi/selectors";

const getSubmitWorkError = (ipiList: Ipi[], isCreator: boolean) => {
  if (ipiList.length === 0 && isCreator) {
    return {
      message: ApiErrorCodeMessages.MISSING_IPI_ASSOCIATION,
      code: ApiErrorCodeTypes.MISSING_IPI_ASSOCIATION,
    };
  }
  return {
    message: "Unexpected error occurred",
  };
};

export const updateWorkEffect = (
  workId: string,
  formData: WorkFormValues
): ThunkAction<
  Promise<{
    work?: Work;
  } | void>,
  AppState,
  void,
  Action<string>
> => async (
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState
) => {
  dispatch(updateWorkAction());

  try {
    const state = getState().works;
    let work = state.work as Work;

    const changedCreatorIpi = creatorIpiHasChanged(work, formData);
    if (changedCreatorIpi) {
      work = await dispatch(
        changeCreatorIpiEffect(
          changedCreatorIpi.oldReference,
          changedCreatorIpi.newIpi
        )
      );
    }

    const changedAttributes = extractDirtyWorkAttrs(work, formData);
    if (Object.keys(changedAttributes).length) {
      await dispatch(patchWorkEffect(changedAttributes));
    }

    dispatch(updateWorkSuccessAction());
    const worksState = getState().works;
    return {
      work: worksState.work,
    };
  } catch (e) {
    dispatch(updateWorkFailureAction());
    dispatch(
      addAlertAction(
        AlertType.ERROR,
        `${toasts.error.work.SAVE_WORK} ${e.message}`
      )
    );
    throw WorkRegistrationErrorType.SAVE_ERROR;
  }
};

export const postWorkEffect = (data: {
  title: string;
  titleLanguageCode?: string;
  creator: {
    ipiNameNumber: number;
    role?: PartyRoleType;
    publishers?: Array<{
      ipiNameNumber: number;
    }>;
  };
}): ThunkAction<Promise<Work | void>, AppState, void, Action<string>> => async (
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState
) => {
  dispatch(postWorkAction());
  const state = getState();
  const link = state.works._workListItemsLinks?.create;
  try {
    const response = await axios.request({
      url: link?.href,
      method: link?.method,
      data,
      withCredentials: true,
    });
    const createdWork = response.data.response.work.item;
    dispatch(postWorkSuccessAction(createdWork));

    return createdWork;
  } catch (e) {
    dispatch(postWorkFailureAction());
    const error = handleError(e.response, "work");
    const ipiList = getIpiListSelector(state);
    const ipiNotAdded =
      e.response.status === 400 &&
      doesUserHaveRoles(state, [AccountRoles.CREATOR]) &&
      (ipiList.length === 0 || !ipiList.find((el) => el.default));

    dispatch(
      addAlertAction(
        AlertType.ERROR,
        ipiNotAdded
          ? toasts.error.work.MISSING_IPI
          : `${toasts.error.work.CREATE_WORK} ${error.message}`
      )
    );
    if (ipiNotAdded) {
      throw WorkRegistrationErrorType.SAVE_ERROR_IPI;
    } else {
      throw WorkRegistrationErrorType.CREATION_ERROR;
    }
  }
};

export const patchWorkEffect = (
  work: WorkPatch
): ThunkAction<Promise<void>, AppState, void, Action<string>> => async (
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState
) => {
  dispatch(patchWorkAction());
  const _link = getState().works.work?._links.update;
  const formattedData: WorkPatch = {
    ...work,
    creators: convertWorkCreatorSharesFromPercentage(work.creators),
  };
  try {
    const response = await axios.request<WorkResponse>({
      url: _link?.href,
      method: _link?.method,
      data: formattedData,
      withCredentials: true,
    });
    const { item } = response.data.response.work;
    const formattedResponse: Work = {
      ...item,
      creators: convertWorkCreatorSharesToPercentage(item.creators),
    };
    dispatch(patchWorkSuccessAction(formattedResponse));
  } catch (e) {
    throw handleError(e.response, "work");
  }
};

export const registerWorkEffect = (
  workId: string
): ThunkAction<
  Promise<WorkResponse | void>,
  AppState,
  void,
  Action<string>
> => async (
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState: () => AppState
) => {
  dispatch(registerWorkAction());
  const _link = getWorkLinksSelector(getState())?.submit;
  if (!_link) {
    const userIpiList = getState().ipi.ipiList;
    const isCreator = Boolean(
      getState().signIn.account?.roles.includes(AccountRoles.CREATOR)
    );
    dispatch(registerWorkFailureAction());
    const error = getSubmitWorkError(userIpiList, isCreator);
    dispatch(
      addAlertAction(
        AlertType.ERROR,
        `${toasts.error.work.REGISTRATION} ${error.message}`
      )
    );
    throw WorkRegistrationErrorType.SUBMIT_ERROR;
  } else {
    try {
      await axios.request<WorkRegistrationResponse>({
        url: _link.href.replace("{guid}", workId),
        method: _link.method,
        withCredentials: true,
      });
      dispatch(registerWorkSuccessAction());
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.work.REGISTRATION)
      );
    } catch (e) {
      const error = handleError(e.response, "work");
      dispatch(registerWorkFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.work.REGISTRATION} ${error.message}`
        )
      );
      throw WorkRegistrationErrorType.SUBMIT_ERROR;
    }
  }
};
