import {
  DatePickerProps,
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";
import { FieldProps, getIn } from "formik";
import React, { useState } from "react";
import DateFnsUtils from "@date-io/date-fns";
import { CalendarToday } from "@material-ui/icons";
import { TooltipTypes } from "../../../types/tooltips";
import { format } from "date-fns";
import { TooltipMap } from "../../atoms/TooltipMap";
import { Calendar24, ChevronLeft24, ChevronRight24 } from "@carbon/icons-react";
import { createStyles, makeStyles, Theme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    keyboardIcon: {
      color: theme.palette.info.light,
    },
  })
);

interface FormikDatePickerProps {
  tooltip?: TooltipTypes;
  dateFormat?: string;
  disableErrors?: boolean;
  infiniteOption?: boolean;
}

export const getInfiniteDate = () => {
  const d = new Date();
  d.setFullYear(9999, 11, 31);
  d.setMilliseconds(0);
  d.setSeconds(0);
  d.setMinutes(0);
  return d;
};

export const FormikDatePicker: React.FC<
  FormikDatePickerProps & DatePickerProps & FieldProps
> = ({
  form: {
    setFieldValue,
    setFieldError,
    isSubmitting,
    errors,
    touched,
    submitCount,
    values,
  },
  field: { name, value, onBlur },
  tooltip,
  dateFormat,
  disableErrors,
  infiniteOption,
  ...props
}) => {
  const classes = useStyles();
  const errorText = getIn(errors, name);
  const touchedVal = getIn(touched, name);
  const hasError = disableErrors
    ? false
    : (touchedVal || submitCount > 0) && errorText !== undefined;
  const [isOpen, setIsOpen] = useState(false);
  const isInfinite = value && format(new Date(value), "yyyy") === "9999";

  const setInfinite = () => {
    const infinite = getInfiniteDate();
    setFieldValue(name, format(infinite, "yyyy-MM-dd"));
    setIsOpen(false);
    onBlur(name);
  };

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <TooltipMap to={isOpen ? "None" : tooltip || "None"}>
        <KeyboardDatePicker
          {...props}
          maxDateMessage=""
          minDateMessage=""
          onOpen={() => setIsOpen(true)}
          onClose={() => setIsOpen(false)}
          format={isInfinite ? "'Infinite'" : dateFormat || "dd/MM/yyyy"}
          disabled={isSubmitting || props.disabled}
          error={hasError}
          helperText={hasError ? errorText : ""}
          showTodayButton={infiniteOption}
          rightArrowIcon={<ChevronRight24 />}
          leftArrowIcon={<ChevronLeft24 />}
          todayLabel={
            infiniteOption && <div onClick={setInfinite}>Infinite</div>
          }
          maxDate={infiniteOption ? new Date(9999, 11, 31) : undefined}
          onError={(error) => {
            if (error && errorText !== `${error}`) {
              setFieldError(name, `${error}`);
            }
          }}
          DialogProps={{
            open: isOpen,
          }}
          keyboardIcon={<Calendar24 className={classes.keyboardIcon} />}
          InputProps={{
            endAdornment: (
              <CalendarToday
                data-testid={`open-datepicker-${name}`}
                color="action"
              />
            ),
            inputProps: {
              "data-testid": `datepicker-${name}`,
            },
          }}
          onChange={(value) => {
            value &&
              !isNaN(value?.getTime()) &&
              setFieldValue(name, format(value, "yyyy-MM-dd"));

            if (!value) setFieldValue(name, undefined);
          }}
          onBlur={onBlur(name)}
          value={value ? new Date(value) : null}
        />
      </TooltipMap>
    </MuiPickersUtilsProvider>
  );
};
