import React from "react";
import {
  Box,
  Button,
  createStyles,
  makeStyles,
  Paper,
  Theme,
  Typography,
  useTheme,
} from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      height: "100%",
      width: "100%",
    },
    message: {
      paddingTop: theme.spacing(2.5),
    },
    button: {
      maxWidth: 240,
    },
    content: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      flexDirection: "column",
      marginTop: theme.spacing(11),
    },
  })
);

interface IpiEmptyStateProps {
  withButton?: boolean;
  onClick: () => void;
}

export const IpiEmptyState: React.FC<IpiEmptyStateProps> = ({
  withButton,
  onClick,
}) => {
  const classes = useStyles();
  const theme = useTheme();
  return (
    <Paper className={classes.root}>
      <Box className={classes.content}>
        <img
          src={`${process.env.PUBLIC_URL}/static/media/svg/person-questionmark.svg`}
          alt="Person icon"
        />
        <Box pb={`${theme.spacing(withButton ? 4.5 : 1)}px`}>
          <Typography
            className={classes.message}
            variant="h6"
            color="textSecondary"
            align="center"
          >
            Please add an IPI number
          </Typography>
          <Typography color="textSecondary" align="center">
            In order to register a work, you need to add your IPI number
          </Typography>
        </Box>
        {withButton && (
          <Button
            fullWidth
            variant="contained"
            color="primary"
            className={classes.button}
            onClick={onClick}
          >
            Add your IPI number now
          </Button>
        )}
      </Box>
    </Paper>
  );
};
