import { Redirect, Route, Switch, useLocation } from "react-router-dom";
import React from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "./App.reducer";
import {
  getBootstrapLinksSelector,
  getFetchEmployeesLinkSelector,
  getSettingsFetchLinkSelector,
  getUserIsSocAdminSelector,
} from "./components/pages/SignIn/selectors";
import { paths } from "./utils/common";
import { Alerts } from "./components/pages/Alerts";
import { EmailVerificationResponse } from "./components/organisms/EmailVerificationResponse";
import { SignOut } from "./components/pages/SignOut";
import { PageAgreementsList } from "./components/pages/PageAgreementsList";
import { Templates } from "./components/pages/Templates";
import { Employees } from "./components/pages/Employees";
import { ListWorks } from "./components/pages/ListWorks";
import { AgreementRegistration } from "./components/pages/AgreementRegistration";
import { SignUpView } from "./components/pages/SignUp";
import { Agreements } from "./components/pages/Agreements";
import { AgreementTemplatesListPage } from "./components/pages/AgreementTemplates";
import { Works } from "./components/pages/Works";
import { PrivateRoute } from "./components/atoms/PrivateRoute";
import { Sidebar } from "./components/pages/Sidebar";
import { IpiVerificationResponse } from "./components/organisms/IpiVerificationResponse";
import { WorkRegistration } from "./components/pages/WorkRegistration";
import { AgreementTemplate } from "./components/pages/AgreementTemplate";
import { Profile } from "./components/pages/Profile";
import { Notifications } from "./components/pages/Notifications";
import { Settings } from "./components/pages/Settings";
import { AuthLogout } from "./components/pages/AuthLogout/component";
import { AuthError } from "./components/pages/AuthError/component";
import { isDevelopment } from "./utils/helpers";
import { navigationItems } from "./components/organisms/Sidebar";
import { FaqDrawer } from "./components/organisms/FaqDrawer";
import { getFaqAccessObject } from "./components/organisms/FaqDrawer/utils";
import { AccountNotApproved } from "./components/atoms/AccountNotApproved/component";
import { getInviteEmployeeLinkSelector } from "./components/pages/Company/selectors";
import { WorkTemplate } from "./components/pages/WorkTemplate";
import { SignInPage } from "./components/pages/SignIn";
import { AppContainer } from "./components/organisms/AppContainer";
import { RouteComponentProps } from "react-router";

export const App = () => {
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();
  const { pathname } = useLocation();

  const state = useAppSelector((state) => state);
  const auth = Boolean(state.signIn.authenticated);
  const bootstrapLinks = getBootstrapLinksSelector(state);
  const isUserSocAdmin = getUserIsSocAdminSelector(state);
  const accessAgreements = Boolean(bootstrapLinks?.agreements);
  const accessWorks = Boolean(
    bootstrapLinks?.works || bootstrapLinks?.workDrafts
  );
  const accessWorkTemplates = Boolean(bootstrapLinks?.workTemplates);
  const accessSettings = Boolean(getSettingsFetchLinkSelector(state));
  const accessEmployees = Boolean(getFetchEmployeesLinkSelector(state));
  const accessEmployeeInvites = Boolean(getInviteEmployeeLinkSelector(state));
  const firstPage = navigationItems.find(({ access }) => access(state));

  return (
    <AppContainer>
      <Alerts />
      <Route
        exact
        path={paths.INDEX}
        render={() => <Redirect to={firstPage?.pathname || paths.WORKS} />}
      />
      <Route exact path={paths.NOT_APPROVED} component={AccountNotApproved} />
      <Route exact path={paths.AUTH_ERROR} component={AuthError} />
      <Route exact path={paths.AUTH_LOGOUT} component={AuthLogout} />
      <Route exact path={paths.SIGN_IN} component={SignInPage} />
      <Route exact path={`${paths.SIGN_IN}/:subPage`} component={SignInPage} />
      <Route exact path={`${paths.SIGN_UP}/:subPage`} component={SignUpView} />
      <Route exact path={paths.SIGN_UP} component={SignUpView} />
      <Route
        exact
        path={paths.WORKS}
        render={() => <Redirect to={paths.WORKS_LIST} />}
      />
      <Route
        exact
        path={paths.AGREEMENTS}
        render={() => <Redirect to={paths.AGREEMENTS_LIST} />}
      />
      {auth && (
        <React.Fragment>
          {pathname !== paths.SIGN_OUT && <Sidebar />}
          <Route
            exact
            path={paths.PROFILE}
            render={() => <Redirect to={paths.PROFILE_PERSONAL} />}
          />
          <Route
            exact
            path={paths.EMPLOYEES}
            render={() => <Redirect to={paths.EMPLOYEES_LIST} />}
          />
          <Switch>
            <PrivateRoute
              auth={auth}
              exact
              path={paths.SIGN_OUT}
              component={SignOut}
            />
            <PrivateRoute
              auth={auth && !isUserSocAdmin}
              exact
              path={paths.WORKS_REGISTER}
              component={WorkRegistration}
            />
            <PrivateRoute
              auth={auth}
              exact
              path={`${paths.WORKS_REGISTER}/:id`}
              component={WorkRegistration}
            />
            <PrivateRoute
              exact
              auth={auth && accessAgreements && !isUserSocAdmin}
              path={paths.AGREEMENTS_REGISTER}
              component={AgreementRegistration}
            />
            <PrivateRoute
              exact
              auth={auth && accessAgreements}
              path={`${paths.AGREEMENTS_REGISTER}/:id`}
              component={AgreementRegistration}
            />
            <PrivateRoute
              exact
              auth={auth}
              path={paths.WORK_TEMPLATE}
              component={WorkTemplate}
            />
            <PrivateRoute
              exact
              auth={auth}
              path={`${paths.WORK_TEMPLATE}/:id`}
              component={WorkTemplate}
            />
            <PrivateRoute
              exact
              auth={auth && accessAgreements}
              path={`${paths.AGREEMENT_TEMPLATE}/:guid`}
              component={AgreementTemplate}
            />
            <PrivateRoute
              exact
              auth={auth && accessAgreements}
              path={paths.AGREEMENT_TEMPLATE}
              component={AgreementTemplate}
            />
            <PrivateRoute auth={auth} path={paths.WORKS} component={Works} />
            <PrivateRoute
              auth={auth}
              path={paths.PROFILE}
              component={Profile}
            />
            <PrivateRoute
              auth={auth && accessAgreements}
              path={paths.AGREEMENTS}
              component={Agreements}
            />
          </Switch>
          <PrivateRoute
            exact
            auth={auth && accessAgreements}
            path={paths.AGREEMENTS_LIST}
            component={PageAgreementsList}
          />
          <PrivateRoute
            exact
            auth={auth && accessAgreements}
            path={paths.AGREEMENTS_DRAFT_LIST}
            component={PageAgreementsList}
          />
          <PrivateRoute
            exact
            auth={auth && accessAgreements && !isUserSocAdmin && isDevelopment}
            path={paths.AGREEMENTS_TEMPLATE_LIST}
            component={AgreementTemplatesListPage}
          />
          <PrivateRoute
            exact
            auth={auth}
            path={paths.WORKS_LIST}
            component={ListWorks}
          />
          <PrivateRoute
            exact
            auth={auth && accessWorkTemplates}
            path={paths.WORKS_TEMPLATE_LIST}
            component={Templates}
          />
          <PrivateRoute
            exact
            auth={auth && accessWorks}
            path={paths.WORKS_DRAFT_LIST}
            component={ListWorks}
          />
          <PrivateRoute
            auth={auth}
            exact
            path={paths.NOTIFICATIONS}
            component={Notifications}
          />
          <PrivateRoute
            auth={auth}
            exact
            path={`${paths.PROFILE_IPI}/:verifyState`}
            component={(
              props: RouteComponentProps<{ verifyState: string }>
            ) => (
              <IpiVerificationResponse
                {...props}
                dispatch={dispatch}
                dispatchEffect={dispatchEffect}
              />
            )}
          />
          <PrivateRoute
            auth={auth}
            exact
            path={`${paths.PROFILE_PERSONAL}/:verifyState`}
            component={(
              props: RouteComponentProps<{ verifyState: string }>
            ) => (
              <EmailVerificationResponse
                {...props}
                dispatchEffect={dispatchEffect}
              />
            )}
          />
          <PrivateRoute
            auth={auth && accessEmployees}
            exact
            path={paths.EMPLOYEES_LIST}
            component={Employees}
          />
          <PrivateRoute
            auth={auth && accessEmployeeInvites}
            exact
            path={paths.EMPLOYEES_INVITES}
            component={Employees}
          />
          {isDevelopment && (
            <PrivateRoute
              auth={auth && accessSettings}
              exact
              path={paths.SETTINGS_GENERAL}
              component={Settings}
            />
          )}
          <PrivateRoute
            auth={auth && accessSettings}
            exact
            path={paths.SETTINGS_THEME}
            component={Settings}
          />
          <PrivateRoute
            exact
            auth={auth && accessSettings}
            path={paths.SETTINGS}
            component={() => <Redirect to={paths.SETTINGS_THEME} />}
          />
          <FaqDrawer
            access={getFaqAccessObject(state)}
            dispatch={dispatch}
            faqState={state.faq}
          />
        </React.Fragment>
      )}
    </AppContainer>
  );
};
