import { SettingsTheme, Theme } from "../../../utils/models";

export const SET_THEME = "SET_THEME";
export const TOGGLE_DARK_MODE = "TOGGLE_DARK_MODE";
export const UPLOAD_CUSTOM_LOGO = "UPLOAD_CUSTOM_LOGO";
export const UPLOAD_CUSTOM_LOGO_SUCCESS = "UPLOAD_CUSTOM_LOGO_SUCCESS";
export const UPLOAD_CUSTOM_LOGO_FAILURE = "UPLOAD_CUSTOM_LOGO_FAILURE";
export const FETCH_THEMES = "FETCH_THEMES";
export const FETCH_THEMES_SUCCESS = "FETCH_THEMES_SUCCESS";
export const FETCH_THEMES_FAILURE = "FETCH_THEMES_FAILURE";
export const UPDATE_THEME = "UPDATE_THEME";
export const UPDATE_THEME_SUCCESS = "UPDATE_THEME_SUCCESS";
export const UPDATE_THEME_FAILURE = "UPDATE_THEME_FAILURE";
export const CREATE_THEME = "CREATE_THEME";
export const CREATE_THEME_SUCCESS = "CREATE_THEME_SUCCESS";
export const CREATE_THEME_FAILURE = "CREATE_THEME_FAILURE";
export const DELETE_THEME = "DELETE_THEME";
export const DELETE_THEME_SUCCESS = "DELETE_THEME_SUCCESS";
export const DELETE_THEME_FAILURE = "DELETE_THEME_FAILURE";
export const FETCH_PUBLISHED_THEME = "FETCH_PUBLISHED_THEME";
export const FETCH_PUBLISHED_THEME_SUCCESS = "FETCH_PUBLISHED_THEME_SUCCESS";
export const FETCH_PUBLISHED_THEME_FAILURE = "FETCH_PUBLISHED_THEME_FAILURE";
export const TOGGLE_THEME_DRAWER = "TOGGLE_THEME_DRAWER";

export interface SetThemeAction {
  type: typeof SET_THEME;
  theme: Theme;
}

export interface FetchPublishedThemeAction {
  type: typeof FETCH_PUBLISHED_THEME;
}

export interface FetchPublishedThemeActionSuccess {
  type: typeof FETCH_PUBLISHED_THEME_SUCCESS;
}

export interface FetchPublishedThemeActionFailure {
  type: typeof FETCH_PUBLISHED_THEME_FAILURE;
}

export interface ToggleDarkModeAction {
  type: typeof TOGGLE_DARK_MODE;
}

export interface UploadCustomLogoAction {
  type: typeof UPLOAD_CUSTOM_LOGO;
}

export interface UploadCustomLogoActionActionSuccess {
  type: typeof UPLOAD_CUSTOM_LOGO_SUCCESS;
  logo: string;
}

export interface UploadCustomLogoActionActionFailure {
  type: typeof UPLOAD_CUSTOM_LOGO_FAILURE;
}

export interface FetchThemesAction {
  type: typeof FETCH_THEMES;
}

export interface FetchThemesActionSuccess {
  type: typeof FETCH_THEMES_SUCCESS;
  themes: SettingsTheme[];
}

export interface FetchThemesActionFailure {
  type: typeof FETCH_THEMES_FAILURE;
}

export interface UpdateThemeAction {
  type: typeof UPDATE_THEME;
}

export interface UpdateThemeActionSuccess {
  type: typeof UPDATE_THEME_SUCCESS;
  theme: SettingsTheme;
  defaultThemeUpdated: boolean;
}

export interface UpdateThemeActionFailure {
  type: typeof UPDATE_THEME_FAILURE;
}

export interface CreateThemeAction {
  type: typeof CREATE_THEME;
}

export interface CreateThemeActionSuccess {
  type: typeof CREATE_THEME_SUCCESS;
  theme: SettingsTheme;
}

export interface CreateThemeActionFailure {
  type: typeof CREATE_THEME_FAILURE;
}

export interface DeleteThemeAction {
  type: typeof DELETE_THEME;
}

export interface DeleteThemeSuccessAction {
  type: typeof DELETE_THEME_SUCCESS;
  themeId: string;
  isDefaultBranch: boolean;
}

export interface DeleteThemeActionFailure {
  type: typeof DELETE_THEME_FAILURE;
}

export interface ToggleThemeDrawerAction {
  type: typeof TOGGLE_THEME_DRAWER;
}

export type ThemeActionTypes =
  | SetThemeAction
  | FetchPublishedThemeAction
  | FetchPublishedThemeActionSuccess
  | FetchPublishedThemeActionFailure
  | ToggleDarkModeAction
  | UploadCustomLogoAction
  | UploadCustomLogoActionActionFailure
  | UploadCustomLogoActionActionSuccess
  | FetchThemesAction
  | FetchThemesActionFailure
  | FetchThemesActionSuccess
  | UpdateThemeAction
  | UpdateThemeActionFailure
  | UpdateThemeActionSuccess
  | CreateThemeAction
  | CreateThemeActionFailure
  | CreateThemeActionSuccess
  | DeleteThemeAction
  | DeleteThemeActionFailure
  | DeleteThemeSuccessAction
  | ToggleThemeDrawerAction;
