import { ThunkAction, ThunkDispatch } from "redux-thunk";
import Promise from "promise-polyfill";
import { AppState } from "../../../App.reducer";
import { Action } from "redux";
import { toggleRevisionAction, toggleRevisionSuccessAction } from "./actions";

export const toggleRevisionEffect = (data: {
  agreement: boolean;
  work: boolean;
}): ThunkAction<Promise<void>, AppState, void, Action<string>> => async (
  dispatch: ThunkDispatch<AppState, void, Action<string>>
) => {
  dispatch(toggleRevisionAction());

  //TODO:: implement backend when available
  const mockedPromise = new Promise((resolve) => {
    setTimeout(() => {
      resolve();
    }, 1000);
  });
  await mockedPromise;
  dispatch(toggleRevisionSuccessAction(data));
};
