import React from "react";
import {
  Box,
  createStyles,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { BrandLogo } from "../BrandLogo";
import { CenteredPaper } from "../CenteredPaper";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    icon: {
      paddingTop: theme.spacing(0.5),
      paddingBottom: theme.spacing(4.5),
    },
    logo: {
      width: 184,
      padding: theme.spacing(7, 0, 0, 0),
      margin: theme.spacing(0, 0, -1, 0),
    },
    primaryText: {
      paddingBottom: theme.spacing(3),
      width: 270,
    },
    paper: {
      maxWidth: 470,
    },
  })
);

export const AccountNotApproved = () => {
  const classes = useStyles();
  return (
    <CenteredPaper className={classes.paper}>
      <Box
        display="flex"
        flexDirection="column"
        justifyContent="center"
        alignItems="center"
        height={1}
      >
        <img
          src={`${process.env.PUBLIC_URL}/static/media/svg/mail.svg`}
          alt="mail icon"
          className={classes.icon}
        />
        <Typography
          variant="body1"
          color="textSecondary"
          align="center"
          className={classes.primaryText}
        >
          Thank you for signing up to the Session Registration Platform!
        </Typography>
        <Typography variant="body1" color="textSecondary" align="center">
          You will receive an email after the administrator confirms your
          registration.
        </Typography>
        <BrandLogo className={classes.logo} />
      </Box>
    </CenteredPaper>
  );
};
