import React, { useState } from "react";
import {
  Button,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@material-ui/core";
import { AppDispatchEffect } from "../../../App.reducer";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { deleteTemplateEffect } from "../../pages/Templates/effects";
import { Dialog } from "../Dialog";

interface DialogDeleteWorkTemplateProps {
  showDialog: boolean;
  hideDialog: () => void;
  dispatchEffect: AppDispatchEffect;
  workTemplateGuid: string;
}

export const DialogDeleteWorkTemplate: React.FC<DialogDeleteWorkTemplateProps> = ({
  showDialog,
  hideDialog,
  workTemplateGuid,
  dispatchEffect,
}) => {
  const [loading, setLoading] = useState(false);

  const handleSuccess = () => {
    setLoading(false);
    hideDialog();
  };
  const handleFail = () => setLoading(false);
  const handleClose = () => hideDialog();

  const handleConfirm = async () => {
    setLoading(true);
    try {
      await dispatchEffect(deleteTemplateEffect(workTemplateGuid));
      handleSuccess();
    } catch (e) {
      handleFail();
    }
  };

  return (
    <Dialog variant="simple" open={showDialog} onClose={handleClose}>
      <DialogTitle>Delete Template</DialogTitle>
      <DialogContent>
        <DialogContentText>
          Are you sure you want to delete a template?
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
        <ButtonSubmit
          submitting={loading}
          onClick={handleConfirm}
          variant="text"
        >
          Delete
        </ButtonSubmit>
      </DialogActions>
    </Dialog>
  );
};
