import {
  CREATE_THEME_SUCCESS,
  DELETE_THEME_SUCCESS,
  FETCH_PUBLISHED_THEME,
  FETCH_PUBLISHED_THEME_FAILURE,
  FETCH_PUBLISHED_THEME_SUCCESS,
  FETCH_THEMES,
  FETCH_THEMES_FAILURE,
  FETCH_THEMES_SUCCESS,
  SET_THEME,
  ThemeActionTypes,
  TOGGLE_DARK_MODE,
  TOGGLE_THEME_DRAWER,
  UPDATE_THEME_SUCCESS,
} from "./actions.types";
import { Theme } from "../../../utils/models";
import { LoadingState } from "../../../types/enums";
import { getStateWithCurrentTheme } from "./utils";
import { ThemeState } from "./types";
import { getTheme } from "@session-id-limited/client-module-ui-components";

const sessionTheme = getTheme("light");

export const DEFAULT_THEME_ID = "DEFAULT_THEME_ID";

export const defaultTheme: Theme = {
  id: DEFAULT_THEME_ID,
  appBarColor: "#BBB4B0",
  textColor: "#000000",
  title: "Theme name",
};

export const themeInitialState: ThemeState = {
  list: [],
  current: defaultTheme,
  listLoadingState: LoadingState.PRISTINE,
  themeLoadingState: LoadingState.PRISTINE,
  ...sessionTheme,
  palette: {
    ...sessionTheme.palette,
    type: "light",
  },
  overrides: {
    ...sessionTheme.overrides,
    MuiInputBase: {
      ...sessionTheme.overrides?.MuiInputBase,
      input: {
        ...sessionTheme.overrides?.MuiInputBase?.input,
        "&:-webkit-autofill": {
          "-webkit-background-clip": "text",
        },
      },
    },
  },
  typography: sessionTheme.typography,
  drawerOpen: true,
};

export const themeReducer = (
  state = themeInitialState,
  action: ThemeActionTypes
): ThemeState => {
  switch (action.type) {
    case SET_THEME: {
      return getStateWithCurrentTheme(state, action.theme);
    }

    case TOGGLE_DARK_MODE: {
      return {
        ...state,
        palette: {
          ...state.palette,
          type: state.palette?.type === "light" ? "dark" : "light",
        },
      };
    }

    case FETCH_THEMES: {
      return {
        ...state,
        list: [],
        listLoadingState: LoadingState.FETCHING,
      };
    }

    case FETCH_THEMES_SUCCESS: {
      return {
        ...state,
        list: action.themes,
        listLoadingState: LoadingState.FETCHED,
      };
    }

    case FETCH_THEMES_FAILURE: {
      return {
        ...state,
        listLoadingState: LoadingState.ERROR,
      };
    }

    case FETCH_PUBLISHED_THEME: {
      return {
        ...state,
        themeLoadingState: LoadingState.FETCHING,
      };
    }

    case FETCH_PUBLISHED_THEME_FAILURE: {
      return {
        ...state,
        themeLoadingState: LoadingState.ERROR,
      };
    }

    case FETCH_PUBLISHED_THEME_SUCCESS: {
      return {
        ...state,
        themeLoadingState: LoadingState.FETCHED,
      };
    }

    case UPDATE_THEME_SUCCESS: {
      return {
        ...state,
        list: state.list.map((theme) => {
          if (action.defaultThemeUpdated) {
            return theme.data.id === DEFAULT_THEME_ID ? action.theme : theme;
          } else {
            return theme.data.id === action.theme.data.id
              ? action.theme
              : theme;
          }
        }),
      };
    }

    case CREATE_THEME_SUCCESS: {
      return {
        ...state,
        list: [...state.list, action.theme],
      };
    }

    case DELETE_THEME_SUCCESS: {
      return {
        ...state,
        list: action.isDefaultBranch
          ? state.list.map((theme) =>
              theme.data.id === action.themeId
                ? { ...theme, data: defaultTheme }
                : theme
            )
          : state.list.filter((theme) => theme.data.id !== action.themeId),
      };
    }

    case TOGGLE_THEME_DRAWER: {
      return {
        ...state,
        drawerOpen: !state.drawerOpen,
      };
    }

    default:
      return state;
  }
};
