import {
  SET_SIDEBAR_VARIANT,
  SIDEBAR_TOGGLE,
  SidebarActionTypes,
} from "./actions.types";
import { SidebarVariant } from "../../../utils/models";

export const sidebarToggleAction = (show?: boolean): SidebarActionTypes => ({
  type: SIDEBAR_TOGGLE,
  show,
});

export const setSidebarVariantAction = (
  variant: SidebarVariant
): SidebarActionTypes => ({
  type: SET_SIDEBAR_VARIANT,
  variant,
});
