import React, { useState } from "react";
import { IpiSearchCreatorOption, WorkCreator } from "../../../utils/models";
import { FetchIpiCreatorsResponse } from "../../../api.types";
import { createCurrentTime } from "../../../utils/helpers";
import { FieldProps } from "formik";
import { PartyRoleType } from "../../../types/enums";
import {
  Box,
  createStyles,
  DialogContent,
  DialogTitle,
  makeStyles,
} from "@material-ui/core";
import { WorkFormValues } from "../FormWorkRegistration";
import { Theme } from "@material-ui/core/styles";
import { FabExtended } from "../../atoms/FabExtended";
import { IpiSearchOption } from "../../molecules/IpiSearchOption";
import { Dialog } from "../../molecules/Dialog";
import { AutocompleteSelect } from "../AutocompleteSelect";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    popperPosition: {
      marginRight: -60,
    },
    fabButton: {
      position: "absolute",
      bottom: theme.spacing(4),
      right: theme.spacing(4),
      zIndex: 1,
    },
    dialog: {
      width: 456,
      height: 192,
    },
  })
);

interface FormikAddWorkWriterProps {
  apiSource: string;
}

export const FormikAddWorkWriter: React.FC<
  FieldProps<any, WorkFormValues> & FormikAddWorkWriterProps
> = ({ apiSource, form: { setFieldValue, values, isSubmitting } }) => {
  const [open, setOpen] = useState(false);
  const [searchPhrase, setSearchPhrase] = useState("");
  const classes = useStyles();

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const handleSelect = (option: IpiSearchCreatorOption | null) => {
    if (!option) {
      return;
    }
    const time = createCurrentTime();
    const creator: WorkCreator = {
      updated: time,
      created: time,
      role: PartyRoleType.C,
      ipi: {
        ipiNameNumber: option.ipiNameNumber,
        ipiReference: "",
        verified: false,
      },
      profile: option.profile,
      isSelf: false,
    };
    setFieldValue("creators", [...values.creators, creator]);
    handleClose();
  };

  const filterOutSelected = (options: IpiSearchCreatorOption[]) => {
    return options.filter(
      (option) =>
        !values.creators.find(
          (el: WorkCreator) =>
            el.ipi.ipiNameNumber === Number(option.ipiNameNumber)
        )
    );
  };

  return (
    <React.Fragment>
      <Box className={classes.fabButton}>
        <FabExtended onClick={handleOpen}>Add Creator</FabExtended>
      </Box>

      <Dialog
        variant="complex"
        open={open}
        onClose={handleClose}
        classes={{ paper: classes.dialog }}
      >
        <DialogTitle>Add Creator</DialogTitle>
        <DialogContent>
          <AutocompleteSelect<IpiSearchCreatorOption, FetchIpiCreatorsResponse>
            label="Search IPI Name or Full IPI Number"
            onSelect={handleSelect}
            apiSource={`${apiSource.replace(/{.*.}/, "")}&searchPhrase={query}`}
            setApiResponseToOptions={({ response }) =>
              response.ipiSearchResult.items
            }
            getOptionLabel={(option) => `${option.ipiNameNumber}`}
            openOnFocus
            textFieldProps={{
              onChange: (event) => setSearchPhrase(event.target.value),
            }}
            filterOptions={filterOutSelected}
            renderOption={(option) => (
              <IpiSearchOption searchPhrase={searchPhrase} {...option} />
            )}
          />
        </DialogContent>
      </Dialog>
    </React.Fragment>
  );
};
