import { WorkFormValues } from "../FormWorkRegistration";
import { FormValues, Option } from "./component";
import {
  WorkCreator,
  WorkCreatorPublisher,
  WorkPublisher,
} from "../../../utils/models";
import {
  findCreatorAndFormatToOption,
  findPublisherAndFormatToOption,
  WorkAgreementType,
} from "../TabWorkAgreements/component.utils";
import { TemplateFormValues } from "../FormWorkTemplate";
import { WorkAgreementListItem } from "../TabWorkAgreements";

const publisherToOption = (
  publishers: WorkPublisher[],
  publisher: WorkCreatorPublisher
) => ({
  label:
    publishers.find((el) => el.ipi.ipiNameNumber === publisher.ipiNameNumber)
      ?.profile.displayName || "",
  ipi: publisher.ipiNameNumber,
  isPublisher: true,
});

export const getCreatorPublishers: (
  values: WorkFormValues | TemplateFormValues,
  creatorIpi?: number
) => Option[] = (values, creatorIpi) =>
  values.creators
    .find((el) => el.ipi.ipiNameNumber === creatorIpi)
    ?.publishers?.map((publisher) =>
      publisherToOption(values.publishers, publisher)
    ) || [];

export const getPublisherCreators: (
  values: WorkFormValues | TemplateFormValues,
  publisherIpi?: number
) => Option[] = (values, publisherIpi) =>
  values.creators
    .filter((creator) =>
      creator.publishers?.find(
        (publisher) => publisher.ipiNameNumber === publisherIpi
      )
    )
    .map((creator) => ({
      label: creator.profile.displayName,
      ipi: creator.ipi.ipiNameNumber,
      isPublisher: false,
    }));

export const getOptions: (
  values: WorkFormValues | TemplateFormValues,
  filter?: {
    onlyPublisher?: boolean;
    onlyCreator?: boolean;
  }
) => Option[] = (values, filter) => {
  const options: Option[] = [];
  const isUnique = (options: Option[], ipi: number) =>
    !Boolean(options.find((el) => el.ipi === ipi));

  values.creators.forEach((creator) => {
    isUnique(options, creator.ipi.ipiNameNumber) &&
      options.push({
        label: creator.profile.displayName,
        ipi: creator.ipi.ipiNameNumber,
        isPublisher: false,
      });
    creator.publishers?.forEach((publisher) => {
      isUnique(options, publisher.ipiNameNumber) &&
        options.push(publisherToOption(values.publishers, publisher));
    });
  });

  if (filter?.onlyPublisher) {
    return options.filter((el) => el.isPublisher);
  }

  if (filter?.onlyCreator) {
    return options.filter((el) => !el.isPublisher);
  }

  return options;
};

export const isSubmitButtonDisabled = ({
  creatorOrPublisher,
  agreementNumber,
  publisher,
}: FormValues) => {
  if (creatorOrPublisher?.isPublisher) {
    return !Boolean(agreementNumber);
  }
  if (creatorOrPublisher?.isPublisher === false) {
    return !Boolean(agreementNumber && publisher);
  }
  return true;
};

export const createCreatorPublisherAssociation: (
  creators: WorkCreator[],
  association: FormValues,
  variant: WorkAgreementType
) => WorkCreator[] = (creators, association, variant) => {
  const matchCreator = (creator: WorkCreator) =>
    variant === WorkAgreementType.CREATOR_PUBLISHER
      ? creator.ipi.ipiNameNumber === association.creatorOrPublisher?.ipi
      : true;
  const matchPublisher = (publisher: WorkCreatorPublisher) =>
    variant === WorkAgreementType.CREATOR_PUBLISHER
      ? publisher.ipiNameNumber === association.publisher?.ipi
      : publisher.ipiNameNumber === association.creatorOrPublisher?.ipi;

  return creators.map((creator) => {
    if (matchCreator(creator)) {
      return {
        ...creator,
        publishers: creator.publishers?.map((publisher) => {
          if (matchPublisher(publisher)) {
            return {
              ...publisher,
              creatorPublisherSocietyAgreementNumber:
                association.agreementNumber,
            };
          }
          return publisher;
        }),
      };
    }
    return creator;
  });
};

export const getInitialValues: (
  workFormValues: WorkFormValues | TemplateFormValues,
  edit?: WorkAgreementListItem
) => FormValues = (workFormValues, edit) => {
  const creatorOrPublisher = findCreatorAndFormatToOption(
    workFormValues.creators,
    edit
  );
  const publisher = findPublisherAndFormatToOption(
    workFormValues,
    WorkAgreementType.CREATOR_PUBLISHER,
    edit
  );
  return {
    creatorOrPublisher,
    publisher: publisher && {
      ipi: publisher.ipi,
      isPublisher: publisher.isPublisher,
      label: publisher.label,
    },
    agreementNumber: publisher?.agreementNumber || "",
  };
};
