export const REGISTER_WORK = "REGISTER_WORK";
export const REGISTER_WORK_SUCCESS = "REGISTER_WORK_SUCCESS";
export const REGISTER_WORK_FAILURE = "REGISTER_WORK_FAILURE";

export interface RegisterWorkAction {
  type: typeof REGISTER_WORK;
}

export interface RegisterWorkSuccessAction {
  type: typeof REGISTER_WORK_SUCCESS;
}

export interface RegisterWorkFailureAction {
  type: typeof REGISTER_WORK_FAILURE;
}

export type WorkRegistrationActionTypes =
  | RegisterWorkAction
  | RegisterWorkSuccessAction
  | RegisterWorkFailureAction;
