import { AppState } from "../../../App.reducer";

export const getThemesSelector = (state: AppState) => {
  return state.theme.list;
};

export const getCurrentThemeIdSelector = (state: AppState) => {
  return state.theme.current.id;
};

export const getThemesLoadingStateSelector = (state: AppState) => {
  return state.theme.listLoadingState;
};

export const getCurrentThemeSelector = (state: AppState) => {
  return state.theme.current;
};

export const getCurrentThemeLoadingStateSelector = (state: AppState) => {
  return state.theme.themeLoadingState;
};

export const getCompanyOrganizationKeySelector = (state: AppState) =>
  state.signIn.company?.organizationKey;

export const getThemeDrawerOpenSelector = (state: AppState) =>
  state.theme.drawerOpen
