import {
  FETCH_WORK_SCHEMA_SUCCESS,
  ValidationSchemaActionTypes,
} from "./action.types";
import { WorkSchemaJSON } from "./models";

export interface ValidationSchemaState {
  work?: WorkSchemaJSON;
}

export const validationSchemaInitialState: ValidationSchemaState = {};

export const validationSchemaReducer = (
  state = validationSchemaInitialState,
  action: ValidationSchemaActionTypes
): ValidationSchemaState => {
  switch (action.type) {
    case FETCH_WORK_SCHEMA_SUCCESS: {
      return { ...state, work: action.schema };
    }

    default:
      return state;
  }
};
