import React from "react";
import {
  Button,
  createStyles,
  Grid,
  makeStyles,
  Typography,
} from "@material-ui/core";
import { WorkCreatorPublisher } from "../../../utils/models";
import { CellPrShareSummary } from "../../atoms/CellPrShareSummary";
import { CellCreatorSummary } from "../../atoms/CellCreatorSummary";
import { RowCreatorListItem } from "../RowCreatorListItem";
import { SvgElbowLine } from "../../atoms/SvgElbowLine";
import { CellRoleSummary } from "../../atoms/CellRoleSummary";
import { CellMrShareSummary } from "../../atoms/CellMrShareSummary";
import { CellAvatarSummary } from "../../atoms/CellAvatarSummary";
import { Theme } from "@material-ui/core/styles";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    sumValue: {
      paddingRight: 30,
    },
    sumValueDense: {
      paddingRight: 28,
    },
    summaryRow: {
      paddingLeft: theme.spacing(2),
      alignItems: "center",
    },
    prShareText: {
      width: theme.spacing(7.5),
    },
  })
);

interface RowPublishersSummaryProps {
  publishers: WorkCreatorPublisher[];
  onShow: () => void;
}

export const RowPublishersSummary: React.FC<RowPublishersSummaryProps> = ({
  publishers,
  onShow,
}) => {
  const classes = useStyles();

  const mechanicalSum = publishers.reduce(
    (acc, curr) => acc + (curr.mrShare || 0),
    0
  );
  const performanceSum = publishers.reduce(
    (acc, curr) => acc + (curr.prShare || 0),
    0
  );

  return (
    <React.Fragment>
      {publishers.length > 0 && (
        <RowCreatorListItem className={classes.summaryRow}>
          <Grid container>
            <CellAvatarSummary>
              <SvgElbowLine />
            </CellAvatarSummary>
            <CellCreatorSummary>
              <Button variant="text" onClick={onShow}>
                Show {publishers.length} Publisher
                {publishers.length > 1 && "s"}
              </Button>
            </CellCreatorSummary>
            <CellRoleSummary />
            <CellPrShareSummary>
              <Typography
                className={classes.prShareText}
                color="textSecondary"
                align="right"
              >
                {performanceSum}%
              </Typography>
            </CellPrShareSummary>
            <CellMrShareSummary>
              <Typography color="textSecondary" align="right">
                {mechanicalSum}%
              </Typography>
            </CellMrShareSummary>
            <Grid item xs={1} />
          </Grid>
        </RowCreatorListItem>
      )}
    </React.Fragment>
  );
};
