import React from "react";
import { createStyles, makeStyles } from "@material-ui/core";
import clsx from "clsx";
import { Theme } from "@material-ui/core/styles";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    svg: {
      position: "absolute",
      top: -35,
      left: 16,
    },
    path: {
      stroke: theme.palette.custom.state.selected,
    },
  })
);

interface SvgElbowLineProps {
  height?: number;
  className?: string;
}

export const SvgElbowLine: React.FC<SvgElbowLineProps> = ({
  height = 38,
  className,
}) => {
  const classes = useStyles();
  return (
    <svg
      width="20"
      height={height}
      viewBox={`0 0 20 ${height}`}
      fill="none"
      className={clsx(classes.svg, className)}
    >
      <path
        className={classes.path}
        d={`M10,0 L10,${height} L20,${height}`}
        strokeOpacity="1"
      />
    </svg>
  );
};
