import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { RouteComponentProps } from "react-router";
import React from "react";
import { AccountRoles, LoadingState } from "../../../types/enums";
import {
  getWorksListLinks,
  getWorksListLoadingStateSelector,
  getWorksListSelector,
  getWorksListTypeSelector,
} from "./selectors";
import {
  getIpiListLoadingStateSelector,
  getIpiListSelector,
} from "../Ipi/selectors";
import { Ipi } from "../../../utils/models";
import { paths } from "../../../utils/common";
import { PageNoContent } from "../../molecules/PageNoContent";
import { TableWorks } from "../../organisms/TableWorks";
import { IpiEmptyState } from "../../molecules/IpiEmptyState";
import { doesUserHaveRoles } from "../../../utils/helpers";
import { getUserIsSocAdminSelector } from "../SignIn/selectors";
import { TableWorksAdministrator } from "../../organisms/TableWorksAdministrator";
import { getTemplatesListLinks } from "../Templates/selectors";
import { LoadingScreen } from "../../atoms/LoadingScreen";
import { openIpiSelectionMenuAction } from "../Ipi/actions";

export const ListWorks: React.FC<RouteComponentProps> = ({
  location: { pathname },
  history: { push },
}) => {
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();
  const isDraftsPage = pathname === paths.WORKS_DRAFT_LIST;
  const isMyWorksPage = pathname === paths.WORKS_LIST;
  const state = useAppSelector((state) => state);
  const works = getWorksListSelector(state);
  const ipiList = getIpiListSelector(state);
  const loadingState = getWorksListLoadingStateSelector(state);
  const worksListType = getWorksListTypeSelector(state);
  const ipiListLoadingState = getIpiListLoadingStateSelector(state);
  const canDuplicateWork = Boolean(
    getWorksListLinks(state)?.create && getWorksListLinks(state)?.work
  );
  const canCreateWorkFromTemplate = Boolean(
    getTemplatesListLinks(state)?.create
  );
  const isPublisher = doesUserHaveRoles(state, [AccountRoles.PUBLISHER]);
  const isUserSocAdmin = getUserIsSocAdminSelector(state);
  const workListLinks = getWorksListLinks(state);

  const onEmptyIpiClick = () => {
    push(paths.PROFILE_IPI);
    dispatch(openIpiSelectionMenuAction(true));
  };

  const getNoContentPage = (isDraftsPage: boolean, ipiList: Ipi[]) => {
    if (isDraftsPage) {
      return (
        <PageNoContent
          primaryText="No Drafts"
          secondaryText="Click the + button to create a new draft"
        />
      );
    }

    if (ipiList.length === 0 && ipiListLoadingState === LoadingState.FETCHED) {
      return <IpiEmptyState withButton onClick={onEmptyIpiClick} />;
    }

    return (
      <PageNoContent
        primaryText="No Works"
        secondaryText="Click the + button to create or register a new work"
      />
    );
  };

  const loadingWorksPage = isMyWorksPage && worksListType === "DRAFTS";
  const loadingDraftsPage = isDraftsPage && worksListType === "WORKS";

  if (
    loadingState === LoadingState.PRISTINE ||
    loadingState === LoadingState.FETCHING ||
    loadingWorksPage ||
    loadingDraftsPage ||
    ipiListLoadingState === LoadingState.FETCHING
  ) {
    return <LoadingScreen />;
  }

  return works.length === 0 ? (
    getNoContentPage(isDraftsPage, ipiList)
  ) : (
    <React.Fragment>
      {isUserSocAdmin ? (
        <TableWorksAdministrator
          works={works}
          dispatchEffect={dispatchEffect}
        />
      ) : (
        <TableWorks
          works={works}
          dispatchEffect={dispatchEffect}
          canDuplicateWork={canDuplicateWork}
          canCreateWorkFromTemplate={canCreateWorkFromTemplate}
          isPublisher={isPublisher}
          dispatch={dispatch}
          workListLinks={workListLinks}
        />
      )}
    </React.Fragment>
  );
};
