import {
  SET_SIDEBAR_VARIANT,
  SIDEBAR_TOGGLE,
  SidebarActionTypes,
} from "./actions.types";

export interface SidebarState {
  sidebarWidth: number;
  isSidebarOpen: boolean;
  variant: "persistent" | "swipeable";
}

export const sidebarInitialState: SidebarState = {
  sidebarWidth: 240,
  isSidebarOpen: false,
  variant: "persistent",
};

export const sidebarReducer = (
  state = sidebarInitialState,
  action: SidebarActionTypes
): SidebarState => {
  switch (action.type) {
    case SIDEBAR_TOGGLE: {
      return {
        ...state,
        isSidebarOpen:
          action.show === undefined ? !state.isSidebarOpen : action.show,
      };
    }

    case SET_SIDEBAR_VARIANT: {
      return {
        ...state,
        variant: action.variant,
      };
    }

    default:
      return state;
  }
};
