import { ApiError, Error } from "../api.types";
import { ApiErrorCodeMessages, ApiErrorCodeTypes } from "../../src/types/enums";
import { AxiosResponse } from "axios";

const DEFAULT_ERROR_MESSAGE =
  "An unexpected error has occurred. Please try again later.";

export const handleError = (
  response?: AxiosResponse<{ error?: ApiError }>,
  resource?: string
): Error => {
  const errorObj = response?.data.error;
  let message;
  let field = undefined;
  let code = errorObj?.errorCode;
  if (errorObj?.details && errorObj.details.length > 0) {
    message =
      ApiErrorCodeMessages[errorObj.details[0].errorCode as ApiErrorCodeTypes];
    field = errorObj.details[0].parameters
      ? errorObj.details[0].parameters[0]
      : undefined;
    code = errorObj.details[0].errorCode;
  }
  if (!message && errorObj?.errorCode) {
    message = ApiErrorCodeMessages[errorObj.errorCode as ApiErrorCodeTypes];
  }
  if (!message && (errorObj?.statusCode || response?.status)) {
    const status = Number(errorObj?.statusCode || response?.status);
    message = handleStatusError(status, resource);
  }
  return {
    message: message || DEFAULT_ERROR_MESSAGE,
    code,
    field,
    status: errorObj?.statusCode,
  };
};

const handleStatusError = (status: number, resource?: string) => {
  const errorMessages = getErrorMessages(resource);
  switch (status) {
    case 400:
      return errorMessages.BAD_REQUEST;
    case 403:
      return errorMessages.FORBIDDEN;
    case 404:
      return errorMessages.NOT_FOUND;
    case 405:
      return errorMessages.METHOD_NOT_ALLOWED;
    case 409:
      return errorMessages.CONFLICT;
    case 429:
      return errorMessages.TOO_MANY_CALLS;
    case 500:
      return errorMessages.SERVER_ERROR;
    case 502:
      return errorMessages.BAD_GATEWAY;
    default:
      return errorMessages.DEFAULT;
  }
};

export const getErrorMessages = (resource = "resource") => ({
  BAD_REQUEST: "Input data is invalid. Please adjust it and try again.",
  FORBIDDEN: "You don't have permission to perform this action.",
  NOT_FOUND: `Requested ${resource} does not exist.`,
  METHOD_NOT_ALLOWED:
    "Invalid request sent. Please contact support@session.id for assistance.",
  CONFLICT: `Resource/data (${resource}) conflict.`,
  TOO_MANY_CALLS: "Too many attempts. Please wait a while and try again.",
  SERVER_ERROR: "Unexpected server error. Please try again.",
  BAD_GATEWAY: "Unexpected server error. Please try again later.",
  DEFAULT: "An unexpected error has occurred. Please try again later.",
});
