import React from "react";
import { PageNoContent } from "../../molecules/PageNoContent";

interface PageEmptyStateEmployeesProps {
  primaryText?: string;
  withSecondaryText?: boolean;
}

export const PageEmptyStateEmployees: React.FC<PageEmptyStateEmployeesProps> = ({
  primaryText = "No Employees",
  withSecondaryText = true,
}) => {
  return (
    <PageNoContent
      primaryText={primaryText}
      secondaryText={
        withSecondaryText
          ? "Click the + Invite button to invite a user"
          : undefined
      }
      iconSrc={`${process.env.PUBLIC_URL}/static/media/svg/person.svg`}
    />
  );
};
