import React, {
  MouseEvent,
  ReactElement,
  useEffect,
  useRef,
  useState,
} from "react";
import {
  Box,
  CircularProgress,
  createStyles,
  Grid,
  List,
  ListItem,
  makeStyles,
  Paper,
  PopperPlacementType,
  Theme,
} from "@material-ui/core";
import debounce from "lodash/debounce";
import axios from "axios";
import { LoadingState } from "../../../types/enums";
import clsx from "clsx";
import { InputSearch } from "../../atoms/InputSearch";
import { SearchBoxPopper } from "../../atoms/SearchBoxPopper";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    rootContainer: {
      maxWidth: 283,
      minWidth: 283,
      marginBottom: theme.spacing(1),
    },
    listItem: {
      paddingTop: theme.spacing(0.5),
      paddingBottom: theme.spacing(0.5),
    },
    list: {
      maxHeight: 286,
      overflowY: "scroll",
    },
  })
);

interface SearchBoxProps {
  apiSource: string;
  openComponent?: ReactElement;
  renderOption: (option: any) => React.ReactNode;
  optionsSelector: (apiResponse: any) => any[];
  onSelect: (option: any) => void;
  filterOptions?: (options: any[]) => any[];
  className?: string;
  placeholder?: string;
  onChange?: (value: string) => void;
  parentAnchor?: HTMLElement | null;
  onClose?: () => void;
  customContent?: ReactElement;
  resultsHeader?: ReactElement;
  noSearchResultsContent?: ReactElement;
  disabled?: boolean;
  upperContent?: ReactElement;
  popperPlacement?: PopperPlacementType;
  popperModifiers?: object;
}

export const SearchBox: React.FC<SearchBoxProps> = ({
  openComponent,
  renderOption,
  apiSource,
  optionsSelector,
  onSelect,
  className,
  placeholder,
  filterOptions = (el: any[]) => el,
  onChange,
  parentAnchor,
  onClose,
  customContent,
  resultsHeader,
  disabled,
  noSearchResultsContent,
  upperContent,
  popperPlacement,
  popperModifiers,
}) => {
  const classes = useStyles();
  const componentIsMounted = useRef(true);
  const inputRef = useRef<HTMLInputElement>();
  const [anchor, setAnchor] = useState<HTMLElement | null>(null);
  const [apiResponse, setApiResponse] = useState<any>();
  const [loadingState, setLoadingState] = useState(LoadingState.PRISTINE);
  const handleClose = () => {
    onClose && onClose();
    setAnchor(null);
    setApiResponse(null);
    setLoadingState(LoadingState.PRISTINE);
  };
  const handleOpen = (event: MouseEvent<HTMLElement>) => {
    !disabled && setAnchor(event.currentTarget);
  };

  const handleApiSearch = async (value: string) => {
    if (value.length > 2) {
      onChange && onChange(value);
      const url = apiSource.replace("{query}", value);
      setLoadingState(LoadingState.FETCHING);
      try {
        const response = await axios.get(url, { withCredentials: true });
        if (componentIsMounted.current) {
          setLoadingState(LoadingState.FETCHED);
          setApiResponse(response.data);
        }
      } catch (e) {
        if (componentIsMounted.current) {
          setLoadingState(LoadingState.ERROR);
          setApiResponse(null);
        }
      }
    } else {
      setLoadingState(LoadingState.PRISTINE);
      setApiResponse(null);
    }
  };

  const debouncedHandleApiSearch = debounce(handleApiSearch, 500);

  const handleOnSelect = (option: any) => () => {
    onSelect(option);
    handleClose();
  };

  useEffect(() => {
    handleApiSearch(inputRef.current?.value || "");
    inputRef.current?.focus();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [apiSource]);

  const options = apiResponse
    ? filterOptions(optionsSelector(apiResponse))
    : [];

  useEffect(() => {
    return () => {
      componentIsMounted.current = false;
    };
  }, []);

  useEffect(() => {
    if ((parentAnchor || anchor) && inputRef) {
      inputRef.current?.focus();
    }
  }, [anchor, parentAnchor, inputRef]);

  return (
    <React.Fragment>
      {openComponent &&
        React.cloneElement(openComponent, { onClick: handleOpen })}
      <SearchBoxPopper
        anchor={parentAnchor || anchor}
        onClose={handleClose}
        popperPlacement={popperPlacement}
        popperModifiers={popperModifiers}
      >
        <Paper elevation={4} className={clsx(classes.rootContainer, className)}>
          {upperContent}
          <Grid container>
            <Grid item xs={12}>
              <Box p={2}>
                <InputSearch
                  align="right"
                  placeholder={placeholder}
                  onChange={debouncedHandleApiSearch}
                  inputRef={inputRef}
                  autofocus
                  fullWidth
                />
              </Box>
            </Grid>
            {resultsHeader &&
            loadingState === LoadingState.FETCHED &&
            options.length > 0 ? (
              <Grid item xs={12}>
                {resultsHeader}
              </Grid>
            ) : null}
            <Grid item xs={12}>
              {customContent &&
              loadingState === LoadingState.PRISTINE &&
              options.length === 0
                ? React.cloneElement(customContent, { onClick: handleClose })
                : null}
              {loadingState === LoadingState.FETCHING && (
                <Box
                  display="flex"
                  justifyContent="center"
                  pb={2}
                  pt={2}
                  width={280}
                >
                  <CircularProgress size={40} />
                </Box>
              )}
              {loadingState === LoadingState.FETCHED && apiResponse && (
                <React.Fragment>
                  {options.length > 0 ? (
                    <List className={classes.list}>
                      {options.slice(0, 20).map((el, index) => (
                        <ListItem
                          data-testid={`list-item-${index}`}
                          key={index}
                          button
                          className={classes.listItem}
                          onClick={handleOnSelect(el)}
                        >
                          {renderOption(el)}
                        </ListItem>
                      ))}
                    </List>
                  ) : (
                    <React.Fragment>
                      {noSearchResultsContent
                        ? React.cloneElement(noSearchResultsContent, {
                            onClick: handleClose,
                          })
                        : null}
                    </React.Fragment>
                  )}
                </React.Fragment>
              )}
            </Grid>
          </Grid>
        </Paper>
      </SearchBoxPopper>
    </React.Fragment>
  );
};
