import { Box, createStyles, makeStyles, Theme, Typography } from "@material-ui/core";
import React from "react";
import { AgreementMember } from "../../../utils/models";
import { IdentityTypeLabel } from "../../../types/enums";
import { TooltipMap } from "../../atoms/TooltipMap";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      overflow: "hidden",
      marginRight: theme.spacing(1),
    },
    primaryText: {
      lineHeight: 1,
      overflow: "hidden",
      textOverflow: "ellipsis",
      WebkitLineClamp: 2,
      display: "-webkit-box",
      WebkitBoxOrient: "vertical",
    },
    cisacCodeText: {
      lineHeight: 1,
      overflow: "hidden",
      textOverflow: "ellipsis",
      WebkitLineClamp: 1,
      display: "-webkit-box",
      WebkitBoxOrient: "vertical",
    },
    secondaryText: {
      paddingTop: theme.spacing(0.5),
      maxWidth: 125,
      display: "inline-flex",
      alignItems: "center",
    },
  })
);

interface CardAgreementMemberProps {
  member: AgreementMember;
}

export const CardAgreementMember = ({
  member: { displayName, type, prSocietyAffiliation },
}: CardAgreementMemberProps) => {
  const classes = useStyles();
  return (
    <Box className={classes.root} justifyContent="center">
      <Box display="flex" flexWrap="noWrap" alignItems="center">
        <Typography variant="body1" className={classes.primaryText}>
          {displayName}
        </Typography>
        {prSocietyAffiliation && (
          <TooltipMap to="Cisac">
            <Typography
              variant="body2"
              color="textSecondary"
              className={classes.cisacCodeText}
            >
              &nbsp;({prSocietyAffiliation})
            </Typography>
          </TooltipMap>
        )}
      </Box>
      <Typography
        variant="body2"
        color="textSecondary"
        noWrap
        className={classes.secondaryText}
        data-testid="member-type"
      >
        {IdentityTypeLabel[type]}
      </Typography>
    </Box>
  );
};
