import { ThunkAction, ThunkDispatch } from "redux-thunk";
import { AppState } from "../../../App.reducer";
import { Action } from "redux";
import {
  deleteAgreementAction,
  deleteAgreementFailureAction,
  deleteAgreementSuccessAction,
  fetchAgreementAction,
  fetchAgreementFailureAction,
  fetchAgreementsAction,
  fetchAgreementsFailureAction,
  fetchAgreementsSuccessAction,
  fetchAgreementSuccessAction,
  patchAgreementAction,
  patchAgreementFailureAction,
  patchAgreementSuccessAction,
  postAgreementAction,
  postAgreementDuplicateAction,
  postAgreementDuplicateSuccessAction,
  postAgreementFailureAction,
  postAgreementSuccessAction,
  registerAgreementAction,
  registerAgreementFailureAction,
  registerAgreementSuccessAction,
} from "./actions";
import axios from "axios";
import {
  FetchAgreementResponse,
  FetchAgreementsResponse,
} from "../../../api.types";
import {
  Agreement,
  AgreementListItem,
  AgreementPatch,
  AgreementPost,
} from "../../../utils/models";
import { ApiErrorCodeMessages } from "../../../types/enums";
import { Href } from "../../../utils/models";
import {
  convertTermSharesToInt,
  getAgreementLink,
  getFormattedAgreement,
  replaceUsaLicenseIndicatorNONE,
} from "./effects.utils";
import {
  getAgreementLinksSelector,
  getAgreementsLinksSelector,
  getCreateAgreementLinkSelector,
} from "./selectors";
import Promise from "promise-polyfill";
import { handleError } from "../../../utils/errorHandler";
import { addAlertAction } from "../Alerts/actions";
import { AlertType } from "../Alerts/actions.types";

import { toasts } from "../../../utils/toasts";
import { buildAgreementQueryUrl } from "../../../utils/helpers";
import { fetchAgreementsResponse } from "../../../__mocks__/constants";

export const fetchAgreementsEffect =
  (data?: {
    next?: boolean;
    query?: string;
  }): ThunkAction<
    Promise<void | FetchAgreementsResponse>,
    AppState,
    void,
    Action<string>
  > =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    const { next, query } = data || {};
    dispatch(fetchAgreementsAction(next));

    const state = getState();
    const link = state.signIn._links?.agreements?.href;
    const linkNext = state.agreements._links?.next?.href;
    const url =
      next && linkNext
        ? linkNext
        : buildAgreementQueryUrl({ url: link || "", query });

    try {
      // const response = await axios.request<FetchAgreementsResponse>({
      //   url,
      //   method: "GET",
      //   withCredentials: true,
      // });
      const { items, _links, meta } =
        fetchAgreementsResponse.response.agreement;
      dispatch(
        fetchAgreementsSuccessAction({
          agreements: items,
          _links,
          meta,
          next: next && Boolean(linkNext),
        })
      );
      return fetchAgreementsResponse;
    } catch (e) {
      const error = handleError(e.response, "agreements");
      dispatch(fetchAgreementsFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.agreement.GET_LIST} ${error.message}`
        )
      );
      throw e;
    }
  };

export const fetchAgreementEffect =
  (
    guid: string
  ): ThunkAction<Promise<Agreement>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchAgreementAction());
    const link = getAgreementsLinksSelector(getState())?.agreement;
    try {
      const response = await axios.request<FetchAgreementResponse>({
        url: link?.href.replace("{guid}", guid),
        method: link?.method,
        withCredentials: true,
      });
      const { item } = response.data.response.agreement;
      dispatch(fetchAgreementSuccessAction(getFormattedAgreement(item)));
      return item;
    } catch (e) {
      dispatch(fetchAgreementFailureAction());
      const error = handleError(e.response, "agreement");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.agreement.GET_SINGLE} ${error.message}`
        )
      );
      throw error;
    }
  };

export const postAgreementEffect =
  (
    data: AgreementPost
  ): ThunkAction<Promise<Agreement>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(postAgreementAction());
    const link = getCreateAgreementLinkSelector(getState());
    try {
      const response = await axios.request<FetchAgreementResponse>({
        url: link?.href,
        method: link?.method,
        data,
        withCredentials: true,
      });
      const { item } = response.data.response.agreement;
      dispatch(postAgreementSuccessAction(item));
      return item;
    } catch (e) {
      const error = handleError(e.response, "agreement");
      dispatch(postAgreementFailureAction());
      throw error;
    }
  };

export const patchAgreementEffect =
  (data: {
    guid: string;
    patch: AgreementPatch;
  }): ThunkAction<Promise<Agreement>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(patchAgreementAction());

    const { usaLicenseIndicator } = data.patch;
    const link = await getAgreementLink(
      "update",
      data.guid,
      dispatch,
      getState
    );

    const dirty: AgreementPatch = {
      ...data.patch,
      usaLicenseIndicator: replaceUsaLicenseIndicatorNONE(
        usaLicenseIndicator,
        null
      ),
      terms: data.patch.terms
        ? convertTermSharesToInt(data.patch.terms)
        : undefined,
    };

    try {
      const response = await axios.request<FetchAgreementResponse>({
        url: link.href,
        method: link.method,
        data: dirty,
        withCredentials: true,
      });
      const { item } = response.data.response.agreement;
      const formattedAgreement = getFormattedAgreement(item);
      dispatch(patchAgreementSuccessAction(formattedAgreement));
      return formattedAgreement;
    } catch (e) {
      const error = handleError(e.response, "agreement");
      dispatch(patchAgreementFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.agreement.SAVE} ${error.message}`
        )
      );
      throw error;
    }
  };

export const registerAgreementEffect =
  (guid: string): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(registerAgreementAction());
    try {
      const link = await getAgreementLink("submit", guid, dispatch, getState);
      try {
        await axios.request({
          url: link.href,
          method: link.method,
          withCredentials: true,
        });
        dispatch(registerAgreementSuccessAction());
      } catch (e) {
        const error = handleError(e.response, "agreement");
        throw error;
      }
    } catch (e) {
      dispatch(registerAgreementFailureAction());
      const state = getState();
      if (!getAgreementLinksSelector(state)?.submit) {
        const addPartyLink = getAgreementLinksSelector(state)?.addParty;
        if (addPartyLink) {
          throw ApiErrorCodeMessages.MISSING_PARTY;
        }
        throw e;
      }
    }
  };

export const deleteAgreementEffect =
  (
    guid: string,
    link: Href
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(deleteAgreementAction());
    try {
      await axios.request({
        url: link.href.replace("{guid}", guid),
        method: link.method,
        withCredentials: true,
      });
      dispatch(deleteAgreementSuccessAction(guid));
    } catch (e) {
      const error = handleError(e.response, "agreement");
      dispatch(deleteAgreementFailureAction());
      throw error;
    }
  };

export const postAgreementDuplicateEffect =
  (
    agreementGUid: string
  ): ThunkAction<Promise<void | string>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    //TODO:: implement backend when available
    dispatch(postAgreementDuplicateAction());

    return new Promise((resolve) => {
      const agreementToDuplicate = getState().agreements.list.find(
        (el) => el.guid === agreementGUid
      ) as AgreementListItem;
      const response: AgreementListItem = {
        ...agreementToDuplicate,
        guid: `copy-${agreementToDuplicate.guid}`,
        title: `Copy Of ${agreementToDuplicate.title}`,
      };
      dispatch(postAgreementDuplicateSuccessAction(response));
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.agreement.DUPLICATED)
      );
      setTimeout(() => {
        resolve(response.guid);
      }, 1000);
    });
  };
