import React from "react";
import { createStyles, makeStyles, Theme, Typography } from "@material-ui/core";
import { ColumnBreakpoints, WorkCreator } from "../../../utils/models";
import { useFormikContext } from "formik";
import clsx from "clsx";
import { RowCreatorListItem } from "../RowCreatorListItem";
import {
  CellPrShare,
  prShareCellDenseWidth,
  prShareCellWidth,
} from "../../atoms/CellPrShare";
import {
  CellMrShare,
  mrShareCellDenseWidth,
  mrShareCellWidth,
} from "../../atoms/CellMrShare";
import { WorkFormValues } from "../../organisms/FormWorkRegistration";
import { CellAvatar } from "../../atoms/CellAvatar";
import { CellCreator } from "../../atoms/CellCreator";
import { CellRole } from "../../atoms/CellRole";
import { CellCollaboration } from "../../atoms/CellCollaboration";
import { CellSplit } from "../../atoms/CellSplit";
import { CellOptions } from "../../atoms/CellOptions";
import { getSplitShareSum } from "./utils";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    sumValue: {
      paddingLeft: theme.spacing(2),
    },
    summaryRow: {
      alignItems: "center",
    },
    bottomLine: {
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
    bottomLineError: {
      borderBottom: `2px solid ${theme.palette.error.main}`,
    },
    cellHeight: {
      height: 52,
    },
    error: {
      color: theme.palette.error.main,
    },
    shareError: {
      minWidth: mrShareCellWidth + prShareCellWidth,
    },
    shareErrorDense: {
      minWidth: mrShareCellDenseWidth + prShareCellDenseWidth,
    },
  })
);

interface RowGridCreatorTotalProps {
  creators: WorkCreator[];
  hideSplitColumn?: boolean;
  hideOptionsColumn?: boolean;
  hideCollaborationColumn?: boolean;
  columnBreakPoints: ColumnBreakpoints;
}

export const RowGridCreatorTotal: React.FC<RowGridCreatorTotalProps> = ({
  hideSplitColumn,
  hideOptionsColumn,
  hideCollaborationColumn,
  creators,
  columnBreakPoints: bp,
}) => {
  const classes = useStyles();

  const {
    errors: { mechanicalSplitSum, performanceSplitSum },
  } = useFormikContext<WorkFormValues>();

  const mrShareError = mechanicalSplitSum;
  const prShareError = performanceSplitSum;

  const mechanicalSum = getSplitShareSum(creators, "mrShare");
  const performanceSum = getSplitShareSum(creators, "prShare");
  return (
    <React.Fragment>
      <RowCreatorListItem className={classes.summaryRow}>
        <CellAvatar />
        <CellCreator dense={bp.creator} />
        <CellRole
          dense={bp.role}
          display="flex"
          justifyContent="flex-start"
          alignSelf="start"
        >
          <Typography variant="body2" color="textSecondary">
            Total
          </Typography>
        </CellRole>
        <CellPrShare
          dense={bp.prShare}
          className={clsx({
            [classes.cellHeight]: true,
            [classes.bottomLine]: !prShareError && !mrShareError,
            [classes.bottomLineError]: prShareError,
          })}
        >
          <Typography
            variant="subtitle1"
            className={clsx(classes.sumValue, {
              [classes.error]: mrShareError || performanceSum > 100,
            })}
          >
            {performanceSum}%
          </Typography>
        </CellPrShare>
        <CellMrShare
          dense={bp.mrShare}
          className={clsx({
            [classes.cellHeight]: true,
            [classes.bottomLine]: !prShareError && !mrShareError,
            [classes.bottomLineError]: mrShareError,
          })}
        >
          <Typography
            variant="subtitle1"
            className={clsx(classes.sumValue, {
              [classes.error]: prShareError || mechanicalSum > 100,
            })}
          >
            {mechanicalSum}%
          </Typography>
        </CellMrShare>

        {!(hideSplitColumn || bp.split) && (
          <CellSplit
            className={clsx(classes.cellHeight, {
              [classes.bottomLine]: !(prShareError || mrShareError),
            })}
          />
        )}
        {!(hideCollaborationColumn || bp.collaboration) && (
          <CellCollaboration
            className={clsx(classes.cellHeight, {
              [classes.bottomLine]: !(prShareError || mrShareError),
            })}
          />
        )}
        {!hideOptionsColumn && (
          <CellOptions
            className={clsx(classes.cellHeight, {
              [classes.bottomLine]: bp.split && !(prShareError || mrShareError),
            })}
          />
        )}
      </RowCreatorListItem>

      {(mrShareError || prShareError) && (
        <RowCreatorListItem noPadding>
          <CellAvatar />
          <CellCreator dense={bp.creator} />
          <CellRole dense={bp.role} />
          {/* Using these divs to correctly scroll & focus error message. 
              Not really a better place to add names for such case. */}
          <div {...({ name: "performanceSplitSum" } as any)} />
          <div {...({ name: "mechanicalSplitSum" } as any)} />
          <Typography
            className={clsx({
              [classes.shareError]: !bp.prShare && !bp.mrShare,
              [classes.shareErrorDense]: bp.prShare && bp.mrShare,
            })}
            variant="caption"
            color="error"
          >
            The sum of all
            {mrShareError && !prShareError && " MR "}
            {!mrShareError && prShareError && " PR "}
            {mrShareError && prShareError && " MR / PR "}
            {mrShareError || prShareError}
          </Typography>

          {!(hideSplitColumn || bp.split) && <CellSplit />}
          {!(hideCollaborationColumn || bp.collaboration) && (
            <CellCollaboration />
          )}
          {!hideOptionsColumn && <CellOptions />}
        </RowCreatorListItem>
      )}
    </React.Fragment>
  );
};
