import {
  createStyles,
  makeStyles,
  Theme as MaterialUiTheme,
  Typography,
  Breadcrumbs,
  Link,
} from "@material-ui/core";
import React from "react";
import { AppDispatch } from "../../../App.reducer";
import {
  FaqAccess,
  getFaqObjectByPath,
  getNormalizedFaqObject,
  getFullPathByParents,
} from "../FaqDrawer/utils";
import { setFaqPathAction, resetItemAction } from "../../pages/Faq/actions";

const useStyles = makeStyles((theme: MaterialUiTheme) =>
  createStyles({
    breadCrumbLink: {
      color: theme.palette.text.secondary,
      cursor: "pointer",
    },
    breadcrumbText: {
      display: "flex",
      alignItems: "center",
      height: theme.spacing(3),
    },
    indicatedBreadcrumb: {
      paddingBottom: theme.spacing(2.25),
      "&:last-child": {
        borderBottom: `2px solid ${theme.palette.primary.main}`,
        paddingBottom: theme.spacing(2),
        "& h6": {
          color: theme.palette.text.primary,
        },
      },
    },
    separator: {
      paddingBottom: theme.spacing(2.25),
    },
  })
);

interface BreadcrumbsFaqProps {
  dispatch: AppDispatch;
  path?: string;
  access: FaqAccess;
}

export const BreadcrumbsFaq: React.FC<BreadcrumbsFaqProps> = ({
  dispatch,
  path,
  access,
}) => {
  const classes = useStyles();
  const separatedPaths = path ? path.split(".") : [];

  const faqObjectFull = React.useMemo(
    () => getFaqObjectByPath(access, "home"),
    [access]
  );

  const normalizedObject = React.useMemo(
    () => getNormalizedFaqObject(faqObjectFull),
    [faqObjectFull]
  );

  const handleLinkClick = (destinationPath: string) => () => {
    const fullPath = getFullPathByParents(normalizedObject, destinationPath);
    if (path !== destinationPath) {
      dispatch(setFaqPathAction(fullPath));
      dispatch(resetItemAction());
    }
  };
  return (
    <Breadcrumbs
      separator=">"
      classes={{
        li: classes.indicatedBreadcrumb,
        separator: classes.separator,
      }}
    >
      {separatedPaths.map((path) => {
        const faqObject = normalizedObject[path];
        if (faqObject) {
          return (
            <Link
              className={classes.breadCrumbLink}
              key={path}
              onClick={handleLinkClick(path)}
            >
              <Typography
                variant="subtitle2"
                color="textSecondary"
                className={classes.breadcrumbText}
              >
                {faqObject.title}
              </Typography>
            </Link>
          );
        }
        return null;
      })}
    </Breadcrumbs>
  );
};
