import React from "react";
import {
  CircularProgress,
  createStyles,
  makeStyles,
  Theme,
} from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    circularProgress: {
      margin: 4,
      position: "absolute",
      top: "50%",
      left: "50%",
      marginTop: -12,
      marginLeft: -12,
    },
  })
);

interface ButtonCircularProgressProps {
  variant?: string;
  loaderColor?: "dark";
  size?: number;
}

export const ButtonCircularProgress: React.FC<ButtonCircularProgressProps> = ({
  loaderColor,
  variant,
  size = 24,
}) => {
  const classes = useStyles();
  return (
    <CircularProgress
      className={classes.circularProgress}
      size={size}
      color="primary"
    />
  );
};
