import React, { useEffect, useState } from "react";
import { FormControl, MenuItem, Select } from "@material-ui/core";

interface MenuSelectProps {
  options: Array<{ value: string; label: string }>;
  value: string;
  interceptOnChange?: (value: string) => void;
}

export const MenuSelect: React.FC<MenuSelectProps> = ({
  children,
  options,
  value,
  interceptOnChange,
  ...props
}) => {
  const [state, setState] = useState(value);

  const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    const value = event.target.value as string;
    if (interceptOnChange) {
      interceptOnChange(value);
    } else {
      setState(value);
    }
  };

  useEffect(() => {
    setState(value);
  }, [value]);

  return (
    <FormControl>
      <Select {...props} value={state} onChange={handleChange} disableUnderline>
        {options.map((el) => (
          <MenuItem key={el.value} value={el.value} data-testid="option">
            {el.label}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};
