import React, { RefObject, useState } from "react";
import {
  Box,
  IconButton,
  makeStyles,
  Table,
  TableBody,
  TableContainer,
  TableHead,
  TableRow,
  Theme,
} from "@material-ui/core";
import MuiTableCell from "@material-ui/core/TableCell";
import { createStyles, withStyles } from "@material-ui/core/styles";
import { FastField } from "formik";
import { AgreementTerm } from "../../../utils/models";
import clsx from "clsx";
import { getDialogAlertCacheKey } from "../../molecules/DialogRemoveAgreementTerm/utils";
import { AgreementFormPermissions } from "../FormAgreement/utils";
import { FormikTableTerritorySelect } from "../FormikTableTerritorySelect";
import { DialogRemoveAgreementTerm } from "../../molecules/DialogRemoveAgreementTerm";
import { TableAgreementShares } from "../TableAgreementShares";
import { ActionMenu } from "../ActionMenu";
import { FormikTableDateSelect } from "../FormikTableDateSelect";
import { FormikDateSwitch } from "../FormikDateSwitch";
import { TooltipMap } from "../../atoms/TooltipMap";
import { Icon } from "../../atoms/Icon";
import { TermToggleIcon } from "../../molecules/TermToggleIcon";

const useStyles = makeStyles((theme: Theme) => {
  const borderStyle = `1px solid ${theme.palette.custom.state.dragged}`;

  return createStyles({
    tableRow: {
      border: borderStyle,
      borderRadius: 4,
    },
    table: {
      borderCollapse: "unset",
      paddingTop: theme.spacing(1),
    },
    territoriesCell: {
      minWidth: 170,
      width: "100%",
      paddingLeft: theme.spacing(2),
      borderLeft: borderStyle,
      borderTopLeftRadius: 8,
      borderBottomLeftRadius: 0,
    },
    startDateCell: {
      minWidth: 225,
    },
    priorRoyaltiesCell: {
      minWidth: 200,
    },
    terminationCell: {
      minWidth: 180,
    },
    chevronCell: {
      minWidth: 55,
      maxWidth: 55,
      borderRight: borderStyle,
      borderTopRightRadius: 8,
      borderBottomRightRadius: 0,
      padding: 0,
      paddingRight: 2,
    },
    rightType: {
      fontSize: theme.typography.body2.fontSize,
    },
    switchPadding: {
      paddingLeft: theme.spacing(1),
    },
    emptyCell: {
      border: "none",
      padding: theme.spacing(1),
    },
    territoriesHeaderCell: {
      paddingLeft: theme.spacing(2),
    },
    tableWrapper: {
      minWidth: 676,
    },
  });
});

const TableHeaderCell = withStyles((theme) => ({
  root: {
    ...theme.typography.body2,
    color: theme.palette.text.secondary,
    border: "none",
    paddingBottom: theme.spacing(1),
  },
}))(MuiTableCell);

const TableCell = withStyles((theme) => {
  const borderStyle = `1px solid ${theme.palette.custom.state.dragged}`;
  return {
    root: {
      borderTop: borderStyle,
      borderBottom: borderStyle,
      paddingTop: theme.spacing(1),
      paddingBottom: theme.spacing(1),
    },
  };
})(MuiTableCell);

interface TableAgreementTermsProps {
  showHeader: boolean;
  parent: string;
  term: AgreementTerm;
  onDelete: () => void;
  permissions: AgreementFormPermissions;
  profileRef: string;
  termsLength: number;
  termRef?: RefObject<HTMLDivElement | undefined>;
}

export const TableAgreementTerms: React.FC<TableAgreementTermsProps> = ({
  showHeader,
  parent,
  term,
  onDelete,
  permissions,
  profileRef,
  termsLength,
  termRef,
}) => {
  const classes = useStyles();
  const [showShares, setShowShares] = useState(true);
  const [showConfirm, setShowConfirm] = useState(false);
  const handleRemove = () => {
    const hideAlert =
      localStorage.getItem(getDialogAlertCacheKey(profileRef)) === "true";
    if (hideAlert) {
      setShowConfirm(false);
      onDelete();
    } else {
      setShowConfirm(true);
    }
  };

  return (
    <React.Fragment>
      {showConfirm && (
        <DialogRemoveAgreementTerm
          onConfirm={onDelete}
          closeDialog={() => setShowConfirm(false)}
          profileRef={profileRef}
        />
      )}
      <Box
        display="inline-flex"
        width={1}
        overflow="auto"
        //@ts-ignore. Solved in material v5 https://github.com/mui-org/material-ui/issues/17010
        ref={termRef}
      >
        <TableContainer className={classes.tableWrapper}>
          <Table className={classes.table}>
            {showHeader && (
              <TableHead>
                <TableRow>
                  <TableHeaderCell
                    align="left"
                    className={clsx(
                      classes.territoriesCell,
                      classes.territoriesHeaderCell
                    )}
                  >
                    <TooltipMap
                      to="AgreementTerritories"
                      placement="bottom-start"
                    >
                      Territories
                    </TooltipMap>
                  </TableHeaderCell>
                  <TableHeaderCell
                    align="left"
                    className={classes.startDateCell}
                  >
                    <TooltipMap to="AgreementStartDate">Start Date</TooltipMap>
                  </TableHeaderCell>
                  <TableHeaderCell
                    align="left"
                    className={classes.priorRoyaltiesCell}
                  >
                    <TooltipMap to="AgreementPriorRoyalties">
                      Prior Royalties
                    </TooltipMap>
                  </TableHeaderCell>
                  <TableHeaderCell
                    align="left"
                    className={classes.terminationCell}
                  >
                    <TooltipMap to="AgreementTermination">
                      Expected Termination
                    </TooltipMap>
                  </TableHeaderCell>
                  <TableHeaderCell
                    align="center"
                    className={classes.chevronCell}
                  />
                </TableRow>
              </TableHead>
            )}
            <TableBody>
              <TableRow>
                <TableCell align="left" className={classes.territoriesCell}>
                  <FastField
                    component={FormikTableTerritorySelect}
                    disabled={!permissions.updateTerm}
                    name={`${parent}.territories`}
                  />
                </TableCell>
                <TableCell align="left" className={classes.startDateCell}>
                  <FastField
                    component={FormikTableDateSelect}
                    disabled={!permissions.updateTerm}
                    name={`${parent}.startDate`}
                  />
                </TableCell>
                <TableCell
                  align="left"
                  className={clsx(
                    classes.priorRoyaltiesCell,
                    classes.switchPadding
                  )}
                >
                  <TooltipMap to="AgreementPriorRoyaltiesDate">
                    <FastField
                      component={FormikDateSwitch}
                      disabled={!permissions.updateTerm}
                      name={`${parent}.priorRoyalties`}
                    />
                  </TooltipMap>
                </TableCell>
                <TableCell
                  align="left"
                  className={clsx(
                    classes.terminationCell,
                    classes.switchPadding
                  )}
                >
                  <FastField
                    component={FormikDateSwitch}
                    disabled={!permissions.updateTerm}
                    name={`${parent}.endDate`}
                    indefinite
                  />
                </TableCell>
                <TableCell align="right" className={classes.chevronCell}>
                  <TermToggleIcon
                    show={showShares}
                    onClick={() => setShowShares(!showShares)}
                  />
                </TableCell>
              </TableRow>
              {!showShares && (
                <TableRow>
                  <MuiTableCell className={classes.emptyCell} />
                </TableRow>
              )}
            </TableBody>
          </Table>
          {showShares && (
            <TableAgreementShares
              parent={`${parent}.shares`}
              shares={term.shares || []}
              permissions={permissions}
              profileRef={profileRef}
            />
          )}
        </TableContainer>
        <Box pt={showHeader ? 7.25 : 0.25} pl={1}>
          {termsLength > 1 ? (
            <ActionMenu
              items={[
                {
                  label: "Remove",
                  onClick: handleRemove,
                  disabled: !permissions.deleteTerm,
                },
              ]}
            >
              <IconButton>
                <Icon name="listItemOptions" />
              </IconButton>
            </ActionMenu>
          ) : (
            <Box pl={6} />
          )}
        </Box>
      </Box>
    </React.Fragment>
  );
};
