import React from "react";
import { FieldProps, getIn, insert } from "formik";
import { createStyles, makeStyles } from "@material-ui/core";

import { Copy24 } from "@carbon/icons-react";
import { Theme } from "@material-ui/core/styles";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    root: {
      color: theme.palette.primary.main,
      "&:hover": {
        cursor: "pointer",
      },
    },
  })
);

interface FormikCopyShareProps {
  index: number;
}

export const FormikCopyShare: React.FC<FormikCopyShareProps & FieldProps> = ({
  field: { name },
  form: { setFieldValue, values, isSubmitting },
  index,
}) => {
  const classes = useStyles();
  const handleCopy = () => {
    if (isSubmitting) return;
    const copy = getIn(values, `${name}.${index}`);
    const shares = getIn(values, name);
    const newState = insert(shares, index, copy);
    setFieldValue(name, newState);
  };

  return (
    <Copy24
      data-testid="copy-share"
      onClick={handleCopy}
      className={classes.root}
    />
  );
};
