export const fetchCountriesEffect =
  (): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchCountriesAction());

    try {
      // const response = await axios.request<FetchCountriesResponse>({
      //   url: `${apiUrls.base}/companies/countries`,
      //   method: "GET",
      //   withCredentials: true,
      // });
      dispatch(
        fetchCountriesSuccessAction(
          fetchCountriesResponseMock.response.country.items
        )
      );
    } catch (e) {
      const error = handleError(e.response, "countries");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.GET_COUNTRIES} ${error.message}`
        )
      );
      throw e;
    }
  };

