export const postCompanyInviteEffect =
  (
    targetEmailAddress: string
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(postCompanyInviteAction());
    const link = getInviteEmployeeLinkSelector(getState());
    try {
      await axios.request({
        url: link?.href,
        data: { targetEmailAddress },
        method: link?.method,
        withCredentials: true,
      });
      dispatch(postCompanyInviteSuccessAction());
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.company.INVITE_SENT)
      );
    } catch (e) {
      const error = handleError(e.response, "invite");
      dispatch(postCompanyInviteFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.SEND_INVITE} ${error.message}`
        )
      );
      throw e;
    }
  };

