export const deleteInviteEffect =
  (
    inviteToken: string
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(deleteCompanyInviteAction());
    const href = getDeleteInviteLinkSelector(getState());
    const url = href?.href.replace("{inviteToken}", inviteToken);
    try {
      await axios.request<FetchCompanyInvitesResponse>({
        url: url,
        method: href?.method,
        withCredentials: true,
      });
      dispatch(deleteCompanyInviteSuccessAction(inviteToken));
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.company.INVITE_DELETE)
      );
    } catch (e) {
      const error = handleError(e.response, "invite");
      dispatch(deleteCompanyInviteFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.DELETE_INVITE} ${error.message}`
        )
      );
      throw e;
    }
  };

