export const patchEmployeeEffect =
  ({
    employeeGuid,
    group,
  }: {
    employeeGuid: string;
    group: SetGroupAction[];
  }): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(patchEmployeeAction());
    const link = getPatchEmployeeGroupLinkSelector(getState());

    try {
      const response = await axios.request<PatchEmployeeResponse>({
        url: link?.href.replace("{userGuid}", employeeGuid),
        data: {
          operations: group,
        },
        method: link?.method,
        withCredentials: true,
      });

      dispatch(
        patchEmployeeSuccessAction(response.data.response.employee.item)
      );
    } catch (e) {
      const error = handleError(e.response, "employee");
      dispatch(patchEmployeeFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.UPDATE_EMPLOYEE} ${error.message}`
        )
      );
      throw e;
    }
  };

export const fetchCompanyDetailsEffect =
  (): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchCompanyDetailsAction());
    const link = getCompanyLinkSelector(getState());

    try {
      // const response = await axios.request<FetchCompanyDetailsResponse>({
      //   url: link?.href,
      //   method: link?.method,
      //   withCredentials: true,
      // });
      dispatch(
        fetchCompanyDetailsSuccessAction(
          fetchCompanyDetailsResponse.response.company.item
        )
      );
    } catch (e) {
      dispatch(fetchCompanyDetailsFailureAction());
      const error = handleError(e.response, "company");
      dispatch(patchEmployeeFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.GET_COMPANY} ${error.message}`
        )
      );
      throw e;
    }
  };

export const patchCompanyDetailsEffect =
  (
    data: AddressPatch
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(patchCompanyDetailsAction());
    const link = getUpdateCompanyDetailsLinkSelector(getState());
    try {
      await axios.request({
        url: link?.href,
        method: link?.method,
        data,
        withCredentials: true,
      });
      dispatch(patchCompanyDetailsSuccessAction());
      dispatch(
        addAlertAction(
          AlertType.SUCCESS,
          toasts.success.auth.UPDATE_PUBLISHER_PROFILE
        )
      );
    } catch (e) {
      dispatch(patchCompanyDetailsFailureAction());
      const error = handleError(e.response, "company");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.auth.UPDATE_PUBLISHER_PROFILE} ${error.message}`
        )
      );
    }
  };
