import React from "react";
import { createStyles, makeStyles } from "@material-ui/core";
import { Field } from "formik";
import { BootstrapAccount, BootstrapCompany } from "../../../utils/models";
import { RowCreatorLeftContent } from "../../atoms/RowCreatorLeftContent";
import { FormikAddWorkPublisher } from "../../organisms/FormikAddWorkPublisher";

const useStyles = makeStyles(() =>
  createStyles({
    listItem: {
      paddingTop: 0,
      paddingBottom: 0,
    },
    popper: {
      zIndex: 1000,
    },
  })
);

interface ButtonAddPublisherRowProps {
  writerPath: string;
  publishersCount: number;
  disabled?: boolean;
  isPublisher?: boolean;
  isCreator?: boolean;
  company?: BootstrapCompany;
  account?: BootstrapAccount;
  ipiSearchPublishersApi?: string;
  onAdded: () => void;
}

export const ButtonAddPublisherRow: React.FC<ButtonAddPublisherRowProps> = ({
  writerPath,
  publishersCount,
  disabled,
  company,
  account,
  isPublisher,
  isCreator,
  ipiSearchPublishersApi,
  onAdded
}) => {
  const classes = useStyles();

  return (
    <RowCreatorLeftContent
      className={publishersCount ? classes.listItem : undefined}
    >
      <Field
        component={FormikAddWorkPublisher}
        ipiSearchPublishersApi={ipiSearchPublishersApi || ""}
        writerPath={writerPath}
        disabled={disabled}
        isPublisher={isPublisher}
        isCreator={isCreator}
        company={company}
        account={account}
        onAdded={onAdded}
      />
    </RowCreatorLeftContent>
  );
};
