import { Box, Grid, MenuItem, Select } from "@material-ui/core";
import { SelectProps } from "formik-material-ui";
import React, { ChangeEvent } from "react";
import { EnumLabel, StringEnum } from "../../../types/general";
import { FieldProps, getIn } from "formik";
import { TooltipMap } from "../../atoms/TooltipMap";
import { TooltipTypes } from "../../../types/tooltips";
import { CaretDown24 } from "@carbon/icons-react";

interface Props<T extends StringEnum> extends SelectProps {
  options: T;
  tooltips?: { [id: string]: TooltipTypes };
  disabledOptions?: string[];
  optionToLabel?: { [id: string]: EnumLabel<T> };
  labels?: EnumLabel<T>;
  disabled?: boolean;
  onChange?: (value: any) => void;
  onOpen?: () => void;
  interceptOnChange?: boolean;
  value?: any;
}

export const FormikEnumSelect = <T extends StringEnum>({
  options,
  labels,
  optionToLabel,
  disabledOptions = [],
  disabled,
  onChange,
  onOpen,
  tooltips,
  form: { values, setFieldValue, isSubmitting },
  field: { name },
  value: valueProp,
  meta,
  interceptOnChange,
  ...props
}: Props<T> & FieldProps) => {
  const value = valueProp || getIn(values, name);

  const printOptions = Object.keys(optionToLabel ? optionToLabel : options);

  const handleChange = (event: ChangeEvent<{ value: any }>) => {
    if (interceptOnChange) {
      onChange && onChange(event.target.value);
    } else {
      setFieldValue(name, event.target.value);
      onChange && onChange(event.target.value);
    }
  };
  return (
    <Select
      {...props}
      variant="filled"
      value={value}
      onOpen={onOpen}
      disabled={disabled || isSubmitting}
      onChange={handleChange}
      IconComponent={disabled ? "div" : CaretDown24}
    >
      {printOptions.map((item) => (
        <MenuItem
          key={options[item]}
          value={options[item] || ""}
          disabled={disabledOptions.includes(item)}
        >
          <TooltipMap
            to={tooltips && tooltips[item] ? tooltips[item] : "None"}
            disableCursor={!disabled}
          >
            {labels ? (
              <Grid container spacing={0} justify="space-between">
                <Grid item>{labels[item]}</Grid>
                <Grid item>
                  <Box component="span" color="text.hint">
                    {options[item]}
                  </Box>
                </Grid>
              </Grid>
            ) : optionToLabel ? (
              optionToLabel[item]
            ) : (
              options[item]
            )}
          </TooltipMap>
        </MenuItem>
      ))}
    </Select>
  );
};
