import React from "react";
import { Grid } from "@material-ui/core";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { InfiniteBorder } from "../../atoms/InfiniteBorder";
import { useAgreementsListStyles } from "../../molecules/ListHeaderAgreements";

export const HeaderListAgreementTemplates: React.FC = () => {
  const classes = useAgreementsListStyles();

  return (
    <ContainerListItem data-testid="agreement-templates-list">
      <Grid item className={classes.statusColumn} />
      <Grid item className={classes.agreementColumn}>
        <ListColumnTitle>Agreement</ListColumnTitle>
        <InfiniteBorder />
      </Grid>
      <Grid item className={classes.assignorColumn}>
        <ListColumnTitle>Assignor</ListColumnTitle>
      </Grid>
      <Grid item className={classes.assigneeColumn}>
        <ListColumnTitle>Assignee</ListColumnTitle>
      </Grid>
      <Grid item className={classes.assigneeColumn}>
        <ListColumnTitle>Last Updated</ListColumnTitle>
      </Grid>
      <Grid item className={classes.buttonColumn} />
      <Grid item className={classes.optionsColumn} />
    </ContainerListItem>
  );
};
