import { Typography, TypographyProps } from "@material-ui/core";
import React from "react";

export const ListColumnTitle: React.FC<TypographyProps> = ({
  children,
  align,
}) => {
  return (
    <Typography color="textSecondary" variant="body2" align={align} noWrap>
      {children}
    </Typography>
  );
};
