import {
  Button,
  createStyles,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  makeStyles,
  Theme as MaterialUiTheme,
} from "@material-ui/core";
import React from "react";
import { Form, Formik } from "formik";
import { AppDispatchEffect } from "../../../App.reducer";
import { deleteThemeEffect } from "../../pages/Theme/effects";
import { Theme } from "../../../utils/models";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { Dialog } from "../Dialog";

const useStyles = makeStyles((theme: MaterialUiTheme) =>
  createStyles({
    dialog: {
      maxWidth: 450,
      width: "100%",
    },
  })
);

export interface DialogDeleteThemeProps {
  onClose: () => void;
  open: boolean;
  dispatchEffect: AppDispatchEffect;
  theme: Theme;
  callback?: (themeId: string) => void;
}

export const DialogDeleteTheme: React.FC<DialogDeleteThemeProps> = ({
  onClose,
  dispatchEffect,
  theme,
  open,
  callback,
}) => {
  const handleClose = () => onClose();
  const classes = useStyles();

  return (
    <Formik
      initialValues={{}}
      onSubmit={async () => {
        try {
          await dispatchEffect(deleteThemeEffect(theme));
          onClose();
          callback && callback(theme.id);
        } catch (e) {}
      }}
    >
      {({ isSubmitting }) => (
        <Dialog
          variant="simple"
          classes={{ paper: classes.dialog }}
          open={open}
          onClose={handleClose}
          fullWidth
        >
          <Form>
            <DialogTitle>Delete Theme</DialogTitle>
            <DialogContent>
              <DialogContentText>
                Are you sure you want to remove the theme&nbsp;
                <strong>{theme.title}</strong>?
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleClose} color="primary">
                Cancel
              </Button>
              <ButtonSubmit submitting={isSubmitting} variant="text">
                Remove
              </ButtonSubmit>
            </DialogActions>
          </Form>
        </Dialog>
      )}
    </Formik>
  );
};
