import React from "react";
import {
  CircularProgress,
  createStyles,
  makeStyles,
  Paper,
  PaperProps,
  Theme,
} from "@material-ui/core";
import clsx from "clsx";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    root: {
      width: "100%",
      height: "100%",
      display: "flex",
      justifyContent: "center",
    },
    loader: {
      marginTop: theme.spacing(16),
      color: theme.palette.info.main,
    },
  })
);

export const LoadingScreen: React.FC<PaperProps> = ({ children, ...props }) => {
  const classes = useStyles();
  return (
    <Paper square {...props} className={clsx(props.className, classes.root)}>
      <CircularProgress className={classes.loader} />
      {children}
    </Paper>
  );
};
