import React from "react";
import { Button } from "@material-ui/core";
import { CellRole } from "../../atoms/CellRole";
import { CellCreator } from "../../atoms/CellCreator";
import { CellPrShare } from "../../atoms/CellPrShare";
import { CellMrShare } from "../../atoms/CellMrShare";
import { CellCollaboration } from "../../atoms/CellCollaboration";
import { CellSplit } from "../../atoms/CellSplit";
import { CellAvatar } from "../../atoms/CellAvatar";
import { RowCreatorListItem } from "../RowCreatorListItem";
import { CellOptions } from "../../atoms/CellOptions";
import { ColumnBreakpoints } from "../../../utils/models";

interface RowEvenSplitProps {
  onMechanicalEven: () => void;
  onPerformanceEven: () => void;
  hideSplitColumn?: boolean;
  hideOptionsColumn?: boolean;
  hideCollaborationColumn?: boolean;
  isSubmitting?: boolean;
  columnBreakPoints: ColumnBreakpoints;
}

export const RowEvenSplit: React.FC<RowEvenSplitProps> = ({
  onMechanicalEven,
  onPerformanceEven,
  hideSplitColumn,
  hideOptionsColumn,
  hideCollaborationColumn,
  isSubmitting,
  columnBreakPoints: bp,
}) => {
  return (
    <RowCreatorListItem>
      <CellAvatar />
      <CellCreator dense={bp.creator} />
      <CellRole dense={bp.role} />
      <CellPrShare dense={bp.prShare}>
        <Button
          data-testid="Even PR share"
          size="small"
          variant="outlined"
          onClick={onPerformanceEven}
          disabled={isSubmitting}
        >
          Even
        </Button>
      </CellPrShare>
      <CellMrShare dense={bp.mrShare}>
        <Button
          data-testid="Even MR share"
          size="small"
          variant="outlined"
          onClick={onMechanicalEven}
          disabled={isSubmitting}
        >
          Even
        </Button>
      </CellMrShare>
      {!(hideSplitColumn || bp.split) && <CellSplit />}
      {!(hideCollaborationColumn || bp.collaboration) && <CellCollaboration />}
      {!hideOptionsColumn && <CellOptions />}
    </RowCreatorListItem>
  );
};
