import React, { useEffect, useRef, useState } from "react";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { Grid, ListItem, ListItemText, Typography } from "@material-ui/core";
import { StatusIndicator } from "../../atoms/StatusIndicator";
import { format } from "date-fns";
import { useHeaderTableWorksAdministratorStyles } from "../../organisms/HeaderTableWorksAdministrator/component";
import { WorkListItem } from "../../../utils/models";
import { TooltipMap } from "../../atoms/TooltipMap";
import { isElementOverflowed } from "../../../utils/helpers";
import { workListTooltipStatusMap } from "../../../utils/common";

interface ListItemWorkAdminProps {
  work: WorkListItem;
  onClick: () => void;
  hideBreakpoint: boolean;
  windowWidth: number;
}

export const ListItemWorkAdmin: React.FC<ListItemWorkAdminProps> = ({
  work: { altTitle, title, created, creators, updated, status },
  onClick,
  windowWidth,
  hideBreakpoint,
}) => {
  const classes = useHeaderTableWorksAdministratorStyles();
  const [showTitleTooltip, setShowTitleTooltip] = useState(false);
  const [showSecondaryTitleTooltip, setShowSecondaryTitleTooltip] = useState(
    false
  );
  const [showMemberTooltip, setShowMemberTooltip] = useState(false);
  const titleRef = useRef<HTMLSpanElement>();
  const secondaryTitleRef = useRef<HTMLSpanElement>();
  const memberRef = useRef<HTMLSpanElement>();

  const memberName =
    (creators && creators.length > 0 && creators[0].displayName) || "";

  useEffect(() => {
    const isTitleOverflowed = isElementOverflowed(titleRef);
    const isSecondaryTitleOverflowed = isElementOverflowed(secondaryTitleRef);
    const isMemberOverflowed = isElementOverflowed(memberRef);
    setShowTitleTooltip(isTitleOverflowed);
    setShowSecondaryTitleTooltip(isSecondaryTitleOverflowed);
    setShowMemberTooltip(isMemberOverflowed);
  }, [windowWidth, titleRef, secondaryTitleRef, memberRef]);

  return (
    <ListItem
      button
      disableGutters
      className={classes.listRow}
      onClick={onClick}
    >
      <ContainerListItem leftOffset={hideBreakpoint ? 5.5 : 0}>
        <Grid item className={classes.statusColumn}>
          {status && (
            <StatusIndicator
              className={classes.status}
              status={status}
              mapStatusToTooltip={workListTooltipStatusMap}
            />
          )}
        </Grid>
        <Grid item className={classes.titleColumn}>
          <ListItemText
            className={classes.workName}
            classes={{ multiline: classes.listItemText }}
            primary={
              <Typography
                innerRef={titleRef}
                variant="subtitle1"
                color="primary"
              >
                {showTitleTooltip ? (
                  <TooltipMap variant="listItems" tooltipText={title}>
                    {title}
                  </TooltipMap>
                ) : (
                  title
                )}
              </Typography>
            }
            secondary={
              altTitle && (
                <Typography
                  innerRef={secondaryTitleRef}
                  color="textSecondary"
                  variant="body2"
                >
                  {showSecondaryTitleTooltip ? (
                    <TooltipMap variant="listItems" tooltipText={altTitle}>
                      {altTitle}
                    </TooltipMap>
                  ) : (
                    altTitle
                  )}
                </Typography>
              )
            }
          />
        </Grid>
        {hideBreakpoint && (
          <React.Fragment>
            <Grid item className={classes.ipiColumn}>
              <Typography variant="subtitle1" color="textSecondary" noWrap>
                {(creators &&
                  creators?.length > 0 &&
                  creators[0].ipiNameNumber) ||
                  ""}
              </Typography>
            </Grid>
            <Grid item className={classes.memberColumn}>
              <Typography
                innerRef={memberRef}
                variant="subtitle1"
                color="textSecondary"
                noWrap
              >
                {showMemberTooltip ? (
                  <TooltipMap variant="listItems" tooltipText={memberName}>
                    {memberName}
                  </TooltipMap>
                ) : (
                  memberName
                )}
              </Typography>
            </Grid>
            <Grid item className={classes.iswcColumn}>
              <Typography variant="subtitle1" color="textSecondary" noWrap />
            </Grid>
            <Grid item className={classes.lastUpdatedColumn}>
              <Typography
                color="textSecondary"
                noWrap
                data-testid="updated-time"
              >
                {updated && format(new Date(updated.iso), "MMM dd, yyyy")}
              </Typography>
            </Grid>
            <Grid item className={classes.createdColumn}>
              <Typography
                variant="subtitle1"
                color="textSecondary"
                noWrap
                data-testid="created-time"
              >
                {created && format(new Date(created.iso), "MMM dd, yyyy")}
              </Typography>
            </Grid>
          </React.Fragment>
        )}
      </ContainerListItem>
    </ListItem>
  );
};
