import {
  Box,
  Button,
  createStyles,
  Grid,
  makeStyles,
  Theme as MaterialUiTheme,
} from "@material-ui/core";
import React, { useState } from "react";
import { Field } from "formik";
import { Theme } from "../../../utils/models";
import { TextField } from "formik-material-ui";
import { DEFAULT_THEME_ID } from "../../pages/Theme/reducer";
import { AppDispatchEffect } from "../../../App.reducer";
import {
  createAndSetThemeEffect,
  updateAndSetThemeEffect,
  uploadCustomLogoEffect,
} from "../../pages/Theme/effects";
import { CustomLogoUpload } from "../../molecules/CustomLogoUpload";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { FormikThemeColorSelect } from "../../atoms/FormikThemeColorSelect";
import { NEW_THEME_ID } from "../ThemeOptionsDrawer";
import { DialogSetTheme } from "../../atoms/DialogSetTheme/component";

const useStyles = makeStyles((theme: MaterialUiTheme) =>
  createStyles({
    firsColumn: {
      display: "flex",
      flexDirection: "column",
      justifyContent: "space-between",
      alignItems: "flex-start",
      maxWidth: 310,
      width: "100%",
    },
    middleColumn: {
      marginLeft: theme.spacing(5),
    },
    lastColumn: {
      display: "flex",
      justifyContent: "flex-end",
      alignItems: "flex-end",
      height: 117,
      marginLeft: "auto",
    },
    gridContainer: {
      height: "100%",
      width: "100%",
      flexWrap: "nowrap",
      paddingRight: theme.spacing(3),
    },
    textColorSelect: {
      marginTop: theme.spacing(2),
    },
    colorSelectContainer: {
      marginLeft: theme.spacing(5),
    },
    actionsContainer: {
      height: 36,
    },
    saveButton: {
      marginLeft: theme.spacing(3),
    },
    themeTitle: {
      marginTop: theme.spacing(2.5),
    },
  })
);

interface FormNewThemeProps {
  dispatchEffect: AppDispatchEffect;
  setFieldValue: (key: string, value: any) => void;
  values: { theme: Theme };
  onCancel: () => void;
  onClose: () => void;
  onDeleteClick: (theme: Theme) => void;
  onSaveSuccess: () => void;
  submitForm: () => Promise<void>;
}

export const FormNewTheme: React.FC<FormNewThemeProps> = ({
  dispatchEffect,
  setFieldValue,
  values,
  onCancel,
  onClose,
  onDeleteClick,
  onSaveSuccess,
  submitForm,
}) => {
  const classes = useStyles();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [showSetThemeConfirmDialog, setShowSetThemeConfirmDialog] = useState(
    false
  );
  const onSave = async () => {
    setShowSetThemeConfirmDialog(false);
    setIsSubmitting(true);
    try {
      if (values.theme.id === NEW_THEME_ID) {
        const createdTheme = await dispatchEffect(
          createAndSetThemeEffect(values.theme)
        );
        setFieldValue("theme", createdTheme);
        onClose();
      } else {
        const updatedTheme = await dispatchEffect(
          updateAndSetThemeEffect(values.theme)
        );
        setFieldValue("theme", updatedTheme);
      }
      onSaveSuccess();
    } catch (e) {}
    setIsSubmitting(false);
  };

  return (
    <React.Fragment>
      {showSetThemeConfirmDialog && (
        <DialogSetTheme
          onConfirm={onSave}
          onCancel={() => setShowSetThemeConfirmDialog(false)}
        />
      )}
      <Grid container className={classes.gridContainer}>
        <Grid item className={classes.firsColumn}>
          <Field
            className={classes.themeTitle}
            component={TextField}
            name="theme.title"
            label="Theme name"
            fullWidth
          />
          <Button
            variant="text"
            onClick={() => onDeleteClick(values.theme)}
            disabled={[DEFAULT_THEME_ID, NEW_THEME_ID].includes(
              values.theme.id
            )}
          >
            Delete Theme
          </Button>
        </Grid>
        <Grid item className={classes.middleColumn}>
          <Box display="flex">
            <CustomLogoUpload
              uploadImage={async (image) => {
                await dispatchEffect(uploadCustomLogoEffect(image));
                setFieldValue("theme.logo", image);
              }}
              onDelete={() => {
                setFieldValue("theme.logo", undefined);
              }}
              customLogo={values.theme.logo}
            />
            <Box className={classes.colorSelectContainer}>
              <Field
                component={FormikThemeColorSelect}
                label="Primary Colour"
                name="theme.appBarColor"
              />
              <Field
                component={FormikThemeColorSelect}
                label="Text Colour"
                name="theme.textColor"
                className={classes.textColorSelect}
                disableAlpha
              />
            </Box>
          </Box>
        </Grid>
        <Grid item className={classes.lastColumn}>
          <Box className={classes.actionsContainer}>
            <Button size="small" onClick={onCancel}>
              Cancel
            </Button>
            <ButtonSubmit
              type="button"
              variant="contained"
              color="primary"
              size="small"
              disabled={isSubmitting}
              onClick={(event) => {
                event.preventDefault();
                setShowSetThemeConfirmDialog(true);
              }}
              submitting={isSubmitting}
              className={classes.saveButton}
            >
              {values.theme.id === NEW_THEME_ID
                ? "Create & Set Theme"
                : "Set Theme"}
            </ButtonSubmit>
          </Box>
        </Grid>
      </Grid>
    </React.Fragment>
  );
};
