import React, { useState } from "react";
import MenuItem from "@material-ui/core/MenuItem";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import { Select } from "formik-material-ui";
import { FieldProps } from "formik";
import { Box, createStyles, makeStyles, Theme } from "@material-ui/core";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    filled: {
      "&$filled": {
        paddingRight: theme.spacing(2),
      },
    },
    secondaryValue: {
      paddingRight: theme.spacing(2),
    },
  })
);

interface FormikMenuSelectProps {
  options: Array<{ value: string; label?: string }>;
  label: string;
  hideValues?: boolean;
  disabled?: boolean;
}

export const FormikMenuSelect: React.FC<FormikMenuSelectProps & FieldProps> = ({
  children,
  label,
  options,
  hideValues,
  disabled,
  ...props
}) => {
  const classes = useStyles();
  const { name } = props.field;
  const { isSubmitting } = props.form;
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  return (
    <FormControl variant="filled" fullWidth={true}>
      <InputLabel htmlFor={name}>{label}</InputLabel>
      <Select
        {...props}
        disabled={disabled || isSubmitting}
        onOpen={handleOpen}
        onClose={handleClose}
        classes={{ filled: classes.filled }}
        inputProps={{
          id: name,
        }}
      >
        {options.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label ? (
              <Box
                width={1}
                display="inline-flex"
                justifyContent="space-between"
              >
                <Box>{option.label}</Box>
                {!hideValues && (
                  <Box
                    data-testid="secondary-value"
                    className={clsx({ [classes.secondaryValue]: !open })}
                  >
                    <Box color="text.secondary">{option.value}</Box>
                  </Box>
                )}
              </Box>
            ) : (
              option.label
            )}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};
