import React from "react";
import { ButtonBase, makeStyles, Theme, Typography } from "@material-ui/core";
import { FieldProps } from "formik";
import { AgreementShare } from "../../../utils/models";
import { ipiRightsPerforming } from "../../organisms/DialogIpiRights/utils";
import { AgreementRightType } from "../../../types/enums";
import { AgreementFormValues } from "../../organisms/FormAgreement";
import { Add24 } from "@carbon/icons-react";
import { createStyles } from "@material-ui/core/styles";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    button: {
      display: "flex",
      alignItems: "center",
      marginLeft: theme.spacing(1),
    },
    icon: {
      marginRight: theme.spacing(1),
    },
  })
);

interface FormikAddAgreementShareProps {
  disabled?: boolean;
}

export const FormikAddAgreementShare: React.FC<
  FormikAddAgreementShareProps & FieldProps<any, AgreementFormValues>
> = ({ form: { setFieldValue }, field: { name }, disabled }) => {
  const classes = useStyles();
  const handleAddShare = () => {
    const share: AgreementShare = {
      rightType: AgreementRightType.P,
      usageTypes: ipiRightsPerforming.map((el) => el.code),
      writerShareRetained: 0,
      publisherShareRetained: 0,
      assigneeCollectibleShare: 0,
    };
    setFieldValue(name, share);
  };

  return (
    <ButtonBase
      className={classes.button}
      disabled={disabled}
      onClick={handleAddShare}
    >
      <Add24 className={classes.icon} />
      <Typography color="primary" variant="button">
        Add Share
      </Typography>
    </ButtonBase>
  );
};
