import {
  Container,
  createStyles,
  makeStyles,
  Paper,
  Theme,
} from "@material-ui/core";
import React from "react";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: "100%",
      height: "100%",
      flex: "1 1 auto",
      overflow: "auto",
      overflowX: "hidden",
    },
    listContainer: {
      padding: theme.spacing(0),
      marginTop: theme.spacing(5.5),
    },
  })
);

interface ContainerTableListProps {
  elevation?: number;
  containerClass?: string;
  rootClass?: string;
  disableMaxWidth?: boolean;
  children: any;
}

export const ContainerTableList: React.FC<ContainerTableListProps> = ({
  children,
  elevation,
  containerClass,
  rootClass,
  disableMaxWidth,
}) => {
  const classes = useStyles();
  return (
    <Paper square className={clsx(classes.root, rootClass)} elevation={elevation}>
      <Container
        maxWidth={disableMaxWidth ? false : undefined}
        className={clsx(classes.listContainer, containerClass)}
      >
        {children}
      </Container>
    </Paper>
  );
};
