import {
  FaqActionTypes,
  SET_FAQ_PATH,
  SET_ITEM,
  RESET_ITEM,
  OPEN_DRAWER,
  CLOSE_DRAWER,
  SET_DEFAULT_FAQ_PATH,
} from "./actions.types";

export interface FaqState {
  open: boolean;
  path?: string;
  defaultPath?: string;
  item?: string;
}

export const faqInitialState: FaqState = {
  open: false,
};

export const faqReducer = (
  state: FaqState = faqInitialState,
  action: FaqActionTypes
): FaqState => {
  switch (action.type) {
    case SET_FAQ_PATH: {
      return {
        ...state,
        path: action.path,
      };
    }

    case SET_DEFAULT_FAQ_PATH: {
      return {
        ...state,
        defaultPath: action.path,
      };
    }

    case SET_ITEM: {
      return {
        ...state,
        item: action.item,
      };
    }

    case RESET_ITEM: {
      return {
        ...state,
        item: undefined,
      };
    }

    case OPEN_DRAWER: {
      return {
        ...state,
        open: true,
      };
    }

    case CLOSE_DRAWER: {
      return {
        ...state,
        open: false,
      };
    }

    default:
      return state;
  }
};
