import {
  createStyles,
  makeStyles,
  Theme as MaterialUiTheme,
} from "@material-ui/core";
import React from "react";
import { BootstrapAccount } from "../../../utils/models";
import { AppDispatchEffect } from "../../../App.reducer";
import { isDevelopment } from "../../../utils/helpers";
import { URL_NAMESPACE } from "../../../api.config";

const useStyles = makeStyles((theme: MaterialUiTheme) =>
  createStyles({
    userCard: {
      padding: theme.spacing(2),
      minHeight: 171,
      maxHeight: 171,
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },
    lowerContent: {
      margin: theme.spacing(0, 0, 1, 3),
    },
    avatar: {
      marginBottom: theme.spacing(2),
      background: theme.palette.primary.main,
      cursor: isDevelopment ? "pointer" : "cursor",
      color: theme.palette.primary.contrastText,
    },
    fullName: {
      paddingLeft: theme.spacing(0.5),
      paddingBottom: theme.spacing(0.5),
    },
    mail: {
      paddingLeft: theme.spacing(0.5),
    },
    customLogo: {
      height: "100%",
      width: "100%",
      objectFit: "scale-down",
    },
  })
);

interface CardUserProps {
  account: BootstrapAccount;
  dispatchEffect: AppDispatchEffect;
  logo?: string;
  isSocAdmin?: boolean;
}

export const CardUser: React.FC<CardUserProps> = ({
  account: {
    accountEmail,
    profile: { displayName, initials, profileImage },
  },
  logo,
  dispatchEffect,
  isSocAdmin,
}) => {
  const classes = useStyles();

  const getLogoSrc = () => {
    //  Temporary solution for demo.
    //  Hardcode white STIM logo.
    //  Hardcode white Session logo.
    if (URL_NAMESPACE === "org-stim") {
      return `${process.env.PUBLIC_URL}/static/media/svg/stim-logo-white.svg`;
    }

    return `${process.env.PUBLIC_URL}/static/media/svg/session-logo-vertical-white.svg`;
  };

  return (
    <div className={classes.userCard}>
      <img
        src={getLogoSrc()}
        alt="custom logo"
        className={classes.customLogo}
      />

      {/* Deprecated design. Maybe will be needed later */}
      {/*<div className={classes.lowerContent}>*/}
      {/*  {isDevelopment ? (*/}
      {/*    <DialogChangeAvatar*/}
      {/*      dispatchEffect={dispatchEffect}*/}
      {/*      openComponent={*/}
      {/*        <Avatar src={profileImage} className={classes.avatar}>*/}
      {/*          {initials}*/}
      {/*        </Avatar>*/}
      {/*      }*/}
      {/*    />*/}
      {/*  ) : (*/}
      {/*    <Avatar className={classes.avatar}>{initials}</Avatar>*/}
      {/*  )}*/}

      {/*  <Typography*/}
      {/*    data-testid="display-name"*/}
      {/*    className={classes.fullName}*/}
      {/*    variant="h4"*/}
      {/*  >*/}
      {/*    {displayName}*/}
      {/*  </Typography>*/}
      {/*  <Typography*/}
      {/*    className={classes.mail}*/}
      {/*    variant="body2"*/}
      {/*    color="textSecondary"*/}
      {/*  >*/}
      {/*    {email}*/}
      {/*  </Typography>*/}
      {/*</div>*/}
    </div>
  );
};
