import React from "react";
import {
  createMuiTheme,
  Theme,
  ThemeProvider,
  useTheme,
} from "@material-ui/core/styles";
import { getTheme } from "@session-id-limited/client-module-ui-components";

const inverse = (theme: Theme) =>
  createMuiTheme(getTheme(theme.palette.type === "light" ? "dark" : "light"));

export const ThemeInverse: React.FC = ({ children }) => {
  const theme = useTheme();
  return <ThemeProvider theme={inverse(theme)}>{children}</ThemeProvider>;
};
