import React from "react";
import { changePasswordEffect } from "../../pages/SignIn/effects";
import { PasswordPolicy } from "../../pages/SignIn/actions.types";
import { Field, Form, Formik } from "formik";
import {
  Box,
  Button,
  createStyles,
  Grid,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { AppDispatchEffect } from "../../../App.reducer";
import * as yup from "yup";
import { passwordValidation } from "../../../utils/passwordValidation";
import { Error } from "../../../api.types";
import { ApiErrorCodeTypes } from "../../../types/enums";
import { FormikPasswordInput } from "../../atoms/FormikPasswordInput";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    row: {
      marginTop: theme.spacing(2),
    },
  })
);

const changePasswordValidation = (passwordPolicy?: PasswordPolicy) => {
  return yup.object().shape({
    currentPassword: yup.string().required("Required"),
    newPassword: passwordPolicy
      ? passwordValidation(passwordPolicy, "currentPassword")
      : yup.string().required("Required"),
  });
};

interface FormPasswordChangeProps {
  dispatchEffect: AppDispatchEffect;
  passwordPolicy?: PasswordPolicy;
}

export const FormPasswordChange: React.FC<FormPasswordChangeProps> = ({
  dispatchEffect,
  passwordPolicy,
}) => {
  const classes = useStyles();

  const hasChangedValues = ({
    currentPassword,
    newPassword,
  }: {
    currentPassword: string;
    newPassword: string;
  }) => Boolean(currentPassword) || Boolean(newPassword);

  return (
    <Formik
      onSubmit={({ currentPassword, newPassword }, { setFieldError }) => {
        return dispatchEffect(
          changePasswordEffect(currentPassword, newPassword)
        ).catch((e: Error) => {
          if (e.field) {
            setFieldError(e.field, e.message);
          }
          if (e.code === ApiErrorCodeTypes.INCORRECT_PASSWORD) {
            setFieldError("currentPassword", e.message);
          }
        });
      }}
      validationSchema={changePasswordValidation(passwordPolicy)}
      initialValues={{
        currentPassword: "",
        newPassword: "",
      }}
    >
      {({ isSubmitting, handleSubmit, resetForm, values }) => (
        <Form data-testid="password-change-form">
          <Box pt={4} pb={5}>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <Typography variant="h6">Password</Typography>
              </Grid>
              <Grid item sm={6} xs={12}>
                <Field
                  data-testid="current-password"
                  component={FormikPasswordInput}
                  required
                  label="Current Password"
                  autoComplete="new-password" // set as new-password to prevent auto filling current password
                  name="currentPassword"
                  variant="filled"
                />
              </Grid>
              <Grid item sm={6} xs={12}>
                <Field
                  data-testid="new-password"
                  component={FormikPasswordInput}
                  required
                  passwordPolicy={passwordPolicy}
                  label="New Password"
                  autoComplete="new-password"
                  name="newPassword"
                  variant="filled"
                />
              </Grid>
            </Grid>
            <Grid
              container
              justify="flex-end"
              direction="row"
              className={classes.row}
            >
              {hasChangedValues(values) && (
                <Button
                  color="primary"
                  onClick={() => resetForm()}
                  disabled={isSubmitting}
                >
                  Cancel
                </Button>
              )}
              <Button
                type="submit"
                color="primary"
                onClick={() => handleSubmit()}
                disabled={isSubmitting || !hasChangedValues(values)}
              >
                Update
              </Button>
            </Grid>
          </Box>
        </Form>
      )}
    </Formik>
  );
};
