import { AppState } from "../../../App.reducer";
import { doesUserHaveRoles } from "../../../utils/helpers";
import { AccountRoles, LoadingState } from "../../../types/enums";
import { getCurrentThemeLoadingStateSelector } from "../Theme/selectors";

export const getAccountSelector = (state: AppState) => state.signIn.account;

export const getCompanySelector = (state: AppState) => state.signIn.company;

export const getUserRoles = (state: AppState) => state.signIn.account?.roles;

export const getWorksListFetchLinkSelector = (state: AppState) =>
  state.signIn._links?.works;

export const getWorkBaseLinkSelector = (state: AppState) =>
  (state.signIn._links?.work?.href || "").replace("/{guid}", "");

export const getTemplatesListFetchLinkSelector = (state: AppState) =>
  state.signIn._links?.workTemplates;

export const getWorkDraftsListFetchLinkSelector = (state: AppState) =>
  state.signIn._links?.workDrafts;

export const getIpiListFetchLinkSelector = (state: AppState) =>
  state.signIn._links?.ipis;

export const getAgreementsListFetchLinkSelector = (state: AppState) =>
  state.signIn._links?.agreements;

export const getNotificationsListFetchLinkSelector = (state: AppState) =>
  state.signIn._links?.notifications;

export const getLegalProfileLinkSelector = (state: AppState) =>
  state.signIn._links?.myLegalProfile;

export const getUpdateProfileLinkSelector = (state: AppState) =>
  state.signIn.legalProfile?._links.update;

export const getLegalProfileLoadingStateSelector = (state: AppState) =>
  state.signIn.legalProfileLoadingState;

export const getFetchEmployeesLinkSelector = (state: AppState) =>
  state.signIn._links?.employees;

export const getFetchInvitesLinkSelector = (state: AppState) =>
  state.signIn._links?.invites;

export const getUpdateCompanyDetailsLinkSelector = (state: AppState) =>
  state.company.companyDetails?._links.updateDetails;

export const getCompanyDetailsSelector = (state: AppState) =>
  state.company.companyDetails;

export const getSettingsFetchLinkSelector = (state: AppState) =>
  state.signIn._links?.organizationSettingVersion;

export const getCompanyLinkSelector = (state: AppState) =>
  state.signIn._links?.getCompany;

export const getUserIsSocAdminSelector = (state: AppState) =>
  doesUserHaveRoles(state, [AccountRoles.PRO_SOCIETY]);

export const getAuthPolicySelector = (state: AppState) =>
  state.signIn.authPolicy;

export const getBootstrapLoadingStateSelector = (state: AppState) =>
  state.signIn.bootstrapLoadingState;

export const getBootstrapLinksSelector = (state: AppState) =>
  state.signIn._links;

export const getChangeEmailLinkSelector = (state: AppState) =>
  state.signIn._links?.changeEmail;

export const getChangePasswordLinkSelector = (state: AppState) =>
  state.signIn._links?.changePassword;

export const getPasswordPolicyLinkSelector = (state: AppState) =>
  getBootstrapLinksSelector(state)?.passwordPolicy ||
  state.signIn._links?.passwordPolicy;

export const isProfilePageAvailableSelector = (state: AppState) => {
  return Boolean(
    getLegalProfileLinkSelector(state) ||
      getChangeEmailLinkSelector(state) ||
      getChangePasswordLinkSelector(state) ||
      getIpiListFetchLinkSelector(state)
  );
};

export const getNotificationsUnseenCountLinkSelector = (state: AppState) =>
  state.signIn._links?.notificationsUnseenCount;

export const getSignInLoadingStateSelector = (state: AppState) =>
  state.signIn.signInLoadingState;

export const getIsAppInitializingSelector = (state: AppState) => {
  const loadingStates = [LoadingState.PRISTINE, LoadingState.FETCHING];
  const signInLoadingState = getSignInLoadingStateSelector(state);
  const themeLoadingState = getCurrentThemeLoadingStateSelector(state);
  const bootstrapLoadingState = getBootstrapLoadingStateSelector(state);
  const themeLoading = loadingStates.includes(themeLoadingState);
  const bootstrapLoading = loadingStates.includes(bootstrapLoadingState);
  const signingInManually = signInLoadingState === LoadingState.FETCHING;
  return (themeLoading || bootstrapLoading) && !signingInManually;
};

export const getChangeEmailConfirmLinkSelector = (state: AppState) =>
  state.signIn.changeEmailConfirmHref;
