import React from "react";
import {
  Box,
  createStyles,
  List,
  makeStyles,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import {
  BootstrapAccount,
  BootstrapCompany,
  Ipi,
  Work,
  WorkCreator,
  WorkPublisher,
} from "../../../utils/models";
import { FieldArray } from "formik";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import { mediaHide } from "../../../utils/media";
import { RowGridCreator } from "../../molecules/RowGridCreator";
import { HeaderCreatorList } from "../../molecules/HeaderCreatorList";
import { ContainerContent } from "../../atoms/ContainerContent";
import { RowGridPublisherRelationship } from "../RowGridPublisherRelationship";
import { RowEvenSplit } from "../../molecules/RowEvenSplit";
import { RowGridCreatorTotal } from "../../molecules/RowGridCreatorTotal";
import { WorkFormValues } from "../FormWorkRegistration";
import { Theme } from "@material-ui/core/styles";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    listOuterWrapper: {
      width: "100%",
      display: "flex",
      justifyContent: "center",
      height: "100%",
      overflow: "overlay",
    },
    container: {
      display: "flex",
      flexDirection: "column",
      width: "100%",
      height: "100%",
    },
    containerContent: {
      paddingBottom: theme.spacing(25),
    },
  })
);

interface ListCreatorsProps {
  work?: Work;
  creators: WorkCreator[];
  publishers: WorkPublisher[];
  setFieldValue: (key: string, value: any) => void;
  isSubmitting?: boolean;
  hideSplitColumn?: boolean;
  hideOptionsColumn?: boolean;
  hideCollaborationColumn?: boolean;
  editable?: boolean;
  dispatchEffect: AppDispatchEffect;
  dispatch: AppDispatch;
  ipiList: Ipi[];
  isTemplate?: boolean;
  breakpointOffset?: number;
  values?: WorkFormValues;
  isPublisher?: boolean;
  isCreator?: boolean;
  company?: BootstrapCompany;
  account?: BootstrapAccount;
  ipiSearchPublishersApi?: string;
}

export const ListCreators: React.FC<ListCreatorsProps> = ({
  creators,
  ipiSearchPublishersApi,
  publishers,
  setFieldValue,
  isSubmitting,
  hideSplitColumn,
  hideOptionsColumn,
  hideCollaborationColumn,
  editable,
  dispatchEffect,
  isTemplate,
  ipiList,
  breakpointOffset,
  work,
  isPublisher,
  isCreator,
  company,
  account,
  dispatch,
}) => {
  const theme = useTheme();
  const classes = useStyles();
  const useMediaQueryWithOffset = (media: number) =>
    useMediaQuery(theme.breakpoints.down(media + (breakpointOffset || 0)));

  const columnBreakPointsHooks = {
    split: useMediaQueryWithOffset(mediaHide.splitColumn),
    collaboration: useMediaQueryWithOffset(mediaHide.collaborationColumn),
    role: useMediaQueryWithOffset(mediaHide.roleColumn),
    mrShare: useMediaQueryWithOffset(mediaHide.mrShareColumn),
    prShare: useMediaQueryWithOffset(mediaHide.prShareColumn),
    creator: useMediaQueryWithOffset(mediaHide.creatorColumn),
  };

  const columnBreakPoints = {
    ...columnBreakPointsHooks,
    split: isTemplate || columnBreakPointsHooks.split,
    collaboration: isTemplate || columnBreakPointsHooks.collaboration,
  };

  const splitEven = (key: "mrShare" | "prShare") => {
    const length = creators.length;
    const even = Number((100 / length).toFixed(2));
    const updatedCreators = creators.map((el, index) => {
      const isLast = index === creators.length - 1;
      if (isLast) {
        const remainder = Number((100 - (length - 1) * even).toFixed(2));
        const rounded = Number(remainder.toPrecision(4));
        return { ...el, [key]: rounded };
      }
      return { ...el, [key]: even };
    });
    setFieldValue("creators", updatedCreators);
  };

  return (
    <React.Fragment>
      {creators.length > 0 && (
        <FieldArray name="creators">
          {(arrayHelpers) => (
            <Box className={classes.container}>
              <HeaderCreatorList
                hideOptionsColumn={hideOptionsColumn || !editable}
                hideSplitColumn={hideSplitColumn}
                hideCollaborationColumn={hideCollaborationColumn}
                columnBreakPoints={columnBreakPoints}
                dispatch={dispatch}
              />
              <Box className={classes.listOuterWrapper}>
                <Box component={List} width={1}>
                  <ContainerContent className={classes.containerContent}>
                    {creators.map((creator, index) => (
                      <React.Fragment key={`creators.${index}`}>
                        <RowGridCreator
                          isPublisher={isPublisher}
                          isAuthor={Boolean(creator.isSelf)}
                          isTemplate={isTemplate}
                          work={work}
                          creatorPath={`creators.${index}`}
                          creator={creator}
                          onDelete={() => arrayHelpers.remove(index)}
                          hideOptionsColumn={hideOptionsColumn}
                          hideSplitColumn={hideSplitColumn}
                          hideCollaborationColumn={hideCollaborationColumn}
                          editable={editable}
                          dispatchEffect={dispatchEffect}
                          ipiList={ipiList}
                          columnBreakPoints={columnBreakPoints}
                          isSubmitting={isSubmitting}
                          account={account}
                        />
                        <RowGridPublisherRelationship
                          ipiSearchPublishersApi={ipiSearchPublishersApi}
                          dispatchEffect={dispatchEffect}
                          creatorPath={`creators.${index}`}
                          setFieldValue={setFieldValue}
                          publishers={publishers}
                          creator={creator}
                          editable={editable}
                          hideOptionsColumn={hideOptionsColumn}
                          hideSplitColumn={hideSplitColumn}
                          hideCollaborationColumn={hideCollaborationColumn}
                          columnBreakPoints={columnBreakPoints}
                          isPublisher={isPublisher}
                          isCreator={isCreator}
                          company={company}
                          account={account}
                          isSubmitting={isSubmitting}
                        />
                      </React.Fragment>
                    ))}
                    {editable && (
                      <RowEvenSplit
                        isSubmitting={isSubmitting}
                        onMechanicalEven={() => splitEven("mrShare")}
                        onPerformanceEven={() => splitEven("prShare")}
                        hideOptionsColumn={hideOptionsColumn}
                        hideSplitColumn={hideSplitColumn}
                        hideCollaborationColumn={hideCollaborationColumn}
                        columnBreakPoints={columnBreakPoints}
                      />
                    )}
                    <RowGridCreatorTotal
                      creators={creators}
                      hideOptionsColumn={hideOptionsColumn || !editable}
                      hideSplitColumn={hideSplitColumn}
                      hideCollaborationColumn={hideCollaborationColumn}
                      columnBreakPoints={columnBreakPoints}
                    />
                  </ContainerContent>
                </Box>
              </Box>
            </Box>
          )}
        </FieldArray>
      )}
    </React.Fragment>
  );
};
