import React from "react";
import { Box, createStyles, List, makeStyles, Theme } from "@material-ui/core";
import { ContainerContent } from "../../atoms/ContainerContent";
import { HeaderCreatorListSummary } from "../../molecules/HeaderCreatorListSummary";
import { RowGridCreatorSummary } from "../../molecules/RowGridCreatorSummary";
import { RowGridCreatorTotalSummary } from "../../molecules/RowGridCreatorTotalSummary";
import { RowGridPublisherRelationshipSummary } from "../RowGridPublisherRelationshipSummary";
import { WorkFormValues } from "../FormWorkRegistration";

export const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    listContainer: {
      paddingRight: theme.spacing(8.5),
      overflow: "overlay",
      height: "100%",
      marginTop: 0,
    },
    containerClass: {
      height: "100%",
      paddingRight: 0,
    },
    inner: {
      display: "flex",
      flexDirection: "column",
      height: "100%",
    },
    paddingBottom: {
      paddingBottom: theme.spacing(3),
    },
  })
);

interface ListCreatorsSummaryProps {
  values: WorkFormValues;
  isPublisher?: boolean;
}

export const ListCreatorsSummary: React.FC<ListCreatorsSummaryProps> = ({
  values,
  isPublisher,
}) => {
  const classes = useStyles();
  const avatarColumnWidth = 55;
  const { creators, publishers } = values;

  return (
    <React.Fragment>
      {creators.length > 0 && (
        <ContainerContent
          shiftLeft={avatarColumnWidth}
          containerClassName={classes.containerClass}
          className={classes.inner}
          disableMaxWidth
        >
          <HeaderCreatorListSummary />
          <Box className={classes.listContainer}>
            <Box component={List}  className={classes.paddingBottom}>
              {creators.map((creator, index) => (
                <React.Fragment key={creator.ipi.ipiNameNumber}>
                  <RowGridCreatorSummary creator={creator} />
                  {creator.publishers?.length ? (
                    <RowGridPublisherRelationshipSummary
                      creator={creator}
                      publishers={publishers}
                      isPublisher={isPublisher}
                    />
                  ) : null}
                </React.Fragment>
              ))}
              <RowGridCreatorTotalSummary creators={creators} />
            </Box>
          </Box>
        </ContainerContent>
      )}
    </React.Fragment>
  );
};
