import {
  FAILURE,
  IN_PROGRESS,
  PARTIALLY_PROCESSED,
  REJECTED,
  SUCCESS,
  VALIDATION_ERROR,
  WorkListItemStatusType,
} from "../../../utils/models";

export const workFilterStatusAll: WorkListItemStatusType[] = [
  PARTIALLY_PROCESSED,
  IN_PROGRESS,
  SUCCESS,
  REJECTED,
  FAILURE,
  VALIDATION_ERROR,
];

export const getWorkFilters: (
  filters: string | null,
  isDraftsPage: boolean
) => WorkListItemStatusType[] = (filters, isDraftsPage) => {
  if (isDraftsPage) {
    return [];
  }
  if (!filters) {
    return workFilterStatusAll;
  }
  const newFilters = (filters || "").split(",");
  const valid =
    newFilters.find(
      (filter) =>
        !workFilterStatusAll.includes(filter as WorkListItemStatusType)
    ) === undefined;

  return valid ? (newFilters as WorkListItemStatusType[]) : [];
};
