import React from "react";
import {
  Box,
  createStyles,
  ListItem,
  makeStyles,
  Paper,
  Theme,
  Typography,
} from "@material-ui/core";
import { SocietyLogo } from "../../atoms/SocietyLogo/component";
import {
  ACKNOWLEDGED,
  FAILURE,
  IN_PROGRESS,
  NO_ACK_INTEGRATION,
  NO_INTEGRATION,
  REJECTED,
  WorkRegistrationSubmission,
  WorkSubmissionStatusType,
} from "../../../utils/models";
import { StatusIndicator } from "../../atoms/StatusIndicator";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    listItemRoot: {
      display: "flex",
      alignItems: "center",
      width: "100%",
      minHeight: 72,
      backgroundColor: theme.palette.background.paper,
      padding: theme.spacing(1.5, 2.5),
      marginBottom: theme.spacing(1),
      border: `1px solid ${theme.palette.action.selected}`,
      borderRadius: theme.spacing(1),
    },
    logo: {
      display: "flex",
      justifyContent: "center",
      width: 100,
      paddingRight: theme.spacing(2),
    },
    description: {
      display: "flex",
      flexDirection: "column",
      flex: "1 1 auto",
    },
    border: {
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
    bold: {
      fontWeight: 500,
    },
  })
);

const getSubmissionStatusText = (
  status: WorkSubmissionStatusType,
  customHandler?: (status: WorkSubmissionStatusType) => React.ReactNode
) => {
  const customStatus = customHandler && customHandler(status);
  if (customStatus) {
    return customStatus;
  }
  if (status === IN_PROGRESS) {
    return "Work submission is in progress.";
  }
  if (status === NO_INTEGRATION) {
    return "Work not submitted. The society is not integrated with Session.";
  }
  if (status === FAILURE) {
    return "Work not submitted.";
  }
  if (status === REJECTED) {
    return "Work submission rejected by the society.";
  }
  if ([ACKNOWLEDGED, NO_ACK_INTEGRATION].includes(status)) {
    return "Work successfully submitted.";
  }
  return status;
};

interface ListItemWorkSubmissionProps {
  submission: WorkRegistrationSubmission;
  customStatusHandler?: (status: WorkSubmissionStatusType) => React.ReactNode;
}

export const ListItemWorkSubmission: React.FC<ListItemWorkSubmissionProps> = ({
  submission,
  customStatusHandler,
}) => {
  const classes = useStyles();
  return (
    <ListItem
      ContainerComponent={Paper}
      key={`${submission.recipient.guid}${submission.created.timestamp}`}
      className={classes.listItemRoot}
    >
      <Box className={classes.logo}>
        <SocietyLogo logoSrc={submission.recipient.logo} />
      </Box>
      <Box className={classes.description}>
        <Typography
          variant="body2"
          color="textSecondary"
          className={classes.bold}
        >
          {submission.recipient.displayName}
        </Typography>
        <Typography>
          {getSubmissionStatusText(submission.status, customStatusHandler)}
        </Typography>
      </Box>
      <Box>
        <StatusIndicator
          status={submission.status}
          mapStatusToTooltip={{
            [IN_PROGRESS]: "In Progress",
            [NO_INTEGRATION]: "Not Submitted",
            [ACKNOWLEDGED]: "Submitted",
            [NO_ACK_INTEGRATION]: "Submitted",
            [FAILURE]: "Error",
            [REJECTED]: "Rejected",
          }}
        />
      </Box>
    </ListItem>
  );
};
