import {
  Box,
  Container,
  createStyles,
  makeStyles,
  Theme,
  Typography,
  useTheme,
} from "@material-ui/core";
import React from "react";
import clsx from "clsx";

const arrowWidth = 220;
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    containerWrapper: {
      width: "100%",
      height: "100%",
      backgroundColor: theme.palette.background.paper,
    },
    root: {
      display: "flex",
      flexDirection: "column",
      alignItems: "flex-start",
      width: "100%",
      height: "100%",
    },
    stateContainer: {
      display: "flex",
      flexDirection: "column",
      justifyContent: "flex-end",
      alignItems: "center",
      marginLeft: "auto",
      marginRight: "auto",
    },
    withArrow: {
      marginLeft: "auto",
      marginRight: "auto",
      position: "relative",
      left: -arrowWidth / 2,
      minWidth: 650,
      [theme.breakpoints.down("xs")]: {
        minWidth: "initial",
        left: "initial",
      },
    },
    stateIcon: {
      paddingBottom: theme.spacing(4),
    },
    arrow: {
      width: arrowWidth,
      marginLeft: theme.spacing(-5),
      [theme.breakpoints.down("xs")]: {
        display: "none",
      },
    },
    content: {
      display: "flex",
      width: "100%",
    },
    contentAlignCenter: {
      display: "flex",
      width: "100%",
      height: "100%",
      justifyContent: "center",
      alignItems: "center",
    },
  })
);

interface PageNoContentProps {
  primaryText: string;
  secondaryText?: string;
  disableMainIcon?: boolean;
  iconSrc?: string;
  alignContentCenter?: boolean;
  contentAboveText?: React.ReactNode;
  contentBelowText?: React.ReactNode;
  paddingTop?: number;
  classes?: {
    root?: string;
  };
}

export const PageNoContent: React.FC<PageNoContentProps> = ({
  primaryText,
  secondaryText,
  iconSrc = `${process.env.PUBLIC_URL}/static/media/svg/file-icon.svg`,
  disableMainIcon = false,
  alignContentCenter = false,
  contentAboveText,
  contentBelowText,
  paddingTop,
  classes: outerClasses,
}) => {
  const classes = useStyles();
  const theme = useTheme();

  return (
    <Box className={classes.containerWrapper}>
      <Container
        className={clsx(classes.root, outerClasses?.root)}
        data-testid="no-content-page"
      >
        <Box
          pt={`${alignContentCenter ? 0 : paddingTop || theme.spacing(11)}px`}
          className={
            alignContentCenter ? classes.contentAlignCenter : classes.content
          }
        >
          <Box className={clsx(classes.stateContainer)}>
            {!disableMainIcon && (
              <img
                src={iconSrc}
                alt="no-content-icon"
                className={classes.stateIcon}
              />
            )}
            {contentAboveText}
            <Typography variant="h6" align="center" color="textSecondary">
              {primaryText}
            </Typography>
            {secondaryText && (
              <Typography variant="body1" align="center" color="textSecondary">
                {secondaryText}
              </Typography>
            )}
            {contentBelowText}
          </Box>
        </Box>
      </Container>
    </Box>
  );
};
