import React from "react";
import { Box, createStyles, makeStyles, Theme } from "@material-ui/core";
import { ChevronSortDown32, ChevronSortUp32 } from "@carbon/icons-react";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    chevronIconWrapper: {
      position: "relative",
      "&:hover": {
        cursor: "pointer",
      },
    },
    chevronIconUp: {
      top: -10,
    },
    chevronIconDown: {
      top: -22,
    },
    icon: {
      position: "absolute",
      right: theme.spacing(3.5),
      color: theme.palette.primary.main,
    },
  })
);

interface TermToggleIconProps {
  show: boolean;
  onClick: () => void;
}

export const TermToggleIcon: React.FC<TermToggleIconProps> = ({
  show,
  onClick,
}) => {
  const classes = useStyles();
  return (
    <Box className={classes.chevronIconWrapper} onClick={onClick}>
      {show ? (
        <ChevronSortUp32
          className={clsx(classes.icon, classes.chevronIconUp)}
        />
      ) : (
        <ChevronSortDown32
          className={clsx(classes.icon, classes.chevronIconDown)}
        />
      )}
    </Box>
  );
};
