import React, { useEffect } from "react";
import { Redirect, RouteComponentProps } from "react-router";
import { parseDlrPayload } from "../../../utils/DLR";
import { useAppDispatch } from "../../../App.reducer";
import { paths } from "../../../utils/common";
import { addAlertAction } from "../Alerts/actions";
import { AlertType } from "../Alerts/actions.types";
import { getErrorMessages } from "../../../utils/errorHandler";

export const AuthError: React.FC<RouteComponentProps> = ({
  location: { search },
}) => {
  const dispatch = useAppDispatch();
  const payload = parseDlrPayload(search);

  useEffect(() => {
    if (payload) {
      const errorMessage = getErrorMessages().SERVER_ERROR;
      dispatch(addAlertAction(AlertType.ERROR, errorMessage));
    }
  }, [payload, dispatch]);

  return <Redirect to={paths.SIGN_IN} />;
};
