import React, { useEffect } from "react";
import {
  Box,
  createStyles,
  Grid,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { useLocation } from "react-router";
import {
  getAccountSelector,
  getChangeEmailConfirmLinkSelector,
  getCompanyDetailsSelector,
  getCompanyLinkSelector,
  getCompanySelector,
  getLegalProfileLoadingStateSelector,
  getUpdateCompanyDetailsLinkSelector,
  getUserIsSocAdminSelector,
} from "../../pages/SignIn/selectors";
import {
  AppDispatch,
  AppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { LoadingState } from "../../../types/enums";
import {
  getCountriesLoadingStateSelector,
  getCountriesSelector,
} from "../../pages/Company/selectors";
import {
  fetchCompanyDetailsEffect,
  fetchCountriesEffect,
} from "../../pages/Company/effects";

import { paths } from "../../../utils/common";
import { TabPanel } from "../../atoms/TabPanel";
import { ContainerTabPanel } from "../../atoms/ContainerTabPanel";
import { FormPublisherDetails } from "../FormPublisherDetails";
import { FormCompanyProfile } from "../FormCompanyProfile";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { TabMenu } from "../../molecules/TabMenu";
import { LoadingScreen } from "../../atoms/LoadingScreen";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    appBarContent: {
      width: "100%",
      minHeight: 48,
      marginTop: theme.spacing(1),
      display: "inline-flex",
      alignItems: "baseline",
      justifyContent: "space-between",
      transition: theme.transitions.create(["padding"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
  })
);

const getTabs = (isUserSocAdmin: boolean) => [
  {
    label: "Personal",
    link: paths.PROFILE_PERSONAL,
    breadcrumb: "Personal Details",
  },
  {
    label: isUserSocAdmin ? "Society Details" : "Publisher Details",
    link: paths.PROFILE_PUBLISHER,
    breadcrumb: isUserSocAdmin ? "Society Details" : "Publisher Details",
    access: getCompanyLinkSelector,
  },
];

interface PublisherProfileProps {
  dispatchEffect: AppDispatchEffect;
  dispatch: AppDispatch;
}

export const PublisherProfile: React.FC<PublisherProfileProps> = ({
  dispatchEffect,
  dispatch,
}) => {
  const classes = useStyles();
  const { pathname } = useLocation();
  const state = useAppSelector((state) => state);
  const { authPolicy } = state.signIn;
  const account = getAccountSelector(state);
  const company = getCompanySelector(state);
  const countriesLoadingState = getCountriesLoadingStateSelector(state);
  const countries = getCountriesSelector(state);
  const isUserSocAdmin = getUserIsSocAdminSelector(state);
  const canUpdatePublisherDetails = Boolean(
    getUpdateCompanyDetailsLinkSelector(state)
  );
  const companyDetails = getCompanyDetailsSelector(state);
  const legalProfileLoadingState = getLegalProfileLoadingStateSelector(state);
  const changeEmailConfirmLink = getChangeEmailConfirmLinkSelector(state);

  const filteredTabs = getTabs(isUserSocAdmin).filter(({ access }) =>
    access ? Boolean(access(state)) : true
  );
  const currentTab = filteredTabs.find(({ link }) => link.includes(pathname));

  useEffect(() => {
    if (countriesLoadingState === LoadingState.PRISTINE) {
      dispatchEffect(fetchCountriesEffect());
    }
  }, [countriesLoadingState, dispatchEffect, company]);

  useEffect(() => {
    if (pathname === paths.PROFILE_PUBLISHER) {
      dispatchEffect(fetchCompanyDetailsEffect());
    }
  }, [pathname, dispatchEffect]);

  return (
    <ContainerTabPanel>
      <ContainerAppBar
        dispatch={dispatch}
        parentTitle="Profile"
        title={currentTab?.breadcrumb}
        secondaryContent={
          <Box>
            <Grid container>
              <Grid item xs={7}>
                {!isUserSocAdmin ? (
                  <Typography variant="h4">{company?.name}</Typography>
                ) : (
                  <Box height={40} />
                )}
              </Grid>
              <Box className={classes.appBarContent}>
                <TabMenu tabs={filteredTabs} />
              </Box>
            </Grid>
          </Box>
        }
      />
      {legalProfileLoadingState === LoadingState.FETCHING ? (
        <LoadingScreen />
      ) : (
        <React.Fragment>
          <TabPanel value={pathname} index={paths.PROFILE_PERSONAL}>
            <FormCompanyProfile
              dispatchEffect={dispatchEffect}
              profileData={{
                firstName: account?.firstName || "",
                lastName: account?.lastName || "",
                email: account?.accountEmail || "",
              }}
              passwordPolicy={authPolicy?.passwordPolicy}
              changeEmailConfirmLink={changeEmailConfirmLink}
            />
          </TabPanel>
          <TabPanel value={pathname} index={paths.PROFILE_PUBLISHER}>
            <FormPublisherDetails
              countries={countries}
              editable={canUpdatePublisherDetails}
              dispatchEffect={dispatchEffect}
              ipiNameNumber={company?.ipiNameNumber}
              companyData={companyDetails?.address || {}}
              dispatch={dispatch}
            />
          </TabPanel>
        </React.Fragment>
      )}
    </ContainerTabPanel>
  );
};
