import React from "react";
import { FieldProps, getIn, insert, swap } from "formik";
import { DragDropContext, DropResult } from "react-beautiful-dnd";
import { AgreementShare } from "../../../utils/models";

export const FormikShareDragAndDrop: React.FC<FieldProps> = ({
  form: { setFieldValue, values },
  children,
}) => {
  const onDragEnd = (dropResults: DropResult) => {
    const { destination, draggableId, source } = dropResults;
    if (!destination) {
      return;
    }
    const shares = getIn(values, destination.droppableId);
    const isSameTermContext = destination.droppableId === source.droppableId;

    if (isSameTermContext && source.index === destination?.index) {
      return;
    }

    if (isSameTermContext && source.index !== destination?.index) {
      const newState = swap(shares, source.index, destination.index);
      return setFieldValue(destination.droppableId, newState);
    }

    const draggableShare = getIn(values, draggableId);
    const destinationState = insert(shares, destination.index, draggableShare);
    const sourceState = getIn(values, source.droppableId)?.filter(
      (_: AgreementShare, index: number) => index !== source.index
    );
    setFieldValue(source.droppableId, sourceState);
    setFieldValue(destination.droppableId, destinationState);
  };

  return <DragDropContext onDragEnd={onDragEnd}>{children}</DragDropContext>;
};
