import React from "react";
import { createStyles, Grid, makeStyles, Theme } from "@material-ui/core";
import clsx from "clsx";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    cell: {
      position: "relative",
      // paddingLeft: theme.spacing(1),
    },
    header: {
      paddingBottom: theme.spacing(1.5),
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
  })
);

interface CellRoleSummaryProps {
  className?: string;
  header?: boolean;
}

export const CellRoleSummary: React.FC<CellRoleSummaryProps> = ({
  children,
  className,
  header = false,
}) => {
  const classes = useStyles();
  return (
    <Grid
      item
      xs={4}
      className={clsx(classes.cell, className, { [classes.header]: header })}
    >
      {children}
    </Grid>
  );
};
