import React, { useCallback, useEffect, useRef, useState } from "react";
import TextField, { TextFieldProps } from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import {
  Box,
  CircularProgress,
  createStyles,
  debounce,
  IconButton,
  InputAdornment,
  makeStyles,
  Popper,
  PopperProps,
  Theme,
} from "@material-ui/core";
import { AutocompleteRenderInputParams } from "@material-ui/lab/Autocomplete/Autocomplete";
import { AutocompleteProps } from "@material-ui/lab";
import axios from "axios";
import clsx from "clsx";
import { Close24, Search24 } from "@carbon/icons-react";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    loader: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
    popupIndicatorOpen: {
      transform: "none",
    },
    clearIndicator: {
      padding: theme.spacing(0.25),
    },
    clearIndicatorIcon: {
      color: theme.palette.info.light,
    },
    clearIndicatorIconSelected: {
      color: theme.palette.info.light,
    },
    searchIcon: {
      marginRight: theme.spacing(1.25),
      color: theme.palette.info.light,
    },
  })
);

const PopperComponent = (props: PopperProps) => (
  <Popper
    {...props}
    modifiers={{
      flip: {
        enabled: false,
      },
    }}
  />
);

interface AutocompleteSelectProps<Option, ApiResponse> {
  label?: string;
  apiSource?: string;
  setApiResponseToOptions?: (response: ApiResponse) => Option[];
  onSelect?: (value: Option | null) => void;
  value?: Option;
  textFieldProps?: TextFieldProps;
  renderInput?: (params: AutocompleteRenderInputParams) => React.ReactNode;
  options?: Option[];
  freeSolo?: boolean;
}

export const AutocompleteSelect = <Option, ApiResponse = undefined>(
  props: AutocompleteSelectProps<Option, ApiResponse> &
    Omit<
      AutocompleteProps<Option, false, false, true>,
      "renderInput" | "onSelect" | "options" | "freeSolo"
    >
) => {
  const {
    label,
    apiSource,
    setApiResponseToOptions,
    onSelect,
    textFieldProps,
    renderInput,
    options = [],
    loading: loadingProp = false,
    classes: classesProp,
    value: valueProp,
    freeSolo = true,
    onChange,
    ...rest
  } = props;
  const classes = useStyles();
  const componentIsMounted = useRef(true);
  const [loading, setLoading] = useState(apiSource ? false : loadingProp);
  const [value, setValue] = useState(valueProp || null);
  const [apiResultOptions, setApiResultOptions] = useState<Option[]>([]);

  const handleApiSearch = async (value: string) => {
    setApiResultOptions([]);

    if (value.length === 0) {
      return;
    }

    if (setApiResponseToOptions && apiSource) {
      setLoading(true);
      const url = apiSource.replace("{query}", value);
      try {
        const response: { data: ApiResponse } = await axios.get(url, {
          withCredentials: true,
        });
        if (componentIsMounted.current) {
          setLoading(false);
          setApiResultOptions(setApiResponseToOptions(response.data));
        }
      } catch (e) {
        if (componentIsMounted.current) {
          setLoading(false);
          setApiResultOptions([]);
        }
      }
    } else {
      setLoading(false);
    }
  };

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const debouncedHandleApiSearch = useCallback(
    debounce(handleApiSearch, 500),
    []
  );

  const handleOnClose = () => {
    if (apiSource) {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (!apiSource) {
      setLoading(loadingProp);
    }
  }, [apiSource, loadingProp]);

  useEffect(() => {
    setValue(valueProp || null);
  }, [valueProp]);

  useEffect(() => {
    return () => {
      componentIsMounted.current = false;
    };
  }, []);

  return (
    <Autocomplete<Option, false, false, true>
      id={`auto-complete-${label}`}
      freeSolo={freeSolo || undefined}
      selectOnFocus
      clearOnBlur
      handleHomeEndKeys
      value={value}
      options={apiSource ? apiResultOptions : options}
      loading={loading}
      onClose={handleOnClose}
      onChange={(e, value, reason, details) => {
        onSelect && typeof value !== "string" && onSelect(value);
        if (reason === "clear") {
          setApiResultOptions([]);
        }
        onChange && onChange(e, value, reason, details);
      }}
      classes={{
        popupIndicatorOpen: clsx(apiSource && classes.popupIndicatorOpen),
        clearIndicator: classes.clearIndicator,
        ...classesProp,
      }}
      loadingText={
        <Box className={classes.loader}>
          <CircularProgress color="primary" />
        </Box>
      }
      closeIcon={!value && <Close24 className={classes.clearIndicatorIcon} />}
      PopperComponent={PopperComponent}
      renderInput={
        renderInput ||
        ((params) => (
          <TextField
            {...params}
            label={label}
            fullWidth
            autoFocus={rest.openOnFocus}
            {...textFieldProps}
            onChange={(event) => {
              textFieldProps?.onChange && textFieldProps.onChange(event);
              if (apiSource) {
                debouncedHandleApiSearch(event.target.value.trim());
              }
            }}
            InputProps={{
              ...params.InputProps,
              ...textFieldProps?.InputProps,
              ...(freeSolo
                ? {
                    endAdornment: (
                      <React.Fragment>
                        {textFieldProps?.InputProps?.endAdornment}
                        <InputAdornment position="end">
                          <Box
                            onClick={() => {
                              if (apiSource) {
                                setApiResultOptions([]);
                              }
                            }}
                          >
                            {params.InputProps.endAdornment}
                          </Box>
                          {value ? (
                            <IconButton
                              onClick={() => {
                                setValue(null);
                                onSelect && onSelect(null);
                              }}
                            >
                              <Close24
                                className={classes.clearIndicatorIconSelected}
                              />
                            </IconButton>
                          ) : (
                            <Search24 className={classes.searchIcon} />
                          )}
                        </InputAdornment>
                      </React.Fragment>
                    ),
                  }
                : {}),
            }}
          />
        ))
      }
      {...rest}
    />
  );
};
