import React, { useEffect, useRef, useState } from "react";
import { Grid, ListItem, ListItemText, Typography } from "@material-ui/core";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { StatusIndicator } from "../../atoms/StatusIndicator";
import { TooltipMap } from "../../atoms/TooltipMap";
import { format } from "date-fns";
import { ListItemRippleDisabler } from "../../atoms/ListItemRippleDisabler";
import { ActionMenu } from "../../organisms/ActionMenu";
import { WorkListItem } from "../../../utils/models";
import { useTableWorksStyles } from "../../organisms/HeaderTableWorks";
import { isElementOverflowed } from "../../../utils/helpers";
import { Icon } from "../../atoms/Icon";
import { workListTooltipStatusMap } from "../../../utils/common";

interface ListItemWorkProps {
  work: WorkListItem;
  onClick: () => void;
  onCreateTemplateFromWork: () => void;
  onDuplicate: () => void;
  onLeaveWork: () => void;
  canLeaveWork: boolean;
  canDuplicateWork?: boolean;
  canCreateWorkFromTemplate?: boolean;
  windowWidth: number;
  hideBreakpoint: boolean;
  onDownloadCwr?: () => void;
}

export const ListItemWork: React.FC<ListItemWorkProps> = ({
  work: { guid, title, created, creators, updated, status },
  canLeaveWork,
  onLeaveWork,
  hideBreakpoint,
  windowWidth,
  canCreateWorkFromTemplate,
  canDuplicateWork,
  onDuplicate,
  onClick,
  onCreateTemplateFromWork,
  onDownloadCwr,
}) => {
  const classes = useTableWorksStyles();
  const [disableRipple, setDisableRipple] = useState(false);
  const titleRef = useRef<HTMLSpanElement>();
  const creatorsRef = useRef<HTMLParagraphElement>();
  const [showTitleTooltip, setShowTitleTooltip] = useState(false);
  const [showCreatorsTooltip, setCreatorsTitleTooltip] = useState(false);
  const creatorsText = creators?.map((el: any) => el.displayName).join(", ");

  const actionMenuOptions = [
    ...(canDuplicateWork ? [{ label: "Duplicate", onClick: onDuplicate }] : []),
    ...(canCreateWorkFromTemplate
      ? [
          {
            label: "Create template",
            onClick: onCreateTemplateFromWork,
          },
        ]
      : []),
    ...(canLeaveWork ? [{ label: "Leave work", onClick: onLeaveWork }] : []),
    ...(onDownloadCwr
      ? [{ label: "Download CWR", onClick: onDownloadCwr }]
      : []),
  ];

  useEffect(() => {
    const isTitleOverflowed = isElementOverflowed(titleRef);
    const isCreatorsOverflowed = isElementOverflowed(creatorsRef);
    setShowTitleTooltip(isTitleOverflowed);
    setCreatorsTitleTooltip(isCreatorsOverflowed);
  }, [windowWidth, titleRef, creatorsRef]);

  return (
    <ListItem
      button
      disableGutters
      className={classes.listRow}
      disableRipple={disableRipple}
      onClick={onClick}
    >
      <ContainerListItem leftOffset={hideBreakpoint ? 5.5 : 0}>
        <Grid item className={classes.statusColumn}>
          {status && (
            <StatusIndicator
              status={status}
              className={classes.status}
              mapStatusToTooltip={workListTooltipStatusMap}
            />
          )}
        </Grid>
        <Grid item className={classes.nameColumn}>
          <ListItemText
            className={classes.workName}
            classes={{ multiline: classes.listItemText }}
            primary={
              <Typography
                innerRef={titleRef}
                variant="subtitle1"
                color="primary"
              >
                {showTitleTooltip ? (
                  <TooltipMap variant="listItems" tooltipText={title}>
                    {title}
                  </TooltipMap>
                ) : (
                  title
                )}
              </Typography>
            }
            secondary={
              creatorsText && (
                <Typography
                  innerRef={creatorsRef}
                  color="textSecondary"
                  variant="body2"
                >
                  {showCreatorsTooltip ? (
                    <TooltipMap variant="listItems" tooltipText={creatorsText}>
                      {creatorsText}
                    </TooltipMap>
                  ) : (
                    creatorsText
                  )}
                </Typography>
              )
            }
          />
        </Grid>
        {hideBreakpoint && (
          <React.Fragment>
            <Grid item className={classes.iswcColumn}>
              <Typography color="textSecondary" noWrap />
            </Grid>
            <Grid item className={classes.lastUpdatedColumn}>
              <Typography
                variant="subtitle1"
                color="textSecondary"
                noWrap
                data-testid="updated-time"
              >
                {updated && format(new Date(updated.iso), "MMM dd, yyyy")}
              </Typography>
            </Grid>
            <Grid item className={classes.createdColumn}>
              <Typography
                variant="subtitle1"
                color="textSecondary"
                noWrap
                data-testid="created-time"
              >
                {created && format(new Date(created.iso), "MMM dd, yyyy")}
              </Typography>
            </Grid>
          </React.Fragment>
        )}
        <Grid item className={classes.optionsColumn}>
          <ListItemRippleDisabler
            setDisableRipple={setDisableRipple}
            onClick={(e) => e.stopPropagation()}
          >
            {actionMenuOptions.length > 0 && (
              <ActionMenu items={actionMenuOptions}>
                <Icon
                  name="listItemOptions"
                  data-testid={`work.${guid}.options`}
                />
              </ActionMenu>
            )}
          </ListItemRippleDisabler>
        </Grid>
      </ContainerListItem>
    </ListItem>
  );
};
