import React from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core";
import { FabExtended } from "../../atoms/FabExtended";
import { ActionMenu } from "../../organisms/ActionMenu";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    fab: {
      zIndex: 1,
      position: "fixed",
      bottom: theme.spacing(4),
      right: theme.spacing(4),
      paddingTop: theme.spacing(2),
    },
  })
);

interface MenuAddWorkAgreementProps {
  onOpenAgreementNumber: () => void;
  onOpenSubPublishingAgreementNumber: () => void;
}

export const MenuAddWorkAgreement: React.FC<MenuAddWorkAgreementProps> = ({
  onOpenAgreementNumber,
  onOpenSubPublishingAgreementNumber,
}) => {
  const classes = useStyles();
  return (
    <ActionMenu
      items={[
        { label: "Agreement Number", onClick: onOpenAgreementNumber },
        {
          label: "Sub-Publishing Agreement Number",
          onClick: onOpenSubPublishingAgreementNumber,
        },
      ]}
      wrapperClassName={classes.fab}
      anchorOrigin={{
        vertical: "top",
        horizontal: "left",
      }}
      transformOrigin={{
        vertical: "bottom",
        horizontal: "left",
      }}
    >
      <FabExtended>Add Agreement Number</FabExtended>
    </ActionMenu>
  );
};
