import React from "react";
import { Box, Button, Grid } from "@material-ui/core";
import { paths } from "../../../utils/common";
import { useHistory } from "react-router-dom";
import { CenteredPaper } from "../../atoms/CenteredPaper";

interface SignUpVariantProps {}

export const SignUpVariant: React.FC<SignUpVariantProps> = () => {
  const { push } = useHistory();

  const handleCreator = () => {
    push(`${paths.SIGN_UP}/${paths.subPage.CREATOR}`);
  };

  const handlePublisher = () => {
    push(`${paths.SIGN_UP}/${paths.subPage.COMPANY}`);
  };

  return (
    <CenteredPaper>
      <Grid container>
        <Grid item xs={12} component={Box} pt={1} pb={2}>
          <Button
            variant="contained"
            color="primary"
            fullWidth
            onClick={handleCreator}
          >
            Creator
          </Button>
        </Grid>
        <Grid item xs={12} component={Box} pt={2}>
          <Button
            variant="contained"
            color="primary"
            fullWidth
            onClick={handlePublisher}
          >
            Publisher
          </Button>
        </Grid>
      </Grid>
    </CenteredPaper>
  );
};
