import React from "react";
import { ButtonFabAddNew } from "../../atoms/ButtonFabAddNew";
import { paths } from "../../../utils/common";
import { createStyles, makeStyles, Theme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      position: "absolute",
      bottom: theme.spacing(4),
      right: theme.spacing(4),
      zIndex: 1,
    },
  })
);

interface AgreementsFabProps {
  isSocietyAdmin: boolean;
  isTemplatesPage: boolean;
  isAgreementsPage: boolean;
  isDraftsPage: boolean;
  canCreateAgreement: boolean;
}

export const AgreementsFab: React.FC<AgreementsFabProps> = ({
  isDraftsPage,
  isTemplatesPage,
  canCreateAgreement,
  isAgreementsPage,
  isSocietyAdmin,
}) => {
  const classes = useStyles();
  if (isSocietyAdmin) {
    return null;
  }
  if (isTemplatesPage) {
    return (
      <ButtonFabAddNew
        to={paths.AGREEMENTS_REGISTER}
        text="New Template"
        className={classes.root}
      />
    );
  }
  if ((isAgreementsPage || isDraftsPage) && canCreateAgreement) {
    return (
      <ButtonFabAddNew
        to={paths.AGREEMENTS_REGISTER}
        text="New Agreement"
        className={classes.root}
      />
    );
  }
  return <React.Fragment />;
};
