import React from "react";
import {
  Box,
  Button,
  createStyles,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  makeStyles,
  Theme,
} from "@material-ui/core";
import { TextField } from "formik-material-ui";
import * as yup from "yup";
import { Field, Form, Formik } from "formik";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { Dialog } from "../Dialog";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dialog: (props: { width: number }) => ({
      maxWidth: props.width,
      width: "100%",
    }),
  })
);

interface DialogCreateAgreementFromTemplateProps {
  onSubmit: (title: string) => void;
  onCancel?: () => void;
  width?: number;
  open: boolean;
}
export const DialogCreateAgreementFromTemplate: React.FC<DialogCreateAgreementFromTemplateProps> = ({
  onSubmit,
  onCancel,
  width = 280,
  open,
}) => {
  const classes = useStyles({ width });
  return (
    <Dialog
      variant="complex"
      open={open}
      onClose={onCancel}
      classes={{ paper: classes.dialog }}
    >
      <DialogTitle>Create agreement from template</DialogTitle>
      <DialogContent>
        <Box pb={1}>Provide a title for your new agreement.</Box>
        <Formik
          onSubmit={({ title }) => onSubmit(title)}
          validationSchema={yup.object().shape({
            title: yup.string().required("Required"),
          })}
          initialValues={{ title: "" }}
        >
          {({ isSubmitting }) => (
            <Form data-testid="form">
              <Box pt={1}>
                <Grid container>
                  <Field
                    component={TextField}
                    inputProps={{
                      "data-testid": "title-input",
                    }}
                    autoFocus
                    name="title"
                    margin="dense"
                    label="Agreement title"
                    fullWidth
                    variant="filled"
                  />
                  <DialogActions>
                    <Button variant="text" color="primary" onClick={onCancel}>
                      Cancel
                    </Button>
                    <ButtonSubmit
                      variant="text"
                      color="primary"
                      submitting={isSubmitting}
                    >
                      Create
                    </ButtonSubmit>
                  </DialogActions>
                </Grid>
              </Box>
            </Form>
          )}
        </Formik>
      </DialogContent>
    </Dialog>
  );
};
