import React, { useEffect, useState } from "react";
import {
  Box,
  Button,
  createStyles,
  DialogActions,
  DialogContent,
  DialogTitle,
  makeStyles,
  Theme,
  Typography
} from "@material-ui/core";
import {
  _WorkRegistrationSubmissionLinks,
  IN_PROGRESS,
  NO_INTEGRATION,
  WorkRegistration,
  WorkRegistrationSubmission
} from "../../../utils/models";
import { AppDispatchEffect } from "../../../App.reducer";
import { fetchWorkRegistrationSubmissionsEffect } from "../../pages/Works/effects";
import { Skeleton } from "@material-ui/lab";
import { ListItemWorkSubmission } from "../ListItemWorkSubmission/component";
import { ButtonDownloadCwr } from "../../atoms/ButtonDownloadCwr";
import { Dialog } from "../Dialog";
import { LoadingState } from "../../../types/enums";
import Promise from "promise-polyfill";
import { delay } from "../../../utils/helpers";
import { FetchWorkRegistrationSubmissionsResponse } from "../../../api.types";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dialog: {
      width: "100%",
      maxWidth: 832,
      height: 560,
    },
    listWrapper: {
      display: "flex",
      flexDirection: "column",
    },
    mySocietiesTitle: {
      paddingBottom: theme.spacing(1),
    },
    otherSocietiesTitle: {
      paddingBottom: theme.spacing(1),
      paddingTop: theme.spacing(3),
    },
    skeletonTextLoader: {
      height: theme.spacing(2),
      marginBottom: theme.spacing(1),
      width: 150,
    },
    skeletonLoader: {
      marginBottom: theme.spacing(1),
      height: theme.spacing(8.5),
      width: "100%",
    },
  })
);

interface DialogSubmissionsProps {
  showDialog: boolean;
  hideDialog: () => void;
  workRegistration?: WorkRegistration;
  dispatchEffect: AppDispatchEffect;
  onDownloadCwr: (link: string) => void;
}

const hasPendingSubmission = (submissions: WorkRegistrationSubmission[]) =>
  submissions.find((el) => el.status === IN_PROGRESS);

export const DialogSubmissions: React.FC<DialogSubmissionsProps> = ({
  showDialog,
  hideDialog,
  workRegistration,
  dispatchEffect,
  onDownloadCwr,
}) => {
  const classes = useStyles();
  const [loadingState, setLoadingState] = useState(LoadingState.PRISTINE);
  const [submissions, setSubmissions] = useState<{
    items: WorkRegistrationSubmission[];
    _links: _WorkRegistrationSubmissionLinks;
  }>({ items: [], _links: {} });
  const [isRefetching, setIsRefetching] = useState(false);

  const mySocieties: WorkRegistrationSubmission[] = submissions.items.filter(
    (el) => el.isMine
  );
  const otherSocieties: WorkRegistrationSubmission[] = submissions.items.filter(
    (el) => !el.isMine
  );

  const handleDownloadCwr = (
    { workGuid, workVersion }: WorkRegistration,
    link: string
  ) => () => {
    const formattedLink = link
      .replace("{workGuid}", workGuid)
      .replace("{workVersion}", workVersion);
    onDownloadCwr(formattedLink);
  };

  const fetchSubmissionsEffect: (
    registration: WorkRegistration
  ) => Promise<FetchWorkRegistrationSubmissionsResponse> = (registration) =>
    dispatchEffect(
      fetchWorkRegistrationSubmissionsEffect({
        workGuid: registration.workGuid,
        workVersion: registration.workVersion,
      })
    );

  const refetchPendingSubmissions: (
    registration: WorkRegistration,
    submissions: WorkRegistrationSubmission[]
  ) => void = async (registration, submissions) => {
    setIsRefetching(true);
    await delay(3000);
    if (!showDialog) {
      return;
    }
    const {
      response: {
        workSubmission: { items, _links },
      },
    } = await fetchSubmissionsEffect(registration);
    if (hasPendingSubmission(items)) {
      refetchPendingSubmissions(registration, items);
    } else {
      setSubmissions({ items, _links });
    }
  };

  const fetchSubmissions = async () => {
    if (workRegistration) {
      setLoadingState(LoadingState.FETCHING);
      try {
        let response = await fetchSubmissionsEffect(workRegistration);
        if (!response.response.workSubmission.items.length) {
          await delay(3000);
          response = await fetchSubmissionsEffect(workRegistration);
        }
        const { items, _links } = response.response.workSubmission;
        setSubmissions({ items, _links });
      } finally {
        setLoadingState(LoadingState.FETCHED);
      }
    }
  };

  useEffect(() => {
    if (showDialog && workRegistration && !isRefetching) {
      fetchSubmissions();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [workRegistration, showDialog]);

  useEffect(() => {
    if (
      showDialog &&
      loadingState === LoadingState.FETCHED &&
      workRegistration &&
      hasPendingSubmission(submissions.items)
    ) {
      refetchPendingSubmissions(workRegistration, submissions.items);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [workRegistration, submissions, loadingState, showDialog]);

  return (
    <Dialog
      variant="complex"
      open={showDialog}
      onClose={hideDialog}
      PaperProps={{ className: classes.dialog }}
      scroll="paper"
    >
      <DialogTitle>Society Submissions</DialogTitle>
      <DialogContent>
        <Box className={classes.listWrapper}>
          {[LoadingState.PRISTINE, LoadingState.FETCHING].includes(
            loadingState
          ) && (
            <Box pt={1}>
              <Skeleton className={classes.skeletonTextLoader} variant="rect" />
              <Skeleton className={classes.skeletonLoader} variant="rect" />
              <Skeleton className={classes.skeletonLoader} variant="rect" />
            </Box>
          )}

          {submissions.items.length === 0 &&
            loadingState === LoadingState.FETCHED && (
              <Box pt={17}>
                <Typography variant="h6" color="textSecondary" align="center">
                  No Submissions
                </Typography>
                <Typography
                  variant="body1"
                  color="textSecondary"
                  align="center"
                >
                  See registration errors
                </Typography>
              </Box>
            )}

          {submissions.items.length > 0 &&
            loadingState === LoadingState.FETCHED && (
              <React.Fragment>
                {mySocieties.length ? (
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    className={classes.mySocietiesTitle}
                  >
                    My Society
                  </Typography>
                ) : null}
                {mySocieties.map((submission) => (
                  <ListItemWorkSubmission
                    key={`${submission.recipient.guid}${submission.created.timestamp}`}
                    submission={submission}
                    customStatusHandler={(status) => {
                      if (status === NO_INTEGRATION) {
                        return (
                          <React.Fragment>
                            Your society is not integrated with Session, <br />
                            please download your CWR and submit to them
                            manually.
                          </React.Fragment>
                        );
                      }
                    }}
                  />
                ))}
                {otherSocieties.length ? (
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    className={classes.otherSocietiesTitle}
                  >
                    Other Societies
                  </Typography>
                ) : null}
                {otherSocieties.map((submission) => (
                  <ListItemWorkSubmission
                    key={`${submission.recipient.guid}${submission.created.timestamp}`}
                    submission={submission}
                  />
                ))}
              </React.Fragment>
            )}
        </Box>
      </DialogContent>
      <DialogActions>
        {workRegistration && submissions._links.downloadCwr?.href && (
          <ButtonDownloadCwr
            onClick={handleDownloadCwr(
              workRegistration,
              submissions._links.downloadCwr.href
            )}
          />
        )}
        <Button variant="contained" color="primary" onClick={hideDialog}>
          Close
        </Button>
      </DialogActions>
    </Dialog>
  );
};
