import { TemplateListItem } from "../../../utils/models";
import { RefObject, useRef } from "react";
import { isElementOverflowed } from "../../../utils/helpers";

type ShowCreatorNameTooltip = { [index: number]: boolean };
type CreatorNameRefs = {
  [index: number]: RefObject<HTMLSpanElement>;
};

export const showCreatorNameTooltipData: (
  template: TemplateListItem
) => ShowCreatorNameTooltip = (template) =>
  (template.creators || []).reduce(
    (acc, cur) => ({
      ...acc,
      [cur.ipiNameNumber]: false,
    }),
    {}
  );

export const creatorNameRefsData: (
  template: TemplateListItem
) => CreatorNameRefs = (template) =>
  (template.creators || []).reduce(
    (acc, cur) => ({
      ...acc,
      [cur.ipiNameNumber]: useRef<HTMLSpanElement>(),
    }),
    {}
  );

export const updateCreatorNameTooltipData: (
  showCreatorNameTooltip: ShowCreatorNameTooltip,
  creatorNameRefs: CreatorNameRefs
) => ShowCreatorNameTooltip = (showCreatorNameTooltip, creatorNameRefs) =>
  Object.keys(showCreatorNameTooltip).reduce(
    (acc, cur) => ({
      ...acc,
      [cur]: isElementOverflowed(creatorNameRefs[Number(cur)]),
    }),
    {}
  );
