import React from "react";
import { useAppDispatchEffect, useAppSelector } from "../../../App.reducer";
import { RouteComponentProps } from "react-router";
import { LoadingState } from "../../../types/enums";
import {
  getTemplatesListLinks,
  getTemplatesLoadingStateSelector,
  getTemplatesSelector,
} from "./selectors";
import { PageNoContent } from "../../molecules/PageNoContent";
import { ListTemplates } from "../../organisms/ListTemplates";
import { getWorksListLinks } from "../ListWorks/selectors";
import { LoadingScreen } from "../../atoms/LoadingScreen";

export const Templates: React.FC<RouteComponentProps> = ({ history }) => {
  const dispatchEffect = useAppDispatchEffect();
  const state = useAppSelector((state) => state);
  const templates = getTemplatesSelector(state);
  const loadingState = getTemplatesLoadingStateSelector(state);
  const canCreateTemplate = Boolean(getTemplatesListLinks(state)?.create);
  const canCreateWork = Boolean(getWorksListLinks(state)?.create);
  const canDeleteTemplate = Boolean(getTemplatesListLinks(state)?.delete);

  if (
    loadingState === LoadingState.PRISTINE ||
    loadingState === LoadingState.FETCHING
  ) {
    return <LoadingScreen />;
  }

  return loadingState === LoadingState.FETCHED && templates.length === 0 ? (
    <PageNoContent
      primaryText="No Templates"
      secondaryText="Click the + button to create a new template"
    />
  ) : (
    <ListTemplates
      dispatchEffect={dispatchEffect}
      templates={templates}
      history={history}
      canCreateWorkFromTemplate={canCreateWork}
      canCreateTemplate={canCreateTemplate}
      canDeleteTemplate={canDeleteTemplate}
    />
  );
};
