import React from "react";
import { Redirect, useLocation } from "react-router";
import { paths } from "../../../utils/common";
import "url-polyfill";
import { parseDlrPayload } from "../../../utils/DLR";

interface WithDLRProps {
  dlr: { [path: string]: string };
}

export const WithDLR: React.FC<WithDLRProps> = ({ dlr, children }) => {
  const { pathname, search } = useLocation();

  const handleDLR = () => {
    const payload = parseDlrPayload(search);

    if (payload && dlr[payload.action]) {
      return `${dlr[payload.action]}${search}`;
    }

    return paths.INDEX;
  };

  return pathname.startsWith("/dlr") ? (
    <Redirect to={handleDLR()} />
  ) : (
    <React.Fragment>{children}</React.Fragment>
  );
};
