import { LoadingState } from "../../../types/enums";
import { DRAFT } from "../../../utils/models";
import { AppState } from "../../../App.reducer";
import {
  getAgreementsLoadingStateSelector,
  getAgreementsSelector,
} from "../AgreementRegistration/selectors";
import {
  getAgreementTemplatesLoadingStateSelector,
  getAgreementTemplatesSelector,
} from "../AgreementTemplates/selectors";
import { paths } from "../../../utils/common";

export const isSearchDisabled = (
  search: string,
  pathname: string,
  state: AppState
) => {
  if (search) {
    return false;
  }

  const agreements = getAgreementsSelector(state);
  const templates = getAgreementTemplatesSelector(state);
  const agreementsLoadingState = getAgreementsLoadingStateSelector(state);
  const templatesLoadingState = getAgreementTemplatesLoadingStateSelector(
    state
  );
  const isDraftsPage = pathname === paths.AGREEMENTS_DRAFT_LIST;
  const isAgreementsPage = pathname === paths.AGREEMENTS_LIST;
  const isTemplatesPage = pathname === paths.AGREEMENTS_TEMPLATE_LIST;

  if (isDraftsPage) {
    return (
      agreementsLoadingState === LoadingState.FETCHED &&
      agreements.filter((el) => el.status === DRAFT).length === 0
    );
  }

  if (isAgreementsPage) {
    return (
      agreementsLoadingState === LoadingState.FETCHED &&
      agreements.filter((el) => el.status !== DRAFT).length === 0
    );
  }

  if (isTemplatesPage) {
    return (
      templatesLoadingState === LoadingState.FETCHED && templates.length === 0
    );
  }

  return false;
};
