import React from "react";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { GridListHeaderItem } from "../../atoms/GridListHeaderItem";
import clsx from "clsx";

export const useAgreementsListStyles = makeStyles<Theme>((theme) =>
  createStyles({
    containerListItem: {
      paddingLeft: theme.spacing(8.5),
      [theme.breakpoints.only("xs")]: {
        paddingLeft: theme.spacing(3)
      },
    },
    listRow: {
      minHeight: 85,
    },
    statusColumn: {
      minWidth: 50,
      maxWidth: 50,
      textAlign: "center",

      [theme.breakpoints.only("xs")]: {
        minWidth: 40,
        maxWidth: 40,
      },
    },
    agreementColumn: {
      position: "relative",
      width: "100%",
      maxWidth: 245,
      minWidth: 150,
      textOverflow: "ellipsis",
      "& span, & p": {
        overflow: "hidden",
        textOverflow: "ellipsis",
        whiteSpace: "nowrap",
      },
    },
    assignorColumn: {
      width: "100%",
      maxWidth: 245,
      minWidth: 150,
      padding: theme.spacing(0, 2),
    },
    assigneeColumn: {
      width: "100%",
      maxWidth: 245,
      minWidth: 150,
      padding: theme.spacing(0, 2),
    },
    updatedColumn: {
      width: "100%",
      maxWidth: 245,
      minWidth: 150,
      padding: theme.spacing(0, 2),
    },
    createdColumn: {
      width: "100%",
      maxWidth: 245,
      minWidth: 150,
      padding: theme.spacing(0, 2),
    },
    optionsColumn: {
      marginLeft: "auto",
      minWidth: 30,
    },
    buttonColumn: {
      minWidth: 132,
      maxWidth: 160,
      width: "100%",
    },
    button: {
      paddingTop: 0,
      paddingBottom: 0,
      minWidth: 129,
    },
    menuIcon: {
      color: theme.palette.info.main,
    },
    status: {
      display: "flex",
      justifyContent: "flex-start",
    },
  })
);

interface ListHeaderAgreementsProps {
  hideColumn?: { status?: boolean };
  className?: string;
}

export const ListHeaderAgreements: React.FC<ListHeaderAgreementsProps> = ({
  hideColumn,
  className,
}) => {
  const classes = useAgreementsListStyles();
  return (
    <ContainerListItem
      data-testid="agreements-list"
      className={clsx(className, classes.containerListItem)}
    >
      {!hideColumn?.status && (
        <GridListHeaderItem item className={classes.statusColumn} />
      )}
      <GridListHeaderItem className={classes.agreementColumn}>
        <ListColumnTitle>Agreement</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.assignorColumn}>
        <ListColumnTitle>Assignor</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.assigneeColumn}>
        <ListColumnTitle>Assignee</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.assigneeColumn}>
        <ListColumnTitle>Updated</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.assigneeColumn}>
        <ListColumnTitle>Created</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.optionsColumn} />
    </ContainerListItem>
  );
};
