import React, { useState } from "react";
import { AgreementMember, IpiSearchCreatorOption } from "../../../utils/models";
import {
  Box,
  Button,
  createStyles,
  makeStyles,
  Tab,
  Tabs,
  Theme,
} from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import { EntityType, IdentityType } from "../../../types/enums";
import { FieldProps } from "formik";
import { FetchWritersResponse } from "../../../api.types";
import { CardPublisher } from "../CardPublisher";
import { AgreementFormValues } from "../../organisms/FormAgreement";
import { AvatarTitle } from "../AvatarTitle";
import { SearchBox } from "../../organisms/SearchBox";
import { getHexColorWithOpacity } from "../../pages/Theme/utils";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    tab: {
      minWidth: "50%",
    },
    nonActiveTab: {
      color: getHexColorWithOpacity(theme.palette.primary.main, 75),
    },
  })
);

interface FormikAddAgreementMemberProps {
  hasAssignee: boolean;
  hasAssignor: boolean;
  disabled?: boolean;
  apiSourcePublisher: string;
  apiSourceCreator: string;
}

export const FormikAddAgreementMember: React.FC<
  FormikAddAgreementMemberProps & FieldProps<any, AgreementFormValues>
> = ({
  form: { setFieldValue, values },
  hasAssignee,
  hasAssignor,
  disabled,
  apiSourceCreator,
  apiSourcePublisher,
}) => {
  const classes = useStyles();
  const [anchor, setAnchor] = useState<HTMLElement | null>(null);
  const [searchPhrase, setSearchPhrase] = useState("");
  const [apiSource, setApiSource] = useState<IdentityType>(
    IdentityType.CREATOR
  );
  const unavailableIpiNumbers = [
    Number(values.assignee?.ipiNameNumber),
    Number(values.assignor?.ipiNameNumber),
  ];

  const disableAssigneeShare = () => {
    const terms = values.terms.map((el) => ({
      ...el,
      shares: el.shares
        ? el.shares.map((share) => ({ ...share, assigneeCollectibleShare: 0 }))
        : [],
    }));
    setFieldValue("terms", terms);
  };

  const handleSelect = (option: IpiSearchCreatorOption) => {
    const isAssigneeRoleTaken = Boolean(values.assignee);
    const isAssignorRoleTaken = Boolean(values.assignor);

    const member: AgreementMember = {
      type: apiSource,
      ipiNameNumber: option.ipiNameNumber,
      displayName: option.profile.displayName,
      permissions: {
        read: false,
        write: false,
        owner: false,
      },
    };

    if (apiSource === IdentityType.CREATOR) {
      disableAssigneeShare();
      setFieldValue("assignor", member);
      if (isAssignorRoleTaken) {
        setFieldValue("assignee", values.assignor);
      }
    } else {
      setFieldValue(isAssigneeRoleTaken ? "assignor" : "assignee", member);
    }
  };

  const apiSourceType =
    apiSource === IdentityType.CREATOR ? apiSourceCreator : apiSourcePublisher;

  const searchLink = `${apiSourceType.replace(
    /{.*.}/,
    ""
  )}&searchPhrase={query}`;

  return (
    <React.Fragment>
      <Box width={160}>
        <Button
          disabled={disabled || (hasAssignee && hasAssignor)}
          variant="text"
          color="primary"
          startIcon={<AddIcon />}
          onClick={(anchor) => {
            setAnchor(anchor.currentTarget);
          }}
        >
          Add Party
        </Button>
      </Box>

      <SearchBox
        parentAnchor={anchor}
        onClose={() => setAnchor(null)}
        placeholder={
          apiSource === IdentityType.CREATOR
            ? "Search Last Name, IPI"
            : "Search Name, IPI"
        }
        apiSource={searchLink}
        onSelect={handleSelect}
        onChange={setSearchPhrase}
        upperContent={
          <Tabs
            value={apiSource}
            onChange={(e, source) => {
              setApiSource(source);
              setSearchPhrase(searchPhrase);
            }}
            textColor="primary"
            indicatorColor="primary"
          >
            <Tab
              value={EntityType.CREATOR}
              label="Creators"
              className={classes.tab}
              classes={{ root: classes.nonActiveTab }}
            />
            <Tab
              value={EntityType.PUBLISHER}
              label="Publishers"
              className={classes.tab}
              classes={{ root: classes.nonActiveTab }}
            />
          </Tabs>
        }
        renderOption={(option: IpiSearchCreatorOption) => (
          <AvatarTitle avatar={option.profile.initials} alignItems="flex-start">
            <CardPublisher
              publisher={{
                displayName: option.profile.displayName,
                ipiNameNumber: option.ipiNameNumber,
              }}
              wrapDisplayName
              highlightText={searchPhrase}
            />
          </AvatarTitle>
        )}
        optionsSelector={(data: FetchWritersResponse) =>
          data.response.ipiSearchResult.items
        }
        filterOptions={(options: IpiSearchCreatorOption[]) =>
          options.filter(
            (option) =>
              !unavailableIpiNumbers.includes(Number(option.ipiNameNumber))
          )
        }
      />
    </React.Fragment>
  );
};
