import React from "react";
import {
  ButtonBase,
  createStyles,
  makeStyles,
  Typography,
} from "@material-ui/core";
import {
  ColumnBreakpoints,
  SUCCESS,
  WorkCreatorPublisher,
  WorkPublisher,
} from "../../../utils/models";
import clsx from "clsx";
import { CellRole } from "../../atoms/CellRole";
import { StatusIndicator } from "../../atoms/StatusIndicator";
import { CellCreator } from "../../atoms/CellCreator";
import { CellPrShare } from "../../atoms/CellPrShare";
import { CellMrShare } from "../../atoms/CellMrShare";
import { CellCollaboration } from "../../atoms/CellCollaboration";
import { CellSplit } from "../../atoms/CellSplit";
import { CellAvatar } from "../../atoms/CellAvatar";
import { mapStatusToTooltip } from "../../atoms/FormikSplitStatusIndicator";
import { RowCreatorListItem } from "../RowCreatorListItem";
import { SvgElbowLine } from "../../atoms/SvgElbowLine";
import { CellOptions } from "../../atoms/CellOptions";
import { Theme } from "@material-ui/core/styles";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    mrShareSumValue: {
      paddingRight: theme.spacing(7),
    },
    prShareSumValue: {
      paddingRight: theme.spacing(4),
    },
    sumValueDense: {
      paddingRight: theme.spacing(2.75),
    },
    summaryRow: {
      alignItems: "center",
      marginBottom: theme.spacing(-2.25),
    },
  })
);

interface RowPublishersProps {
  publishers: WorkPublisher[];
  creatorPublishers: WorkCreatorPublisher[];
  hideOptionsColumn?: boolean;
  hideSplitColumn?: boolean;
  hideCollaborationColumn?: boolean;
  onShow: () => void;
  columnBreakPoints: ColumnBreakpoints;
  editable?: boolean;
}

export const RowPublishers: React.FC<RowPublishersProps> = ({
  publishers,
  creatorPublishers,
  onShow,
  hideOptionsColumn,
  hideCollaborationColumn,
  hideSplitColumn,
  editable,
  columnBreakPoints: bp,
}) => {
  const classes = useStyles();

  const mechanicalSum = creatorPublishers.reduce(
    (acc, curr) => acc + (curr.mrShare || 0),
    0
  );
  const performanceSum = creatorPublishers.reduce(
    (acc, curr) => acc + (curr.prShare || 0),
    0
  );

  //TODO:: Update logic when split approvals implemented
  // const splitsAccepted = !split.contributorShares.find(
  //   (el) => el.status === "UNDECIDED"
  // );
  // const correspondingPublishers = publishers.filter((el) =>
  //   creatorPublishers.find((cb) => cb.ipiNameNumber === el.ipi.ipiNameNumber)
  // );
  // const collaborationAccepted = !correspondingPublishers.find(
  //   (el) => !el.isSelf
  // );

  return (
    <React.Fragment>
      {creatorPublishers.length > 0 && (
        <RowCreatorListItem className={classes.summaryRow}>
          <CellAvatar>
            <SvgElbowLine />
          </CellAvatar>
          <CellCreator>
            <Typography
              variant="button"
              component={ButtonBase}
              onClick={onShow}
            >
              Show {creatorPublishers.length} Publisher
              {creatorPublishers.length > 1 && "s"}
            </Typography>
          </CellCreator>
          <CellRole dense={bp.role} />
          <CellPrShare dense={bp.prShare}>
            <Typography
              color="textSecondary"
              align="right"
              className={clsx(classes.prShareSumValue, {
                [classes.sumValueDense]: bp.prShare,
              })}
            >
              {performanceSum}%
            </Typography>
          </CellPrShare>
          <CellMrShare dense={bp.mrShare}>
            <Typography
              color="textSecondary"
              align="right"
              className={clsx(classes.mrShareSumValue, {
                [classes.sumValueDense]: bp.mrShare,
              })}
            >
              {mechanicalSum}%
            </Typography>
          </CellMrShare>
          {!(hideSplitColumn || bp.split) && (
            <CellSplit>
              {/*//TODO:: Update when split approvals implemented*/}
              {/*<StatusIndicator*/}
              {/*  status={splitsAccepted ? REGISTERED : DRAFT}*/}
              {/*  mapStatusToTooltip={mapStatusToTooltip}*/}
              {/*/>*/}
              <StatusIndicator
                status={SUCCESS}
                mapStatusToTooltip={mapStatusToTooltip}
              />
            </CellSplit>
          )}
          {!(hideCollaborationColumn || bp.collaboration) && (
            <CellCollaboration>
              <StatusIndicator
                status={SUCCESS}
                mapStatusToTooltip={mapStatusToTooltip}
              />
            </CellCollaboration>
          )}
          {!hideOptionsColumn && editable && <CellOptions />}
        </RowCreatorListItem>
      )}
    </React.Fragment>
  );
};
