import React, { useEffect } from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { PublisherProfile } from "../../organisms/PublisherProfile";
import { CreatorProfile } from "../../organisms/CreatorProfile";
import {
  getAuthPolicySelector,
  getChangePasswordLinkSelector,
  getCompanySelector,
  getLegalProfileLoadingStateSelector,
  getPasswordPolicyLinkSelector,
} from "../SignIn/selectors";
import { fetchAuthPolicyEffect } from "../SignIn/effects";
import { fetchLegalProfileEffect } from "./effects";
import { LoadingState } from "../../../types/enums";

export const Profile = () => {
  const state = useAppSelector((state) => state);
  const isPublisher = Boolean(getCompanySelector(state));
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();
  const changePasswordLink = getChangePasswordLinkSelector(state);
  const passwordPolicyLink = getPasswordPolicyLinkSelector(state);
  const authPolicy = getAuthPolicySelector(state);
  const legalProfileLoadingState = getLegalProfileLoadingStateSelector(state);

  useEffect(() => {
    if (changePasswordLink && passwordPolicyLink && !authPolicy) {
      dispatchEffect(fetchAuthPolicyEffect());
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (
      [LoadingState.PRISTINE, LoadingState.ERROR].includes(
        legalProfileLoadingState
      )
    ) {
      dispatchEffect(fetchLegalProfileEffect());
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (isPublisher) {
    return (
      <PublisherProfile dispatchEffect={dispatchEffect} dispatch={dispatch} />
    );
  }

  return <CreatorProfile dispatchEffect={dispatchEffect} dispatch={dispatch} />;
};
