import React from "react";
import { Dialog } from "../../molecules/Dialog";
import {
  Box,
  Button,
  createStyles,
  DialogActions,
  DialogContent,
  DialogTitle,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { TextField } from "formik-material-ui";
import { Field, FieldProps, Form, Formik } from "formik";
import { WorkFormValues } from "../FormWorkRegistration";
import { FormikAutocompleteSelect } from "../../molecules/FormikAutocompleteSelect";
import {
  createCreatorPublisherAssociation,
  getCreatorPublishers,
  getInitialValues,
  getOptions,
  isSubmitButtonDisabled,
} from "./component.utils";
import { WorkAgreementListItem } from "../TabWorkAgreements";
import { WorkCreator } from "../../../utils/models";
import {
  removeWorkAgreementAssociation,
  WorkAgreementType,
} from "../TabWorkAgreements/component.utils";
import { TemplateFormValues } from "../FormWorkTemplate";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dialog: {
      width: 528,
    },
    submitButton: {
      width: 106,
    },
  })
);

export type Option = { label: string; ipi: number; isPublisher: boolean };

export interface FormValues {
  creatorOrPublisher?: Option;
  publisher?: Option;
  agreementNumber?: string;
}

interface FormAddWorkAgreementNumberProps {
  open: boolean;
  onClose: () => void;
  workFormValues: WorkFormValues | TemplateFormValues;
  edit?: WorkAgreementListItem;
  setFieldValue: (name: string, value: any) => void;
}

export const FormAddWorkAgreementNumber: React.FC<FormAddWorkAgreementNumberProps> = ({
  open,
  onClose,
  workFormValues,
  edit,
  setFieldValue,
}) => {
  const classes = useStyles();

  const getLabel = (creatorOrPublisher?: Option) => {
    if (!creatorOrPublisher) {
      return "Creator / Publisher";
    }
    return creatorOrPublisher.isPublisher ? "Publisher" : "Creator";
  };

  const onSubmit = (values: FormValues) => {
    let updatedCreators: WorkCreator[] = workFormValues.creators;
    if (edit) {
      updatedCreators = removeWorkAgreementAssociation(
        updatedCreators,
        edit,
        WorkAgreementType.CREATOR_PUBLISHER
      );
    }

    updatedCreators = createCreatorPublisherAssociation(
      updatedCreators,
      values,
      values.creatorOrPublisher?.isPublisher
        ? WorkAgreementType.SUB_PUBLISHER
        : WorkAgreementType.CREATOR_PUBLISHER
    );

    setFieldValue("creators", updatedCreators);
    onClose();
  };

  return (
    <Dialog
      variant="complex"
      open={open}
      classes={{ paper: classes.dialog }}
      onClose={onClose}
    >
      <Formik
        onSubmit={onSubmit}
        initialValues={getInitialValues(workFormValues, edit)}
      >
        {({ values, setFieldValue }) => (
          <Form>
            <DialogTitle>{edit ? "Edit" : "Add"} Agreement Number</DialogTitle>
            <DialogContent>
              <Typography variant="body2" color="textSecondary">
                For
              </Typography>
              <Field name="creatorOrPublisher">
                {(props: FieldProps) => (
                  <FormikAutocompleteSelect<Option>
                    {...props}
                    freeSolo={false}
                    label={getLabel(values.creatorOrPublisher)}
                    options={getOptions(workFormValues)}
                    getOptionSelected={(option, value) =>
                      option?.ipi === value?.ipi
                    }
                    getOptionLabel={(option) => option?.label}
                    onChange={(event, value, reason, details) => {
                      if (
                        details?.option.ipi !==
                          values.creatorOrPublisher?.ipi &&
                        values.publisher
                      ) {
                        setFieldValue("publisher", undefined);
                      }
                    }}
                  />
                )}
              </Field>
              {values.creatorOrPublisher?.isPublisher === false && (
                <Box pt={3}>
                  <Field name="publisher">
                    {(props: FieldProps) => (
                      <FormikAutocompleteSelect<Option>
                        {...props}
                        freeSolo={false}
                        label="Publisher"
                        options={getCreatorPublishers(
                          workFormValues,
                          values.creatorOrPublisher?.ipi
                        )}
                        getOptionSelected={(option, value) =>
                          option?.ipi === value?.ipi
                        }
                        getOptionLabel={(option) => option?.label}
                      />
                    )}
                  </Field>
                </Box>
              )}
              <Box pt={3} pb={2}>
                <Field
                  component={TextField}
                  label="Agreement Number"
                  name="agreementNumber"
                  variant="filled"
                  fullWidth
                />
              </Box>
            </DialogContent>
            <DialogActions>
              <Button onClick={onClose}>Cancel</Button>
              <Button
                type="submit"
                color="primary"
                variant="contained"
                className={classes.submitButton}
                disabled={isSubmitButtonDisabled(values)}
              >
                {edit ? "Edit" : "Add"}
              </Button>
            </DialogActions>
          </Form>
        )}
      </Formik>
    </Dialog>
  );
};
