import React from "react";
import {
  Box,
  BoxProps,
  createStyles,
  Grid,
  makeStyles,
} from "@material-ui/core";
import clsx from "clsx";
import { Theme } from "@material-ui/core/styles";

export const prShareCellWidth = 120;
export const prShareCellDenseWidth = 100;

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    cell: {
      position: "relative",
      minWidth: prShareCellWidth,
      maxWidth: prShareCellWidth,
      paddingLeft: theme.spacing(3),
    },
    dense: {
      minWidth: prShareCellDenseWidth,
      maxWidth: prShareCellDenseWidth,
    },
    header: {
      paddingBottom: theme.spacing(1.5),
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
  })
);

interface CellPrShareProps extends BoxProps {
  dense?: boolean;
  header?: boolean;
}

export const CellPrShare: React.FC<CellPrShareProps> = ({
  children,
  dense,
  className,
  header = false,
  ...props
}) => {
  const classes = useStyles();

  return (
    <Box
      {...props}
      component={Grid}
      className={clsx(classes.cell, className, {
        [classes.dense]: dense,
        [classes.header]: header,
      })}
    >
      {children}
    </Box>
  );
};
