import React from "react";
import { FieldProps, getIn } from "formik";
import { SplitStatusType } from "../../../types/enums";
import { DRAFT, SUCCESS } from "../../../utils/models";
import { StatusIndicator } from "../StatusIndicator";

export const mapStatusToTooltip = {
  [SUCCESS]: "Accepted",
  [DRAFT]: "Pending",
};

export const FormikSplitStatusIndicator: React.FC<FieldProps> = ({
  field: { name },
  form: { values },
}) => {
  const value = getIn(values, `${name}.status`);
  return (
    <React.Fragment>
      {value && (
        <StatusIndicator
          status={value === SplitStatusType.ACCEPTED ? SUCCESS : DRAFT}
          mapStatusToTooltip={mapStatusToTooltip}
        />
      )}
    </React.Fragment>
  );
};
