import React, { useRef, useState } from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import SearchIcon from "@material-ui/icons/Search";
import { IconButton } from "@material-ui/core";
import clsx from "clsx";
import { InputSearch } from "../../atoms/InputSearch";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    hidden: {
      display: "none",
    },
    shrink: {
      opacity: 0,
      width: 0,
    },
    grow: {
      width: 170,
      marginLeft: theme.spacing(1),
    },
  })
);

interface InputSearchShrinkProps {
  onChange?: (query: string) => void;
  search?: string;
}

export const InputSearchShrink: React.FC<InputSearchShrinkProps> = ({
  onChange,
  search,
}) => {
  const classes = useStyles();
  const [expand, setExpand] = useState(false);
  const inputRef = useRef<HTMLInputElement>();

  const handleExpand = () => {
    setExpand(true);
    inputRef.current?.focus();
  };

  const handleBlur = (event: React.FocusEvent<HTMLInputElement>) => {
    const isEmpty = !event.currentTarget.value;
    if (isEmpty) {
      setExpand(false);
    }
  };
  const urlParams = new URLSearchParams(search);
  const [queryInput, setQueryInput] = useState(urlParams.get("search") || "");
  return (
    <React.Fragment>
      <IconButton
        color="inherit"
        onClick={handleExpand}
        className={clsx({ [classes.hidden]: expand })}
      >
        <SearchIcon />
      </IconButton>

      <InputSearch
        className={expand ? classes.grow : classes.shrink}
        onBlur={handleBlur}
        inputRef={inputRef}
        value={queryInput}
        onChange={(query) => {
          setQueryInput(query);
          onChange && onChange(query);
        }}
      />
    </React.Fragment>
  );
};
