import { SortOrder, WorkSortVariant } from "../../../utils/models";
import { initialSortParams } from "./component";

export const getSortBy: (sortBy: string | null) => WorkSortVariant = (
  sortBy: string | null
) => {
  const variants = ["title", "updated", "created", "status"];
  return (variants.includes(sortBy || "")
    ? sortBy
    : initialSortParams.sortBy) as WorkSortVariant;
};

export const getSortOrder: (sortOrder: string | null) => SortOrder = (
  sortOrder: string | null
) => {
  const variants = ["asc", "desc"];
  return (variants.includes(sortOrder || "")
    ? sortOrder
    : initialSortParams.sortOrder) as SortOrder;
};
