import {
  _BootstrapLinks,
  BootstrapAccount,
  BootstrapCompany,
  Dlr,
  Href,
  LegalProfile,
  PasswordReset,
} from "../../../utils/models";

export interface PasswordPolicy {
  minimumLength: number;
  notSameAsCurrent: boolean;
  requireLowercase: boolean;
  requireNumbers: boolean;
  requireSpecialCharacters: boolean;
  requireUppercase: boolean;
  pattern: string;
}

export interface AuthPolicy {
  passwordPolicy: PasswordPolicy;
}

export const BOOTSTRAP = "BOOTSTRAP";
export const BOOTSTRAP_SUCCESS = "BOOTSTRAP_SUCCESS";
export const BOOTSTRAP_FAILURE = "BOOTSTRAP_FAILURE";
export const SIGN_IN = "SIGN_IN";
export const SIGN_IN_SUCCESS = "SIGN_IN_SUCCESS";
export const SIGN_IN_FAILURE = "SIGN_IN_FAILURE";
export const SIGN_OUT = "SIGN_OUT";
export const SIGN_OUT_SUCCESS = "SIGN_OUT_SUCCESS";
export const FETCH_AUTH_POLICY = "FETCH_AUTH_POLICY";
export const FETCH_AUTH_POLICY_SUCCESS = "FETCH_AUTH_POLICY_SUCCESS";
export const PATCH_PROFILE = "PATCH_PROFILE";
export const PATCH_PROFILE_FAIL = "PATCH_PROFILE_FAIL";
export const PATCH_PROFILE_SUCCESS = "PATCH_PROFILE_SUCCESS";
export const CHANGE_PASSWORD = "CHANGE_PASSWORD";
export const CHANGE_PASSWORD_FAIL = "CHANGE_PASSWORD_FAIL";
export const CHANGE_PASSWORD_SUCCESS = "CHANGE_PASSWORD_SUCCESS";
export const CHANGE_EMAIL_REQUEST = "CHANGE_EMAIL_REQUEST";
export const CHANGE_EMAIL_REQUEST_FAIL = "CHANGE_EMAIL_REQUEST_FAIL";
export const CHANGE_EMAIL_REQUEST_SUCCESS = "CHANGE_EMAIL_REQUEST_SUCCESS";
export const CHANGE_EMAIL_CONFIRM = "CHANGE_EMAIL_CONFIRM";
export const CHANGE_EMAIL_CONFIRM_FAIL = "CHANGE_EMAIL_CONFIRM_FAIL";
export const CHANGE_EMAIL_CONFIRM_SUCCESS = "CHANGE_EMAIL_CONFIRM_SUCCESS";
export const CONFIRM_EMAIL_CHANGE = "CONFIRM_EMAIL_CHANGE";
export const CONFIRM_EMAIL_CHANGE_SUCCESS = "CONFIRM_EMAIL_CHANGE_SUCCESS";
export const CONFIRM_EMAIL_CHANGE_FAILURE = "CONFIRM_EMAIL_CHANGE_FAILURE";
export const CHANGE_AVATAR = "CHANGE_AVATAR";
export const CHANGE_AVATAR_FAIL = "CHANGE_AVATAR_FAIL";
export const CHANGE_AVATAR_SUCCESS = "CHANGE_AVATAR_SUCCESS";
export const SHOW_EMAIL_NOTIFICATION = "SHOW_EMAIL_NOTIFICATION";
export const RESET_PASSWORD = "RESET_PASSWORD";
export const RESET_PASSWORD_FAIL = "RESET_PASSWORD_FAIL";
export const RESET_PASSWORD_SUCCESS = "RESET_PASSWORD_SUCCESS";
export const FETCH_PROFILE = "FETCH_PROFILE";
export const FETCH_PROFILE_SUCCESS = "FETCH_PROFILE_SUCCESS";
export const FETCH_PROFILE_FAIL = "FETCH_PROFILE_ERROR";

export interface BootstrapAction {
  type: typeof BOOTSTRAP;
}

export interface BootstrapSuccessAction {
  type: typeof BOOTSTRAP_SUCCESS;
  data: {
    _links?: _BootstrapLinks;
    account?: BootstrapAccount;
    company?: BootstrapCompany;
    dlr?: Dlr;
  };
}

export interface BootstrapFailureAction {
  type: typeof BOOTSTRAP_FAILURE;
}

export interface SignInAction {
  type: typeof SIGN_IN;
}

export interface SignInSuccessAction {
  type: typeof SIGN_IN_SUCCESS;
}

export interface SignInFailureAction {
  type: typeof SIGN_IN_FAILURE;
}

export interface SignOutAction {
  type: typeof SIGN_OUT;
}

export interface SignOutSuccessAction {
  type: typeof SIGN_OUT_SUCCESS;
}

export interface FetchAuthPolicyAction {
  type: typeof FETCH_AUTH_POLICY;
}

export interface FetchAuthPolicySuccessAction {
  type: typeof FETCH_AUTH_POLICY_SUCCESS;
  authPolicy: AuthPolicy;
}

export interface PatchProfileAction {
  type: typeof PATCH_PROFILE;
  patch: Partial<BootstrapAccount>;
}

export interface PatchProfileSuccessAction {
  type: typeof PATCH_PROFILE_SUCCESS;
  profile: LegalProfile;
}

export interface PatchProfileFailAction {
  type: typeof PATCH_PROFILE_FAIL;
}

export interface ChangePasswordAction {
  type: typeof CHANGE_PASSWORD;
}

export interface ChangePasswordSuccessAction {
  type: typeof CHANGE_PASSWORD_SUCCESS;
}

export interface ChangePasswordFailAction {
  type: typeof CHANGE_PASSWORD_FAIL;
}

export interface ChangeEmailRequestAction {
  type: typeof CHANGE_EMAIL_REQUEST;
}

export interface ChangeEmailRequestSuccessAction {
  type: typeof CHANGE_EMAIL_REQUEST_SUCCESS;
  confirmHref: Href;
}

export interface ChangeEmailRequestFailAction {
  type: typeof CHANGE_EMAIL_REQUEST_FAIL;
}

export interface ChangeEmailConfirmAction {
  type: typeof CHANGE_EMAIL_CONFIRM;
}

export interface ChangeEmailConfirmSuccessAction {
  type: typeof CHANGE_EMAIL_CONFIRM_SUCCESS;
  email: string;
}

export interface ChangeEmailConfirmFailAction {
  type: typeof CHANGE_EMAIL_CONFIRM_FAIL;
}

export interface ConfirmEmailChangeAction {
  type: typeof CONFIRM_EMAIL_CHANGE;
}

export interface ConfirmEmailChangeSuccessAction {
  type: typeof CONFIRM_EMAIL_CHANGE_SUCCESS;
}

export interface ConfirmEmailChangeFailureAction {
  type: typeof CONFIRM_EMAIL_CHANGE_FAILURE;
}

export interface ChangeAvatarAction {
  type: typeof CHANGE_AVATAR;
}

export interface ChangeAvatarSuccessAction {
  type: typeof CHANGE_AVATAR_SUCCESS;
  avatarSrc: string;
}

export interface ChangeAvatarFailAction {
  type: typeof CHANGE_AVATAR_FAIL;
  error: string;
}

export interface ShowEmailNotificationAction {
  type: typeof SHOW_EMAIL_NOTIFICATION;
  show: boolean;
}

export interface ResetPasswordAction {
  type: typeof RESET_PASSWORD;
}

export interface ResetPasswordSuccessAction {
  type: typeof RESET_PASSWORD_SUCCESS;
  data: PasswordReset;
}

export interface ResetPasswordFailureAction {
  type: typeof RESET_PASSWORD_FAIL;
}

export interface FetchProfileAction {
  type: typeof FETCH_PROFILE;
}

export interface FetchProfileSuccessAction {
  type: typeof FETCH_PROFILE_SUCCESS;
  profile: LegalProfile;
}

export interface FetchProfileFailureAction {
  type: typeof FETCH_PROFILE_FAIL;
}

export type ActionTypes =
  | BootstrapAction
  | BootstrapSuccessAction
  | BootstrapFailureAction
  | SignInAction
  | SignInSuccessAction
  | SignInFailureAction
  | SignOutAction
  | SignOutSuccessAction
  | FetchAuthPolicyAction
  | FetchAuthPolicySuccessAction
  | PatchProfileAction
  | PatchProfileSuccessAction
  | PatchProfileFailAction
  | ChangePasswordAction
  | ChangePasswordSuccessAction
  | ChangePasswordFailAction
  | ChangeEmailRequestAction
  | ChangeEmailRequestSuccessAction
  | ChangeEmailRequestFailAction
  | ChangeEmailConfirmAction
  | ChangeEmailConfirmSuccessAction
  | ChangeEmailConfirmFailAction
  | ConfirmEmailChangeAction
  | ConfirmEmailChangeSuccessAction
  | ConfirmEmailChangeFailureAction
  | ChangeAvatarAction
  | ChangeAvatarSuccessAction
  | ChangeAvatarFailAction
  | ShowEmailNotificationAction
  | ResetPasswordAction
  | ResetPasswordSuccessAction
  | ResetPasswordFailureAction
  | FetchProfileAction
  | FetchProfileSuccessAction
  | FetchProfileFailureAction;
