import Promise from "promise-polyfill";
import { Action } from "redux";
import { ThunkAction, ThunkDispatch } from "redux-thunk";
import { AppState } from "../../../App.reducer";
import { AlertType } from "../Alerts/actions.types";
import { addAlertAction } from "../Alerts/actions";
import {
  createThemeAction,
  createThemeActionFailure,
  createThemeActionSuccess,
  deleteThemeAction,
  deleteThemeActionFailure,
  deleteThemeSuccessAction,
  fetchPublishedThemeAction,
  fetchPublishedThemeActionFailure,
  fetchPublishedThemeActionSuccess,
  fetchThemesAction,
  fetchThemesActionFailure,
  fetchThemesActionSuccess,
  setThemeAction,
  updateThemeAction,
  updateThemeActionFailure,
  updateThemeActionSuccess,
  uploadCustomLogoAction,
  uploadCustomLogoActionActionFailure,
  uploadCustomLogoActionActionSuccess,
} from "./actions";
import { handleError } from "../../../utils/errorHandler";
import { Theme } from "../../../utils/models";
import { getCurrentThemeSelector, getThemesSelector } from "./selectors";
import { DEFAULT_THEME_ID, defaultTheme } from "./reducer";
import { apiUrls, URL_NAMESPACE } from "../../../api.config";
import axios from "axios";
import {
  createInitialSettingTheme,
  getNewThemeId,
  getOrganizationNamespace,
} from "./utils";
import {
  FetchPublishedSettingsResponse,
  FetchSettingThemeVersionResponse,
} from "../../../api.types";
import { toasts } from "../../../utils/toasts";
import { SETTINGS_SCOPE } from "../../../types/enums";
import { setApiKeyWebAction } from "../Settings/actions";
import { fetchSettingThemeVersionResponseMock } from "../../../mocks";

export const uploadCustomLogoEffect =
  (image: string): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>) => {
    dispatch(uploadCustomLogoAction());

    //TODO:: implement backend when available
    const mockedRequestPromise = new Promise((resolve) => {
      setTimeout(() => {
        resolve();
      }, 1000);
    });

    try {
      await mockedRequestPromise;
      dispatch(uploadCustomLogoActionActionSuccess(image));
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.settings.UPLOAD_LOGO)
      );
    } catch (e) {
      const error = handleError(e.response, "logo");
      dispatch(uploadCustomLogoActionActionFailure());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.settings.UPLOAD_LOGO} ${error.message}`
        )
      );
    }
  };

export const fetchThemesEffect =
  (): ThunkAction<Promise<void | unknown>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchThemesAction());
    try {
      const organizationNamespace = getOrganizationNamespace(getState());
      // const response = await axios.get<FetchSettingThemeVersionResponse>(
      //   `${apiUrls.base}/settings/public/admin/${organizationNamespace}/scopes/theme/versions/latest`,
      //   { withCredentials: true }
      // );
      const { item } = fetchSettingThemeVersionResponseMock.response.setting;
      dispatch(fetchThemesActionSuccess([item]));
    } catch (e) {
      if (e.response.status === 404) {
        dispatch(
          fetchThemesActionSuccess([createInitialSettingTheme(defaultTheme)])
        );
      } else {
        const error = handleError(e.response, "themes");
        dispatch(fetchThemesActionFailure());
        dispatch(
          addAlertAction(
            AlertType.ERROR,
            `${toasts.error.settings.GET_THEMES} ${error.message}`
          )
        );
      }
    }
  };

export const fetchPublishedThemeEffect =
  (): ThunkAction<Promise<void | unknown>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchPublishedThemeAction());
    try {
      const response = await axios.request<FetchPublishedSettingsResponse>({
        url: `${apiUrls.base}/settings/public?scopes=${URL_NAMESPACE}:theme,${URL_NAMESPACE}:api-key-web`,
        method: "GET",
        withCredentials: true,
      });
      const { items } = response.data.response.settings;
      const theme = items.find((el) => el.scope === SETTINGS_SCOPE.THEME);
      const apiKeyWeb = items.find(
        (el) => el.scope === SETTINGS_SCOPE.API_KEY_WEB
      );

      if (theme) {
        dispatch(setThemeAction(theme.data as Theme));
      }

      if (apiKeyWeb) {
        dispatch(
          setApiKeyWebAction((apiKeyWeb.data as { apiKey: string }).apiKey)
        );
      }

      dispatch(fetchPublishedThemeActionSuccess());
    } catch (e) {
      const error = handleError(e.response, "theme");
      dispatch(fetchPublishedThemeActionFailure());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.settings.GET_THEME} ${error.message}`
        )
      );
    }
  };

export const updateAndSetThemeEffect =
  (
    theme: Theme
  ): ThunkAction<Promise<void | unknown>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    const state = getState();
    dispatch(updateThemeAction());
    const settingsThemeList = getThemesSelector(state);
    try {
      const organizationNamespace = getOrganizationNamespace(state);
      const processedTheme =
        theme.id === DEFAULT_THEME_ID
          ? {
              ...theme,
              id: getNewThemeId([]),
            }
          : theme;
      const settingTheme = settingsThemeList.find(
        (t) => t.data.id === theme.id
      );
      const response = await axios.request<FetchSettingThemeVersionResponse>({
        url:
          settingTheme?._links?.createNewVersion.href ||
          `${apiUrls.base}/settings/public/admin/${organizationNamespace}/scopes/theme`,
        method: settingTheme?._links?.createNewVersion.method || "POST",
        data: {
          data: processedTheme,
          publishOnSave: true,
        },
        withCredentials: true,
      });
      const { item } = response.data.response.setting;
      dispatch(updateThemeActionSuccess(item, theme.id === DEFAULT_THEME_ID));
      if (URL_NAMESPACE === organizationNamespace) {
        dispatch(setThemeAction(item.data));
      }
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.settings.UPDATE_THEME)
      );
      return processedTheme;
    } catch (e) {
      // TODO: (Temporary solution) remove this when upload is implemented in BE
      dispatch(updateThemeActionFailure());
      if (e.response.data.error.message === "Payload too large") {
        dispatch(
          addAlertAction(AlertType.ERROR, toasts.error.settings.SET_THEME_LOGO)
        );
      } else {
        const error = handleError(e.response, "theme");
        dispatch(
          addAlertAction(
            AlertType.ERROR,
            `${toasts.error.settings.UPDATE_THEME} ${error.message}`
          )
        );
      }
      throw e;
    }
  };

export const createAndSetThemeEffect =
  (
    newTheme: Theme
  ): ThunkAction<Promise<Theme | unknown>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    const state = getState();
    const themes = getThemesSelector(state);
    dispatch(createThemeAction());
    try {
      const organizationNamespace = getOrganizationNamespace(state);
      const newThemeWithId = {
        ...newTheme,
        id: getNewThemeId(themes),
      };
      const response = await axios.request<FetchSettingThemeVersionResponse>({
        url: `${apiUrls.base}/settings/public/admin/${organizationNamespace}/scopes/theme`,
        method: "POST",
        data: { data: newThemeWithId, publishOnSave: true },
        withCredentials: true,
      });
      const { item } = response.data.response.setting;
      dispatch(createThemeActionSuccess(item));
      dispatch(setThemeAction(item.data));
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.settings.CREATE_THEME)
      );
      return newThemeWithId;
    } catch (e) {
      const error = handleError(e.response, "theme");
      dispatch(createThemeActionFailure());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.settings.CREATE_THEME} ${error.message}`
        )
      );
      throw e;
    }
  };

//TODO: implement delete functionality when it's available in BE
export const deleteThemeEffect =
  (
    theme: Theme
  ): ThunkAction<Promise<void | unknown>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    const state = getState();
    const currentTheme = getCurrentThemeSelector(state);
    const newTheme = currentTheme.id === theme.id ? defaultTheme : currentTheme;
    dispatch(deleteThemeAction());
    try {
      const organizationNamespace = getOrganizationNamespace(state);
      await axios.request<FetchSettingThemeVersionResponse>({
        url: `${apiUrls.base}/settings/public/admin/${organizationNamespace}/scopes/theme`,
        method: "POST",
        data: { data: newTheme },
        withCredentials: true,
      });
      dispatch(deleteThemeSuccessAction(theme.id, true));
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.settings.DELETE_THEME)
      );
    } catch (e) {
      const error = handleError(e.response, "theme");
      dispatch(deleteThemeActionFailure());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.settings.DELETE_THEME} ${error.message}`
        )
      );
    }
  };
