import React, { useState } from "react";
import { Box, Button, Grid, ListItem, Typography } from "@material-ui/core";
import { format } from "date-fns";
import { AgreementListItem } from "../../../utils/models";
import { useHistory } from "react-router";
import { Href } from "../../../utils/models";
import { paths } from "../../../utils/common";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ListItemRippleDisabler } from "../../atoms/ListItemRippleDisabler";
import { ActionMenu } from "../ActionMenu";
import { useAgreementsListStyles } from "../../molecules/ListHeaderAgreements";
import { Icon } from "../../atoms/Icon";

interface ListItemAgreementTemplateProps {
  template: AgreementListItem;
  handleOnRemove: (
    guid: string,
    links: {
      deleteAgreement?: Href;
    }
  ) => void;
  handleOnCreateFrom: (createFrom?: Href) => void;
}

export const ListItemAgreementTemplate: React.FC<ListItemAgreementTemplateProps> = ({
  template: {
    guid,
    submitterAgreementNumbers,
    assignee,
    assignor,
    updated,
    title,
    _links,
  },
  handleOnCreateFrom,
  handleOnRemove,
}) => {
  const classes = useAgreementsListStyles();
  const { push } = useHistory();
  const [disableRipple, setDisableRipple] = useState(false);
  const stopPropagation = (e: React.MouseEvent<HTMLSpanElement>) =>
    e.stopPropagation();

  return (
    <ListItem
      disableRipple={disableRipple}
      disableGutters
      button
      className={classes.listRow}
      onClick={() => push(`${paths.AGREEMENT_TEMPLATE}/${guid}`)}
    >
      <ContainerListItem>
        <Grid item className={classes.statusColumn} />
        <Grid item className={classes.agreementColumn}>
          <Box display="flex" flexDirection="column">
            <Typography variant="subtitle1">{title}</Typography>
            {submitterAgreementNumbers?.map((el, index) => (
              <Typography
                key={`${index}${el}`}
                variant="body2"
                color="textSecondary"
              >
                {el}
              </Typography>
            ))}
          </Box>
        </Grid>
        <Grid item className={classes.assignorColumn}>
          {assignor && (
            <Box display="flex" flexDirection="column">
              <Typography variant="body1">{assignor.displayName}</Typography>
              <Typography variant="body2" color="textSecondary">
                IPI {assignor.ipiNameNumber}
              </Typography>
            </Box>
          )}
        </Grid>
        <Grid item className={classes.assigneeColumn}>
          {assignee && (
            <Box display="flex" flexDirection="column">
              <Typography variant="body1">{assignee.displayName}</Typography>
              <Typography variant="body2" color="textSecondary">
                IPI {assignee.ipiNameNumber}
              </Typography>
            </Box>
          )}
        </Grid>
        <Grid item className={classes.updatedColumn}>
          <Box display="flex" flexDirection="column">
            <Typography variant="subtitle1" color="textSecondary">
              {format(updated.timestamp * 1000, "LLLL dd, yyyy")}
            </Typography>
          </Box>
        </Grid>
        <Grid item className={classes.buttonColumn}>
          <ListItemRippleDisabler
            setDisableRipple={setDisableRipple}
            onClick={stopPropagation}
          >
            <Button
              variant="outlined"
              className={classes.button}
              onClick={() => handleOnCreateFrom(_links?.createFrom)}
            >
              Create From
            </Button>
          </ListItemRippleDisabler>
        </Grid>
        <Grid item className={classes.optionsColumn}>
          <ListItemRippleDisabler
            setDisableRipple={setDisableRipple}
            onClick={stopPropagation}
          >
            <ActionMenu
              items={[
                ...(_links?.deleteAgreement
                  ? [
                      {
                        label: _links?.deleteAgreement ? "Delete" : "",
                        to: "",
                        onClick: () => handleOnRemove(guid, _links),
                      },
                    ]
                  : []),
              ]}
            >
              <Icon name="listItemOptions" />
            </ActionMenu>
          </ListItemRippleDisabler>
        </Grid>
      </ContainerListItem>
    </ListItem>
  );
};
