import {
  BootstrapCompany,
  DRAFT,
  Template,
  Work,
  WorkPatch,
} from "../../../utils/models";
import { paths } from "../../../utils/common";
import { WorkFormValues } from "../../organisms/FormWorkRegistration";
import { formatWorkCreatorsPatchData } from "./effects.utils";
import { TemplateFormValues } from "../../organisms/FormWorkTemplate";

export const getParentPath = (work?: Work) => {
  if (work?.status === DRAFT) {
    return paths.WORKS_DRAFT_LIST;
  }
  return paths.WORKS_LIST;
};

export const parseToPartialWorkPatch: (
  work: Work | WorkFormValues
) => WorkPatch = (work: Work | WorkFormValues) => ({
  title: work.title,
  altTitle: work.altTitle || "",
  titleLanguageCode: work.titleLanguageCode,
  altTitleLanguageCode: work.altTitleLanguageCode,
  musicalDistributionCategory: work.musicalDistributionCategory,
  hasBeenRecorded: work.hasBeenRecorded,
  grandRightsIndicator: work.grandRightsIndicator,
  firstRefusalRecordingIndicator: work.firstRefusalRecordingIndicator,
  duration: work.duration,
  firstReleaseDate: work.firstReleaseDate || null,
});

export const creatorIpiHasChanged = (
  work: Work | Template,
  workFormValues: WorkFormValues | TemplateFormValues
) => {
  const oldStateCreator = work.creators?.find((el) => el.isSelf);
  const newStateCreator = workFormValues.creators.find((el) => el.isSelf);

  if (
    oldStateCreator &&
    newStateCreator &&
    oldStateCreator?.ipi.ipiNameNumber !== newStateCreator?.ipi.ipiNameNumber
  ) {
    return {
      oldReference: oldStateCreator.ipi.ipiReference,
      newIpi: newStateCreator.ipi.ipiNameNumber,
    };
  }
};

export const extractDirtyWorkAttrs = (
  work: Work,
  workFormValues: WorkFormValues
) => {
  const prev: WorkPatch = parseToPartialWorkPatch(work);
  const curr: WorkPatch = parseToPartialWorkPatch(workFormValues);
  const attributes: Array<keyof WorkPatch> = [
    "title",
    "altTitle",
    "titleLanguageCode",
    "altTitleLanguageCode",
    "musicalDistributionCategory",
    "hasBeenRecorded",
    "firstReleaseDate",
    "grandRightsIndicator",
    "firstRefusalRecordingIndicator",
    "duration",
  ];

  const creatorsPatch = formatWorkCreatorsPatchData(work, workFormValues);
  const dirty: { [index: string]: any } = {
    ...(creatorsPatch && { creators: creatorsPatch }),
  };
  attributes.forEach((attr) => {
    if (prev[attr] !== curr[attr]) {
      dirty[attr] = curr[attr];
    }
  });
  return dirty;
};

export const pickValuesForPostWork = (
  values: WorkFormValues,
  company?: BootstrapCompany
) => {
  const creatorAssociatedWithCompany =
    company &&
    values.creators.find((el) =>
      el.publishers?.find(
        (publisher) => publisher.ipiNameNumber === company.ipiNameNumber
      )
    );
  const creator = creatorAssociatedWithCompany || values.creators[0];

  return {
    title: values.title,
    titleLanguageCode: values.titleLanguageCode,
    creator: {
      ipiNameNumber: creator?.ipi.ipiNameNumber,
      role: creator?.role,
      publishers: creator?.publishers?.map((el) => ({
        ipiNameNumber: el.ipiNameNumber,
      })),
    },
  };
};
