import React from "react";
import {
  Box,
  Button,
  createStyles,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { format } from "date-fns";
import {
  formatDuration,
  getCategoryLabel,
  getHasBeenRecordedLabel,
  getYesOrNoLabel,
} from "./utils";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { ContainerTableList } from "../../molecules/ContainerTableList";
import { WorkFormValues } from "../FormWorkRegistration";
import { ListCreatorsSummary } from "../ListCreatorsSummary";
import clsx from "clsx";
import { BootstrapAccount } from "../../../utils/models";
import { ButtonDownloadCwr } from "../../atoms/ButtonDownloadCwr";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    detailWrapper: {
      display: "flex",
      paddingBottom: theme.spacing(2),
    },
    detail: {
      width: 100,
      marginLeft: theme.spacing(1.5),
    },
    detailSummary: {
      width: 120,
      textAlign: "right",
      color: theme.palette.text.secondary,
      fontFamily: "Inter",
    },
    dialog: {
      maxWidth: 1024,
      maxHeight: "80vh",
      width: "100%",
      height: "100%",
    },
    dialogContent: {
      paddingRight: 0,
      paddingLeft: 0,
    },
    higherContent: {
      padding: theme.spacing(5),
    },
    dialogTitle: {
      padding: 0,
    },
    dialogActions: {
      padding: theme.spacing(3),
    },
    mainTitle: {
      padding: theme.spacing(1, 0),
    },
    ellipsisOverflow: {
      overflow: "hidden",
      textOverflow: "ellipsis",
      whiteSpace: "nowrap",
    },
    label: {
      fontFamily: "Inter",
    },
    durationWrapper: {
      display: "inline-flex",
      width: 100,
      marginLeft: theme.spacing(1.5),
      height: theme.spacing(2),
    },
  })
);

interface DialogWorkSummaryProps {
  open: boolean;
  handleClose: () => void;
  isSubmitting: boolean;
  onSubmit?: () => void;
  values: WorkFormValues;
  submitForm?: () => Promise<any>;
  isPublisher?: boolean;
  account?: BootstrapAccount;
  onDownloadCwr?: () => void;
}

export const DialogWorkSummary: React.FC<DialogWorkSummaryProps> = ({
  open,
  handleClose,
  isSubmitting,
  values,
  onSubmit,
  submitForm,
  isPublisher,
  account,
  onDownloadCwr,
}) => {
  const classes = useStyles();
  const {
    title,
    altTitle,
    duration,
    hasBeenRecorded,
    grandRightsIndicator,
    firstReleaseDate,
    musicalDistributionCategory,
    firstRefusalRecordingIndicator,
  } = values;
  return (
    <Dialog
      open={open}
      onClose={() => {
        handleClose();
      }}
      classes={{ paper: classes.dialog }}
      fullWidth
    >
      <Grid container className={classes.higherContent}>
        <Grid item xs={4}>
          <Typography
            variant="caption"
            color="textSecondary"
            className={classes.label}
          >
            Work Summary
          </Typography>
          <DialogTitle disableTypography className={classes.dialogTitle}>
            <Typography
              variant="h5"
              className={clsx(classes.mainTitle, classes.ellipsisOverflow)}
            >
              {title}
            </Typography>
            <Typography
              className={classes.ellipsisOverflow}
              variant="subtitle1"
            >
              {altTitle}
            </Typography>
          </DialogTitle>
        </Grid>
        <Grid item xs={4}>
          <Box display="flex" flexDirection="column" alignItems="flex-end">
            <Box className={classes.detailWrapper}>
              <Typography variant="caption" className={classes.detailSummary}>
                Duration:
              </Typography>
              <Box className={classes.durationWrapper}>
                <Typography variant="caption">
                  {`${formatDuration(duration)}`}
                </Typography>
                &nbsp;
                <Typography variant="caption" color="textSecondary">
                  (mm:ss)
                </Typography>
              </Box>
            </Box>
            <Box className={classes.detailWrapper}>
              <Typography variant="caption" className={classes.detailSummary}>
                First Release Date:
              </Typography>
              <Typography className={classes.detail} variant="caption">
                {firstReleaseDate
                  ? format(new Date(firstReleaseDate), "dd/MM/yyyy")
                  : "N/A"}
              </Typography>
            </Box>
            <Box className={classes.detailWrapper}>
              <Typography variant="caption" className={classes.detailSummary}>
                Grand Rights:
              </Typography>
              <Typography className={classes.detail} variant="caption">
                {getYesOrNoLabel(Boolean(grandRightsIndicator))}
              </Typography>
            </Box>
          </Box>
        </Grid>
        <Grid item xs={4}>
          <Box display="flex" flexDirection="column" alignItems="flex-end">
            <Box className={classes.detailWrapper}>
              <Typography variant="caption" className={classes.detailSummary}>
                Category:
              </Typography>
              <Typography variant="caption" className={classes.detail}>
                {getCategoryLabel(musicalDistributionCategory)}
              </Typography>
            </Box>
            <Box className={classes.detailWrapper}>
              <Typography variant="caption" className={classes.detailSummary}>
                Recorded:
              </Typography>
              <Typography className={classes.detail} variant="caption">
                {getHasBeenRecordedLabel(hasBeenRecorded)}
              </Typography>
            </Box>
            <Box className={classes.detailWrapper}>
              <Typography variant="caption" className={classes.detailSummary}>
                First Refusal:
              </Typography>
              <Typography className={classes.detail} variant="caption">
                {getYesOrNoLabel(Boolean(firstRefusalRecordingIndicator))}
              </Typography>
            </Box>
          </Box>
        </Grid>
      </Grid>

      <DialogContent className={classes.dialogContent}>
        <ContainerTableList elevation={0} disableMaxWidth>
          <ListCreatorsSummary values={values} isPublisher={isPublisher} />
        </ContainerTableList>
      </DialogContent>
      <DialogActions className={classes.dialogActions}>
        {onSubmit && submitForm ? (
          <>
            <Button onClick={handleClose} variant="text">
              Cancel
            </Button>
            <ButtonSubmit
              submitting={isSubmitting}
              onClick={() => {
                onSubmit();
                submitForm();
              }}
            >
              Submit
            </ButtonSubmit>
          </>
        ) : (
          <React.Fragment>
            <ButtonDownloadCwr onClick={onDownloadCwr} />
            <Button onClick={handleClose} color="primary" variant="contained">
              Close
            </Button>
          </React.Fragment>
        )}
      </DialogActions>
    </Dialog>
  );
};
