import React from "react";
import { Select } from "formik-material-ui";
import { Field, FieldAttributes } from "formik";

export const SelectAppBar: React.FC<FieldAttributes<any>> = ({
  children,
  ...props
}) => {
  return (
    <Field fullWidth variant="filled" component={Select} {...props}>
      {children}
    </Field>
  );
};
