import React from "react";
import { useListEmployeesInvitesStyles } from "../ListEmployeesInvites";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { GridListHeaderItem } from "../../atoms/GridListHeaderItem";

export const HeaderListEmployeesInvites: React.FC = () => {
  const classes = useListEmployeesInvitesStyles();

  return (
    <ContainerListItem leftOffset={0}>
      <GridListHeaderItem className={classes.nameColumn}>
        <ListColumnTitle>Email</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.dataColumn}>
        <ListColumnTitle>Status</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.dataColumn}>
        <ListColumnTitle>Invited</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.dataColumn}>
        <ListColumnTitle>Expiry Date</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.optionsColumn} />
    </ContainerListItem>
  );
};
