import { WorkFormValues } from "../FormWorkRegistration";
import { WorkCreator, WorkCreatorPublisher } from "../../../utils/models";
import { WorkAgreementListItem } from "./component";
import { TemplateFormValues } from "../FormWorkTemplate";

export enum WorkAgreementType {
  CREATOR_PUBLISHER = "creatorPublisherSocietyAgreementNumber",
  SUB_PUBLISHER = "publisherSocietyAgreementNumber",
}

export const getAgreementNumbers = (
  workFormValues: WorkFormValues | TemplateFormValues,
  type: WorkAgreementType
) => {
  const result: WorkAgreementListItem[] = [];
  const getAgreementNumber = (publisher: WorkCreatorPublisher) =>
    type === WorkAgreementType.CREATOR_PUBLISHER
      ? publisher.creatorPublisherSocietyAgreementNumber
      : publisher.publisherSocietyAgreementNumber;

  workFormValues.creators.forEach((creator) => {
    creator.publishers?.forEach((publisher) => {
      const agreementNumber = getAgreementNumber(publisher);
      if (agreementNumber) {
        result.push({
          agreementNumber,
          creatorIpi: creator.ipi.ipiNameNumber,
          publisherIpi: publisher.ipiNameNumber,
          publisher:
            workFormValues.publishers.find(
              (el) => el.ipi.ipiNameNumber === publisher.ipiNameNumber
            )?.profile.displayName || "",
          creator: creator.profile.displayName,
        });
      }
    });
  });

  return result;
};

export const removeWorkAgreementAssociation: (
  creators: WorkCreator[],
  association: WorkAgreementListItem,
  key: WorkAgreementType
) => WorkCreator[] = (creators, association, key) =>
  creators.map((creator) => {
    if (creator.ipi.ipiNameNumber === association.creatorIpi) {
      return {
        ...creator,
        publishers: creator.publishers?.map((publisher) => {
          if (publisher.ipiNameNumber === association.publisherIpi) {
            return {
              ...publisher,
              [key]: "",
            };
          }
          return publisher;
        }),
      };
    }
    return creator;
  });

export const findCreatorAndFormatToOption = (
  creators: WorkCreator[],
  edit?: WorkAgreementListItem
) => {
  const creator = creators.find(
    (creator) => creator.ipi.ipiNameNumber === edit?.creatorIpi
  );
  if (creator) {
    return {
      label: creator.profile.displayName,
      ipi: creator.ipi.ipiNameNumber,
      isPublisher: false,
    };
  }
};

export const findPublisherAndFormatToOption = (
  workFormValues: WorkFormValues | TemplateFormValues,
  key: WorkAgreementType,
  edit?: WorkAgreementListItem
) => {
  const creator = workFormValues.creators.find(
    (creator) => creator.ipi.ipiNameNumber === edit?.creatorIpi
  );
  const creatorPublisher = creator?.publishers?.find(
    (publisher) => publisher.ipiNameNumber === edit?.publisherIpi
  );
  const publisher = workFormValues.publishers.find(
    (publisher) => publisher.ipi.ipiNameNumber === edit?.publisherIpi
  );

  if (publisher && creatorPublisher) {
    return {
      label: publisher.profile.displayName || "",
      ipi: publisher.ipi.ipiNameNumber,
      isPublisher: true,
      agreementNumber: creatorPublisher[key],
    };
  }
};
