import { IconButton } from "@material-ui/core";
import { Link } from "react-router-dom";
import React from "react";
import { useAppSelector } from "../../../App.reducer";
import { paths } from "../../../utils/common";
import { Notification24, NotificationNew24 } from "@carbon/icons-react";

export const ButtonNotifications = () => {
  const unseenNotificationsCount = useAppSelector(
    (state) => state.notifications.unseenNotificationsCount
  );
  return (
    <Link to={paths.NOTIFICATIONS}>
      <IconButton color="inherit">
        {unseenNotificationsCount ? <NotificationNew24 /> : <Notification24 />}
      </IconButton>
    </Link>
  );
};
