import React from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { LoadingState } from "../../../types/enums";
import {
  getAgreementTemplatesLoadingStateSelector,
  getAgreementTemplatesSelector,
} from "./selectors";
import { ListAgreementTemplates } from "../../organisms/ListAgreementTemplates";
import { PageNoContent } from "../../molecules/PageNoContent";
import { LoadingScreen } from "../../atoms/LoadingScreen";

export const AgreementTemplatesListPage = () => {
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();
  const state = useAppSelector((state) => state);
  const agreementTemplates = getAgreementTemplatesSelector(state);
  const loadingState = getAgreementTemplatesLoadingStateSelector(state);

  if (
    loadingState === LoadingState.PRISTINE ||
    loadingState === LoadingState.FETCHING
  ) {
    return <LoadingScreen />;
  }

  return loadingState === LoadingState.FETCHED &&
    agreementTemplates.length === 0 ? (
    <PageNoContent
      primaryText="No Templates"
      secondaryText="Click the + button to create a new template"
    />
  ) : (
    <ListAgreementTemplates
      dispatch={dispatch}
      dispatchEffect={dispatchEffect}
      templates={agreementTemplates}
    />
  );
};
