import React, { useState } from "react";
import { Button, createStyles, makeStyles, Theme } from "@material-ui/core";
import { WorkCreator, WorkPublisher } from "../../../utils/models";
import { RowGridPublisherSummary } from "../../molecules/RowGridPublisherSummary";
import { RowCreatorLeftContent } from "../../atoms/RowCreatorLeftContent";
import { RowPublishersSummary } from "../../molecules/RowPublishersSummary";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    showLessRow: {
      paddingLeft: theme.spacing(2),
      paddingTop: 0,
      paddingBottom: 0,
      height: theme.spacing(1),
      marginBottom: theme.spacing(1.5),
    },
  })
);

interface RowGridPublisherRelationshipProps {
  publishers: WorkPublisher[];
  creator: WorkCreator;
  isPublisher?: boolean;
}

export const RowGridPublisherRelationshipSummary: React.FC<RowGridPublisherRelationshipProps> = ({
  publishers,
  creator,
  isPublisher,
}) => {
  const classes = useStyles();
  const [showDetails, setShowDetails] = useState(false);

  const creatorPublishers =
    creator.publishers?.map((el) => el.ipiNameNumber) || [];
  const childPublishers = publishers.filter((el) =>
    creatorPublishers.includes(el.ipi.ipiNameNumber)
  );

  const handleShowDetails = () => setShowDetails(true);
  const handleHideDetails = () => setShowDetails(false);

  return (
    <React.Fragment>
      {showDetails ? (
        <React.Fragment>
          {childPublishers.map((publisher, index) => {
            const creatorPublisher = creator.publishers?.find(
              (el) => el.ipiNameNumber === publisher.ipi.ipiNameNumber
            );
            return (
              creatorPublisher && (
                <RowGridPublisherSummary
                  first={index === 0}
                  key={publisher.ipi.ipiNameNumber}
                  publisher={publisher}
                  creatorPublisher={creatorPublisher}
                  isPublisher={isPublisher}
                />
              )
            );
          })}

          <RowCreatorLeftContent className={classes.showLessRow}>
            <Button variant="text" onClick={handleHideDetails}>
              Show Less
            </Button>
          </RowCreatorLeftContent>
        </React.Fragment>
      ) : (
        <RowPublishersSummary
          publishers={creator.publishers || []}
          onShow={handleShowDetails}
        />
      )}
    </React.Fragment>
  );
};
