export const fetchCountriesAction = (): CompanyActionTypes => ({
  type: FETCH_COUNTRIES,
});

export const fetchCountriesSuccessAction = (
  countries: Country[]
): CompanyActionTypes => ({
  type: FETCH_COUNTRIES_SUCCESS,
  countries,
});

