import React, { ChangeEvent, useEffect, useRef, useState } from "react";
import { FormikPercentageInput } from "../FormikPercentageInput";
import { FieldProps } from "formik";
import { handleKeyDown } from "../FormikPercentageInput/utils";

interface FormikAgreementShareInputProps extends FieldProps {
  disabled: boolean;
}

export const FormikAgreementShareInput: React.FC<FormikAgreementShareInputProps> = ({
  children,
  form,
  field,
  disabled,
  ...props
}) => {
  const { setFieldValue, setFieldTouched, isSubmitting } = form;
  const { name, value } = field;
  const inputRef = useRef<HTMLInputElement>();
  const [state, setState] = useState(value);
  const [focused, setFocused] = useState(false);

  const handleChange = (e: ChangeEvent<HTMLTextAreaElement>) => {
    setState(e.target.value === "" ? "" : Number(e.target.value));
  };

  const handleOnFocus = () => {
    if (isSubmitting) return;
    Number(state) === 0 && setState("");
    setFocused(true);
  };

  const handleOnBlur = () => {
    setFocused(false);
    setFieldTouched(name);
    state === "" && setState(0);
    if (Number(state) !== Number(value)) {
      setFieldValue(name, state === "" ? 0 : state);
    }
  };

  const handleKeyUp = ({
    key,
    keyCode,
  }: React.KeyboardEvent<HTMLDivElement>) => {
    if (key === "Enter" || keyCode === 13) {
      handleOnBlur();
      inputRef.current?.blur();
    }
  };

  useEffect(() => {
    setState(value);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [value]);

  return (
    <FormikPercentageInput
      handleChange={handleChange}
      handleOnFocus={handleOnFocus}
      handleOnBlur={handleOnBlur}
      onKeyDown={handleKeyDown}
      onKeyUp={handleKeyUp}
      inputRef={inputRef}
      form={form}
      field={field}
      value={!focused && (state === 0 || state === "") ? "0.00" : state}
      helperText=""
      disabled={disabled}
      {...props}
    />
  );
};
