import React from "react";
import {
  createStyles,
  makeStyles,
  Typography,
  ListItem,
  Theme,
} from "@material-ui/core";
import KeyboardArrowRightIcon from "@material-ui/icons/KeyboardArrowRight";
import { AppDispatch } from "../../../App.reducer";
import { setFaqPathAction } from "../../pages/Faq/actions";
import { FaqNavigationItem } from "../../../utils/models";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    navigationListItem: {
      height: 72,
      display: "flex",
      justifyContent: "space-between",
      padding: theme.spacing(0, 3),
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
  })
);

interface NavigationItemFaqProps {
  dispatch: AppDispatch;
  item: FaqNavigationItem;
  currentPath?: string;
}

export const NavigationItemFaq: React.FC<NavigationItemFaqProps> = ({
  dispatch,
  item,
  currentPath,
}) => {
  const classes = useStyles();
  const handleLinkClick = () =>
    dispatch(setFaqPathAction(`${currentPath}.${item.id}`));

  return (
    <ListItem
      button
      onClick={handleLinkClick}
      className={classes.navigationListItem}
    >
      <Typography variant="subtitle1" color="textSecondary">
        {item.title}
      </Typography>
      <KeyboardArrowRightIcon />
    </ListItem>
  );
};
