import React, { useEffect } from "react";
import { Box, Grid, Typography } from "@material-ui/core";
import { RouteComponentProps } from "react-router";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import {
  getCompanySelector,
  getFetchEmployeesLinkSelector,
  getUserIsSocAdminSelector,
} from "../SignIn/selectors";
import {
  fetchCompanyInvitesEffect,
  fetchEmployeesEffect,
} from "../Company/effects";
import {
  getDeleteInviteLinkSelector,
  getEmployeesLoadingStateSelector,
  getEmployeesSelector,
  getInviteEmployeeLinkSelector,
  getInvitesLoadingStateSelector,
  getInvitesSelector,
  getPatchEmployeeGroupLinkSelector,
} from "../Company/selectors";
import { LoadingState } from "../../../types/enums";
import { paths } from "../../../utils/common";
import { TabPanel } from "../../atoms/TabPanel";
import { DialogEmployeeInvite } from "../../organisms/DialogEmployeeInvite";
import { ContainerTabPanel } from "../../atoms/ContainerTabPanel";
import { PageEmptyStateEmployees } from "../../atoms/PageEmptyStateEmployees";
import { ListEmployees } from "../../organisms/ListEmployees";
import { ListEmployeesInvites } from "../../organisms/ListEmployeesInvites";
import { EmployeesNavigationBar } from "../../organisms/EmployeesNavigationBar";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { LoadingScreen } from "../../atoms/LoadingScreen";

const isLoading = (state: LoadingState) =>
  [LoadingState.PRISTINE, LoadingState.FETCHING].includes(state);

const isEmpty = (state: LoadingState, employeesLength: number) =>
  [LoadingState.FETCHED, LoadingState.ERROR].includes(state) &&
  employeesLength === 0;

export const Employees: React.FC<RouteComponentProps> = ({
  location: { pathname },
}) => {
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();
  const state = useAppSelector((state) => state);
  const company = getCompanySelector(state);
  const employees = getEmployeesSelector(state);
  const invites = getInvitesSelector(state);
  const employeesLoadingState = getEmployeesLoadingStateSelector(state);
  const invitesLoadingState = getInvitesLoadingStateSelector(state);
  const canInviteEmployee = Boolean(getInviteEmployeeLinkSelector(state));
  const canDeleteInvite = Boolean(getDeleteInviteLinkSelector(state));
  const isSocietyAdmin = getUserIsSocAdminSelector(state);
  const canChangeEmployeeGroup = Boolean(
    getPatchEmployeeGroupLinkSelector(state)
  );
  const canListEmployees = Boolean(getFetchEmployeesLinkSelector(state));

  const getEmployeesListContent = () => {
    if (isLoading(employeesLoadingState)) {
      return <LoadingScreen />;
    }

    if (isEmpty(employeesLoadingState, employees.length)) {
      return <PageEmptyStateEmployees />;
    }

    if (canListEmployees) {
      return (
        <ListEmployees
          employees={employees}
          dispatchEffect={dispatchEffect}
          canChangeGroup={canChangeEmployeeGroup}
        />
      );
    }

    return null;
  };

  useEffect(() => {
    if (pathname === paths.EMPLOYEES_LIST) {
      dispatchEffect(fetchEmployeesEffect());
    }

    if (pathname === paths.EMPLOYEES_INVITES) {
      dispatchEffect(fetchCompanyInvitesEffect());
    }
  }, [dispatchEffect, pathname]);

  return (
    <React.Fragment>
      <ContainerTabPanel>
        <ContainerAppBar
          dispatch={dispatch}
          title="Employees"
          secondaryContent={
            <Box>
              <Grid container>
                <Grid item xs={7}>
                  <Typography variant="h4">{company?.name}</Typography>
                </Grid>
                <EmployeesNavigationBar state={state} />
              </Grid>
            </Box>
          }
        />
        <TabPanel value={pathname} index={paths.EMPLOYEES_LIST}>
          {getEmployeesListContent()}
        </TabPanel>
        <TabPanel value={pathname} index={paths.EMPLOYEES_INVITES}>
          <ListEmployeesInvites
            invites={invites}
            invitesLoadingState={invitesLoadingState}
            dispatchEffect={dispatchEffect}
            canDeleteInvite={canDeleteInvite}
          />
        </TabPanel>
      </ContainerTabPanel>
      <DialogEmployeeInvite
        dispatchEffect={dispatchEffect}
        companyName={company?.name || ""}
        onInviteSuccess={() => dispatchEffect(fetchCompanyInvitesEffect())}
        showButton={canInviteEmployee}
        isSocietyAdmin={isSocietyAdmin}
      />
    </React.Fragment>
  );
};
