import React, { useState } from "react";
import {
  Box,
  Button,
  createStyles,
  Grid,
  makeStyles,
  Paper,
  Theme,
  Typography,
} from "@material-ui/core";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import {
  Field,
  FieldProps,
  Form,
  Formik,
  FormikErrors,
  FormikHelpers,
} from "formik";
import {
  hasBeenRecorderCodeNames,
  languageCodeNames,
  MusicalCategoryCodeNames,
  WorkNavigationTabs,
  WorkRegistrationErrorType,
} from "../../../types/enums";
import {
  _WorkListItemLinks,
  BootstrapAccount,
  BootstrapCompany,
  ERROR,
  Ipi,
  NOT_SUBMITTED,
  Work,
  WorkCreator,
  WorkPublisher,
  WorkRegistration,
} from "../../../utils/models";
import isEmpty from "lodash/isEmpty";
import {
  buildSaveValidationSchema,
  buildSubmitValidationSchema,
} from "./component.validator";
import {
  postWorkEffect,
  registerWorkEffect,
  updateWorkEffect,
} from "../../pages/WorkRegistration/effects";
import {
  addUserToCreatorsList,
  convertToValueLabel,
  mapValuesDeep,
  mergeEnumLabels,
  preventEnterOnKeyDown,
  scrollToFormErrors,
} from "../../../utils/helpers";
import * as H from "history";
import { titleLangCodeRequiredRegex } from "../../../utils/regex";
import { WorkSchemaJSON } from "../../../validationSchema/models";
import { addAlertAction } from "../../pages/Alerts/actions";
import { AlertType } from "../../pages/Alerts/actions.types";
import { fetchWorkRegistrationsEffect } from "../../pages/Works/effects";
import {
  getParentPath,
  pickValuesForPostWork,
} from "../../pages/WorkRegistration/utils";
import { paths } from "../../../utils/common";
import { TabCreators } from "../../molecules/TabCreators";
import { LeavingGuard } from "../LeavingGuard";
import { TabsNavigation } from "../../molecules/TabsNavigation";
import { WorkTitleInput } from "../../molecules/WorkTitleInput";
import { TabRegistrations } from "../../molecules/TabRegistrations";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { TabDetails } from "../../molecules/TabDetails";
import { FormikAddWorkWriter } from "../FormikAddWorkWriter";
import { DialogWorkSummary } from "../DialogWorkSummary";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { toasts } from "../../../utils/toasts";
import { BlockNavigation } from "../../atoms/BlockNavigation/component";
import { DialogWarning } from "../../molecules/DialogWarning";
import { workToFormData } from "./utils";
import { TabWorkAgreements } from "../TabWorkAgreements";
import { openErrorTab, validateMissingTitle } from "./component.utils";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    form: {
      height: "100%",
      display: "flex",
      flexDirection: "column",
    },
    appBarContent: {
      width: "100%",
      marginTop: theme.spacing(1.5),
      display: "inline-flex",
      alignItems: "baseline",
      justifyContent: "space-between",
      transition: theme.transitions.create(["padding"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    saveButton: {
      marginLeft: theme.spacing(3),
    },
    submitButton: {
      marginLeft: theme.spacing(1),
    },
    formControl: {
      width: "100%",
    },
    durationPickerFix: {
      position: "relative",
      top: -2,
    },
    buttonsContainer: {
      display: "flex",
      alignItems: "flex-end",
      justifyContent: "flex-end",
      marginBottom: theme.spacing(-1),
      marginLeft: "auto",
    },
    content: {
      position: "relative",
      height: "100%",
      overflow: "auto",
    },
    containerSpacingFix: {
      marginBottom: 0,
    },
    tabDetailsContainer: {
      display: "flex",
      flexDirection: "column",
      overflowX: "hidden",
      height: "100%",
    },
    displayNone: {
      display: "none",
    },
  })
);

export interface WorkFormValues {
  title: string;
  altTitle: string;
  duration?: number;
  creators: WorkCreator[];
  publishers: WorkPublisher[];
  hasBeenRecorded?: boolean;
  grandRightsIndicator?: boolean;
  firstRefusalRecordingIndicator?: boolean;
  firstReleaseDate?: string;
  musicalDistributionCategory?: string;
  titleLanguageCode?: string;
  altTitleLanguageCode?: string;
  mechanicalSplitSum?: string;
  performanceSplitSum?: string;
  companyPublisherError?: string;
}

interface FormWorkRegistrationProps {
  dispatch: AppDispatch;
  dispatchEffect: AppDispatchEffect;
  history: H.History;
  work?: Work;
  workListItemsLinks: _WorkListItemLinks;
  account?: BootstrapAccount;
  company?: BootstrapCompany;
  workValidationSchema?: WorkSchemaJSON;
  workRegistrations: WorkRegistration[];
  ipiList: Ipi[];
  breakpointOffset?: number;
  isPublisher?: boolean;
  isCreator?: boolean;
  isSocAdmin?: boolean;
  breadCrumb?: string;
}

export const INIT_WRITER_GUID = "INIT_WRITER_GUID";

export const FormWorkRegistration: React.FC<FormWorkRegistrationProps> = ({
  dispatch,
  dispatchEffect,
  work,
  workListItemsLinks,
  account,
  history,
  workValidationSchema,
  workRegistrations: workRegistrationsProp,
  ipiList,
  breakpointOffset,
  isPublisher,
  isCreator,
  company,
  breadCrumb = "New",
  isSocAdmin,
}) => {
  const classes = useStyles();
  const [workSaving, setWorkSaving] = useState(false);
  const [workRegistering, setWorkRegistering] = useState(false);
  const [summaryDialogOpen, setSummaryDialogOpen] = useState(false);
  const [showRequiredAssociationDialog, setShowRequiredAssociationDialog] =
    useState(false);
  // const { titleLanguageCode, altTitleLanguageCode } =
  //   workValidationSchema?.properties.body.properties || {};
  const [activeTab, setActiveTab] = useState<WorkNavigationTabs>(
    WorkNavigationTabs.PARTIES
  );
  const [redirectingToWork, setRedirectingToWork] = useState(false);
  const [redirectingToSubmissionsDialog, setRedirectingToSubmissionsDialog] =
    useState(false);
  const [workRegistrations, setWorkRegistrations] = useState(
    workRegistrationsProp
  );
  const [openRegistrationDialog, setOpenRegistrationDialog] =
    useState<WorkRegistration>();

  const handleRedirectToWork = (guid: string) => {
    setRedirectingToWork(true);
    history.push({
      pathname: `${paths.WORKS_REGISTER}/${guid}`,
      search: history.location.search,
    });
    setRedirectingToWork(false);
  };

  const initialValues: WorkFormValues = {
    title: "",
    altTitle: "",
    duration: undefined,
    creators: isPublisher
      ? []
      : addUserToCreatorsList(
          account,
          ipiList.find((el) => el.default)
        ),
    publishers: [],
    hasBeenRecorded: false,
    grandRightsIndicator: false,
    firstRefusalRecordingIndicator: false,
    musicalDistributionCategory: "UNC",
  };

  const editable = Boolean(work?._links.update || !work?.guid);

  const languageCodes = mergeEnumLabels(
    languageCodeNames,
    workValidationSchema?.properties.body.properties.titleLanguageCode?.enum
  );

  const schemaProps = {
    schema: workValidationSchema,
    company,
    isPublisher,
  };
  const validationSchema = workRegistering
    ? buildSubmitValidationSchema(schemaProps)
    : buildSaveValidationSchema(schemaProps);

  const handleSave = (errors: FormikErrors<WorkFormValues>) => {
    setWorkRegistering(false);
    setWorkSaving(true);
    openErrorTab(errors, setActiveTab);
    if (errors.companyPublisherError) {
      setShowRequiredAssociationDialog(true);
    }
  };

  const handleSubmit = (errors: FormikErrors<WorkFormValues>) => {
    setWorkSaving(false);
    setWorkRegistering(true);
    openErrorTab(errors, setActiveTab);
  };

  const redirectToSubmissionsDialog = (
    workRegistration: WorkRegistration[]
  ) => {
    setRedirectingToSubmissionsDialog(true);
    const timeStamps = workRegistration.map((el) => el.created.timestamp);
    const newestRegistration = workRegistration.find(
      (el) => el.created.timestamp === Math.max(...timeStamps)
    );
    setOpenRegistrationDialog(newestRegistration);
    setActiveTab(WorkNavigationTabs.REGISTRATIONS);
    history.push({
      search: `?tab=${WorkNavigationTabs.REGISTRATIONS}`,
    });
    setRedirectingToSubmissionsDialog(false);
  };

  const onSubmit = async (
    values: WorkFormValues,
    { setStatus, resetForm }: FormikHelpers<WorkFormValues>,
    forLeavingGuard?: boolean
  ) => {
    try {
      let currentWork = work;
      if (!work?.guid) {
        const createdWork = await dispatchEffect(
          postWorkEffect(pickValuesForPostWork(values, company))
        );
        currentWork = createdWork;
        if (!forLeavingGuard) {
          handleRedirectToWork(createdWork.guid);
        }
      }
      if (currentWork) {
        const newFormData = await dispatchEffect(
          updateWorkEffect(currentWork.guid, values)
        );

        if (workRegistering) {
          await dispatchEffect(registerWorkEffect(currentWork.guid));
          setSummaryDialogOpen(false);
          const workRegistrations: WorkRegistration[] = await dispatchEffect(
            fetchWorkRegistrationsEffect(currentWork.guid)
          );
          setWorkRegistrations(workRegistrations);
          redirectToSubmissionsDialog(workRegistrations);
        } else {
          dispatch(
            addAlertAction(
              AlertType.SUCCESS,
              [ERROR, NOT_SUBMITTED].includes(currentWork.status)
                ? toasts.success.work.WORK_SAVE(isSocAdmin)
                : toasts.success.work.DRAFT_SAVE(isSocAdmin)
            )
          );
        }
        resetForm({ values: workToFormData(newFormData.work, initialValues) });
      }
    } catch (e) {
      if (e === WorkRegistrationErrorType.SUBMIT_ERROR) {
        setStatus("SUBMIT_FAILED_CHANGES_SAVED");
      }
      if (e === WorkRegistrationErrorType.SAVE_ERROR_IPI) {
        setStatus(WorkRegistrationErrorType.SAVE_ERROR_IPI);
      }
      if (forLeavingGuard) {
        throw e;
      }
    }
  };

  const showRegistrationsTab = Boolean(work) && workRegistrations.length > 0;
  const ipiSearchCreatorsLink =
    work?._links.ipiSearchCreators?.href ||
    workListItemsLinks.ipiSearchCreators?.href;
  const ipiSearchPublishersLink =
    work?._links.ipiSearchPublishers?.href ||
    workListItemsLinks.ipiSearchPublishers?.href;

  return (
    <React.Fragment>
      <Formik
        initialValues={
          work ? workToFormData(work, initialValues) : initialValues
        }
        validationSchema={validationSchema}
        onSubmit={onSubmit}
      >
        {(formikProps) => {
          const {
            values,
            setFieldValue,
            errors,
            touched,
            isSubmitting,
            dirty,
            submitForm,
            validateForm,
            setTouched,
          } = formikProps;
          const languageCodeRequired = !titleLangCodeRequiredRegex.test(
            values.title.toUpperCase()
          );
          const altLanguageCodeRequired = !titleLangCodeRequiredRegex.test(
            values.altTitle.toUpperCase()
          );
          return (
            <Form
              className={classes.form}
              onKeyDown={preventEnterOnKeyDown}
              data-testid="work-form"
            >
              {ipiSearchCreatorsLink && editable && activeTab === "parties" ? (
                <Field>
                  {(props: FieldProps) => (
                    <FormikAddWorkWriter
                      {...props}
                      apiSource={ipiSearchCreatorsLink}
                    />
                  )}
                </Field>
              ) : null}
              <ContainerAppBar
                dispatch={dispatch}
                parentTitle="Works"
                parentTo={getParentPath(work)}
                title={breadCrumb}
                secondaryContent={
                  <Box pt={2} pb={0}>
                    <Grid container className={classes.containerSpacingFix}>
                      <WorkTitleInput
                        placeholder="My New Music Work"
                        languageCodeRequired={languageCodeRequired}
                        languageCodes={languageCodes}
                        disabled={!editable || isSubmitting}
                      />
                      <Box className={classes.appBarContent}>
                        <TabsNavigation<WorkNavigationTabs>
                          addToUrl
                          activeTab={activeTab}
                          setActiveTab={setActiveTab}
                          tabs={[
                            {
                              label: "Parties",
                              value: WorkNavigationTabs.PARTIES,
                            },
                            {
                              label: "Details",
                              value: WorkNavigationTabs.DETAILS,
                            },
                            {
                              label: "Agreements",
                              value: WorkNavigationTabs.AGREEMENTS,
                              disable: isCreator,
                            },
                            {
                              label: "Registrations",
                              value: WorkNavigationTabs.REGISTRATIONS,
                              disable: !showRegistrationsTab,
                            },
                          ]}
                        />
                        {!isSocAdmin && (
                          <Box display="inline-flex">
                            <Button
                              data-testid="work-cancel"
                              onClick={() => {
                                history.push(getParentPath());
                              }}
                            >
                              Cancel
                            </Button>
                            <ButtonSubmit
                              data-testid="work-save"
                              variant="text"
                              color="inherit"
                              className={classes.saveButton}
                              submitting={workSaving && isSubmitting}
                              disabled={isSubmitting || !editable}
                              onClick={() => {
                                scrollToFormErrors(errors);
                                handleSave(errors);
                              }}
                            >
                              Save
                            </ButtonSubmit>
                            <Button
                              data-testid="work-submit"
                              variant="contained"
                              color="primary"
                              className={classes.submitButton}
                              disabled={isSubmitting || !editable}
                              onClick={async () => {
                                await handleSubmit(errors);
                                const validationErrors = await validateForm(
                                  values
                                );
                                scrollToFormErrors(validationErrors);
                                validateMissingTitle(
                                  dispatch,
                                  errors,
                                  validationErrors
                                );
                                setTouched(mapValuesDeep(values, () => true));
                                if (errors.companyPublisherError) {
                                  setShowRequiredAssociationDialog(true);
                                }
                                if (isEmpty(validationErrors)) {
                                  setSummaryDialogOpen(true);
                                }
                              }}
                            >
                              Submit
                            </Button>
                          </Box>
                        )}
                      </Box>
                    </Grid>
                  </Box>
                }
              />
              <Paper square className={classes.content}>
                <Box
                  display={
                    activeTab === WorkNavigationTabs.PARTIES
                      ? "initial"
                      : "none"
                  }
                  height={1}
                >
                  <TabCreators
                    work={work}
                    creators={values.creators}
                    ipiSearchPublishersApi={ipiSearchPublishersLink}
                    publishers={values.publishers}
                    setFieldValue={setFieldValue}
                    isSubmitting={isSubmitting}
                    editable={editable}
                    dispatchEffect={dispatchEffect}
                    ipiList={ipiList}
                    breakpointOffset={breakpointOffset}
                    isPublisher={isPublisher}
                    isCreator={isCreator}
                    company={company}
                    account={account}
                    dispatch={dispatch}
                    errors={errors}
                    touched={touched}
                  />
                </Box>
                <Box
                  display={
                    activeTab === WorkNavigationTabs.DETAILS
                      ? "initial"
                      : "none"
                  }
                  height={1}
                >
                  <TabDetails
                    altLanguageCodeRequired={altLanguageCodeRequired}
                    distributionCategories={convertToValueLabel(
                      MusicalCategoryCodeNames
                    )}
                    hasBeenRecordedOptions={convertToValueLabel(
                      hasBeenRecorderCodeNames
                    )}
                    languageCodes={languageCodes}
                    editable={editable}
                  />
                </Box>
                <Box
                  display={
                    activeTab === WorkNavigationTabs.REGISTRATIONS
                      ? "initial"
                      : "none"
                  }
                  height={1}
                >
                  <TabRegistrations
                    workRegistrations={workRegistrations}
                    dispatchEffect={dispatchEffect}
                    isTabOpen={activeTab === "registrations"}
                    openRegistrationDialog={openRegistrationDialog}
                  />
                </Box>
                {!isCreator && (
                  <Box
                    display={
                      activeTab === WorkNavigationTabs.AGREEMENTS
                        ? "initial"
                        : "none"
                    }
                    height={1}
                  >
                    <TabWorkAgreements
                      editable={editable}
                      workFormValues={values}
                      setFieldValue={setFieldValue}
                    />
                  </Box>
                )}
              </Paper>
              <LeavingGuard
                when={dirty && !isSubmitting}
                navigate={(path) => history.push(path)}
                shouldBlockNavigation={(location) => {
                  // Ignore search params and only check pathname
                  return location.pathname !== history.location.pathname;
                }}
                onSubmitPromise={async () => {
                  const validationErrors = await validateForm();
                  setTouched(mapValuesDeep(values, () => true));
                  handleSave(validationErrors);
                  if (Object.keys(validationErrors).length) {
                    throw validationErrors;
                  } else {
                    await onSubmit(values, formikProps, true);
                  }
                }}
              />
              <BlockNavigation
                when={
                  isSubmitting &&
                  !redirectingToWork &&
                  !redirectingToSubmissionsDialog
                }
              />
              <DialogWorkSummary
                account={account}
                isSubmitting={isSubmitting}
                open={summaryDialogOpen}
                handleClose={() => setSummaryDialogOpen(false)}
                onSubmit={() => handleSubmit(errors)}
                values={values}
                submitForm={submitForm}
                isPublisher={isPublisher}
              />
            </Form>
          );
        }}
      </Formik>
      {showRequiredAssociationDialog && (
        <DialogWarning
          dialogTitle="Publisher Missing"
          labelConfirm="Ok"
          onConfirm={() => setShowRequiredAssociationDialog(false)}
          onCancel={() => setShowRequiredAssociationDialog(false)}
          hideCancelButton
          width={400}
          message={
            <React.Fragment>
              <Typography color="textSecondary" variant="body2">
                You must associate <b>{company?.name}</b> with at least one
                creator in order to submit this work.
              </Typography>
            </React.Fragment>
          }
        />
      )}
    </React.Fragment>
  );
};
