import React from "react";

interface ListItemRippleDisablerProps {
  children: React.ReactNode;
  setDisableRipple: (state: boolean) => void;
}

export const ListItemRippleDisabler: React.FC<
  ListItemRippleDisablerProps &
    React.DetailedHTMLProps<
      React.HTMLAttributes<HTMLSpanElement>,
      HTMLSpanElement
    >
> = ({ children, setDisableRipple, ...props }) => (
  <span
    {...props}
    onMouseEnter={() => setDisableRipple(true)}
    onMouseLeave={() => setDisableRipple(false)}
  >
    {children}
  </span>
);
