import { connectRouter } from "connected-react-router";
import { History } from "history";
import { AnyAction, combineReducers, Dispatch, Reducer } from "redux";
import {
  alertsInitialState,
  alertsReducer,
} from "./components/pages/Alerts/reducer";
import {
  signInInitialState,
  signInReducer,
} from "./components/pages/SignIn/reducer";
import {
  TypedUseSelectorHook,
  useDispatch as useDispatchGeneric,
  useSelector as useSelectorGeneric,
} from "react-redux";
import {
  worksInitialState,
  worksReducer,
} from "./components/pages/Works/reducer";
import { ThunkAction } from "redux-thunk";
import {
  workRegistrationInitialState,
  workRegistrationReducer,
} from "./components/pages/WorkRegistration/reducer";
import {
  notificationsInitialState,
  notificationsReducer,
} from "./components/pages/Notifications/reducer";
import {
  sidebarInitialState,
  sidebarReducer,
} from "./components/pages/Sidebar/reducer";
import {
  templatesInitialState,
  templatesReducer,
} from "./components/pages/Templates/reducer";
import { ipiInitialState, ipiReducer } from "./components/pages/Ipi/reducer";
import {
  validationSchemaInitialState,
  validationSchemaReducer,
} from "./validationSchema/reducer";
import { SIGN_OUT_SUCCESS } from "./components/pages/SignIn/actions.types";
import {
  agreementsInitialState,
  agreementsReducer,
} from "./components/pages/AgreementRegistration/reducer";
import {
  companyInitialState,
  companyReducer,
} from "./components/pages/Company/reducer";
import { agreementTemplatesReducer } from "./components/pages/AgreementTemplates/reducer";
import { themeInitialState, themeReducer } from "./components/pages/Theme";
import {
  settingsInitialState,
  settingsReducer,
} from "./components/pages/Settings/reducer";
import { faqInitialState, faqReducer } from "./components/pages/Faq/reducer";

export const useAppSelector: TypedUseSelectorHook<AppState> = useSelectorGeneric;

type TDispatch<R> = (action: ThunkAction<R, AppState, void, AnyAction>) => R;
export type AppDispatch = Dispatch;
export type AppDispatchEffect = TDispatch<Promise<any | AnyAction>>;
export const useAppDispatch: () => AppDispatch = useDispatchGeneric;
export const useAppDispatchEffect: () => AppDispatchEffect = useDispatchGeneric;

export const rootReducers = {
  theme: themeReducer,
  signIn: signInReducer,
  alerts: alertsReducer,
  sidebar: sidebarReducer,
  works: worksReducer,
  workRegistration: workRegistrationReducer,
  notifications: notificationsReducer,
  templates: templatesReducer,
  ipi: ipiReducer,
  validationSchema: validationSchemaReducer,
  agreements: agreementsReducer,
  agreementTemplates: agreementTemplatesReducer,
  company: companyReducer,
  settings: settingsReducer,
  faq: faqReducer,
};

export const createRootReducer = (history: History): Reducer => {
  const appReducer = combineReducers({
    ...rootReducers,
    router: connectRouter(history),
  });

  const rootReducer = (state: AppState, action: AnyAction) => {
    if (action.type === SIGN_OUT_SUCCESS) {
      state = {
        theme: {
          ...themeInitialState,
          themeLoadingState: state.theme.themeLoadingState,
          current: state.theme.current,
          palette: state.theme.palette,
        },
        agreementTemplates: agreementsInitialState,
        agreements: agreementsInitialState,
        alerts: {
          ...alertsInitialState,
          silent: state.alerts.silent,
        },
        company: companyInitialState,
        faq: faqInitialState,
        ipi: ipiInitialState,
        notifications: notificationsInitialState,
        router: state.router,
        settings: {
          ...settingsInitialState,
          apiKeyWeb: state.settings.apiKeyWeb,
        },
        sidebar: sidebarInitialState,
        signIn: signInInitialState,
        templates: templatesInitialState,
        validationSchema: validationSchemaInitialState,
        workRegistration: workRegistrationInitialState,
        works: worksInitialState,
      };
    }

    return appReducer(state, action);
  };
  return rootReducer;
};

const rootReducerType = combineReducers({
  ...rootReducers,
  router: connectRouter({} as History),
});

export type AppState = ReturnType<typeof rootReducerType>;
