import React from "react";
import { Box, createStyles, makeStyles, Theme } from "@material-ui/core";
import { paths } from "../../../utils/common";
import { TabMenu } from "../../molecules/TabMenu";
import {
  getFetchEmployeesLinkSelector,
  getFetchInvitesLinkSelector,
} from "../../pages/SignIn/selectors";
import { AppState } from "../../../App.reducer";

const tabs = [
  {
    label: "Employees",
    link: paths.EMPLOYEES_LIST,
    access: getFetchEmployeesLinkSelector,
  },
  {
    label: "Invites",
    link: paths.EMPLOYEES_INVITES,
    access: getFetchInvitesLinkSelector,
  },
];

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    filterIcon: {
      alignSelf: "center",
      marginLeft: theme.spacing(1.75),
    },
    appBarContent: {
      width: "100%",
      marginTop: theme.spacing(1),
      display: "inline-flex",
      alignItems: "baseline",
      justifyContent: "space-between",
    },
  })
);

interface EmployeesNavigationBarProps {
  state: AppState;
}

export const EmployeesNavigationBar: React.FC<EmployeesNavigationBarProps> = ({
  state,
}) => {
  const classes = useStyles();
  const filteredTabs = tabs.filter(({ access }) =>
    access ? Boolean(access(state)) : true
  );

  return (
    <Box className={classes.appBarContent}>
      <TabMenu tabs={filteredTabs} />
      {/* TODO:: Be does not support filtering and search. Implement later. */}
      {/*<Box ml="auto" mr={1}>*/}
      {/*  <InputSearch onChange={() => {}} />*/}
      {/*</Box>*/}
      {/*<FilterListIcon color="inherit" className={classes.filterIcon} />*/}
    </Box>
  );
};
