import { ThunkAction, ThunkDispatch } from "redux-thunk";
import Promise from "promise-polyfill";
import { AppState } from "../../../App.reducer";
import { Action } from "redux";
import axios, { Method } from "axios";
import {
  deleteIpiAction,
  deleteIpiFailureAction,
  deleteIpiSuccessAction,
  fetchIntegratedSocietiesAction,
  fetchIntegratedSocietiesFailureAction,
  fetchIntegratedSocietiesSuccessAction,
  fetchIpiAssociationsAction,
  fetchIpiAssociationsFailureAction,
  fetchIpiAssociationsSuccessAction,
  fetchIpiListAction,
  fetchIpiListFailureAction,
  fetchIpiListSuccessAction,
  fetchSocietiesAction,
  fetchSocietiesFailureAction,
  fetchSocietiesSuccessAction,
  patchIpiAction,
  patchIpiFailureAction,
  patchIpiSuccessAction,
  postAddIpiManuallyAction,
  postAddIpiManuallyFailureAction,
  postAddIpiManuallySuccessAction,
  postAddIpiV1Action,
  postAddIpiV1FailureAction,
  postAddIpiV1SuccessAction,
  postAddIpiV2Action,
  postAddIpiV2FailureAction,
  postAddIpiV2SuccessAction,
  postVerifyIpiV1Action,
  postVerifyIpiV1SuccessAction,
} from "./actions";
import {
  FetchIpiAssociationsResponse,
  FetchIpiResponse,
  FetchSocietiesResponse,
  IdentifierVerificationResponse,
  PatchIpiResponse,
  PostAddIpiManuallyResponse,
} from "../../../api.types";
import { addAlertAction } from "../Alerts/actions";
import { AlertType } from "../Alerts/actions.types";
import { AssociatedWork, Ipi } from "../../../utils/models";
import { handleError } from "../../../utils/errorHandler";
import { toasts } from "../../../utils/toasts";
import {
  getAddIpiV0LinkSelector,
  getAddIpiV1LinkSelector,
  getAddIpiV2LinkSelector,
} from "./selectors";
import { getIpisLink } from "./effects.utils";
import { getIpiListFetchLinkSelector } from "../SignIn/selectors";
import { fetchIpiResponse } from "../../../__mocks__/constants";

export const fetchIpiListEffect =
  (): ThunkAction<
    Promise<void | FetchIpiResponse>,
    AppState,
    void,
    Action<string>
  > =>
  async (
    dispatch: ThunkDispatch<AppState, void, Action<string>>,
    getState: () => AppState
  ) => {
    const ipiListLink = getIpiListFetchLinkSelector(getState());
    dispatch(fetchIpiListAction());
    try {
      // const fetchIpiResponse = await axios.get<FetchIpiResponse>(
      //   ipiListLink?.href || "",
      //   { withCredentials: true }
      // );
      const { items, _links } = fetchIpiResponse.response.ipi;
      dispatch(fetchIpiListSuccessAction(items, _links));
      return fetchIpiResponse;
    } catch (e) {
      const error = handleError(e.response, "IPIS");
      dispatch(fetchIpiListFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.GET_IPIS} ${error.message}`
        )
      );
    }
  };

export const fetchIpiAssociationsEffect =
  (
    ipiGuid: string
  ): ThunkAction<
    Promise<void | AssociatedWork[] | null>,
    AppState,
    void,
    Action<string>
  > =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchIpiAssociationsAction());
    const ipiAssociationsLink =
      getState().ipi.linksIpi?.associations.href || "";
    try {
      const response = await axios.get<FetchIpiAssociationsResponse>(
        ipiAssociationsLink?.replace("{guid}", ipiGuid),
        {
          withCredentials: true,
        }
      );
      dispatch(fetchIpiAssociationsSuccessAction());
      return response.data.response.associatedWorks.items;
    } catch (e) {
      const error = handleError(e.response, "IPI associations");
      dispatch(fetchIpiAssociationsFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.IPI_WORK_ASSOCIATIONS} ${error.message}`
        )
      );
      return null;
    }
  };

export const fetchIntegratedSocietiesEffect =
  (): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchIntegratedSocietiesAction());
    const societiesURL = await getIpisLink(
      "listIntegratedSocieties",
      dispatch,
      getState
    );
    try {
      const response = await axios.get<FetchSocietiesResponse>(
        societiesURL.href,
        { withCredentials: true }
      );
      const { items, _links } = response.data.response.societies;
      dispatch(fetchIntegratedSocietiesSuccessAction(items, _links));
    } catch (e) {
      dispatch(fetchIntegratedSocietiesFailureAction());
      const error = handleError(e.response, "societies");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.GET_SOCIETIES} ${error.message}`
        )
      );
    }
  };

export const fetchSocietiesEffect =
  (): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchSocietiesAction());
    const societiesURL = await getIpisLink("listSocieties", dispatch, getState);
    try {
      const response = await axios.get<FetchSocietiesResponse>(
        societiesURL.href,
        { withCredentials: true }
      );
      const { items } = response.data.response.societies;
      dispatch(fetchSocietiesSuccessAction(items));
    } catch (e) {
      dispatch(fetchSocietiesFailureAction());
      const error = handleError(e.response, "societies");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.GET_SOCIETIES} ${error.message}`
        )
      );
    }
  };

export const postAddIpiV2Effect =
  (
    societyGuid: string
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(postAddIpiV2Action());
    const addIpiV2URL = getAddIpiV2LinkSelector(getState());

    try {
      const response = await axios.post<IdentifierVerificationResponse>(
        addIpiV2URL?.href || "",
        {
          societyId: societyGuid,
        },
        { withCredentials: true }
      );
      dispatch(postAddIpiV2SuccessAction());
      const redirect =
        response.data.response.identifierVerification._links.redirect.href;
      redirect && window.location.replace(redirect);
    } catch (e) {
      const error = handleError(e.response, "IPI");
      dispatch(postAddIpiV2FailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.ADD_IPI} ${error.message}`
        )
      );
    }
  };

export const postAddIpiV1Effect =
  (
    societyGuid: string,
    email: string,
    ipiNumber: string,
    makeDefault: boolean
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(postAddIpiV1Action());
    const addIpiV1URL = getAddIpiV1LinkSelector(getState());

    try {
      await axios.post<IdentifierVerificationResponse>(
        addIpiV1URL?.href || "",
        {
          society: societyGuid,
          email,
          ipiNameNumber: ipiNumber,
          makeDefault,
        },
        { withCredentials: true }
      );
      dispatch(postAddIpiV1SuccessAction());
      dispatch(addAlertAction(AlertType.INFO, toasts.info.CHECK_EMAIL));
    } catch (e) {
      const error = handleError(e.response, "IPI");
      dispatch(postAddIpiV1FailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.ADD_IPI} ${error.message}`
        )
      );
    }
  };

export const postVerifyIpiV1Effect =
  (
    url: string,
    method: Method,
    token: string
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(postVerifyIpiV1Action());
    try {
      await axios.request({
        url,
        method,
        data: { token },
        withCredentials: true,
      });
      dispatch(postVerifyIpiV1SuccessAction());
    } catch (e) {
      const error = handleError(e.response, "IPI");
      throw error;
    }
  };

export const patchIpiEffect =
  (ipi: Ipi): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(patchIpiAction());
    const patchIpiURL = getState().ipi.linksIpi?.patch.href || "";
    try {
      const response = await axios.patch<PatchIpiResponse>(
        patchIpiURL.replace("{ipiNameNumber}", `${ipi.ipiNameNumber}`),
        {
          default: ipi.default,
        },
        {
          withCredentials: true,
        }
      );
      dispatch(patchIpiSuccessAction(response.data.response.ipi.item));
    } catch (e) {
      dispatch(patchIpiFailureAction());
      const error = handleError(e.response, "IPI");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.UPDATE_IPI} ${error.message}`
        )
      );
    }
  };

export const postIpiManually =
  ({
    ipiNameNumber,
    makeDefault,
    prSocietyAffiliation,
  }: {
    ipiNameNumber: number;
    makeDefault: boolean;
    prSocietyAffiliation: number;
  }): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(postAddIpiManuallyAction());
    const link = getAddIpiV0LinkSelector(getState());
    try {
      const response = await axios.post<PostAddIpiManuallyResponse>(
        link?.href || "",
        {
          ipiNameNumber: ipiNameNumber,
          default: makeDefault,
          prSocietyAffiliation,
        },
        {
          withCredentials: true,
        }
      );
      dispatch(
        postAddIpiManuallySuccessAction(response.data.response.ipi.item)
      );
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.company.ADD_IPI)
      );
    } catch (e) {
      const error = handleError(e.response, "IPI");
      const errorMessage = `${toasts.error.company.ADD_IPI} ${error.message}`;
      dispatch(postAddIpiManuallyFailureAction());
      dispatch(addAlertAction(AlertType.ERROR, errorMessage));
      throw new Error(errorMessage);
    }
  };

export const deleteIpiEffect =
  (
    ipiGuid: string
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(deleteIpiAction());
    const deleteIpiLink = getState().ipi.linksIpi?.deleteIpi.href || "";
    try {
      await axios.delete(deleteIpiLink.replace("{guid}", ipiGuid), {
        withCredentials: true,
      });
      dispatch(deleteIpiSuccessAction(ipiGuid));
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.company.DELETE_IPI)
      );
    } catch (e) {
      const error = handleError(e.response, "IPI");
      dispatch(deleteIpiFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.company.DELETE_IPI} ${error.message}`
        )
      );
    }
  };
