import { useTheme } from "@material-ui/core/styles";
import React, { useState } from "react";
import { Box, useMediaQuery } from "@material-ui/core";
import { _WorkListItemLinks, WorkListItem } from "../../../utils/models";
import { useHistory } from "react-router";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import { paths } from "../../../utils/common";
import { DialogLeaveWork } from "../../molecules/DialogLeaveWork";
import "url-polyfill";
import { downloadCwrEffect, workCloneEffect } from "../../pages/Works/effects";
import { HeaderTableWorks } from "../HeaderTableWorks";
import { TableList } from "../../molecules/TableList";
import { InfiniteScroll, ListType } from "../InfiniteScroll/component";
import { createTemplateFromWorkEffect } from "../../pages/Templates/effects";
import { ListItemWork } from "../../molecules/ListItemWork/component";
import { useWindowSize } from "../../../hooks";

interface TableWorksProps {
  works: WorkListItem[];
  dispatchEffect: AppDispatchEffect;
  isPublisher?: boolean;
  canDuplicateWork?: boolean;
  canCreateWorkFromTemplate?: boolean;
  dispatch: AppDispatch;
  workListLinks?: _WorkListItemLinks;
}

export const TableWorks = ({
  works,
  dispatchEffect,
  isPublisher,
  canDuplicateWork,
  canCreateWorkFromTemplate,
  dispatch,
  workListLinks,
}: TableWorksProps) => {
  const theme = useTheme();
  const hideBreakpoint = useMediaQuery(theme.breakpoints.up("sm"));
  const { push } = useHistory();
  const [showDialog, setShowDialog] = useState(false);
  const [workGuid, setWorkGuid] = useState("");
  const [windowWidth] = useWindowSize({ subtractSidebarWidth: true });

  const handleDuplicate = (workGuid: string) => {
    try {
      push(paths.WORKS_REGISTER);
      dispatchEffect(workCloneEffect(workGuid));
    } catch (e) {}
  };

  const handleCreateTemplateFromWork = (workGuid: string) => {
    try {
      push(paths.WORK_TEMPLATE);
      dispatchEffect(createTemplateFromWorkEffect(workGuid));
    } catch (e) {}
  };

  const handleDownloadCwr = (url: string) => () => {
    dispatchEffect(downloadCwrEffect(url));
  };

  return (
    <React.Fragment>
      <DialogLeaveWork
        showDialog={showDialog}
        hideDialog={() => setShowDialog(false)}
        workGuid={workGuid}
        dispatchEffect={dispatchEffect}
        isPublisher={isPublisher}
      />
      <TableList
        listId={ListType.WORKS}
        header={<HeaderTableWorks dispatch={dispatch} />}
      >
        <InfiniteScroll
          list={ListType.WORKS}
          dataLength={works.length}
          dispatchEffect={dispatchEffect}
        >
          {works.map((work) => (
            <ListItemWork
              key={work.guid}
              work={work}
              onClick={() => push(`${paths.WORKS_REGISTER}/${work.guid}`)}
              onCreateTemplateFromWork={() =>
                handleCreateTemplateFromWork(work.guid)
              }
              onDuplicate={() => handleDuplicate(work.guid)}
              canDuplicateWork={canDuplicateWork}
              canCreateWorkFromTemplate={canCreateWorkFromTemplate}
              canLeaveWork={Boolean(workListLinks?.leave)}
              hideBreakpoint={hideBreakpoint}
              windowWidth={windowWidth}
              onLeaveWork={() => {
                setWorkGuid(work.guid);
                setShowDialog(true);
              }}
              onDownloadCwr={
                work._links?.downloadCwr &&
                handleDownloadCwr(work._links?.downloadCwr.href)
              }
            />
          ))}
          <Box height={46} />
        </InfiniteScroll>
      </TableList>
    </React.Fragment>
  );
};
