import React from "react";
import {
  Box,
  Button,
  createStyles,
  DialogActions,
  DialogContent,
  DialogTitle,
  makeStyles,
  Typography,
} from "@material-ui/core";
import { ButtonCircularProgress } from "../../atoms/ButtonCircularProgress";
import { Theme } from "@material-ui/core/styles";
import { Dialog } from "../Dialog";

interface StyleProps {
  width: number | string;
}

const useStyles = makeStyles<Theme, StyleProps>(() =>
  createStyles({
    dialog: (props) => ({
      minWidth: 400,
      maxWidth: props.width,
      width: "100%",
    }),
  })
);

interface DialogWarningProps {
  onConfirm: () => void;
  onCancel?: () => void;
  message: string | React.ReactNode;
  labelCancel?: string;
  labelConfirm?: string;
  width?: number | string;
  hideCancelButton?: boolean;
  dialogTitle?: string;
  isLoading?: boolean;
}
export const DialogWarning: React.FC<DialogWarningProps> = ({
  onConfirm,
  onCancel,
  message,
  width = 280,
  labelCancel = "No",
  labelConfirm = "Yes",
  dialogTitle = "Are you sure?",
  hideCancelButton,
  isLoading,
}) => {
  const classes = useStyles({ width });

  return (
    <Dialog
      variant="simple"
      open={true}
      onClose={onCancel}
      classes={{ paper: classes.dialog }}
      data-testid="dialog-warning"
    >
      <DialogTitle>{dialogTitle}</DialogTitle>
      <DialogContent>
        <Box pb={1}>
          {typeof message === "string" ? (
            <Typography color="textSecondary" variant="body2">
              {message}
            </Typography>
          ) : (
            message
          )}
        </Box>
      </DialogContent>
      <DialogActions>
        {!hideCancelButton && (
          <Button variant="text" color="primary" onClick={onCancel}>
            {labelCancel}
          </Button>
        )}
        <Button
          variant="text"
          color="primary"
          onClick={onConfirm}
          disabled={isLoading}
        >
          {isLoading ? <ButtonCircularProgress variant="text" /> : labelConfirm}
        </Button>
      </DialogActions>
    </Dialog>
  );
};
