import {
  FETCH_NOTIFICATIONS,
  FETCH_NOTIFICATIONS_FAILURE,
  FETCH_NOTIFICATIONS_SUCCESS,
  FETCH_UNSEEN_NOTIFICATIONS_COUNT,
  FETCH_UNSEEN_NOTIFICATIONS_COUNT_FAILURE,
  FETCH_UNSEEN_NOTIFICATIONS_COUNT_SUCCESS,
  NotificationsActionTypes,
  PATCH_NOTIFICATION,
  PATCH_NOTIFICATION_FAILURE,
  PATCH_NOTIFICATION_SUCCESS,
} from "./actions.types";
import {
  _NotificationsLinks,
  ListMeta,
  Notification,
} from "../../../utils/models";

export const fetchNotificationsAction = (
  next?: boolean
): NotificationsActionTypes => ({
  type: FETCH_NOTIFICATIONS,
  next,
});

export const fetchNotificationsSuccessAction = ({
  notifications,
  _links,
  next,
  meta,
}: {
  notifications: Notification[];
  _links: _NotificationsLinks;
  next?: boolean;
  meta?: ListMeta;
}): NotificationsActionTypes => ({
  type: FETCH_NOTIFICATIONS_SUCCESS,
  notifications,
  _links,
  next,
  meta,
});

export const fetchNotificationsFailureAction = (): NotificationsActionTypes => ({
  type: FETCH_NOTIFICATIONS_FAILURE,
});

export const patchNotificationAction = (): NotificationsActionTypes => ({
  type: PATCH_NOTIFICATION,
});

export const patchNotificationSuccessAction = (
  guid: string
): NotificationsActionTypes => ({
  type: PATCH_NOTIFICATION_SUCCESS,
  guid,
});

export const patchNotificationFailureAction = (): NotificationsActionTypes => ({
  type: PATCH_NOTIFICATION_FAILURE,
});

export const fetchUnseenNotificationsCountAction = (): NotificationsActionTypes => ({
  type: FETCH_UNSEEN_NOTIFICATIONS_COUNT,
});

export const fetchUnseenNotificationsCountSuccessAction = (
  count: number
): NotificationsActionTypes => ({
  type: FETCH_UNSEEN_NOTIFICATIONS_COUNT_SUCCESS,
  count,
});

export const fetchUnseenNotificationsCountFailureAction = (): NotificationsActionTypes => ({
  type: FETCH_UNSEEN_NOTIFICATIONS_COUNT_FAILURE,
});
