const stringifyPublisher = ({
  ipiNameNumber,
  mrShare,
  prShare,
  creatorPublisherSocietyAgreementNumber,
  publisherSocietyAgreementNumber,
}: {
  ipiNameNumber: number;
  mrShare?: number;
  prShare?: number;
  creatorPublisherSocietyAgreementNumber?: string;
  publisherSocietyAgreementNumber?: string;
}) =>
  JSON.stringify({
    ipi: ipiNameNumber,
    creatorPublisherSocietyAgreementNumber,
    publisherSocietyAgreementNumber,
    ...((mrShare || mrShare === 0) && { mrShare }),
    ...((prShare || prShare === 0) && { prShare }),
  });

const stringifyPublishers = (
  array?: Array<{ ipiNameNumber: number; mrShare?: number; prShare?: number }>
) => JSON.stringify((array || []).map(stringifyPublisher));

const getPublisherOperations = (
  newState: WorkCreatorPublisher[],
  oldState: WorkCreatorPublisher[]
) => {
  const result: WorkPatchCreatorPublisher[] = [];

  // check for deleted publishers
  oldState.forEach((el) => {
    const isInNewState = newState.find(
      (publisher) => publisher.ipiNameNumber === el.ipiNameNumber
    );
    if (!isInNewState) {
      result.push({
        op: "REMOVE",
        publisher: { ipiNameNumber: el.ipiNameNumber },
      });
    }
  });

  newState.forEach((el) => {
    const match = oldState.find(
      (publisher) => publisher.ipiNameNumber === el.ipiNameNumber
    );

    // check for updated publishers
    if (match && stringifyPublisher(el) !== stringifyPublisher(match)) {
      result.push({
        op: "PATCH",
        publisher: el,
      });
    }

    // check for added publishers
    if (!match) {
      result.push({
        op: "ADD",
        publisher: el,
      });
    }
  });

  return result;
};

export const formatWorkCreatorsPatchData = (
  oldState: Work | Template,
  newState: WorkFormValues | TemplateFormValues,
  isTemplate?: boolean
) => {
  const result: WorkPatchCreator[] = [];
  const allowOperation = (creator: WorkCreator) =>
    !creator.isSelf || isTemplate;

  // Check for newly added creators
  newState.creators.forEach((el) => {
    const isInOldState = oldState.creators?.find(
      (creator) => creator.ipi.ipiNameNumber === el.ipi.ipiNameNumber
    );
    if (!isInOldState && allowOperation(el)) {
      result.push({
        op: "ADD",
        creator: {
          ipiNameNumber: el.ipi.ipiNameNumber,
          mrShare: el.mrShare,
          prShare: el.prShare,
          role: el.role,
          ...(el.publishers && {
            publishers: el.publishers.map((el) => ({
              op: "ADD",
              publisher: el,
            })),
          }),
        },
      });
    }
  });

  // Check for deleted creators
  oldState.creators?.forEach((el) => {
    const isInNewState = newState.creators.find(
      (creator) => creator.ipi.ipiNameNumber === el.ipi.ipiNameNumber
    );
    if (!isInNewState && allowOperation(el)) {
      result.push({
        op: "REMOVE",
        creator: {
          ipiNameNumber: el.ipi.ipiNameNumber,
        },
      });
    }
  });

  // Check for updated creators
  newState.creators.forEach((el) => {
    const oldStateMatch = oldState.creators?.find(
      (creator) => creator.ipi.ipiNameNumber === el.ipi.ipiNameNumber
    );
    const roleChanged = oldStateMatch && oldStateMatch.role !== el.role;
    const mrShareChanged =
      oldStateMatch && oldStateMatch.mrShare !== el.mrShare;
    const prShareChanged =
      oldStateMatch && oldStateMatch.prShare !== el.prShare;
    const publishersChanged =
      oldStateMatch &&
      stringifyPublishers(oldStateMatch.publishers) !==
        stringifyPublishers(el.publishers);

    if (roleChanged || publishersChanged || mrShareChanged || prShareChanged) {
      result.push({
        op: "PATCH",
        creator: {
          ipiNameNumber: el.ipi.ipiNameNumber,
          ...(mrShareChanged && { mrShare: el.mrShare }),
          ...(prShareChanged && { prShare: el.prShare }),
          ...(roleChanged && { role: el.role }),
          ...(publishersChanged && {
            publishers: getPublisherOperations(
              el.publishers || [],
              oldStateMatch?.publishers || []
            ),
          }),
        },
      });
    }
  });

  // Check for updated owner creator which IPI was changed
  const oldStateOwner = oldState.creators?.find((el) => el.isSelf);
  const newStateOwner = newState.creators.find((el) => el.isSelf);
  const roleChanged = oldStateOwner?.role !== newStateOwner?.role;
  const mrShareChanged = oldStateOwner?.mrShare !== newStateOwner?.mrShare;
  const prShareChanged = oldStateOwner?.prShare !== newStateOwner?.prShare;
  const publishersChanged =
    stringifyPublishers(oldStateOwner?.publishers) !==
    stringifyPublishers(newStateOwner?.publishers);

  if (
    oldStateOwner &&
    newStateOwner &&
    oldStateOwner.ipi.ipiNameNumber !== newStateOwner.ipi.ipiNameNumber &&
    (roleChanged || publishersChanged || mrShareChanged || prShareChanged)
  ) {
    result.push({
      op: "PATCH",
      creator: {
        ipiNameNumber: newStateOwner.ipi.ipiNameNumber,
        ...(mrShareChanged && { mrShare: newStateOwner.mrShare }),
        ...(prShareChanged && { prShare: newStateOwner.prShare }),
        ...(roleChanged && { role: newStateOwner.role }),
        ...(publishersChanged && {
          publishers: getPublisherOperations(
            newStateOwner.publishers || [],
            oldStateOwner?.publishers || []
          ),
        }),
      },
    });
  }

  return result.length ? result : undefined;
};
