import React from "react";
import { useAppDispatch, useAppSelector } from "../../../App.reducer";
import { AlertsComponent } from "../../organisms/Alerts";
import { getSidebarOffsetSelector } from "../Sidebar/selectors";

export const Alerts = () => {
  const dispatch = useAppDispatch();
  const state = useAppSelector((state) => state);
  const shiftLeft = getSidebarOffsetSelector(state);
  return (
    <AlertsComponent
      dispatch={dispatch}
      alerts={state.alerts.alerts}
      shiftLeft={shiftLeft}
    />
  );
};
