import React, { useEffect } from "react";
import { AppDispatchEffect } from "../../../App.reducer";
import { Field, Form, Formik } from "formik";
import {
  Button,
  Container,
  createStyles,
  Grid,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { TextField } from "formik-material-ui";
import * as yup from "yup";
import { FormikPasswordInput } from "../../atoms/FormikPasswordInput";
import { PasswordReset } from "../../../utils/models";
import { useHistory } from "react-router-dom";
import { hasEmptyValues } from "../../../utils/helpers";
import { resetPasswordConfirmEffect } from "../../pages/SignIn/effects";
import { paths } from "../../../utils/common";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      maxWidth: 384,
    },
    gridContainer: {
      textAlign: "center",
    },
    header: {
      paddingBottom: theme.spacing(3.5),
    },
    submitButton: {
      marginTop: theme.spacing(3),
    },
  })
);

interface FormPasswordResetConfirmProps {
  dispatchEffect: AppDispatchEffect;
  passwordReset?: PasswordReset;
}

interface FormValues {
  code: string;
  password: string;
  newPassword: string;
}

const initialValues = {
  code: "",
  password: "",
  newPassword: "",
};

const validationSchema = yup.object().shape({
  code: yup.string().required("Code is required"),
  password: yup
    .string()
    .required("Password is required")
    .min(6, "Password is too short"),
  newPassword: yup
    .string()
    .required("Password is required")
    .oneOf([yup.ref("password")], "Passwords must match"),
});

export const FormPasswordResetConfirm: React.FC<FormPasswordResetConfirmProps> = ({
  dispatchEffect,
  passwordReset,
}) => {
  const classes = useStyles();
  const { push } = useHistory();

  const handleSubmit = async ({ newPassword, code }: FormValues) => {
    if (passwordReset) {
      try {
        await dispatchEffect(
          resetPasswordConfirmEffect(
            { code, newPassword, email: passwordReset.email },
            passwordReset._links.confirmForgotPassword
          )
        );
        push(paths.SIGN_IN);
      } catch (e) {}
    }
  };

  useEffect(() => {
    if (!passwordReset) {
      push(`${paths.SIGN_IN}/${paths.subPage.RESET_PASSWORD}`);
    }
  }, [passwordReset, push]);

  return (
    <Container className={classes.container}>
      <Formik
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={handleSubmit}
      >
        {({ isSubmitting, values }) => (
          <Grid
            container
            component={Form}
            spacing={3}
            className={classes.gridContainer}
          >
            <Grid item xs={12}>
              <Typography variant="h5" className={classes.header}>
                Set Password
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Field component={TextField} name="code" label="Code" fullWidth />
            </Grid>
            <Grid item xs={12}>
              <Field
                component={FormikPasswordInput}
                name="password"
                label="New Password"
                fullWidth
                margin="none"
              />
            </Grid>
            <Grid item xs={12}>
              <Field
                component={FormikPasswordInput}
                name="newPassword"
                label="Confirm New Password"
                fullWidth
                margin="none"
              />
            </Grid>
            <Grid item xs={12}>
              <Button
                type="submit"
                color="primary"
                variant="contained"
                fullWidth
                disabled={isSubmitting || hasEmptyValues(values)}
                className={classes.submitButton}
              >
                Set Password
              </Button>
            </Grid>
          </Grid>
        )}
      </Formik>
    </Container>
  );
};
