import React from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { LoadingState } from "../../../types/enums";
import { ListIpiNumbers } from "../../organisms/ListIpiNumbers";
import { IpiEmptyState } from "../../molecules/IpiEmptyState";
import { openIpiSelectionMenuAction } from "./actions";
import { LoadingScreen } from "../../atoms/LoadingScreen";

export const Ipi = () => {
  const dispatch = useAppDispatch();
  const dispatchEffect = useAppDispatchEffect();
  const state = useAppSelector((state) => state);
  const { ipiList, ipiListLoadingState } = state.ipi;
  const handleOpenIpiSelectionMenu = () =>
    dispatch(openIpiSelectionMenuAction(true));

  if (
    ipiListLoadingState === LoadingState.PRISTINE ||
    ipiListLoadingState === LoadingState.FETCHING
  ) {
    return <LoadingScreen />;
  }

  return (ipiListLoadingState === LoadingState.FETCHED ||
    ipiListLoadingState === LoadingState.ERROR) &&
    !ipiList.length ? (
    <IpiEmptyState withButton onClick={handleOpenIpiSelectionMenu} />
  ) : (
    <ListIpiNumbers dispatchEffect={dispatchEffect} ipiList={ipiList} />
  );
};
