import { namespaceRegex } from "./utils/regex";

export const getUrlNamespace = (url?: string) => {
  const namespaceRegexResult = namespaceRegex.exec(
    url || window.location.origin
  );
  const namespace = namespaceRegexResult ? namespaceRegexResult[1] : null;

  if (!namespace || namespace === "test" || namespace === "qa") {
    return "org-session";
  }
  return `org-${namespace}`;
};

enum Env {
  QA = "QA",
  TEST = "TEST",
}

const env =
  window.location.href.indexOf("qa.session.id") > -1 ? Env.QA : Env.TEST;

const baseUrl =
  env === Env.QA ? "https://api.qa.session.id" : "https://api.test.session.id";

export const URL_NAMESPACE = getUrlNamespace();

export const apiUrls = {
  base: baseUrl,
  bootstrap: `${baseUrl}/bootstrap/${URL_NAMESPACE}`,
  auth: `${baseUrl}/authentication`,
  signOut: `${baseUrl}/authentication/logout`,
  passwordForgot: `${baseUrl}/authentication/password/forgot`,
  passwordForgotConfirm: `${baseUrl}/authentication/password/forgot/verify`,
  createAccount: `${baseUrl}/authentication/register`,
  validateSignUpToken: `${baseUrl}/authentication/register/token/{token}/validate`,
  signUp: `${baseUrl}/authentication/register/finalize`,
  authPolicy: `${baseUrl}/authentication/password/policy`,
  changePassword: `${baseUrl}/user/identity/management/change-password`,
  changeEmail: `${baseUrl}/user/identity/management/change-email`,
  refresh: `${baseUrl}/authentication/refresh`,
  works: `${baseUrl}/works`,
  templates: `${baseUrl}/works/templates`,
  agreementTemplates: `${baseUrl}/agreements/templates`,
  cwr: `${baseUrl}/cwr`,
  creatorIdentifiers: `${baseUrl}/creator-identifiers`,
  changeAvatar: `${baseUrl}/change-avatar`, // TODO: change according to backend
  notifications: `${baseUrl}/notification`,
  agreements: `${baseUrl}/agreements`, // TODO:: change with BE provided links when available
  companies: `${baseUrl}/companies`, // TODO:: change with BE provided links when available
};

export const protectedUrls = [
  apiUrls.auth,
  apiUrls.signOut,
  apiUrls.refresh,
  apiUrls.works,
  apiUrls.cwr,
  apiUrls.creatorIdentifiers,
  apiUrls.agreements,
];
