import {
  ADD_ALERT,
  AlertActionTypes,
  AlertType,
  REMOVE_ALERT,
  SILENT_ALERTS,
} from "./actions.types";

export const addAlertAction = (
  alertType: AlertType,
  message: string
): AlertActionTypes => ({
  type: ADD_ALERT,
  alertType,
  message,
});

export const removeAlertAction = (index: number): AlertActionTypes => ({
  type: REMOVE_ALERT,
  index,
});

export const silentAlertsAction = (state: boolean): AlertActionTypes => ({
  type: SILENT_ALERTS,
  state,
});
