import React, { useState } from "react";
import { Box, useMediaQuery, useTheme } from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { AppDispatchEffect } from "../../../App.reducer";
import * as H from "history";
import { TemplateListItem } from "../../../utils/models";
import {
  createFromEffect,
  duplicateTemplateEffect,
} from "../../pages/Templates/effects";
import { HeaderTemplate } from "../HeaderTemplate";
import { TableList } from "../../molecules/TableList";
import { HeaderListTemplates } from "../HeaderListTemplates";
import { InfiniteScroll, ListType } from "../InfiniteScroll/component";
import { paths } from "../../../utils/common";
import { useHistory } from "react-router";
import { DialogDeleteWorkTemplate } from "../../molecules/DialogDeleteWorkTemplate";
import { useWindowSize } from "../../../hooks";

export const useTemplatesListStyles = makeStyles((theme: Theme) =>
  createStyles({
    gridRow: {
      alignItems: "center"
    },
    listRow: {
      minHeight: 85,
    },
    list: {
      paddingTop: theme.spacing(2),
    },
    titleColumn: {
      position: "relative",
      width: "100%",
      minWidth: 180,
    },
    creatorsColumn: {
      minWidth: 230,
      paddingLeft: theme.spacing(3),
      paddingRight: theme.spacing(2),
      width: "50%",
    },
    lastUpdatedColumn: {
      minWidth: 100,
      maxWidth: 130,
      width: "100%",
    },
    buttonColumn: {
      minWidth: 132,
      maxWidth: 160,
      textAlign: "right",
      width: "100%",
    },
    optionsColumn: {
      minWidth: 70,
      maxWidth: 70,
      textAlign: "right",
      paddingRight: theme.spacing(1),
    },
    button: {
      paddingTop: 0,
      paddingBottom: 0,
      minWidth: 129,
    },
  })
);

interface ListTemplatesProps {
  dispatchEffect: AppDispatchEffect;
  templates: TemplateListItem[];
  history: H.History;
  canCreateWorkFromTemplate?: boolean;
  canCreateTemplate?: boolean;
  canDeleteTemplate?: boolean;
}

export const ListTemplates: React.FC<ListTemplatesProps> = ({
  templates,
  dispatchEffect,
  history,
  canCreateWorkFromTemplate = false,
  canCreateTemplate = false,
  canDeleteTemplate = false,
}) => {
  const theme = useTheme();
  const breakpointXs = useMediaQuery(theme.breakpoints.down("xs"));
  const { push } = useHistory();
  const [deleteTemplateGuid, setDeleteTemplateGuid] = useState("");

  const handleTemplateCloning = (guid: string) => {
    try {
      push(paths.WORK_TEMPLATE);
      dispatchEffect(duplicateTemplateEffect(guid));
    } catch (e) {}
  };

  const handleCreateWorkFromTemplate = (guid: string) => {
    try {
      push(paths.WORKS_REGISTER);
      dispatchEffect(createFromEffect(guid));
    } catch (e) {}
  };

  const [windowWidth] = useWindowSize({ subtractSidebarWidth: true });

  return (
    <React.Fragment>
      <DialogDeleteWorkTemplate
        dispatchEffect={dispatchEffect}
        showDialog={Boolean(deleteTemplateGuid)}
        workTemplateGuid={deleteTemplateGuid}
        hideDialog={() => setDeleteTemplateGuid("")}
      />
      <TableList
        listId={ListType.WORK_TEMPLATES}
        header={<HeaderListTemplates breakpointXs={breakpointXs} />}
      >
        <InfiniteScroll
          list={ListType.WORK_TEMPLATES}
          dataLength={templates.length}
          dispatchEffect={dispatchEffect}
        >
          {templates.length > 0 &&
            templates.map((template) => (
              <HeaderTemplate
                key={template.guid}
                template={template}
                history={history}
                onCreateFrom={handleCreateWorkFromTemplate}
                handleTemplateCloning={handleTemplateCloning}
                handleDelete={setDeleteTemplateGuid}
                showCreateFromButton={canCreateWorkFromTemplate}
                canCloneTemplate={canCreateTemplate}
                canDeleteTemplate={canDeleteTemplate}
                windowWidth={windowWidth}
              />
            ))}
          <Box height={46} />
        </InfiniteScroll>
      </TableList>
    </React.Fragment>
  );
};
