import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { useLocation } from "react-router-dom";
import React from "react";
import { SidebarComponent } from "../../organisms/Sidebar";
import { ThemeProvider } from "@material-ui/styles";
import { createMuiTheme } from "@material-ui/core";
import { getTheme } from "@session-id-limited/client-module-ui-components";

export const Sidebar = () => {
  const dispatch = useAppDispatch();
  const dispatchEffect = useAppDispatchEffect();
  const { pathname } = useLocation();
  const state = useAppSelector((state) => state);

  return (
    <ThemeProvider theme={createMuiTheme(getTheme("dark"))}>
      <SidebarComponent
        dispatch={dispatch}
        dispatchEffect={dispatchEffect}
        state={state}
        pathname={pathname}
      />
    </ThemeProvider>
  );
};
