import React, { useEffect } from "react";
import { useAppDispatchEffect } from "../../../App.reducer";
import { AppLoader } from "../../atoms/AppLoader/component";
import { RouteComponentProps } from "react-router";
import { handleUserSignOut } from "./component.utils";

export const SignOut: React.FC<RouteComponentProps<{ subPage: string }>> = ({
  history: { push },
}) => {
  const dispatchEffect = useAppDispatchEffect();

  useEffect(() => {
    handleUserSignOut(dispatchEffect, push);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return <AppLoader />;
};
