import {
  Box,
  createStyles,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import React from "react";
import parse from "autosuggest-highlight/parse";
import match from "autosuggest-highlight/match";
import clsx from "clsx";
import { IpiVerifiedIcon } from "../IpiVerifiedIcon";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      overflow: "hidden",
    },
    primaryText: {
      lineHeight: 1,
    },
    secondaryText: {
      paddingTop: theme.spacing(0.5),
      maxWidth: 125,
      display: "inline-flex",
      alignItems: "center",
    },
    bold: {
      fontWeight: 800,
    },
    multilineText: {
      overflow: "hidden",
      textOverflow: "ellipsis",
      WebkitLineClamp: 2,
      display: "-webkit-box",
      WebkitBoxOrient: "vertical",
    },
  })
);

interface CardPublisherProps {
  ipiVerified?: boolean;
  publisher: {
    displayName: string;
    ipiNameNumber: number;
  };
  wrapDisplayName?: boolean;
  highlightText?: string;
  multiline?: boolean;
}

export const CardPublisher: React.FC<CardPublisherProps> = ({
  publisher: { displayName = "", ipiNameNumber },
  ipiVerified,
  wrapDisplayName,
  highlightText = "",
  multiline,
}: CardPublisherProps) => {
  const classes = useStyles();
  const matches = match(displayName, highlightText);
  const parts = parse(displayName, matches);
  return (
    <Box className={classes.root}>
      <Typography
        variant="subtitle1"
        className={clsx(classes.primaryText, {
          [classes.multilineText]: multiline,
        })}
        noWrap={!wrapDisplayName}
      >
        {highlightText
          ? parts.map((part, index) => (
              <span
                key={index}
                className={clsx({ [classes.bold]: part.highlight })}
              >
                {part.text}
              </span>
            ))
          : displayName}
      </Typography>
      {ipiNameNumber && (
        <Typography
          variant="body2"
          color="textSecondary"
          noWrap
          className={classes.secondaryText}
          data-testid="ipi-name-number"
        >
          IPI {ipiNameNumber} {ipiVerified && <IpiVerifiedIcon />}
        </Typography>
      )}
    </Box>
  );
};
