import { AgreementRightType } from "../../../types/enums";
import {
  ipiRightsMechanical,
  ipiRightsPerforming,
  ipiRightsPrint,
  ipiRightsSynchronisation,
} from "../DialogIpiRights/utils";

export const getIpiRightsByType = (type: AgreementRightType) => {
  if (type === AgreementRightType.M) return ipiRightsMechanical;
  if (type === AgreementRightType.P) return ipiRightsPerforming;
  if (type === AgreementRightType.T) return ipiRightsPrint;
  if (type === AgreementRightType.S) return ipiRightsSynchronisation;
  return [];
};
