import {
  CREATE_FROM,
  DELETE_TEMPLATE,
  DELETE_TEMPLATE_FAILURE,
  DELETE_TEMPLATE_SUCCESS,
  FETCH_TEMPLATE,
  FETCH_TEMPLATE_FAILURE,
  FETCH_TEMPLATE_SUCCESS,
  FETCH_TEMPLATES,
  FETCH_TEMPLATES_FAILURE,
  FETCH_TEMPLATES_SUCCESS,
  FETCH_WORK_TEMPLATE_SCHEMA,
  FETCH_WORK_TEMPLATE_SCHEMA_FAILURE,
  FETCH_WORK_TEMPLATE_SCHEMA_SUCCESS,
  PATCH_TEMPLATE,
  PATCH_TEMPLATE_SUCCESS,
  POST_TEMPLATE,
  POST_TEMPLATE_FAILURE,
  POST_TEMPLATE_SUCCESS,
  RESET_TEMPLATES_LOADING_STATES,
  TEMPLATE_CLONE,
  TEMPLATE_CLONE_FAILURE,
  TEMPLATE_CLONE_SUCCESS,
  TemplatesActionTypes,
  UPDATE_TEMPLATE,
  UPDATE_TEMPLATE_FAILURE,
  UPDATE_TEMPLATE_SUCCESS,
} from "./actions.types";
import {
  _TemplatesListLinks,
  Template,
  TemplateListItem,
} from "../../../utils/models";
import { WorkSchemaJSON } from "../../../validationSchema/models";

export const fetchWorkTemplateSchemaAction = (): TemplatesActionTypes => ({
  type: FETCH_WORK_TEMPLATE_SCHEMA,
});

export const fetchWorkTemplateSchemaSuccessAction = (
  schema: WorkSchemaJSON
): TemplatesActionTypes => ({
  type: FETCH_WORK_TEMPLATE_SCHEMA_SUCCESS,
  schema,
});

export const fetchWorkTemplateSchemaFailureAction = (): TemplatesActionTypes => ({
  type: FETCH_WORK_TEMPLATE_SCHEMA_FAILURE,
});

export const fetchTemplatesAction = (next?: boolean): TemplatesActionTypes => ({
  type: FETCH_TEMPLATES,
  next,
});

export const fetchTemplatesSuccessAction = ({
  templates,
  _links,
  next,
}: {
  templates: TemplateListItem[];
  _links: _TemplatesListLinks;
  next?: boolean;
}): TemplatesActionTypes => ({
  type: FETCH_TEMPLATES_SUCCESS,
  templates,
  _links,
  next,
});

export const fetchTemplatesFailureAction = (): TemplatesActionTypes => ({
  type: FETCH_TEMPLATES_FAILURE,
});

export const fetchTemplateAction = (): TemplatesActionTypes => ({
  type: FETCH_TEMPLATE,
});

export const fetchTemplateSuccessAction = (
  template: Template
): TemplatesActionTypes => ({
  type: FETCH_TEMPLATE_SUCCESS,
  template,
});

export const fetchTemplateFailureAction = (): TemplatesActionTypes => ({
  type: FETCH_TEMPLATE_FAILURE,
});

export const postTemplateAction = (): TemplatesActionTypes => ({
  type: POST_TEMPLATE,
});

export const postTemplateSuccessAction = (
  template: Template
): TemplatesActionTypes => ({
  type: POST_TEMPLATE_SUCCESS,
  template,
});

export const postTemplateFailureAction = (): TemplatesActionTypes => ({
  type: POST_TEMPLATE_FAILURE,
});

export const patchTemplateAction = (): TemplatesActionTypes => ({
  type: PATCH_TEMPLATE,
});

export const patchTemplateSuccessAction = (
  template: Template
): TemplatesActionTypes => ({
  type: PATCH_TEMPLATE_SUCCESS,
  template,
});

export const deleteTemplateAction = (): TemplatesActionTypes => ({
  type: DELETE_TEMPLATE,
});

export const deleteTemplateSuccessAction = (
  guid: string
): TemplatesActionTypes => ({
  type: DELETE_TEMPLATE_SUCCESS,
  guid,
});

export const deleteTemplateFailureAction = (): TemplatesActionTypes => ({
  type: DELETE_TEMPLATE_FAILURE,
});

export const createFromAction = (): TemplatesActionTypes => ({
  type: CREATE_FROM,
});

export const updateTemplateAction = (): TemplatesActionTypes => ({
  type: UPDATE_TEMPLATE,
});

export const updateTemplateSuccessAction = (): TemplatesActionTypes => ({
  type: UPDATE_TEMPLATE_SUCCESS,
});

export const updateTemplateFailureAction = (): TemplatesActionTypes => ({
  type: UPDATE_TEMPLATE_FAILURE,
});

export const resetTemplatesLoadingStates = (): TemplatesActionTypes => ({
  type: RESET_TEMPLATES_LOADING_STATES,
});

export const templateCloneAction = (): TemplatesActionTypes => ({
  type: TEMPLATE_CLONE,
});

export const templateCloneSuccessAction = (
  template: Template
): TemplatesActionTypes => ({
  type: TEMPLATE_CLONE_SUCCESS,
  template,
});

export const templateCloneFailureAction = (): TemplatesActionTypes => ({
  type: TEMPLATE_CLONE_FAILURE,
});
