import { FieldAttributes, FieldProps } from "formik";
import React, { useEffect, useState } from "react";
import { PasswordPolicy } from "../../pages/SignIn/actions.types";
import {
  createStyles,
  FormHelperText,
  IconButton,
  InputAdornment,
  makeStyles,
  TextField,
  Theme,
} from "@material-ui/core";
import { buildPasswordPolicyMessage } from "../../../utils/helpers";
import { View24, ViewOff24 } from "@carbon/icons-react";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    policyText: {
      marginLeft: theme.spacing(1.75),
      lineHeight: `${theme.spacing(1.75)}px`,
    },
    passwordShowIcon: {
      color: theme.palette.custom.primary.disabled,
    },
  })
);

interface FormikPasswordInputProps extends FieldProps {
  passwordPolicy?: PasswordPolicy;
  renderPolicyMessage?: boolean;
  onShowPolicy?: () => void;
  onHidePolicy?: () => void;
}

export const FormikPasswordInput: React.FC<
  FormikPasswordInputProps & FieldAttributes<any>
> = ({
  passwordPolicy,
  onHidePolicy,
  onShowPolicy,
  renderPolicyMessage = true,
  form: { errors, touched, values, isSubmitting },
  field: { name, onBlur, onChange },
  "data-testid": dataTestId,
  ...props
}) => {
  const classes = useStyles();
  const [isFocused, setIsFocused] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const passwordPolicyMessage = passwordPolicy
    ? buildPasswordPolicyMessage(passwordPolicy)
    : "";

  const showPolicyMessage =
    (Boolean(errors[name]) && touched[name] && passwordPolicyMessage) ||
    (passwordPolicyMessage && isFocused);

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    event.preventDefault();
  };

  useEffect(() => {
    if (onShowPolicy && onHidePolicy) {
      showPolicyMessage ? onShowPolicy() : onHidePolicy();
    }
  }, [showPolicyMessage, onShowPolicy, onHidePolicy]);

  return (
    <React.Fragment>
      <TextField
        margin="normal"
        label="Password"
        type={showPassword ? "text" : "password"}
        fullWidth
        name={name}
        value={values[name]}
        helperText={touched[name] && errors[name]}
        onBlur={(e) => {
          onBlur(e);
          setIsFocused(false);
        }}
        onFocus={() => setIsFocused(true)}
        onChange={onChange}
        error={Boolean(touched[name]) && Boolean(errors[name])}
        disabled={isSubmitting}
        {...props}
        inputProps={{
          "data-testid": dataTestId || "password-input",
        }}
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <IconButton
                edge="end"
                data-testid="toggle-visibility"
                aria-label="toggle password visibility"
                onClick={handleClickShowPassword}
                onMouseDown={handleMouseDownPassword}
              >
                {showPassword ? (
                  <ViewOff24
                    className={classes.passwordShowIcon}
                    data-testid="visible"
                  />
                ) : (
                  <View24
                    className={classes.passwordShowIcon}
                    data-testid="hidden"
                  />
                )}
              </IconButton>
            </InputAdornment>
          ),
        }}
      />
      {renderPolicyMessage && showPolicyMessage && (
        <FormHelperText className={classes.policyText}>
          {passwordPolicyMessage}
        </FormHelperText>
      )}
    </React.Fragment>
  );
};
