import React, { useEffect } from "react";
import { RouteComponentProps } from "react-router";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { SignUpVariant } from "../../molecules/SignUpVariant";
import { ContainerWithLogo } from "../../molecules/ContainerWithLogo";
import { paths } from "../../../utils/common";
import { FormSignUp } from "../../organisms/FormSignUp";
import { FormSignUpCompanyRequest } from "../../organisms/FormSignUpCompanyRequest";
import { handleSignupInvitationPayload } from "../../../utils/DLR";
import { SignUpInvitationPayload } from "../../../utils/models";
import {
  getAuthPolicySelector,
  getPasswordPolicyLinkSelector,
} from "../SignIn/selectors";
import { fetchAuthPolicyEffect } from "../SignIn/effects";

const getSignUpTitle = (subPage: string, dlr?: SignUpInvitationPayload) => {
  if (dlr?.ipiNameNumber) {
    return "Publisher employee sign up";
  }
  if (dlr?.cisacCode) {
    return "Society employee sign up";
  }
  return "Creator sign up";
};

export const SignUpView: React.FC<RouteComponentProps<{ subPage: string }>> = ({
  match: {
    params: { subPage },
  },
  location: { search },
}) => {
  const state = useAppSelector((state) => state);
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();
  const passwordPolicyLink = getPasswordPolicyLinkSelector(state);
  const passwordPolicy = getAuthPolicySelector(state);

  const showSignUpForm = [
    paths.subPage.CREATOR,
    paths.subPage.SIGN_UP_INVITE,
  ].includes(subPage);

  const dlrPayload =
    subPage === paths.subPage.SIGN_UP_INVITE
      ? handleSignupInvitationPayload(search) || undefined
      : undefined;

  useEffect(() => {
    if (!passwordPolicy && passwordPolicyLink) {
      dispatchEffect(fetchAuthPolicyEffect());
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [showSignUpForm]);

  return (
    <ContainerWithLogo>
      {!subPage && <SignUpVariant />}
      {showSignUpForm && (
        <FormSignUp
          dispatchEffect={dispatchEffect}
          dispatch={dispatch}
          title={getSignUpTitle(subPage, dlrPayload)}
          inviteDlr={dlrPayload}
          passwordPolicy={passwordPolicy?.passwordPolicy}
        />
      )}
      {subPage === paths.subPage.COMPANY && (
        <FormSignUpCompanyRequest
          dispatchEffect={dispatchEffect}
          dispatch={dispatch}
        />
      )}
    </ContainerWithLogo>
  );
};
