import React from "react";
import { LoadingState } from "../../../types/enums";
import { AutocompleteSelect } from "../../organisms/AutocompleteSelect";
import { createStyles, makeStyles, Theme } from "@material-ui/core";
import { notListedSocietyOptionMock } from "../../organisms/AddIpiNumber";
import { Society } from "../../../utils/models";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    notListedSocietyOption: {
      paddingBottom: 0,
      "& li:last-of-type": {
        borderTop: `1px solid ${theme.palette.divider}`,
      },
    },
  })
);

const getOptions = (
  societies: Society[],
  addManuallyEnabled: boolean,
  loadingState: LoadingState
) =>
  societies
    .map((el) => ({
      title: el.acronym,
      value: el,
    }))
    .concat(
      addManuallyEnabled && loadingState === LoadingState.FETCHED
        ? [
            {
              title: notListedSocietyOptionMock.acronym,
              value: notListedSocietyOptionMock,
            },
          ]
        : []
    );

interface FormikSocietyAutocompleteProps {
  loadingState: LoadingState;
  integratedSocieties: Society[];
  addManuallyEnabled: boolean;
  selectedSociety: Society | null;
  setSelectedSociety: (option: Society | null) => void;
  disabled: boolean;
}

export const FormikSocietyAutocomplete: React.FC<FormikSocietyAutocompleteProps> = ({
  selectedSociety,
  setSelectedSociety,
  loadingState,
  integratedSocieties,
  addManuallyEnabled,
  disabled,
}) => {
  const classes = useStyles();

  return (
    <AutocompleteSelect
      label="Select Society"
      loading={loadingState === LoadingState.FETCHING}
      options={getOptions(
        integratedSocieties,
        addManuallyEnabled,
        loadingState
      )}
      value={
        selectedSociety
          ? {
              title: selectedSociety.acronym,
              value: selectedSociety,
            }
          : null
      }
      onSelect={(selected) => setSelectedSociety(selected?.value || null)}
      getOptionSelected={(option, value) =>
        option?.value?.id === value?.value?.id
      }
      getOptionLabel={(option) => option?.title || ""}
      disabled={disabled}
      openOnFocus
      classes={{
        listbox: classes.notListedSocietyOption,
      }}
    />
  );
};
