import {
  Box,
  createStyles,
  makeStyles,
  RootRef,
  Theme,
  Typography,
} from "@material-ui/core";
import React, { useCallback, useState } from "react";
import { useDropzone } from "react-dropzone";
import CloudDownloadIcon from "@material-ui/icons/CloudDownload";
import Promise from "promise-polyfill";
import { ButtonThemeHover } from "../../atoms/ButtonThemeHover";
import { TooltipMap } from "../../atoms/TooltipMap";
import clsx from "clsx";
import { LinearProgress } from "../../atoms/LinearProgress/component";
import { DialogWarning } from "../DialogWarning";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dropzone: {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      width: "100%",
      height: 104,
      outline: "none",
      background: theme.palette.grey[100],
      border: `2px dashed ${theme.palette.grey[400]}`,
      justifyContent: "center",
      cursor: "pointer",
    },
    dropzoneUploading: {
      cursor: "default",
      background: theme.palette.background.paper,
      border: `1px solid ${theme.palette.divider}`,
    },
    contentMessage: {
      paddingTop: theme.spacing(0.5),
      color: theme.palette.action.disabled,
      fontWeight: theme.typography.fontWeightRegular,
      whiteSpace: "normal",
      textAlign: "center",
      padding: theme.spacing(0, 4),
    },
    container: {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      width: 185,
    },
    customLogoText: {
      userSelect: "none",
      paddingTop: theme.spacing(1),
    },
    customLogoImg: {
      maxHeight: "100%",
      maxWidth: "100%",
      objectFit: "contain",
      objectPosition: "center",
    },
    trashButton: {
      display: "none",
      position: "absolute",
      right: theme.spacing(0.5),
      bottom: theme.spacing(1),
    },
    customLogoDisplay: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      border: "1px dashed grey",
      height: 104,
      width: 185,

      position: "relative",
      "&:hover": {
        "& button": {
          display: "flex",
        },
      },
    },
    loadingContainer: {
      width: "100%",
      padding: theme.spacing(0, 3.5),
      textAlign: "center",
    },
  })
);

const convertImage = (files: Array<File>) =>
  new Promise<any>((resolve, reject) => {
    if (files.length !== 1) {
      reject("Add single file");
    }
    const reader = new FileReader();
    reader.readAsDataURL(files[0]);
    reader.onload = () => {
      const avatarDataUrl = reader.result;
      if (!avatarDataUrl) {
        reject("Image file conversion failed");
      }
      resolve(reader.result);
    };
  });

interface CustomLogoUploadProps {
  uploadImage: (image: string) => void;
  customLogo?: string;
  onDelete: () => void;
}

export const CustomLogoUpload: React.FC<CustomLogoUploadProps> = ({
  uploadImage,
  customLogo,
  onDelete,
}) => {
  const classes = useStyles();
  const [isUploading, setIsUploading] = useState(false);
  const [showLogoDeleteDialog, setShowLogoDeleteDialog] = useState(false);

  const onDrop = useCallback(
    async (acceptedFiles) => {
      try {
        setIsUploading(true);
        const convertedImage = await convertImage(acceptedFiles);
        await uploadImage(convertedImage);
      } catch (e) {
        console.error(e);
      } finally {
        setIsUploading(false);
      }
    },
    [uploadImage]
  );

  const { getRootProps, getInputProps } = useDropzone({
    maxSize: 5000000,
    multiple: false,
    accept: ["image/jpeg", "image/png", "image/bmp"],
    onDrop,
  });

  const { ref, ...rootProps } = getRootProps();

  return (
    <React.Fragment>
      {showLogoDeleteDialog && (
        <DialogWarning
          width={400}
          onConfirm={() => {
            setShowLogoDeleteDialog(false);
            onDelete();
          }}
          onCancel={() => setShowLogoDeleteDialog(false)}
          message="Are you sure you want to remove the custom logo for this theme?"
          dialogTitle="Remove Custom Logo"
          labelCancel="Cancel"
          labelConfirm="Remove"
        />
      )}
      <Box className={classes.container}>
        {customLogo ? (
          <Box className={classes.customLogoDisplay}>
            <img
              src={customLogo}
              alt="custom logo"
              className={classes.customLogoImg}
            />
            <ButtonThemeHover
              onClick={() => setShowLogoDeleteDialog(true)}
              className={classes.trashButton}
            >
              <TooltipMap to="Delete" disableCursor>
                <img
                  src={`${process.env.PUBLIC_URL}/static/media/svg/trashbin.svg`}
                  alt="delete"
                />
              </TooltipMap>
            </ButtonThemeHover>
          </Box>
        ) : (
          <RootRef rootRef={ref}>
            <div
              {...rootProps}
              className={clsx(classes.dropzone, {
                [classes.dropzoneUploading]: isUploading,
              })}
            >
              {isUploading ? (
                <Box className={classes.loadingContainer}>
                  <Typography color="textSecondary" variant="body2">
                    Uploading...
                  </Typography>
                  <LinearProgress />
                </Box>
              ) : (
                <React.Fragment>
                  <CloudDownloadIcon color="disabled" fontSize="large" />
                  <Typography
                    color="textSecondary"
                    variant="body2"
                    className={classes.contentMessage}
                  >
                    Drag image here or click to Upload
                  </Typography>
                  <input {...getInputProps()} />
                </React.Fragment>
              )}
            </div>
          </RootRef>
        )}
        <Typography
          variant="body2"
          color="textSecondary"
          className={classes.customLogoText}
        >
          Custom Logo
        </Typography>
      </Box>
    </React.Fragment>
  );
};
