import { LoadingState } from "../../../types/enums";
import {
  AgreementActionTypes,
  DELETE_AGREEMENT_SUCCESS,
  FETCH_AGREEMENT,
  FETCH_AGREEMENT_FAILURE,
  FETCH_AGREEMENT_SUCCESS,
  FETCH_AGREEMENTS,
  FETCH_AGREEMENTS_FAILURE,
  FETCH_AGREEMENTS_SUCCESS,
  PATCH_AGREEMENT_SUCCESS,
  POST_AGREEMENT_DUPLICATE_SUCCESS,
  POST_AGREEMENT_SUCCESS,
} from "./actions.types";
import {
  _AgreementsLinks,
  Agreement,
  AgreementListItem,
  ListMeta,
} from "../../../utils/models";
import { agreementToListItem } from "./reducer.utils";

export interface AgreementsState {
  item?: Agreement;
  listMeta?: ListMeta;
  list: AgreementListItem[];
  listLoadingState: LoadingState;
  _links?: _AgreementsLinks;
}

export const agreementsInitialState: AgreementsState = {
  list: [],
  listLoadingState: LoadingState.PRISTINE,
};

export const agreementsReducer = (
  state: AgreementsState = agreementsInitialState,
  action: AgreementActionTypes
) => {
  switch (action.type) {
    case FETCH_AGREEMENTS: {
      return {
        ...state,
        listLoadingState: action.next
          ? LoadingState.FETCHED
          : LoadingState.FETCHING,
      };
    }

    case FETCH_AGREEMENTS_SUCCESS: {
      return {
        ...state,
        list: action.next
          ? [...state.list, ...action.agreements]
          : action.agreements,
        listMeta: action.meta,
        listLoadingState: LoadingState.FETCHED,
        _links: action._links,
      };
    }

    case FETCH_AGREEMENTS_FAILURE: {
      return {
        ...state,
        listLoadingState: LoadingState.ERROR,
      };
    }

    case POST_AGREEMENT_SUCCESS: {
      return {
        ...state,
        item: action.agreement,
        list: [agreementToListItem(action.agreement), ...state.list],
      };
    }

    case PATCH_AGREEMENT_SUCCESS: {
      return {
        ...state,
        item: action.agreement,
        list: state.list.map((el) =>
          el.guid === action.agreement.guid
            ? agreementToListItem(action.agreement)
            : el
        ),
      };
    }

    case FETCH_AGREEMENT:
    case FETCH_AGREEMENT_FAILURE: {
      return { ...state, item: undefined };
    }

    case FETCH_AGREEMENT_SUCCESS: {
      return { ...state, item: action.agreement };
    }

    case DELETE_AGREEMENT_SUCCESS: {
      return {
        ...state,
        list: state.list.filter((el) => el.guid !== action.guid),
      };
    }

    case POST_AGREEMENT_DUPLICATE_SUCCESS: {
      return { ...state, list: [action.agreement, ...state.list] };
    }

    default:
      return state;
  }
};
