import React, { useState } from "react";
import * as yup from "yup";
import { Field, FieldProps, Form, Formik } from "formik";
import {
  Box,
  Button,
  Container,
  Grid,
  IconButton,
  InputAdornment,
  Typography,
} from "@material-ui/core";
import { TextField } from "formik-material-ui";
import { FormikPasswordInput } from "../../atoms/FormikPasswordInput";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import { signUpEffect } from "../../pages/SignIn/effects";
import { createStyles, makeStyles } from "@material-ui/core/styles";
import { Information24 } from "@carbon/icons-react";
import { TooltipMap } from "../../atoms/TooltipMap";
import { useHistory } from "react-router-dom";
import { paths } from "../../../utils/common";
import { showEmailNotificationAction } from "../../pages/SignIn/actions";
import { hasEmptyValues } from "../../../utils/helpers";
import { SignUpInvitationPayload } from "../../../utils/models";
import { PasswordPolicy } from "../../pages/SignIn/actions.types";
import { SignUpResponse } from "../../../api.types";
import { addAlertAction } from "../../pages/Alerts/actions";
import { AlertType } from "../../pages/Alerts/actions.types";
import { toasts } from "../../../utils/toasts";

const useStyles = makeStyles((theme) =>
  createStyles({
    paper: {
      display: "flex",
      height: "100%",
      width: "100%",
    },
    container: {
      maxWidth: 625,
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      justifyContent: "center",
    },
    gridContainer: {
      textAlign: "center",
    },
    header: {
      paddingBottom: theme.spacing(3),
    },
    button: {
      width: 272,
    },
    informationIcon: {
      color: theme.palette.primary.main,
    },
    tooltip: {
      maxWidth: 160,
    },
  })
);

const validationSchema = yup.object().shape({
  email: yup
    .string()
    .trim()
    .email("Email is not valid")
    .required("Email is required"),
  firstName: yup.string().required("First name is required"),
  lastName: yup.string().required("Last name is required"),
  displayName: yup.string().required("Display name is required"),
  password: yup
    .string()
    .required("Password is required")
    .min(6, "Password is too short"),
  passwordRepeat: yup
    .string()
    .required("Password is required")
    .oneOf([yup.ref("password")], "Passwords must match"),
});

interface FormValues {
  email: string;
  firstName: string;
  lastName: string;
  displayName: string;
  password: string;
  passwordRepeat: string;
}

const getInitialValues = (dlr?: SignUpInvitationPayload) => ({
  email: dlr?.email || "",
  firstName: dlr?.firstName || "",
  lastName: dlr?.lastName || "",
  displayName: dlr?.lastName || "",
  password: "",
  passwordRepeat: "",
});

interface FormSignUpProps {
  dispatchEffect: AppDispatchEffect;
  dispatch: AppDispatch;
  title: string;
  inviteDlr?: SignUpInvitationPayload;
  passwordPolicy?: PasswordPolicy;
}

export const FormSignUp: React.FC<FormSignUpProps> = ({
  dispatchEffect,
  dispatch,
  title,
  inviteDlr,
  passwordPolicy,
}) => {
  const classes = useStyles();
  const { push } = useHistory();
  const [emailError, setEmailError] = useState<string>();
  const handleSubmit = async ({
    passwordRepeat,
    email,
    ...data
  }: FormValues) => {
    setEmailError(undefined);
    try {
      const { response }: SignUpResponse = await dispatchEffect(
        signUpEffect(
          { ...data, email: email.trim(), inviteToken: inviteDlr?.inviteToken },
          inviteDlr?._links.signUp
        )
      );
      dispatch(showEmailNotificationAction(response.emailConfirmationRequired));
      if (!response.emailConfirmationRequired) {
        dispatch(
          addAlertAction(AlertType.SUCCESS, toasts.success.auth.FINISH_SIGNUP)
        );
      }
      push(paths.SIGN_IN);
    } catch (e) {
      if (e.response.status === 409) {
        setEmailError("Email is already in use");
      }
    }
  };

  const handleSignIn = () => {
    push(paths.SIGN_IN);
  };

  return (
    <Container className={classes.container}>
      <Formik
        initialValues={getInitialValues(inviteDlr)}
        validationSchema={validationSchema}
        onSubmit={handleSubmit}
      >
        {({ isSubmitting, errors, touched, values }) => (
          <Grid
            container
            component={Form}
            spacing={3}
            className={classes.gridContainer}
          >
            <Grid item xs={12}>
              <Typography className={classes.header} variant="h5">
                {title}
              </Typography>
            </Grid>
            <Grid item sm={6} xs={12}>
              <Field
                component={TextField}
                name="firstName"
                label="First Name"
                fullWidth
              />
            </Grid>
            <Grid item sm={6} xs={12}>
              <Field
                component={TextField}
                name="lastName"
                label="Last Name"
                fullWidth
              />
            </Grid>
            <Grid item sm={6} xs={12}>
              <Field
                component={TextField}
                name="email"
                label="Email Address"
                fullWidth
                error={Boolean((errors.email && touched.email) || emailError)}
                helperText={emailError}
                autoComplete="username"
              />
            </Grid>
            <Grid item sm={6} xs={12}>
              <Field name="displayName">
                {(props: FieldProps) => (
                  <TextField
                    {...props}
                    label="Display Name"
                    fullWidth
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <TooltipMap
                            variant="clickable"
                            to="DisplayName"
                            classNamePopper={classes.tooltip}
                          >
                            <IconButton edge="end">
                              <Information24
                                className={classes.informationIcon}
                              />
                            </IconButton>
                          </TooltipMap>
                        </InputAdornment>
                      ),
                    }}
                  />
                )}
              </Field>
            </Grid>
            <Grid item sm={6} xs={12}>
              <Field
                component={FormikPasswordInput}
                name="password"
                label="Password"
                fullWidth
                margin="none"
                passwordPolicy={passwordPolicy}
                autoComplete="new-password"
              />
            </Grid>
            <Grid item sm={6} xs={12}>
              <Field
                component={FormikPasswordInput}
                name="passwordRepeat"
                label="Password Confirmation"
                fullWidth
                margin="none"
                passwordPolicy={passwordPolicy}
                autoComplete="new-password"
              />
            </Grid>
            <Grid item xs={12}>
              <Box pt={3}>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  className={classes.button}
                  disabled={isSubmitting || hasEmptyValues(values)}
                >
                  Sign Up
                </Button>
              </Box>
            </Grid>
            <Grid item xs={12}>
              <Button
                color="primary"
                variant="outlined"
                className={classes.button}
                onClick={handleSignIn}
              >
                Sign In
              </Button>
            </Grid>
          </Grid>
        )}
      </Formik>
    </Container>
  );
};
