import { ConnectedRouter } from "connected-react-router";
import React from "react";
import ReactDOM from "react-dom";
import { Provider } from "react-redux";
import { App } from "./App";
import { configureStore, history } from "./App.store";
import { createAxiosInterceptor, createRefreshInterceptor } from "./auth";
import { DLR } from "./utils/DLR";
import { Theme } from "./components/pages/Theme";
import { WithDLR } from "./components/atoms/WithDLR";

const store = configureStore(undefined);

createAxiosInterceptor(store);
createRefreshInterceptor(store);

ReactDOM.render(
  <Provider store={store}>
    <Theme>
      <ConnectedRouter history={history}>
        <WithDLR dlr={DLR}>
          <App />
        </WithDLR>
      </ConnectedRouter>
    </Theme>
  </Provider>,
  document.getElementById("root")
);
