import React, { useEffect } from "react";
import {
  createStyles,
  makeStyles,
  Theme,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import { sidebarInitialState } from "../../pages/Sidebar/reducer";
import { ContainerFillHeight } from "../../atoms/ContainerFillHeight";
import clsx from "clsx";
import {
  getIsAppInitializingSelector,
  getNotificationsUnseenCountLinkSelector,
} from "../../pages/SignIn/selectors";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { isLoggedOutPath, removeAppLoaderNode } from "../../../utils/helpers";
import { fetchIpiListEffect } from "../../pages/Ipi/effects";
import { shouldFetchIpiListSelector } from "../../pages/Ipi/selectors";
import { fetchUnseenNotificationsCountEffect } from "../../pages/Notifications/effects";
import { getApiKeyWebSelector } from "../../pages/Settings/selectors";
import { useHistory, useLocation } from "react-router-dom";
import { fetchPublishedThemeEffect } from "../../pages/Theme/effects";
import {
  setSidebarVariantAction,
  sidebarToggleAction,
} from "../../pages/Sidebar/actions";
import { handleBootstrapRequest } from "../../pages/SignIn/effects.utils";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    app: {
      width: "100%",
      overflow: "hidden",
      transition: theme.transitions.create(["margin", "width"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    appShift: {
      width: `calc(100% - ${sidebarInitialState.sidebarWidth}px)`,
      marginLeft: sidebarInitialState.sidebarWidth,
      transition: theme.transitions.create(["margin", "width"], {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
  })
);

export const AppContainer: React.FC = ({ children }) => {
  const classes = useStyles();
  const theme = useTheme();
  const breakpointMdUp = useMediaQuery(theme.breakpoints.up("md"));
  const state = useAppSelector((state) => state);
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();
  const { pathname } = useLocation();
  const { push } = useHistory();

  const auth = Boolean(state.signIn.authenticated);
  const isSidebarOpen = state.sidebar.isSidebarOpen;
  const sidebarVariant = state.sidebar.variant;
  const isLoggedOut = isLoggedOutPath(pathname);
  const showAppLoader = getIsAppInitializingSelector(state);
  const shouldFetchIpiList = shouldFetchIpiListSelector(state);
  const apiKeyWeb = getApiKeyWebSelector(state);

  const accessNotificationsCount = Boolean(
    getNotificationsUnseenCountLinkSelector(state)
  );

  useEffect(() => {
    dispatch(sidebarToggleAction(breakpointMdUp));
    dispatch(
      setSidebarVariantAction(breakpointMdUp ? "persistent" : "swipeable")
    );
  }, [breakpointMdUp, dispatch, auth]);

  useEffect(() => {
    //TODO:: this effect should be renamed to more generic name, because
    //       it does more than fetching theme. It fetches all public settings
    dispatchEffect(fetchPublishedThemeEffect());
  }, [dispatchEffect]);

  useEffect(() => {
    if (apiKeyWeb) {
      handleBootstrapRequest(dispatchEffect, pathname, push);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [apiKeyWeb]);

  useEffect(() => {
    if (!isLoggedOut && auth && accessNotificationsCount) {
      dispatchEffect(fetchUnseenNotificationsCountEffect());
    }
  }, [isLoggedOut, auth, dispatchEffect, accessNotificationsCount]);

  useEffect(() => {
    if (shouldFetchIpiList) {
      dispatchEffect(fetchIpiListEffect());
    }
  }, [shouldFetchIpiList, dispatchEffect]);

  useEffect(() => {
    if (!showAppLoader) {
      removeAppLoaderNode();
    }
  }, [showAppLoader]);

  return (
    <ContainerFillHeight
      className={clsx({
        [classes.app]: !isLoggedOut,
        [classes.appShift]:
          !isLoggedOut && isSidebarOpen && sidebarVariant !== "swipeable",
      })}
    >
      {children}
    </ContainerFillHeight>
  );
};
