import { useTheme } from "@material-ui/core/styles";
import React from "react";
import { useMediaQuery } from "@material-ui/core";
import { WorkListItem } from "../../../utils/models";
import { useHistory } from "react-router";
import { paths } from "../../../utils/common";
import "url-polyfill";
import { InfiniteScroll, ListType } from "../InfiniteScroll/component";
import { AppDispatchEffect } from "../../../App.reducer";
import { TableList } from "../../molecules/TableList";
import { HeaderTableWorksAdministrator } from "../HeaderTableWorksAdministrator/component";
import { ListItemWorkAdmin } from "../../molecules/ListItemWorkAdmin/component";
import { useWindowSize } from "../../../hooks";

interface TableWorksAdministratorProps {
  works: WorkListItem[];
  dispatchEffect: AppDispatchEffect;
}

export const TableWorksAdministrator = ({
  works,
  dispatchEffect,
}: TableWorksAdministratorProps) => {
  const theme = useTheme();
  const hideBreakpoint = useMediaQuery(theme.breakpoints.up("sm"));
  const { push } = useHistory();
  const [windowWidth] = useWindowSize({ subtractSidebarWidth: true });

  return (
    <React.Fragment>
      {works.length > 0 && (
        <TableList
          listId={ListType.WORKS}
          header={<HeaderTableWorksAdministrator />}
        >
          <InfiniteScroll
            list={ListType.WORKS}
            dataLength={works.length}
            dispatchEffect={dispatchEffect}
          >
            {works.map((work) => (
              <ListItemWorkAdmin
                key={work.guid}
                work={work}
                hideBreakpoint={hideBreakpoint}
                windowWidth={windowWidth}
                onClick={() => {
                  push(`${paths.WORKS_REGISTER}/${work.guid}`);
                }}
              />
            ))}
          </InfiniteScroll>
        </TableList>
      )}
    </React.Fragment>
  );
};
