import { paths } from "../../utils/common";
import { useLocation } from "react-router-dom";
import { AppState, useAppSelector } from "../../App.reducer";
import {
  getWorkLinksSelector,
  getWorkSelector,
  getWorksListCreateLink,
} from "../../components/pages/ListWorks/selectors";
import {
  getTemplateSelector,
  getTemplatesListCreateLink,
} from "../../components/pages/Templates/selectors";
import { useEffect, useState } from "react";
import { getCreateAgreementLinkSelector } from "../../components/pages/AgreementRegistration/selectors";
import { getUserIsSocAdminSelector } from "../../components/pages/SignIn/selectors";
import {
  AgreementNavigationTabs,
  WorkNavigationTabs,
  WorkTemplateNavigationTabs,
} from "../../types/enums";
import { getInviteEmployeeLinkSelector } from "../../components/pages/Company/selectors";
import { getThemeDrawerOpenSelector } from "../../components/pages/Theme/selectors";

const canAddCreatorOnWorkForm = (
  pathname: string,
  tabName: string | null,
  state: AppState
) => {
  const {
    works: { work, _workListItemsLinks },
  } = state;
  const ipiSearchLink =
    work?._links.ipiSearchCreators || _workListItemsLinks?.ipiSearchCreators;

  if (
    pathname.startsWith(paths.WORKS_REGISTER) &&
    tabName === WorkNavigationTabs.PARTIES
  ) {
    return (
      (ipiSearchLink && Boolean(getWorkLinksSelector(state)?.update)) ||
      !getWorkSelector(state)?.guid
    );
  }
};

const canAddWorkAgreementOnWorkForm = (
  pathname: string,
  tabName: string | null,
  state: AppState
) => {
  if (
    pathname.startsWith(paths.WORKS_REGISTER) &&
    tabName === WorkNavigationTabs.AGREEMENTS
  ) {
    return (
      Boolean(getWorkLinksSelector(state)?.update) ||
      !getWorkSelector(state)?.guid
    );
  }
};

const canAddWorkAgreementOnWorkTemplateForm = (
  pathname: string,
  tabName: string | null,
  state: AppState
) => {
  if (
    pathname.startsWith(paths.WORK_TEMPLATE) &&
    tabName === WorkTemplateNavigationTabs.AGREEMENTS
  ) {
    return (
      Boolean(getTemplateSelector(state)?._links.update) ||
      !getTemplateSelector(state)?.guid
    );
  }
};

const canAddCreatorOnWorkTemplateForm = (
  pathname: string,
  tabName: string | null,
  state: AppState
) => {
  const {
    templates: { template, _links },
  } = state;
  const ipiSearchLink =
    template?._links.ipiSearchCreators || _links?.ipiSearchCreators;

  if (
    pathname.startsWith(paths.WORK_TEMPLATE) &&
    tabName === WorkNavigationTabs.PARTIES
  ) {
    return (
      (ipiSearchLink && Boolean(getTemplateSelector(state)?._links.update)) ||
      !getTemplateSelector(state)?.guid
    );
  }
};

const canAddNewTermOnAgreementForm = (pathname: string) => {
  return pathname.startsWith(paths.AGREEMENTS_REGISTER);
};

const canCreateAgreement = (state: AppState) =>
  !getUserIsSocAdminSelector(state) &&
  Boolean(getCreateAgreementLinkSelector(state));

const canCreateWorkTemplate = (state: AppState) =>
  Boolean(getTemplatesListCreateLink(state));

const canCreateWork = (state: AppState) =>
  Boolean(getWorksListCreateLink(state));

const canInviteEmployee = (pathname: string, state: AppState) => {
  const hasInviteLink = Boolean(getInviteEmployeeLinkSelector(state));
  return pathname.startsWith(paths.EMPLOYEES) && hasInviteLink;
};

const isThemeDrawerClose = (state: AppState) => {
  return !getThemeDrawerOpenSelector(state);
};

export const useFaqButtonPosition = () => {
  const [shiftFaq, setShiftFaq] = useState(false);
  const { pathname, search } = useLocation();
  const state = useAppSelector((state) => state);
  const urlParams = new URLSearchParams(search);

  useEffect(() => {
    const tabName = urlParams.get("tab");
    switch (pathname) {
      case paths.WORKS_LIST:
      case paths.WORKS_DRAFT_LIST: {
        setShiftFaq(canCreateWork(state));
        return;
      }

      case paths.WORKS_TEMPLATE_LIST: {
        setShiftFaq(canCreateWorkTemplate(state));
        return;
      }

      case paths.AGREEMENTS_LIST:
      case paths.AGREEMENTS_DRAFT_LIST:
      case paths.AGREEMENTS_TEMPLATE_LIST: {
        setShiftFaq(canCreateAgreement(state));
        return;
      }

      case paths.PROFILE_IPI:
      case paths.PROFILE_IPN: {
        setShiftFaq(true);
        return;
      }

      case paths.SETTINGS_THEME: {
        setShiftFaq(isThemeDrawerClose(state));
        return;
      }
    }

    if (canAddNewTermOnAgreementForm(pathname)) {
      setShiftFaq(tabName === AgreementNavigationTabs.PARTY_SHARES);
      return;
    }

    if (canAddCreatorOnWorkForm(pathname, tabName, state)) {
      setShiftFaq(tabName === WorkNavigationTabs.PARTIES);
      return;
    }

    if (canAddWorkAgreementOnWorkForm(pathname, tabName, state)) {
      setShiftFaq(tabName === WorkNavigationTabs.AGREEMENTS);
      return;
    }

    if (canAddWorkAgreementOnWorkTemplateForm(pathname, tabName, state)) {
      setShiftFaq(tabName === WorkTemplateNavigationTabs.AGREEMENTS);
      return;
    }

    if (canAddCreatorOnWorkTemplateForm(pathname, tabName, state)) {
      setShiftFaq(tabName === WorkTemplateNavigationTabs.PARTIES);
      return;
    }

    if (canInviteEmployee(pathname, state)) {
      setShiftFaq(true);
      return;
    }

    setShiftFaq(false);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathname, search, state]);
  return shiftFaq;
};
