import React from "react";
import {
  Box,
  Button,
  createStyles,
  ListItem,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { StatusIndicator } from "../../atoms/StatusIndicator";
import {
  FAILURE,
  IN_PROGRESS,
  INTERNAL_FAILURE,
  NO_INTEGRATION,
  REJECTED,
  SUCCESS,
  VALIDATION_ERROR,
  WorkRegistration
} from "../../../utils/models";
import { ActionMenu } from "../../organisms/ActionMenu";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      alignItems: "center",
      width: "100%",
      minHeight: 56,
      backgroundColor: theme.palette.background.paper,
      padding: theme.spacing(2, 2.5),
      border: `1px solid ${theme.palette.action.selected}`,
      borderRadius: theme.spacing(1),
      marginBottom: theme.spacing(1),
    },
    status: {
      marginRight: theme.spacing(2.5),
    },
    content: {
      width: "100%",
      display: "flex",
      flexDirection: "column",
      marginRight: theme.spacing(3),
    },
    secondaryText: {
      fontWeight: 500,
      paddingBottom: theme.spacing(1),
    },
    highlight: {
      color: theme.palette.primary.main,
    },
  })
);

interface RegistrationsPanelProps {
  showBorder?: boolean;
  registration: WorkRegistration;
  onSubmissionsDialogOpen: () => void;
  onViewRegistrationDialogOpen: () => void;
  onErrorsDialogOpen: () => void;
  onDownloadCwr?: () => void;
}

export const RegistrationsPanel: React.FC<RegistrationsPanelProps> = ({
  showBorder = true,
  registration,
  onSubmissionsDialogOpen,
  onViewRegistrationDialogOpen,
  onErrorsDialogOpen,
  onDownloadCwr,
}) => {
  const classes = useStyles(showBorder);
  const { status } = registration;

  const getStatusMessage = () => {
    switch (status) {
      case IN_PROGRESS:
        return { primary: "Registration is in progress" };

      case INTERNAL_FAILURE:
        return {
          primary: "Registration error",
          secondary: (
            <Typography
              variant="body2"
              color="textSecondary"
              className={classes.secondaryText}
            >
              Unable to submit the work please try again later and contact&nbsp;
              <span className={classes.highlight}>support@session.id</span> if
              the issue persists.
            </Typography>
          ),
        };

      case VALIDATION_ERROR:
        return {
          primary: "Registration error - action required",
          secondary:
            "Please review work registration errors, apply needed changes and submit the registration again.",
        };

      case NO_INTEGRATION:
        return {
          primary: "Not submitted to the Society",
          secondary:
            "The society is not integrated with Session, download the CWR and submit manually.",
        };

      case SUCCESS:
        return { primary: "Registration successfully submitted" };

      case REJECTED:
        return {
          primary: "Registration rejected by the society",
          secondary:
            "More details about the errors in the registration can be found in ACK information.",
        };

      case FAILURE: {
        return {
          primary: "Registration error",
          secondary: "An unexpected error has occurred. Please try again later."
        }
      }

      default:
        return { primary: status };
    }
  };

  const statusMessage = getStatusMessage();

  return (
    <ListItem className={classes.root}>
      <Box className={classes.status}>
        <StatusIndicator
          status={status}
          mapStatusToTooltip={{
            [IN_PROGRESS]: "In Progress",
            [VALIDATION_ERROR]: "Failed",
            [INTERNAL_FAILURE]: "Failed",
            [NO_INTEGRATION]: "Not Submitted",
            [SUCCESS]: "Registered",
            [REJECTED]: "Rejected",
          }}
        />
      </Box>
      <Box className={classes.content}>
        <Typography>{statusMessage.primary}</Typography>

        {statusMessage.secondary && (
          <React.Fragment>
            {typeof statusMessage.secondary === "object" ? (
              statusMessage.secondary
            ) : (
              <Typography
                variant="body2"
                color="textSecondary"
                className={classes.secondaryText}
              >
                {statusMessage.secondary}
              </Typography>
            )}
          </React.Fragment>
        )}
      </Box>
      <ActionMenu
        anchorOrigin={{ vertical: "bottom", horizontal: "left" }}
        items={[
          ...(status === VALIDATION_ERROR
            ? [
                {
                  label: "View Registration Errors",
                  onClick: onErrorsDialogOpen,
                },
              ]
            : [
                {
                  label: "View Submissions",
                  onClick: onSubmissionsDialogOpen,
                },
              ]),
          {
            label: "View Summary",
            onClick: onViewRegistrationDialogOpen,
          },
          ...(onDownloadCwr
            ? [{ label: "Download CWR", onClick: onDownloadCwr }]
            : []),
        ]}
      >
        <Button variant="outlined">Actions</Button>
      </ActionMenu>
    </ListItem>
  );
};
