import { AppDispatchEffect } from "../../../App.reducer";
import { fetchBootstrapEffect, signOutEffect } from "../SignIn/effects";
import { SignOutResponse } from "../../../api.types";
import { paths } from "../../../utils/common";

export const handleUserSignOut = async (
  dispatchEffect: AppDispatchEffect,
  push: (path: string) => void
) => {
  try {
    const { response }: SignOutResponse = await dispatchEffect(signOutEffect());
    // await dispatchEffect(fetchBootstrapEffect(response._links.bootstrap));
  } finally {
    push(paths.SIGN_IN);
  }
};
