import {
  Agreement,
  AgreementTerm,
  AgreementTermPatch,
  AgreementTermPatchOperations,
} from "../../../utils/models";
import { ThunkDispatch } from "redux-thunk";
import { AppState } from "../../../App.reducer";
import { Action } from "redux";
import { convertTermShareToFloat, formatDate } from "../../../utils/helpers";
import { fetchAgreementEffect } from "./effects";
import { RelationType, UsaLicenseIndicatorType } from "../../../types/enums";
import { getAgreementLinksSelector } from "./selectors";
import { format } from "date-fns";
import { AgreementFormValues } from "../../organisms/FormAgreement";

export const getTermData = (term: AgreementTerm) => ({
  territories: term.territories,
  startDate: formatDate(term.startDate),
  endDate: formatDate(term.endDate),
  priorRoyalties: term.priorRoyalties
    ? formatDate(term.priorRoyalties)
    : undefined,
});

export const getAgreementLink = async (
  linkName: "update" | "delete" | "submit",
  agreementGuid: string,
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState: () => AppState
) => {
  const link = getAgreementLinksSelector(getState())?.[linkName];
  if (!link) {
    const response = await dispatch(fetchAgreementEffect(agreementGuid));

    const refetchedLinks = response._links;

    const refetchedLink = refetchedLinks?.[linkName];
    if (refetchedLink) return refetchedLink;

    const message = `Unable to find link: ${linkName}`;
    throw new Error(message);
  }
  return link;
};

export const extractDirtyAgreementTermAttrs = (
  prev: AgreementTerm,
  curr: AgreementTerm
) => {
  const parseToAgreementPatch: (term: AgreementTerm) => AgreementTermPatch = (
    term: AgreementTerm
  ) => {
    return {
      endDate: term.endDate
        ? format(new Date(term.endDate), "yyyy-MM-dd")
        : undefined,
      startDate: term.startDate
        ? format(new Date(term.startDate), "yyyy-MM-dd")
        : undefined,
      priorRoyalties: term.priorRoyalties
        ? format(new Date(term.priorRoyalties), "yyyy-MM-dd")
        : null,
      territories: term.territories,
    };
  };

  const previous: AgreementTermPatch = parseToAgreementPatch(prev);
  const current: AgreementTermPatch = parseToAgreementPatch(curr);

  const attributes: Array<keyof AgreementTermPatch> = [
    "endDate",
    "startDate",
    "priorRoyalties",
    "territories",
  ];

  const dirty: { [index: string]: string[] | string | undefined | null } = {};
  attributes.forEach((attr) => {
    if (JSON.stringify(previous[attr]) !== JSON.stringify(current[attr])) {
      dirty[attr] = current[attr];
    }
  });
  return dirty;
};

export const formatAgreementPostData = (form: AgreementFormValues) => {
  const { recordingPrefix, ...rest } = form;
  const submitterAgreementNumbersFiltered = form.submitterAgreementNumbers.filter(
    (item) => Boolean(item)
  );
  return {
    ...rest,
    recordingPrefix: form.libraryMusic ? form.recordingPrefix : undefined,
    submitterAgreementNumbers:
      submitterAgreementNumbersFiltered.length > 0
        ? submitterAgreementNumbersFiltered
        : undefined,
    ownerPartyRole: RelationType.ASSIGNOR,
    //TODO:: Update below when templates implemented on BE

    // form.assignor?.access === PartyMemberAccessType.OWNER
    //   ? RelationType.ASSIGNOR
    //   : form.assignee?.access === PartyMemberAccessType.OWNER
    //   ? RelationType.ASSIGNEE
    //   : undefined,
  };
};

export const replaceUsaLicenseIndicatorNONE = (
  option: UsaLicenseIndicatorType | undefined | null,
  to: undefined | null
) => (option === UsaLicenseIndicatorType.NONE ? to : option);

export const getFormattedAgreement = (agreement: Agreement) => ({
  ...agreement,
  usaLicenseIndicator:
    agreement.usaLicenseIndicator === undefined
      ? UsaLicenseIndicatorType.NONE
      : agreement.usaLicenseIndicator,
  terms: agreement.terms
    ? agreement.terms?.map((el) => ({
        ...el,
        shares: el.shares
          ? el.shares.map((share) => convertTermShareToFloat(share))
          : [],
      }))
    : agreement.terms,
});

export const convertTermSharesToInt = (
  terms: AgreementTermPatchOperations[]
) => {
  const formatted: AgreementTermPatchOperations[] = terms.map((termEl) => ({
    ...termEl,
    term: {
      ...termEl.term,
      shares: termEl.term.shares?.map((shareEl) => ({
        ...shareEl,
        share: {
          ...shareEl.share,
          writerShareRetained: (shareEl.share.writerShareRetained || 0) * 100,
          publisherShareRetained:
            (shareEl.share.publisherShareRetained || 0) * 100,
          assigneeCollectibleShare:
            (shareEl.share.assigneeCollectibleShare || 0) * 100,
        },
      })),
    },
  }));
  return formatted;
};

export const checkIfAttributesChanged = (
  agreement: Agreement,
  formattedFormData: any
) =>
  agreement.title !== formattedFormData.title ||
  agreement.salesOrManufacture !== formattedFormData.salesOrManufacture ||
  agreement.bulkTransferWorks !== formattedFormData.bulkTransferWorks ||
  agreement.libraryMusic !== formattedFormData.libraryMusic ||
  agreement.recordingPrefix !== formattedFormData.recordingPrefix ||
  agreement.submitterAgreementNumbers !==
    formattedFormData.submitterAgreementNumbers ||
  agreement.usaLicenseIndicator !== formattedFormData.usaLicenseIndicator;
