import {
  Box,
  Container,
  createStyles,
  List,
  makeStyles,
  Paper,
  Theme,
} from "@material-ui/core";
import React from "react";
import clsx from "clsx";
import { useInfiniteScrollStyles } from "../../organisms/InfiniteScroll/component";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paperWrapper: {
      display: "flex",
      flexDirection: "column",
      flexGrow: 1,
      overflowY: "auto",
      overflowX: "hidden",
    },
    scrollContainer: {
      overflow: "overlay",
      overflowX: "hidden",
    },
    container: {
      padding: 0,
    },
    upperContainer: {
      marginTop: theme.spacing(5.5),
    },
    maxHeight: {
      height: "100%",
    },
  })
);

interface TableListProps {
  children: any;
  header: any;
  listId?: string;
}

export const TableList: React.FC<TableListProps> = ({
  children,
  header,
  listId,
}) => {
  const classes = useStyles();
  const infiniteScrollClasses = useInfiniteScrollStyles();
  return (
    <Paper square className={classes.paperWrapper}>
      <Container className={clsx(classes.upperContainer, classes.container)}>
        {header}
      </Container>
      <Box
        className={clsx(classes.scrollContainer, {
          [classes.maxHeight]: Boolean(listId),
        })}
        id={listId}
      >
        <Container
          className={clsx(classes.container, {
            [infiniteScrollClasses.infiniteScrollContainer]: Boolean(listId),
          })}
        >
          <List>{children}</List>
        </Container>
      </Box>
    </Paper>
  );
};
