import React, { useEffect, useRef, useState } from "react";
import {
  Box,
  Chip,
  fade,
  Grid,
  IconButton,
  ListItem,
  Typography,
} from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { format } from "date-fns";
import { CompanyInvite } from "../../../utils/models";
import { CompanyInviteType, LoadingState } from "../../../types/enums";
import clsx from "clsx";
import { AppDispatchEffect } from "../../../App.reducer";
import {
  deleteInviteEffect,
  resendInviteEffect,
} from "../../pages/Company/effects";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { DialogWarning } from "../../molecules/DialogWarning";
import { ActionMenu } from "../ActionMenu";
import { HeaderListEmployeesInvites } from "../HeaderListEmployeesInvites";
import { TableList } from "../../molecules/TableList";
import { PageEmptyStateEmployees } from "../../atoms/PageEmptyStateEmployees";
import { Icon } from "../../atoms/Icon";
import { LoadingScreen } from "../../atoms/LoadingScreen";

export const useListEmployeesInvitesStyles = makeStyles((theme: Theme) =>
  createStyles({
    listItemRow: {
      flexDirection: "column",
      minHeight: theme.spacing(9),
      "&:hover": {
        cursor: "default",
      },
    },
    nameColumn: {
      position: "relative",
      minWidth: 140,
      width: "100%",
      marginLeft: 55,
    },
    dataColumn: {
      width: "100%",
      minWidth: 100,
      maxWidth: 170,
    },
    optionsColumn: {
      display: "flex",
      width: "100%",
      minWidth: 50,
      maxWidth: 50,
      justifyContent: "center",
    },
    chip: {
      marginRight: theme.spacing(1),
      marginBottom: theme.spacing(0.5),
      marginTop: theme.spacing(0.5),
      color: theme.palette.common.black,
    },
    expiredChip: {
      backgroundColor: fade(theme.palette.error.light, 0.3),
    },
    ellipsisOverflow: {
      overflow: "hidden",
      textOverflow: "ellipsis",
      whiteSpace: "nowrap",
    },
    dateColumns: {
      display: "inline-flex",
      justifyContent: "space-between",
      width: "100%",
    },
  })
);

interface ListEmployeesInvitesProps {
  invites: CompanyInvite[];
  dispatchEffect: AppDispatchEffect;
  canDeleteInvite: boolean;
  invitesLoadingState: LoadingState;
}

export const ListEmployeesInvites: React.FC<ListEmployeesInvitesProps> = ({
  invites,
  dispatchEffect,
  canDeleteInvite,
  invitesLoadingState,
}) => {
  const classes = useListEmployeesInvitesStyles();
  const componentIsMounted = useRef(true);
  const [deleteInvite, setDeleteInvite] = useState<undefined | CompanyInvite>();
  const [submittingDialog, setSubmittingDialog] = useState(false);
  const [resendInvite, setResendInvite] = useState<undefined | CompanyInvite>();

  const getStatusLabel = (status: CompanyInviteType) => {
    switch (status) {
      case CompanyInviteType.PENDING: {
        return "Pending";
      }
      case CompanyInviteType.EXPIRED: {
        return "Expired";
      }
    }
  };

  useEffect(() => {
    return () => {
      componentIsMounted.current = false;
    };
  }, []);

  if (
    invitesLoadingState === LoadingState.PRISTINE ||
    invitesLoadingState === LoadingState.FETCHING
  ) {
    return <LoadingScreen />;
  }

  return (
    <React.Fragment>
      {invitesLoadingState === LoadingState.FETCHED && invites.length > 0 ? (
        <TableList header={<HeaderListEmployeesInvites />}>
          {invites.map((invite) => {
            const {
              targetEmail,
              status,
              created,
              expirationDate,
              inviteToken,
            } = invite;
            return (
              <ListItem
                button
                key={inviteToken}
                className={classes.listItemRow}
                disableRipple
              >
                <ContainerListItem leftOffset={-2}>
                  <Box className={classes.nameColumn}>
                    <Typography className={classes.ellipsisOverflow}>
                      {targetEmail}
                    </Typography>
                  </Box>
                  <Box className={classes.dataColumn}>
                    <Chip
                      label={getStatusLabel(status)}
                      className={clsx(classes.chip, {
                        [classes.expiredChip]:
                          status === CompanyInviteType.EXPIRED,
                      })}
                    />
                  </Box>
                  <Box className={classes.dataColumn}>
                    <Typography
                      className={classes.dateColumns}
                      variant="subtitle1"
                      color="textSecondary"
                    >
                      {format(created.timestamp * 1000, "MMM d, yyyy")}
                    </Typography>
                  </Box>
                  <Box className={classes.dataColumn}>
                    <Typography
                      className={classes.dateColumns}
                      variant="subtitle1"
                      color="textSecondary"
                    >
                      {format(expirationDate.timestamp * 1000, "MMM d, yyyy")}
                    </Typography>
                  </Box>
                  <Grid item className={classes.optionsColumn}>
                    <ActionMenu
                      items={[
                        ...(status === CompanyInviteType.EXPIRED
                          ? [
                              {
                                label: "Invite",
                                onClick: () => {
                                  setResendInvite(invite);
                                },
                              },
                            ]
                          : []),
                        ...(canDeleteInvite
                          ? [
                              {
                                label: "Delete",
                                onClick: () => {
                                  setDeleteInvite(invite);
                                },
                              },
                            ]
                          : []),
                      ]}
                    >
                      <IconButton size="small">
                        <Icon name="listItemOptions" />
                      </IconButton>
                    </ActionMenu>
                  </Grid>
                </ContainerListItem>
              </ListItem>
            );
          })}
        </TableList>
      ) : (
        <PageEmptyStateEmployees primaryText="No Invites" />
      )}
      {deleteInvite && (
        <DialogWarning
          dialogTitle="Delete Invite"
          labelCancel="Cancel"
          labelConfirm="Delete"
          isLoading={submittingDialog}
          width="min-content"
          onConfirm={() => {
            setSubmittingDialog(true);
            return dispatchEffect(
              deleteInviteEffect(deleteInvite?.inviteToken)
            ).finally(() => {
              if (componentIsMounted.current) {
                setDeleteInvite(undefined);
                setSubmittingDialog(false);
              }
            });
          }}
          onCancel={() => {
            setDeleteInvite(undefined);
            setSubmittingDialog(false);
          }}
          message={
            <Typography>
              Are you sure you want to delete the invite for&nbsp;
              <strong>{deleteInvite.targetEmail}</strong>?
            </Typography>
          }
        />
      )}
      {resendInvite && (
        <DialogWarning
          dialogTitle="Invite"
          labelCancel="Cancel"
          labelConfirm="Invite"
          isLoading={submittingDialog}
          width={450}
          onConfirm={() => {
            setSubmittingDialog(true);
            return dispatchEffect(
              resendInviteEffect(resendInvite?.inviteToken)
            ).finally(() => {
              if (componentIsMounted.current) {
                setResendInvite(undefined);
                setSubmittingDialog(false);
              }
            });
          }}
          onCancel={() => {
            setResendInvite(undefined);
            setSubmittingDialog(false);
          }}
          message={
            <Typography>
              Are you sure you want to invite&nbsp;
              <strong>{resendInvite.targetEmail}</strong>?
              <br />
              Invites will automatically expire after 7 days.
            </Typography>
          }
        />
      )}
    </React.Fragment>
  );
};
