import React, { useRef } from "react";
import { _IpiLinks, Society } from "../../../utils/models";
import { createStyles, Fab, makeStyles, Theme } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import { AppDispatchEffect } from "../../../App.reducer";
import { LoadingState } from "../../../types/enums";
import { DialogAddIPI } from "../DialogAddIpi";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      marginRight: theme.spacing(2),
    },
    listItem: {
      paddingLeft: theme.spacing(2.5),
    },
    buttonWrapper: {
      padding: theme.spacing(1, 2.5),
    },
    fabIcon: {
      marginRight: theme.spacing(1.5),
    },
    popover: {
      maxWidth: 408,
      minWidth: 350,
      width: "100%",
      padding: theme.spacing(2, 1.5),
    },
    noWrap: {
      whiteSpace: "nowrap",
    },
    notListedSocietyOption: {
      paddingBottom: 0,
      "& li:last-of-type": {
        borderTop: `1px solid ${theme.palette.divider}`,
      },
    },
    popoverTitle: {
      paddingBottom: theme.spacing(2),
    },
    fabButton: {
      position: "absolute",
      bottom: theme.spacing(4),
      right: theme.spacing(4),
      zIndex: 1,
    },
  })
);

interface SelectSocietyProps {
  societies: Society[];
  integratedSocieties: Society[];
  societiesLoadingState: LoadingState;
  integratedSocietiesLoadingState: LoadingState;
  dispatchEffect: AppDispatchEffect;
  popoverOpen: boolean;
  setPopoverOpen: (value: boolean) => void;
  ipiListLength: number;
  addManuallyEnabled: boolean;
  onPopoverClose?: () => void;
  ipiListLinks: _IpiLinks;
}

export const notListedSocietyOptionMock: Society = {
  verification: {
    version: 0,
    enabled: true,
  },
  acronym: "Not Listed",
  id: "NOT_LISTED",
  country: "",
  type: "",
  created: {
    iso: "",
    timestamp: 0,
  },
  updated: {
    iso: "",
    timestamp: 0,
  },
};

export const AddIpiNumber = ({
  societies,
  integratedSocieties,
  societiesLoadingState,
  integratedSocietiesLoadingState,
  dispatchEffect,
  popoverOpen,
  setPopoverOpen,
  ipiListLength,
  addManuallyEnabled,
  onPopoverClose,
  ipiListLinks,
}: SelectSocietyProps) => {
  const classes = useStyles();
  const addButton = useRef<HTMLButtonElement | null>(null);
  const handleClose = () => {
    setPopoverOpen(false);
    onPopoverClose && onPopoverClose();
  };

  return (
    <React.Fragment>
      <Fab
        variant="extended"
        color="primary"
        className={classes.fabButton}
        onClick={() => setPopoverOpen(true)}
        ref={addButton}
      >
        <AddIcon className={classes.fabIcon} />
        Add IPI Number
      </Fab>

      <DialogAddIPI
        open={popoverOpen}
        onClose={handleClose}
        dispatchEffect={dispatchEffect}
        addManuallyEnabled={addManuallyEnabled}
        ipiListLength={ipiListLength}
        societies={societies}
        integratedSocieties={integratedSocieties}
        integratedSocietiesLoadingState={integratedSocietiesLoadingState}
        societiesLoadingState={societiesLoadingState}
        ipiListLinks={ipiListLinks}
      />
    </React.Fragment>
  );
};
