import { AppState } from "../../../App.reducer";

export const getAgreementTemplatesSelector = (state: AppState) =>
  state.agreementTemplates.list;

export const getAgreementTemplateSelector = (state: AppState) =>
  state.agreementTemplates.item;

export const getAgreementTemplatesLoadingStateSelector = (state: AppState) =>
  state.agreementTemplates.listLoadingState;

export const getAgreementTemplatesLinksSelector = (state: AppState) =>
  state.agreementTemplates._links;

export const getAgreementTemplateLinksSelector = (state: AppState) =>
  state.agreementTemplates.item?._links;
