import React, { useState } from "react";
import {
  ButtonBase,
  createStyles,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import {
  BootstrapAccount,
  BootstrapCompany,
  WorkCreator,
  WorkPublisher,
} from "../../../utils/models";
import { AppDispatchEffect } from "../../../App.reducer";
import { ColumnBreakpoints } from "../../../utils/models";
import { RowGridPublisher } from "../../molecules/RowGridPublisher";
import { RowCreatorLeftContent } from "../../atoms/RowCreatorLeftContent";
import { ButtonAddPublisherRow } from "../../molecules/ButtonAddPublisherRow";
import { RowPublishers } from "../../molecules/RowPublishers";
import { findPublisherIndex } from "./utils";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    showLessRow: {
      paddingTop: 0,
      paddingBottom: 0,
      height: theme.spacing(1),
      marginBottom: theme.spacing(1.5),
    },
  })
);

interface RowGridPublisherRelationshipProps {
  dispatchEffect: AppDispatchEffect;
  creatorPath: string;
  publishers: WorkPublisher[];
  creator: WorkCreator;
  hideOptionsColumn?: boolean;
  hideSplitColumn?: boolean;
  hideCollaborationColumn?: boolean;
  editable?: boolean;
  setFieldValue: (key: string, value: any) => void;
  columnBreakPoints: ColumnBreakpoints;
  isPublisher?: boolean;
  isCreator?: boolean;
  company?: BootstrapCompany;
  account?: BootstrapAccount;
  isSubmitting?: boolean;
  ipiSearchPublishersApi?: string;
}

export const RowGridPublisherRelationship: React.FC<RowGridPublisherRelationshipProps> = ({
  publishers,
  ipiSearchPublishersApi,
  editable,
  hideSplitColumn,
  hideCollaborationColumn,
  hideOptionsColumn,
  creator,
  setFieldValue,
  creatorPath,
  columnBreakPoints,
  isPublisher,
  isCreator,
  company,
  account,
  isSubmitting,
}) => {
  const classes = useStyles();
  const [showDetails, setShowDetails] = useState(false);
  const creatorPublishers =
    creator.publishers?.map((el) => el.ipiNameNumber) || [];
  const childPublishers = publishers.filter((el) =>
    creatorPublishers.includes(el.ipi.ipiNameNumber)
  );

  const handlePublisherDelete = (publisherIpiNameNumber: number) => {
    const updatedCreatorPublishers = (creator.publishers || []).filter(
      (el) => el.ipiNameNumber !== publisherIpiNameNumber
    );
    setFieldValue(`${creatorPath}.publishers`, updatedCreatorPublishers);

    const matchingPublishers = publishers.filter(
      (el) => el.ipi.ipiNameNumber === publisherIpiNameNumber
    );
    if (!matchingPublishers.length) {
      setFieldValue("publishers", []);
    }
  };

  const handleShowDetails = () => setShowDetails(true);
  const handleHideDetails = () => setShowDetails(false);

  return (
    <React.Fragment>
      {showDetails ? (
        <React.Fragment>
          {childPublishers.map((publisher, index) => (
            <RowGridPublisher
              first={index === 0}
              key={publisher.ipi.ipiNameNumber}
              publisher={publisher}
              onDelete={() =>
                handlePublisherDelete(publisher.ipi.ipiNameNumber)
              }
              parentPath={`${creatorPath}.publishers.${findPublisherIndex(
                publisher,
                creator
              )}`}
              editable={editable}
              disableShareInputs={isCreator}
              hideSplitColumn={hideSplitColumn}
              hideOptionsColumn={hideOptionsColumn}
              hideCollaborationColumn={hideCollaborationColumn}
              columnBreakPoints={columnBreakPoints}
              isPublisher={isPublisher}
              isSubmitting={isSubmitting}
            />
          ))}

          {childPublishers.length > 0 && (
            <RowCreatorLeftContent className={classes.showLessRow}>
              <Typography
                variant="button"
                component={ButtonBase}
                onClick={handleHideDetails}
              >
                Show Less
              </Typography>
            </RowCreatorLeftContent>
          )}
        </React.Fragment>
      ) : (
        <RowPublishers
          hideSplitColumn={hideSplitColumn}
          hideCollaborationColumn={hideCollaborationColumn}
          hideOptionsColumn={hideOptionsColumn}
          creatorPublishers={creator.publishers || []}
          publishers={publishers}
          onShow={handleShowDetails}
          editable={editable}
          columnBreakPoints={columnBreakPoints}
        />
      )}
      <ButtonAddPublisherRow
        ipiSearchPublishersApi={ipiSearchPublishersApi}
        writerPath={creatorPath}
        publishersCount={childPublishers.length}
        disabled={
          !ipiSearchPublishersApi &&
          (!editable || (isCreator && childPublishers.length > 0))
        }
        isPublisher={isPublisher}
        isCreator={isCreator}
        company={company}
        account={account}
        onAdded={handleShowDetails}
      />
    </React.Fragment>
  );
};
