import React from "react";
import {
  makeStyles,
  Table,
  TableBody,
  TableHead,
  TableRow,
  Theme,
  Typography,
} from "@material-ui/core";
import clsx from "clsx";
import TableCell from "@material-ui/core/TableCell";
import {
  AgreementRightType,
  AgreementRightTypeLabel,
} from "../../../types/enums";
import { createStyles, withStyles } from "@material-ui/core/styles";
import { AgreementShare } from "../../../utils/models";
import { TooltipMap } from "../../atoms/TooltipMap";
import { getIpiRightsByType } from "../../organisms/TableAgreementShares/utils";

const useStyles = makeStyles((theme: Theme) => {
  const backgroundColor = theme.palette.custom.background.input;
  const thickBorder = `8px solid ${backgroundColor}`;
  const borderStyle = `1px solid ${theme.palette.custom.state.dragged}`;
  return createStyles({
    table: {
      borderCollapse: "unset",
      background: backgroundColor,
      borderRight: borderStyle,
      borderBottom: borderStyle,
      borderLeft: borderStyle,
      borderBottomLeftRadius: theme.spacing(1),
      borderBottomRightRadius: theme.spacing(1),
      paddingBottom: theme.spacing(1),
      marginBottom: theme.spacing(1),
    },
    rightTypeCell: {
      minWidth: 130,
      width: "100%",
      paddingLeft: theme.spacing(4),
    },
    firstCell: {
      borderLeft: thickBorder,
      borderTopLeftRadius: theme.spacing(2),
      borderBottomLeftRadius: theme.spacing(2),
    },
    lastCell: {
      borderRight: thickBorder,
      borderTopRightRadius: theme.spacing(2),
      borderBottomRightRadius: theme.spacing(2),
    },
    ipiRightsCell: {
      maxWidth: 160,
      minWidth: 160,
    },
    creatorCell: {
      minWidth: 160,
    },
    publisherCell: {
      minWidth: 160,
    },
    assigneeCell: {
      minWidth: 214,
    },
    rightType: {
      height: 40,
      borderLeft: thickBorder,
      borderTopLeftRadius: theme.spacing(2),
      borderBottomLeftRadius: theme.spacing(2),
      paddingLeft: theme.spacing(1),
      paddingRight: 0,
    },
    emptyCell: {
      border: "none",
      padding: 0,
      background: backgroundColor,
    },
    tableCell: {
      background: theme.palette.background.paper,
      borderTop: thickBorder,
      paddingTop: theme.spacing(1.5),
      paddingBottom: theme.spacing(1.5),
    },
    noPadding: {
      paddingTop: 0,
      paddingBottom: 0,
    },
    noWrap: {
      whiteSpace: "nowrap",
    },
    rightTypeLabel: {
      marginLeft: theme.spacing(-1),
    },
  });
});

const TableHeaderCell = withStyles((theme) => ({
  root: {
    ...theme.typography.body2,
    color: theme.palette.text.secondary,
    border: "none",
    paddingBottom: 0,
    paddingTop: theme.spacing(3),
  },
}))(TableCell);

interface TableAgreementSharesSummaryProps {
  shares: AgreementShare[];
}

export const TableAgreementSharesSummary: React.FC<TableAgreementSharesSummaryProps> = ({
  shares,
}) => {
  const classes = useStyles();

  const getRightTypeLabel = (rightType: AgreementRightType) => {
    switch (rightType) {
      case AgreementRightType.M: {
        return AgreementRightTypeLabel.M;
      }
      case AgreementRightType.P: {
        return AgreementRightTypeLabel.P;
      }
      case AgreementRightType.S: {
        return AgreementRightTypeLabel.S;
      }
      case AgreementRightType.T: {
        return AgreementRightTypeLabel.T;
      }
      default: {
        return "";
      }
    }
  };

  const formatShare = (num: number) => {
    if (num === 100) {
      return `${num}%`;
    }
    return `${(Math.round(num * 100) / 100).toFixed(2)}%`;
  };

  return (
    <Table className={classes.table}>
      <TableHead>
        {shares.length > 0 && (
          <TableRow>
            <TableHeaderCell
              className={clsx(classes.rightTypeCell, classes.noWrap)}
              align="left"
            >
              Right Type
            </TableHeaderCell>
            <TableHeaderCell
              className={clsx(classes.ipiRightsCell, classes.noWrap)}
              align="left"
            >
              IPI Rights
            </TableHeaderCell>
            <TableHeaderCell
              className={clsx(classes.creatorCell, classes.noWrap)}
              align="left"
            >
              Creator
            </TableHeaderCell>
            <TableHeaderCell
              className={clsx(classes.publisherCell, classes.noWrap)}
              align="left"
            >
              Publisher
            </TableHeaderCell>
            <TableHeaderCell
              className={clsx(classes.assigneeCell, classes.noWrap)}
              align="left"
            >
              Sub-Publisher
            </TableHeaderCell>
          </TableRow>
        )}
      </TableHead>
      <TableBody>
        <React.Fragment>
          {shares.map(
            ({
              rightType,
              usageTypes,
              publisherShareRetained,
              assigneeCollectibleShare,
              writerShareRetained,
              guid,
            }) => {
              const usageTypesLabel = usageTypes.join(" ");
              const isAllUsageTypesSelected =
                getIpiRightsByType(rightType).length === usageTypes.length;
              return (
                <TableRow key={guid}>
                  <TableCell
                    align="left"
                    className={clsx(
                      classes.rightTypeCell,
                      classes.tableCell,
                      classes.firstCell
                    )}
                  >
                    <Typography
                      variant="subtitle1"
                      color="textPrimary"
                      className={classes.rightTypeLabel}
                    >
                      {getRightTypeLabel(rightType)}
                    </Typography>
                  </TableCell>
                  <TableCell
                    className={clsx(classes.ipiRightsCell, classes.tableCell)}
                    align="left"
                  >
                    <Typography variant="subtitle1" color="textPrimary" noWrap>
                      <TooltipMap
                        variant="listItems"
                        tooltipText={usageTypesLabel}
                      >
                        {isAllUsageTypesSelected ? "All" : usageTypesLabel}
                      </TooltipMap>
                    </Typography>
                  </TableCell>
                  <TableCell
                    className={clsx(
                      classes.creatorCell,
                      classes.tableCell,
                      classes.noPadding
                    )}
                    align="left"
                  >
                    <Typography variant="subtitle1" color="textPrimary">
                      {formatShare(writerShareRetained)}
                    </Typography>
                  </TableCell>
                  <TableCell
                    className={clsx(
                      classes.publisherCell,
                      classes.tableCell,
                      classes.noPadding
                    )}
                    align="left"
                  >
                    <Typography variant="subtitle1" color="textPrimary">
                      {formatShare(publisherShareRetained)}
                    </Typography>
                  </TableCell>
                  <TableCell
                    className={clsx(
                      classes.assigneeCell,
                      classes.tableCell,
                      classes.noPadding,
                      classes.lastCell
                    )}
                    align="left"
                  >
                    <Typography variant="subtitle1" color="textPrimary">
                      {formatShare(assigneeCollectibleShare)}
                    </Typography>
                  </TableCell>
                </TableRow>
              );
            }
          )}
        </React.Fragment>
        <TableRow>
          <TableCell colSpan={7} className={classes.emptyCell} />
        </TableRow>
      </TableBody>
    </Table>
  );
};
