import React from "react";
import { Grid } from "@material-ui/core";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { InfiniteBorder } from "../../atoms/InfiniteBorder";
import { useListNotificationsStyles } from "../ListNotifications";

export const HeaderListNotifications: React.FC = () => {
  const classes = useListNotificationsStyles();

  return (
    <ContainerListItem leftOffset={10} rightOffset={2}>
      <Grid item className={classes.avatarColumn} />
      <Grid item className={classes.fromColumn}>
        <ListColumnTitle>From</ListColumnTitle>
        <InfiniteBorder />
      </Grid>
      <Grid item className={classes.messageColumn}>
        <ListColumnTitle>Message</ListColumnTitle>
      </Grid>
      <Grid item className={classes.dateColumn}>
        <ListColumnTitle>Date</ListColumnTitle>
      </Grid>
    </ContainerListItem>
  );
};
