import { createStyles, makeStyles, Theme } from "@material-ui/core";
import React from "react";
import { FieldProps } from "formik";
import { createTermWithShare } from "./utils";
import { AgreementFormValues } from "../../organisms/FormAgreement";
import { FabExtended } from "../FabExtended";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    button: {
      position: "fixed",
      bottom: theme.spacing(4),
      right: theme.spacing(4),
      zIndex: 1,
    },
  })
);

interface FormikAddAgreementTermProps {
  disabled?: boolean;
  onClick?: () => void;
}

export const FormikAddAgreementTerm: React.FC<
  FormikAddAgreementTermProps & FieldProps<any, AgreementFormValues>
> = ({ form: { setFieldValue }, field: { name }, disabled, onClick }) => {
  const classes = useStyles();
  const handleAddShare = () => {
    const term = createTermWithShare();
    setFieldValue(name, term);
    onClick && onClick();
  };

  return (
    <FabExtended
      data-testid="add-term"
      onClick={handleAddShare}
      className={classes.button}
      disabled={disabled}
    >
      Add Term
    </FabExtended>
  );
};
