import { Agreement, AgreementListItem } from "../../../utils/models";

export const agreementToListItem: (
  agreement: Agreement
) => AgreementListItem = (agreement: Agreement) =>
  ({
    guid: agreement.guid,
    title: agreement.title,
    created: agreement.created,
    updated: agreement.updated,
    status: agreement.status,
    submitterAgreementNumbers: agreement.submitterAgreementNumbers,
    assignor: agreement.assignor,
    assignee: agreement.assignee,
  } as AgreementListItem);
