import React, { useState } from "react";
import { AppDispatchEffect } from "../../../App.reducer";
import {
  Box,
  Button,
  Container,
  createStyles,
  Grid,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { Field, Form, Formik } from "formik";
import { TextField } from "formik-material-ui";
import { FormikPasswordInput } from "../../atoms/FormikPasswordInput";
import * as yup from "yup";
import { useHistory } from "react-router-dom";
import { paths } from "../../../utils/common";
import { handleUserSignIn } from "./component.utils";
import { hasEmptyValues } from "../../../utils/helpers";
import { InlineTopLoader } from "../../atoms/InlineTopLoader";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      maxWidth: 384,
    },
    gridContainer: {
      textAlign: "center",
    },
    header: {
      paddingBottom: theme.spacing(3.5),
    },
  })
);

interface FormSignInProps {
  dispatchEffect: AppDispatchEffect;
}

export interface FormValues {
  email: string;
  password: string;
}

const initialValues = {
  email: "",
  password: "",
};

const validationSchema = yup.object().shape({
  email: yup
    .string()
    .trim()
    .email("Email is not valid")
    .required("Email is required"),
  password: yup.string().required("Password is required"),
});

export const FormSignIn: React.FC<FormSignInProps> = ({ dispatchEffect }) => {
  const classes = useStyles();
  const { push } = useHistory();
  const [incorrectCredentials, setIncorrectCredentials] = useState<string>();
  const handleSubmit = async (data: FormValues) => {
    setIncorrectCredentials(undefined);
    try {
      await handleUserSignIn(
        { ...data, email: data.email.trim() },
        dispatchEffect,
        push
      );
    } catch (e) {
      if (e.response.status === 400) {
        setIncorrectCredentials("Incorrect credentials, please try again");
      }
    }
  };

  const handleSignUp = () => {
    push(paths.SIGN_UP);
  };

  const handleCantLogIn = () => {
    push(`${paths.SIGN_IN}/${paths.subPage.RESET_PASSWORD}`);
  };

  return (
    <Container className={classes.container}>
      <Formik
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={handleSubmit}
      >
        {({ isSubmitting, values, errors, touched }) => (
          <React.Fragment>
            {isSubmitting && <InlineTopLoader />}
            <Grid
              container
              component={Form}
              spacing={3}
              className={classes.gridContainer}
            >
              <Grid item xs={12}>
                <Typography className={classes.header} variant="h5">
                  Sign In
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Field
                  component={TextField}
                  name="email"
                  label="Email Address"
                  fullWidth
                  error={Boolean(
                    (touched.email && errors.email) || incorrectCredentials
                  )}
                  autoComplete="username"
                />
              </Grid>
              <Grid item xs={12}>
                <Field
                  component={FormikPasswordInput}
                  name="password"
                  label="Password"
                  fullWidth
                  margin="none"
                  error={Boolean(
                    (touched.password && errors.password) ||
                      incorrectCredentials
                  )}
                  helperText={
                    incorrectCredentials ||
                    (touched.password && errors.password)
                  }
                  autoComplete="current-password"
                />
              </Grid>

              <Grid item xs={12}>
                <Box pt={incorrectCredentials ? 0.6 : 3}>
                  <Button
                    type="submit"
                    variant="contained"
                    color="primary"
                    fullWidth
                    disabled={isSubmitting || hasEmptyValues(values)}
                  >
                    Sign In
                  </Button>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Button
                  color="primary"
                  variant="outlined"
                  fullWidth
                  onClick={handleSignUp}
                >
                  Sign Up
                </Button>
              </Grid>
              <Grid item xs={12}>
                <Button variant="text" fullWidth onClick={handleCantLogIn}>
                  Cant log in?
                </Button>
              </Grid>
            </Grid>
          </React.Fragment>
        )}
      </Formik>
    </Container>
  );
};
