import React, { useEffect, useState } from "react";
import Snackbar from "@material-ui/core/Snackbar";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import clsx from "clsx";
import { AppDispatch } from "../../../App.reducer";
import { removeAlertAction } from "../../pages/Alerts/actions";
import { Alert } from "../../pages/Alerts/actions.types";
import debounce from "lodash/debounce";

interface StyleProps {
  shiftLeft: number;
}

const useStyles = makeStyles<Theme, StyleProps>((theme) =>
  createStyles({
    root: {
      marginLeft: 0,
      transition: theme.transitions.create(["margin"], {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
      "& .MuiPaper-root": {
        maxWidth: "60vw",
      },
    },
    shift: ({ shiftLeft }) => ({
      marginLeft: shiftLeft,
    }),
  })
);

interface AlertsComponentProps {
  dispatch: AppDispatch;
  alerts: Alert[];
  shiftLeft: number;
}

export const AlertsComponent: React.FC<AlertsComponentProps> = ({
  dispatch,
  alerts,
  shiftLeft,
}) => {
  const classes = useStyles({ shiftLeft });
  const [open, setOpen] = useState(false);
  const priorityAlert = alerts.length > 0 ? alerts[0] : null;
  const handleClose = (reason?: string) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
    const delayedDispatch = debounce(() => {
      dispatch(removeAlertAction(0));
    }, 300);
    delayedDispatch();
  };

  const messageGuard = (message: any) =>
    // This should never happen if types are correct. But its possible to throw an Error object with unknown type
    // and dispatch it to Alerts. Which will crash the app.
    typeof message === "object" ? "" : message;

  useEffect(() => {
    if (alerts.length > 0) {
      setOpen(true);
    }
  }, [alerts]);

  return (
    priorityAlert && (
      <Snackbar
        data-testid="alert"
        className={clsx(classes.root, { [classes.shift]: shiftLeft })}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        open={open}
        autoHideDuration={6000}
        onClose={(e, reason) => handleClose(reason)}
        message={messageGuard(priorityAlert.message)}
      />
    )
  );
};
