import { Work } from "../../../utils/models";
import { WorkFormValues } from "./component";

export const workToFormData: (
  work: Work,
  initialValues?: WorkFormValues
) => WorkFormValues = (work, initialValues) => {
  return {
    title: work.title || "",
    altTitle: work.altTitle || "",
    titleLanguageCode:
      work.titleLanguageCode || initialValues?.titleLanguageCode,
    altTitleLanguageCode:
      work.altTitleLanguageCode || initialValues?.altTitleLanguageCode,
    duration: work.duration,
    creators: work.creators || [],
    publishers: work.publishers || [],
    firstReleaseDate: work.firstReleaseDate,
    hasBeenRecorded: work.hasBeenRecorded,
    grandRightsIndicator: work.grandRightsIndicator,
    firstRefusalRecordingIndicator: work.firstRefusalRecordingIndicator,
    musicalDistributionCategory: work.musicalDistributionCategory,
  };
};
