import { createStyles, makeStyles, Theme } from "@material-ui/core";
import React from "react";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    border: {
      position: ({ relative }: BorderProps) =>
        relative ? "relative" : "absolute",
      left: 0,
      paddingBottom: ({ noPadding }: BorderProps) => (noPadding ? 0 : 18),
      width: "100%",
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
  })
);

interface BorderProps {
  relative?: boolean;
  noPadding?: boolean;
}

export const Border: React.FC<BorderProps> = ({
  relative = false,
  noPadding = false,
}) => {
  const classes = useStyles({ relative, noPadding });
  return <div className={classes.border} />;
};
