import React from "react";
import { Box, BoxProps, createStyles, Grid, makeStyles, Theme } from "@material-ui/core";
import clsx from "clsx";

export const splitCellWidth = 64;

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    cell: {
      minWidth: splitCellWidth,
      textAlign: "center",
    },
    header: {
      paddingBottom: theme.spacing(1.5),
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
  })
);

interface CellSplitProps extends BoxProps {
  header?: boolean;
}

export const CellSplit: React.FC<CellSplitProps> = ({
  children,
  className,
  header,
  ...props
}) => {
  const classes = useStyles();

  return (
    <Box
      {...props}
      component={Grid}
      className={clsx(classes.cell, className, {
        [classes.header]: header,
      })}
    >
      {children}
    </Box>
  );
};
