import React from "react";
import { createStyles, makeStyles, Tabs, Theme } from "@material-ui/core";
import { useLocation } from "react-router-dom";
import { AppState } from "../../../App.reducer";
import { Tab } from "../../atoms/Tab";
import { Href } from "../../../utils/models";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    indicator: {
      height: 1,
    },
  })
);

interface TabMenuProps {
  tabs: Array<{
    label: string;
    link: string;
    access?: (state: AppState) => Href | undefined | boolean;
  }>;
  onChange?: () => void;
}

export const TabMenu: React.FC<TabMenuProps> = ({ tabs, onChange }) => {
  const classes = useStyles();
  const { pathname } = useLocation();

  const match = tabs.find(({ link }) => link === pathname);

  return tabs.length > 0 ? (
    <Tabs
      classes={{ indicator: classes.indicator }}
      value={match?.link || tabs[0].link}
      indicatorColor="primary"
    >
      {tabs.map(({ label, link }) => (
        <Tab
          label={label}
          value={link}
          to={link}
          key={link}
          onClick={onChange}
        />
      ))}
    </Tabs>
  ) : (
    <React.Fragment />
  );
};
