import { Button } from "@material-ui/core";
import React from "react";
import { ButtonProps } from "@material-ui/core/Button";
import { ButtonCircularProgress } from "../ButtonCircularProgress";

interface ButtonSubmitProps {
  submitting?: boolean;
}

export const ButtonSubmit: React.FC<ButtonSubmitProps & ButtonProps> = ({
  disabled,
  submitting,
  children,
  ...props
}) => {
  return (
    <Button
      variant="contained"
      color="primary"
      type="submit"
      disabled={disabled || submitting}
      {...props}
    >
      {submitting ? (
        <ButtonCircularProgress variant={props.variant} />
      ) : (
        children
      )}
    </Button>
  );
};
