import { WorkCreator } from "../../../utils/models";
import { FormValues } from "./component";

export const createSubPublisherAssociation: (
  creators: WorkCreator[],
  formValues: FormValues
) => WorkCreator[] = (creators, formValues) => {
  return creators.map((creator) => {
    if (creator.ipi.ipiNameNumber === formValues.creator?.ipi) {
      return {
        ...creator,
        publishers: creator.publishers?.map((publisher) => {
          if (publisher.ipiNameNumber === formValues.publisher?.ipi) {
            return {
              ...publisher,
              publisherSocietyAgreementNumber: formValues.agreementNumber,
            };
          }
          return publisher;
        }),
      };
    }
    return creator;
  });
};

export const isSubmitButtonDisabled = ({
  creator,
  agreementNumber,
  publisher,
}: FormValues) => !Boolean(creator && publisher && agreementNumber);
