import { PartyRoleType, PartyRoleTypeLabel } from "../../../types/enums";

export const formatPartyRole = (dense: boolean) => (role: PartyRoleType) => {
  if (role === PartyRoleType.C) {
    if (dense) {
      return "C";
    }
    return PartyRoleTypeLabel.C;
  }

  if (role === PartyRoleType.A) {
    if (dense) {
      return "A";
    }
    return PartyRoleTypeLabel.A;
  }

  if (role === PartyRoleType.CA) {
    if (dense) {
      return "CA";
    }
    return PartyRoleTypeLabel.CA;
  }
};
