import { Template, WorkTemplatePatch } from "../../../utils/models";
import { TemplateFormValues } from "../../organisms/FormWorkTemplate";
import { formatWorkCreatorsPatchData } from "../WorkRegistration/effects.utils";

const parseWorkTemplateToWorkTemplatePatch: (
  template: Template | TemplateFormValues
) => WorkTemplatePatch = (template: Template | TemplateFormValues) => ({
  title: template.title || "",
  firstRefusalRecordingIndicator: template.firstRefusalRecordingIndicator,
  grandRightsIndicator: template.grandRightsIndicator,
  hasBeenRecorded: template.hasBeenRecorded,
  musicalDistributionCategory: template.musicalDistributionCategory || "",
});

export const extractDirtyWorkTemplateAttrs = (
  template: Template,
  templateFormValues: TemplateFormValues,
  isTemplate?: boolean
) => {
  const prev: WorkTemplatePatch = parseWorkTemplateToWorkTemplatePatch(
    template
  );
  const curr: WorkTemplatePatch = parseWorkTemplateToWorkTemplatePatch(
    templateFormValues
  );
  const attributes: Array<keyof WorkTemplatePatch> = [
    "title",
    "firstRefusalRecordingIndicator",
    "grandRightsIndicator",
    "hasBeenRecorded",
    "musicalDistributionCategory",
  ];

  const creatorsPatch = formatWorkCreatorsPatchData(
    template,
    templateFormValues,
    isTemplate
  );
  const dirty: { [index: string]: any } = {
    ...(creatorsPatch && { creators: creatorsPatch }),
  };
  attributes.forEach((attr) => {
    if (prev[attr] !== curr[attr]) {
      dirty[attr] = curr[attr];
    }
  });
  return dirty;
};
