import React, { useState } from "react";
import { Box, createStyles, makeStyles, Theme } from "@material-ui/core";
import { paths } from "../../../utils/common";
import { TabMenu } from "../../molecules/TabMenu";
import { isDevelopment } from "../../../utils/helpers";
import { InputSearch } from "../../atoms/InputSearch";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "inline-flex",
      justifyContent: "space-between",
      width: "100%",
      paddingTop: theme.spacing(6),
      [theme.breakpoints.only("xs")]: {
        paddingTop: theme.spacing(2),
      },
    },
  })
);

const getTabs = (isSocietyAdmin: boolean) => [
  { label: "Agreements", link: paths.AGREEMENTS_LIST },
  ...(isDevelopment && !isSocietyAdmin
    ? [
        {
          label: "Templates",
          link: paths.AGREEMENTS_TEMPLATE_LIST,
        },
      ]
    : []),
  { label: "Drafts", link: paths.AGREEMENTS_DRAFT_LIST },
];

interface AgreementsNavigationBarProps {
  isSocietyAdmin: boolean;
  search: string;
  onQueryChange: (query: string) => void;
  textFieldBreakpoint: boolean;
  searchDisabled: boolean;
}

export const AgreementsNavigationBar: React.FC<AgreementsNavigationBarProps> = ({
  isSocietyAdmin,
  search,
  onQueryChange,
  textFieldBreakpoint,
  searchDisabled,
}) => {
  const classes = useStyles();
  const urlParams = new URLSearchParams(search);
  const [queryInput, setQueryInput] = useState(urlParams.get("search") || "");

  const handleInputChange = (value: string) => {
    setQueryInput(value);
    onQueryChange(value);
  };

  const handleTabMenuChange = () => {
    setQueryInput("");
  };

  return (
    <Box className={classes.root}>
      <TabMenu tabs={getTabs(isSocietyAdmin)} onChange={handleTabMenuChange} />
      {textFieldBreakpoint && (
        <Box ml="auto" mr={1}>
          <InputSearch
            disabled={searchDisabled}
            value={queryInput}
            onChange={handleInputChange}
          />
        </Box>
      )}
    </Box>
  );
};
