import {
  SET_API_KEY_WEB,
  SettingsActionTypes,
  TOGGLE_REVISION_SUCCESS,
} from "./actions.types";

export interface SettingsState {
  revision: {
    work: boolean;
    agreement: boolean;
  };
  apiKeyWeb?: string;
}

export const settingsInitialState: SettingsState = {
  revision: {
    agreement: false,
    work: false,
  },
};

export const settingsReducer = (
  state = settingsInitialState,
  action: SettingsActionTypes
): SettingsState => {
  switch (action.type) {
    case TOGGLE_REVISION_SUCCESS: {
      return { ...state, revision: action.revision };
    }

    case SET_API_KEY_WEB: {
      return {
        ...state,
        apiKeyWeb: action.apiKeyWeb,
      };
    }

    default:
      return state;
  }
};
