import { Box } from "@material-ui/core";
import React from "react";

interface ContainerFillHeightProps {
  className?: string;
}

export const ContainerFillHeight: React.FC<ContainerFillHeightProps> = ({
  children,
  className,
}) => (
  <Box
    height="100vh"
    display="flex"
    flexDirection="column"
    className={className}
  >
    {children}
  </Box>
);
