import React, { useEffect, useState } from "react";
import { Field, FieldProps, getIn } from "formik";
import { Box, Switch, Typography } from "@material-ui/core";
import { format } from "date-fns";
import { getInfiniteDate } from "../FormikDatePicker";
import { FormikTableDateSelect } from "../FormikTableDateSelect";

interface FormikDateSwitchProps {
  indefinite?: boolean;
  disabled?: boolean;
}

export const FormikDateSwitch: React.FC<FieldProps & FormikDateSwitchProps> = ({
  form: { setFieldValue, values },
  field: { name },
  indefinite,
  disabled,
}) => {
  const value: string | undefined = getIn(values, name);

  const hasIndefinite = () =>
    indefinite && value && format(new Date(value), "yyyy") === "9999";

  const [switchOn, setSwitchOn] = useState(
    hasIndefinite() ? false : Boolean(value)
  );

  const onSwitchOff = () => {
    if (indefinite) {
      const date = getInfiniteDate();

      setFieldValue(name, date.toISOString());
    } else {
      setFieldValue(name, undefined);
    }
  };

  const onSwitchOn = () => {
    const date = new Date();
    setFieldValue(name, date.toISOString());
  };

  const handleSwitchChange = () => {
    const state = !switchOn;
    !state ? onSwitchOff() : onSwitchOn();
    setSwitchOn(state);
  };

  useEffect(() => {
    setSwitchOn(hasIndefinite() ? false : Boolean(value));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [value, indefinite]);

  return (
    <Box display="inline-flex" alignItems="center">
      <Switch
        disabled={disabled}
        color="primary"
        checked={switchOn}
        onChange={handleSwitchChange}
      />

      {switchOn && (
        <Field
          component={FormikTableDateSelect}
          name={name}
          disabled={disabled}
        />
      )}

      {indefinite && !switchOn && (
        <Typography variant="body2">Indefinite</Typography>
      )}
    </Box>
  );
};
