import React, { useEffect, useState } from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { useMediaQuery, useTheme } from "@material-ui/core";
import { fetchWorksEffect } from "./effects";
import { useLocation } from "react-router-dom";
import debounce from "lodash/debounce";
import { fetchTemplatesEffect } from "../Templates/effects";
import {
  getWorksListCreateLink,
  getWorksListLinks,
} from "../ListWorks/selectors";
import {
  getTemplatesListCreateLink,
  getTemplatesListLinks,
} from "../Templates/selectors";
import { paths } from "../../../utils/common";
import { WorksNavigationBar } from "../../organisms/WorksNavigationBar";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { InputSearchShrink } from "../../organisms/InputSearchShrink";
import "url-polyfill";
import {
  getSortBy,
  getSortOrder,
} from "../../organisms/HeaderTableWorks/utils";
import { useHistory } from "react-router";
import { getWorkFilters } from "../../organisms/WorksNavigationBar/utils";
import { concatQueryUrl } from "../../../utils/helpers";
import { getBootstrapLinksSelector } from "../SignIn/selectors";
import { isSearchDisabled, setWorksListUrlParams } from "./utils";
import { WorksFab } from "../../molecules/WorksFab";

export const Works = () => {
  const theme = useTheme();
  const breakpointXs = useMediaQuery(theme.breakpoints.down("xs"));
  const { pathname, search } = useLocation();
  const { push } = useHistory();
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();
  const state = useAppSelector((state) => state);
  const isDraftsPage = pathname === paths.WORKS_DRAFT_LIST;
  const isMyWorksPage = pathname === paths.WORKS_LIST;
  const isTemplatesPage = pathname === paths.WORKS_TEMPLATE_LIST;
  const worksListLinks = getWorksListLinks(state);
  const templatesListLinks = getTemplatesListLinks(state);
  const templatesListLink = getBootstrapLinksSelector(state)?.workTemplates;
  const canCreateWork = Boolean(getWorksListCreateLink(state));
  const canCreateTemplate = Boolean(getTemplatesListCreateLink(state));
  const [isUrlParamsDefined, setIsUrlParamsDefined] = useState(false);

  useEffect(() => {
    if ([paths.WORKS, paths.WORKS_LIST].includes(pathname)) {
      setWorksListUrlParams(search, push);
    }
    setIsUrlParamsDefined(true);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathname]);

  useEffect(() => {
    if (isUrlParamsDefined) {
      const urlParams = new URLSearchParams(search);
      const query = urlParams.get("search") || undefined;
      const sortParams = {
        sortBy: getSortBy(urlParams.get("sortBy")),
        sortOrder: getSortOrder(urlParams.get("sortOrder")),
      };

      if (
        isDraftsPage ||
        isMyWorksPage ||
        (isTemplatesPage && !worksListLinks)
      ) {
        const filters = getWorkFilters(urlParams.get("filters"), isDraftsPage);
        dispatchEffect(
          fetchWorksEffect({
            isDraftsPage,
            statuses: filters,
            sortParams,
            query,
          })
        );
      }

      if (templatesListLink && (isTemplatesPage || !templatesListLinks)) {
        dispatchEffect(fetchTemplatesEffect({ sortParams, query }));
      }
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    dispatchEffect,
    pathname,
    search,
    isDraftsPage,
    isTemplatesPage,
    isMyWorksPage,
    isUrlParamsDefined,
  ]);

  const handleQueryChange = debounce((query: string) => {
    if (query.length > 2 || query === "") {
      const updatedSearch = concatQueryUrl(search, { search: query });
      push({
        search: updatedSearch,
      });
    }
  }, 500);

  return (
    <React.Fragment>
      <ContainerAppBar
        dispatch={dispatch}
        title="Works"
        userIcon={
          breakpointXs && (
            <InputSearchShrink onChange={handleQueryChange} search={search} />
          )
        }
        secondaryContent={
          <WorksNavigationBar
            isDraftsPage={isDraftsPage}
            onQueryChange={handleQueryChange}
            pathname={pathname}
            search={search}
            searchDisabled={isSearchDisabled(search, pathname, state)}
            onFilterChange={(filter) => {
              const updatedSearch = concatQueryUrl(search, { filters: filter });
              push({
                search: updatedSearch,
              });
            }}
            state={state}
          />
        }
      />
      <WorksFab
        pathname={pathname}
        canCreateTemplate={canCreateTemplate}
        canCreateWork={canCreateWork}
      />
    </React.Fragment>
  );
};
