import React from "react";
import {
  createStyles,
  FormControl,
  FormControlLabel,
  FormHelperText,
  makeStyles,
  Radio,
  RadioGroup,
  Theme,
  Typography,
} from "@material-ui/core";
import { FieldProps } from "formik";
import { TooltipTypes } from "../../../types/tooltips";
import { TooltipMap } from "../../atoms/TooltipMap";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    groupLabel: {
      paddingBottom: theme.spacing(2),
    },
    label: {
      paddingLeft: theme.spacing(1),
    },
    formControlLabel: {
      paddingBottom: theme.spacing(0.75),
    },
    formControl: {
      padding: theme.spacing(2),
    },
  })
);

interface FormikRadioSelectProps {
  label: string;
  options: Array<{ label: string; value: any; disabled?: boolean }>;
  disabled?: boolean;
  labelTooltip?: TooltipTypes;
}

export const FormikRadioSelect: React.FC<
  FormikRadioSelectProps & FieldProps
> = ({
  label,
  options,
  form: { setFieldValue, isSubmitting, errors },
  field: { name, value },
  disabled,
  labelTooltip = "None",
}) => {
  const classes = useStyles();

  const error = errors[name];

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    let value: any = event.target.value;
    if (value === "false") value = false;
    if (value === "true") value = true;
    setFieldValue(name, value);
  };

  return (
    <FormControl component="fieldset" className={classes.formControl}>
      <Typography
        variant="body2"
        color="textSecondary"
        className={classes.groupLabel}
      >
        <TooltipMap to={labelTooltip} placement="top">{label}</TooltipMap>
      </Typography>
      <RadioGroup onChange={handleChange} value={value ?? null} name={name}>
        {options.map(({ label, value, disabled: disabledOption }) => (
          <FormControlLabel
            key={`${name}:${label}`}
            value={value}
            label={label}
            disabled={isSubmitting || disabledOption || disabled}
            classes={{ label: classes.label, root: classes.formControlLabel }}
            control={
              <Radio
                color="primary"
                disabled={isSubmitting || disabled || disabledOption}
              />
            }
          />
        ))}
      </RadioGroup>
      {error && <FormHelperText error={true}>{error}</FormHelperText>}
    </FormControl>
  );
};
