import React from "react";
import { RowCreatorListItem } from "../../molecules/RowCreatorListItem";
import { CellAvatarSummary } from "../CellAvatarSummary";
import clsx from "clsx";

interface RowCreatorLeftContentProps {
  className?: string;
}

export const RowCreatorLeftContent: React.FC<RowCreatorLeftContentProps> = ({
  className,
  children,
}) => {
  return (
    <RowCreatorListItem className={clsx(className)} noPadding>
      <CellAvatarSummary />
      {children}
    </RowCreatorListItem>
  );
};
