import { territories } from "../../../utils/territoryCodes";

const territoriesArr = Object.keys(territories).map((el) => territories[el]);

export const getFormattedTerritories = (territories: string[]) =>
  territories.map((el) => {
    const include = el.slice(0, 1) === "I";
    const territory = territoriesArr.find((c) => c.tisCode === el.slice(1));
    const prefix = include ? "+" : "-";
    return `${prefix}${territory?.tisA} `;
  });
