import React, { useState } from "react";
import { AgreementListItem } from "../../../utils/models";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import { Href } from "../../../utils/models";
import { addAlertAction } from "../../pages/Alerts/actions";
import { AlertType } from "../../pages/Alerts/actions.types";
import {
  createAgreementFromTemplateEffect,
  deleteAgreementTemplateEffect,
} from "../../pages/AgreementTemplates/effects";
import { useHistory } from "react-router";
import { paths } from "../../../utils/common";
import { DialogCreateAgreementFromTemplate } from "../../molecules/DialogCreateAgreementFromTemplate";
import { DialogWarning } from "../../molecules/DialogWarning";
import { ListItemAgreementTemplate } from "../ListItemAgreementTemplate";
import { HeaderListAgreementTemplates } from "../HeaderListAgreementTemplates";
import { TableList } from "../../molecules/TableList";
import { Box } from "@material-ui/core";

const ownerWarning =
  "The agreement template and all of it's associated data will be permanently deleted.";

interface ListAgreementTemplatesProps {
  templates: AgreementListItem[];
  dispatchEffect: AppDispatchEffect;
  dispatch: AppDispatch;
}

export const ListAgreementTemplates: React.FC<ListAgreementTemplatesProps> = ({
  templates,
  dispatchEffect,
  dispatch,
}) => {
  const history = useHistory();
  const [showConfirmDialog, setShowConfirmDialog] = useState(false);
  const [showCreateFromDialog, setShowCreateFromDialog] = useState(false);
  const [dialogMessage, setDialogMessage] = useState("");
  const [deleteProps, setDeleteProps] = useState<
    { guid: string; link?: Href } | undefined
  >(undefined);
  const [createFromLink, setCreateFromLink] = useState<Href | undefined>(
    undefined
  );

  const handleRemove = (
    guid: string,
    {
      deleteAgreement,
    }: {
      deleteAgreement?: Href;
      leaveAgreement?: Href;
    }
  ) => {
    if (deleteAgreement) {
      setDeleteProps({ guid, link: deleteAgreement });
      setDialogMessage(deleteAgreement ? ownerWarning : "");
      setShowConfirmDialog(true);
    }
  };

  const handleCreateFrom = (createFrom?: Href) => {
    if (createFrom) {
      setCreateFromLink(createFrom);
      setShowCreateFromDialog(true);
    }
  };

  const handleDeleteConfirm = async () => {
    if (deleteProps?.link) {
      try {
        await dispatchEffect(
          deleteAgreementTemplateEffect(deleteProps.guid, deleteProps.link)
        );
      } catch (e) {}
    }
    handleClose();
  };

  const handleClose = () => {
    setShowConfirmDialog(false);
    setDeleteProps(undefined);
    setDialogMessage("");
  };

  const handleCreateFromClose = () => {
    setShowCreateFromDialog(false);
    setDeleteProps(undefined);
  };

  const onCreateFromSubmit = async (title: string) => {
    if (createFromLink) {
      try {
        const guid = await dispatchEffect(
          createAgreementFromTemplateEffect(createFromLink, title)
        );
        history.push(`${paths.AGREEMENTS_REGISTER}/${guid}`);
      } catch (e) {
        dispatch(
          addAlertAction(
            AlertType.ERROR,
            `Couldn't create agreement. ${e.message}`
          )
        );
      }
    }
  };

  return (
    <React.Fragment>
      {showConfirmDialog && (
        <DialogWarning
          width={400}
          onConfirm={handleDeleteConfirm}
          onCancel={handleClose}
          message={dialogMessage}
        />
      )}
      <DialogCreateAgreementFromTemplate
        open={showCreateFromDialog}
        width={450}
        onSubmit={onCreateFromSubmit}
        onCancel={handleCreateFromClose}
      />
      {templates.length > 0 && (
        <TableList header={<HeaderListAgreementTemplates />}>
          {templates.map((el, index) => (
            <ListItemAgreementTemplate
              key={index}
              template={el}
              handleOnRemove={handleRemove}
              handleOnCreateFrom={handleCreateFrom}
            />
          ))}
          <Box height={60} />
        </TableList>
      )}
    </React.Fragment>
  );
};
