import React, { useState } from "react";
import {
  Box,
  IconButton,
  makeStyles,
  Table,
  TableBody,
  TableHead,
  TableRow,
  Theme,
} from "@material-ui/core";
import TableCell from "@material-ui/core/TableCell";
import { createStyles, withStyles } from "@material-ui/core/styles";
import {
  AgreementRightType,
  AgreementRightTypeLabel,
  IdentityType,
} from "../../../types/enums";
import {
  FastField,
  Field,
  FieldArray,
  FieldProps,
  getIn,
  useFormikContext,
} from "formik";
import { AgreementShare } from "../../../utils/models";
import clsx from "clsx";
import { AgreementFormPermissions } from "../FormAgreement/utils";
import { getDialogAlertCacheKey } from "../../molecules/DialogRemoveAgreementShare/utils";
import { getIpiRightsByType } from "./utils";
import { Draggable, Droppable } from "react-beautiful-dnd";
import { FormikIpiRightsSelect } from "../../molecules/FormikIpiRightsSelect";
import { FormikAddAgreementShare } from "../../atoms/FormikAddAgreementShare";
import { FormikCopyShare } from "../../atoms/FormikCopyShare";
import { AgreementFormValues } from "../FormAgreement";
import { DialogRemoveAgreementShare } from "../../molecules/DialogRemoveAgreementShare";
import { ActionMenu } from "../ActionMenu";
import { FormikEnumSelect } from "../../molecules/FormikEnumSelect";
import { TooltipMap } from "../../atoms/TooltipMap";
import { Icon } from "../../atoms/Icon";
import { FormikAgreementShareInput } from "../../atoms/FormikAgreementShareInput";
import { FormikTermShareErrorRow } from "../FormikTermShareErrorRow";

const useStyles = makeStyles((theme: Theme) => {
  const backgroundColor = theme.palette.custom.background.input;
  const thickBorder = `8px solid ${backgroundColor}`;
  const thinBorder = `1px solid ${backgroundColor}`;
  const borderStyle = `1px solid ${theme.palette.custom.state.dragged}`;
  return createStyles({
    table: {
      borderCollapse: "unset",
      background: backgroundColor,
      borderRight: borderStyle,
      borderBottom: borderStyle,
      borderLeft: borderStyle,
      borderBottomLeftRadius: theme.spacing(1),
      borderBottomRightRadius: theme.spacing(1),
    },
    dragCell: {
      borderLeft: thickBorder,
      borderTopLeftRadius: theme.spacing(2),
      borderBottomLeftRadius: theme.spacing(2),
      paddingLeft: theme.spacing(2),
      paddingRight: 0,
    },
    rightTypeCell: {
      minWidth: 130,
    },
    ipiRightsCell: {
      width: "100%",
    },
    creatorCell: {
      minWidth: 225,
    },
    publisherCell: {
      minWidth: 200,
    },
    assigneeCell: {
      minWidth: 180,
    },
    optionsCell: {
      borderRight: thickBorder,
      borderTopRightRadius: theme.spacing(2),
      borderBottomRightRadius: theme.spacing(2),
      padding: theme.spacing(0, 2, 0, 0),
    },
    addShareCell: {
      padding: theme.spacing(1.5, 2, 1.5, 0),
      border: "none",
    },
    rightType: {
      height: 40,
    },
    emptyCell: {
      border: "none",
      padding: theme.spacing(1),
      background: theme.palette.background.paper,
    },
    tableCell: {
      background: theme.palette.background.paper,
      borderTop: thickBorder,
      borderBottom: thickBorder,
      paddingTop: theme.spacing(1),
      paddingBottom: theme.spacing(1),
    },
    noPadding: {
      paddingTop: 0,
      paddingBottom: 0,
    },
    draggingRow: {
      "& td": {
        border: "none",
        borderTop: thinBorder,
        borderBottom: thinBorder,
        "&:first-of-type": {
          borderLeft: thinBorder,
          borderTopLeftRadius: theme.spacing(1),
          borderBottomLeftRadius: theme.spacing(1),
        },
        "&:last-of-type": {
          borderRight: thinBorder,
          borderTopRightRadius: theme.spacing(1),
          borderBottomRightRadius: theme.spacing(1),
        },
      },
    },
  });
});

const TableHeaderCell = withStyles((theme) => ({
  root: {
    ...theme.typography.body2,
    color: theme.palette.text.secondary,
    border: "none",
    paddingBottom: 0,
    paddingTop: theme.spacing(3),
  },
}))(TableCell);

interface TableAgreementSharesProps {
  parent: string;
  shares: AgreementShare[];
  permissions: AgreementFormPermissions;
  profileRef: string;
}

export const TableAgreementShares: React.FC<TableAgreementSharesProps> = ({
  parent,
  shares,
  permissions,
  profileRef,
}) => {
  const classes = useStyles();
  const {
    setFieldValue,
    errors,
    touched,
    values: { assignee, assignor },
  } = useFormikContext<AgreementFormValues>();
  const [showConfirm, setShowConfirm] = useState(false);
  const [removeIndex, setRemoveIndex] = useState<undefined | number>(undefined);
  const oneOfPartyIsCreator =
    assignee?.type === IdentityType.CREATOR ||
    assignor?.type === IdentityType.CREATOR;

  const handleRemove = (initRemove: () => void) => {
    const hideAlert =
      localStorage.getItem(getDialogAlertCacheKey(profileRef)) === "true";
    if (hideAlert) {
      initRemove();
      setShowConfirm(false);
      setRemoveIndex(undefined);
    } else {
      setShowConfirm(true);
    }
  };

  const handleRightTypeChange = (value: AgreementRightType, path: string) => {
    const changedIpiRights = getIpiRightsByType(value);
    setFieldValue(
      `${path}.usageTypes`,
      changedIpiRights.map((el) => el.code)
    );
  };

  const showShareErrorsRow = (path: string) => {
    const paths = [
      "writerShareRetained",
      "publisherShareRetained",
      "assigneeCollectibleShare",
    ];
    return paths.find((name) =>
      Boolean(
        getIn(errors, `${path}.${name}`) && getIn(touched, `${path}.${name}`)
      )
    );
  };

  return (
    <React.Fragment>
      <FieldArray name={parent}>
        {(arrayHelpers) => (
          <React.Fragment>
            {showConfirm && (
              <DialogRemoveAgreementShare
                profileRef={profileRef}
                onCancel={() => setShowConfirm(false)}
                onConfirm={() => {
                  if (removeIndex !== undefined) {
                    arrayHelpers.remove(removeIndex);
                  }
                  setShowConfirm(false);
                  setRemoveIndex(undefined);
                }}
              />
            )}
            <Table className={classes.table}>
              <TableHead>
                {shares.length > 0 && (
                  <TableRow>
                    <TableHeaderCell
                      className={classes.dragCell}
                      align="center"
                    />
                    <TableHeaderCell
                      className={classes.rightTypeCell}
                      align="left"
                    >
                      <Box pl={1.5}>Right Type</Box>
                    </TableHeaderCell>
                    <TableHeaderCell
                      className={classes.ipiRightsCell}
                      align="left"
                    >
                      <TooltipMap to="AgreementIPIRights">
                        IPI Rights
                      </TooltipMap>
                    </TableHeaderCell>
                    <TableHeaderCell
                      className={classes.creatorCell}
                      align="left"
                    >
                      <TooltipMap to="CreatorShareLabel">Creator</TooltipMap>
                    </TableHeaderCell>
                    <TableHeaderCell
                      className={classes.publisherCell}
                      align="left"
                    >
                      <TooltipMap to="PublisherShareLabel">
                        Publisher
                      </TooltipMap>
                    </TableHeaderCell>
                    <TableHeaderCell
                      className={classes.assigneeCell}
                      align="left"
                    >
                      <TooltipMap to="AgreementSubPublisher">
                        Sub-Publisher
                      </TooltipMap>
                    </TableHeaderCell>
                    <TableHeaderCell
                      className={classes.optionsCell}
                      align="center"
                    />
                  </TableRow>
                )}
              </TableHead>
              <Droppable droppableId={parent}>
                {(provided) => (
                  <TableBody ref={provided.innerRef}>
                    <React.Fragment>
                      {shares.map((el, index) => {
                        const path = `${parent}.${index}`;
                        return (
                          <React.Fragment key={path}>
                            <Draggable
                              key={path}
                              draggableId={path}
                              index={index}
                            >
                              {(provided, snapshot) => (
                                <TableRow
                                  ref={provided.innerRef}
                                  className={clsx({
                                    [classes.draggingRow]: snapshot.isDragging,
                                  })}
                                  {...provided.draggableProps}
                                  {...provided.dragHandleProps}
                                >
                                  <TableCell
                                    className={clsx(
                                      classes.dragCell,
                                      classes.tableCell
                                    )}
                                    align="center"
                                  >
                                    <TooltipMap
                                      to="Copy"
                                      enterDelay={0}
                                      disableCursor
                                    >
                                      <FastField
                                        component={FormikCopyShare}
                                        name={parent}
                                        index={index}
                                      />
                                    </TooltipMap>
                                  </TableCell>
                                  <TableCell
                                    align="left"
                                    className={clsx(
                                      classes.rightTypeCell,
                                      classes.tableCell
                                    )}
                                  >
                                    <Field
                                      component={FormikEnumSelect}
                                      disabled={!permissions.updateShare}
                                      onChange={(value: AgreementRightType) =>
                                        handleRightTypeChange(value, path)
                                      }
                                      className={classes.rightType}
                                      name={`${path}.rightType`}
                                      type="text"
                                      options={AgreementRightType}
                                      optionToLabel={{
                                        [AgreementRightType.M]:
                                          AgreementRightTypeLabel.M,
                                        [AgreementRightType.P]:
                                          AgreementRightTypeLabel.P,
                                        [AgreementRightType.T]:
                                          AgreementRightTypeLabel.T,
                                        [AgreementRightType.S]:
                                          AgreementRightTypeLabel.S,
                                      }}
                                      disableUnderline
                                    />
                                  </TableCell>
                                  <TableCell
                                    className={clsx(
                                      classes.ipiRightsCell,
                                      classes.tableCell
                                    )}
                                    align="left"
                                  >
                                    <FastField
                                      component={FormikIpiRightsSelect}
                                      disabled={!permissions.updateShare}
                                      name={path}
                                    />
                                  </TableCell>

                                  <TableCell
                                    className={clsx(
                                      classes.creatorCell,
                                      classes.tableCell,
                                      classes.noPadding
                                    )}
                                    align="left"
                                  >
                                    <FastField
                                      name={`${path}.writerShareRetained`}
                                    >
                                      {(props: FieldProps) => (
                                        <FormikAgreementShareInput
                                          {...props}
                                          disabled={!permissions.updateShare}
                                        />
                                      )}
                                    </FastField>
                                  </TableCell>
                                  <TableCell
                                    className={clsx(
                                      classes.publisherCell,
                                      classes.tableCell,
                                      classes.noPadding
                                    )}
                                    align="left"
                                  >
                                    <FastField
                                      name={`${path}.publisherShareRetained`}
                                    >
                                      {(props: FieldProps) => (
                                        <FormikAgreementShareInput
                                          {...props}
                                          disabled={!permissions.updateShare}
                                        />
                                      )}
                                    </FastField>
                                  </TableCell>
                                  <TableCell
                                    className={clsx(
                                      classes.assigneeCell,
                                      classes.tableCell,
                                      classes.noPadding
                                    )}
                                    align="left"
                                  >
                                    <Box
                                      display="inline-flex"
                                      alignItems="center"
                                    >
                                      <FastField
                                        name={`${path}.assigneeCollectibleShare`}
                                      >
                                        {(props: FieldProps) => (
                                          <FormikAgreementShareInput
                                            {...props}
                                            disabled={
                                              oneOfPartyIsCreator ||
                                              !permissions.updateShare
                                            }
                                          />
                                        )}
                                      </FastField>
                                    </Box>
                                  </TableCell>

                                  <TableCell
                                    className={clsx(
                                      classes.optionsCell,
                                      classes.tableCell
                                    )}
                                    align="center"
                                  >
                                    <ActionMenu
                                      items={[
                                        {
                                          label: "Remove",
                                          disabled: !permissions.deleteShare,
                                          onClick: () => {
                                            setRemoveIndex(index);
                                            handleRemove(() =>
                                              arrayHelpers.remove(index)
                                            );
                                          },
                                        },
                                      ]}
                                    >
                                      <IconButton size="small">
                                        <Icon name="listItemOptions" />
                                      </IconButton>
                                    </ActionMenu>
                                  </TableCell>
                                </TableRow>
                              )}
                            </Draggable>
                            {showShareErrorsRow(path) && (
                              <Field component={FormikTermShareErrorRow} />
                            )}
                          </React.Fragment>
                        );
                      })}
                      {provided.placeholder}
                    </React.Fragment>
                    <TableRow>
                      <TableCell
                        colSpan={7}
                        align="left"
                        className={classes.addShareCell}
                      >
                        <Field
                          component={FormikAddAgreementShare}
                          disabled={!permissions.addShare}
                          name={`${parent}.${shares.length}`}
                        />
                      </TableCell>
                    </TableRow>
                  </TableBody>
                )}
              </Droppable>
            </Table>
          </React.Fragment>
        )}
      </FieldArray>
    </React.Fragment>
  );
};
