export const buildInitialValues = (agreement: Agreement) => {
  const form: AgreementFormValues = {
    title: agreement.title,
    submitterAgreementNumbers: agreement.submitterAgreementNumbers || [""],
    recordingPrefix: agreement.recordingPrefix || "",
    libraryMusic: agreement?.libraryMusic,
    bulkTransferWorks: agreement?.bulkTransferWorks,
    usaLicenseIndicator:
      agreement?.usaLicenseIndicator || UsaLicenseIndicatorType.NONE,
    agreementType: "general", //todo:: missing?
    salesOrManufacture: agreement?.salesOrManufacture,
    assignor: agreement?.assignor,
    assignee: agreement?.assignee,
    terms: agreement.terms || [],
  };
  return form;
};

const parseAgreementToAgreementPatch: (
  agreement: Agreement | AgreementFormValues
) => AgreementPatch = (agreement: Agreement | AgreementFormValues) => {
  const submitterAgreementNumbers = (
    agreement.submitterAgreementNumbers || []
  ).filter(Boolean);
  const dirty: AgreementPatch = {
    title: agreement.title || "",
    salesOrManufacture: agreement.salesOrManufacture || "",
    bulkTransferWorks: agreement.bulkTransferWorks,
    libraryMusic: agreement.libraryMusic,
    recordingPrefix: agreement.recordingPrefix || "",
    usaLicenseIndicator:
      agreement.usaLicenseIndicator || UsaLicenseIndicatorType.NONE,
    submitterAgreementNumbers: submitterAgreementNumbers.length
      ? submitterAgreementNumbers
      : null,
    assigneeIpiNameNumber: agreement.assignee?.ipiNameNumber || null,
    assignorIpiNameNumber: agreement.assignor?.ipiNameNumber || null,
  };
  return dirty;
};

const stringifyTermShare = ({
  rightType,
  usageTypes,
  writerShareRetained,
  publisherShareRetained,
  assigneeCollectibleShare,
}: AgreementShare) =>
  JSON.stringify({
    ...(rightType && { rightType }),
    ...(usageTypes.length && { usageTypes: usageTypes.sort() }),
    ...(!isNaN(writerShareRetained) && {
      writerShareRetained,
    }),
    ...(!isNaN(publisherShareRetained) && {
      publisherShareRetained,
    }),
    ...(!isNaN(assigneeCollectibleShare) && {
      assigneeCollectibleShare,
    }),
  });

const stringifyTerm = ({
  territories,
  startDate,
  endDate,
  priorRoyalties,
}: AgreementTerm) => {
  const toDate = (date: string) => format(new Date(date), "yyyy-MM-dd");
  const startDateFormatted = toDate(startDate);
  const endDateFormatted = toDate(endDate);
  const priorRoyaltiesFormatted = priorRoyalties
    ? toDate(priorRoyalties)
    : null;
  return JSON.stringify({
    ...(territories.length && { territories: territories }),
    ...(startDateFormatted && { startDate: startDateFormatted }),
    ...(endDateFormatted && { endDate: endDateFormatted }),
    ...(priorRoyaltiesFormatted && { priorRoyalties: priorRoyaltiesFormatted }),
  });
};

const handleTermAdd = (
  term: AgreementTerm,
  operations: AgreementTermPatchOperations[]
) => {
  const childShares = term.shares?.map((el) => ({
    op: Operation.ADD,
    share: formatTermShareToPatch(el),
  }));
  operations.push({
    op: Operation.ADD,
    term: {
      ...formatTermToPatch(term),
      shares: childShares?.length ? childShares : undefined,
    },
  });
};

const handleTermSharesAdd = (
  term: AgreementTerm,
  operations: AgreementTermPatchOperations[]
) => {
  const newShares = term.shares?.filter((share) => !share.guid);
  if (newShares?.length) {
    operations.push({
      op: Operation.PATCH,
      term: {
        guid: term.guid,
        shares: newShares.map((el) => ({
          op: Operation.ADD,
          share: formatTermShareToPatch(el),
        })),
      },
    });
  }
};

const handleTermsAndSharesUpdate = (
  term: AgreementTerm,
  oldStateTerms: AgreementTerm[],
  operations: AgreementTermPatchOperations[]
) => {
  const prevTermMatch = oldStateTerms.find((el) => el.guid === term.guid);
  const currShares = term.shares?.filter((el) => el.guid);
  const isTermChanged =
    prevTermMatch && stringifyTerm(term) !== stringifyTerm(prevTermMatch);
  const changedShares = currShares?.filter((share) => {
    const match = prevTermMatch?.shares?.find((el) => el.guid === share.guid);
    return match && stringifyTermShare(share) !== stringifyTermShare(match);
  });
  if (changedShares?.length || isTermChanged) {
    operations.push({
      op: Operation.PATCH,
      term: {
        guid: term.guid,
        ...(isTermChanged &&
          prevTermMatch &&
          extractDirtyAgreementTermAttrs(prevTermMatch, term)),
        shares: changedShares?.length
          ? changedShares.map((share) => {
              const match = prevTermMatch?.shares?.find(
                (el) => el.guid === share.guid
              );
              return {
                op: Operation.PATCH,
                share: {
                  guid: share.guid,
                  ...(share.rightType !== match?.rightType && {
                    rightType: share.rightType,
                  }),
                  ...(share.usageTypes.sort().toString() !==
                    match?.usageTypes.sort().toString() && {
                    usageTypes: share.usageTypes,
                  }),
                  ...(share.writerShareRetained !==
                    match?.writerShareRetained && {
                    writerShareRetained: share.writerShareRetained,
                  }),
                  ...(share.publisherShareRetained !==
                    match?.publisherShareRetained && {
                    publisherShareRetained: share.publisherShareRetained,
                  }),
                  ...(share.assigneeCollectibleShare !==
                    match?.assigneeCollectibleShare && {
                    assigneeCollectibleShare: share.assigneeCollectibleShare,
                  }),
                },
              };
            })
          : undefined,
      },
    });
  }
};

const handleTermsAndSharesDelete = (
  oldStateTerm: AgreementTerm,
  newStateTerms: AgreementTerm[],
  operations: AgreementTermPatchOperations[]
) => {
  // Check if term still exists
  const termMatch = newStateTerms.find((el) => el.guid === oldStateTerm.guid);

  if (!termMatch) {
    operations.push({
      op: Operation.REMOVE,
      term: {
        guid: oldStateTerm.guid,
      },
    });
  } else {
    // check if child shares is not deleted
    const currTermMatch = newStateTerms.find(
      (el) => el.guid === oldStateTerm.guid
    );
    const removedShares = oldStateTerm?.shares?.filter(
      (prevShare) =>
        !currTermMatch?.shares?.find(
          (currShare) => currShare.guid === prevShare.guid
        )
    );
    if (removedShares?.length) {
      operations.push({
        op: Operation.PATCH,
        term: {
          guid: oldStateTerm.guid,
          shares: removedShares.map((el) => ({
            op: Operation.REMOVE,
            share: {
              guid: el.guid,
            },
          })),
        },
      });
    }
  }
};

export const formatAgreementTermsPatchData = (
  terms: AgreementTerm[],
  formTerms: AgreementTerm[]
) => {
  const operations: AgreementTermPatchOperations[] = [];

  formTerms.forEach((term) => {
    if (term.guid) {
      handleTermSharesAdd(term, operations);
      handleTermsAndSharesUpdate(term, terms, operations);
    } else {
      handleTermAdd(term, operations);
    }
  });

  terms.forEach((term) => {
    handleTermsAndSharesDelete(term, formTerms, operations);
  });

  return operations;
};

const isMemberPermissionsChanged = (
  agreement: Agreement,
  agreementFormValues: AgreementFormValues
) => {
  const getNonOwner = ({
    assignor,
    assignee,
  }: {
    assignor?: AgreementMember;
    assignee?: AgreementMember;
  }) => {
    if (assignor?.permissions.owner === false) {
      return assignor;
    }
    if (assignee?.permissions.owner === false) {
      return assignee;
    }
  };
  const prevOwner = getNonOwner({
    assignee: agreement.assignee,
    assignor: agreement.assignor,
  });
  const currOwner = getNonOwner({
    assignee: agreementFormValues.assignee,
    assignor: agreementFormValues.assignor,
  });

  const stringify = (member: AgreementMember) =>
    JSON.stringify(member.permissions);

  return prevOwner && currOwner && stringify(prevOwner) !== stringify(currOwner)
    ? currOwner
    : null;
};

const getOtherAccess = ({ permissions: { write, read } }: AgreementMember) => {
  if (!write && !read) {
    return AgreementMemberPermissionType.NONE;
  }
  if (write) {
    return AgreementMemberPermissionType.WRITE;
  }
  if (read) {
    return AgreementMemberPermissionType.READ;
  }
};

export const extractDirtyAgreementAttrs = (
  agreement: Agreement,
  agreementFormValues: AgreementFormValues
) => {
  const prev: AgreementPatch = parseAgreementToAgreementPatch(agreement);
  const curr: AgreementPatch = parseAgreementToAgreementPatch(
    agreementFormValues
  );
  const attributes: Array<keyof AgreementPatch> = [
    "title",
    "salesOrManufacture",
    "bulkTransferWorks",
    "libraryMusic",
    "recordingPrefix",
    "usaLicenseIndicator",
    "submitterAgreementNumbers",
    "assigneeIpiNameNumber",
    "assignorIpiNameNumber",
  ];

  const termsPatch = formatAgreementTermsPatchData(
    agreement.terms || [],
    agreementFormValues.terms
  );

  const otherAccess: AgreementMember | null = isMemberPermissionsChanged(
    agreement,
    agreementFormValues
  );

  const dirty: {
    [index: string]: any;
  } = {
    ...(termsPatch.length ? { terms: termsPatch } : {}),
    ...(otherAccess && { otherAccess: getOtherAccess(otherAccess) }),
  };
  attributes.forEach((attr) => {
    if (JSON.stringify(prev[attr]) !== JSON.stringify(curr[attr])) {
      dirty[attr] = curr[attr];
    }
  });
  return dirty;
};

export const getOwnerPartyRole = ({
  assignee,
  assignor,
}: AgreementFormValues) => {
  if (assignee?.permissions.owner) {
    return RelationType.ASSIGNEE;
  }
  if (assignor?.permissions.owner) {
    return RelationType.ASSIGNOR;
  }
};

export const openErrorTab = (
  errors: FormikErrors<AgreementFormValues>,
  setActiveTab: (tab: AgreementNavigationTabs) => void
) => {
  const {
    assignee,
    assignor,
    terms,
    bulkTransferWorks,
    libraryMusic,
    recordingPrefix,
    agreementType,
    submitterAgreementNumbers,
    salesOrManufacture,
    usaLicenseIndicator,
  } = errors;

  const partySharesTabError = assignee || assignor || terms;
  const generalTabError =
    bulkTransferWorks ||
    libraryMusic ||
    recordingPrefix ||
    agreementType ||
    submitterAgreementNumbers ||
    salesOrManufacture ||
    usaLicenseIndicator;

  if (partySharesTabError) setActiveTab(AgreementNavigationTabs.PARTY_SHARES);
  if (generalTabError) setActiveTab(AgreementNavigationTabs.GENERAL);

  // Need to give some time for tab to open before trying to scroll
  setTimeout(() => {
    scrollToFormErrors(errors);
  }, 100);
};
