import {
  SET_API_KEY_WEB,
  SettingsActionTypes,
  TOGGLE_REVISION,
  TOGGLE_REVISION_SUCCESS,
  TOGGLE_REVISION_SUCCESS_FAILURE,
} from "./actions.types";

export const toggleRevisionAction = (): SettingsActionTypes => ({
  type: TOGGLE_REVISION,
});

export const toggleRevisionSuccessAction = ({
  work,
  agreement,
}: {
  work: boolean;
  agreement: boolean;
}): SettingsActionTypes => ({
  type: TOGGLE_REVISION_SUCCESS,
  revision: {
    work,
    agreement,
  },
});

export const setApiKeyWebAction = (apiKeyWeb: string): SettingsActionTypes => ({
  type: SET_API_KEY_WEB,
  apiKeyWeb,
});

export const toggleRevisionFailureAction = (): SettingsActionTypes => ({
  type: TOGGLE_REVISION_SUCCESS_FAILURE,
});
