import React, { useEffect } from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { RouteComponentProps } from "react-router";
import { AccountRoles, LoadingState } from "../../../types/enums";
import {
  getTemplateCloneLoadingStateSelector,
  getTemplateCloneSelector,
  getTemplateLoadingStateSelector,
  getTemplateSelector,
  getTemplatesListLinks,
  getTemplatesLoadingStateSelector,
  getTemplateValidationSchemaLoadingStateSelector,
  getTemplateValidationSchemaSelector,
} from "../Templates/selectors";
import { getAccountSelector, getCompanySelector } from "../SignIn/selectors";
import {
  getIpiListLoadingStateSelector,
  getIpiListSelector,
} from "../Ipi/selectors";
import { PageWorkRegistrationLoading } from "../../molecules/PageWorkRegistrationLoading";
import { FormWorkTemplate } from "../../organisms/FormWorkTemplate";
import { doesUserHaveRoles } from "../../../utils/helpers";
import {
  fetchTemplateEffect,
  fetchTemplatesEffect,
  fetchWorkTemplateValidationSchemaEffect,
} from "../Templates/effects";

export const WorkTemplate: React.FC<RouteComponentProps<{ id: string }>> = ({
  match: {
    params: { id },
  },
  history,
}) => {
  const dispatch = useAppDispatch();
  const dispatchEffect = useAppDispatchEffect();
  const state = useAppSelector((state) => state);
  const template = getTemplateSelector(state);
  const templateLoadingState = getTemplateLoadingStateSelector(state);
  const account = getAccountSelector(state);
  const ipiList = getIpiListSelector(state);
  const ipiListLoadingState = getIpiListLoadingStateSelector(state);
  const company = getCompanySelector(state);
  const templateListLinks = getTemplatesListLinks(state);
  const templatesListLinks = getTemplatesListLinks(state);
  const templatesLoadingState = getTemplatesLoadingStateSelector(state);
  const validationSchema = getTemplateValidationSchemaSelector(state);
  const validationSchemaLoadingState = getTemplateValidationSchemaLoadingStateSelector(
    state
  );
  const templateClone = getTemplateCloneSelector(state);
  const templateCloneLoadingState = getTemplateCloneLoadingStateSelector(state);

  useEffect(() => {
    const handleTemplateFetch = async (id: string) => {
      try {
        if (!templatesListLinks) {
          await dispatchEffect(fetchTemplatesEffect({ next: false }));
        }
        dispatchEffect(fetchTemplateEffect(id));
      } catch (e) {}
    };
    if (id && id !== template?.guid) {
      handleTemplateFetch(id);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id, template, dispatchEffect]);

  useEffect(() => {
    if (!validationSchema) {
      dispatchEffect(fetchWorkTemplateValidationSchemaEffect());
    }
    if (!templateListLinks) {
      dispatchEffect(fetchTemplatesEffect({ next: false }));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatchEffect]);

  return (
    <React.Fragment>
      {[
        templatesLoadingState,
        templateLoadingState,
        ipiListLoadingState,
        validationSchemaLoadingState,
        templateCloneLoadingState,
      ].includes(LoadingState.FETCHING) ? (
        <PageWorkRegistrationLoading
          dispatch={dispatch}
          breadCrumb={id ? "Edit Template" : "New Template"}
        />
      ) : (
        <FormWorkTemplate
          dispatch={dispatch}
          dispatchEffect={dispatchEffect}
          history={history}
          account={account}
          template={id ? template : templateClone}
          validationSchema={validationSchema}
          ipiList={ipiList}
          company={company}
          isPublisher={doesUserHaveRoles(state, [AccountRoles.PUBLISHER])}
          isCreator={doesUserHaveRoles(state, [AccountRoles.CREATOR])}
          templateListItemsLinks={{ ...templateListLinks }}
        />
      )}
    </React.Fragment>
  );
};
