import React, { useEffect, useState } from "react";
import { Box, createStyles, makeStyles, Theme } from "@material-ui/core";
import { useLocation } from "react-router";
import {
  AppDispatch,
  AppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import {
  getAccountSelector,
  getAuthPolicySelector,
  getChangeEmailConfirmLinkSelector,
  getIpiListFetchLinkSelector,
  getLegalProfileLinkSelector,
  getLegalProfileLoadingStateSelector,
} from "../../pages/SignIn/selectors";
import {
  getAddIpiV0LinkSelector,
  getIntegratedSocietiesLoadingStateSelector,
  getIntegratedSocietiesSelector,
  getIpiListLinksSelector,
  getIpiListSelector,
  getSocietiesLoadingStateSelector,
  getSocietiesSelector,
} from "../../pages/Ipi/selectors";
import { paths } from "../../../utils/common";
import { TabPanel } from "../../atoms/TabPanel";
import { Ipi } from "../../pages/Ipi";
import { ContainerTabPanel } from "../../atoms/ContainerTabPanel";
import { FormProfile } from "../FormProfile";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { ButtonFabAddNew } from "../../atoms/ButtonFabAddNew";
import { AddIpiNumber } from "../AddIpiNumber";
import { TabMenu } from "../../molecules/TabMenu";
import { LoadingState } from "../../../types/enums";
import {
  fetchIntegratedSocietiesEffect,
  fetchSocietiesEffect,
} from "../../pages/Ipi/effects";
import { openIpiSelectionMenuAction } from "../../pages/Ipi/actions";
import { LoadingScreen } from "../../atoms/LoadingScreen";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    appBarContent: {
      width: "100%",
      marginTop: theme.spacing(6),
      display: "inline-flex",
      alignItems: "baseline",
      justifyContent: "space-between",
    },
    fabAddIpn: {
      position: "absolute",
      bottom: theme.spacing(4),
      right: theme.spacing(4),
      zIndex: 1,
    },
  })
);

const creatorTabs = [
  {
    label: "Personal",
    link: paths.PROFILE_PERSONAL,
    access: getLegalProfileLinkSelector,
  },
  {
    label: "IPI Numbers",
    link: paths.PROFILE_IPI,
    access: getIpiListFetchLinkSelector,
  },
  // { label: "IPN Numbers", link: "/profile/ipn" },
  // { label: "Connections", link: "/profile/connections" }
];

interface PublisherProfileProps {
  dispatchEffect: AppDispatchEffect;
  dispatch: AppDispatch;
}

export const CreatorProfile: React.FC<PublisherProfileProps> = ({
  dispatchEffect,
  dispatch,
}) => {
  const classes = useStyles();
  const { pathname } = useLocation();
  const state = useAppSelector((state) => state);
  const account = getAccountSelector(state);
  const authPolicy = getAuthPolicySelector(state);
  const integratedSocieties = getIntegratedSocietiesSelector(state);
  const societies = getSocietiesSelector(state);
  const societiesLoadingState = getSocietiesLoadingStateSelector(state);
  const integratedSocietiesLoadingState = getIntegratedSocietiesLoadingStateSelector(
    state
  );
  const ipiList = getIpiListSelector(state);
  const ipiListLinks = getIpiListLinksSelector(state);
  const addIpiV0Link = getAddIpiV0LinkSelector(state);
  const { openIpiSelectionMenu } = state.ipi;
  const legalProfileLoadingState = getLegalProfileLoadingStateSelector(state);
  const changeEmailConfirmLink = getChangeEmailConfirmLinkSelector(state);
  const [popoverOpen, setPopoverOpen] = useState(false);

  const filteredTabs = creatorTabs.filter(({ access }) =>
    access ? Boolean(access(state)) : true
  );

  const currentTab = filteredTabs.find(({ link }) => link.includes(pathname));

  useEffect(() => {
    if (openIpiSelectionMenu) {
      setPopoverOpen(true);
    }
  }, [openIpiSelectionMenu]);

  useEffect(() => {
    if (
      popoverOpen &&
      [societiesLoadingState, integratedSocietiesLoadingState].includes(
        LoadingState.PRISTINE
      )
    ) {
      dispatchEffect(fetchIntegratedSocietiesEffect());
      dispatchEffect(fetchSocietiesEffect());
    }
  }, [
    popoverOpen,
    societiesLoadingState,
    integratedSocietiesLoadingState,
    dispatchEffect,
  ]);

  return (
    <React.Fragment>
      <ContainerTabPanel>
        <ContainerAppBar
          dispatch={dispatch}
          parentTitle="Profile"
          title={currentTab?.label}
          secondaryContent={
            <Box className={classes.appBarContent}>
              <TabMenu tabs={filteredTabs} />
            </Box>
          }
        />
        {legalProfileLoadingState === LoadingState.FETCHING ? (
          <LoadingScreen />
        ) : (
          <React.Fragment>
            <TabPanel value={pathname} index={paths.PROFILE_PERSONAL}>
              {account && (
                <FormProfile
                  dispatchEffect={dispatchEffect}
                  account={account}
                  passwordPolicy={authPolicy?.passwordPolicy}
                  changeEmailConfirmLink={changeEmailConfirmLink}
                />
              )}
            </TabPanel>
            <TabPanel value={pathname} index={paths.PROFILE_IPI}>
              <Ipi />
            </TabPanel>
          </React.Fragment>
        )}
      </ContainerTabPanel>
      {pathname === paths.PROFILE_IPN && (
        <ButtonFabAddNew
          to={paths.PROFILE_IPN}
          text="Add IPN Number"
          className={classes.fabAddIpn}
        />
      )}
      {pathname === paths.PROFILE_IPI && ipiListLinks && (
        <AddIpiNumber
          societies={societies}
          integratedSocieties={integratedSocieties}
          societiesLoadingState={societiesLoadingState}
          integratedSocietiesLoadingState={integratedSocietiesLoadingState}
          dispatchEffect={dispatchEffect}
          popoverOpen={popoverOpen}
          setPopoverOpen={setPopoverOpen}
          onPopoverClose={() => {
            dispatch(openIpiSelectionMenuAction(false));
          }}
          ipiListLength={ipiList.length}
          addManuallyEnabled={Boolean(addIpiV0Link)}
          ipiListLinks={ipiListLinks}
        />
      )}
    </React.Fragment>
  );
};
