import { AppState } from "../../../App.reducer";
import { LoadingState } from "../../../types/enums";
import { getBootstrapLinksSelector } from "../SignIn/selectors";

export const getIpiListSelector = (state: AppState) => state.ipi.ipiList;

export const getIpiListLoadingStateSelector = (state: AppState) =>
  state.ipi.ipiListLoadingState;

export const getAddIpiManuallyLoadingStateSelector = (state: AppState) =>
  state.ipi.addIpiManuallyLoadingState;

export const getDeleteIpiLinkSelector = (state: AppState) =>
  state.ipi.linksIpi?.deleteIpi;

export const getIpiListLinksSelector = (state: AppState) => state.ipi.linksIpi;

export const isAddIpiLoadingSelector = (state: AppState) => {
  const {
    addIpiManuallyLoadingState,
    addIpiV1LoadingState,
    addIpiV2LoadingState,
  } = state.ipi;
  return (
    addIpiManuallyLoadingState === LoadingState.FETCHING ||
    addIpiV2LoadingState === LoadingState.FETCHING ||
    addIpiV1LoadingState === LoadingState.FETCHING
  );
};

export const getIntegratedSocietiesSelector = (state: AppState) =>
  state.ipi.integratedSocieties;

export const getSocietiesSelector = (state: AppState) => state.ipi.societies;

export const getIntegratedSocietiesLoadingStateSelector = (state: AppState) =>
  state.ipi.integratedSocietiesLoadingState;

export const getSocietiesLoadingStateSelector = (state: AppState) =>
  state.ipi.societiesLoadingState;

export const getAddIpiV0LinkSelector = (state: AppState) =>
  state.ipi.linksSociety?.addIpiV0;

export const getAddIpiV1LinkSelector = (state: AppState) =>
  state.ipi.linksSociety?.addIpiV1;

export const getAddIpiV2LinkSelector = (state: AppState) =>
  state.ipi.linksSociety?.addIpiV2;

export const shouldFetchIpiListSelector = (state: AppState) => {
  const auth = Boolean(state.signIn.authenticated);
  const ipiListLoadingState = getIpiListLoadingStateSelector(state);
  const fetchIpiListLink = getBootstrapLinksSelector(state)?.ipis;

  return (
    auth && ipiListLoadingState === LoadingState.PRISTINE && fetchIpiListLink
  );
};
