import React, { useState } from "react";
import * as yup from "yup";
import { Field, Form, Formik } from "formik";
import { Box, Button, Container, Grid, Typography } from "@material-ui/core";
import { TextField } from "formik-material-ui";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import { createStyles, makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";
import { paths } from "../../../utils/common";
import { hasEmptyValues } from "../../../utils/helpers";
import { IPIRegex } from "../../../utils/regex";
import { signUpCompanyEffect } from "../../pages/SignUp/effects";
import { CenteredPaper } from "../../atoms/CenteredPaper";

const useStyles = makeStyles((theme) =>
  createStyles({
    paper: {
      display: "flex",
      height: "100%",
      width: "100%",
    },
    container: {
      maxWidth: 625,
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      justifyContent: "center",
    },
    gridContainer: {
      textAlign: "center",
    },
    header: {
      paddingBottom: theme.spacing(3),
    },
    button: {
      width: 272,
    },
    informationIcon: {
      color: theme.palette.primary.main,
    },
    tooltip: {
      maxWidth: 160,
    },
    notificationHeader: {
      paddingBottom: theme.spacing(3),
    },
  })
);

interface FormSignUpCompanyRequestProps {
  dispatchEffect: AppDispatchEffect;
  dispatch: AppDispatch;
}

interface FormValues {
  email: string;
  firstName: string;
  lastName: string;
  ipiNameNumber: string;
}

const initialValues = {
  email: "",
  firstName: "",
  lastName: "",
  ipiNameNumber: "",
};

const validationSchema = yup.object().shape({
  email: yup
    .string()
    .trim()
    .email("Email is not valid")
    .required("Email is required"),
  firstName: yup.string().required("First name is required"),
  lastName: yup.string().required("Last name is required"),
  ipiNameNumber: yup
    .string()
    .required("IPI Name Number is required")
    .matches(new RegExp(IPIRegex), "IPI Name Number is not valid"),
});

export const FormSignUpCompanyRequest: React.FC<FormSignUpCompanyRequestProps> = ({
  dispatchEffect,
  dispatch,
}) => {
  const classes = useStyles();
  const { push } = useHistory();
  const [showEmailNotification, setShowEmailNotification] = useState(false);

  const handleSubmit = async (data: FormValues) => {
    try {
      await dispatchEffect(signUpCompanyEffect(data));
      setShowEmailNotification(true);
    } catch (e) {}
  };

  const handleSignIn = () => {
    push(paths.SIGN_IN);
  };

  if (showEmailNotification) {
    return (
      <CenteredPaper width={374}>
        <Typography
          variant="h5"
          align="center"
          className={classes.notificationHeader}
        >
          Thank you for signing up!
        </Typography>
        <Typography variant="body1" align="center">
          Once your sign up request is approved you will receive a confirmation
          email
        </Typography>
      </CenteredPaper>
    );
  }

  return (
    <Container className={classes.container}>
      <Formik
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={handleSubmit}
      >
        {({ isSubmitting, values }) => (
          <Grid
            container
            component={Form}
            spacing={3}
            className={classes.gridContainer}
          >
            <Grid item xs={12}>
              <Typography className={classes.header} variant="h5">
                Publishing company sign up
              </Typography>
            </Grid>
            <Grid item sm={6} xs={12}>
              <Field
                component={TextField}
                name="ipiNameNumber"
                label="Publisher IPI Name Number"
                fullWidth
              />
            </Grid>
            <Grid item sm={6} xs={12}>
              <Field
                component={TextField}
                name="email"
                label="Email Address"
                fullWidth
                inputProps={{
                  form: {
                    autocomplete: "off",
                  },
                }}
              />
            </Grid>
            <Grid item sm={6} xs={12}>
              <Field
                component={TextField}
                name="firstName"
                label="First Name"
                fullWidth
              />
            </Grid>
            <Grid item sm={6} xs={12}>
              <Field
                component={TextField}
                name="lastName"
                label="Last Name"
                fullWidth
              />
            </Grid>
            <Grid item xs={12}>
              <Box pt={3}>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  className={classes.button}
                  disabled={isSubmitting || hasEmptyValues(values)}
                >
                  Request Sign Up
                </Button>
              </Box>
            </Grid>
            <Grid item xs={12}>
              <Button
                color="primary"
                variant="outlined"
                className={classes.button}
                onClick={handleSignIn}
              >
                Sign In
              </Button>
            </Grid>
          </Grid>
        )}
      </Formik>
    </Container>
  );
};
