import axios from "axios";
import { Store } from "redux";
import { AppState } from "./App.reducer";
import { push } from "connected-react-router";
import { paths } from "./utils/common";
import { signOutSuccessAction } from "./components/pages/SignIn/actions";
import { silentAlertsAction } from "./components/pages/Alerts/actions";
import Promise from "promise-polyfill";

export const createAxiosInterceptor = (store: Store<AppState>) => {
  axios.interceptors.request.use((config) => {
    const apiKeyWeb = store.getState().settings.apiKeyWeb;
    if (apiKeyWeb) {
      config.headers["x-api-key"] = apiKeyWeb;
    }
    return config;
  });
};

