import {
  DELETE_IPI_SUCCESS,
  FETCH_INTEGRATED_SOCIETIES,
  FETCH_INTEGRATED_SOCIETIES_FAILURE,
  FETCH_INTEGRATED_SOCIETIES_SUCCESS,
  FETCH_IPI_LIST,
  FETCH_IPI_LIST_FAILURE,
  FETCH_IPI_LIST_SUCCESS,
  FETCH_SOCIETIES,
  FETCH_SOCIETIES_FAILURE,
  FETCH_SOCIETIES_SUCCESS,
  IpiActionTypes,
  OPEN_IPI_SELECTION_MENU,
  PATCH_IPI_SUCCESS,
  POST_ADD_IPI_MANUALLY,
  POST_ADD_IPI_MANUALLY_FAILURE,
  POST_ADD_IPI_MANUALLY_SUCCESS,
  POST_ADD_IPI_V1,
  POST_ADD_IPI_V1_FAILURE,
  POST_ADD_IPI_V1_SUCCESS,
  POST_ADD_IPI_V2,
  POST_ADD_IPI_V2_FAILURE,
  POST_ADD_IPI_V2_SUCCESS,
} from "./actions.types";
import { _IpiLinks, _SocietyLinks, Ipi, Society } from "../../../utils/models";
import { LoadingState } from "../../../types/enums";

export interface IpiState {
  ipiList: Ipi[];
  integratedSocieties: Society[];
  societies: Society[];
  linksIpi?: _IpiLinks;
  linksSociety?: _SocietyLinks;
  ipiListLoadingState: LoadingState;
  integratedSocietiesLoadingState: LoadingState;
  societiesLoadingState: LoadingState;
  addIpiManuallyLoadingState: LoadingState;
  addIpiV1LoadingState: LoadingState;
  addIpiV2LoadingState: LoadingState;
  openIpiSelectionMenu: boolean;
}

export const ipiInitialState: IpiState = {
  ipiList: [],
  integratedSocieties: [],
  societies: [],
  integratedSocietiesLoadingState: LoadingState.PRISTINE,
  societiesLoadingState: LoadingState.PRISTINE,
  ipiListLoadingState: LoadingState.PRISTINE,
  addIpiManuallyLoadingState: LoadingState.PRISTINE,
  addIpiV1LoadingState: LoadingState.PRISTINE,
  addIpiV2LoadingState: LoadingState.PRISTINE,
  openIpiSelectionMenu: false,
};

export const ipiReducer = (
  state: IpiState = ipiInitialState,
  action: IpiActionTypes
): IpiState => {
  switch (action.type) {
    case FETCH_IPI_LIST: {
      return { ...state, ipiListLoadingState: LoadingState.FETCHING };
    }

    case FETCH_IPI_LIST_SUCCESS: {
      return {
        ...state,
        ipiList: action.ipiList,
        linksIpi: action.links,
        ipiListLoadingState: LoadingState.FETCHED,
      };
    }

    case FETCH_IPI_LIST_FAILURE: {
      return { ...state, ipiListLoadingState: LoadingState.ERROR };
    }

    case FETCH_SOCIETIES: {
      return {
        ...state,
        societiesLoadingState: LoadingState.FETCHING,
      };
    }

    case FETCH_SOCIETIES_SUCCESS: {
      return {
        ...state,
        societies: action.societies,
        societiesLoadingState: LoadingState.FETCHED,
      };
    }

    case FETCH_SOCIETIES_FAILURE: {
      return {
        ...state,
        societiesLoadingState: LoadingState.ERROR,
      };
    }

    case FETCH_INTEGRATED_SOCIETIES: {
      return {
        ...state,
        integratedSocietiesLoadingState: LoadingState.FETCHING,
      };
    }

    case FETCH_INTEGRATED_SOCIETIES_SUCCESS: {
      return {
        ...state,
        integratedSocietiesLoadingState: LoadingState.FETCHED,
        integratedSocieties: action.societies,
        linksSociety: action.links,
      };
    }

    case FETCH_INTEGRATED_SOCIETIES_FAILURE: {
      return {
        ...state,
        integratedSocietiesLoadingState: LoadingState.ERROR,
      };
    }

    case PATCH_IPI_SUCCESS: {
      return {
        ...state,
        ipiList: state.ipiList.map((el: Ipi) =>
          el.ipiNameNumber === action.ipi.ipiNameNumber
            ? { ...el, ...action.ipi }
            : { ...el, default: false }
        ),
      };
    }

    case POST_ADD_IPI_MANUALLY: {
      return { ...state, addIpiManuallyLoadingState: LoadingState.FETCHING };
    }

    case POST_ADD_IPI_MANUALLY_SUCCESS: {
      const index = state.ipiList.findIndex(
        (el) => el.ipiNameNumber === action.ipi.ipiNameNumber
      );
      const isNewDefault = action.ipi.default;
      const ipiList = isNewDefault
        ? state.ipiList.map((el) => ({ ...el, default: false }))
        : [...state.ipiList];

      return {
        ...state,
        addIpiManuallyLoadingState: LoadingState.FETCHED,
        ipiList:
          index !== -1
            ? [
                ...ipiList.slice(0, index),
                action.ipi,
                ...ipiList.slice(index + 1),
              ]
            : [action.ipi, ...ipiList],
      };
    }

    case POST_ADD_IPI_MANUALLY_FAILURE: {
      return { ...state, addIpiManuallyLoadingState: LoadingState.ERROR };
    }

    case DELETE_IPI_SUCCESS: {
      const ipiList = state.ipiList.filter(
        (ipi) => ipi.ipiReference !== action.ipiGuid
      );
      return {
        ...state,
        ipiList,
      };
    }

    case POST_ADD_IPI_V1: {
      return { ...state, addIpiV1LoadingState: LoadingState.FETCHING };
    }

    case POST_ADD_IPI_V1_SUCCESS: {
      return { ...state, addIpiV1LoadingState: LoadingState.FETCHED };
    }

    case POST_ADD_IPI_V1_FAILURE: {
      return { ...state, addIpiV1LoadingState: LoadingState.ERROR };
    }

    case POST_ADD_IPI_V2: {
      return { ...state, addIpiV2LoadingState: LoadingState.FETCHING };
    }

    case POST_ADD_IPI_V2_SUCCESS: {
      return { ...state, addIpiV2LoadingState: LoadingState.FETCHED };
    }

    case POST_ADD_IPI_V2_FAILURE: {
      return { ...state, addIpiV2LoadingState: LoadingState.ERROR };
    }

    case OPEN_IPI_SELECTION_MENU: {
      return { ...state, openIpiSelectionMenu: action.open };
    }

    default:
      return state;
  }
};
