import React, { useState } from "react";
import { Form, Formik, FormikErrors, FormikHelpers, FormikState } from "formik";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import {
  Box,
  Button,
  createStyles,
  Grid,
  makeStyles,
  Paper,
  Theme,
} from "@material-ui/core";
import { useHistory } from "react-router";
import { agreementValidationSchema } from "./component.validator";
import {
  _AgreementsLinks,
  Agreement,
  AgreementMember,
  AgreementTerm,
  BootstrapAccount,
  BootstrapCompany,
} from "../../../utils/models";
import {
  AgreementNavigationTabs,
  ApiErrorCodeMessages,
  ApiErrorCodeTypes,
  IdentityType,
  SalesOrManufacture,
  UsaLicenseIndicatorType,
} from "../../../types/enums";
import {
  patchAgreementEffect,
  postAgreementEffect,
  registerAgreementEffect,
} from "../../pages/AgreementRegistration/effects";
import {
  alertAgreementRegisterFailure,
  alertAgreementRegisterSuccess,
  alertAgreementSaveSuccess,
  buildInitialValues,
  extractDirtyAgreementAttrs,
  getOwnerPartyRole,
  getPermissions,
  getSocAdminPermissions,
  noPermissions,
  openErrorTab,
} from "./utils";
import { createTermWithShare } from "../../atoms/FormikAddAgreementTerm/utils";
import {
  isDevelopment,
  mapValuesDeep,
  preventEnterOnKeyDown,
  scrollToFormErrors,
} from "../../../utils/helpers";
import isEmpty from "lodash/isEmpty";
import Promise from "promise-polyfill";
import { paths } from "../../../utils/common";
import { DialogAgreementSummary } from "../DialogAgreementSummary";
import { LeavingGuard } from "../LeavingGuard";
import { TabsNavigation } from "../../molecules/TabsNavigation";
import { TabPartyShares } from "../TabPartyShares";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { InputFieldAppBar } from "../../atoms/InputFieldAppBar";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { TabGeneral } from "../TabGeneral";
import { addAlertAction } from "../../pages/Alerts/actions";
import { AlertType } from "../../pages/Alerts/actions.types";
import { handleError } from "../../../utils/errorHandler";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    form: {
      height: "100%",
      display: "flex",
      flexDirection: "column",
    },
    appBarContent: {
      width: "100%",
      marginTop: theme.spacing(2),
      display: "inline-flex",
      alignItems: "baseline",
      justifyContent: "space-between",
      transition: theme.transitions.create(["padding"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    content: {
      position: "relative",
      height: "100%",
      overflow: "auto",
    },
    saveButton: {
      marginLeft: theme.spacing(3),
    },
    submitButton: {
      marginLeft: theme.spacing(1),
    },
    buttonsContainer: {
      display: "flex",
      alignItems: "flex-end",
      justifyContent: "flex-end",
      marginBottom: theme.spacing(-1),
      marginLeft: "auto",
    },
    partyShareTab: {
      overflowX: "hidden",
    },
  })
);

export interface FormAgreementProps {
  dispatch: AppDispatch;
  dispatchEffect: AppDispatchEffect;
  account: BootstrapAccount;
  company: BootstrapCompany;
  agreement?: Agreement;
  breadCrumb?: string;
  agreementRevisionEnabled?: boolean;
  isSocAdmin?: boolean;
  agreementsListLinks: _AgreementsLinks;
}

export interface AgreementFormValues {
  title: string;
  submitterAgreementNumbers: string[];
  libraryMusic?: boolean;
  usaLicenseIndicator?: UsaLicenseIndicatorType;
  bulkTransferWorks?: boolean;
  agreementType: string;
  salesOrManufacture?: SalesOrManufacture;
  recordingPrefix: string;
  assignor?: AgreementMember;
  assignee?: AgreementMember;
  terms: AgreementTerm[];
}

export const FormAgreement: React.FC<FormAgreementProps> = ({
  dispatch,
  dispatchEffect,
  company,
  agreement,
  breadCrumb = "New",
  account,
  isSocAdmin = false,
  agreementsListLinks,
}) => {
  const classes = useStyles();
  const history = useHistory();
  const [activeTab, setActiveTab] = useState<AgreementNavigationTabs>(
    AgreementNavigationTabs.PARTY_SHARES
  );
  const [submitVariant, setSubmitVariant] = useState<"save" | "register">();
  const [summaryDialogOpen, setSummaryDialogOpen] = useState(false);
  const handleSave = () => setSubmitVariant("save");
  const handleSubmit = () => setSubmitVariant("register");
  const permissions = isSocAdmin
    ? getSocAdminPermissions()
    : getPermissions(agreement);

  const initialValues: AgreementFormValues = {
    title: "",
    submitterAgreementNumbers: [""],
    recordingPrefix: "",
    libraryMusic: false,
    bulkTransferWorks: true,
    usaLicenseIndicator: UsaLicenseIndicatorType.NONE,
    agreementType: "general",
    salesOrManufacture: SalesOrManufacture.SALES,
    assignor: {
      type: IdentityType.PUBLISHER,
      ipiNameNumber: Number(company.ipiNameNumber),
      displayName: company.name,
      permissions: {
        owner: true,
        read: true,
        write: true,
      },
    },
    terms: [createTermWithShare()],
  };

  const createOrUpdateAgreement = async (values: AgreementFormValues) => {
    let currentAgreement = agreement;
    try {
      if (!agreement?.guid) {
        currentAgreement = await dispatchEffect(
          postAgreementEffect({
            title: values.title,
            role: getOwnerPartyRole(values),
          })
        );
      }
      if (currentAgreement) {
        const dirtyAttributes = extractDirtyAgreementAttrs(
          currentAgreement,
          values
        );
        currentAgreement = await dispatchEffect(
          patchAgreementEffect({
            guid: currentAgreement.guid,
            patch: dirtyAttributes,
          })
        );
      }
      return currentAgreement;
    } catch (agr) {
      throw agr;
    }
  };

  const executePreSubmitValidation = async (
    values: AgreementFormValues,
    validateForm: (values?: any) => Promise<FormikErrors<AgreementFormValues>>
  ) => {
    const errors = await validateForm();
    let customErrors = {};
    if (!values.assignee || !values.assignor) {
      dispatch(
        addAlertAction(AlertType.ERROR, ApiErrorCodeMessages.MISSING_PARTY)
      );
      customErrors = {
        [ApiErrorCodeTypes.MISSING_PARTY]: ApiErrorCodeMessages.MISSING_PARTY,
      };
    }
    return { ...errors, ...customErrors };
  };

  const leaveGuardPromise = (
    validateForm: (values?: any) => Promise<FormikErrors<AgreementFormValues>>,
    setFieldTouched: (
      field: string,
      isTouched?: boolean | undefined,
      shouldValidate?: boolean | undefined
    ) => void,
    values: AgreementFormValues,
    resetForm: (nextState?: Partial<FormikState<AgreementFormValues>>) => void
  ) => {
    return new Promise<void>(async (resolve, reject) => {
      const errors = await validateForm();
      if (!isEmpty(errors)) {
        Object.keys(errors).forEach((key) => {
          setFieldTouched(key);
        });
        reject();
      } else {
        try {
          await handleOnSubmit(
            values,
            { resetForm } as FormikHelpers<AgreementFormValues>,
            true
          );
          resolve();
        } catch (e) {
          reject();
        }
      }
    });
  };

  const handleAgreementRedirect = (guid: string) => {
    history.push({
      pathname: `${paths.AGREEMENTS_REGISTER}/${guid}`,
      search: history.location.search,
    });
  };

  const handleAgreementRegister = async (
    agreementGuid: string,
    forLeavingGuard?: boolean
  ) => {
    try {
      await dispatchEffect(registerAgreementEffect(agreementGuid));
      dispatch(alertAgreementRegisterSuccess);
      if (!forLeavingGuard) {
        history.push(paths.AGREEMENTS_LIST);
      }
    } catch (e) {
      const error = handleError(e.response, "agreement");
      dispatch(alertAgreementRegisterFailure(error.message));
      if (!forLeavingGuard) {
        handleAgreementRedirect(agreementGuid);
      }
    }
  };

  const handleOnSubmit = async (
    values: AgreementFormValues,
    { resetForm }: FormikHelpers<AgreementFormValues>,
    forLeavingGuard?: boolean
  ) => {
    try {
      const newAgreement = await createOrUpdateAgreement(values);
      if (newAgreement && submitVariant === "register") {
        return handleAgreementRegister(newAgreement.guid, forLeavingGuard);
      } else {
        dispatch(alertAgreementSaveSuccess);
        if (!forLeavingGuard && newAgreement) {
          handleAgreementRedirect(newAgreement.guid);
          resetForm({ values: buildInitialValues(newAgreement) });
        }
      }
    } catch (e) {
      throw agreement;
    }
  };
  return (
    <Formik
      initialValues={
        agreement?.guid ? buildInitialValues(agreement) : initialValues
      }
      validationSchema={agreementValidationSchema}
      onSubmit={handleOnSubmit}
    >
      {({
        isSubmitting,
        values,
        dirty,
        validateForm,
        setFieldTouched,
        resetForm,
        submitForm,
        setTouched,
        errors,
      }) => (
        <Form
          className={classes.form}
          onKeyDown={preventEnterOnKeyDown}
          data-testid="agreement-form"
        >
          <ContainerAppBar
            dispatch={dispatch}
            parentTitle="Agreements"
            parentTo={paths.AGREEMENTS_LIST}
            title={breadCrumb}
            secondaryContent={
              <Box pt={2} pb={0}>
                <Grid container>
                  <Grid item xs={9}>
                    <InputFieldAppBar
                      placeholder="New Agreement"
                      readonly={!permissions.updateAgreement}
                      name="title"
                      data-testid="agreement-title-input"
                    />
                  </Grid>
                  <Box className={classes.appBarContent}>
                    <TabsNavigation<AgreementNavigationTabs>
                      activeTab={activeTab}
                      setActiveTab={setActiveTab}
                      addToUrl
                      tabs={[
                        {
                          label: "Party Shares",
                          value: AgreementNavigationTabs.PARTY_SHARES,
                        },
                        {
                          label: "General",
                          value: AgreementNavigationTabs.GENERAL,
                        },
                        {
                          label: "Submissions",
                          value: AgreementNavigationTabs.SUBMISSIONS,
                          disable:
                            !isDevelopment ||
                            agreement?.status === "DRAFT" ||
                            !agreement,
                        },
                      ]}
                    />
                    {(permissions.submit || permissions.updateAgreement) && (
                      <Box display="inline-flex">
                        <Button
                          color="inherit"
                          onClick={() => {
                            history.push(paths.AGREEMENTS);
                          }}
                        >
                          Cancel
                        </Button>
                        <ButtonSubmit
                          data-testid="save-agreement-button"
                          variant="text"
                          className={classes.saveButton}
                          submitting={submitVariant === "save" && isSubmitting}
                          disabled={
                            isSubmitting ||
                            !permissions.updateAgreement ||
                            !dirty
                          }
                          onClick={() => {
                            handleSave();
                            openErrorTab(errors, setActiveTab);
                            scrollToFormErrors(errors);
                          }}
                        >
                          Save
                        </ButtonSubmit>
                        <Button
                          variant="contained"
                          color="primary"
                          className={classes.submitButton}
                          disabled={
                            isSubmitting ||
                            (agreement?.guid ? !permissions.submit : false)
                          }
                          onClick={async () => {
                            handleSubmit();
                            const errors = await executePreSubmitValidation(
                              values,
                              validateForm
                            );
                            openErrorTab(errors, setActiveTab);
                            scrollToFormErrors(errors);
                            setTouched(mapValuesDeep(values, () => true));
                            if (isEmpty(errors)) {
                              setSummaryDialogOpen(true);
                            }
                          }}
                        >
                          Submit
                        </Button>
                      </Box>
                    )}
                  </Box>
                </Grid>
              </Box>
            }
          />
          <Paper
            square
            className={classes.content}
            component={
              activeTab === "submissions"
                ? ({ children }) => (
                    <Box className={classes.content}>{children}</Box>
                  )
                : undefined
            }
          >
            <Box
              display={activeTab === "partyShares" ? "flex" : "none"}
              height={1}
              pt={4.5}
              className={classes.partyShareTab}
            >
              <TabPartyShares
                agreement={agreement}
                dispatch={dispatch}
                dispatchEffect={dispatchEffect}
                profileRef={account.userGuid}
                formValues={values}
                permissions={isSubmitting ? noPermissions : permissions}
                agreementsListLinks={agreementsListLinks}
              />
            </Box>
            <Box display={activeTab === "general" ? "flex" : "none"} height={1}>
              <TabGeneral
                formValues={values}
                disabled={!permissions.updateAgreement || isSubmitting}
              />
            </Box>
            {/*TODO:: Update when BE is implemented */}
            {/*{isDevelopment && (*/}
            {/*  <Box*/}
            {/*    display={activeTab === "submissions" ? "flex" : "none"}*/}
            {/*    height={1}*/}
            {/*  >*/}
            {/*    <TabSubmissionsAgreements*/}
            {/*      agreementRegistrations={[*/}
            {/*        {*/}
            {/*          ...workRegistrationWithSubmissions,*/}
            {/*          submissions: [workRegistrationSubmission],*/}
            {/*        },*/}
            {/*      ]}*/}
            {/*      dispatchEffect={dispatchEffect}*/}
            {/*    />*/}
            {/*  </Box>*/}
            {/*)}*/}
          </Paper>
          <DialogAgreementSummary
            open={summaryDialogOpen}
            values={values}
            onCancel={() => setSummaryDialogOpen(false)}
            isSubmitting={isSubmitting}
            onSubmit={() => {
              handleSubmit();
              return submitForm();
            }}
          />
          <LeavingGuard
            when={dirty && !isSubmitting}
            navigate={(path) => history.push(path)}
            shouldBlockNavigation={(location) => {
              // Ignore search params and only check pathname
              return location.pathname !== history.location.pathname;
            }}
            onSubmitPromise={() =>
              leaveGuardPromise(
                validateForm,
                setFieldTouched,
                values,
                resetForm
              )
            }
          />
        </Form>
      )}
    </Formik>
  );
};
