export const TOGGLE_REVISION = "TOGGLE_REVISION";
export const TOGGLE_REVISION_SUCCESS = "TOGGLE_REVISION_SUCCESS";
export const TOGGLE_REVISION_SUCCESS_FAILURE = "TOGGLE_REVISION_FAILURE";
export const SET_API_KEY_WEB = "SET_API_KEY_WEB";

export interface ToggleRevisionAction {
  type: typeof TOGGLE_REVISION;
}

export interface ToggleRevisionSuccessAction {
  type: typeof TOGGLE_REVISION_SUCCESS;
  revision: {
    work: boolean;
    agreement: boolean;
  };
}

export interface ToggleRevisionFailureAction {
  type: typeof TOGGLE_REVISION_SUCCESS_FAILURE;
}

export interface SetApiKeyWebAction {
  type: typeof SET_API_KEY_WEB;
  apiKeyWeb: string;
}

export type SettingsActionTypes =
  | ToggleRevisionAction
  | ToggleRevisionSuccessAction
  | ToggleRevisionFailureAction
  | SetApiKeyWebAction;
