import React from "react";
import {
  Badge,
  Box,
  createStyles,
  Drawer,
  makeStyles,
  Theme,
} from "@material-ui/core";
import { AppDispatch, AppDispatchEffect, AppState } from "../../../App.reducer";
import { sidebarInitialState } from "../../pages/Sidebar/reducer";
import { sidebarToggleAction } from "../../pages/Sidebar/actions";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import { iOS } from "../../../utils/helpers";
import {
  getAccountSelector,
  getAgreementsListFetchLinkSelector,
  getFetchEmployeesLinkSelector,
  getNotificationsListFetchLinkSelector,
  getSettingsFetchLinkSelector,
  getUserIsSocAdminSelector,
  getWorksListFetchLinkSelector,
  isProfilePageAvailableSelector,
} from "../../pages/SignIn/selectors";
import {
  getIsSidebarOpenSelector,
  getSidebarVariantSelector,
} from "../../pages/Sidebar/selectors";
import { getCurrentThemeSelector } from "../../pages/Theme/selectors";
import { getNotificationsCount } from "../../pages/Notifications/selectors";
import { paths } from "../../../utils/common";
import { CardUser } from "../../atoms/CardUser";
import { ListNavigation } from "../../molecules/ListNavigation";
import { SidebarPoweredBy } from "../../molecules/SidebarPoweredBy";
import { ThemeInverse } from "../../atoms/ThemeInverse";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    drawer: {
      backgroundColor: theme.palette.background.default,
      maxWidth: sidebarInitialState.sidebarWidth,
      width: "100%",
    },
    sidebar: {
      display: "flex",
      flexDirection: "column",
      flexGrow: 1,
      padding: theme.spacing(3, 1.5, 2, 1.5),
    },
    badge: {
      position: "initial",
      transform: "initial",
    },
    lowerContent: {
      display: "flex",
      flexGrow: 1,
      alignItems: "flex-end",
    },
  })
);

export const navigationItems = [
  {
    title: "Works",
    pathname: paths.WORKS,
    access: getWorksListFetchLinkSelector,
  },
  {
    title: "Agreements",
    pathname: paths.AGREEMENTS,
    access: getAgreementsListFetchLinkSelector,
  },
  {
    title: "Profile",
    pathname: paths.PROFILE,
    access: isProfilePageAvailableSelector,
  },
  {
    title: "Employees",
    pathname: paths.EMPLOYEES,
    access: getFetchEmployeesLinkSelector,
  },
  {
    title: "Settings",
    pathname: paths.SETTINGS,
    access: getSettingsFetchLinkSelector,
  },
  {
    title: "Notifications",
    pathname: paths.NOTIFICATIONS,
    access: getNotificationsListFetchLinkSelector,
  },
];

interface SidebarProps {
  dispatch: AppDispatch;
  dispatchEffect: AppDispatchEffect;
  pathname: string;
  state: AppState;
}

export const SidebarComponent: React.FC<SidebarProps> = ({
  dispatch,
  dispatchEffect,
  pathname,
  state,
}) => {
  const classes = useStyles();
  const isSidebarOpen = getIsSidebarOpenSelector(state);
  const sidebarVariant = getSidebarVariantSelector(state);
  const theme = getCurrentThemeSelector(state);
  const isSocAdmin = getUserIsSocAdminSelector(state);
  const notificationsCount = getNotificationsCount(state);
  const account = getAccountSelector(state);

  const filteredNavItems = navigationItems.filter((el) =>
    el.access ? Boolean(el.access(state)) : true
  );

  const content = (
    <React.Fragment>
      {account && (
        <CardUser
          account={account}
          dispatchEffect={dispatchEffect}
          logo={theme.logo}
          isSocAdmin={isSocAdmin}
        />
      )}
      <div className={classes.sidebar} role="presentation">
        <ListNavigation
          navigationItems={filteredNavItems}
          pathname={pathname}
          secondaryContent={({ pathname }) =>
            pathname === paths.NOTIFICATIONS &&
            Boolean(notificationsCount) && (
              <Box pl={1}>
                <Badge
                  color="primary"
                  classes={{ badge: classes.badge }}
                  badgeContent={notificationsCount}
                  invisible={!notificationsCount}
                  anchorOrigin={{
                    vertical: "top",
                    horizontal: "left",
                  }}
                />
              </Box>
            )
          }
        />
        <Box className={classes.lowerContent}>
          <ThemeInverse>
            <SidebarPoweredBy />
          </ThemeInverse>
        </Box>
      </div>
    </React.Fragment>
  );

  return sidebarVariant === "persistent" ? (
    <Drawer
      classes={{ paper: classes.drawer }}
      open={isSidebarOpen}
      variant="persistent"
      onClose={() => dispatch(sidebarToggleAction())}
    >
      {content}
    </Drawer>
  ) : (
    <SwipeableDrawer
      classes={{ paper: classes.drawer }}
      open={isSidebarOpen}
      disableDiscovery={iOS}
      disableBackdropTransition={!iOS}
      onOpen={() => dispatch(sidebarToggleAction())}
      onClose={() => {
        dispatch(sidebarToggleAction());
      }}
    >
      {content}
    </SwipeableDrawer>
  );
};
