import React, { useState } from "react";
import { FieldProps } from "formik";
import { ChromePicker } from "react-color";
import {
  Box,
  createStyles,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    themeColorSelectContainer: {
      display: "flex",
      cursor: "pointer",
      minWidth: 220,
    },
    colorDisplay: {
      width: 40,
      height: 40,
      borderRadius: "50%",
      backgroundColor: ({ color }: { color: string }) => color,
      border: `1px solid ${theme.palette.grey[500]}`,
    },

    colorCode: {
      color: theme.palette.text.secondary,
      textTransform: "uppercase",
    },
    colorPicker: {
      position: "absolute",
      top: 0,
      zIndex: 10,
      marginLeft: 180,
    },
    cover: {
      position: "fixed",
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
  })
);

interface FormikThemeColorSelectProps {
  label: string;
  onClick: () => void;
  className?: string;
  disableAlpha?: boolean;
}

export const FormikThemeColorSelect: React.FC<
  FormikThemeColorSelectProps & FieldProps
> = ({
  field: { name, value },
  form: { setFieldValue },
  label,
  className = "",
  disableAlpha = false,
}) => {
  const classes = useStyles({ color: value });
  const [pickerOpen, setPickerOpen] = useState(false);
  return (
    <React.Fragment>
      <Box
        data-testid="open-color-picker"
        className={`${classes.themeColorSelectContainer} ${className}`}
        onClick={() => setPickerOpen(true)}
      >
        <div className={classes.colorDisplay} />
        <Box display="flex" flexDirection="column" pl={2.25}>
          <Typography variant="body2">{label}</Typography>
          <Typography variant="body2" className={classes.colorCode}>
            {value}
          </Typography>
        </Box>
        {pickerOpen && (
          <Box className={classes.colorPicker} data-testid="color-picker">
            <ChromePicker
              color={value}
              disableAlpha={disableAlpha}
              onChangeComplete={(color) => {
                const {
                  rgb: { r, g, b, a },
                } = color;
                const rgba = `rgba(${r}, ${g}, ${b}, ${a})`;
                setFieldValue(name, a === 1 ? color.hex : rgba);
              }}
              styles={{
                default: {
                  picker: { width: 190, height: "100%" },
                  saturation: { paddingBottom: "45%" },
                },
              }}
            />
          </Box>
        )}
      </Box>
      {pickerOpen && (
        <div className={classes.cover} onClick={() => setPickerOpen(false)} />
      )}
    </React.Fragment>
  );
};
