import { Button, Grid, ListItem, Typography, useMediaQuery, useTheme } from "@material-ui/core";
import { format } from "date-fns";
import React, { useEffect, useRef, useState } from "react";
import * as H from "history";
import { TemplateListItem } from "../../../utils/models";
import { paths } from "../../../utils/common";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ListItemRippleDisabler } from "../../atoms/ListItemRippleDisabler";
import { useTemplatesListStyles } from "../ListTemplates";
import { ActionMenu } from "../ActionMenu";
import { TooltipMap } from "../../atoms/TooltipMap";
import { creatorNameRefsData, showCreatorNameTooltipData, updateCreatorNameTooltipData } from "./component.utils";
import { isElementOverflowed } from "../../../utils/helpers";
import { Icon } from "../../atoms/Icon";

interface HeaderTemplateProps {
  template: TemplateListItem;
  history: H.History;
  onCreateFrom: (guid: string) => void;
  handleDelete: (guid: string) => void;
  handleTemplateCloning: (guid: string) => void;
  showCreateFromButton: boolean;
  canCloneTemplate: boolean;
  canDeleteTemplate: boolean;
  windowWidth?: number;
}

export const HeaderTemplate: React.FC<HeaderTemplateProps> = ({
  template,
  history,
  onCreateFrom,
  handleDelete,
  showCreateFromButton,
  canCloneTemplate,
  handleTemplateCloning,
  canDeleteTemplate,
  windowWidth,
}) => {
  const classes = useTemplatesListStyles();
  const [disableRipple, setDisableRipple] = useState(false);
  const theme = useTheme();
  const breakpointXs = useMediaQuery(theme.breakpoints.down("xs"));
  const [showTitleTooltip, setShowTitleTooltip] = useState(false);
  const [showCreatorNameTooltip, setShowCreatorNameTooltip] = useState(
    showCreatorNameTooltipData(template)
  );
  const titleRef = useRef<HTMLSpanElement>();
  const creatorNameRefs = creatorNameRefsData(template);
  const stopPropagation = (e: React.MouseEvent<HTMLSpanElement>) =>
    e.stopPropagation();

  useEffect(() => {
    const isTitleOverflowed = isElementOverflowed(titleRef);
    setShowTitleTooltip(isTitleOverflowed);
  }, [windowWidth, titleRef]);

  useEffect(() => {
    const updatedState = updateCreatorNameTooltipData(
      showCreatorNameTooltip,
      creatorNameRefs
    );
    setShowCreatorNameTooltip(updatedState);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [windowWidth]);

  return (
    <ListItem
      disableRipple={disableRipple}
      button
      disableGutters
      className={classes.listRow}
      onClick={() => {
        history.push(`${paths.WORK_TEMPLATE}/${template.guid}`);
      }}
    >
      <ContainerListItem
        leftOffset={breakpointXs ? 5 : 9}
        className={classes.gridRow}
      >
        <Grid item className={classes.titleColumn}>
          <Typography noWrap innerRef={titleRef}>
            {showTitleTooltip ? (
              <TooltipMap variant="listItems" tooltipText={template.title}>
                {template.title}
              </TooltipMap>
            ) : (
              template.title
            )}
          </Typography>
        </Grid>
        <Grid item className={classes.creatorsColumn}>
          {template.creators?.map((creator) => (
            <Typography
              key={creator.ipiNameNumber}
              color="textSecondary"
              noWrap
              ref={creatorNameRefs[creator.ipiNameNumber]}
            >
              {showCreatorNameTooltip[creator.ipiNameNumber] ? (
                <TooltipMap
                  variant="listItems"
                  tooltipText={creator.displayName}
                >
                  {creator.displayName}
                </TooltipMap>
              ) : (
                creator.displayName
              )}
            </Typography>
          ))}
        </Grid>
        <Grid item className={classes.lastUpdatedColumn}>
          <Typography color="textSecondary" noWrap>
            {template?.updated &&
              format(new Date(template.updated.iso), "MMM dd, yyyy")}
          </Typography>
        </Grid>
        <Grid item className={classes.buttonColumn}>
          <ListItemRippleDisabler
            setDisableRipple={setDisableRipple}
            onClick={stopPropagation}
          >
            {showCreateFromButton && (
              <Button
                variant="outlined"
                className={classes.button}
                size="small"
                onClick={() => onCreateFrom(template.guid)}
              >
                Create From
              </Button>
            )}
          </ListItemRippleDisabler>
        </Grid>

        <Grid item className={classes.optionsColumn}>
          <ListItemRippleDisabler
            setDisableRipple={setDisableRipple}
            onClick={stopPropagation}
          >
            <ActionMenu
              items={[
                ...(canCloneTemplate
                  ? [
                      {
                        label: "Duplicate",
                        onClick: () => handleTemplateCloning(template.guid),
                      },
                    ]
                  : []),
                ...(canDeleteTemplate
                  ? [
                      {
                        label: "Delete",
                        onClick: () => handleDelete(template.guid),
                      },
                    ]
                  : []),
              ]}
            >
              <Icon name="listItemOptions" />
            </ActionMenu>
          </ListItemRippleDisabler>
        </Grid>
      </ContainerListItem>
    </ListItem>
  );
};
