import React from "react";
import { createStyles, Grid, GridProps, makeStyles } from "@material-ui/core";
import clsx from "clsx";

const useStyles = makeStyles(() =>
  createStyles({
    cell: {
      minWidth: 50,
      minHeight: 40,
      textAlign: "center",
      alignSelf: "center",
    },
  })
);

export const CellAgreementOptions: React.FC<GridProps> = ({
  children,
  className,
}) => {
  const classes = useStyles();

  return (
    <Grid item className={clsx(classes.cell, className)}>
      {children}
    </Grid>
  );
};
