import React, { useEffect } from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { Box, Paper, useTheme } from "@material-ui/core";
import { fetchNotificationsEffect } from "./effects";
import { LoadingState } from "../../../types/enums";
import { PageNoContent } from "../../molecules/PageNoContent";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { ListNotifications } from "../../organisms/ListNotifications";
import { LoadingScreen } from "../../atoms/LoadingScreen";
import { RouteComponentProps } from "react-router";

export const Notifications: React.FC<RouteComponentProps> = ({ history }) => {
  const theme = useTheme();
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();

  const { notifications, fetchNotificationsLoadingState } = useAppSelector(
    (state) => state.notifications
  );

  useEffect(() => {
    dispatchEffect(fetchNotificationsEffect());
  }, [dispatchEffect]);

  const renderContent = () => {
    if (
      fetchNotificationsLoadingState === LoadingState.PRISTINE ||
      fetchNotificationsLoadingState === LoadingState.FETCHING
    ) {
      return <LoadingScreen />;
    }

    if (
      fetchNotificationsLoadingState === LoadingState.FETCHED &&
      notifications.length === 0
    ) {
      return (
        <PageNoContent
          primaryText="No Notifications"
          secondaryText="You do not have any notifications yet, once you start interacting with Session you'll receive notifications here"
          iconSrc={`${process.env.PUBLIC_URL}/static/media/svg/notifications.svg`}
        />
      );
    }
    return (
      <ListNotifications
        notifications={notifications}
        dispatchEffect={dispatchEffect}
        history={history}
      />
    );
  };

  return (
    <Box component={Paper} height={1} display="flex" flexDirection="column">
      <ContainerAppBar
        dispatch={dispatch}
        title="Notifications"
        secondaryContent={<Box height={`${theme.spacing(12)}px`} />}
      />
      {renderContent()}
    </Box>
  );
};
