import React, { MouseEvent, useRef } from "react";
import { Menu, MenuItem, MenuProps } from "@material-ui/core";
import { Link } from "react-router-dom";
import { TransitionProps } from "@material-ui/core/transitions/transition";
import { AppState } from "../../../App.reducer";
import { Href } from "../../../utils/models";

interface MenuActionProps {
  items: Array<{
    label: string;
    onClick?: (anchor: null | HTMLElement) => void;
    to?: string;
    disabled?: boolean;
    access?: (state: AppState) => Href | undefined;
  }>;
  children: React.ReactNode;
  transitionDuration?: TransitionProps["timeout"] | "auto";
  disabled?: boolean;
  wrapperClassName?: string;
}

export const ActionMenu: React.FC<MenuActionProps & Partial<MenuProps>> = ({
  items,
  transitionDuration,
  disabled,
  children,
  wrapperClassName,
  ...menuProps
}) => {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const handleClick = (event: MouseEvent<HTMLElement>) => {
    !disabled && setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const actionMenuRef = useRef();

  return items.length > 0 ? (
    <React.Fragment>
      {React.cloneElement(
        <div className={wrapperClassName} onClick={handleClick}>
          {children}
        </div>
      )}
      <Menu
        ref={actionMenuRef}
        transitionDuration={transitionDuration}
        keepMounted
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleClose}
        getContentAnchorEl={null}
        anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
        transformOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
        {...menuProps}
      >
        {items.map(({ label, onClick, to, disabled }, index) => (
          <MenuItem
            data-testid={`menu.${index}.${label}`}
            component={to ? Link : MenuItem}
            disabled={disabled}
            to={to || undefined}
            key={`${label}:${index}`}
            onClick={() => {
              onClick && onClick(anchorEl);
              handleClose();
            }}
          >
            {label}
          </MenuItem>
        ))}
      </Menu>
    </React.Fragment>
  ) : (
    <React.Fragment />
  );
};
