import React from "react";
import { FieldProps } from "formik";
import {
  Box,
  createStyles,
  FormControlLabel,
  makeStyles,
  Radio,
  Theme as MaterialUiTheme,
  Typography,
} from "@material-ui/core";
import { Theme } from "../../../utils/models";
import { ButtonThemeHover } from "../../atoms/ButtonThemeHover";
import { ThemePreview } from "../ThemePreview";
import { TooltipMap } from "../../atoms/TooltipMap";
import { LoadingState } from "../../../types/enums";
import { SkeletonThemeOption } from "../SkeletonThemeOption";
import { ThemePreviewStatic } from "../../atoms/ThemePreviewStatic";

const useStyles = makeStyles((theme: MaterialUiTheme) =>
  createStyles({
    root: {
      "&:hover": {
        cursor: "pointer",
      },
    },
    container: {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      width: 160,
      height: 104,
    },
    preview: {
      position: "relative",
      width: 160,
      height: 104,
    },
    radioGroupRoot: {
      display: "flex",
    },
    trashButton: {
      display: "none",
      position: "absolute",
      right: theme.spacing(0.5),
      bottom: theme.spacing(5),
    },
    paletteButton: {
      display: "flex",
      position: "absolute",
      right: theme.spacing(0.5),
      bottom: theme.spacing(0.5),
    },
    defaultThemeThumbnail: {
      cursor: "pointer",
    },
    buttonText: {
      marginLeft: theme.spacing(1),
    },
    previewAvatar: {
      border: `2px solid ${theme.palette.custom.secondary.green}`,
      boxShadow: "none",
      width: 156,
      height: 104,
    },
  })
);

interface FormikThemesListSelectProps {
  index: number;
  themes: Theme[];
  onEditClick: (theme: Theme) => void;
  onDeleteClick: (theme: Theme) => void;
  themesLoadingState: LoadingState;
}

export const FormikThemesListSelect: React.FC<
  FormikThemesListSelectProps & FieldProps
> = ({
  field: { name, value },
  form: { setFieldValue },
  themes,
  onEditClick,
  onDeleteClick,
  themesLoadingState,
}) => {
  const classes = useStyles();

  /* Currently theme editing is disabled and we only display static image.
     Will remove this const and update listing when Figma designs are final and approved. */
  const showStaticOption = true;

  return (
    <Box display="flex" pr={3}>
      {[LoadingState.FETCHED, LoadingState.ERROR].includes(
        themesLoadingState
      ) ? (
        <React.Fragment>
          {showStaticOption ? (
            <Box className={classes.container}>
              <Box className={classes.preview}>
                <ThemePreviewStatic className={classes.previewAvatar} />
              </Box>
              <Box display="flex" alignItems="center" pt={1}>
                <FormControlLabel
                  value="light"
                  control={<Radio checked={true} name={name} color="primary" />}
                  label="Light"
                />
              </Box>
            </Box>
          ) : (
            themes.map((theme) => (
              <Box className={classes.container} key={theme.id}>
                <Box className={classes.preview}>
                  <React.Fragment>
                    <ThemePreview
                      theme={theme}
                      onClick={() => setFieldValue(name, theme)}
                    />
                    {/* Uncomment when delete BE functionality is implemented */}
                    {/* {theme.id !== DEFAULT_THEME_ID && (
                    <ButtonThemeHover
                      onClick={() => onDeleteClick(theme)}
                      className={classes.trashButton}
                    >
                      <TooltipMap to="Delete" disableCursor>
                        <img src="/static/media/svg/trashbin.svg" alt="delete" />
                      </TooltipMap>
                    </ButtonThemeHover>
                  )} */}
                    <ButtonThemeHover
                      onClick={() => onEditClick(theme)}
                      className={classes.paletteButton}
                    >
                      <TooltipMap to="Customize" disableCursor>
                        <Box display="flex">
                          <img
                            src={`${process.env.PUBLIC_URL}/static/media/svg/palette.svg`}
                            alt="edit"
                          />
                          <Typography
                            variant="body2"
                            color="secondary"
                            className={classes.buttonText}
                          >
                            Customise
                          </Typography>
                        </Box>
                      </TooltipMap>
                    </ButtonThemeHover>
                  </React.Fragment>
                </Box>
                <Box display="flex" alignItems="center">
                  <Radio
                    checked={value.id === theme.id}
                    name={name}
                    value={theme}
                    onChange={() => setFieldValue(name, theme)}
                    color="primary"
                  />
                  <Typography variant="body2">{theme.title}</Typography>
                </Box>
              </Box>
            ))
          )}
        </React.Fragment>
      ) : (
        <SkeletonThemeOption />
      )}
    </Box>
  );
};
