export const ADD_ALERT = "ADD_ALERT";
export const REMOVE_ALERT = "REMOVE_ALERT";
export const SILENT_ALERTS = "SILENT_ALERTS";

export enum AlertType {
  SUCCESS = "SUCCESS",
  ERROR = "ERROR",
  WARNING = "WARNING",
  INFO = "INFO",
}

export interface Alert {
  type: AlertType;
  message: string;
}

export interface AddAlertAction {
  type: typeof ADD_ALERT;
  alertType: AlertType;
  message: string;
}

export interface RemoveAlertAction {
  type: typeof REMOVE_ALERT;
  index: number;
}

export interface SilentAlertsAction {
  type: typeof SILENT_ALERTS;
  state: boolean;
}

export type AlertActionTypes =
  | AddAlertAction
  | RemoveAlertAction
  | SilentAlertsAction;
