import React, { useState } from "react";
import {
  Button,
  Checkbox,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControlLabel as MuiFormControlLabel,
} from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import { getDialogAlertCacheKey } from "./utils";
import { Dialog } from "../Dialog";

interface DialogRemoveAgreementShareProps {
  onConfirm: () => void;
  onCancel: () => void;
  profileRef: string;
}

const FormControlLabel = withStyles((theme) => ({
  label: {
    ...theme.typography.body2,
    color: theme.palette.text.secondary,
  },
}))(MuiFormControlLabel);

export const DialogRemoveAgreementShare: React.FC<DialogRemoveAgreementShareProps> = ({
  onConfirm,
  onCancel,
  profileRef,
}) => {
  const [checked, setChecked] = useState(false);
  const handleChecked = () => {
    setChecked(!checked);
  };
  const handleConfirm = () => {
    if (checked) {
      localStorage.setItem(getDialogAlertCacheKey(profileRef), "true");
    }
    onConfirm();
  };
  return (
    <Dialog variant="simple" open={true} onClose={onCancel}>
      <DialogTitle>Are you sure?</DialogTitle>
      <DialogContent>
        <FormControlLabel
          control={
            <Checkbox
              checked={checked}
              onChange={handleChecked}
              color="primary"
            />
          }
          label="Dont show this message again"
        />
      </DialogContent>
      <DialogActions>
        <Button variant="text" color="primary" onClick={onCancel}>
          Cancel
        </Button>
        <Button variant="text" color="primary" onClick={handleConfirm}>
          Remove
        </Button>
      </DialogActions>
    </Dialog>
  );
};
