import React from "react";
import { Grid } from "@material-ui/core";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { InfiniteBorder } from "../../atoms/InfiniteBorder";
import { TooltipMap } from "../../atoms/TooltipMap";
import { useListIpiNumbersStyles } from "../ListIpiNumbers";

export const HeaderListIpiNumbers: React.FC = () => {
  const classes = useListIpiNumbersStyles();

  return (
    <ContainerListItem leftOffset={6}>
      <Grid item className={classes.ipiNumberColumn}>
        <ListColumnTitle>
          <TooltipMap to="IpiNumber"> IPI Number</TooltipMap>
        </ListColumnTitle>
        <InfiniteBorder />
      </Grid>
      <Grid item className={classes.ipiNameColumn}>
        <ListColumnTitle>
          <TooltipMap to="IpiName">IPI Name</TooltipMap>
        </ListColumnTitle>
      </Grid>
      <Grid item className={classes.societyColumn}>
        <ListColumnTitle>Society</ListColumnTitle>
      </Grid>
      <Grid item className={classes.statusColumn}>
        <ListColumnTitle>
          <TooltipMap to="IpiStatus">Status</TooltipMap>
        </ListColumnTitle>
      </Grid>
      <Grid item className={classes.defaultColumn}>
        <ListColumnTitle>
          <TooltipMap to="IpiDefault">Default</TooltipMap>
        </ListColumnTitle>
      </Grid>
    </ContainerListItem>
  );
};
