import React from "react";
import { patchProfileEffect } from "../../pages/SignIn/effects";
import { Field, Form, Formik } from "formik";
import {
  Box,
  Button,
  createStyles,
  Grid,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { TextField } from "formik-material-ui";
import { AppDispatchEffect } from "../../../App.reducer";
import * as yup from "yup";
import { BootstrapAccount } from "../../../utils/models";
import { TooltipMap } from "../../atoms/TooltipMap";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    submitButtonGrid: {
      display: "flex",
      justifyContent: "flex-end",
      alignItems: "flex-end",
      flexGrow: 1,
      padding: theme.spacing(1.5, 1.5, 0, 1.5),
    },
  })
);

interface FormCompanyPersonalDataProps {
  dispatchEffect: AppDispatchEffect;
  profileData: { firstName: string; lastName: string };
}

const personalDataFormValidation = yup.object().shape({
  firstName: yup.string().required("First name is required"),
  lastName: yup.string().required("Last name is required"),
});

export const FormCompanyPersonalData: React.FC<FormCompanyPersonalDataProps> = ({
  dispatchEffect,
  profileData: { firstName, lastName },
}) => {
  const classes = useStyles();

  const hasChangedValues = (form: Partial<BootstrapAccount>) => {
    const getProps = (props: Partial<BootstrapAccount>) => ({
      firstName: props.firstName,
      lastName: props.lastName,
    });

    return (
      JSON.stringify(getProps({ firstName, lastName })) !==
      JSON.stringify(getProps(form))
    );
  };

  return (
    <Formik
      onSubmit={(values) => dispatchEffect(patchProfileEffect(values))}
      validationSchema={personalDataFormValidation}
      initialValues={{ firstName, lastName }}
    >
      {({ isSubmitting, handleSubmit, resetForm, values }) => (
        <Form>
          <Box pt={8} pb={6.5}>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <Typography variant="h6">Name</Typography>
              </Grid>
              <Grid item sm={6} xs={12}>
                <TooltipMap to="FirstNameInput">
                  <Field
                    component={TextField}
                    disabled={isSubmitting}
                    label="First Name"
                    name="firstName"
                    variant="filled"
                    fullWidth
                  />
                </TooltipMap>
              </Grid>
              <Grid item sm={6} xs={12}>
                <TooltipMap to="LastNameInput">
                  <Field
                    component={TextField}
                    label="Last Name"
                    name="lastName"
                    variant="filled"
                    fullWidth
                  />
                </TooltipMap>
              </Grid>
              <Box className={classes.submitButtonGrid}>
                {hasChangedValues(values) && (
                  <Button
                    color="primary"
                    onClick={() => resetForm()}
                    disabled={isSubmitting}
                  >
                    Cancel
                  </Button>
                )}
                <Button
                  type="submit"
                  color="primary"
                  onClick={() => handleSubmit()}
                  disabled={isSubmitting || !hasChangedValues(values)}
                >
                  Update
                </Button>
              </Box>
            </Grid>
          </Box>
        </Form>
      )}
    </Formik>
  );
};
