import { AppDispatch } from "../../../App.reducer";
import { FormikErrors } from "formik";
import { WorkFormValues } from "./component";
import { addAlertAction } from "../../pages/Alerts/actions";
import { AlertType } from "../../pages/Alerts/actions.types";
import { toasts } from "../../../utils/toasts";
import { WorkNavigationTabs } from "../../../types/enums";
import { scrollToFormErrors } from "../../../utils/helpers";

export const validateMissingTitle = (
  dispatch: AppDispatch,
  errors: FormikErrors<WorkFormValues>,
  validationErrors: FormikErrors<WorkFormValues>
) => {
  if (
    !Object.keys(errors).includes("title") &&
    Object.keys(validationErrors).includes("title")
  ) {
    dispatch(
      addAlertAction(
        AlertType.ERROR,
        toasts.error.work.SUBMIT_WORK_WITHOUT_TITLE
      )
    );
  }
};

export const openErrorTab = (
  errors: FormikErrors<WorkFormValues>,
  setActiveTab: (tab: WorkNavigationTabs) => void
) => {
  const {
    creators,
    performanceSplitSum,
    mechanicalSplitSum,
    altTitle,
    duration,
    firstReleaseDate,
    musicalDistributionCategory,
    hasBeenRecorded,
  } = errors;

  const creatorTabError = creators || performanceSplitSum || mechanicalSplitSum;
  const detailTabError =
    altTitle ||
    duration ||
    firstReleaseDate ||
    musicalDistributionCategory ||
    hasBeenRecorded;

  if (creatorTabError) setActiveTab(WorkNavigationTabs.PARTIES);
  if (detailTabError) setActiveTab(WorkNavigationTabs.DETAILS);

  // Need to give some time for tab to open before trying to scroll
  setTimeout(() => {
    scrollToFormErrors(errors);
  }, 100);
};
