import React from "react";
import { createStyles, Grid, GridProps, makeStyles } from "@material-ui/core";
import clsx from "clsx";

const useStyles = makeStyles(() =>
  createStyles({
    cell: {
      maxWidth: 200,
      minWidth: 90,
      width: "100%",
    },
  })
);

export const CellAgreementIpi: React.FC<GridProps> = ({
  children,
  className,
}) => {
  const classes = useStyles();

  return (
    <Grid item className={clsx(classes.cell, className)}>
      {children}
    </Grid>
  );
};
