import React from "react";
import clsx from "clsx";
import {
  createStyles,
  makeStyles,
  Theme,
  Typography,
  TypographyProps,
} from "@material-ui/core";
import match from "autosuggest-highlight/match";
import parse from "autosuggest-highlight/parse";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    primaryText: {
      lineHeight: 1,
    },
    bold: {
      fontWeight: 700,
    },
    multilineText: {
      overflow: "hidden",
      textOverflow: "ellipsis",
      WebkitLineClamp: 2,
      display: "-webkit-box",
      WebkitBoxOrient: "vertical",
    },
  })
);

interface HighlightTextProps {
  multiline?: boolean;
  wrapText?: boolean;
  text: string;
  highlightText: string;
}

export const HighlightText: React.FC<HighlightTextProps & TypographyProps> = ({
  multiline,
  highlightText,
  text,
  wrapText,
  children,
  ...props
}) => {
  const classes = useStyles();

  const matches = match(text, highlightText);
  const parts = parse(text, matches);

  return (
    <Typography
      variant="body1"
      color="primary"
      className={clsx(classes.primaryText, {
        [classes.multilineText]: multiline,
      })}
      noWrap={!wrapText}
      {...props}
    >
      {highlightText
        ? parts.map((part, index) => (
            <span
              key={index}
              className={clsx({ [classes.bold]: part.highlight })}
            >
              {part.text}
            </span>
          ))
        : text}
    </Typography>
  );
};
