import React, { useState } from "react";
import {
  Avatar,
  Box,
  Button,
  ListItem,
  Typography,
  useTheme,
} from "@material-ui/core";
import {
  createMuiTheme,
  createStyles,
  makeStyles,
  Theme,
  ThemeProvider,
} from "@material-ui/core/styles";
import { differenceInMinutes, format, isToday, isYesterday } from "date-fns";
import { AppDispatchEffect } from "../../../App.reducer";
import * as H from "history";
import { Notification } from "../../../utils/models";
import { patchNotificationEffect } from "../../pages/Notifications/effects";
import {
  acceptInviteEffect,
  declineInviteEffect,
} from "../../pages/Works/effects";
import clsx from "clsx";
import { paths } from "../../../utils/common";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ListItemRippleDisabler } from "../../atoms/ListItemRippleDisabler";
import { TableList } from "../../molecules/TableList";
import { HeaderListNotifications } from "../HeaderListNotifications";
import { InfiniteScroll, ListType } from "../InfiniteScroll/component";

export const useListNotificationsStyles = makeStyles((theme: Theme) =>
  createStyles({
    avatarColumn: {
      minWidth: 55,
      paddingRight: theme.spacing(1),
      alignSelf: "center",
    },
    fromColumn: {
      position: "relative",
      minWidth: 140,
      maxWidth: 140,
    },
    messageColumn: {
      width: "100%",
      minWidth: 240,
      padding: theme.spacing(0, 2),
    },
    dateColumn: {
      minWidth: 170,
    },
    inviteButton: {
      paddingTop: 0,
      paddingBottom: 0,
    },
    declineButton: {
      marginLeft: theme.spacing(1.5),
    },
    unreadMessageText: {
      fontWeight: 500,
      color: theme.palette.common.black,
    },
  })
);

const acceptButtonTheme = (theme: Theme) =>
  createMuiTheme({
    palette: {
      primary: {
        main: theme.palette.success.light,
        contrastText: theme.palette.background.paper,
      },
    },
  });

const declineButtonTheme = (theme: Theme) =>
  createMuiTheme({
    palette: {
      primary: {
        main: theme.palette.error.dark,
      },
    },
  });

interface ListNotificationsProps {
  notifications: Notification[];
  dispatchEffect: AppDispatchEffect;
  history: H.History;
}

export const ListNotifications: React.FC<ListNotificationsProps> = ({
  notifications,
  dispatchEffect,
  history,
}) => {
  const classes = useListNotificationsStyles();
  const theme = useTheme();
  const [disableRipple, setDisableRipple] = useState(false);
  const invitePending = false; //TODO:: remove this when other types of notifications are available

  const formatDate = (dateTimestamp: number, isUnread: boolean) => {
    const date = new Date().setTime(dateTimestamp * 1000);
    const minutes = differenceInMinutes(new Date(), new Date(date));
    const today = isToday(date);
    const yesterday = isYesterday(date);

    if (minutes < 10) {
      return (
        <Typography
          className={clsx({ [classes.unreadMessageText]: isUnread })}
          color="textSecondary"
        >
          A few minutes ago
        </Typography>
      );
    }

    return (
      <React.Fragment>
        <Typography
          className={clsx({ [classes.unreadMessageText]: isUnread })}
          color="textSecondary"
        >
          {today && "Today"}
          {yesterday && "Yesterday"}
          {!today && !yesterday && format(date, "MMM d, yyyy")}
        </Typography>
        <Typography
          className={clsx({ [classes.unreadMessageText]: isUnread })}
          color="textSecondary"
        >
          {format(date, "p").toLowerCase()}
        </Typography>
      </React.Fragment>
    );
  };

  const handleRowClick = (guid: string, isUnread: boolean) => {
    if (isUnread) {
      dispatchEffect(patchNotificationEffect(guid));
    }
  };

  return (
    <React.Fragment>
      {notifications.length > 0 && (
        <TableList
          listId={ListType.NOTIFICATIONS}
          header={<HeaderListNotifications />}
        >
          <InfiniteScroll
            dataLength={notifications.length}
            list={ListType.NOTIFICATIONS}
            dispatchEffect={dispatchEffect}
          >
            {notifications.map(
              ({ sender, body, created, guid, isUnread }, index) => (
                <ListItem
                  button
                  key={guid}
                  disableGutters
                  disableRipple={disableRipple}
                  onClick={() => handleRowClick(guid, isUnread)}
                >
                  <ContainerListItem leftOffset={10} rightOffset={2}>
                    <Box className={classes.avatarColumn}>
                      <Avatar src={sender?.profileImage}>
                        {sender?.initials}
                      </Avatar>
                    </Box>
                    <Box className={classes.fromColumn}>
                      <Typography>{sender?.displayName}</Typography>
                    </Box>
                    <Box className={classes.messageColumn}>
                      <Typography
                        className={clsx({
                          [classes.unreadMessageText]: isUnread,
                        })}
                        color="textSecondary"
                      >
                        {body}
                      </Typography>
                    </Box>
                    <Box className={classes.dateColumn}>
                      <Box
                        justifyContent="space-between"
                        display="inline-flex"
                        width={1}
                      >
                        {/*TODO:: deprecate 'created'. Leave 'created.timestamp'*/}
                        {formatDate(
                          typeof created === "number"
                            ? created
                            : created?.timestamp || 0,
                          isUnread
                        )}
                      </Box>
                    </Box>
                  </ContainerListItem>
                  {invitePending && (
                    <Box width={1} pl={28.5}>
                      <Box display="inline-flex" alignItems="center">
                        <ThemeProvider theme={acceptButtonTheme(theme)}>
                          <ListItemRippleDisabler
                            setDisableRipple={setDisableRipple}
                          >
                            <Button
                              color="primary"
                              variant="contained"
                              className={classes.inviteButton}
                              disableElevation
                              onClick={async () => {
                                //TODO:: need valid id
                                await dispatchEffect(acceptInviteEffect("123"));
                                history.push(`${paths.WORKS_REGISTER}/${123}`);
                              }}
                            >
                              Accept
                            </Button>
                          </ListItemRippleDisabler>
                        </ThemeProvider>
                        <ThemeProvider theme={declineButtonTheme(theme)}>
                          <ListItemRippleDisabler
                            setDisableRipple={setDisableRipple}
                          >
                            <Button
                              color="primary"
                              variant="outlined"
                              className={clsx(
                                classes.inviteButton,
                                classes.declineButton
                              )}
                              onClick={() => {
                                //TODO:: need valid id
                                dispatchEffect(declineInviteEffect("123"));
                              }}
                            >
                              Decline
                            </Button>
                          </ListItemRippleDisabler>
                        </ThemeProvider>
                      </Box>
                    </Box>
                  )}
                </ListItem>
              )
            )}
          </InfiniteScroll>
        </TableList>
      )}
    </React.Fragment>
  );
};
