import {
  Box,
  createStyles,
  Drawer,
  IconButton,
  makeStyles,
  Theme as MaterialUiTheme,
  Typography,
} from "@material-ui/core";
import React, { Dispatch, SetStateAction, useState } from "react";
import KeyboardArrowLeftIcon from "@material-ui/icons/KeyboardArrowLeft";
import clsx from "clsx";
import { AppDispatchEffect } from "../../../App.reducer";
import { Theme } from "../../../utils/models";
import { Field } from "formik";
import { defaultTheme } from "../../pages/Theme/reducer";
import { FormikThemesListSelect } from "../../molecules/FormikThemesListSelect";
import { DialogDeleteTheme } from "../../molecules/DialogDeleteTheme";
import { FormNewTheme } from "../FormNewTheme";
import { LoadingState } from "../../../types/enums";
import { ChevronSortDown32, ChevronSortUp32 } from "@carbon/icons-react";

const themeDrawerHeightOpen = 240;
const themeDrawerHeightClosed = 72;

const useStyles = makeStyles((theme: MaterialUiTheme) =>
  createStyles({
    drawerOpen: {
      position: "relative",
      height: themeDrawerHeightOpen,
      overflowY: "hidden",
      transition: theme.transitions.create("height", {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    drawerClose: {
      position: "relative",
      transition: theme.transitions.create("height", {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      overflowY: "hidden",
      height: themeDrawerHeightClosed,
    },
    drawer: {
      zIndex: 1,
      position: "absolute",
      width: "100%",
      bottom: 0,
      flexShrink: 0,
      whiteSpace: "nowrap",
    },
    toolbar: {
      display: "flex",
      alignItems: "center",
      justifyContent: "space-between",
      padding: theme.spacing(0, 1.5),
      minHeight: themeDrawerHeightClosed,
    },
    relative: {
      position: "relative",
    },
    drawerContentContainer: {
      display: "flex",
      height: "100%",
      overflowX: "auto",
      overflowY: "hidden",
      padding: theme.spacing(0, 3),
    },
    closedDrawerOverlay: {
      position: "absolute",
      top: 0,
      bottom: 0,
      left: 0,
      right: 0,
      zIndex: 10,
    },
    themeOptionsTitle: {
      marginLeft: theme.spacing(1.5),
    },
    toggleIcon: {
      color: theme.palette.primary.main,
    },
  })
);

export const NEW_THEME_ID = "NEW_THEME_ID";

export const newTheme: Theme = {
  id: NEW_THEME_ID,
  title: "",
  appBarColor: defaultTheme.appBarColor,
  textColor: defaultTheme.textColor,
};

interface ThemeOptionsDrawerProps {
  dispatchEffect: AppDispatchEffect;
  themes: Theme[];
  open: boolean;
  setOpen: Dispatch<SetStateAction<boolean>>;
  setFieldValue: (key: string, value: any) => void;
  values: { theme: Theme };
  onEditThemeOpen: () => void;
  onEditThemeClose: () => void;
  submitForm: () => Promise<void>;
  themesLoadingState: LoadingState;
}

export const ThemeOptionsDrawer: React.FC<ThemeOptionsDrawerProps> = ({
  dispatchEffect,
  themes,
  open,
  setOpen,
  setFieldValue,
  values,
  onEditThemeOpen,
  onEditThemeClose,
  submitForm,
  themesLoadingState,
}) => {
  const classes = useStyles();
  const [editThemeOpen, setEditThemeOpen] = useState(false);
  const [previousTheme, setPreviousTheme] = useState<Theme>();
  const [deleteTheme, setDeleteTheme] = useState<Theme>();

  const handleEditThemeCancel = () => {
    setEditThemeOpen(false);
    onEditThemeClose();
    if (previousTheme) {
      setFieldValue("theme", previousTheme);
    }
    setPreviousTheme(undefined);
  };

  const closeEditForm = () => {
    setEditThemeOpen(false);
    onEditThemeClose();
    setPreviousTheme(undefined);
  };

  const handleEditThemeClick = (theme: Theme) => {
    setEditThemeOpen(true);
    onEditThemeOpen();
    setPreviousTheme(values.theme);
    setFieldValue("theme", theme);
  };

  return (
    <Drawer
      anchor="bottom"
      variant="permanent"
      className={clsx(classes.drawer, {
        [classes.drawerOpen]: open,
        [classes.drawerClose]: !open,
      })}
      classes={{
        paper: clsx({
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        }),
      }}
    >
      <div className={classes.toolbar}>
        <Box display="flex" alignItems="center">
          {editThemeOpen && (
            <IconButton onClick={handleEditThemeCancel}>
              <KeyboardArrowLeftIcon />
            </IconButton>
          )}
          <Typography
            variant="h6"
            className={clsx({ [classes.themeOptionsTitle]: !editThemeOpen })}
          >
            {editThemeOpen ? "Theme Customization" : "Theme Options"}
          </Typography>
        </Box>
        <IconButton onClick={() => setOpen(!open)}>
          {open ? (
            <ChevronSortDown32 className={classes.toggleIcon} />
          ) : (
            <ChevronSortUp32 className={classes.toggleIcon} />
          )}
        </IconButton>
      </div>
      <Box
        className={clsx(classes.drawerContentContainer, {
          [classes.relative]: !open,
        })}
      >
        {!open && <Box className={classes.closedDrawerOverlay} />}
        {editThemeOpen ? (
          <FormNewTheme
            submitForm={submitForm}
            onDeleteClick={(theme: Theme) => setDeleteTheme(theme)}
            dispatchEffect={dispatchEffect}
            setFieldValue={setFieldValue}
            values={values}
            onCancel={handleEditThemeCancel}
            onClose={closeEditForm}
            onSaveSuccess={() => setPreviousTheme(undefined)}
          />
        ) : (
          <React.Fragment>
            {/* TODO: Enable new theme button, when BE implements setting API branches functionality */}
            {/* <ButtonNewTheme onClick={() => handleEditThemeClick(newTheme)} /> */}
            <Field
              component={FormikThemesListSelect}
              name="theme"
              themes={themes}
              onEditClick={handleEditThemeClick}
              onDeleteClick={(theme: Theme) => setDeleteTheme(theme)}
              themesLoadingState={themesLoadingState}
            />
          </React.Fragment>
        )}
      </Box>
      {deleteTheme && (
        <DialogDeleteTheme
          dispatchEffect={dispatchEffect}
          theme={deleteTheme}
          open={Boolean(deleteTheme)}
          onClose={() => setDeleteTheme(undefined)}
          callback={() => {
            setEditThemeOpen(false);
          }}
        />
      )}
    </Drawer>
  );
};
