import React from "react";
import { FieldProps } from "formik";
import { TextFieldProps } from "@material-ui/core";
import { AutocompleteSelect } from "../../organisms/AutocompleteSelect";
import { AutocompleteProps } from "@material-ui/lab";

interface FormikAutocompleteSelectProps<Option, ApiResponse>
  extends FieldProps {
  label: string;
  apiSource?: string;
  setApiResponseToOptions?: (response: ApiResponse) => Option[];
  textFieldProps?: TextFieldProps;
  options?: Option[];
  freeSolo?: boolean;
}

export const FormikAutocompleteSelect = <Option, ApiResponse = undefined>(
  props: FormikAutocompleteSelectProps<Option, ApiResponse> &
    Omit<
      AutocompleteProps<Option, false, false, true>,
      "renderInput" | "onSelect" | "options" | "freeSolo"
    >
) => {
  const {
    form: { setFieldValue, setFieldTouched },
    field: { name, value },
    meta: { error, touched },
    label,
    textFieldProps,
    value: valueProp,
    freeSolo,
    ...rest
  } = props;

  const handleOnBlur = () => {
    setFieldTouched(name);
  };

  return (
    <AutocompleteSelect<Option, ApiResponse>
      freeSolo={freeSolo}
      label={label}
      value={value || null}
      onBlur={handleOnBlur}
      onSelect={(option) => setFieldValue(name, option)}
      textFieldProps={{
        ...textFieldProps,
        disabled: rest.disabled,
        error: Boolean(touched && error),
        helperText: touched && error ? error : undefined,
      }}
      {...rest}
    />
  );
};
