import React from "react";
import {
  Box,
  BoxProps,
  createStyles,
  Grid,
  makeStyles,
} from "@material-ui/core";
import clsx from "clsx";
import { Theme } from "@material-ui/core/styles";

export const mrShareCellWidth = 120;
export const mrShareCellDenseWidth = 100;

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    cell: {
      position: "relative",
      minWidth: mrShareCellWidth,
      maxWidth: mrShareCellWidth,
    },
    dense: {
      minWidth: mrShareCellDenseWidth,
      maxWidth: mrShareCellDenseWidth,
    },
    header: {
      paddingBottom: theme.spacing(1.5),
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
  })
);

interface CellMrShareProps extends BoxProps {
  dense?: boolean;
  header?: boolean;
}

export const CellMrShare: React.FC<CellMrShareProps> = ({
  children,
  dense,
  className,
  header = false,
  ...props
}) => {
  const classes = useStyles();
  return (
    <Box
      {...props}
      component={Grid}
      className={clsx(classes.cell, className, {
        [classes.dense]: dense,
        [classes.header]: header,
      })}
    >
      {children}
    </Box>
  );
};
