import { AppDispatchEffect } from "../../../App.reducer";
import { Authentication } from "../../../utils/models";
import { fetchBootstrapEffect, signInEffect } from "../../pages/SignIn/effects";
import { paths } from "../../../utils/common";
import { FormValues } from "./component";

export const handleUserSignIn = async (
  data: FormValues,
  dispatchEffect: AppDispatchEffect,
  push: (path: string) => void
) => {
  const response: Authentication = await dispatchEffect(signInEffect(data));
  await dispatchEffect(fetchBootstrapEffect(response._links.bootstrap));
  push(paths.INDEX);
};
