import {
  createStyles,
  makeStyles,
  Typography,
  withStyles,
  Theme,
  Accordion,
  AccordionSummary,
  AccordionDetails,
} from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import React, { useEffect, useRef } from "react";
import { AppDispatch } from "../../../App.reducer";
import { setItemAction } from "../../pages/Faq/actions";
import { FaqItem } from "../../../utils/models";

const ExpansionPanel = withStyles((theme: Theme) => ({
  root: {
    boxShadow: "none",
    overflow: "hidden",
    "&:not(:first-child)": {
      borderTop: `1px solid ${theme.palette.grey[300]}`,
    },
    "&:not(:last-child)": {
      borderBottom: 0,
    },
    "&:last-child": {
      borderBottom: `1px solid ${theme.palette.grey[300]}`,
    },
    "&:before": {
      display: "none",
    },
    "&$expanded": {
      margin: "auto",
    },
  },
  expanded: {},
}))(Accordion);

const ExpansionPanelSummary = withStyles((theme: Theme) => ({
  root: {
    borderBottom: `1px solid ${theme.palette.grey[300]}`,
    marginBottom: -1,
    minHeight: 56,
    "&$expanded": {
      minHeight: 56,
    },
  },
  content: {
    "&$expanded": {
      margin: theme.spacing(1.5, 0),
    },
  },
  expanded: {},
}))(AccordionSummary);

const ExpansionPanelDetails = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(AccordionDetails);

const useStyles = makeStyles(() =>
  createStyles({
    expansionPanelMainText: {
      maxWidth: "100%",
      whiteSpace: "pre-line",
    },
  })
);

interface ExpansionPanelFaqProps {
  dispatch: AppDispatch;
  item: FaqItem;
  expandedItemId?: string;
  defaultOpen?: boolean;
}

export const ExpansionPanelFaq: React.FC<ExpansionPanelFaqProps> = ({
  dispatch,
  item,
  expandedItemId,
  defaultOpen,
}) => {
  const classes = useStyles();
  const expansionPanelSummaryRef = useRef<any>(null);
  const handleChange = () =>
    dispatch(setItemAction(expandedItemId === item.id ? undefined : item.id));

  const handleAutoScroll = () => {
    expansionPanelSummaryRef.current.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    if (defaultOpen) {
      dispatch(setItemAction(item.id));
    }
  }, [defaultOpen, dispatch, item.id]);

  return (
    <ExpansionPanel
      square
      expanded={expandedItemId === item.id}
      onChange={handleChange}
      TransitionProps={{ onEntered: handleAutoScroll }}
    >
      <ExpansionPanelSummary
        ref={expansionPanelSummaryRef}
        expandIcon={<ExpandMoreIcon />}
        aria-controls={`${item.id}-contents`}
      >
        <Typography variant="subtitle1" color="textSecondary">
          {item.title}
        </Typography>
      </ExpansionPanelSummary>
      <ExpansionPanelDetails>
        <Typography variant="body2" className={classes.expansionPanelMainText}>
          {item.content}
        </Typography>
      </ExpansionPanelDetails>
    </ExpansionPanel>
  );
};
