import { KeyboardEvent } from "react";

export const handleKeyDown = (e: KeyboardEvent<HTMLInputElement>) => {
  const allowedChars = "0123456789.";
  const target = e.target as HTMLInputElement;
  const contains = (stringValue: string, charValue: string) =>
    stringValue.indexOf(charValue) > -1;
  const invalidKey =
    (e.key.length === 1 && !contains(allowedChars, e.key)) ||
    (e.key === "." && contains(target.value, "."));
  invalidKey && e.preventDefault();
};
