export const fetchWorksEffect =
  ({
    isDraftsPage,
    statuses = [],
    sortParams = initialSortParams,
    query,
    next,
  }: {
    isDraftsPage: boolean;
    statuses?: WorkListItemStatusType[];
    sortParams?: {
      sortBy: WorkSortVariant;
      sortOrder: SortOrder;
    };
    query?: string;
    next?: boolean;
  }): ThunkAction<
    Promise<void | WorksListResponse>,
    AppState,
    void,
    Action<string>
  > =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchWorksAction(isDraftsPage ? "DRAFTS" : "WORKS", next));
    const state = getState();
    const links = state.signIn._links;
    const linkNext = getWorksListLinks(state)?.next?.href;
    const requestURL = isDraftsPage ? links?.workDrafts : links?.works;

    if (!requestURL) {
      return;
    }

    const url =
      next && linkNext
        ? linkNext
        : buildWorkQueryUrl({
            url: requestURL.href,
            statuses,
            sortParams,
            query,
          });

    try {
      // const response = await axios.request<WorksListResponse>({
      //   url,
      //   method: requestURL?.method,
      //   withCredentials: true,
      // });
      const { items, _links, meta } = fetchWorksResponseMock.response.work;
      dispatch(
        fetchWorksSuccessAction({
          works: items,
          worksType: isDraftsPage ? "DRAFTS" : "WORKS",
          _links,
          next,
          meta,
        })
      );
      return fetchWorksResponseMock;
    } catch (e) {
      dispatch(fetchWorksFailureAction());
      const error = handleError(e.response, "works");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.work.GET_WORK} ${error.message}`
        )
      );
      throw e;
    }
  };

export const fetchWorkEffect =
  (
    id: string
  ): ThunkAction<Promise<WorkResponse>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchWorkAction());
    try {
      // const link = getBootstrapLinksSelector(getState())?.work;
      // const response = await axios.request<WorkResponse>({
      //   url: link?.href.replace("{guid}", id),
      //   method: link?.method,
      //   withCredentials: true,
      // });
      const { item } = workResponse.response.work;
      const formattedData: Work = {
        ...item,
        creators: convertWorkCreatorSharesToPercentage(item.creators),
      };
      dispatch(fetchWorkSuccessAction(formattedData));
      return workResponse;
    } catch (e) {
      const error = handleError(e.response, "work");
      dispatch(fetchWorkFailureAction());
      throw error;
    }
  };

