export const fetchWorkRegistrationValidationErrorsEffect =
  (
    link: string
  ): ThunkAction<
    Promise<WorkRegistrationValidationError[]>,
    AppState,
    void,
    Action<string>
  > =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>) => {
    try {
      const response = await axios.get<FetchWorkRegistrationValidationErrors>(
        link,
        {
          withCredentials: true,
        }
      );
      return response.data.response.workRegistrationErrors.items;
    } catch (e) {
      const error = handleError(e.response, "work submission errors");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.work.GET_REGISTRATION_VALIDATION_ERRORS} ${error.message}`
        )
      );
      throw e;
    }
  };

