  (id: string): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>) => {
    dispatch(acceptSplitAction());
    try {
      await axios.put(`${apiUrls.works}/splits/vote/${id}`, {
        withCredentials: true,
      });
      dispatch(acceptSplitSuccessAction());
    } catch (e) {
      const error = handleError(e.response, "split");
      throw error;
    }
  };

