export const leaveWorkEffect =
  ({
    link,
    ipiReference = "",
    workGuid,
  }: {
    ipiReference?: string;
    link: Href;
    workGuid: string;
  }): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(leaveWorkAction());
    try {
      await axios.request({
        url: link.href.replace("{ipiReference}", ipiReference),
        method: link.method,
        withCredentials: true,
      });
      dispatch(leaveWorkSuccessAction(workGuid));
    } catch (e) {
      if (e.response?.status === 400 && e.response?.data?.error?.message) {
        dispatch(addAlertAction(AlertType.ERROR, toasts.error.work.CANT_LEAVE));
      }
      throw handleError(e.response, "work");
    }
  };

export const workCloneEffect =
  (
    workGuid: string
  ): ThunkAction<Promise<void | string>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(workCloneAction());
    try {
      const link = getBootstrapLinksSelector(getState())?.work;
      const response = await axios.request<WorkResponse>({
        url: link?.href.replace("{guid}", workGuid),
        method: "GET",
        withCredentials: true,
      });
      const workToClone: Work = {
        ...response.data.response.work.item,
        guid: "",
        title: "",
        altTitle: "",
        titleLanguageCode: "",
        altTitleLanguageCode: "",
        duration: undefined,
        firstReleaseDate: undefined,
        status: DRAFT,
        version: "",
        owner: "",
        creators: convertWorkCreatorSharesToPercentage(
          response.data.response.work.item.creators
        ),
      };
      dispatch(workCloneSuccessAction(workToClone));
    } catch (e) {
      dispatch(workCloneFailureAction());
      const error = handleError(e.response, "work");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.work.CLONE_WORK} ${error.message}`
        )
      );
      throw e;
    }
  };

export const changeCreatorIpiEffect =
  (
    ipiReference: string,
    desiredIpiNameNumber: number
  ): ThunkAction<Promise<Work>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(changeCreatorIpiAction());
    const link = getState().works.work?._links.changeIpi;
    try {
      const response = await axios.request<WorkResponse>({
        url: link?.href.replace("{ipiReference}", ipiReference),
        method: link?.method,
        data: {
          desiredIpiNameNumber,
        },
        withCredentials: true,
      });
      dispatch(changeCreatorIpiSuccessAction(response.data.response.work.item));
      return response.data.response.work.item;
    } catch (e) {
      dispatch(changeCreatorIpiFailureAction());
      const error = handleError(e.response, "work");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.work.CHANGE_IPI} ${error.message}`
        )
      );
      throw e;
    }
  };

export const downloadCwrEffect =
  (url: string): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>) => {
    try {
      const {
        data: { response },
      } = await axios.get<DownloadCwrResponse>(url, { withCredentials: true });
      downloadFile(response._links.download.href);
    } catch (e) {
      const error = handleError(e.response, "CWR");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.work.DOWNLOAD_CWR} ${error.message}`
        )
      );
      throw e;
    }
  };
