import React from "react";

import {
  createStyles,
  Dialog as MuiDialog,
  DialogProps as MuiDialogProps,
  makeStyles,
  Theme,
} from "@material-ui/core";
import clsx from "clsx";

interface StyleProps {
  variant: "simple" | "complex";
}

const useStyles = makeStyles<Theme, StyleProps>((theme) =>
  createStyles({
    root: ({ variant }) => ({
      "& .MuiDialogTitle-root": {
        padding:
          variant === "complex"
            ? theme.spacing(5, 5, 3)
            : theme.spacing(3, 3, 2),
        "& .MuiTypography-root": {
          lineHeight: 1,
        },
      },
      "& .MuiDialogContent-root": {
        padding:
          variant === "complex" ? theme.spacing(1, 5) : theme.spacing(1, 3),
      },
      "& .MuiDialogActions-root": {
        padding:
          variant === "complex" ? theme.spacing(2, 3, 3, 3) : theme.spacing(1),
      },
    }),
  })
);

interface DialogProps {
  variant?: "simple" | "complex";
}

export const Dialog: React.FC<DialogProps & MuiDialogProps> = ({
  children,
  className,
  variant = "simple",
  ...props
}) => {
  const classes = useStyles({ variant });

  return (
    <MuiDialog className={clsx(classes.root, className)} {...props}>
      {children}
    </MuiDialog>
  );
};
