import { ThunkAction, ThunkDispatch } from "redux-thunk";
import { AppState } from "../App.reducer";
import { Action } from "redux";
import axios from "axios";
import { fetchWorkSchema, fetchWorkSchemaSuccess } from "./action";
import { WorkSchemaResponse } from "../api.types";
import { getWorkBaseLinkSelector } from "../components/pages/SignIn/selectors";

export const fetchWorkValidationSchemaEffect = (): ThunkAction<
  Promise<void>,
  AppState,
  void,
  Action<string>
> => async (
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState
) => {
  dispatch(fetchWorkSchema());
  const link = getWorkBaseLinkSelector(getState());
  const response = await axios.request<WorkSchemaResponse>({
    url: link,
    method: "POST",
    headers: { Accept: "application/json+schema" },
    withCredentials: true,
  });
  dispatch(fetchWorkSchemaSuccess(response.data.response.schema));
};
