import React, { useState } from "react";
import InfiniteScrollComponent from "react-infinite-scroll-component";
import {
  AppDispatchEffect,
  AppState,
  useAppSelector,
} from "../../../App.reducer";
import { fetchWorksEffect } from "../../pages/Works/effects";
import { useHistory, useLocation } from "react-router";
import { getSortBy, getSortOrder } from "../HeaderTableWorks/utils";
import { getWorkFilters } from "../WorksNavigationBar/utils";
import { paths } from "../../../utils/common";
import { fetchTemplatesEffect } from "../../pages/Templates/effects";
import { fetchAgreementsEffect } from "../../pages/AgreementRegistration/effects";
import { getAgreementsLinksSelector } from "../../pages/AgreementRegistration/selectors";
import { getNotificationsLinkSelector } from "../../pages/Notifications/selectors";
import { fetchNotificationsEffect } from "../../pages/Notifications/effects";
import { createStyles, makeStyles } from "@material-ui/core";
import {
  FetchAgreementsResponse,
  WorksListResponse,
  WorkTemplatesListResponse,
} from "../../../api.types";
import { getWorksListLinks } from "../../pages/ListWorks/selectors";
import { getTemplatesListLinks } from "../../pages/Templates/selectors";

export const useInfiniteScrollStyles = makeStyles(() =>
  createStyles({
    infiniteScrollComponent: {
      flex: 1,
    },
    infiniteScrollContainer: {
      height: "100%",
      display: "flex",
      flexDirection: "column",
      "& ul": {
        flex: 1,
        paddingBottom: 0,
      },
      "& .infinite-scroll-component__outerdiv": {
        display: "flex",
        flexDirection: "column",
        height: "100%",
      },
    },
  })
);

export enum ListType {
  WORKS = "WORKS",
  WORK_TEMPLATES = "WORK_TEMPLATES",
  AGREEMENTS = "AGREEMENTS",
  NOTIFICATIONS = "NOTIFICATIONS",
}

const hasMoreToFetch = (
  list: ListType,
  dataLength: number,
  state: AppState
) => {
  if (list === ListType.WORKS) {
    return Boolean(getWorksListLinks(state)?.next);
  }
  if (list === ListType.WORK_TEMPLATES) {
    return Boolean(getTemplatesListLinks(state)?.next);
  }
  if (list === ListType.AGREEMENTS) {
    return Boolean(getAgreementsLinksSelector(state)?.next);
  }
  if (list === ListType.NOTIFICATIONS) {
    return Boolean(getNotificationsLinkSelector(state)?.next);
  }
  return false;
};

interface InfiniteScrollProps {
  dataLength: number;
  list: ListType;
  dispatchEffect: AppDispatchEffect;
}

export const InfiniteScroll: React.FC<InfiniteScrollProps> = ({
  children,
  dataLength,
  list,
  dispatchEffect,
}) => {
  const classes = useInfiniteScrollStyles();
  const state = useAppSelector((state) => state);
  const { location } = useHistory();
  const { pathname } = useLocation();
  const [hasMore, setHasMore] = useState(
    hasMoreToFetch(list, dataLength, state)
  );
  const isDraftsPage = pathname === paths.WORKS_DRAFT_LIST;
  const urlParams = new URLSearchParams(location.search);

  const onLoadMore = async () => {
    switch (list) {
      case ListType.WORKS: {
        const sortParams = {
          sortBy: getSortBy(urlParams.get("sortBy")),
          sortOrder: getSortOrder(urlParams.get("sortOrder")),
        };
        const query = urlParams.get("search") || undefined;
        const statuses = getWorkFilters(urlParams.get("filters"), isDraftsPage);
        try {
          const { response }: WorksListResponse = await dispatchEffect(
            fetchWorksEffect({
              isDraftsPage,
              next: true,
              sortParams,
              query,
              statuses,
            })
          );
          if (!response.work._links?.next) {
            setHasMore(false);
          }
        } catch (e) {}
        break;
      }

      case ListType.WORK_TEMPLATES: {
        const sortParams = {
          sortBy: getSortBy(urlParams.get("sortBy")),
          sortOrder: getSortOrder(urlParams.get("sortOrder")),
        };
        const query = urlParams.get("search") || undefined;
        try {
          const { response }: WorkTemplatesListResponse = await dispatchEffect(
            fetchTemplatesEffect({ next: true, query, sortParams })
          );
          if (!response.workTemplate._links?.next) {
            setHasMore(false);
          }
        } catch (e) {}
        break;
      }

      case ListType.AGREEMENTS: {
        try {
          const { response }: FetchAgreementsResponse = await dispatchEffect(
            fetchAgreementsEffect({next: true})
          );
          if (!response.agreement._links?.next) {
            setHasMore(false);
          }
        } catch (e) {}
        break;
      }

      case ListType.NOTIFICATIONS: {
        try {
          const {
            response: {
              notificationHistoryLog: { _links },
            },
          } = await dispatchEffect(fetchNotificationsEffect(true));
          if (!_links?.next) {
            setHasMore(false);
          }
        } catch (e) {}
        break;
      }

      default:
        return;
    }
  };

  return (
    <InfiniteScrollComponent
      className={classes.infiniteScrollComponent}
      dataLength={dataLength}
      next={onLoadMore}
      hasMore={hasMore}
      scrollableTarget={list}
      loader={null}
    >
      {children}
    </InfiniteScrollComponent>
  );
};
