import React from "react";
import {
  KeyboardTimePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import { FieldProps, getIn } from "formik";
import { KeyboardTimePickerProps } from "formik-material-ui-pickers";
import {
  convertDayTimeToSeconds,
  convertSecondsToDate,
} from "../../../utils/helpers";

export const FormikTimePicker: React.FC<
  KeyboardTimePickerProps & FieldProps
> = ({ form, field, label, ...props }) => {
  const {
    errors,
    touched,
    setFieldError,
    setFieldValue,
    isSubmitting,
    submitCount,
  } = form;
  const { name, value, onBlur } = field;

  const errorText = getIn(errors, name);
  const touchedVal = getIn(touched, name);
  const hasError = (touchedVal || submitCount > 0) && errorText !== undefined;

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <KeyboardTimePicker
        {...props}
        disabled={isSubmitting || props.disabled}
        error={hasError}
        helperText={hasError ? errorText : "mm:ss"}
        label={label}
        name={name}
        format="mm:ss"
        ampm={false}
        keyboardIcon={false}
        InputAdornmentProps={{
          disablePointerEvents: true,
        }}
        onError={(error) => {
          if (error && errorText !== `${error}`) {
            setFieldError(name, `${error}`);
          }
        }}
        onChange={(value) => {
          value
            ? !isNaN(value.getTime()) &&
              setFieldValue(name, convertDayTimeToSeconds(value as Date))
            : setFieldValue(name, null);
        }}
        onBlur={onBlur(name)}
        value={value || value === 0 ? convertSecondsToDate(value) : null}
      />
    </MuiPickersUtilsProvider>
  );
};
