import React, { useState } from "react";
import {
  Box,
  Chip,
  makeStyles,
  Table,
  TableBody,
  TableHead,
  TableRow,
  Theme,
  Typography,
} from "@material-ui/core";
import clsx from "clsx";
import MuiTableCell from "@material-ui/core/TableCell";
import { createStyles, withStyles } from "@material-ui/core/styles";
import { AgreementTerm } from "../../../utils/models";
import { getFormattedTerritories } from "../../organisms/FormikTableTerritorySelect/utils";
import { format } from "date-fns";
import { TableAgreementSharesSummary } from "../TableAgreementSharesSummary";
import { TooltipMap } from "../../atoms/TooltipMap";
import { TermToggleIcon } from "../TermToggleIcon";

interface StyleProps {
  isExpanded?: boolean;
}

const useStyles = makeStyles<Theme, StyleProps>((theme) => {
  const borderStyle = `1px solid ${theme.palette.custom.state.dragged}`;

  return createStyles({
    table: {
      borderCollapse: "unset",
      minWidth: 530,
    },
    territoriesCell: ({ isExpanded }) => ({
      minWidth: 170,
      width: "100%",
      maxWidth: 390,
      paddingLeft: theme.spacing(2),
      borderLeft: borderStyle,
      borderTopLeftRadius: theme.spacing(1),
      borderBottomLeftRadius: isExpanded ? 0 : theme.spacing(1),
    }),
    startDateCell: {
      minWidth: 160,
    },
    priorRoyaltiesCell: {
      minWidth: 160,
    },
    terminationCell: {
      minWidth: 180,
    },
    chevronCell: ({ isExpanded }) => ({
      minWidth: 35,
      maxWidth: 35,
      borderRight: borderStyle,
      borderTopRightRadius: theme.spacing(1),
      borderBottomRightRadius: isExpanded ? 0 : theme.spacing(1),
      padding: 0,
      paddingRight: 2,
    }),
    emptyCell: {
      border: "none",
      padding: theme.spacing(1),
    },
    cellPadding: {
      paddingLeft: theme.spacing(2),
      paddingRight: theme.spacing(2),
    },
    noWrap: {
      whiteSpace: "nowrap",
    },
    territoryChip: {
      margin: theme.spacing(0.5, 1, 0.5, 0),
    },
    emptyRow: {
      "& th": {
        paddingTop: theme.spacing(3),
        borderBottom: "none",
      },
    },
    headerRow: {
      "& th:first-of-type": {
        paddingLeft: 0,
      },
    },
    territoriesChips: {
      overflow: "hidden",
      whiteSpace: "nowrap",
      textOverflow: "ellipsis",
    },
  });
});

const TableHeaderCell = withStyles((theme) => ({
  root: {
    ...theme.typography.body2,
    color: theme.palette.text.secondary,
    border: "none",
    paddingBottom: theme.spacing(1),
  },
}))(MuiTableCell);

const TableCell = withStyles((theme) => {
  const borderStyle = `1px solid ${theme.palette.custom.state.dragged}`;
  return {
    root: {
      borderTop: borderStyle,
      borderBottom: borderStyle,
      paddingTop: theme.spacing(1.5),
      paddingBottom: theme.spacing(1.5),
    },
  };
})(MuiTableCell);

interface TableAgreementTermsSummaryProps {
  term: AgreementTerm;
  hideHeader: boolean;
}

export const TableAgreementTermsSummary: React.FC<TableAgreementTermsSummaryProps> = ({
  term: { territories, shares, endDate, priorRoyalties, startDate },
  hideHeader,
}) => {
  const [showShares, setShowShares] = useState(true);
  const classes = useStyles({ isExpanded: showShares });
  const terr = getFormattedTerritories(territories);

  return (
    <React.Fragment>
      <Table className={classes.table}>
        {!hideHeader && (
          <TableHead>
            <TableRow className={classes.headerRow}>
              <TableHeaderCell align="left" className={clsx(classes.noWrap)}>
                <TooltipMap to="AgreementTerritories">Territories</TooltipMap>
              </TableHeaderCell>
              <TableHeaderCell align="left" className={classes.startDateCell}>
                <TooltipMap to="AgreementStartDate">Start Date</TooltipMap>
              </TableHeaderCell>
              <TableHeaderCell
                align="left"
                className={clsx(classes.priorRoyaltiesCell, classes.noWrap)}
              >
                <TooltipMap to="AgreementPriorRoyalties">
                  Prior Royalties
                </TooltipMap>
              </TableHeaderCell>
              <TableHeaderCell align="left" className={classes.noWrap}>
                <TooltipMap to="AgreementTermination">
                  Expected Termination
                </TooltipMap>
              </TableHeaderCell>
              <TableHeaderCell
                align="center"
                className={clsx(classes.chevronCell, classes.noWrap)}
              />
            </TableRow>
            <TableRow className={classes.emptyRow}>
              <TableCell colSpan={5} />
            </TableRow>
          </TableHead>
        )}
        <TableBody>
          <TableRow>
            <TableCell align="left" className={clsx(classes.territoriesCell)}>
              <Box className={classes.territoriesChips}>
                <TooltipMap variant="listItems" tooltipText={terr.join(" ")}>
                  {terr.map((el) => (
                    <Chip
                      key={el}
                      label={el}
                      className={classes.territoryChip}
                    />
                  ))}
                </TooltipMap>
              </Box>
            </TableCell>
            <TableCell align="left" className={classes.startDateCell}>
              <Typography variant="subtitle1" color="textPrimary">
                {startDate ? format(new Date(startDate), "dd/MM/yyyy") : "N/A"}
              </Typography>
            </TableCell>
            <TableCell
              align="left"
              className={clsx(classes.priorRoyaltiesCell, classes.cellPadding)}
            >
              <Typography variant="subtitle1" color="textPrimary">
                {priorRoyalties
                  ? format(new Date(priorRoyalties), "dd/MM/yyyy")
                  : "N/A"}
              </Typography>
            </TableCell>
            <TableCell
              align="left"
              className={clsx(classes.terminationCell, classes.cellPadding)}
            >
              <Typography variant="subtitle1" color="textPrimary">
                {format(new Date(endDate), "yyyy") === "9999" ? (
                  <React.Fragment>Indefinite</React.Fragment>
                ) : (
                  format(new Date(endDate), "dd/MM/yyyy")
                )}
              </Typography>
            </TableCell>
            <TableCell align="right" className={classes.chevronCell}>
              <TermToggleIcon
                show={showShares}
                onClick={() => setShowShares(!showShares)}
              />
            </TableCell>
          </TableRow>
          {!showShares && (
            <TableRow>
              <MuiTableCell className={classes.emptyCell} />
            </TableRow>
          )}
        </TableBody>
      </Table>
      {showShares && shares && <TableAgreementSharesSummary shares={shares} />}
    </React.Fragment>
  );
};
