import { AppState } from "../../../App.reducer";
import { ThunkDispatch } from "redux-thunk";
import { Action } from "redux";
import { fetchIpiListEffect } from "./effects";

export const getIpisLink = async (
  linkName: "listSocieties" | "listIntegratedSocieties",
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState: () => AppState
) => {
  const link = getState().ipi?.linksIpi?.[linkName];
  if (!link) {
    const response = await dispatch(fetchIpiListEffect());
    const refetchedLink =
      response && response?.response?.ipi?._links?.[linkName];
    if (refetchedLink) return refetchedLink;
    const message = `Unable to find link: ${linkName}`;
    throw new Error(message);
  }
  return link;
};
