import { ensureDomain, parseBase64 } from "./helpers";
import { paths } from "./common";
import "url-polyfill";
import { Href, SignUpInvitationPayload } from "./models";

export const DLR: { [path: string]: string } = {
  IPI_VERIFICATION_CANCELLED: `${paths.PROFILE_IPI}/cancel`,
  IPI_VERIFICATION_SUCCESS: `${paths.PROFILE_IPI}/success`,
  IPI_VERIFICATION_FAILED: `${paths.PROFILE_IPI}/error`,
  IPI_VERIFICATION_FINALIZE: `${paths.PROFILE_IPI}/verify`,
  VERIFY_USER_ATTRIBUTE: `${paths.PROFILE_PERSONAL}/confirm-email-change`,
  AUTH_INCOMPLETE_IDENTITY: `${paths.SIGN_UP}/incomplete-identity`,
  AUTH_SIGN_IN_SUCCESSFUL: `${paths.SIGN_IN}/success`,
  AUTH_APPROVAL_REQUIRED: `${paths.SIGN_IN}/not-approved`,
  AUTH_LOGOUT: paths.AUTH_LOGOUT,
  AUTH_SIGN_IN_ERROR: paths.AUTH_ERROR,
  AUTH_LOGOUT_ERROR: paths.AUTH_ERROR,
  AUTH_INTEGRATION_ERROR: paths.AUTH_ERROR,
  SIGNUP_INVITATION: `${paths.SIGN_UP}/${paths.subPage.SIGN_UP_INVITE}`,
};

export const parseDlrPayload = (search: string) => {
  try {
    const base64 = new URLSearchParams(search).get("b") || "";
    return parseBase64(base64);
  } catch (e) {
    return null;
  }
};

export const handleSignupInvitationPayload = (search: string) => {
  const payload: SignUpInvitationPayload = parseDlrPayload(search);
  return payload && ensureDomain(payload._links.signUp.href) ? payload : null;
};

export const handleAuthSignInSuccessPayload = (search: string) => {
  const payload = parseDlrPayload(search);
  if (!payload) {
    return null;
  }
  const {
    _links: { bootstrap },
  } = payload;
  return ensureDomain(bootstrap.href) ? bootstrap : null;
};

export const handleConfirmEmailChangePayload = (search: string) => {
  const payload = parseDlrPayload(search);
  if (!payload) {
    return null;
  }
  const code = new URLSearchParams(search).get("code") || "";

  const {
    _links: {
      verifyUserAttribute: { method, href },
    },
  } = payload;

  return ensureDomain(href) ? { href, method, code } : null;
};

export const handleIpiVerifyV1Payload = (search: string) => {
  const payload = parseDlrPayload(search);
  if (!payload) {
    return null;
  }
  const {
    token,
    _links: {
      verifyIpi: { href, method },
    },
  } = payload;
  return ensureDomain(href) ? { href, method, token } : null;
};

export const handleIpiVerifySuccessPayload = (search: string) => {
  const payload = parseDlrPayload(search);
  if (!payload) {
    return null;
  }
  const {
    verifiedIpis,
    _links: {
      listIpi: { href },
    },
  } = payload;
  return ensureDomain(href) ? { href, verifiedIpis } : null;
};

type MissingAttributes =
  | "firstName"
  | "lastName"
  | "role"
  | "companyIpiNameNumber";
export const handleSignUpIncompleteIdentityPayload: (
  search: string
) => {
  missingAttributes: MissingAttributes[];
  completeSignUp: Href;
} | null = (search: string) => {
  const payload = parseDlrPayload(search);
  if (!payload) {
    return null;
  }
  const {
    missingAttributes,
    _links: { completeSignUp },
  } = payload;
  return ensureDomain(completeSignUp.href)
    ? { completeSignUp, missingAttributes }
    : null;
};
