import {
  ADD_ALERT,
  Alert,
  AlertActionTypes,
  REMOVE_ALERT,
  SILENT_ALERTS,
} from "./actions.types";

export interface AlertsState {
  alerts: Alert[];
  silent: boolean;
}

export const alertsInitialState: AlertsState = {
  alerts: [],
  silent: false,
};

export const alertsReducer = (
  state = alertsInitialState,
  action: AlertActionTypes
): AlertsState => {
  switch (action.type) {
    case ADD_ALERT: {
      if (state.silent) {
        return state;
      }
      return {
        ...state,
        alerts: [
          ...state.alerts,
          { type: action.alertType, message: action.message },
        ],
      };
    }

    case REMOVE_ALERT: {
      return {
        ...state,
        alerts: state.alerts.filter((el, index) => index !== action.index),
      };
    }

    case SILENT_ALERTS: {
      return { ...state, silent: action.state };
    }

    default:
      return state;
  }
};
