export interface FetchWorkTemplateSchemaAction {
  type: typeof FETCH_WORK_TEMPLATE_SCHEMA;
}

export interface FetchWorkTemplateSchemaSuccessAction {
  type: typeof FETCH_WORK_TEMPLATE_SCHEMA_SUCCESS;
  schema: WorkSchemaJSON;
}

export interface FetchWorkTemplateSchemaFailureAction {
  type: typeof FETCH_WORK_TEMPLATE_SCHEMA_FAILURE;
}

export interface FetchTemplatesAction {
  type: typeof FETCH_TEMPLATES;
  next?: boolean;
}

