import React, { ChangeEvent, useState } from "react";
import { FieldProps, getIn } from "formik";
import {
  Box,
  Button,
  Chip,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
  Typography,
} from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { Territory } from "../../../utils/models";
import { territories } from "../../../utils/territoryCodes";
import { getFormattedTerritories } from "./utils";
import { AgreementFormValues } from "../FormAgreement";
import clsx from "clsx";
import { Add16, Subtract16 } from "@carbon/icons-react";
import { darken } from "@material-ui/core/styles/colorManipulator";
import { TerritoriesInfoBanner } from "../../molecules/TerritoriesInfoBanner";
import { ThemeInverse } from "../../atoms/ThemeInverse";
import { Dialog } from "../../molecules/Dialog";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    option: {
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      width: "100%",
      minHeight: 48,
    },
    tisA: {
      paddingLeft: theme.spacing(2),
    },
    chipIcon: {
      color: theme.palette.info.main,
    },
    chip: {
      margin: theme.spacing(0, 2, 1, 0),
    },
    chipDeleteIcon: {
      color: theme.palette.info.main,
      "&:hover": {
        color: theme.palette.info.main,
      },
    },
    chipGreen: {
      backgroundColor: theme.palette.custom.tagChip.green,
      "&:hover": {
        backgroundColor: darken(theme.palette.custom.tagChip.green, 0.1),
      },
    },
    chipRed: {
      backgroundColor: theme.palette.custom.tagChip.red,
      "&:hover": {
        backgroundColor: darken(theme.palette.custom.tagChip.red, 0.1),
      },
    },
    territoryChip: {
      margin: theme.spacing(0.5, 1, 0.5, 0),
    },
    dialog: {
      width: "100%",
      height: "100%",
      maxWidth: 528,
      maxHeight: 568,
    },
    bold: {
      fontWeight: "bold",
    },
    dialogContent: {
      display: "flex",
      flexDirection: "column",
      justifyContent: "space-between",
    },
  })
);

const territoriesArr = Object.keys(territories).map((el) => territories[el]);

interface FormikTableTerritorySelectProps {
  disabled?: boolean;
}

export const FormikTableTerritorySelect: React.FC<
  FormikTableTerritorySelectProps & FieldProps<any, AgreementFormValues>
> = ({ form: { values, setFieldValue }, field: { name }, disabled }) => {
  const classes = useStyles();
  const selected: string[] = getIn(values, name);
  const [value, setValue] = React.useState<string[]>(selected);
  const [showDialog, setShowDialog] = useState(false);
  const [showAlert, setShowAlert] = useState(true);

  const handleClose = () => {
    setShowDialog(false);
    setValue(selected);
  };
  const handleConfirm = () => {
    setFieldValue(name, value);
    setShowDialog(false);
  };

  const handleOnDelete = (territory: string) => () => {
    const filtered = value.filter((el) => el !== territory);
    setValue(filtered);
  };

  const handleChipClicked = (territory: string) => () => {
    const updated = value.map((el) => {
      if (el === territory) {
        const tisCode = el.slice(1);
        const state = el.slice(0, 1);
        const prefix = state === "I" ? "E" : "I";
        return `${prefix}${tisCode}`;
      }
      return el;
    });
    setValue(updated);
  };

  const handleChange = (event: ChangeEvent<{}>, newValue: Territory[]) => {
    const formatted = newValue.map((el) => {
      const included = `I${el.tisCode}`;
      const excluded = `E${el.tisCode}`;
      return value.includes(excluded) ? excluded : included;
    });
    setValue(formatted);
  };

  const territories = getFormattedTerritories(selected);

  return (
    <React.Fragment>
      {territories.map((el) => (
        <Chip
          key={el}
          label={el}
          onClick={() => setShowDialog(true)}
          className={classes.territoryChip}
        />
      ))}

      <Dialog
        variant="complex"
        open={showDialog}
        onClose={handleClose}
        classes={{ paper: classes.dialog }}
      >
        <DialogTitle>Territories</DialogTitle>
        <DialogContent classes={{ root: classes.dialogContent }}>
          <Box>
            <Autocomplete
              multiple
              disabled={disabled}
              options={territoriesArr}
              getOptionLabel={(option) => option.name}
              renderTags={() => null}
              closeIcon={null}
              onChange={handleChange}
              filterSelectedOptions
              classes={{
                popupIndicator: classes.searchIcon,
              }}
              value={value.reduce((acc: Territory[], curr: string) => {
                const foundTerritory = territoriesArr.find(
                  (c) => c.tisCode === curr.slice(1)
                );
                if (foundTerritory) {
                  return [...acc, foundTerritory];
                }
                return acc;
              }, [])}
              filterOptions={(options, { inputValue }) => {
                const upperCase = inputValue.toUpperCase();
                return options.filter(
                  ({ tisA, name, tisCode }: Territory) =>
                    tisA.includes(upperCase) ||
                    name.includes(upperCase) ||
                    tisCode.includes(upperCase)
                );
              }}
              renderOption={(el: Territory) => (
                <Box className={classes.option}>
                  <Typography>{el.name}</Typography>
                  <Typography color="textSecondary" className={classes.tisA}>
                    {el.tisA}
                  </Typography>
                </Box>
              )}
              renderInput={(params) => (
                <TextField
                  {...params}
                  fullWidth
                  placeholder="Search Territories"
                />
              )}
            />
            <Box pt={5}>
              <React.Fragment>
                {value.length === 0 ? (
                  <Typography color="error">
                    At least 1 territory right is required
                  </Typography>
                ) : (
                  value.map((el) => {
                    const isIncluded = el.slice(0, 1) === "I";
                    return (
                      <Chip
                        key={el}
                        className={clsx(classes.chip, {
                          [classes.chipGreen]: isIncluded,
                          [classes.chipRed]: !isIncluded,
                        })}
                        disabled={disabled}
                        onClick={handleChipClicked(el)}
                        onDelete={handleOnDelete(el)}
                        classes={{ deleteIcon: classes.chipDeleteIcon }}
                        icon={
                          isIncluded ? (
                            <Add16 className={classes.chipIcon} />
                          ) : (
                            <Subtract16 className={classes.chipIcon} />
                          )
                        }
                        label={
                          territoriesArr.find(
                            (territory) => territory.tisCode === el.slice(1)
                          )?.tisA
                        }
                      />
                    );
                  })
                )}
              </React.Fragment>
            </Box>
          </Box>
          {showAlert && (
            <ThemeInverse>
              <TerritoriesInfoBanner onClose={() => setShowAlert(false)} />
            </ThemeInverse>
          )}
        </DialogContent>
        <DialogActions>
          <Button color="primary" onClick={handleClose}>
            Cancel
          </Button>
          <Button
            variant="contained"
            color="primary"
            onClick={handleConfirm}
            disabled={disabled || value.length === 0}
          >
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
};
