import React, { useEffect, useState } from "react";
import {
  Box,
  Button,
  createStyles,
  makeStyles,
  Paper,
  Theme as MaterialUiTheme,
  Typography,
} from "@material-ui/core";
import {
  AppDispatch,
  AppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import {
  getCurrentThemeLoadingStateSelector,
  getCurrentThemeSelector,
  getThemesLoadingStateSelector,
  getThemesSelector,
} from "../../pages/Theme/selectors";
import { LoadingState } from "../../../types/enums";
import { Form, Formik } from "formik";
import { ThemeOptionsDrawer } from "../ThemeOptionsDrawer";
import { ContainerTabPanel } from "../../atoms/ContainerTabPanel";
import { ContainerContent } from "../../atoms/ContainerContent";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { settingsTabs } from "../../pages/Settings";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { TabMenu } from "../../molecules/TabMenu";
import { DialogSetTheme } from "../../atoms/DialogSetTheme/component";
import {
  fetchThemesEffect,
  updateAndSetThemeEffect,
} from "../../pages/Theme/effects";
import clsx from "clsx";
import { ThemePreviewStatic } from "../../atoms/ThemePreviewStatic";
import { toggleThemeDrawerAction } from "../../pages/Theme/actions";

const useStyles = makeStyles((theme: MaterialUiTheme) =>
  createStyles({
    appBarContent: {
      width: "100%",
      marginTop: theme.spacing(6),
      display: "inline-flex",
      alignItems: "baseline",
      justifyContent: "space-between",
      transition: theme.transitions.create(["padding"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    appBarContentShift: {
      paddingLeft: theme.spacing(17),
      transition: theme.transitions.create(["padding"], {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    updateButton: {
      marginLeft: theme.spacing(1.5),
    },

    contentContainer: {
      position: "relative",
      display: "flex",
      flexGrow: 1,
      flexDirection: "column",
    },
    form: {
      height: "100%",
      display: "flex",
      flexDirection: "column",
    },
    innerContainer: {
      padding: theme.spacing(0, 6),
    },
    previewTextWrapper: {
      display: "flex",
      paddingTop: theme.spacing(6.5),
    },
    themeTitleText: {
      marginLeft: theme.spacing(1),
    },
    themePreviewWrapper: {
      paddingTop: theme.spacing(2),
    },
    outerContainer: {
      height: "100%",
      transition: theme.transitions.create("height", {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    contentContainerWrapper: {
      width: "100%",
      height: "100%",
      overflowY: "auto",
    },
    paper: {
      height: "100%",
      position: "relative",
      overflow: "hidden",
    },
    label: {
      fontFamily: "Inter",
    },
  })
);

interface SettingsThemeProps {
  dispatchEffect: AppDispatchEffect;
  dispatch: AppDispatch;
  drawerOpen: boolean;
}

export const SettingsTheme: React.FC<SettingsThemeProps> = ({
  dispatchEffect,
  dispatch,
  drawerOpen,
}) => {
  const [editThemeOpen, setEditThemeOpen] = useState(false);
  const [showSetThemeConfirmDialog, setShowSetThemeConfirmDialog] = useState(
    false
  );
  const classes = useStyles({ drawerOpen });
  const state = useAppSelector((state) => state);
  const themes = getThemesSelector(state);
  const themesLoadingState = getThemesLoadingStateSelector(state);
  const currentTheme = getCurrentThemeSelector(state);
  const currentThemeLoading = getCurrentThemeLoadingStateSelector(state);
  const currentThemeLoaded = [
    LoadingState.FETCHED,
    LoadingState.ERROR,
  ].includes(currentThemeLoading);

  const toggleDrawerOpen = () => dispatch(toggleThemeDrawerAction());

  useEffect(() => {
    if (
      ![
        LoadingState.FETCHED,
        LoadingState.FETCHING,
        LoadingState.ERROR,
      ].includes(themesLoadingState)
    ) {
      dispatchEffect(fetchThemesEffect());
    }
  }, [dispatchEffect, themesLoadingState]);

  const handleSetTheme = (
    event: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    event.preventDefault();
    setShowSetThemeConfirmDialog(true);
  };

  return (
    <ContainerTabPanel>
      {currentThemeLoaded ? (
        <Formik
          initialValues={{ theme: currentTheme }}
          onSubmit={async ({ theme }, { setSubmitting }) => {
            try {
              await dispatchEffect(updateAndSetThemeEffect(theme));
            } catch (e) {}
            setSubmitting(false);
          }}
        >
          {({ values, submitForm, setFieldValue, isSubmitting }) => (
            <Form className={classes.form} data-testid="theme-settings-form">
              <ContainerAppBar
                dispatch={dispatch}
                parentTitle="Settings"
                title="Theme"
                secondaryContent={
                  <Box className={classes.appBarContent}>
                    <TabMenu tabs={settingsTabs} />
                    {!editThemeOpen && (
                      <Box display="inline-flex">
                        <Button color="inherit">Cancel</Button>
                        <ButtonSubmit
                          variant="contained"
                          color="primary"
                          submitting={isSubmitting}
                          className={classes.updateButton}
                          onClick={handleSetTheme}
                        >
                          Set Theme
                        </ButtonSubmit>
                      </Box>
                    )}
                  </Box>
                }
              />
              <Paper square className={classes.paper}>
                <Box className={classes.contentContainerWrapper}>
                  <ContainerContent
                    className={classes.innerContainer}
                    containerClassName={classes.outerContainer}
                  >
                    <Box className={classes.contentContainer}>
                      <Box className={classes.previewTextWrapper}>
                        <Typography
                          variant="caption"
                          className={classes.label}
                          color="textSecondary"
                        >
                          Previewing:
                        </Typography>
                        <Typography
                          variant="caption"
                          className={clsx(
                            classes.label,
                            classes.themeTitleText
                          )}
                        >
                          {values.theme.title}
                        </Typography>
                      </Box>
                      <Box className={classes.themePreviewWrapper}>
                        {/* Currently theme editing is disabled and we only display static image.
                            Will uncomment "ThemePreviewInner" component and update it when Figma designs are final and approved. */}
                        <ThemePreviewStatic />

                        {/*<ThemePreview theme={values.theme} />*/}
                      </Box>
                    </Box>
                  </ContainerContent>
                </Box>
                <ThemeOptionsDrawer
                  dispatchEffect={dispatchEffect}
                  themes={themes.map((t) => t.data)}
                  open={drawerOpen}
                  setOpen={toggleDrawerOpen}
                  setFieldValue={setFieldValue}
                  values={values}
                  onEditThemeClose={() => setEditThemeOpen(false)}
                  onEditThemeOpen={() => setEditThemeOpen(true)}
                  submitForm={submitForm}
                  themesLoadingState={themesLoadingState}
                />
              </Paper>
              {showSetThemeConfirmDialog && (
                <DialogSetTheme
                  onConfirm={() => {
                    setShowSetThemeConfirmDialog(false);
                    submitForm();
                  }}
                  onCancel={() => setShowSetThemeConfirmDialog(false)}
                />
              )}
            </Form>
          )}
        </Formik>
      ) : null}
    </ContainerTabPanel>
  );
};
