import React from "react";
import { AppDispatchEffect } from "../../../App.reducer";
import { Field, Form, Formik } from "formik";
import {
  Box,
  Button,
  Container,
  createStyles,
  Grid,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { TextField } from "formik-material-ui";
import * as yup from "yup";
import { useHistory } from "react-router-dom";
import { resetPasswordEffect } from "../../pages/SignIn/effects";
import { paths } from "../../../utils/common";
import { hasEmptyValues } from "../../../utils/helpers";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      maxWidth: 384,
    },
    gridContainer: {
      textAlign: "center",
    },
    header: {
      paddingBottom: theme.spacing(3.5),
    },
    submitButton: {
      marginTop: theme.spacing(3),
    },
    textInstructions: {
      padding: theme.spacing(0, 1),
    },
  })
);

interface FormPasswordResetProps {
  dispatchEffect: AppDispatchEffect;
}

interface FormValues {
  email: string;
}

const initialValues = {
  email: "",
};

const validationSchema = yup.object().shape({
  email: yup
    .string()
    .trim()
    .email("Email is not valid")
    .required("Email is required"),
});

export const FormPasswordReset: React.FC<FormPasswordResetProps> = ({
  dispatchEffect,
}) => {
  const classes = useStyles();
  const { push } = useHistory();

  const handleSubmit = async (data: FormValues) => {
    try {
      await dispatchEffect(resetPasswordEffect(data.email.trim()));
      push(`${paths.SIGN_IN}/${paths.subPage.RESET_PASSWORD_CONFIRM}`);
    } catch (e) {}
  };

  return (
    <Box textAlign="center">
      <Typography className={classes.header} variant="h5">
        Reset password for your Session account
      </Typography>
      <Container className={classes.container}>
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={handleSubmit}
        >
          {({ isSubmitting, values }) => (
            <Grid
              container
              component={Form}
              spacing={3}
              className={classes.gridContainer}
            >
              <Grid item xs={12}>
                <Typography
                  variant="body1"
                  className={classes.textInstructions}
                >
                  You will receive an email with instructions if the email
                  address provided is registered
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Field
                  component={TextField}
                  name="email"
                  label="Email Address"
                  fullWidth
                />
              </Grid>
              <Grid item xs={12}>
                <Button
                  type="submit"
                  color="primary"
                  variant="contained"
                  fullWidth
                  disabled={isSubmitting || hasEmptyValues(values)}
                  className={classes.submitButton}
                >
                  Reset
                </Button>
              </Grid>
            </Grid>
          )}
        </Formik>
      </Container>
    </Box>
  );
};
