import { AppDispatchEffect } from "../../../App.reducer";
import { fetchBootstrapEffect } from "./effects";
import { paths } from "../../../utils/common";
import { isLoggedOutPath, parseDlrToBlob } from "../../../utils/helpers";

export const handleBootstrapRequest = async (
  dispatchEffect: AppDispatchEffect,
  pathname: string,
  push: (path: string) => void
) => {
  try {
    const { response } = await dispatchEffect(fetchBootstrapEffect());

    if (response.dlr) {
      const blob = parseDlrToBlob(response.dlr);
      push(blob ? `/dlr?b=${blob}` : paths.SIGN_IN);
    } else if (response._links.signIn && !isLoggedOutPath(pathname)) {
      push(paths.SIGN_IN);
    } else if (response._links.signOut && isLoggedOutPath(pathname)) {
      push(paths.INDEX);
    }
  } catch (e) {
    push(paths.SIGN_IN);
  }
};
