import {
  FETCH_NOTIFICATIONS,
  FETCH_NOTIFICATIONS_FAILURE,
  FETCH_NOTIFICATIONS_SUCCESS,
  FETCH_UNSEEN_NOTIFICATIONS_COUNT_SUCCESS,
  NotificationsActionTypes,
  PATCH_NOTIFICATION_SUCCESS,
} from "./actions.types";
import {
  _NotificationsLinks,
  ListMeta,
  Notification,
} from "../../../utils/models";
import { LoadingState } from "../../../types/enums";

export interface NotificationsState {
  notifications: Notification[];
  listMeta?: ListMeta;
  fetchNotificationsLoadingState: LoadingState;
  unseenNotificationsCount: number;
  _links?: _NotificationsLinks;
}

export const notificationsInitialState: NotificationsState = {
  notifications: [],
  fetchNotificationsLoadingState: LoadingState.PRISTINE,
  unseenNotificationsCount: 0,
};

export const notificationsReducer = (
  state: NotificationsState = notificationsInitialState,
  action: NotificationsActionTypes
) => {
  switch (action.type) {
    case FETCH_NOTIFICATIONS: {
      return {
        ...state,
        fetchNotificationsLoadingState: action.next
          ? LoadingState.FETCHED
          : LoadingState.FETCHING,
      };
    }
    case FETCH_NOTIFICATIONS_SUCCESS: {
      return {
        ...state,
        notifications: action.next
          ? [...state.notifications, ...action.notifications]
          : action.notifications,
        unseenNotificationsCount: 0,
        _links: action._links,
        listMeta: action.meta,
        fetchNotificationsLoadingState: LoadingState.FETCHED,
      };
    }
    case FETCH_NOTIFICATIONS_FAILURE: {
      return {
        ...state,
        fetchNotificationsLoadingState: LoadingState.ERROR,
      };
    }
    case FETCH_UNSEEN_NOTIFICATIONS_COUNT_SUCCESS: {
      return {
        ...state,
        unseenNotificationsCount: action.count,
      };
    }
    case PATCH_NOTIFICATION_SUCCESS: {
      return {
        ...state,
        notifications: state.notifications.map((notification) => {
          if (notification.guid === action.guid) {
            notification.isUnread = false;
          }
          return notification;
        }),
      };
    }

    default:
      return state;
  }
};
