import React from "react";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { useListEmployeesStyles } from "../ListEmployees";
import { GridListHeaderItem } from "../../atoms/GridListHeaderItem";

export const HeaderListEmployees: React.FC = () => {
  const classes = useListEmployeesStyles();

  return (
    <ContainerListItem leftOffset={5} rightOffset={2}>
      <GridListHeaderItem className={classes.avatarColumn} />
      <GridListHeaderItem className={classes.nameColumn}>
        <ListColumnTitle>Name</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.roleColumn}>
        <ListColumnTitle>Role</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.dateColumn}>
        <ListColumnTitle>Created</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.optionsColumn} />
    </ContainerListItem>
  );
};
