import React from "react";
import { WorkRegistrationValidationError } from "../../../utils/models";
import {
  Box,
  createStyles,
  ListItem,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { CloseFilled24 } from "@carbon/icons-react";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      alignItems: "center",
      width: "100%",
      minHeight: 56,
      backgroundColor: theme.palette.background.paper,
      padding: theme.spacing(2, 2.5),
      border: `1px solid ${theme.palette.action.selected}`,
      borderRadius: theme.spacing(1),
      marginBottom: theme.spacing(1),
    },
    label: {
      fontFamily: "Inter",
    },
    textWrapper: {
      display: "flex",
      alignSelf: "center",
    },
    iconWrapper: {
      paddingRight: theme.spacing(3),
      display: "flex",
      alignSelf: "center",
    },
    icon: {
      color: theme.palette.error.main,
    },
    time: {
      paddingLeft: theme.spacing(3),
      marginLeft: "auto",
      display: "flex",
      alignSelf: "center",
    },
  })
);

interface WorkValidationErrorPanelProps {
  error: WorkRegistrationValidationError;
  time: string;
}

export const WorkValidationErrorPanel: React.FC<WorkValidationErrorPanelProps> = ({
  error,
  time,
}) => {
  const classes = useStyles();
  return (
    <ListItem className={classes.root}>
      <Box className={classes.iconWrapper}>
        <CloseFilled24 className={classes.icon} />
      </Box>
      <Box className={classes.textWrapper}>
        <Typography
          variant="caption"
          color="textSecondary"
          className={classes.label}
        >
          {error.message}
        </Typography>
      </Box>
      <Box className={classes.time}>
        <Typography
          variant="caption"
          color="textSecondary"
          className={classes.label}
        >
          {time.replace(" ", "").toLowerCase()}
        </Typography>
      </Box>
    </ListItem>
  );
};
