import React, { useState } from "react";
import { Field, Form, Formik } from "formik";
import {
  Box,
  Button,
  createStyles,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { TextField } from "formik-material-ui";
import * as yup from "yup";
import { AppDispatchEffect } from "../../../App.reducer";
import { postCompanyInviteEffect } from "../../pages/Company/effects";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { FabExtended } from "../../atoms/FabExtended";
import { Dialog } from "../../molecules/Dialog";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dialog: {
      width: 560,
    },
    fab: {
      position: "absolute",
      bottom: theme.spacing(4),
      right: theme.spacing(4),
      zIndex: 1,
    },
  })
);

interface DialogEmployeeInviteProps {
  dispatchEffect: AppDispatchEffect;
  onInviteSuccess?: () => void;
  companyName: string;
  showButton: boolean;
  isSocietyAdmin: boolean;
}

export const DialogEmployeeInvite: React.FC<DialogEmployeeInviteProps> = ({
  companyName,
  dispatchEffect,
  onInviteSuccess,
  showButton,
  isSocietyAdmin,
}) => {
  const classes = useStyles();
  const [showDialog, setShowDialog] = useState(false);
  const initialValues = { email: "" };
  return (
    <React.Fragment>
      {showButton && (
        <FabExtended
          className={classes.fab}
          onClick={() => setShowDialog(true)}
        >
          New Invite
        </FabExtended>
      )}

      <Formik
        initialValues={initialValues}
        validationSchema={() =>
          yup.object().shape({
            email: yup
              .string()
              .trim()
              .email("Email is not valid")
              .required("Required"),
          })
        }
        onSubmit={async (values, { resetForm }) => {
          try {
            await dispatchEffect(postCompanyInviteEffect(values.email.trim()));
            setShowDialog(false);
            resetForm({ values: initialValues });
            onInviteSuccess && onInviteSuccess();
          } catch (e) {}
        }}
      >
        {({ isSubmitting, values, resetForm }) => (
          <Dialog
            variant="simple"
            open={showDialog}
            onClose={() => {
              setShowDialog(false);
              resetForm({ values: initialValues });
            }}
            classes={{ paper: classes.dialog }}
          >
            <Form>
              <DialogTitle>
                {isSocietyAdmin
                  ? "Invite Society Employee"
                  : "Invite Publisher Employee"}
              </DialogTitle>
              <DialogContent>
                <Grid container>
                  <Grid item xs={12} component={Box} pb={2}>
                    <Typography variant="subtitle1" color="textSecondary">
                      Please enter the email address of an employee of&nbsp;
                      {companyName}. Invites will automatically expire
                      after&nbsp;
                      <strong>7 days</strong>.
                    </Typography>
                  </Grid>
                  <Grid item xs={12} component={Box} pb={3}>
                    <Field
                      component={TextField}
                      fullWidth
                      name="email"
                      label="Email Address"
                    />
                  </Grid>
                </Grid>
              </DialogContent>
              <DialogActions>
                <Button
                  variant="text"
                  color="primary"
                  onClick={() => {
                    setShowDialog(false);
                    resetForm({ values: initialValues });
                  }}
                >
                  Cancel
                </Button>
                <ButtonSubmit
                  variant="text"
                  disabled={!values.email}
                  submitting={isSubmitting}
                >
                  Invite
                </ButtonSubmit>
              </DialogActions>
            </Form>
          </Dialog>
        )}
      </Formik>
    </React.Fragment>
  );
};
