import React, { useState } from "react";
import {
  Box,
  Button,
  Checkbox,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControlLabel as MuiFormControlLabel,
  makeStyles,
  Typography,
} from "@material-ui/core";
import { createStyles, withStyles } from "@material-ui/core/styles";
import { getDialogAlertCacheKey } from "./utils";
import { Dialog } from "../Dialog";

const useStyles = makeStyles(() =>
  createStyles({
    dialog: {
      maxWidth: 339,
      width: "100%",
    },
  })
);

const FormControlLabel = withStyles((theme) => ({
  label: {
    ...theme.typography.body2,
    color: theme.palette.text.secondary,
  },
}))(MuiFormControlLabel);

interface DialogRemoveAgreementTermProps {
  onConfirm: () => void;
  closeDialog: () => void;
  profileRef: string;
}

export const DialogRemoveAgreementTerm: React.FC<DialogRemoveAgreementTermProps> = ({
  onConfirm,
  closeDialog,
  profileRef,
}) => {
  const classes = useStyles();
  const [checked, setChecked] = useState(false);
  const handleChecked = () => {
    setChecked(!checked);
  };

  const handleConfirm = () => {
    if (checked) {
      localStorage.setItem(getDialogAlertCacheKey(profileRef), "true");
    }
    onConfirm();
    closeDialog();
  };
  return (
    <Dialog
      variant="simple"
      open={true}
      onClose={closeDialog}
      classes={{ paper: classes.dialog }}
    >
      <DialogTitle>Are you sure?</DialogTitle>
      <DialogContent>
        <Box pb={1}>
          <Typography color="textSecondary" variant="body2">
            You will also delete the shares associated with this term.
          </Typography>
        </Box>
        <FormControlLabel
          control={
            <Checkbox
              checked={checked}
              onChange={handleChecked}
              color="primary"
            />
          }
          label="Dont show this message again"
        />
      </DialogContent>
      <DialogActions>
        <Button variant="text" color="primary" onClick={closeDialog}>
          Cancel
        </Button>
        <Button variant="text" color="primary" onClick={handleConfirm}>
          Remove
        </Button>
      </DialogActions>
    </Dialog>
  );
};
