import React, { useEffect, useState } from "react";
import { Prompt } from "react-router-dom";
import {
  Box,
  Button,
  createStyles,
  DialogActions,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import * as H from "history";
import { Dialog } from "../../molecules/Dialog";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dialogActions: {
      display: "flex",
      justifyContent: "space-between",
      padding: theme.spacing(1, 2),
    },
    saveButton: {
      minWidth: 0,
    },
    dialogTitle: {
      padding: theme.spacing(2, 3),
    },
  })
);

interface LeavingGuardProps {
  shouldBlockNavigation: (location: H.Location) => boolean;
  when: boolean;
  navigate: (value: string) => void;
  onSubmitPromise: () => Promise<void>;
}

export const LeavingGuard: React.FC<LeavingGuardProps> = ({
  shouldBlockNavigation,
  when,
  navigate,
  onSubmitPromise,
}) => {
  const [dialogOpen, setDialogOpen] = useState(false);
  const [lastLocation, setLastLocation] = useState<any>(null);
  const [submitting, setSubmitting] = useState(false);
  const [confirmedNavigation, setConfirmedNavigation] = useState(false);

  const classes = useStyles();

  const showDialog = (location: any) => {
    setDialogOpen(true);
    setLastLocation(location);
  };

  const closeDialog = (callback?: () => void) => {
    setDialogOpen(false);
    callback && callback();
  };

  const handleBlockedNavigation = (nextLocation: H.Location) => {
    if (!confirmedNavigation && shouldBlockNavigation(nextLocation)) {
      showDialog(nextLocation);
      return false;
    }
    return true;
  };
  const handleConfirmNavigationClick = () => {
    closeDialog(() => {
      if (lastLocation) {
        setConfirmedNavigation(true);
      }
    });
  };

  const handleSave = async () => {
    setSubmitting(true);
    try {
      await onSubmitPromise();
      setSubmitting(false);
      handleConfirmNavigationClick();
    } catch (e) {
      setSubmitting(false);
      setDialogOpen(false);
    }
  };

  useEffect(() => {
    if (confirmedNavigation && lastLocation) {
      navigate(lastLocation.pathname);
    }
  }, [confirmedNavigation, lastLocation, navigate]);

  return (
    <React.Fragment>
      <Prompt when={when} message={handleBlockedNavigation} />
      <Dialog variant="simple" open={dialogOpen} onClose={() => closeDialog()}>
        <Typography variant="subtitle1" className={classes.dialogTitle}>
          Would you like to save changes?
        </Typography>
        <DialogActions className={classes.dialogActions}>
          <Button onClick={() => closeDialog()} color="primary">
            Cancel
          </Button>
          <Box>
            <Button color="primary" onClick={handleConfirmNavigationClick}>
              Discard
            </Button>
            <ButtonSubmit
              color="primary"
              variant="text"
              onClick={handleSave}
              submitting={submitting}
              className={classes.saveButton}
            >
              Save
            </ButtonSubmit>
          </Box>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
};
