import { AppState } from "../../../App.reducer";

export const getSidebarOffsetSelector = (state: AppState) => {
  const { isSidebarOpen, variant, sidebarWidth } = state.sidebar;
  const { authenticated } = state.signIn;

  if (!isSidebarOpen || !authenticated) {
    return 0;
  }

  return variant === "persistent" ? sidebarWidth : 0;
};

export const getIsSidebarOpenSelector = (state: AppState) =>
  state.sidebar.isSidebarOpen;

export const getSidebarVariantSelector = (state: AppState) =>
  state.sidebar.variant;
