import React, { useState } from "react";
import {
  Avatar,
  createStyles,
  IconButton,
  makeStyles,
  Theme,
} from "@material-ui/core";
import { PartyRoleType, PartyRoleTypeLabel } from "../../../types/enums";
import {
  BootstrapAccount,
  ColumnBreakpoints,
  DRAFT,
  Ipi,
  SUCCESS,
  Work,
  WorkCreator,
} from "../../../utils/models";
import { FastField, Field } from "formik";
import { AppDispatchEffect } from "../../../App.reducer";
import { useHistory } from "react-router";
import { paths } from "../../../utils/common";
import { FormikPercentageInput } from "../../atoms/FormikPercentageInput";
import { CellMrShare } from "../../atoms/CellMrShare";
import { DialogLeaveWork } from "../DialogLeaveWork";
import { CellSplit } from "../../atoms/CellSplit";
import { CellAvatar } from "../../atoms/CellAvatar";
import { mapStatusToTooltip } from "../../atoms/FormikSplitStatusIndicator";
import { FormikEnumSelect } from "../FormikEnumSelect";
import { CellOptions } from "../../atoms/CellOptions";
import { CellRole } from "../../atoms/CellRole";
import { StatusIndicator } from "../../atoms/StatusIndicator";
import { CellCreator } from "../../atoms/CellCreator";
import { CellPrShare } from "../../atoms/CellPrShare";
import { CellCollaboration } from "../../atoms/CellCollaboration";
import { CardCreator } from "../../atoms/CardCreator";
import { ActionMenu } from "../../organisms/ActionMenu";
import { RowCreatorListItem } from "../RowCreatorListItem";
import { TooltipMap } from "../../atoms/TooltipMap";
import { Icon } from "../../atoms/Icon";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    avatar: {
      marginLeft: "auto",
      marginTop: theme.spacing(-0.5),
    },
    roleSelect: {
      height: 40,
    },
    roleContainer: {
      display: "flex",
      alignContent: "center",
    },
    icon: {
      color: theme.palette.info.main,
    },
    actionMenuButton: {
      width: theme.spacing(5),
      height: theme.spacing(5),
      marginTop: theme.spacing(-0.5),
    },
  })
);

interface RowGridCreatorProps {
  work?: Work;
  creator: WorkCreator;
  creatorPath: string;
  hideSplitColumn?: boolean;
  hideOptionsColumn?: boolean;
  hideCollaborationColumn?: boolean;
  onDelete: (guid: string) => void;
  editable?: boolean;
  dispatchEffect: AppDispatchEffect;
  isAuthor: boolean;
  isTemplate?: boolean;
  ipiList: Ipi[];
  columnBreakPoints: ColumnBreakpoints;
  isPublisher?: boolean;
  isSubmitting?: boolean;
  account?: BootstrapAccount;
}

export const RowGridCreator: React.FC<RowGridCreatorProps> = ({
  onDelete,
  ipiList,
  creator,
  creatorPath,
  hideOptionsColumn,
  hideSplitColumn,
  hideCollaborationColumn,
  editable,
  dispatchEffect,
  isAuthor,
  isTemplate,
  columnBreakPoints: bp,
  work,
  isPublisher,
  isSubmitting,
  account,
}) => {
  const classes = useStyles();
  const [showLeaveWorkDialog, setShowLeaveWorkDialog] = useState(false);
  const { push } = useHistory();
  const {
    profile: { displayName, initials },
    ipi: { ipiReference },
  } = creator;

  const canLeaveWork = !isTemplate && isAuthor && Boolean(work?._links.leave);

  //TODO:: attributes currently not received from endpoints
  const isni = "",
    ipn = "",
    company = "";

  const leavePage = () =>
    work?.status === DRAFT
      ? push(paths.WORKS_DRAFT_LIST)
      : push(paths.WORKS_LIST);

  const handleLeave = () =>
    work?.guid ? setShowLeaveWorkDialog(true) : leavePage();
  return (
    <React.Fragment>
      {dispatchEffect && (
        <DialogLeaveWork
          isPublisher={isPublisher}
          work={work}
          dispatchEffect={dispatchEffect}
          ipiReference={ipiReference}
          showDialog={showLeaveWorkDialog}
          onLeave={() => leavePage()}
          hideDialog={() => {
            setShowLeaveWorkDialog(false);
          }}
        />
      )}
      <RowCreatorListItem>
        <CellAvatar>
          <Avatar className={classes.avatar}>
            {initials || (isAuthor ? account?.profile.initials : "")}
          </Avatar>
        </CellAvatar>
        <CellCreator dense={bp.creator}>
          <CardCreator
            editable={editable}
            dispatchEffect={dispatchEffect}
            ipiList={ipiList}
            isAuthor={isAuthor}
            creatorPath={creatorPath}
            multiline
            wrapDisplayName
            creator={{
              displayName,
              ipi: Number(creator.ipi?.ipiNameNumber),
              ipiVerified: false, //TODO:: fix ipi?.verified
              isni,
              ipn,
              company,
            }}
          />
        </CellCreator>
        <CellRole dense={bp.role}>
          <Field
            className={classes.roleSelect}
            options={PartyRoleType}
            optionToLabel={{
              [PartyRoleType.C]: bp.role ? "C" : PartyRoleTypeLabel.C,
              [PartyRoleType.A]: bp.role ? "A" : PartyRoleTypeLabel.A,
              [PartyRoleType.CA]: bp.role ? "CA" : PartyRoleTypeLabel.CA,
            }}
            name={`${creatorPath}.role`}
            type="text"
            disableUnderline
            component={FormikEnumSelect}
            disabled={editable ? undefined : !editable}
            fullWidth
          />
        </CellRole>
        <CellPrShare dense={bp.prShare}>
          <TooltipMap to="CreatorShareInputs">
            <FastField
              name={`${creatorPath}.prShare`}
              component={FormikPercentageInput}
              disabled={!editable}
              dense={bp.prShare}
            />
          </TooltipMap>
        </CellPrShare>
        <CellMrShare dense={bp.mrShare}>
          <TooltipMap to="CreatorShareInputs">
            <FastField
              name={`${creatorPath}.mrShare`}
              component={FormikPercentageInput}
              disabled={!editable}
              dense={bp.mrShare}
            />
          </TooltipMap>
        </CellMrShare>
        {!(hideSplitColumn || bp.split) && (
          <CellSplit>
            <StatusIndicator
              status={SUCCESS}
              mapStatusToTooltip={mapStatusToTooltip}
            />
          </CellSplit>
        )}
        {!(hideCollaborationColumn || bp.collaboration) && (
          <CellCollaboration>
            <StatusIndicator
              status={SUCCESS}
              mapStatusToTooltip={mapStatusToTooltip}
            />
          </CellCollaboration>
        )}
        {!hideOptionsColumn && editable && (
          <CellOptions>
            <IconButton size="small" className={classes.actionMenuButton}>
              <ActionMenu
                items={[
                  {
                    label: canLeaveWork ? "Leave work" : "Remove",
                    onClick: () =>
                      canLeaveWork ? handleLeave() : onDelete(""), //TODO:: fix
                    disabled: isSubmitting,
                  },
                ]}
              >
                <Icon
                  name="listItemOptions"
                  data-testid={`${creatorPath}.options`}
                />
              </ActionMenu>
            </IconButton>
          </CellOptions>
        )}
      </RowCreatorListItem>
    </React.Fragment>
  );
};
