import {
  createStyles,
  InputAdornment,
  makeStyles,
  TextField,
  Theme,
} from "@material-ui/core";
import React, { ChangeEvent } from "react";
import { FieldProps, getIn } from "formik";
import { handleKeyDown } from "./utils";
import { TextFieldProps } from "@material-ui/core/TextField/TextField";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    filledInput: {
      width: 72,
      /* Chrome, Safari, Edge, Opera */
      "& input::-webkit-inner-spin-button, & input::-webkit-outer-spin-button": {
        "-webkit-appearance": "none",
        margin: 0,
      },
      /* Firefox */
      "& input[type=number]": {
        "-moz-appearance": "textfield",
      },
      "& > div": {
        height: 39,
        flexDirection: "row-reverse",
        paddingLeft: 0,
      },
      "& input": {
        textAlign: "right",
        padding: theme.spacing(0.5),
      },
      "& .Mui-error": {
        marginLeft: 0,
        marginRight: 0
      },
    },
    dense: {
      width: 80,
    },
    adornment: {
      margin: theme.spacing(0, 1, 0, 0),
      "& p": {
        color: theme.palette.info.main,
      },
    },
  })
);

interface FormikPercentageInputProps extends FieldProps {
  disabled?: boolean;
  dense?: boolean;
  handleChange?: (event: ChangeEvent<HTMLTextAreaElement>) => void;
  handleOnFocus?: () => void;
  handleOnBlur?: (event: React.FocusEvent<HTMLInputElement>) => void;
  value?: string;
}

export const FormikPercentageInput: React.FC<
  FormikPercentageInputProps & TextFieldProps
> = ({
  form: {
    setFieldValue,
    isSubmitting,
    errors,
    touched,
    handleBlur,
    submitCount,
  },
  field: { name, value },
  disabled,
  dense,
  handleChange: handleChangeProp,
  handleOnFocus: handleOnFocusProp,
  handleOnBlur: handleOnBlurProp,
  value: valueProp,
  helperText: helperTextProp,
  ...props
}) => {
  const classes = useStyles();
  const handleChange = (e: ChangeEvent<HTMLTextAreaElement>) => {
    setFieldValue(name, e.target.value === "" ? "" : Number(e.target.value));
  };

  const handleOnFocus = () => {
    if (!disabled) {
      value === 0 && setFieldValue(name, "");
    }
  };

  const handleOnBlur = (event: React.FocusEvent<HTMLInputElement>) => {
    value === "" && setFieldValue(name, 0);
    handleBlur(event);
  };

  const formatValue = () => {
    if (valueProp !== undefined) {
      return valueProp;
    }
    return value || value === 0 ? value : "";
  };

  return (
    <TextField
      {...props}
      name={name}
      onChange={handleChangeProp || handleChange}
      onBlur={handleOnBlurProp || handleOnBlur}
      onFocus={handleOnFocusProp || handleOnFocus}
      disabled={isSubmitting || disabled}
      helperText={
        helperTextProp !== undefined
          ? helperTextProp
          : (getIn(touched, name) || submitCount > 0) && getIn(errors, name)
      }
      error={
        (getIn(touched, name) || submitCount > 0) &&
        Boolean(getIn(errors, name))
      }
      value={formatValue()}
      type="number"
      variant="filled"
      className={`${classes.filledInput} ${dense ? classes.dense : ""}`}
      inputProps={{
        "data-testid": name,
      }}
      InputProps={{
        onKeyDown: handleKeyDown,
        startAdornment: (
          <InputAdornment className={classes.adornment} position="end">
            %
          </InputAdornment>
        ),
      }}
    />
  );
};
