import React, { useState } from "react";
import {
  Box,
  ClickAwayListener,
  createStyles,
  makeStyles,
  Theme,
  Tooltip,
  Typography,
} from "@material-ui/core";
import {
  TooltipMap as TooltipMapType,
  TooltipTypes,
} from "../../../types/tooltips";
import { AppDispatch } from "../../../App.reducer";
import {
  openDrawerAction,
  setFaqPathAction,
  setItemAction,
} from "../../pages/Faq/actions";
import clsx from "clsx";
import { FaqLink } from "../../../utils/models";

interface TooltipMapProps {
  className?: string;
  classNamePopper?: string;
  variant?: "default" | "listItems" | "clickable";
  enterDelay?: number;
  faqLink?: FaqLink;
  dispatch?: AppDispatch;
  to?: TooltipTypes;
  tooltipText?: string;
  disableCursor?: boolean;
  children: React.ReactNode;
  offset?: number;
  placement?:
    | "bottom-end"
    | "bottom-start"
    | "bottom"
    | "left-end"
    | "left-start"
    | "left"
    | "right-end"
    | "right-start"
    | "right"
    | "top-end"
    | "top-start"
    | "top";
}

interface StyleProps {
  offset?: number;
  disableCursor?: boolean;
  clickable?: boolean;
}

const useStyles = makeStyles<Theme, StyleProps>((theme) =>
  createStyles({
    tooltipWrapper: {
      display: "flex", // align children content
      cursor: ({ disableCursor, clickable }) => {
        if (clickable) {
          return "pointer";
        }
        if (disableCursor) {
          return "inherit";
        }
        return "help";
      },
    },
    learnMoreWrapper: {
      marginTop: theme.spacing(1.5),
    },
    learnMoreText: {
      color: theme.palette.action.active,
      textDecoration: "underline",
      fontWeight: "bold",
      cursor: "pointer",
    },
    popper: ({ offset }) => ({
      ...(offset && { marginTop: theme.spacing(offset) }),
      whiteSpace: "normal",
      textAlign: "left",
    }),
  })
);

const variantListItemsProps = {
  enterDelay: 300,
  placement: "bottom-start",
};

export const TooltipMap: React.FC<TooltipMapProps> = ({
  className,
  classNamePopper,
  to,
  tooltipText,
  placement,
  enterDelay = 1500,
  children,
  faqLink,
  disableCursor: disableCursorProp = false,
  dispatch,
  variant = "default",
  offset,
}: TooltipMapProps) => {
  const [open, setOpen] = useState(false);
  const disableCursor = variant === "listItems" ? true : disableCursorProp;
  const classes = useStyles({
    disableCursor,
    offset,
    clickable: variant === "clickable",
  });
  const hasFaqLink = Boolean(faqLink);

  const onOpen = () => {
    if (tooltipText || (to && Boolean(tooltips[to]))) {
      setOpen(true);
    }
  };
  const onClose = () => setOpen(false);

  const handleFaqLinkClick = () => {
    if (faqLink && dispatch) {
      dispatch(setFaqPathAction(faqLink.path));
      dispatch(setItemAction(faqLink.item));
      dispatch(openDrawerAction());
      onClose();
    }
  };

  const props = variant === "listItems" ? variantListItemsProps : {};
  const commonProps = {
    open: open,
    onOpen: onOpen,
    onClose: onClose,
    className: clsx(classes.tooltipWrapper, className),
    classes: {
      popper: clsx(classes.popper, classNamePopper),
    },
    placement: placement || "bottom",
    enterDelay: enterDelay,
    interactive: hasFaqLink && open,
    title: (
      <React.Fragment>
        {tooltipText || (to && tooltips[to])}
        {hasFaqLink && (
          <Box className={classes.learnMoreWrapper}>
            <Typography
              variant="caption"
              className={classes.learnMoreText}
              onClick={handleFaqLinkClick}
            >
              Learn more
            </Typography>
          </Box>
        )}
      </React.Fragment>
    ),
    ...props,
  };

  if (variant === "clickable") {
    return (
      <ClickAwayListener onClickAway={onClose}>
        <Tooltip
          PopperProps={{
            disablePortal: true,
          }}
          disableFocusListener
          disableHoverListener
          disableTouchListener
          {...commonProps}
        >
          {React.isValidElement(children) ? (
            React.cloneElement(children, {
              onClick: onOpen,
            })
          ) : (
            <React.Fragment />
          )}
        </Tooltip>
      </ClickAwayListener>
    );
  }

  return (
    <Tooltip {...commonProps}>
      <span>{children}</span>
    </Tooltip>
  );
};

