import React, { useState } from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import {
  Box,
  Grid,
  IconButton,
  ListItem,
  ListItemSecondaryAction,
  Typography,
} from "@material-ui/core";
import Radio from "@material-ui/core/Radio";
import { AssociatedWork, Ipi } from "../../../utils/models";
import { AppDispatchEffect, useAppSelector } from "../../../App.reducer";
import { patchIpiEffect } from "../../pages/Ipi/effects";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { DialogDeleteIpi } from "../../molecules/DialogDeleteIpi";
import { ActionMenu } from "../ActionMenu";
import { TooltipMap } from "../../atoms/TooltipMap";
import { HeaderListIpiNumbers } from "../HeaderListIpiNumbers";
import { TableList } from "../../molecules/TableList";
import { DialogWarning } from "../../molecules/DialogWarning";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import { Icon } from "../../atoms/Icon";
import { getDeleteIpiLinkSelector } from "../../pages/Ipi/selectors";

export const useListIpiNumbersStyles = makeStyles((theme: Theme) =>
  createStyles({
    listRow: {
      padding: theme.spacing(1.5, 0),
    },
    list: {
      paddingTop: theme.spacing(2),
    },
    ipiNumberColumn: {
      position: "relative",
      maxWidth: 183,
      minWidth: 100,
      width: "100%",
    },
    ipiNameColumn: {
      minWidth: 100,
      width: "100%",
    },
    societyColumn: {
      maxWidth: 142,
      minWidth: 65,
      width: "100%",
    },
    statusColumn: {
      minWidth: 120,
      textAlign: "center",
    },
    defaultColumn: {
      minWidth: 120,
      maxWidth: 200,
      width: "100%",
      display: "flex",
      justifyContent: "end",
      paddingLeft: theme.spacing(5),
    },
    radioButton: {
      padding: 0,
      marginLeft: theme.spacing(1.5),
    },
    verifyButton: {
      paddingTop: 0,
      paddingBottom: 0,
    },
    verifiedIcon: {
      marginLeft: theme.spacing(0.5),
      width: 13,
      height: 13,
    },
  })
);

interface ListIPINumbersProps {
  ipiList: Ipi[];
  dispatchEffect: AppDispatchEffect;
}

export const ListIpiNumbers: React.FC<ListIPINumbersProps> = ({
  ipiList,
  dispatchEffect,
}) => {
  const classes = useListIpiNumbersStyles();
  const state = useAppSelector((state) => state);
  const [ipiSelectedForDelete, setIpiSelectedForDelete] = useState<Ipi | null>(
    null
  );
  const [associatedWorks, setAssociatedWorks] = useState<AssociatedWork[]>([]);
  const [changeDefaultIpi, setChangeDefaultIpi] = useState<Ipi>();
  const [patchIpiLoading, setPatchIpiLoading] = useState(false);

  const handleDeleteClick = async (ipi: Ipi) => {
    //  TODO:: Not implemented on BE
    // const associatedWorks = await dispatchEffect(
    //   fetchIpiAssociationsEffect(ipi.ipiReference)
    // );
    // if (associatedWorks) {
    //   setIpiSelectedForDelete(ipi);
    //   setAssociatedWorks(associatedWorks);
    // }
  };

  const handleDefaultIpiChange = (ipi: Ipi) => () => {
    setChangeDefaultIpi({ ...ipi, default: true });
  };

  const actionMenuItems = (ipi: Ipi) =>
    [
      {
        label: "Delete",
        onClick: () => handleDeleteClick(ipi),
        access: getDeleteIpiLinkSelector,
      },
    ].filter(({ access }) => (access ? Boolean(access(state)) : true));

  return (
    <React.Fragment>
      {changeDefaultIpi && (
        <DialogWarning
          width={450}
          labelCancel="Cancel"
          labelConfirm="Yes"
          dialogTitle="Set default IPI"
          message="Are you sure you want to update your default IPI?"
          onCancel={() => setChangeDefaultIpi(undefined)}
          isLoading={patchIpiLoading}
          onConfirm={async () => {
            setPatchIpiLoading(true);
            try {
              await dispatchEffect(patchIpiEffect(changeDefaultIpi));
            } catch (e) {
            } finally {
              setPatchIpiLoading(false);
              setChangeDefaultIpi(undefined);
            }
          }}
        />
      )}

      {ipiList.length > 0 && (
        <TableList header={<HeaderListIpiNumbers />}>
          {ipiList.map((el) => (
            <ListItem
              button
              className={classes.listRow}
              key={el.ipiReference}
              onClick={handleDefaultIpiChange(el)}
            >
              <ContainerListItem leftOffset={6}>
                <Grid item className={classes.ipiNumberColumn}>
                  <Typography noWrap>{el.ipiNameNumber}</Typography>
                </Grid>
                <Grid item className={classes.ipiNameColumn}>
                  <Typography color="textSecondary" noWrap>
                    {el.profile.displayName}
                  </Typography>
                </Grid>
                <Grid item className={classes.societyColumn}>
                  <Typography color="textSecondary" noWrap>
                    {el?.prSocietyAffiliation?.acronym}
                  </Typography>
                </Grid>
                <Grid item className={classes.statusColumn}>
                  {el.verified ? (
                    <Box display="inline-flex" alignItems="center">
                      <Typography variant="body1" color="textSecondary">
                        Verified
                      </Typography>
                      <CheckCircleIcon
                        color="primary"
                        className={classes.verifiedIcon}
                      />
                    </Box>
                  ) : (
                    <TooltipMap to="UnverifiedIpiStatus">
                      <Typography variant="body1" color="textSecondary">
                        Unverified
                      </Typography>
                    </TooltipMap>
                  )}
                </Grid>
                <Grid item className={classes.defaultColumn}>
                  <Radio
                    className={classes.radioButton}
                    checked={Boolean(el.default)}
                    name="radio-button"
                    color="primary"
                    inputProps={{ "aria-label": "Default" }}
                  />
                </Grid>
              </ContainerListItem>
              <ListItemSecondaryAction>
                <ActionMenu items={actionMenuItems(el)}>
                  <IconButton>
                    <Icon name="listItemOptions" />
                  </IconButton>
                </ActionMenu>
              </ListItemSecondaryAction>
            </ListItem>
          ))}
        </TableList>
      )}
      {ipiSelectedForDelete && (
        <DialogDeleteIpi
          ipi={ipiSelectedForDelete}
          associatedWorks={associatedWorks}
          showDialog={Boolean(ipiSelectedForDelete)}
          hideDialog={() => {
            setIpiSelectedForDelete(null);
            setAssociatedWorks([]);
          }}
          dispatchEffect={dispatchEffect}
        />
      )}
    </React.Fragment>
  );
};
