import React from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { Box, Typography } from "@material-ui/core";
import { Close32, Information32 } from "@carbon/icons-react";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    infoBanner: {
      display: "inline-flex",
      alignItems: "center",
      width: "100%",
      padding: theme.spacing(2),
      backgroundColor: theme.palette.background.default,
      borderRadius: theme.spacing(1),
    },
    text: {
      color: theme.palette.info.main,
    },
    informationIcon: {
      width: 40,
      height: 40,
      color: theme.palette.info.main,
      marginRight: theme.spacing(2),
    },
    closeIcon: {
      color: theme.palette.info.main,
      marginLeft: theme.spacing(2),
    },
  })
);

interface TerritoriesInfoBannerProps {
  onClose: () => void;
}

export const TerritoriesInfoBanner: React.FC<TerritoriesInfoBannerProps> = ({
  onClose,
}) => {
  const classes = useStyles();
  return (
    <Box className={classes.infoBanner}>
      <Information32 className={classes.informationIcon} />
      <Typography variant="body1" className={classes.text}>
        Click the territory to exclude (-) or include (+) from the agreement
      </Typography>
      <Close32 className={classes.closeIcon} onClick={onClose} />
    </Box>
  );
};
