import {
  createStyles,
  makeStyles,
  Theme,
  useTheme,
} from "@material-ui/core/styles";
import React from "react";
import { useMediaQuery } from "@material-ui/core";
import { useHistory } from "react-router";
import { SortOrder, WorkSortVariant } from "../../../utils/models";
import { AppDispatch } from "../../../App.reducer";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { TableSortLabel } from "../../atoms/TableSortLabel";
import "url-polyfill";
import { TooltipMap } from "../../atoms/TooltipMap";
import { tooltipFaqLinks } from "../FaqDrawer/utils";
import { getSortBy, getSortOrder } from "./utils";
import { GridListHeaderItem } from "../../atoms/GridListHeaderItem";

export const useTableWorksStyles = makeStyles((theme: Theme) =>
  createStyles({
    listRow: {
      minHeight: 85,
    },
    listItemText: {
      // Production build somehow has lower priority of styles injected.
      // Workaround for forcing override.
      "&&": {
        marginTop: 0,
        marginBottom: 0,
      },
    },
    workName: {
      textOverflow: "ellipsis",
      overflow: "hidden",
      "& span, & p": {
        overflow: "hidden",
        textOverflow: "ellipsis",
        whiteSpace: "nowrap",
      },
    },
    statusColumn: {
      minWidth: 50,
      maxWidth: 50,
      textAlign: "center",

      [theme.breakpoints.only("xs")]: {
        minWidth: 40,
        maxWidth: 40,
      },
    },
    nameColumn: {
      width: "100%",
      minWidth: 85,
    },
    iswcColumn: {
      minWidth: 125,
      maxWidth: 300,
      paddingLeft: theme.spacing(2),
      textAlign: "left",
      width: "100%",
    },
    lastUpdatedColumn: {
      minWidth: 140,
      maxWidth: 190,
      width: "100%",
      textAlign: "left",
    },
    createdColumn: {
      minWidth: 140,
      maxWidth: 140,
      textAlign: "left",
    },
    optionsColumn: {
      minWidth: 46,
      maxWidth: 46,
      textAlign: "right",
      paddingRight: theme.spacing(1),
    },
    rowReverse: {
      flexFlow: "row-reverse",
    },
    pad: {
      paddingRight: theme.spacing(3),
      paddingLeft: theme.spacing(3),
    },
    status: {
      display: "flex",
      justifyContent: "flex-start",
    },
  })
);

interface HeaderTableWorksProps {
  dispatch: AppDispatch;
}

export const initialSortParams: {
  sortBy: WorkSortVariant;
  sortOrder: SortOrder;
} = {
  sortBy: "updated",
  sortOrder: "desc",
};

export const HeaderTableWorks: React.FC<HeaderTableWorksProps> = ({
  dispatch,
}) => {
  const classes = useTableWorksStyles();
  const theme = useTheme();
  const hideBreakpoint = useMediaQuery(theme.breakpoints.up("sm"));
  const { location } = useHistory();
  const urlParams = new URLSearchParams(location.search);
  const sortParams = {
    sortBy: getSortBy(urlParams.get("sortBy")),
    sortOrder: getSortOrder(urlParams.get("sortOrder")),
  };

  return (
    <ContainerListItem leftOffset={hideBreakpoint ? 5.5 : 0}>
      <GridListHeaderItem className={classes.statusColumn} />
      <GridListHeaderItem className={classes.nameColumn}>
        <ListColumnTitle>Name</ListColumnTitle>
      </GridListHeaderItem>
      {hideBreakpoint && (
        <React.Fragment>
          <GridListHeaderItem className={classes.iswcColumn}>
            <ListColumnTitle>
              <TooltipMap
                to="ISWC"
                faqLink={tooltipFaqLinks.ISWC}
                dispatch={dispatch}
              >
                ISWC
              </TooltipMap>
            </ListColumnTitle>
          </GridListHeaderItem>
          <GridListHeaderItem className={classes.lastUpdatedColumn}>
            <TableSortLabel
              label="Last Updated"
              workColumn="updated"
              initSortOrder="desc"
              sortParams={sortParams}
            />
          </GridListHeaderItem>
          <GridListHeaderItem className={classes.createdColumn}>
            <TableSortLabel
              label="Created"
              workColumn="created"
              initSortOrder="desc"
              sortParams={sortParams}
            />
          </GridListHeaderItem>
        </React.Fragment>
      )}
      <GridListHeaderItem className={classes.optionsColumn} />
    </ContainerListItem>
  );
};
