import React from "react";
import { createStyles, makeStyles } from "@material-ui/core";
import { Theme } from "@material-ui/core/styles/createMuiTheme";
import clsx from "clsx";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    preview: {
      width: "100%",
      height: "100%",
      borderRadius: theme.spacing(1),
      boxShadow: theme.shadows["2"],
    },
  })
);

interface ThemePreviewStaticProps {
  className?: string;
}

export const ThemePreviewStatic: React.FC<ThemePreviewStaticProps> = ({
  className,
}) => {
  const classes = useStyles();

  return (
    <svg
      width="1360"
      height="896"
      viewBox="0 0 1360 896"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      className={clsx(classes.preview, className)}
    >
      <rect width="1360" height="896" fill="url(#pattern_static_preview)" />
      <defs>
        <pattern
          id="pattern_static_preview"
          patternContentUnits="objectBoundingBox"
          width="1"
          height="1"
        >
          <use
            xlinkHref="#image-static-preview"
            transform="scale(0.000735294 0.00111607)"
          />
        </pattern>
        <image
          id="image-static-preview"
          width="1360"
          height="896"
          xlinkHref="data:image/jpeg;base64,"
        />
      </defs>
    </svg>
  );
};
