import React from "react";
import { createStyles, makeStyles, MenuItem, Paper, Popover, Theme, Typography } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import { CaretDown24 } from "@carbon/icons-react";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    button: {
      textTransform: "none",
      justifyContent: "space-around",
    },
    paper: {
      paddingTop: theme.spacing(0.5),
      paddingBottom: theme.spacing(0.5),
    },
    caretIcon: {
      color: theme.palette.primary.main,
    },
    buttonText: {
      paddingRight: theme.spacing(4),
    },
  })
);

interface DropdownSelectProps {
  selectedOption: any;
  options: any[];
  onChange: (option: any) => void;
  optionLayout?: (option: any) => React.ReactNode;
  startIcon?: React.ReactNode;
}

export const DropdownSelect: React.FC<DropdownSelectProps> = ({
  options,
  optionLayout,
  selectedOption,
  onChange,
  startIcon,
}) => {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState<HTMLElement | null>(null);
  const isOpen = Boolean(anchorEl);

  return (
    <React.Fragment>
      <Button
        className={classes.button}
        onClick={({ currentTarget }) => setAnchorEl(currentTarget)}
        data-testid="anchor-set-button"
      >
        {startIcon}
        <Typography
          noWrap
          color="primary"
          variant="subtitle1"
          className={classes.buttonText}
        >
          {selectedOption.label}
        </Typography>
        <CaretDown24 className={classes.caretIcon} />
      </Button>

      <Popover
        anchorEl={anchorEl}
        open={isOpen}
        onClose={() => setAnchorEl(null)}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        data-testid="popover-component"
      >
        <Paper className={classes.paper}>
          {options.map((option, index) =>
            option.key !== selectedOption.key ? (
              <MenuItem
                key={option.key}
                onClick={() => {
                  setAnchorEl(null);
                  onChange(option);
                }}
              >
                {optionLayout ? optionLayout(option) : option.label}
              </MenuItem>
            ) : null
          )}
        </Paper>
      </Popover>
    </React.Fragment>
  );
};
