import React, { useState } from "react";
import {
  Box,
  Button,
  createStyles,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  makeStyles,
  Theme,
} from "@material-ui/core";
import { AppDispatchEffect } from "../../../App.reducer";
import { AssociatedWork, Ipi } from "../../../utils/models";
import { deleteIpiEffect } from "../../pages/Ipi/effects";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { Dialog } from "../Dialog";

interface DialogDeleteIpiProps {
  showDialog: boolean;
  hideDialog: () => void;
  dispatchEffect: AppDispatchEffect;
  ipi: Ipi | null;
  associatedWorks: AssociatedWork[];
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    associatedWorksContainer: {
      maxHeight: 500,
      margin: theme.spacing(3, 3, 1.5),
      overflow: "auto",
    },
  })
);

export const DialogDeleteIpi: React.FC<DialogDeleteIpiProps> = ({
  showDialog,
  hideDialog,
  ipi,
  associatedWorks,
  dispatchEffect,
}) => {
  const classes = useStyles();
  const [loading, setLoading] = useState(false);

  const handleSuccess = () => {
    setLoading(false);
    hideDialog();
  };
  const handleFail = () => setLoading(false);
  const handleClose = () => hideDialog();

  const handleConfirm = async () => {
    if (associatedWorks.length === 0 && ipi) {
      setLoading(true);
      try {
        await dispatchEffect(deleteIpiEffect(ipi.identityReference));
        handleSuccess();
      } catch (e) {
        handleFail();
      }
    } else {
      hideDialog();
    }
  };

  return (
    <Dialog variant="simple" open={showDialog} onClose={handleClose}>
      <DialogTitle>Remove IPI</DialogTitle>
      <DialogContent>
        {associatedWorks.length > 0 && (
          <DialogContentText variant="body2">
            You cannot delete an IPI number that is associated to existing
            works.
          </DialogContentText>
        )}

        {associatedWorks.length > 0 ? (
          <DialogContentText variant="body2" data-testid="dialog-message">
            To delete&nbsp;
            <b>
              {/*TODO:: fix below when delete is implemented on BE*/}
              {/*{`IPI ${ipi?.ipiNameNumber} (${ipi?.copyrightSociety.acronym}) `}*/}
              {`IPI ${ipi?.ipiNameNumber}`}
            </b>
            {`from your Session account you must associate a different IPI
            number to the following 
            ${associatedWorks.length === 1 ? "work" : "works"} and/or
            ${associatedWorks.length === 1 ? "template" : "templates"}:`}
          </DialogContentText>
        ) : (
          <DialogContentText variant="body2">
            Are you sure you want to remove your&nbsp;
            <b>
              {/*TODO:: ix below when delete is implemented on BE*/}
              {`IPI ${ipi?.ipiNameNumber}`}
            </b>
            &nbsp;from Session?
          </DialogContentText>
        )}
        {associatedWorks.length > 0 && (
          <Box className={classes.associatedWorksContainer}>
            <Box data-testid="associated-works">
              {associatedWorks.map((work) => (
                <DialogContentText
                  key={work.workGuid}
                  color="textPrimary"
                  data-testid={`associated-work-${work.workTitle}`}
                >
                  {work.workTitle}
                </DialogContentText>
              ))}
            </Box>
          </Box>
        )}
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          {associatedWorks.length > 0 ? "Ok" : "Cancel"}
        </Button>
        {associatedWorks.length === 0 && (
          <ButtonSubmit
            submitting={loading}
            onClick={handleConfirm}
            variant="text"
            data-testid="submit-remove-ipi"
          >
            Remove
          </ButtonSubmit>
        )}
      </DialogActions>
    </Dialog>
  );
};
