import React from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import {
  getAgreementDuplicateLinkSelector,
  getAgreementsLinksSelector,
  getAgreementsLoadingStateSelector,
  getAgreementsSelector,
  getCreateAgreementLinkSelector,
} from "../AgreementRegistration/selectors";
import { LoadingState } from "../../../types/enums";
import { RouteComponentProps } from "react-router";
import { DRAFT } from "../../../utils/models";
import { getUserIsSocAdminSelector } from "../SignIn/selectors";
import { paths } from "../../../utils/common";
import { ListAgreements } from "../../organisms/ListAgreements";
import { PageNoContent } from "../../molecules/PageNoContent";
import { ListAgreementsAdministrator } from "../../organisms/ListAgreementsAdministrator";
import { LoadingScreen } from "../../atoms/LoadingScreen";

export const PageAgreementsList: React.FC<RouteComponentProps> = ({
  location: { pathname },
}) => {
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();
  const state = useAppSelector((state) => state);
  const isDraftsPage = pathname === paths.AGREEMENTS_DRAFT_LIST;
  const agreementsAll = getAgreementsSelector(state);
  const agreementsLinks = getAgreementsLinksSelector(state);
  const loadingState = getAgreementsLoadingStateSelector(state);
  const isSocietyAdmin = getUserIsSocAdminSelector(state);
  const canDuplicateAgreement = Boolean(
    getAgreementDuplicateLinkSelector(state)
  );
  const canCreateAgreement = Boolean(getCreateAgreementLinkSelector(state));
  if (
    loadingState === LoadingState.PRISTINE ||
    loadingState === LoadingState.FETCHING
  ) {
    return <LoadingScreen />;
  }

  const agreements = agreementsAll.filter((el) =>
    isDraftsPage ? el.status === DRAFT : el.status !== DRAFT
  );

  if (agreements.length === 0 && loadingState === LoadingState.FETCHED) {
    return (
      <PageNoContent
        primaryText="No Agreements"
        secondaryText={
          canCreateAgreement && !isSocietyAdmin
            ? "Click the + button to create or register a new agreement"
            : undefined
        }
      />
    );
  }

  if (isSocietyAdmin) {
    //TODO:: Update agreements data if its different from regular agreements for admin users when BE is implemented.
    return <ListAgreementsAdministrator agreements={agreements} />;
  }

  return (
    <ListAgreements
      agreements={agreements}
      dispatchEffect={dispatchEffect}
      dispatch={dispatch}
      canDuplicateAgreement={canDuplicateAgreement}
      agreementsLinks={agreementsLinks}
    />
  );
};
