import React from "react";
import { useMediaQuery } from "@material-ui/core";
import { TableSortLabel } from "../../atoms/TableSortLabel";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import {
  createStyles,
  makeStyles,
  Theme,
  useTheme,
} from "@material-ui/core/styles";
import { useHistory } from "react-router";
import { SortOrder, WorkSortVariant } from "../../../utils/models";
import { initialSortParams } from "../HeaderTableWorks";
import { GridListHeaderItem } from "../../atoms/GridListHeaderItem";

export const useHeaderTableWorksAdministratorStyles = makeStyles(
  (theme: Theme) =>
    createStyles({
      listRow: {
        minHeight: 85,
      },
      listItemText: {
        // Production build somehow has lower priority of styles injected.
        // Workaround for forcing override.
        "&&": {
          marginTop: 0,
          marginBottom: 0,
        },
      },
      workName: {
        textOverflow: "ellipsis",
        overflow: "hidden",
        "& span, & p": {
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        },
      },
      statusColumn: {
        minWidth: 50,
        maxWidth: 50,
        textAlign: "center",

        [theme.breakpoints.only("xs")]: {
          minWidth: 40,
          maxWidth: 40,
        },
      },
      titleColumn: {
        position: "relative",
        width: "25%",
        minWidth: 85,
        [theme.breakpoints.only("xs")]: {
          width: "100%",
        },
      },
      ipiColumn: {
        position: "relative",
        width: "10%",
        minWidth: 85,
      },
      memberColumn: {
        position: "relative",
        width: "15%",
        minWidth: 125,
      },
      iswcColumn: {
        width: "15%",
        minWidth: 125,
      },
      lastUpdatedColumn: {
        width: "15%",
        minWidth: 140,
      },
      createdColumn: {
        minWidth: 140,
        width: "15%",
      },
      rowReverse: {
        textAlign: "left",
        flexFlow: "row-reverse",
      },
      pad: {
        paddingRight: theme.spacing(3),
        paddingLeft: theme.spacing(3),
      },
      status: {
        display: "flex",
        justifyContent: "flex-start",
      },
    })
);

export const HeaderTableWorksAdministrator = () => {
  const classes = useHeaderTableWorksAdministratorStyles();
  const theme = useTheme();
  const { location } = useHistory();
  const urlParams = new URLSearchParams(location.search);
  const sortBy = urlParams.get("sortBy") as WorkSortVariant;
  const sortOrder = urlParams.get("sortOrder") as SortOrder;
  const sortParams =
    sortBy && sortOrder ? { sortBy, sortOrder } : initialSortParams;
  const hideBreakpoint = useMediaQuery(theme.breakpoints.up("sm"));

  return (
    <ContainerListItem leftOffset={hideBreakpoint ? 5.5 : 0}>
      <GridListHeaderItem className={classes.statusColumn} />
      <GridListHeaderItem className={classes.titleColumn}>
        <ListColumnTitle>Title</ListColumnTitle>
      </GridListHeaderItem>
      {hideBreakpoint && (
        <React.Fragment>
          <GridListHeaderItem className={classes.ipiColumn}>
            <ListColumnTitle>IPI</ListColumnTitle>
          </GridListHeaderItem>
          <GridListHeaderItem className={classes.memberColumn}>
            <ListColumnTitle>Member</ListColumnTitle>
          </GridListHeaderItem>
          <GridListHeaderItem className={classes.iswcColumn}>
            <ListColumnTitle>ISWC</ListColumnTitle>
          </GridListHeaderItem>
          <GridListHeaderItem className={classes.lastUpdatedColumn}>
            <TableSortLabel
              className={classes.rowReverse}
              label="Last Updated"
              workColumn="updated"
              initSortOrder="desc"
              sortParams={sortParams}
            />
          </GridListHeaderItem>
          <GridListHeaderItem className={classes.createdColumn}>
            <TableSortLabel
              className={classes.rowReverse}
              label="Created"
              workColumn="created"
              initSortOrder="desc"
              sortParams={sortParams}
            />
          </GridListHeaderItem>
        </React.Fragment>
      )}
    </ContainerListItem>
  );
};
