import React from "react";
import {
  createStyles,
  Grid,
  makeStyles,
  Theme,
  Typography,
  useMediaQuery,
} from "@material-ui/core";
import { WorkCreatorPublisher, WorkPublisher } from "../../../utils/models";
import { CellPrShareSummary } from "../../atoms/CellPrShareSummary";
import { CellCreatorSummary } from "../../atoms/CellCreatorSummary";
import { CardPublisher } from "../../atoms/CardPublisher";
import { RowCreatorListItem } from "../RowCreatorListItem";
import { SvgElbowLine } from "../../atoms/SvgElbowLine";
import { CellRoleSummary } from "../../atoms/CellRoleSummary";
import { CellMrShareSummary } from "../../atoms/CellMrShareSummary";
import { PublisherRoleType } from "../../../types/enums";
import { useTheme } from "@material-ui/core/styles";
import { formatPublisherRole } from "./utils";
import { CellOptions } from "../../atoms/CellOptions";
import { CellAvatarSummary } from "../../atoms/CellAvatarSummary";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    avatar: {
      marginLeft: "auto",
    },
    roleText: {
      display: "flex",
      alignItems: "center",
      color: theme.palette.text.secondary,
    },
    roleContainer: {
      display: "flex",
      alignContent: "center",
    },
    showLessButton: {
      border: "1px solid",
    },
    svgRelationship: {
      top: -60,
      left: 32,
    },
    row: {
      maxHeight: 80,
      minHeight: 80,
      paddingLeft: theme.spacing(2),
    },
    prShareText: {
      width: theme.spacing(7.5),
    },
  })
);

interface RowGridPublisherSummaryProps {
  publisher: WorkPublisher;
  first: boolean;
  onClick?: () => void;
  isPublisher?: boolean;
  creatorPublisher: WorkCreatorPublisher;
}

export const RowGridPublisherSummary: React.FC<RowGridPublisherSummaryProps> = ({
  first,
  publisher,
  creatorPublisher: { prShare, mrShare },
}) => {
  const classes = useStyles();
  const theme = useTheme();
  const isSmallWindow = useMediaQuery(theme.breakpoints.down("sm"));
  // const hideSecondaryFields = isPublisher && publisher.role === PublisherRoleType.SE;
  const hideSecondaryFields = false;

  return (
    <React.Fragment>
      <RowCreatorListItem listClassName={classes.row}>
        <Grid container>
          <CellAvatarSummary>{first && <SvgElbowLine />} </CellAvatarSummary>
          <CellCreatorSummary>
            {!first && (
              <SvgElbowLine height={80} className={classes.svgRelationship} />
            )}
            <CardPublisher
              wrapDisplayName
              multiline
              ipiVerified={false} //TODO:: fix
              publisher={{
                ipiNameNumber: publisher.ipi.ipiNameNumber,
                displayName: publisher.profile.displayName,
              }}
            />
          </CellCreatorSummary>
          <CellRoleSummary className={classes.roleText}>
            <Typography variant="subtitle1">
              {formatPublisherRole(isSmallWindow)(PublisherRoleType.E)}
            </Typography>
          </CellRoleSummary>
          {!hideSecondaryFields && (
            <React.Fragment>
              <CellPrShareSummary alignCenter>
                <Typography
                  variant="subtitle1"
                  className={classes.prShareText}
                  align="right"
                >
                  {prShare || 0}%
                </Typography>
              </CellPrShareSummary>
              <CellMrShareSummary alignCenter>
                <Typography variant="subtitle1" align="right">
                  {mrShare || 0}%
                </Typography>
              </CellMrShareSummary>
            </React.Fragment>
          )}
          <CellOptions />
        </Grid>
      </RowCreatorListItem>
    </React.Fragment>
  );
};
