import { createStyles, makeStyles, Theme } from "@material-ui/core";
import React from "react";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    themeHoverButton: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      width: "auto",
      height: 32,
      borderRadius: 4,
      backgroundColor: theme.palette.action.active,
      border: "none",
      padding: theme.spacing(0, 1),
      cursor: "pointer",
      outline: "none",
    },
  })
);

interface ButtonThemeHoverProps {
  onClick?: () => void;
  className?: string;
}

export const ButtonThemeHover: React.FC<ButtonThemeHoverProps> = ({
  children,
  className = "",
  onClick,
}) => {
  const classes = useStyles();
  return (
    <button
      data-testid="button-theme-hover"
      type="button"
      className={clsx(classes.themeHoverButton, className)}
      onClick={onClick}
    >
      {children}
    </button>
  );
};
