import {
  FETCH_WORK_SCHEMA,
  FETCH_WORK_SCHEMA_SUCCESS,
  ValidationSchemaActionTypes,
} from "./action.types";
import { WorkSchemaJSON } from "./models";

export const fetchWorkSchema = (): ValidationSchemaActionTypes => ({
  type: FETCH_WORK_SCHEMA,
});

export const fetchWorkSchemaSuccess = (
  schema: WorkSchemaJSON
): ValidationSchemaActionTypes => ({
  type: FETCH_WORK_SCHEMA_SUCCESS,
  schema,
});
