import {
  AgreementTemplatesActionTypes,
  DELETE_AGREEMENT_TEMPLATE_SUCCESS,
  FETCH_AGREEMENT_TEMPLATE,
  FETCH_AGREEMENT_TEMPLATE_FAILURE,
  FETCH_AGREEMENT_TEMPLATE_SUCCESS,
  FETCH_AGREEMENT_TEMPLATES,
  FETCH_AGREEMENT_TEMPLATES_FAILURE,
  FETCH_AGREEMENT_TEMPLATES_SUCCESS,
  PATCH_AGREEMENT_TEMPLATE_SUCCESS,
  POST_AGREEMENT_TEMPLATE_SUCCESS,
} from "./actions.types";
import { LoadingState } from "../../../types/enums";
import { agreementToListItem } from "../AgreementRegistration/reducer.utils";
import {
  agreementsInitialState,
  AgreementsState,
} from "../AgreementRegistration/reducer";

export const agreementTemplatesReducer = (
  state: AgreementsState = agreementsInitialState,
  action: AgreementTemplatesActionTypes
): AgreementsState => {
  switch (action.type) {
    case FETCH_AGREEMENT_TEMPLATES: {
      return { ...state, listLoadingState: LoadingState.FETCHING };
    }

    case FETCH_AGREEMENT_TEMPLATES_SUCCESS: {
      return {
        ...state,
        list: action.templates,
        listLoadingState: LoadingState.FETCHED,
        _links: action._links,
      };
    }

    case FETCH_AGREEMENT_TEMPLATES_FAILURE: {
      return { ...state, listLoadingState: LoadingState.ERROR };
    }

    case FETCH_AGREEMENT_TEMPLATE:
    case FETCH_AGREEMENT_TEMPLATE_FAILURE: {
      return {
        ...state,
        item: undefined,
      };
    }

    case FETCH_AGREEMENT_TEMPLATE_SUCCESS: {
      return {
        ...state,
        item: action.template,
      };
    }

    case POST_AGREEMENT_TEMPLATE_SUCCESS: {
      return {
        ...state,
        item: action.template,
        list: [agreementToListItem(action.template), ...state.list],
      };
    }

    case PATCH_AGREEMENT_TEMPLATE_SUCCESS: {
      return {
        ...state,
        item: {
          ...state.item,
          ...action.template,
          terms: state.item?.terms,
        },
        list: state.list.map((el) =>
          el.guid === action.template.guid
            ? agreementToListItem(action.template)
            : el
        ),
      };
    }

    case DELETE_AGREEMENT_TEMPLATE_SUCCESS: {
      return {
        ...state,
        list: state.list.filter((el) => el.guid !== action.guid),
      };
    }

    default:
      return state;
  }
};
