import React, { useState } from "react";
import { FieldProps, getIn } from "formik";
import MuiTypography from "@material-ui/core/Typography";
import { withStyles } from "@material-ui/core/styles";
import { getIpiRightsByType } from "../../organisms/TableAgreementShares/utils";
import { DialogIpiRights } from "../../organisms/DialogIpiRights";

const Typography = withStyles((theme) => ({
  root: {
    width: "max-content",
    "&:hover": {
      cursor: "pointer",
      textDecoration: "underline",
    },
  },
}))(MuiTypography);

interface FormikIpiRightsSelectProps {
  disabled?: boolean;
}

export const FormikIpiRightsSelect: React.FC<
  FormikIpiRightsSelectProps & FieldProps
> = ({ form: { setFieldValue, values }, field: { name }, disabled }) => {
  const selected = getIn(values, `${name}.usageTypes`);
  const rightType = getIn(values, `${name}.rightType`);
  const [open, setOpen] = useState(false);
  const handleClose = () => setOpen(false);
  const handleConfirm = (checked: string[]) => {
    setFieldValue(`${name}.usageTypes`, checked);
  };

  const formatResult = () => {
    if (selected.length === 0) {
      return "None";
    }
    const rightsLength = getIpiRightsByType(rightType).length;
    if (selected.length === rightsLength) {
      return "All";
    }

    return selected.join(", ");
  };

  return (
    <React.Fragment>
      <Typography variant="body2" onClick={() => setOpen(true)}>
        {formatResult()}
      </Typography>

      <DialogIpiRights
        onConfirm={handleConfirm}
        onClose={handleClose}
        disabled={disabled}
        selected={selected}
        open={open}
        ipiRights={getIpiRightsByType(rightType)}
      />
    </React.Fragment>
  );
};
