import React from "react";
import {
  Box,
  BoxProps,
  createStyles,
  Grid,
  makeStyles,
  Theme,
} from "@material-ui/core";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    cell: {
      position: "relative",
      width: "100%",
      minWidth: 185,
      paddingLeft: theme.spacing(1),
    },
    dense: {
      minWidth: 70,
    },
    mediaQueryBreakpoint: {
      [theme.breakpoints.only("sm")]: {
        minWidth: 70,
      },
    },
    header: {
      paddingBottom: theme.spacing(1.5),
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
  })
);

interface CellRoleProps extends BoxProps {
  dense?: boolean;
  useMediaQuery?: boolean;
  header?: boolean;
}

export const CellRole: React.FC<CellRoleProps> = ({
  dense,
  children,
  className,
  useMediaQuery,
  header = false,
  ...props
}) => {
  const classes = useStyles();

  return (
    <Box
      {...props}
      component={Grid}
      className={clsx(classes.cell, className, {
        [classes.dense]: dense,
        [classes.mediaQueryBreakpoint]: useMediaQuery,
        [classes.header]: header,
      })}
    >
      {children}
    </Box>
  );
};
