import React from "react";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { useTemplatesListStyles } from "../ListTemplates";
import { useHistory } from "react-router";
import { SortOrder, WorkSortVariant } from "../../../utils/models";
import { initialSortParams } from "../HeaderTableWorks";
import { TableSortLabel } from "../../atoms/TableSortLabel";
import { GridListHeaderItem } from "../../atoms/GridListHeaderItem";

interface HeaderListTemplatesProps {
  breakpointXs: boolean;
}

export const HeaderListTemplates: React.FC<HeaderListTemplatesProps> = ({
  breakpointXs,
}) => {
  const classes = useTemplatesListStyles();
  const { location } = useHistory();
  const urlParams = new URLSearchParams(location.search);
  const sortBy = urlParams.get("sortBy") as WorkSortVariant;
  const sortOrder = urlParams.get("sortOrder") as SortOrder;
  const sortParams =
    sortBy && sortOrder ? { sortBy, sortOrder } : initialSortParams;

  return (
    <ContainerListItem leftOffset={breakpointXs ? 5 : 9}>
      <GridListHeaderItem className={classes.titleColumn}>
        <ListColumnTitle>Template name</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.creatorsColumn}>
        <ListColumnTitle>Creators</ListColumnTitle>
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.lastUpdatedColumn}>
        <TableSortLabel
          label="Last Updated"
          workColumn="updated"
          initSortOrder="desc"
          sortParams={sortParams}
        />
      </GridListHeaderItem>
      <GridListHeaderItem className={classes.buttonColumn} />
      <GridListHeaderItem className={classes.optionsColumn} />
    </ContainerListItem>
  );
};
