import {
  FaqActionTypes,
  SET_FAQ_PATH,
  SET_ITEM,
  RESET_ITEM,
  OPEN_DRAWER,
  CLOSE_DRAWER,
  SET_DEFAULT_FAQ_PATH,
} from "./actions.types";

export const setFaqPathAction = (path?: string): FaqActionTypes => ({
  type: SET_FAQ_PATH,
  path,
});

export const setDefaultFaqPathAction = (path?: string): FaqActionTypes => ({
  type: SET_DEFAULT_FAQ_PATH,
  path,
});

export const resetItemAction = (): FaqActionTypes => ({
  type: RESET_ITEM,
});

export const setItemAction = (item?: string): FaqActionTypes => ({
  type: SET_ITEM,
  item,
});

export const openDrawerAction = (): FaqActionTypes => ({
  type: OPEN_DRAWER,
});

export const closeDrawerAction = (): FaqActionTypes => ({
  type: CLOSE_DRAWER,
});
