import React from "react";
import { Grid } from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import clsx from "clsx";

interface StyleProps {
  leftOffset: number;
  rightOffset: number;
}

const useStyles = makeStyles<Theme, StyleProps>((theme) =>
  createStyles({
    root: ({ leftOffset, rightOffset }) => ({
      paddingRight: theme.spacing(3 + rightOffset),
      paddingLeft: theme.spacing(3 + leftOffset),
      [theme.breakpoints.only("xs")]: {
        paddingRight: theme.spacing(3),
        paddingLeft: theme.spacing(3),
      },
    }),
  })
);

interface ContainerListItemProps {
  leftOffset?: number;
  rightOffset?: number;
  className?: string;
}

export const ContainerListItem: React.FC<ContainerListItemProps> = ({
  children,
  leftOffset = 0,
  rightOffset = 0,
  className,
  ...props
}) => {
  const classes = useStyles({ leftOffset, rightOffset });
  return (
    <Grid
      container
      wrap="nowrap"
      className={clsx(classes.root, className)}
      {...props}
    >
      {children}
    </Grid>
  );
};
