import React, { useState } from "react";
import { Form, Formik, FormikErrors, FormikHelpers, FormikState } from "formik";
import {
  Box,
  Button,
  createStyles,
  Grid,
  makeStyles,
  Paper,
  Theme,
} from "@material-ui/core";
import { useHistory } from "react-router";
import { Agreement } from "../../../utils/models";
import {
  IdentityType,
  SalesOrManufacture,
  UsaLicenseIndicatorType,
} from "../../../types/enums";
import { alertAgreementTemplateSaveSuccess } from "./utils";
import {
  buildInitialValues,
  getPermissions,
  noPermissions,
} from "../FormAgreement/utils";
import { createTermWithShare } from "../../atoms/FormikAddAgreementTerm/utils";
import { preventEnterOnKeyDown } from "../../../utils/helpers";
import isEmpty from "lodash/isEmpty";
import Promise from "promise-polyfill";
import { agreementValidationSchema } from "../FormAgreement/component.validator";
import {
  deleteAgreementTemplateEffect,
  patchAgreementTemplateEffect,
  postAgreementTemplateEffect,
} from "../../pages/AgreementTemplates/effects";
import { addAlertAction } from "../../pages/Alerts/actions";
import { AlertType } from "../../pages/Alerts/actions.types";
import { paths } from "../../../utils/common";
import { AgreementFormValues, FormAgreementProps } from "../FormAgreement";
import { LeavingGuard } from "../LeavingGuard";
import { TabsNavigation } from "../../molecules/TabsNavigation";
import { TabPartyShares } from "../TabPartyShares";
import { DialogWarning } from "../../molecules/DialogWarning";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { ThemeInverse } from "../../atoms/ThemeInverse";
import { TabGeneral } from "../TabGeneral";
import { InputFieldAppBar } from "../../atoms/InputFieldAppBar";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { toasts } from "../../../utils/toasts";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    form: {
      height: "100%",
      display: "flex",
      flexDirection: "column",
    },
    appBarContent: {
      width: "100%",
      marginTop: theme.spacing(5),
      display: "inline-flex",
      alignItems: "baseline",
      justifyContent: "space-between",
      transition: theme.transitions.create(["padding"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    content: {
      height: "100%",
      overflow: "auto",
    },
    submitButton: {
      marginLeft: theme.spacing(1),
    },
    buttonsContainer: {
      display: "flex",
      alignItems: "flex-end",
      justifyContent: "flex-end",
      marginBottom: theme.spacing(-1),
      marginLeft: "auto",
    },
    partyShareTab: {
      overflowX: "hidden",
    },
  })
);

type NavigationTab = "general" | "partyShares";

export const FormAgreementTemplate: React.FC<FormAgreementProps> = ({
  dispatch,
  dispatchEffect,
  company,
  agreement: template,
  breadCrumb = "New Template",
  account,
  agreementsListLinks,
}) => {
  const classes = useStyles();
  const history = useHistory();
  const [activeTab, setActiveTab] = useState<NavigationTab>("partyShares");
  const [showConfirmDialog, setShowConfirmDialog] = useState(false);
  const permissions = getPermissions(template);

  const initialValues: AgreementFormValues = {
    title: "",
    submitterAgreementNumbers: [""],
    recordingPrefix: "",
    libraryMusic: false,
    bulkTransferWorks: true,
    usaLicenseIndicator: UsaLicenseIndicatorType.NONE,
    agreementType: "general",
    salesOrManufacture: SalesOrManufacture.SALES,
    assignor: {
      type: IdentityType.PUBLISHER,
      ipiNameNumber: Number(company.ipiNameNumber),
      displayName: account.profile.displayName,
      permissions: {
        read: true,
        write: true,
        owner: true,
      },
    },
    terms: [createTermWithShare()],
  };

  const createOrUpdateAgreementTemplate = async (
    values: AgreementFormValues
  ) => {
    const newTemplate: Agreement = template?.guid
      ? await dispatchEffect(patchAgreementTemplateEffect(template, values))
      : await dispatchEffect(postAgreementTemplateEffect(values));
    try {
      return newTemplate;
      //TODO:: Update logic when BE implements templates

      // return await dispatchEffect(
      //   saveAgreementTemplateRelationsEffect(
      //     newTemplate,
      //     getFormValuesWithAddedOwner(values, newTemplate)
      //   )
      // );
    } catch (template) {
      throw template;
    }
  };

  const leaveGuardPromise = (
    validateForm: (values?: any) => Promise<FormikErrors<AgreementFormValues>>,
    setFieldTouched: (
      field: string,
      isTouched?: boolean | undefined,
      shouldValidate?: boolean | undefined
    ) => void,
    values: AgreementFormValues,
    resetForm: (nextState?: Partial<FormikState<AgreementFormValues>>) => void
  ) =>
    new Promise<void>(async (resolve, reject) => {
      try {
        const errors = await validateForm();
        if (!isEmpty(errors)) {
          Object.keys(errors).forEach((key) => {
            setFieldTouched(key);
          });
          reject();
        } else {
          try {
            await handleOnSubmit(
              values,
              { resetForm } as FormikHelpers<AgreementFormValues>,
              true
            );
            resolve();
          } catch (e) {
            reject();
          }
        }
      } catch (e) {}
    });

  const handleOnDelete = async () => {
    if (template?._links?.delete) {
      try {
        await dispatchEffect(
          deleteAgreementTemplateEffect(
            template?.guid,
            template?._links?.delete
          )
        );
        dispatch(
          addAlertAction(
            AlertType.SUCCESS,
            toasts.success.agreementTemplate.SAVE
          )
        );
        history.push(paths.AGREEMENTS_TEMPLATE_LIST);
      } catch (e) {}
    }
  };

  const handleOnSubmit = async (
    values: AgreementFormValues,
    { resetForm }: FormikHelpers<AgreementFormValues>,
    forLeavingGuard?: boolean
  ) => {
    try {
      const template = await createOrUpdateAgreementTemplate(values);
      dispatch(alertAgreementTemplateSaveSuccess);
      if (!forLeavingGuard) {
        history.push(`${paths.AGREEMENT_TEMPLATE}/${template.guid}`);
        resetForm({ values: buildInitialValues(template) });
      }
    } catch (templage) {
      throw template;
    }
  };

  return (
    <Formik
      initialValues={
        template?.guid ? buildInitialValues(template) : initialValues
      }
      validationSchema={agreementValidationSchema}
      onSubmit={handleOnSubmit}
    >
      {({
        isSubmitting,
        values,
        dirty,
        validateForm,
        setFieldTouched,
        resetForm,
      }) => (
        <Form
          className={classes.form}
          onKeyDown={preventEnterOnKeyDown}
          data-testid="agreement-template-form"
        >
          <ContainerAppBar
            dispatch={dispatch}
            parentTitle="Agreements"
            parentTo={paths.AGREEMENTS_TEMPLATE_LIST}
            title={breadCrumb}
            secondaryContent={
              <Box pt={4} pb={0}>
                <Grid container>
                  <Grid item xs={9}>
                    <InputFieldAppBar
                      placeholder="New Agreement Template"
                      readonly={!permissions.updateAgreement}
                      name="title"
                      data-testid="agreement-template-title-input"
                      disabled={false}
                    />
                  </Grid>
                  <Box className={classes.appBarContent}>
                    <TabsNavigation<NavigationTab>
                      activeTab={activeTab}
                      setActiveTab={setActiveTab}
                      tabs={[
                        { label: "Party Shares", value: "partyShares" },
                        { label: "General", value: "general" },
                      ]}
                    />
                    <Box display="inline-flex">
                      <ThemeInverse>
                        <Button
                          color="inherit"
                          size="small"
                          onClick={() => {
                            history.push(paths.AGREEMENTS_TEMPLATE_LIST);
                          }}
                        >
                          Cancel
                        </Button>
                        {template?.guid && (
                          <Button
                            color="inherit"
                            size="small"
                            disabled={!template._links?.delete}
                            onClick={() => {
                              setShowConfirmDialog(true);
                            }}
                          >
                            Delete
                          </Button>
                        )}
                        <ButtonSubmit
                          data-testid="save-agreement-template-button"
                          variant="text"
                          size="small"
                          color="inherit"
                          submitting={isSubmitting}
                          disabled={
                            isSubmitting ||
                            !permissions.updateAgreement ||
                            !dirty
                          }
                        >
                          {template ? "Save" : "Create"}
                        </ButtonSubmit>
                      </ThemeInverse>
                    </Box>
                  </Box>
                </Grid>
              </Box>
            }
          />
          <Box className={classes.content} component={Paper}>
            <Box
              display={activeTab === "partyShares" ? "flex" : "none"}
              height={1}
              pt={4.5}
              className={classes.partyShareTab}
            >
              <TabPartyShares
                dispatch={dispatch}
                dispatchEffect={dispatchEffect}
                profileRef={account.userGuid}
                agreement={template}
                formValues={values}
                permissions={isSubmitting ? noPermissions : permissions}
                agreementsListLinks={agreementsListLinks}
              />
            </Box>
            <Box display={activeTab === "general" ? "flex" : "none"} height={1}>
              <TabGeneral
                formValues={values}
                disabled={!permissions.updateAgreement || isSubmitting}
              />
            </Box>
          </Box>
          <LeavingGuard
            when={dirty && !isSubmitting}
            navigate={(path) => history.push(path)}
            shouldBlockNavigation={(location) => true}
            onSubmitPromise={() =>
              leaveGuardPromise(
                validateForm,
                setFieldTouched,
                values,
                resetForm
              )
            }
          />
          {showConfirmDialog && (
            <DialogWarning
              width={400}
              onConfirm={handleOnDelete}
              onCancel={() => setShowConfirmDialog(false)}
              message="Are you sure you want to delete this template"
              dialogTitle="Delete template"
              labelCancel="Cancel"
              labelConfirm="Delete"
            />
          )}
        </Form>
      )}
    </Formik>
  );
};
