import React from "react";
import {
  createStyles,
  Divider,
  makeStyles,
  Paper,
  Theme,
} from "@material-ui/core";
import { AppDispatchEffect } from "../../../App.reducer";
import { PasswordPolicy } from "../../pages/SignIn/actions.types";
import { ContainerContent } from "../../atoms/ContainerContent";
import { FormPasswordChange } from "../FormPasswordChange";
import { FormCompanyPersonalData } from "../FormCompanyPersonalData";
import { FormEmailChange } from "../FormEmailChange";
import { Href } from "../../../utils/models";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    inner: {
      paddingRight: theme.spacing(9),
      [theme.breakpoints.only("xs")]: {
        paddingRight: theme.spacing(5),
      },
    },
    paper: {
      flexGrow: 1,
    },
  })
);

export interface FormCompanyProfileFProps {
  dispatchEffect: AppDispatchEffect;
  profileData: { firstName: string; lastName: string; email: string };
  passwordPolicy?: PasswordPolicy;
  changeEmailConfirmLink?: Href;
}

export const FormCompanyProfile: React.FC<FormCompanyProfileFProps> = ({
  dispatchEffect,
  profileData: { firstName = "", lastName = "", email = "" },
  passwordPolicy,
  changeEmailConfirmLink,
}) => {
  const classes = useStyles();
  return (
    <Paper square className={classes.paper}>
      <ContainerContent className={classes.inner}>
        <FormCompanyPersonalData
          dispatchEffect={dispatchEffect}
          profileData={{ firstName, lastName }}
        />
        <Divider />
        <FormPasswordChange
          dispatchEffect={dispatchEffect}
          passwordPolicy={passwordPolicy}
        />
        <Divider />
        <FormEmailChange
          dispatchEffect={dispatchEffect}
          email={email}
          changeEmailConfirmLink={changeEmailConfirmLink}
        />
      </ContainerContent>
    </Paper>
  );
};
