import React from "react";
import {
  Box,
  createStyles,
  Grid,
  makeStyles,
  Paper,
  Theme,
} from "@material-ui/core";
import { Field, Form, Formik } from "formik";
import { toggleRevisionEffect } from "../../pages/Settings/effects";
import { AlertType } from "../../pages/Alerts/actions.types";
import { SettingsState } from "../../pages/Settings/reducer";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import { addAlertAction } from "../../pages/Alerts/actions";
import { ContainerTabPanel } from "../../atoms/ContainerTabPanel";
import { ContainerContent } from "../../atoms/ContainerContent";
import { FormikRadioSelect } from "../FormikRadioSelect";
import { settingsTabs } from "../../pages/Settings";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { TabMenu } from "../../molecules/TabMenu";
import { toasts } from "../../../utils/toasts";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    appBarContent: {
      width: "100%",
      marginTop: theme.spacing(6),
      display: "inline-flex",
      alignItems: "baseline",
      justifyContent: "space-between",
      transition: theme.transitions.create(["padding"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    appBarContentShift: {
      paddingLeft: theme.spacing(17),
      transition: theme.transitions.create(["padding"], {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    form: {
      display: "flex",
      flexDirection: "column",
      height: "100%",
    },
    cancelButton: {
      marginRight: theme.spacing(1),
    },
    paper: {
      height: "100%"
    }
  })
);

interface GeneralSettingsProps {
  dispatchEffect: AppDispatchEffect;
  dispatch: AppDispatch;
  settings: SettingsState;
}

export const GeneralSettings: React.FC<GeneralSettingsProps> = ({
  dispatchEffect,
  dispatch,
  settings,
}) => {
  const classes = useStyles();
  const { work, agreement } = settings.revision;

  return (
    <ContainerTabPanel>
      <Formik
        initialValues={{ work, agreement }}
        onSubmit={async (values) => {
          try {
            await dispatchEffect(toggleRevisionEffect(values));
            dispatch(
              addAlertAction(
                AlertType.SUCCESS,
                toasts.success.settings.UPDATE_SETTINGS
              )
            );
          } catch (e) {
            dispatch(
              addAlertAction(
                AlertType.ERROR,
                toasts.error.settings.UPDATE_SETTINGS
              )
            );
          }
        }}
      >
        {({ isSubmitting, dirty, resetForm }) => (
          <Form className={classes.form}>
            <ContainerAppBar
              dispatch={dispatch}
              parentTitle="Settings"
              title="General"
              secondaryContent={
                <Box className={classes.appBarContent}>
                  <TabMenu tabs={settingsTabs} />
                  {/*<Box display="inline-flex">*/}
                  {/*    <Button*/}
                  {/*      color="inherit"*/}
                  {/*      size="small"*/}
                  {/*      onClick={() => resetForm()}*/}
                  {/*      className={classes.cancelButton}*/}
                  {/*    >*/}
                  {/*      Cancel*/}
                  {/*    </Button>*/}
                  {/*    <ButtonSubmit*/}
                  {/*      variant="contained"*/}
                  {/*      color="secondary"*/}
                  {/*      size="small"*/}
                  {/*      disabled={!dirty}*/}
                  {/*      submitting={isSubmitting}*/}
                  {/*    >*/}
                  {/*      Save*/}
                  {/*    </ButtonSubmit>*/}
                  {/*</Box>*/}
                </Box>
              }
            />
            <Paper square className={classes.paper}>
              <ContainerContent>
                <Box pt={5}>
                  <Grid container spacing={3}>
                    <Grid item xs={6} sm={6} md={3}>
                      <Field
                        component={FormikRadioSelect}
                        name="work"
                        label="Work Revisions"
                        options={[
                          { label: "On", value: true },
                          { label: "Off", value: false },
                        ]}
                      />
                    </Grid>
                    <Grid item xs={6} sm={6} md={3}>
                      <Field
                        component={FormikRadioSelect}
                        name="agreement"
                        label="Agreement Revisions"
                        options={[
                          { label: "On", value: true },
                          { label: "Off", value: false },
                        ]}
                      />
                    </Grid>
                  </Grid>
                </Box>
              </ContainerContent>
            </Paper>
          </Form>
        )}
      </Formik>
    </ContainerTabPanel>
  );
};
