import React from "react";
import {
  AppBar,
  Box,
  createStyles,
  Link,
  makeStyles,
  Theme,
  Toolbar,
  Typography,
  useMediaQuery,
  useTheme
} from "@material-ui/core";
import { AppDispatch } from "../../../App.reducer";
import { Link as NavLink } from "react-router-dom";
import { HamburgerMenu } from "../HamburgerMenu";
import { ContainerContent } from "../ContainerContent";
import { ProfileMenu } from "../../organisms/ProfileMenu";
import { ButtonNotifications } from "../ButtonNotifications";
import { ButtonLinkBack } from "../ButtonLinkBack";
import { ChevronRight20 } from "@carbon/icons-react";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    root: {
      backgroundColor: theme.palette.background.paper,
      color: "unset",
      minHeight: 172,
      boxShadow: "none",
      borderBottom: `1px solid ${theme.palette.divider}`,
      [theme.breakpoints.down("xs")]: {
        minHeight: 120,
      },
    },
    toolBar: {
      display: "inline-flex",
      justifyContent: "space-between",
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      display: "flex",
      alignItems: "center",
    },
    titleChevron: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
    fabElement: {
      position: "absolute",
      zIndex: 1,
      bottom: theme.spacing(-3.5),
      left: theme.spacing(-1),
      [theme.breakpoints.only("xs")]: {
        left: theme.spacing(-2),
        bottom: theme.spacing(-2.25),
      },
    },
    secondaryContent: {
      marginTop: "auto",
    },
  })
);

interface ContainerAppBarProps {
  dispatch: AppDispatch;
  notificationIcon?: React.ReactNode;
  userIcon?: React.ReactNode;
  parentTitle?: React.ReactNode;
  parentTo?: string;
  title?: React.ReactNode;
  secondaryContent?: React.ReactNode;
  fabElement?: React.ReactNode;
}

export const ContainerAppBar = ({
  dispatch,
  notificationIcon,
  userIcon,
  fabElement,
  parentTitle,
  parentTo,
  title,
  secondaryContent,
}: ContainerAppBarProps) => {
  const classes = useStyles();
  const theme = useTheme();
  const breakpointSm = useMediaQuery(theme.breakpoints.up("sm"));

  const appBarTitle = () => {
    return (
      <Typography variant="subtitle1" className={classes.title}>
        {parentTitle && breakpointSm ? (
          <React.Fragment>
            {parentTo ? (
              <Link
                data-testid="parent-to"
                color="inherit"
                component={NavLink}
                to={parentTo}
              >
                {parentTitle}
              </Link>
            ) : (
              parentTitle
            )}
            <ChevronRight20 className={classes.titleChevron} />
            {title}
          </React.Fragment>
        ) : (
          title
        )}
      </Typography>
    );
  };

  const appBarLeftIcon = () => {
    const result =
      parentTitle && parentTo && !breakpointSm ? (
        <ButtonLinkBack to={parentTo} />
      ) : (
        <HamburgerMenu dispatch={dispatch} />
      );
    return <Box mr={2}>{result}</Box>;
  };

  return (
    <AppBar position="sticky" className={classes.root}>
      <Toolbar className={classes.toolBar}>
        <Box display="inline-flex" ml={-1.5} data-testid="toolbar-header">
          {appBarLeftIcon()}
          {appBarTitle()}
        </Box>
        <Box display="inline-flex" alignItems="center" mr={-1.5}>
          {notificationIcon ? notificationIcon : <ButtonNotifications />}
          {userIcon ? userIcon : <ProfileMenu />}
        </Box>
      </Toolbar>
      {secondaryContent || fabElement ? (
        <ContainerContent containerClassName={classes.secondaryContent}>
          {fabElement ? (
            <Box className={classes.fabElement}>{fabElement}</Box>
          ) : null}
          {secondaryContent && secondaryContent}
        </ContainerContent>
      ) : null}
    </AppBar>
  );
};
