import React, { useEffect } from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { fetchAgreementsEffect } from "../AgreementRegistration/effects";
import { fetchAgreementTemplatesEffect } from "../AgreementTemplates/effects";
import { getUserIsSocAdminSelector } from "../SignIn/selectors";
import { getCreateAgreementLinkSelector } from "../AgreementRegistration/selectors";
import { paths } from "../../../utils/common";
import { AgreementsNavigationBar } from "../../organisms/AgreementsNavigationBar";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { AgreementsFab } from "../../molecules/AgreementsFab/component";
import debounce from "lodash/debounce";
import { concatQueryUrl } from "../../../utils/helpers";
import { RouteComponentProps } from "react-router";
import { InputSearchShrink } from "../../organisms/InputSearchShrink";
import { useMediaQuery, useTheme } from "@material-ui/core";
import { isSearchDisabled } from "./utils";

export const Agreements: React.FC<RouteComponentProps> = ({
  location: { pathname, search },
  history: { push },
}) => {
  const dispatch = useAppDispatch();
  const dispatchEffect = useAppDispatchEffect();
  const state = useAppSelector((state) => state);
  const theme = useTheme();
  const breakpointXs = useMediaQuery(theme.breakpoints.down("xs"));
  const isDraftsPage = pathname === paths.AGREEMENTS_DRAFT_LIST;
  const isAgreementsPage = pathname === paths.AGREEMENTS_LIST;
  const isTemplatesPage = pathname === paths.AGREEMENTS_TEMPLATE_LIST;
  const isSocietyAdmin = getUserIsSocAdminSelector(state);

  const canCreateAgreement = Boolean(getCreateAgreementLinkSelector(state));

  const handleQueryChange = debounce((query: string) => {
    if (query.length > 2 || query === "") {
      const updatedSearch = concatQueryUrl(search, { search: query });
      push({
        search: updatedSearch,
      });
    }
  }, 500);

  useEffect(() => {
    const urlParams = new URLSearchParams(search);
    const query = urlParams.get("search") || undefined;
    if (isDraftsPage || isAgreementsPage) {
      dispatchEffect(fetchAgreementsEffect({ query }));
    }
    if (isTemplatesPage) {
      dispatchEffect(fetchAgreementTemplatesEffect());
    }
  }, [dispatchEffect, isDraftsPage, isAgreementsPage, isTemplatesPage, search]);

  return (
    <React.Fragment>
      <ContainerAppBar
        dispatch={dispatch}
        title="Agreements"
        userIcon={
          breakpointXs && (
            <InputSearchShrink onChange={handleQueryChange} search={search} />
          )
        }
        secondaryContent={
          <AgreementsNavigationBar
            isSocietyAdmin={isSocietyAdmin}
            search={search}
            onQueryChange={handleQueryChange}
            textFieldBreakpoint={!breakpointXs}
            searchDisabled={isSearchDisabled(search, pathname, state)}
          />
        }
      />

      <AgreementsFab
        isDraftsPage={isDraftsPage}
        isAgreementsPage={isAgreementsPage}
        canCreateAgreement={canCreateAgreement}
        isSocietyAdmin={isSocietyAdmin}
        isTemplatesPage={isTemplatesPage}
      />
    </React.Fragment>
  );
};
