import React from "react";
import { ContainerWithLogo } from "../../molecules/ContainerWithLogo";
import { FormSignIn } from "../../organisms/FormSignIn";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { SignUpCheckEmail } from "../../molecules/SignUpCheckEmail";
import { showEmailNotificationAction } from "./actions";
import { RouteComponentProps } from "react-router";
import { FormPasswordReset } from "../../organisms/FormPasswordReset";
import { FormPasswordResetConfirm } from "../../organisms/FormPasswordResetConfirm";
import { paths } from "../../../utils/common";

export const SignInPage: React.FC<RouteComponentProps<{ subPage: string }>> = ({
  match: {
    params: { subPage },
  },
}) => {
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();
  const { showEmailNotification, passwordReset } = useAppSelector(
    (state) => state.signIn
  );

  const handleNotificationClose = () =>
    dispatch(showEmailNotificationAction(false));

  return (
    <ContainerWithLogo>
      <SignUpCheckEmail
        show={showEmailNotification}
        onClose={handleNotificationClose}
      />
      {!subPage && <FormSignIn dispatchEffect={dispatchEffect} />}
      {subPage === paths.subPage.RESET_PASSWORD && (
        <FormPasswordReset dispatchEffect={dispatchEffect} />
      )}
      {subPage === paths.subPage.RESET_PASSWORD_CONFIRM && (
        <FormPasswordResetConfirm
          dispatchEffect={dispatchEffect}
          passwordReset={passwordReset}
        />
      )}
    </ContainerWithLogo>
  );
};
