export interface FaqAccess {
  works: boolean;
  agreements: boolean;
  publisherDetails: boolean;
}

export const getFaqAccessObject = (state: AppState): FaqAccess => ({
  works: Boolean(getWorksListFetchLinkSelector(state)),
  agreements: Boolean(getAgreementsListFetchLinkSelector(state)),
  publisherDetails: Boolean(getCompanySelector(state)),
});

