export const getFilteredByAccessFaqContent = (object: FaqNavigationItem) => {
  const newObject = { ...object };
  if (newObject.navigation) {
    newObject.navigation = newObject.navigation
      .filter(
        (obj: FaqNavigationItem) =>
          obj?.access === true || obj?.access === undefined
      )
      .map((obj: FaqNavigationItem) => getFilteredByAccessFaqContent(obj));
  }
  return newObject;
};

export const getNormalizedFaqObject = (
  object: FaqNavigationItem | null,
  parentId?: string
): { [id: string]: Partial<FaqNavigationItem> } => {
  if (!object) return {};
  const initialObject = {
    [object.id]: { title: object.title, parent: parentId },
  };
  return object.navigation.reduce(
    (
      acc: { [id: string]: Partial<FaqNavigationItem> },
      cur: FaqNavigationItem
    ) => {
      return { ...acc, ...getNormalizedFaqObject(cur, object.id) };
    },
    initialObject
  );
};

export const getFullPathByParents = (
  normalizedFaqObject: { [id: string]: Partial<FaqNavigationItem> },
  initialPath?: string
): string => {
  if (initialPath) {
    const parentId = normalizedFaqObject[initialPath].parent;
    return `${getFullPathByParents(normalizedFaqObject, parentId)}${
      parentId ? "." : ""
    }${initialPath}`;
  }
  return "";
};

export const getFaqObjectByPath = (
  accessObject: FaqAccess,
  path?: string,
  faqObject?: FaqNavigationItem | null
): FaqNavigationItem | null => {
  if (!path) {
    return null;
  }
  const faqContent =
    faqObject || getFilteredByAccessFaqContent(getFaqContent(accessObject));
  const separatedPaths = path.split(".");
  return separatedPaths.reduce(
    (acc: FaqNavigationItem | null, curr: string): FaqNavigationItem | null => {
      if (curr === faqContent.id) {
        return faqContent;
      }
      return acc
        ? acc.navigation.find((navigationItem) => navigationItem.id === curr) ||
            null
        : null;
    },
    faqContent
  );
};

export const getDefaultPathByUrl = (access: FaqAccess, pathname: string) => {
  if (pathname.includes(paths.WORKS) && access.works) {
    return "home.works";
  }
  if (pathname.includes(paths.AGREEMENTS) && access.agreements) {
    return "home.agreements";
  }
  if (pathname.includes(paths.PROFILE_PUBLISHER) && access.publisherDetails) {
    return "home.profile.publisherDetails";
  }
  return "home";
};
