import React from "react";
import { FieldProps, getIn } from "formik";
import {
  createStyles,
  makeStyles,
  TableRow,
  Theme,
  Typography,
} from "@material-ui/core";
import { AgreementFormValues } from "../FormAgreement";
import get from "lodash/get";
import TableCell from "@material-ui/core/TableCell";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    errorCell: {
      border: "none",
      padding: 0,
      paddingRight: theme.spacing(18),
      lineHeight: 0,
    },
    errorCellText: {
      fontFamily: "Inter",
      padding: theme.spacing(0, 2),
    },
    svg: {
      fill: theme.palette.error.main,
    },
  })
);

interface Shares {
  assigneeCollectibleShare: string;
  publisherShareRetained: string;
  writerShareRetained: string;
}

type TermErrors = Array<{
  shares?: Shares[];
}>;

export const FormikTermShareErrorRow: React.FC<
  FieldProps<any, AgreementFormValues>
> = ({ form: { errors, touched } }) => {
  const classes = useStyles();
  const termErrors: Partial<TermErrors> | undefined = getIn(errors, "terms");

  const anyShareFieldWithError = termErrors?.find((term, termIndex) => {
    return term?.shares?.find((share, shareIndex) => {
      const parentPath = `terms.${termIndex}.shares.${shareIndex}`;
      return [
        "assigneeCollectibleShare",
        "publisherShareRetained",
        "writerShareRetained",
      ].find((key) => {
        const shareError = get(share, key);
        if (shareError) {
          const shareErrorPath = `${parentPath}.${key}`;
          return get(touched, shareErrorPath);
        }
        return false;
      });
    });
  });

  return anyShareFieldWithError ? (
    <TableRow>
      <TableCell colSpan={7} className={classes.errorCell} align="right">
        <svg
          width="102"
          height="9"
          viewBox="0 0 102 9"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <rect className={classes.svg} y="3.5" width="1" height="4.5" />
          <rect
            className={classes.svg}
            y="8.5"
            width="1"
            height="102"
            transform="rotate(-90 0 8.5)"
            fill="#E43131"
          />
        </svg>

        <Typography
          variant="caption"
          color="error"
          className={classes.errorCellText}
        >
          The share sum of a right has to equal 100%
        </Typography>

        <svg
          width="102"
          height="9"
          viewBox="0 0 102 9"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <rect
            className={classes.svg}
            width="1"
            height="4.5"
            transform="matrix(-1 0 0 1 102 3.5)"
          />
          <rect
            className={classes.svg}
            width="1"
            height="102"
            transform="matrix(0 -1 -1 0 102 8.5)"
          />
        </svg>
      </TableCell>
    </TableRow>
  ) : null;
};
