import {
  ActionTypes,
  AuthPolicy,
  BOOTSTRAP,
  BOOTSTRAP_FAILURE,
  BOOTSTRAP_SUCCESS,
  CHANGE_AVATAR_SUCCESS,
  CHANGE_EMAIL_CONFIRM_SUCCESS,
  CHANGE_EMAIL_REQUEST_FAIL,
  CHANGE_EMAIL_REQUEST_SUCCESS,
  FETCH_AUTH_POLICY_SUCCESS,
  FETCH_PROFILE,
  FETCH_PROFILE_FAIL,
  FETCH_PROFILE_SUCCESS,
  PATCH_PROFILE_SUCCESS,
  RESET_PASSWORD_FAIL,
  RESET_PASSWORD_SUCCESS,
  SHOW_EMAIL_NOTIFICATION,
  SIGN_IN,
  SIGN_IN_FAILURE,
  SIGN_IN_SUCCESS,
} from "./actions.types";
import { LoadingState } from "../../../types/enums";
import {
  _BootstrapLinks,
  BootstrapAccount,
  BootstrapCompany,
  Href,
  LegalProfile,
  PasswordReset,
} from "../../../utils/models";

export interface SignInState {
  authenticated: boolean;
  email: string;
  company?: BootstrapCompany;
  account?: BootstrapAccount;
  legalProfile?: LegalProfile;
  redirect: string;
  isSubmitting: boolean;
  authPolicy?: AuthPolicy;
  _links?: _BootstrapLinks;
  showEmailNotification: boolean;
  changeEmailConfirmHref?: Href;
  passwordReset?: PasswordReset;
  bootstrapLoadingState: LoadingState;
  signInLoadingState: LoadingState;
  legalProfileLoadingState: LoadingState;
}

export const signInInitialState: SignInState = {
  authenticated: false,
  email: "",
  isSubmitting: false,
  redirect: "",
  showEmailNotification: false,
  bootstrapLoadingState: LoadingState.PRISTINE,
  signInLoadingState: LoadingState.PRISTINE,
  legalProfileLoadingState: LoadingState.PRISTINE,
};

export const signInReducer = (
  state: SignInState = signInInitialState,
  action: ActionTypes
): SignInState => {
  switch (action.type) {
    case BOOTSTRAP: {
      return {
        ...state,
        bootstrapLoadingState: LoadingState.FETCHING,
      };
    }

    case BOOTSTRAP_SUCCESS: {
      const { company, _links, account, dlr } = action.data;
      return {
        ...state,
        bootstrapLoadingState: LoadingState.FETCHED,
        _links: _links,
        account: account,
        company: company,
        authenticated: !_links?.signIn && !dlr,
      };
    }

    case BOOTSTRAP_FAILURE: {
      return {
        ...state,
        bootstrapLoadingState: LoadingState.ERROR,
      };
    }

    case SIGN_IN: {
      return {
        ...state,
        bootstrapLoadingState: LoadingState.FETCHING,
      };
    }

    case SIGN_IN_SUCCESS: {
      return {
        ...state,
        bootstrapLoadingState: LoadingState.FETCHED,
      };
    }

    case SIGN_IN_FAILURE: {
      return {
        ...state,
        bootstrapLoadingState: LoadingState.ERROR,
      };
    }

    case FETCH_AUTH_POLICY_SUCCESS: {
      return {
        ...state,
        authPolicy: action.authPolicy,
      };
    }
    case PATCH_PROFILE_SUCCESS: {
      return {
        ...state,
        legalProfile: action.profile,
        account: state.account
          ? {
              ...state.account,
              firstName: action.profile.firstName,
              lastName: action.profile.lastName,
              profile: {
                ...state.account.profile,
                displayName: action.profile.displayName,
              },
            }
          : undefined,
      };
    }

    case CHANGE_AVATAR_SUCCESS: {
      return {
        ...state,
        account: state.account
          ? {
              ...state.account,
              profile: {
                ...state.account.profile,
                profileImage: action.avatarSrc,
              },
            }
          : undefined,
      };
    }

    case SHOW_EMAIL_NOTIFICATION: {
      return {
        ...state,
        showEmailNotification: action.show,
      };
    }

    case RESET_PASSWORD_SUCCESS: {
      return {
        ...state,
        passwordReset: action.data,
      };
    }

    case RESET_PASSWORD_FAIL: {
      return {
        ...state,
        passwordReset: undefined,
      };
    }

    case FETCH_PROFILE: {
      return {
        ...state,
        legalProfileLoadingState: LoadingState.FETCHING,
      };
    }

    case FETCH_PROFILE_SUCCESS: {
      return {
        ...state,
        legalProfileLoadingState: LoadingState.FETCHED,
        legalProfile: action.profile,
      };
    }

    case FETCH_PROFILE_FAIL: {
      return {
        ...state,
        legalProfileLoadingState: LoadingState.ERROR,
      };
    }

    case CHANGE_EMAIL_REQUEST_SUCCESS: {
      return {
        ...state,
        changeEmailConfirmHref: action.confirmHref,
      };
    }

    case CHANGE_EMAIL_REQUEST_FAIL:
      return {
        ...state,
        changeEmailConfirmHref: undefined,
      };

    case CHANGE_EMAIL_CONFIRM_SUCCESS: {
      return {
        ...state,
        changeEmailConfirmHref: undefined,
        account: state.account
          ? { ...state.account, accountEmail: action.email }
          : undefined,
      };
    }

    default:
      return state;
  }
};
