export const convertSecondsToDate = (seconds: number) => {
  const date = new Date();
  date.setHours(0, 0, 0, 0);
  date.setSeconds(seconds);
  return date;
};

export const convertDayTimeToSeconds = (date: Date) =>
  date.getMinutes() * 60 + date.getSeconds();

export function enumKeys<E>(e: E): (keyof E)[] {
  return Object.keys(e || {}) as (keyof E)[];
}

export const generateGuid = () => {
  const constrain = /^e[a-z0-9]{8}$/;
  const guid = `e${Math.random().toString(36).substr(2, 8)}`;
  return guid.match(constrain) ? guid : undefined;
};

export const convertWorkCreatorSharesToPercentage: (
  creators: WorkCreator[]
) => WorkCreator[] = (creators) => {
  const convertShareAction = (share?: number) => (share ? share / 100 : share);
  return creators.map((creator) => ({
    ...creator,
    mrShare: convertShareAction(creator.mrShare),
    prShare: convertShareAction(creator.prShare),
    publishers: creator.publishers?.map((publisher) => ({
      ...publisher,
      mrShare: convertShareAction(publisher.mrShare),
      prShare: convertShareAction(publisher.prShare),
    })),
  }));
};

export const convertWorkCreatorSharesFromPercentage: (
  operations?: WorkPatchCreator[]
) => WorkPatchCreator[] | undefined = (creators) => {
  const convertShareAction = (share?: number) =>
    share ? Math.floor(share * 100) : share;
  return creators?.map((op) => ({
    ...op,
    creator: {
      ...op.creator,
      mrShare: convertShareAction(op.creator.mrShare),
      prShare: convertShareAction(op.creator.prShare),
      publishers: op.creator.publishers?.map((opPublisher) => ({
        op: opPublisher.op,
        publisher: {
          ...opPublisher.publisher,
          mrShare: convertShareAction(opPublisher.publisher.mrShare),
          prShare: convertShareAction(opPublisher.publisher.prShare),
        },
      })),
    },
  }));
};

export const convertTermShareToFloat: (
  share: AgreementShare
) => AgreementShare = (share: AgreementShare) => ({
  ...share,
  writerShareRetained: share.writerShareRetained
    ? share.writerShareRetained / 100
    : 0,
  publisherShareRetained: share.publisherShareRetained
    ? share.publisherShareRetained / 100
    : 0,
  assigneeCollectibleShare: share.assigneeCollectibleShare
    ? share.assigneeCollectibleShare / 100
    : 0,
});

export const convertTermShareToInt: (share: AgreementShare) => AgreementShare =
  (share: AgreementShare) => ({
    rightType: share.rightType,
    usageTypes: share.usageTypes,
    writerShareRetained: (share.writerShareRetained || 0) * 100,
    publisherShareRetained: (share.publisherShareRetained || 0) * 100,
    assigneeCollectibleShare: (share.assigneeCollectibleShare || 0) * 100,
  });

export const parseDlrToBlob = (dlr: Dlr) => {
  try {
    const encoded = encodeURIComponent(JSON.stringify(dlr));
    return btoa(encoded);
  } catch (e) {
    console.error(e);
    return undefined;
  }
};

export const parseBase64 = (base64?: string) => {
  try {
    return base64 && JSON.parse(decodeURIComponent(atob(base64)));
  } catch (e) {
    console.error(e);
    return undefined;
  }
};

export const ensureDomain = (url: string) =>
  Boolean(url.match(/^https:\/\/api(.[-a-z0-9]+){0,2}.session.id\//));

export const addValidationKeyForRequiredEnum = (schema: {
  required: string[];
  properties: { [index: string]: any };
}) => {
  const { required, properties } = schema;
  return {
    ...schema,
    properties: Object.keys(properties).reduce(
      (acc: { [index: string]: any }, propertyKey) => {
        if (
          required.includes(propertyKey) &&
          Object.keys(properties[propertyKey]).includes("enum")
        ) {
          return {
            ...acc,
            [propertyKey]: {
              ...properties[propertyKey],
              oneOf: properties[propertyKey].enum,
            },
          };
        }
        return { ...acc, [propertyKey]: properties[propertyKey] };
      },
      {}
    ),
  };
};

export const mergeEnumLabels = (
  labels: {
    [id: string]: string;
  },
  types?: string[]
) =>
  types
    ? types.reduce(
        (acc, curr) => ({
          ...acc,
          [curr]: Boolean(labels[curr]) ? labels[curr] : curr,
        }),
        {}
      )
    : {};

export const convertToValueLabel = (obj: { [id: string]: string }) =>
  Object.keys(obj).map((el) => ({ value: el, label: obj[el] }));

export const iOS =
  typeof window !== "undefined" && /iPad|iPhone|iPod/.test(navigator.userAgent);

export const buildPasswordPolicyMessage = ({
  minimumLength,
  requireNumbers,
  requireLowercase,
  requireUppercase,
  requireSpecialCharacters,
}: PasswordPolicy) => {
  const requirements = [];

  if (requireUppercase) {
    requirements.push("uppercase");
  }
  if (requireLowercase) {
    requirements.push("lowercase");
  }

  if (requirements.length) {
    const last = requirements.length - 1;
    requirements[last] = `${requirements[last]}`;
  }

  if (requireNumbers) {
    requirements.push("numbers");
  }
  if (requireSpecialCharacters) {
    requirements.push("special character");
  }

  const { length } = requirements;
  const beginning = `Use ${minimumLength} or more characters`;
  const nounsPrefix = length ? " with a mix of " : "";
  const nouns =
    length > 1
      ? `${requirements.slice(0, length - 1).join(", ")} and ${
          requirements[length - 1]
        }`
      : length
      ? requirements[length - 1]
      : "";

  return `${beginning}${nounsPrefix}${nouns}.`;
};

export const isDevelopment = Boolean(
  window.location.host.match(/^.*(test|dev).session.id|^localhost/)
);

export const addUserToCreatorsList = (
  account?: BootstrapAccount,
  ipi?: Ipi
) => {
  const time = createCurrentTime();
  const creator: WorkCreator = {
    isSelf: true,
    role: PartyRoleType.C,
    created: time,
    updated: time,
    ipi: {
      ipiReference: "",
      ipiNameNumber: Number(ipi?.ipiNameNumber),
      verified: Boolean(ipi?.verified),
    },
    profile: {
      displayName:
        ipi?.profile.displayName || account?.profile.displayName || "",
      initials: ipi?.profile?.initials || account?.profile.initials || "",
    },
  };
  return account ? [creator] : [];
};

export const formatDate = (iso?: string) =>
  format(iso ? new Date(iso) : new Date(), "yyyy-MM-dd");

export const createSplitObject: (props?: {
  writers?: Creator[];
  noShares?: boolean;
}) => Split = (props) => {
  const initialContributorShare: ContributorShare = {
    guid: "",
    memberGuid: INIT_WRITER_GUID,
    status: SplitStatusType.UNDECIDED,
    relativePerformancePercentage: 0,
    relativeMechanicalPercentage: 0,
  };
  const initialSplit: Split = {
    guid: "",
    status: SplitStatusType.UNDECIDED,
    territory: "I2136",
    endDate: formatDate("9999-12-31T00:00:00Z"),
    startDate: formatDate("1900-01-01T00:00:00Z"),
    contributorShares: props?.noShares ? [] : [initialContributorShare],
  };

  return props?.writers
    ? {
        ...initialSplit,
        contributorShares: props.writers.map((el) => ({
          guid: "",
          memberGuid: el.guid,
          status: SplitStatusType.UNDECIDED,
        })),
      }
    : initialSplit;
};

export const replaceUndefinedWorkShareNumToZero: (split: Split) => Split = (
  split: Split
) => ({
  ...split,
  contributorShares: split.contributorShares.map(
    ({
      relativeMechanicalPercentage: mrShare,
      relativePerformancePercentage: prShare,
      ...rest
    }) => ({
      ...rest,
      relativeMechanicalPercentage: mrShare || 0,
      relativePerformancePercentage: prShare || 0,
    })
  ),
});

export const isSplitChanged = (newState: Split, oldState?: Split) => {
  if (oldState?.contributorShares.length !== newState.contributorShares.length)
    return true;

  return Boolean(
    oldState.contributorShares.find((old) => {
      const match = newState.contributorShares.find(
        (el) => el.guid === old.guid
      );
      if (!match) return true;
      return (
        match.relativePerformancePercentage !==
          old.relativePerformancePercentage ||
        match.relativeMechanicalPercentage !== old.relativeMechanicalPercentage
      );
    })
  );
};

export const preventEnterOnKeyDown = (
  keyEvent: React.KeyboardEvent<HTMLElement>
) => {
  if ((keyEvent.charCode || keyEvent.keyCode) === 13) {
    keyEvent.preventDefault();
  }
};

export const doesUserHaveRoles = (state: AppState, roles: AccountRoles[]) => {
  const userRoles = state.signIn.account?.roles || [];
  return Boolean(roles.every((role) => userRoles.indexOf(role) !== -1));
};

export const createCurrentTime: () => Time = () => {
  const date = new Date();
  return {
    iso: date.toISOString(),
    timestamp: Math.floor(date.getTime() / 1000),
  };
};

export const buildWorkQueryUrl = ({
  url,
  statuses,
  sortParams,
  query,
}: {
  url: string;
  statuses?: WorkListItemStatusType[];
  sortParams?: {
    sortBy: WorkSortVariant;
    sortOrder: SortOrder;
  };
  query?: string;
}) => {
  const { origin, pathname, search } = new URL(url.replace(/{.*.}/, ""));
  const baseURL = `${origin}${pathname}`;
  const currentQueryParams = queryString.parse(search);
  const newQueryParams = {
    ...currentQueryParams,
    ...(statuses?.length && { "status[]": statuses }),
    ...(sortParams?.sortBy && { sortBy: sortParams.sortBy }),
    ...(sortParams?.sortOrder && { sortOrder: sortParams.sortOrder }),
    ...(query && { search: query }),
  };
  const parsed = queryString.stringify(newQueryParams);

  return `${baseURL}?${parsed}`;
};

