export const concatQueryUrl = (
  currentSearch: string,
  newProps: { [index: string]: any }
) => {
  const currentQueryParams = queryString.parse(currentSearch);
  const newQueryParams = Object.assign(currentQueryParams, newProps);
  return queryString.stringify(newQueryParams, {
    arrayFormat: "comma",
    encode: true,
  });
};

export const isElementOverflowed = (ref: RefObject<HTMLElement | undefined>) =>
  Boolean(ref.current && ref.current?.offsetWidth < ref.current?.scrollWidth);

export const formatTermToPatch = (term: AgreementTerm) => ({
  territories: term.territories,
  startDate: term.startDate && format(new Date(term.startDate), "yyyy-MM-dd"),
  endDate: term.endDate && format(new Date(term.endDate), "yyyy-MM-dd"),
  priorRoyalties:
    term?.priorRoyalties &&
    format(new Date(term?.priorRoyalties), "yyyy-MM-dd"),
});

export const formatTermShareToPatch = (share: AgreementShare) => ({
  rightType: share.rightType,
  usageTypes: share.usageTypes,
  writerShareRetained: share.writerShareRetained,
  publisherShareRetained: share.publisherShareRetained,
  assigneeCollectibleShare: share.assigneeCollectibleShare,
});

export const downloadFile = (url: string) => {
  const link = document.createElement("a");
  link.href = url;
  document.body.appendChild(link);
  link.click();
  link.parentNode?.removeChild(link);
};

export const mapValuesDeep = (v: object, callback: (prop: object) => any) =>
  isObject(v)
    ? mapValues<object>(v, (v: () => any) => mapValuesDeep(v, callback))
    : callback(v);

export const delay = (ms: number) =>
  new Promise((response) => setTimeout(response, ms));

export const getDeepKeys = (obj: { [index: string]: any }) => {
  let keys: string[] = [];
  for (const key in obj) {
    const keyValue = obj[key];
    const keyValueType = typeof keyValue;
    if (keyValueType === "object") {
      const subKeys = getDeepKeys(keyValue);
      keys = keys.concat(subKeys.map((subKey) => `${key}.${subKey}`));
    } else if (["number", "string", "boolean"].includes(keyValueType)) {
      keys.push(key);
    }
  }
  return keys;
};

const isElementInViewport = (el: HTMLElement) => {
  const { top, left, bottom, right } = el.getBoundingClientRect();
  return (
    top >= 0 &&
    left >= 0 &&
    bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
    right <= (window.innerWidth || document.documentElement.clientWidth)
  );
};

export const scrollToFormErrors = (errors: FormikErrors<{}>) => {
  const errorKeys = getDeepKeys(errors);
  if (errorKeys.length > 0) {
    const element = document.getElementsByName(errorKeys[0])[0];
    if (element && !isElementInViewport(element)) {
      element?.scrollIntoView({ block: "center" });
    }
  }
};

export const removeAppLoaderNode = () => {
  document.getElementById("loader")?.remove();
  const root = document.getElementById("root");
  root?.removeAttribute("style");
  root?.classList.add("appear");
};

export const isLoggedOutPath = (pathname: string) =>
  pathname.startsWith(paths.SIGN_IN) ||
  pathname.startsWith(paths.SIGN_UP) ||
  pathname.startsWith(paths.AUTH_LOGOUT) ||
  pathname === paths.NOT_APPROVED;

export const hasEmptyValues = (values: { [index: string]: string }) => {
  const hasEmpty = Object.keys(values).find((key) => values[key] === "");
  return Boolean(hasEmpty);
};
