import React from "react";
import {
  Box,
  createStyles,
  Grid,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import {
  SalesOrManufactureLabel,
  UsaLicenseIndicatorType,
} from "../../../types/enums";
import { AgreementFormValues } from "../../organisms/FormAgreement";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    bold: {
      fontWeight: 500,
    },
    alignRight: {
      textAlign: "right",
    },
    blockWrapper: {
      display: "flex",
      flexDirection: "column",
      width: "100%",
      textAlign: "right",
      paddingRight: theme.spacing(1.5),
    },
    title: {
      minWidth: 200,
      textAlign: "right",
    },
    detailSummary: {
      width: 200,
      textAlign: "right",
      color: theme.palette.text.secondary,
      fontFamily: "Inter",
    },
    detail: {
      width: 140,
      marginLeft: theme.spacing(1.5),
    },
    agreementNumbers: {
      display: "flex",
      alignItems: "baseline",
    },
    agreementNumbersDetail: {
      display: "flex",
      flexDirection: "column",
    },
    row: {
      paddingBottom: theme.spacing(2),
    },
  })
);

interface AgreementGeneralSummaryProps {
  agreement: AgreementFormValues;
}

export const AgreementGeneralSummary: React.FC<AgreementGeneralSummaryProps> = ({
  agreement: {
    submitterAgreementNumbers,
    salesOrManufacture,
    bulkTransferWorks,
    usaLicenseIndicator,
    libraryMusic,
    agreementType,
    recordingPrefix,
  },
}) => {
  const classes = useStyles();

  return (
    <React.Fragment>
      <Grid item xs={5}>
        <Box display="flex" flexDirection="column" alignItems="flex-end">
          <Box display="flex" className={classes.row}>
            <Typography variant="caption" className={classes.detailSummary}>
              Submitter Agreement Number:
            </Typography>
            <Typography
              variant="caption"
              className={classes.detail}
              data-testid="agreement-number"
            >
              {submitterAgreementNumbers[0] || "N/A"}
            </Typography>
          </Box>
          <Box className={clsx(classes.agreementNumbers, classes.row)}>
            <Typography variant="caption" className={classes.detailSummary}>
              Agreement Number(s):
            </Typography>
            <Box
              data-testid="agreement-numbers"
              className={clsx(classes.detail, classes.agreementNumbersDetail)}
            >
              {submitterAgreementNumbers.slice(1).map((num, index) => (
                <Typography
                  variant="caption"
                  key={`submitterAgreementNumbers-${index}`}
                >
                  {num}
                </Typography>
              ))}
              {!submitterAgreementNumbers.slice(1).length && (
                <Typography variant="caption" className={classes.detail}>
                  N/A
                </Typography>
              )}
            </Box>
          </Box>
          <Box display="flex" className={classes.row}>
            <Typography variant="caption" className={classes.detailSummary}>
              Previously Registered Works:
            </Typography>
            <Typography
              variant="caption"
              className={classes.detail}
              data-testid="bulk-transfer"
            >
              {bulkTransferWorks ? "Transfer to Agreement" : "Dont Transfer"}
            </Typography>
          </Box>
          <Box display="flex" className={classes.row}>
            <Typography variant="caption" className={classes.detailSummary}>
              Type of Agreement:
            </Typography>
            <Typography
              variant="caption"
              className={classes.detail}
              data-testid="agreement-type"
            >
              {agreementType === "general" ? "General" : ""}
              {agreementType === "specific" ? "Specific" : ""}
            </Typography>
          </Box>
        </Box>
      </Grid>
      <Grid item xs={4}>
        <Box display="flex" flexDirection="column" alignItems="flex-start">
          <Box display="flex" className={classes.row}>
            <Typography variant="caption" className={classes.detailSummary}>
              Library Music:
            </Typography>
            <Typography
              variant="caption"
              className={classes.detail}
              data-testid="library-music"
            >
              {libraryMusic ? "Yes" : "No"}
            </Typography>
          </Box>
          <Box display="flex" className={classes.row}>
            <Typography variant="caption" className={classes.detailSummary}>
              Recording Prefix:
            </Typography>
            <Typography
              variant="caption"
              className={classes.detail}
              data-testid="recording-prefix"
            >
              {recordingPrefix || "N/A"}
            </Typography>
          </Box>
          <Box display="flex" className={classes.row}>
            <Typography variant="caption" className={classes.detailSummary}>
              USA License Indicator:
            </Typography>
            <Typography
              variant="caption"
              className={classes.detail}
              data-testid="usa-license-indicator"
            >
              {usaLicenseIndicator === UsaLicenseIndicatorType.NONE
                ? "None"
                : usaLicenseIndicator === UsaLicenseIndicatorType.ASCAP
                ? "ASCAP 010"
                : usaLicenseIndicator === UsaLicenseIndicatorType.BMI
                ? "BMI 021"
                : usaLicenseIndicator === UsaLicenseIndicatorType.CISAC
                ? "SESAC 071"
                : ""}
            </Typography>
          </Box>
          <Box display="flex" className={classes.row}>
            <Typography variant="caption" className={classes.detailSummary}>
              Rights Acquired For:
            </Typography>
            <Typography
              variant="caption"
              className={classes.detail}
              data-testid="acquired-rights"
            >
              {salesOrManufacture
                ? SalesOrManufactureLabel[salesOrManufacture]
                : ""}
            </Typography>
          </Box>
        </Box>
      </Grid>
    </React.Fragment>
  );
};
