import {
  Box,
  Container,
  createStyles,
  makeStyles,
  Theme,
} from "@material-ui/core";
import React from "react";
import clsx from "clsx";

interface StyleProps {
  shiftLeft: number;
}

const useStyles = makeStyles<Theme, StyleProps>((theme) =>
  createStyles({
    container: {
      paddingLeft: theme.spacing(3),
      paddingRight: theme.spacing(3),
    },
    inner: {
      position: "relative",
      paddingLeft: ({ shiftLeft }) =>
        `calc(${theme.spacing(5.5)}px - ${shiftLeft}px)`,

      [theme.breakpoints.only("xs")]: {
        paddingLeft: ({ shiftLeft }) => `calc(${shiftLeft}px)`,
      },
    },
  })
);

interface ContainerContentProps {
  children: React.ReactNode;
  className?: string;
  containerClassName?: string;
  shiftLeft?: number;
  disableMaxWidth?: boolean;
}

export const ContainerContent = ({
  className,
  containerClassName,
  shiftLeft = 0,
  disableMaxWidth,
  children,
}: ContainerContentProps) => {
  const classes = useStyles({ shiftLeft });

  return (
    <Container
      maxWidth={disableMaxWidth ? false : undefined}
      className={clsx(classes.container, containerClassName)}
    >
      <Box className={clsx(classes.inner, className)}>{children}</Box>
    </Container>
  );
};
