import React, { useState } from "react";
import { FieldProps, getIn } from "formik";
import {
  Box,
  ButtonBase,
  createStyles,
  List,
  ListItem,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import {
  BootstrapAccount,
  BootstrapCompany,
  IpiSearchCreatorOption,
  WorkCreator,
  WorkPublisher,
} from "../../../utils/models";
import { FetchWritersResponse } from "../../../api.types";
import { createCurrentTime } from "../../../utils/helpers";
import { CardPublisher } from "../../atoms/CardPublisher";
import { AvatarTitle } from "../../atoms/AvatarTitle";
import { WorkFormValues } from "../FormWorkRegistration";
import { SearchBox } from "../SearchBox";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    listItem: {
      display: "flex",
      flexDirection: "column",
      alignItems: "end",
      paddingTop: 0,
    },
    list: {
      padding: 0,
    },
    publisherTitle: {
      paddingLeft: theme.spacing(2),
    },
  })
);

interface FormikAddWorkPublisherProps {
  writerPath: string;
  disabled?: boolean;
  company?: BootstrapCompany;
  account?: BootstrapAccount;
  ipiSearchPublishersApi: string;
  onAdded: () => void;
}

export const FormikAddWorkPublisher: React.FC<
  FormikAddWorkPublisherProps & FieldProps<any, WorkFormValues>
> = ({
  form: { values, setFieldValue, isSubmitting },
  writerPath,
  disabled,
  company,
  account,
  ipiSearchPublishersApi,
                                            onAdded
}) => {
  const classes = useStyles();
  const [searchPhrase, setSearchPhrase] = useState("");
  const parentWriter: WorkCreator = getIn(values, writerPath);

  const handleSelect = (option: IpiSearchCreatorOption) => {
    const time = createCurrentTime();
    const publisher: WorkPublisher = {
      updated: time,
      created: time,
      ipi: {
        ipiNameNumber: option.ipiNameNumber,
      },
      profile: option.profile,
      isSelf: false,
    };
    setFieldValue(`${writerPath}.publishers`, [
      ...(parentWriter.publishers || []),
      { ipiNameNumber: publisher.ipi.ipiNameNumber },
    ]);
    const isNewPublisher = !values.publishers.find(
      (el) => el.ipi.ipiNameNumber === publisher.ipi.ipiNameNumber
    );
    if (isNewPublisher) {
      setFieldValue("publishers", [...values.publishers, publisher]);
    }
    onAdded();
  };

  const filterOutSelected = (options: IpiSearchCreatorOption[]) => {
    const creator: WorkCreator = getIn(values, writerPath);
    const selectedPublishersIds =
      creator.publishers?.map((el) => el.ipiNameNumber) || [];
    return options.filter(
      (el) => !selectedPublishersIds.includes(el.ipiNameNumber)
    );
  };

  const companyToOption: (
    company: BootstrapCompany
  ) => IpiSearchCreatorOption = (company: BootstrapCompany) => ({
    profile: {
      initials: "",
      displayName: company.name,
      reference: "",
    },
    ipiNameNumber: Number(company.ipiNameNumber),
    firstName: "",
    lastName: "",
    isCreator: false,
    mrSocietyAffiliations: [],
    nameNumberType: "",
    prSocietyAffiliations: [],
  });

  const showCompanyOption =
    company &&
    account &&
    filterOutSelected([companyToOption(company)]).length > 0;

  return (
    <SearchBox
      placeholder="Search Name, IPI"
      onChange={setSearchPhrase}
      apiSource={`${ipiSearchPublishersApi.replace(
        /{.*.}/,
        ""
      )}&searchPhrase={query}`}
      onSelect={handleSelect}
      openComponent={
        <Typography
          data-testid="Add Publisher"
          variant="button"
          component={ButtonBase}
          disabled={disabled || isSubmitting}
        >
          + Add Publisher
        </Typography>
      }
      renderOption={(option: IpiSearchCreatorOption) => (
        <AvatarTitle
          avatar={option.profile.initials || ""}
          alignItems="flex-start"
        >
          <CardPublisher
            publisher={{
              ipiNameNumber: option.ipiNameNumber,
              displayName: option.profile.displayName,
            }}
            wrapDisplayName
            highlightText={searchPhrase}
          />
        </AvatarTitle>
      )}
      optionsSelector={(data: FetchWritersResponse) =>
        data.response.ipiSearchResult.items
      }
      filterOptions={filterOutSelected}
      customContent={
        company && account && showCompanyOption ? (
          <Box>
            <Typography
              variant="subtitle2"
              color="textSecondary"
              className={classes.publisherTitle}
            >
              Linked Publisher
            </Typography>
            <List disablePadding>
              <ListItem
                button
                className={classes.listItem}
                onClick={() => handleSelect(companyToOption(company))}
              >
                <AvatarTitle
                  avatar={account.profile.initials || ""}
                  alignItems="flex-start"
                >
                  <CardPublisher
                    wrapDisplayName
                    publisher={{
                      ipiNameNumber: Number(company.ipiNameNumber),
                      displayName: company.name,
                    }}
                  />
                </AvatarTitle>
              </ListItem>
            </List>
          </Box>
        ) : undefined
      }
    />
  );
};
