import React, { useState } from "react";
import {
  Box,
  Button,
  Checkbox,
  createStyles,
  DialogActions,
  DialogContent,
  DialogTitle,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { Dialog } from "../../molecules/Dialog";
import { Checkbox32, CheckboxCheckedFilled32 } from "@carbon/icons-react";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dialog: {
      maxHeight: 536,
      minWidth: 528,
    },
    dialogContent: {
      paddingLeft: theme.spacing(4),
    },
    checkbox: {
      marginLeft: -4,
    },
    listItem: {
      height: theme.spacing(6),
    },
    list: {
      marginLeft: theme.spacing(-3),
      paddingTop: 0
    },
    submitButton: {
      width: 101,
    },
    toggleAllButton: {
      marginRight: "auto",
    },
  })
);

interface DialogIpiRightsProps {
  onConfirm: (checked: string[]) => void;
  onClose: () => void;
  open: boolean;
  ipiRights: { code: string; name: string }[];
  selected: string[];
  disabled?: boolean;
}

export const DialogIpiRights: React.FC<DialogIpiRightsProps> = ({
  onConfirm,
  open,
  onClose,
  disabled,
  selected,
  ipiRights,
}) => {
  const classes = useStyles();
  const [error, setError] = useState("");
  const [checked, setChecked] = useState<string[]>(
    ipiRights.filter((el) => selected.includes(el.code)).map((el) => el.code)
  );
  const allSelected = ipiRights.length === checked.length;

  const handleClose = () => {
    setChecked(selected);
    onClose();
    setError("");
  };

  const handleConfirm = () => {
    if (checked.length === 0) {
      setError("At least 1 IPI right is required.");
    } else {
      setError("");
      onConfirm(checked);
      onClose();
    }
  };

  const handleOpen = () => setChecked(selected);

  const handleToggleAll = () =>
    setChecked(allSelected ? [] : ipiRights.map((el) => el.code));

  const handleToggle = (code: string) => () => {
    const currentIndex = checked.indexOf(code);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(code);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };

  return (
    <Dialog
      variant="complex"
      open={open}
      onClose={handleClose}
      onEnter={handleOpen}
      classes={{ paper: classes.dialog }}
    >
      <DialogTitle>
        IPI Rights
        <Box>
          {error && (
            <Typography color="error" variant="body2">
              {error}
            </Typography>
          )}
        </Box>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <List className={classes.list}>
          {ipiRights.map((el) => (
            <ListItem
              className={classes.listItem}
              disabled={disabled}
              key={el.code}
              button
              onClick={handleToggle(el.code)}
            >
              <ListItemIcon>
                <Checkbox
                  icon={<Checkbox32 />}
                  checkedIcon={<CheckboxCheckedFilled32 />}
                  className={classes.checkbox}
                  color="primary"
                  checked={
                    checked.indexOf(el.code) !== -1 ||
                    checked.length === ipiRights.length
                  }
                  tabIndex={-1}
                  disableRipple
                />
              </ListItemIcon>
              <ListItemText id={el.code} disableTypography>
                <Typography variant="subtitle1">
                  {el.code} - {el.name}
                </Typography>
              </ListItemText>
            </ListItem>
          ))}
        </List>
      </DialogContent>
      <DialogActions>
        <Button
          color="primary"
          variant="text"
          onClick={handleToggleAll}
          disabled={disabled}
          className={classes.toggleAllButton}
        >
          {allSelected ? "Deselect All" : "Select All"}
        </Button>
        <Button autoFocus onClick={handleClose} color="primary">
          Cancel
        </Button>
        <Button
          variant="contained"
          color="primary"
          onClick={handleConfirm}
          disabled={disabled}
          className={classes.submitButton}
        >
          Ok
        </Button>
      </DialogActions>
    </Dialog>
  );
};
