import {
  PublisherRoleType,
  PublisherRoleTypeLabel,
} from "../../../types/enums";

export const formatPublisherRole = (dense: boolean) => (
  role: PublisherRoleType
) => {
  if (role === PublisherRoleType.E) {
    if (dense) {
      return "Orig.";
    }
    return PublisherRoleTypeLabel.E;
  }

  if (role === PublisherRoleType.SE) {
    if (dense) {
      return "Sub.";
    }
    return PublisherRoleTypeLabel.SE;
  }
};
