import React from "react";
import { ButtonFabAddNew } from "../../atoms/ButtonFabAddNew";
import { paths } from "../../../utils/common";
import { createStyles, makeStyles, Theme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      position: "absolute",
      bottom: theme.spacing(4),
      right: theme.spacing(4),
      zIndex: 1,
    },
  })
);

interface WorksFabProps {
  canCreateWork: boolean;
  canCreateTemplate: boolean;
  pathname: string;
}

export const WorksFab: React.FC<WorksFabProps> = ({
  canCreateTemplate,
  canCreateWork,
  pathname,
}) => {
  const classes = useStyles();
  const isDraftsPage = pathname === paths.WORKS_DRAFT_LIST;
  const isMyWorksPage = pathname === paths.WORKS_LIST;
  const isTemplatesPage = pathname === paths.WORKS_TEMPLATE_LIST;

  if ((isDraftsPage || isMyWorksPage) && canCreateWork) {
    return (
      <ButtonFabAddNew
        to={paths.WORKS_REGISTER}
        variant="extended"
        className={classes.root}
        text="New Work"
      />
    );
  }

  if (isTemplatesPage && canCreateTemplate) {
    return (
      <ButtonFabAddNew
        to={paths.WORK_TEMPLATE}
        variant="extended"
        className={classes.root}
        text="New Template"
      />
    );
  }

  return <React.Fragment />;
};
