import React from "react";
import {
  Box,
  createStyles,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { PublisherRoleTypeLabel } from "../../../types/enums";
import {
  ColumnBreakpoints,
  SUCCESS,
  WorkPublisher,
} from "../../../utils/models";
import { Field, FieldProps } from "formik";
import { CellMrShare } from "../../atoms/CellMrShare";
import { CellSplit } from "../../atoms/CellSplit";
import { CellAvatar } from "../../atoms/CellAvatar";
import { mapStatusToTooltip } from "../../atoms/FormikSplitStatusIndicator";
import { SvgElbowLine } from "../../atoms/SvgElbowLine";
import { CellOptions } from "../../atoms/CellOptions";
import { StatusIndicator } from "../../atoms/StatusIndicator";
import { CellCollaboration } from "../../atoms/CellCollaboration";
import { CellPrShare } from "../../atoms/CellPrShare";
import { RowCreatorListItem } from "../RowCreatorListItem";
import { FormikPercentageInput } from "../../atoms/FormikPercentageInput";
import { CellRole } from "../../atoms/CellRole";
import { CellCreator } from "../../atoms/CellCreator";
import { CardPublisher } from "../../atoms/CardPublisher";
import { ActionMenu } from "../../organisms/ActionMenu";
import { TooltipMap } from "../../atoms/TooltipMap";
import { FormikTypography } from "../../atoms/FormikTypography";
import { Icon } from "../../atoms/Icon";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    avatar: {
      marginLeft: "auto",
    },
    roleSelect: {
      color: theme.palette.text.secondary,
    },
    roleContainer: {
      display: "flex",
      alignContent: "center",
    },
    showLessButton: {
      border: "1px solid",
    },
    svgRelationship: {
      top: -37,
      left: 16,
    },
    row: {
      maxHeight: 80,
      minHeight: 80,
    },
    prShareReadOnly: {
      textAlign: "right",
      paddingRight: theme.spacing(4),
    },
    mrShareReadOnly: {
      textAlign: "right",
      paddingRight: theme.spacing(7),
    },
    icon: {
      color: theme.palette.info.main,
    },
  })
);

interface RowGridPublisherProps {
  publisher: WorkPublisher;
  onDelete: () => void;
  editable?: boolean;
  first: boolean;
  hideSplitColumn?: boolean;
  hideOptionsColumn?: boolean;
  hideCollaborationColumn?: boolean;
  onClick?: () => void;
  columnBreakPoints: ColumnBreakpoints;
  disableShareInputs?: boolean;
  isPublisher?: boolean;
  isSubmitting?: boolean;
  parentPath: string;
}

export const RowGridPublisher: React.FC<RowGridPublisherProps> = ({
  onDelete,
  first,
  publisher,
  editable,
  hideOptionsColumn,
  hideSplitColumn,
  hideCollaborationColumn,
  columnBreakPoints: bp,
  disableShareInputs,
  isPublisher,
  isSubmitting,
  parentPath,
}) => {
  const classes = useStyles();
  const sharesReadOnly = !isPublisher;

  return (
    <React.Fragment>
      <RowCreatorListItem listClassName={classes.row}>
        <CellAvatar>{first && <SvgElbowLine />}</CellAvatar>
        <CellCreator dense={bp.creator}>
          {!first && (
            <SvgElbowLine height={80} className={classes.svgRelationship} />
          )}
          <CardPublisher
            // ipiVerified={publisher.ipi?.verified}
            ipiVerified={false}
            multiline
            wrapDisplayName
            publisher={{
              ipiNameNumber: publisher.ipi.ipiNameNumber,
              displayName: publisher.profile.displayName,
            }}
          />
        </CellCreator>
        <CellRole dense={bp.role}>
          <Typography color="textSecondary">
            {PublisherRoleTypeLabel.E}
          </Typography>

          {/* TODO Role change not available for publisher */}
          {/*<Field*/}
          {/*  className={classes.roleSelect}*/}
          {/*  options={PublisherRoleType}*/}
          {/*  optionToLabel={{*/}
          {/*    [PublisherRoleType.E]: bp.role*/}
          {/*      ? "Orig."*/}
          {/*      : PublisherRoleTypeLabel.E,*/}
          {/*    [PublisherRoleType.SE]: bp.role*/}
          {/*      ? "Sub."*/}
          {/*      : PublisherRoleTypeLabel.SE,*/}
          {/*  }}*/}
          {/*  name={`${parent}.role`}*/}
          {/*  type="text"*/}
          {/*  disableUnderline*/}
          {/*  component={FormikEnumSelect}*/}
          {/*  disabled*/}
          {/*/>*/}
        </CellRole>

        {sharesReadOnly ? (
          <React.Fragment>
            <CellPrShare dense={bp.prShare} className={classes.prShareReadOnly}>
              <Field name={`${parentPath}.prShare`}>
                {(props: FieldProps) => (
                  <FormikTypography
                    {...props}
                    formatValue={(value) => `${value || 0}%`}
                    color="textSecondary"
                  />
                )}
              </Field>
            </CellPrShare>
            <CellMrShare dense={bp.mrShare} className={classes.mrShareReadOnly}>
              <Field name={`${parentPath}.mrShare`}>
                {(props: FieldProps) => (
                  <FormikTypography
                    {...props}
                    formatValue={(value) => `${value || 0}%`}
                    color="textSecondary"
                  />
                )}
              </Field>
            </CellMrShare>
          </React.Fragment>
        ) : (
          <React.Fragment>
            <CellPrShare dense={bp.prShare}>
              <TooltipMap
                to={
                  isPublisher
                    ? "PublisherRolePublisherShareInputs"
                    : "CreatorRolePublisherShareInputs"
                }
              >
                <Field
                  name={`${parentPath}.prShare`}
                  component={FormikPercentageInput}
                  disabled={!editable || disableShareInputs}
                  dense={bp.prShare}
                />
              </TooltipMap>
            </CellPrShare>
            <CellMrShare dense={bp.mrShare}>
              <TooltipMap
                to={
                  isPublisher
                    ? "PublisherRolePublisherShareInputs"
                    : "CreatorRolePublisherShareInputs"
                }
              >
                <Field
                  name={`${parentPath}.mrShare`}
                  component={FormikPercentageInput}
                  disabled={!editable || disableShareInputs}
                  dense={bp.mrShare}
                />
              </TooltipMap>
            </CellMrShare>
          </React.Fragment>
        )}

        {!(hideSplitColumn || bp.split) && (
          <CellSplit>
            {/*<Field*/}
            {/*  name="" //TODO:: Update logic when split approvals implemented*/}
            {/*  component={FormikSplitStatusIndicator}*/}
            {/*/>*/}
            <StatusIndicator
              status={SUCCESS}
              mapStatusToTooltip={mapStatusToTooltip}
            />
          </CellSplit>
        )}
        {!(hideCollaborationColumn || bp.collaboration) && (
          <CellCollaboration>
            <StatusIndicator
              status={SUCCESS}
              mapStatusToTooltip={mapStatusToTooltip}
            />
          </CellCollaboration>
        )}

        {!hideOptionsColumn && editable && (
          <CellOptions>
            <Box display="flex" mr={-1}>
              <ActionMenu
                items={[
                  {
                    label: "Remove",
                    onClick: onDelete,
                    disabled: isSubmitting,
                  },
                ]}
              >
                <Icon
                  name="listItemOptions"
                  data-testid={`${publisher.ipi.ipiNameNumber}.options`}
                />
              </ActionMenu>
            </Box>
          </CellOptions>
        )}
      </RowCreatorListItem>
    </React.Fragment>
  );
};
