import React from "react";
import { Redirect } from "react-router-dom";
import { AppDispatchEffect } from "../../../App.reducer";
import { confirmEmailChangeEffect } from "../../pages/Profile/effects";
import { handleConfirmEmailChangePayload } from "../../../utils/DLR";
import { paths } from "../../../utils/common";
import { RouteComponentProps } from "react-router";

interface EmailVerificationResponseProps {
  dispatchEffect: AppDispatchEffect;
}

export const EmailVerificationResponse: React.FC<
  EmailVerificationResponseProps & RouteComponentProps<{ verifyState: string }>
> = ({
  dispatchEffect,
  match: {
    params: { verifyState },
  },
  location: { search },
}) => {
  if (verifyState === "confirm-email-change") {
    const params = handleConfirmEmailChangePayload(search);

    params &&
      dispatchEffect(
        confirmEmailChangeEffect(params.href, params.method, params.code)
      );
  }

  return <Redirect to={paths.PROFILE_PERSONAL} />;
};
