import React from "react";
import { Box, createStyles, makeStyles, Theme } from "@material-ui/core";
import {
  BootstrapAccount,
  BootstrapCompany,
  Ipi,
  Work,
  WorkCreator,
  WorkPublisher,
} from "../../../utils/models";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import { PageNoContent } from "../PageNoContent";
import { ListCreators } from "../../organisms/ListCreators";
import { ContainerTableList } from "../ContainerTableList";
import { FormikErrors } from "formik";
import { WorkFormValues } from "../../organisms/FormWorkRegistration";
import { FormikTouched } from "formik/dist/types";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    listContainer: {
      height: "100%",
      paddingTop: theme.spacing(7),
      marginTop: 0,
    },
    listRoot: {
      height: "100%",
    },
    creatorRequired: {
      "& *": {
        color: theme.palette.error.main,
      },
    },
  })
);

interface TabCreatorsProps {
  creators: WorkCreator[];
  publishers: WorkPublisher[];
  setFieldValue: (key: string, value: any) => void;
  isSubmitting: boolean;
  editable?: boolean;
  hideSplitColumn?: boolean;
  hideCollaborationColumn?: boolean;
  hideOptionsColumn?: boolean;
  dispatchEffect: AppDispatchEffect;
  isTemplate?: boolean;
  ipiList: Ipi[];
  breakpointOffset?: number;
  work?: Work;
  isPublisher?: boolean;
  isCreator?: boolean;
  company?: BootstrapCompany;
  account?: BootstrapAccount;
  dispatch: AppDispatch;
  ipiSearchPublishersApi?: string;
  errors?: FormikErrors<WorkFormValues>;
  touched?: FormikTouched<WorkFormValues>;
}

export const TabCreators: React.FC<TabCreatorsProps> = ({
  work,
  ipiSearchPublishersApi,
  creators,
  publishers,
  setFieldValue,
  isSubmitting,
  editable = true,
  hideCollaborationColumn,
  hideSplitColumn,
  hideOptionsColumn,
  dispatchEffect,
  isTemplate,
  ipiList,
  breakpointOffset,
  isPublisher,
  isCreator,
  company,
  account,
  dispatch,
  errors,
  touched,
}) => {
  const classes = useStyles();
  const creatorRequired = errors?.creators && touched?.creators;

  return (
    <ContainerTableList
      containerClass={classes.listContainer}
      rootClass={classes.listRoot}
      disableMaxWidth
    >
      {creators.length === 0 ? (
        <PageNoContent
          classes={{
            ...(creatorRequired && { root: classes.creatorRequired }),
          }}
          primaryText="No Creators"
          secondaryText={editable ? "Click the + button to add a creator" : ""}
          iconSrc={`${process.env.PUBLIC_URL}/static/media/svg/person.svg`}
        />
      ) : (
        <Box display="inline-flex" flexGrow={1} width={1} height={1}>
          <ListCreators
            ipiSearchPublishersApi={ipiSearchPublishersApi}
            creators={creators}
            publishers={publishers}
            setFieldValue={setFieldValue}
            hideSplitColumn={hideSplitColumn}
            hideCollaborationColumn={hideCollaborationColumn}
            hideOptionsColumn={hideOptionsColumn}
            isSubmitting={isSubmitting}
            editable={editable}
            dispatchEffect={dispatchEffect}
            work={work}
            isTemplate={isTemplate}
            ipiList={ipiList}
            breakpointOffset={breakpointOffset}
            isPublisher={isPublisher}
            isCreator={isCreator}
            company={company}
            account={account}
            dispatch={dispatch}
          />
        </Box>
      )}
    </ContainerTableList>
  );
};
