import React from "react";
import {
  Avatar,
  createStyles,
  Grid,
  makeStyles,
  Theme,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import { WorkCreator } from "../../../utils/models";
import { formatPartyRole } from "./utils";
import { CellPrShareSummary } from "../../atoms/CellPrShareSummary";
import { CellCreatorSummary } from "../../atoms/CellCreatorSummary";
import { CardCreator } from "../../atoms/CardCreator";
import { RowCreatorListItem } from "../RowCreatorListItem";
import { CellRoleSummary } from "../../atoms/CellRoleSummary";
import { CellMrShareSummary } from "../../atoms/CellMrShareSummary";
import { CellAvatarSummary } from "../../atoms/CellAvatarSummary";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    avatar: {
      marginTop: theme.spacing(-0.5),
    },
    roleText: {
      display: "flex",
      alignItems: "center",
      color: theme.palette.text.secondary,
    },
    listItem: {
      paddingLeft: theme.spacing(2),
    },
    prShareText: {
      width: theme.spacing(7.5),
    },
  })
);

interface RowGridCreatorSummaryProps {
  creator: WorkCreator;
}

export const RowGridCreatorSummary: React.FC<RowGridCreatorSummaryProps> = ({
  creator: {
    role,
    prShare,
    mrShare,
    profile: { displayName, initials },
    ipi: { ipiNameNumber, verified },
  },
}) => {
  const classes = useStyles();
  const theme = useTheme();
  const isSmallWindow = useMediaQuery(theme.breakpoints.down("sm"));
  //TODO:: attributes currently not received from endpoints
  const isni = "",
    ipn = "",
    company = "";
  return (
    <RowCreatorListItem listClassName={classes.listItem}>
      <Grid container>
        <CellAvatarSummary>
          <Avatar className={classes.avatar}>{initials}</Avatar>
        </CellAvatarSummary>
        <CellCreatorSummary>
          <CardCreator
            wrapDisplayName
            multiline
            creator={{
              displayName: displayName,
              ipi: ipiNameNumber,
              ipiVerified: verified,
              isni,
              ipn,
              company,
            }}
          />
        </CellCreatorSummary>
        <CellRoleSummary>
          <Typography variant="subtitle1">
            {formatPartyRole(isSmallWindow)(role)}
          </Typography>
        </CellRoleSummary>
        <CellPrShareSummary alignCenter>
          <Typography
            variant="subtitle1"
            className={classes.prShareText}
            align="right"
          >
            {prShare || 0}%
          </Typography>
        </CellPrShareSummary>
        <CellMrShareSummary alignCenter>
          <Typography align="right">{mrShare || 0}%</Typography>
        </CellMrShareSummary>
        <Grid item xs={1} />
      </Grid>
    </RowCreatorListItem>
  );
};
