import React from "react";
import { Redirect } from "react-router-dom";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import {
  handleIpiVerifySuccessPayload,
  handleIpiVerifyV1Payload,
} from "../../../utils/DLR";
import { addAlertAction } from "../../pages/Alerts/actions";
import { AlertType } from "../../pages/Alerts/actions.types";
import {
  fetchIpiListEffect,
  patchIpiEffect,
  postVerifyIpiV1Effect,
} from "../../pages/Ipi/effects";
import { paths } from "../../../utils/common";
import { Method } from "axios";
import { FetchIpiResponse } from "../../../api.types";
import { RouteComponentProps } from "react-router";

const dispatchSuccess = (dispatch: AppDispatch) =>
  dispatch(
    addAlertAction(
      AlertType.SUCCESS,
      "You have successfully verified your IPI number."
    )
  );

const dispatchError = (dispatch: AppDispatch, message = "") =>
  dispatch(
    addAlertAction(
      AlertType.ERROR,
      `The verification of your IPI number failed.${
        message ? ` ${message}` : ""
      }`
    )
  );

const dispatchCancel = (dispatch: AppDispatch) =>
  dispatch(
    addAlertAction(
      AlertType.SUCCESS,
      "You successfully canceled the verification of IPI."
    )
  );

interface IpiVerificationResponseProps {
  dispatch: AppDispatch;
  dispatchEffect: AppDispatchEffect;
}

export const IpiVerificationResponse: React.FC<
  IpiVerificationResponseProps & RouteComponentProps<{ verifyState: string }>
> = ({
  dispatchEffect,
  dispatch,
  location: { search },
  match: {
    params: { verifyState },
  },
}) => {
  const verifyIpiV1 = async (payload: {
    href: string;
    method: Method;
    token: string;
  }) => {
    const { href, method, token } = payload;
    try {
      await dispatchEffect(postVerifyIpiV1Effect(href, method, token));
      dispatchSuccess(dispatch);
    } catch (e) {
      dispatchError(dispatch, e.message);
    }
  };

  const handleV2Success = async () => {
    dispatchSuccess(dispatch);

    try {
      const data: FetchIpiResponse = await dispatchEffect(fetchIpiListEffect());
      const ipiList = data.response.ipi.items;
      if (ipiList.length === 1 && !ipiList.find((el) => el.default)) {
        dispatchEffect(patchIpiEffect({ ...ipiList[0], default: true }));
      }
    } catch (e) {}
  };

  if (verifyState === "success") {
    const payload = handleIpiVerifySuccessPayload(search);
    payload?.verifiedIpis?.length ? handleV2Success() : dispatchError(dispatch);
  }

  if (verifyState === "error") dispatchError(dispatch);
  if (verifyState === "cancel") dispatchCancel(dispatch);

  if (verifyState === "verify") {
    const payload = handleIpiVerifyV1Payload(search);

    if (payload) {
      verifyIpiV1(payload);
    }
  }

  return <Redirect to={paths.PROFILE_IPI} />;
};
