import { List } from "@material-ui/core";
import React from "react";
import { LinkList } from "../../atoms/LinkList";
import { Href } from "../../../utils/models";
import { AppState } from "../../../App.reducer";

interface NavigationItem {
  title: string;
  pathname: string;
  access?: (state: AppState) => Href | undefined | boolean;
}

interface ListNavigationProps {
  navigationItems: NavigationItem[];
  pathname: string;
  secondaryContent?: (navigationItem: NavigationItem) => React.ReactNode;
}

export const ListNavigation = ({
  navigationItems,
  pathname,
  secondaryContent,
}: ListNavigationProps) => {
  return (
    <List>
      {navigationItems.map((navigationItem) => (
        <LinkList
          key={navigationItem.pathname}
          selected={pathname.includes(navigationItem.pathname)}
          to={navigationItem.pathname}
        >
          {navigationItem.title}
          {secondaryContent ? secondaryContent(navigationItem) : undefined}
        </LinkList>
      ))}
    </List>
  );
};
