import React, { useState } from "react";
import {
  _AgreementsLinks,
  AgreementListItem,
  DRAFT,
} from "../../../utils/models";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import { Href } from "../../../utils/models";
import { deleteAgreementEffect } from "../../pages/AgreementRegistration/effects";
import { addAlertAction } from "../../pages/Alerts/actions";
import { AlertType } from "../../pages/Alerts/actions.types";
import { DialogWarning } from "../../molecules/DialogWarning";
import { AgreementsListItem } from "../../molecules/AgreementsListItem";
import { ListHeaderAgreements } from "../../molecules/ListHeaderAgreements";
import { TableList } from "../../molecules/TableList";
import { InfiniteScroll, ListType } from "../InfiniteScroll/component";
import { Box } from "@material-ui/core";
import { useWindowSize } from "../../../hooks";

const ownerWarning =
  "The agreement and all of it's associated data will be permanently deleted.";
export const otherPartyWarning =
  "Once you leave an agreement you will lose access to the agreement and it's associated data.";

interface ListAgreementsProps {
  agreements: AgreementListItem[];
  dispatchEffect: AppDispatchEffect;
  dispatch: AppDispatch;
  canDuplicateAgreement?: boolean;
  agreementsLinks?: _AgreementsLinks;
}

export const ListAgreements: React.FC<ListAgreementsProps> = ({
  agreements,
  dispatchEffect,
  dispatch,
  canDuplicateAgreement,
  agreementsLinks,
}) => {
  const [showConfirmDialog, setShowConfirmDialog] = useState(false);
  const [dialogMessage, setDialogMessage] = useState("");
  const [deleteProps, setDeleteProps] = useState<
    { guid: string; link?: Href } | undefined
  >(undefined);
  const [windowWidth] = useWindowSize({ subtractSidebarWidth: true });

  const handleDelete = (guid: string) => {
    setDeleteProps({ guid, link: agreementsLinks?.delete });
    setDialogMessage(ownerWarning);
    setShowConfirmDialog(true);
  };

  const handleLeave = (guid: string, link?: Href) => {
    setDeleteProps({ guid, link });
    setDialogMessage(otherPartyWarning);
    setShowConfirmDialog(true);
  };

  const handleDeleteConfirm = () => {
    if (deleteProps?.link) {
      dispatchEffect(
        deleteAgreementEffect(deleteProps.guid, deleteProps.link)
      ).catch((e: Error) => {
        dispatch(
          addAlertAction(
            AlertType.ERROR,
            `Couldn't delete agreement. ${e.message}`
          )
        );
      });
    }
    handleClose();
  };

  const handleClose = () => {
    setShowConfirmDialog(false);
    setDeleteProps(undefined);
    setDialogMessage("");
  };

  return (
    <React.Fragment>
      {showConfirmDialog && (
        <DialogWarning
          width={400}
          onConfirm={handleDeleteConfirm}
          onCancel={handleClose}
          message={dialogMessage}
        />
      )}
      {agreements.length > 0 && (
        <TableList
          listId={ListType.AGREEMENTS}
          header={<ListHeaderAgreements />}
        >
          <InfiniteScroll
            dataLength={agreements.length}
            list={ListType.AGREEMENTS}
            dispatchEffect={dispatchEffect}
          >
            {agreements.map((el, index) => (
              <AgreementsListItem
                key={index}
                agreement={el}
                handleOnDelete={handleDelete}
                handleOnLeave={handleLeave}
                canDuplicateAgreement={canDuplicateAgreement}
                dispatchEffect={dispatchEffect}
                windowWidth={windowWidth}
                showDeleteOption={Boolean(
                  el.status === DRAFT && agreementsLinks?.delete
                )}
              />
            ))}
            <Box height={60} />
          </InfiniteScroll>
        </TableList>
      )}
    </React.Fragment>
  );
};
