import React from "react";
import {
  changeEmailConfirmEffect,
  changeEmailRequestEffect,
} from "../../pages/SignIn/effects";
import { Field, Form, Formik, FormikHelpers } from "formik";
import {
  Box,
  Button,
  createStyles,
  Grid,
  makeStyles,
  Theme,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import { TextField } from "formik-material-ui";
import { AppDispatchEffect } from "../../../App.reducer";
import * as yup from "yup";
import { ApiErrorCodeTypes } from "../../../types/enums";
import { FormikPasswordInput } from "../../atoms/FormikPasswordInput";
import { Href } from "../../../utils/models";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    row: {
      marginTop: theme.spacing(2),
    },
  })
);

const changeEmailValidation = (currentEmail: string, confirmCode: boolean) =>
  confirmCode
    ? yup.object().shape({ confirmCode: yup.string().required("Required") })
    : yup.object().shape({
        newEmail: yup
          .string()
          .trim()
          .email("Email is not valid")
          .notOneOf([currentEmail], "Please provide a new email")
          .required("Required"),
        currentEmail: yup.string().required("Required"),
        password: yup.string().required("Required"),
      });

interface FormValues {
  currentEmail: string;
  newEmail: string;
  password: string;
  confirmCode: string;
}

interface FormEmailChangeProps {
  dispatchEffect: AppDispatchEffect;
  email: string;
  changeEmailConfirmLink?: Href;
}

export const FormEmailChange: React.FC<FormEmailChangeProps> = ({
  dispatchEffect,
  email,
  changeEmailConfirmLink,
}) => {
  const classes = useStyles();
  const theme = useTheme();
  const breakpointXs = useMediaQuery(theme.breakpoints.down("xs"));

  const hasChangedValues = ({ newEmail, password }: FormValues) =>
    Boolean(newEmail) || Boolean(password);

  const handleEmailChangeRequest = async (
    values: FormValues,
    { setFieldError }: FormikHelpers<FormValues>
  ) => {
    try {
      await dispatchEffect(
        changeEmailRequestEffect(values.newEmail.trim(), values.password)
      );
    } catch (e) {
      if (e.code === ApiErrorCodeTypes.BAD_CREDENTIALS) {
        setFieldError("password", e.message);
      }
      if (e.status === 409) {
        setFieldError("newEmail", "Email is already in use");
      }
    }
  };

  const handleEmailConfirmRequest = async (
    values: FormValues,
    { setFieldError }: FormikHelpers<FormValues>
  ) => {
    try {
      await dispatchEffect(
        changeEmailConfirmEffect(values.newEmail, values.confirmCode)
      );
    } catch (e) {
      if (e === ApiErrorCodeTypes.ENTITY_NOT_FOUND) {
        setFieldError("confirmCode", "Incorrect confirmation code");
      }
    }
  };

  const handleOnSubmit = (
    values: FormValues,
    formikBag: FormikHelpers<FormValues>
  ) =>
    changeEmailConfirmLink
      ? handleEmailConfirmRequest(values, formikBag)
      : handleEmailChangeRequest(values, formikBag);

  return (
    <Formik
      enableReinitialize={true}
      onSubmit={handleOnSubmit}
      validationSchema={() =>
        changeEmailValidation(email, Boolean(changeEmailConfirmLink))
      }
      initialValues={{
        currentEmail: email,
        newEmail: "",
        password: "",
        confirmCode: "",
      }}
    >
      {({ isSubmitting, handleSubmit, resetForm, values }) => (
        <Form data-testid="email-change-form">
          <Box pt={4} pb={3}>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <Typography variant="h6">Email</Typography>
              </Grid>
              <Grid item sm={6} xs={12}>
                <Field
                  component={TextField}
                  label="Current Email"
                  name="currentEmail"
                  variant="filled"
                  fullWidth
                  disabled
                />
              </Grid>
              {changeEmailConfirmLink ? (
                <React.Fragment>
                  <Grid item sm={6} xs={12}>
                    <Field
                      inputProps={{
                        "data-testid": "new-email",
                      }}
                      component={TextField}
                      label="New Email"
                      name="newEmail"
                      autoComplete="new-email"
                      variant="filled"
                      fullWidth
                      disabled
                    />
                  </Grid>
                  {!breakpointXs && <Grid item xs={6} />}
                  <Grid item sm={6} xs={12}>
                    <Field
                      component={TextField}
                      label="Confirmation Code"
                      name="confirmCode"
                      variant="filled"
                      fullWidth
                    />
                  </Grid>
                </React.Fragment>
              ) : (
                <React.Fragment>
                  <Grid item sm={6} xs={12}>
                    <Field
                      inputProps={{
                        "data-testid": "new-email",
                      }}
                      component={TextField}
                      label="New Email"
                      name="newEmail"
                      autoComplete="new-email"
                      variant="filled"
                      fullWidth
                    />
                  </Grid>
                  {!breakpointXs && <Grid item xs={6} />}
                  <Grid item sm={6} xs={12}>
                    <Field
                      data-testid="email-change-password"
                      component={FormikPasswordInput}
                      label="Password"
                      autoComplete="email-change-password"
                      name="password"
                      variant="filled"
                      required
                    />
                  </Grid>
                </React.Fragment>
              )}
            </Grid>
            <Grid
              container
              justify="flex-end"
              direction="row"
              className={classes.row}
            >
              {hasChangedValues(values) && (
                <Button
                  color="primary"
                  onClick={() => resetForm()}
                  disabled={isSubmitting}
                >
                  Cancel
                </Button>
              )}
              <Button
                type="submit"
                color="primary"
                onClick={() => handleSubmit()}
                disabled={isSubmitting || !hasChangedValues(values)}
              >
                Update
              </Button>
            </Grid>
          </Box>
        </Form>
      )}
    </Formik>
  );
};
