import React from "react";
import { Grid, Typography } from "@material-ui/core";
import { AgreementMember } from "../../../utils/models";
import {
  IdentityType,
  IdentityTypeLabel,
  PartyMemberAccessTypeLabel,
  RelationType,
  RelationTypeLabel,
} from "../../../types/enums";
import { useAgreementSummaryStyles } from "../../organisms/DialogAgreementSummary";
import { RowCreatorListItem } from "../RowCreatorListItem";

interface RowGridAgreementMemberSummaryProps {
  member: AgreementMember;
  role: RelationType;
}

export const RowGridAgreementMemberSummary: React.FC<RowGridAgreementMemberSummaryProps> = ({
  role,
  member: {
    displayName,
    ipiNameNumber,
    permissions: { owner, read, write },
    type,
  },
}) => {
  const classes = useAgreementSummaryStyles();

  const cellPermissionsText = () => {
    if (type === IdentityType.CREATOR) {
      return PartyMemberAccessTypeLabel.NONE;
    }
    if (owner) {
      return PartyMemberAccessTypeLabel.OWNER;
    }
    if (write) {
      return PartyMemberAccessTypeLabel.WRITE;
    }
    if (read) {
      return PartyMemberAccessTypeLabel.READ;
    }
    return PartyMemberAccessTypeLabel.NONE;
  };

  return (
    <RowCreatorListItem listClassName={classes.listItem}>
      <Grid item className={classes.cellPartyName}>
        <Typography variant="body1" color="textPrimary">
          {displayName}
        </Typography>
        <Typography variant="body2" color="textSecondary">
          {IdentityTypeLabel[type]}
        </Typography>
      </Grid>
      <Grid item className={classes.cellRelation}>
        <Typography variant="subtitle1" color="textPrimary">
          {RelationTypeLabel[role]}
        </Typography>
      </Grid>
      <Grid item className={classes.cellIpi}>
        <Typography variant="subtitle1" color="textPrimary">
          {ipiNameNumber}
        </Typography>
      </Grid>
      <Grid item className={classes.cellPermissions} data-testid="permissions">
        <Typography variant="subtitle1" color="textPrimary">
          {cellPermissionsText()}
        </Typography>
      </Grid>
    </RowCreatorListItem>
  );
};
