import React from "react";
import { createStyles, makeStyles } from "@material-ui/core";
import clsx from "clsx";

const useStyles = makeStyles(() =>
  createStyles({
    logo: {
      width: "100%",
      maxWidth: 100,
      maxHeight: 56,
    },
  })
);

interface SocietyLogoProps {
  logoSrc?: string;
}

export const SocietyLogo: React.FC<SocietyLogoProps> = ({ logoSrc }) => {
  const classes = useStyles();

  const src =
    logoSrc ||
    `${process.env.PUBLIC_URL}/static/media/svg/society-logo-placeholder.svg`;

  return (
    <img className={clsx({ [classes.logo]: logoSrc })} src={src} alt="Logo" />
  );
};
