import { AppState } from "../../../App.reducer";

export const getTemplatesSelector = (state: AppState) =>
  state.templates.templateListItems;

export const getTemplateSelector = (state: AppState) =>
  state.templates.template;

export const getTemplateLoadingStateSelector = (state: AppState) =>
  state.templates.fetchTemplateLoadingState;

export const getTemplatesLoadingStateSelector = (state: AppState) =>
  state.templates.fetchTemplatesLoadingState;

export const getTemplatesListCreateLink = (state: AppState) =>
  state.templates._links?.create;

export const getTemplateValidationSchemaSelector = (state: AppState) =>
  state.templates.templateSchema;

export const getTemplateValidationSchemaLoadingStateSelector = (
  state: AppState
) => state.templates.templateSchemaLoadingState;

export const getTemplatesListLinks = (state: AppState) =>
  state.templates._links;

export const getTemplateCloneSelector = (state: AppState) =>
  state.templates.templateClone;

export const getTemplateCloneLoadingStateSelector = (state: AppState) =>
  state.templates.templateCloneLoadingState;
