export const fetchWorkTemplateValidationSchemaEffect =
  (): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchWorkTemplateSchemaAction());
    const link = getBootstrapLinksSelector(getState())?.workTemplates;
    if (!link) {
      return;
    }
    try {
      const response = await axios.request<WorkSchemaResponse>({
        url: link.href.replace(/{.*.}/, ""),
        method: "POST",
        headers: { Accept: "application/json+schema" },
        withCredentials: true,
      });
      dispatch(
        fetchWorkTemplateSchemaSuccessAction(response.data.response.schema)
      );
    } catch (e) {
      dispatch(fetchWorkTemplateSchemaFailureAction());
    }
  };

export const fetchTemplatesEffect =
  ({
    sortParams = initialSortParams,
    query,
    next,
  }: {
    sortParams?: {
      sortBy: WorkSortVariant;
      sortOrder: SortOrder;
    };
    query?: string;
    next?: boolean;
  }): ThunkAction<
    Promise<void | WorkTemplatesListResponse>,
    AppState,
    void,
    Action<string>
  > =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchTemplatesAction(next));
    const state = getState();
    const linkTemplates = getTemplatesListFetchLinkSelector(state);
    const linkNext = getTemplatesListLinks(state)?.next?.href;

    if (!linkTemplates) {
      return;
    }

    const url =
      next && linkNext
        ? linkNext
        : buildWorkTemplateQueryUrl({
            url: linkTemplates?.href || "",
            sortParams,
            query,
          });

    try {
      // const response = await axios.request<WorkTemplatesListResponse>({
      //   url,
      //   method: linkTemplates?.method,
      //   withCredentials: true,
      // });
      const { items, _links } =
        workTemplatesListResponseMock.response.workTemplate;
      dispatch(
        fetchTemplatesSuccessAction({
          templates: items,
          _links,
          next,
        })
      );
      return workTemplatesListResponseMock;
    } catch (e) {
      const error = handleError(e.response, "templates");
      dispatch(fetchTemplatesFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.workTemplate.GET_LIST} ${error.message}`
        )
      );
    }
  };

export const fetchTemplateEffect =
  (
    guid: string
  ): ThunkAction<Promise<TemplateResponse>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchTemplateAction());
    try {
      const link = getTemplatesListLinks(getState())?.workTemplate;
      const response = await axios.request<TemplateResponse>({
        url: link?.href.replace("{guid}", guid),
        withCredentials: true,
      });
      const { item } = response.data.response.workTemplate;
      const formattedData: Template = {
        ...item,
        creators: convertWorkCreatorSharesToPercentage(item.creators || []),
      };
      dispatch(fetchTemplateSuccessAction(formattedData));
      return response.data;
    } catch (e) {
      const error = handleError(e.response, "template");
      dispatch(fetchTemplateFailureAction());
      throw error;
    }
  };

export const updateTemplateEffect =
  (
    templateGuid: string,
    formData: TemplateFormValues
  ): ThunkAction<
    Promise<{
      template?: Template;
    } | void>,
    AppState,
    void,
    Action<string>
  > =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(updateTemplateAction());

    try {
      const state = getState();
      const { template } = state.templates;
      if (!template) {
        return;
      }

      //TODO:: Check this when BE implemented template update
      // const changedCreatorIpi = creatorIpiHasChanged(template, formData);
      // if (changedCreatorIpi) {
      //   work = await dispatch(
      //       changeCreatorIpiEffect(
      //           changedCreatorIpi.oldReference,
      //           changedCreatorIpi.newIpi
      //       )
      //   )

      const changedAttributes = extractDirtyWorkTemplateAttrs(
        template,
        formData,
        true
      );
      if (Object.keys(changedAttributes).length) {
        await dispatch(patchTemplateEffect(template.guid, changedAttributes));
      }

      dispatch(updateTemplateSuccessAction());
      return {
        template: getState().templates.template,
      };
    } catch (e) {
      dispatch(updateTemplateFailureAction());
      if (e === WorkRegistrationErrorType.SAVE_ERROR_IPI) {
        dispatch(
          addAlertAction(AlertType.ERROR, toasts.error.workTemplate.MISSING_IPI)
        );
      } else {
        dispatch(addAlertAction(AlertType.ERROR, e.message));
      }

      throw e;
    }
  };

export const postTemplateEffect =
  (data: {
    title: string;
  }): ThunkAction<Promise<Template | void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(postTemplateAction());
    const state = getState();
    const link = getTemplatesListLinks(state)?.create;
    try {
      const response = await axios.request<TemplateResponse>({
        url: link?.href,
        method: link?.method,
        data,
        withCredentials: true,
      });
      const { item } = response.data.response.workTemplate;
      dispatch(postTemplateSuccessAction(item));
      return item;
    } catch (e) {
      dispatch(postTemplateFailureAction());
      const error = handleError(e.response, "template");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.workTemplate.CREATE} ${error.message}`
        )
      );
      throw error;
    }
  };

export const patchTemplateEffect =
  (
    guid: string,
    data: WorkTemplatePatch
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(patchTemplateAction());
    const state = getState();
    const _link = getTemplateSelector(state)?._links?.update;

    const formattedData: WorkTemplatePatch = {
      ...data,
      creators: convertWorkCreatorSharesFromPercentage(data.creators),
    };

    try {
      const response = await axios.request<TemplatePatchResponse>({
        url: _link?.href.replace("{guid}", guid),
        method: _link?.method,
        data: formattedData,
        withCredentials: true,
      });
      const { item } = response.data.response.workTemplate;
      const formattedResponse: Template = {
        ...item,
        creators: convertWorkCreatorSharesToPercentage(item.creators || []),
      };
      dispatch(patchTemplateSuccessAction(formattedResponse));
    } catch (e) {
      const ipiList = getIpiListSelector(state);
      const ipiNotAdded =
        e.response.status === 404 &&
        doesUserHaveRoles(state, [AccountRoles.CREATOR]) &&
        (ipiList.length === 0 || !ipiList.find((el) => el.default));

      if (ipiNotAdded) {
        throw WorkRegistrationErrorType.SAVE_ERROR_IPI;
      } else {
        throw handleError(e.response, "template");
      }
    }
  };

export const deleteTemplateEffect =
  (guid: string): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(deleteTemplateAction());
    const link = getTemplatesListLinks(getState())?.delete;
    try {
      await axios.request<TemplatePatchResponse>({
        url: link?.href.replace("{guid}", guid),
        method: link?.method,
        withCredentials: true,
      });
      dispatch(deleteTemplateSuccessAction(guid));
    } catch (e) {
      dispatch(deleteTemplateFailureAction());
      const error = handleError(e.response, "template");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.workTemplate.DELETE} ${error.message}`
        )
      );
      throw e;
    }
  };

export const createFromEffect =
  (guid: string): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(workCloneAction());
    const link = getTemplatesListLinks(getState())?.workTemplate;

    try {
      const { data } = await axios.request<TemplateResponse>({
        url: link?.href.replace("{guid}", guid),
        withCredentials: true,
      });
      const workToCreate: Work = {
        ...data.response.workTemplate.item,
        title: "",
        guid: "",
        altTitle: "",
        titleLanguageCode: "",
        altTitleLanguageCode: "",
        duration: undefined,
        firstReleaseDate: undefined,
        status: DRAFT,
        version: "",
        owner: "",
        creators: convertWorkCreatorSharesToPercentage(
          data.response.workTemplate.item.creators || []
        ),
      };

      dispatch(workCloneSuccessAction(workToCreate));
    } catch (e) {
      dispatch(workCloneFailureAction());
      const error = handleError(e.response, "template");
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.workTemplate.CREATE_WORK_FROM_TEMPLATE} ${error.message}`
        )
      );
      throw e;
    }
  };

