import { ThunkAction, ThunkDispatch } from "redux-thunk";
import Promise from "promise-polyfill";
import { AppState } from "../../../App.reducer";
import { Action } from "redux";
import {
  fetchNotificationsAction,
  fetchNotificationsFailureAction,
  fetchNotificationsSuccessAction,
  fetchUnseenNotificationsCountAction,
  fetchUnseenNotificationsCountFailureAction,
  fetchUnseenNotificationsCountSuccessAction,
  patchNotificationAction,
  patchNotificationFailureAction,
  patchNotificationSuccessAction,
} from "./actions";
import axios from "axios";
import {
  FetchNotificationsResponse,
  FetchUnseenNotificationsCountResponse,
} from "../../../api.types";
import { handleError } from "../../../utils/errorHandler";
import { addAlertAction } from "../Alerts/actions";
import { AlertType } from "../Alerts/actions.types";
import { toasts } from "../../../utils/toasts";
import { getNotificationsUnseenCountLinkSelector } from "../SignIn/selectors";
import { fetchNotificationsResponseMock } from "../../../mocks";

export const fetchNotificationsEffect =
  (
    next?: boolean
  ): ThunkAction<
    Promise<void | FetchNotificationsResponse>,
    AppState,
    void,
    Action<string>
  > =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchNotificationsAction());
    const link = getState().signIn._links?.notifications;
    const linkNext = getState().notifications._links?.next?.href;
    try {
      // const response = await axios.request<FetchNotificationsResponse>({
      //   url: next && linkNext ? linkNext : link?.href,
      //   method: link?.method || "GET",
      //   withCredentials: true,
      // });
      const { items, _links, meta } =
        fetchNotificationsResponseMock.response.notificationHistoryLog;
      dispatch(
        fetchNotificationsSuccessAction({
          notifications: items,
          _links,
          meta,
          next: next && Boolean(linkNext),
        })
      );
      return fetchNotificationsResponseMock;
    } catch (e) {
      const error = handleError(e.response, "notifications");
      dispatch(fetchNotificationsFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.notifications.GET} ${error.message}`
        )
      );
    }
  };

export const patchNotificationEffect =
  (guid: string): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(patchNotificationAction());
    const link = getState().notifications._links?.update;
    try {
      await axios.request<any>({
        url: link?.href.replace("{guid}", guid) || "",
        method: link?.method || "PATCH",
        withCredentials: true,
        data: { isUnread: false },
      });
      dispatch(patchNotificationSuccessAction(guid));
    } catch (e) {
      const error = handleError(e.response, "notification");
      dispatch(patchNotificationFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.notifications.READ_NOTIFICATION} ${error.message}`
        )
      );
    }
  };

export const fetchUnseenNotificationsCountEffect =
  (): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchUnseenNotificationsCountAction());
    const link = getNotificationsUnseenCountLinkSelector(getState());
    try {
      const response =
        await axios.request<FetchUnseenNotificationsCountResponse>({
          url: link?.href || "",
          method: link?.method || "GET",
          withCredentials: true,
        });
      const { count } = response.data.response.unseenNotificationCount.item;
      dispatch(fetchUnseenNotificationsCountSuccessAction(count));
    } catch (e) {
      dispatch(fetchUnseenNotificationsCountFailureAction());
    }
  };
