import React from "react";
import { AvatarTitle } from "../../atoms/AvatarTitle";
import { IpiSearchCreatorOption } from "../../../utils/models";
import {
  Box,
  createStyles,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { HighlightText } from "../../atoms/HighlightText";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    avatar: {
      marginRight: theme.spacing(3),
    },
  })
);

interface IpiSearchOptionProps {
  searchPhrase: string;
}

export const IpiSearchOption: React.FC<
  IpiSearchOptionProps & IpiSearchCreatorOption
> = ({ children, searchPhrase, ...option }) => {
  const classes = useStyles();
  return (
    <AvatarTitle
      avatarClassName={classes.avatar}
      avatar={option.profile.initials || ""}
      alignItems="flex-start"
    >
      <Box pt={0.5}>
        <HighlightText
          variant="subtitle1"
          color="textPrimary"
          text={option.profile.displayName}
          highlightText={searchPhrase}
        />
        <Typography variant="body2" color="textSecondary" noWrap>
          IPI {option.ipiNameNumber}
        </Typography>
      </Box>
    </AvatarTitle>
  );
};
