import AddIcon from "@material-ui/icons/Add";
import { Button } from "@material-ui/core";
import React from "react";
import { FieldProps } from "formik";
import { AgreementFormValues } from "../../organisms/FormAgreement";

interface FormikAddAgreementNumberProps {
  disabled?: boolean;
}

export const FormikAddAgreementNumber: React.FC<
  FormikAddAgreementNumberProps & FieldProps<any, AgreementFormValues>
> = ({ form: { setFieldValue }, field: { name }, disabled }) => {
  const handleAdd = () => {
    setFieldValue(name, "");
  };

  return (
    <Button
      disabled={disabled}
      startIcon={<AddIcon />}
      variant="text"
      color="primary"
      onClick={handleAdd}
    >
      Add Agreement Number
    </Button>
  );
};
