import {
  CREATE_THEME,
  CREATE_THEME_FAILURE,
  CREATE_THEME_SUCCESS,
  DELETE_THEME,
  DELETE_THEME_FAILURE,
  DELETE_THEME_SUCCESS,
  FETCH_PUBLISHED_THEME,
  FETCH_PUBLISHED_THEME_FAILURE,
  FETCH_PUBLISHED_THEME_SUCCESS,
  FETCH_THEMES,
  FETCH_THEMES_FAILURE,
  FETCH_THEMES_SUCCESS,
  SET_THEME,
  ThemeActionTypes,
  TOGGLE_DARK_MODE,
  TOGGLE_THEME_DRAWER,
  UPDATE_THEME,
  UPDATE_THEME_FAILURE,
  UPDATE_THEME_SUCCESS,
  UPLOAD_CUSTOM_LOGO,
  UPLOAD_CUSTOM_LOGO_FAILURE,
  UPLOAD_CUSTOM_LOGO_SUCCESS,
} from "./actions.types";
import { SettingsTheme, Theme } from "../../../utils/models";

export const setThemeAction = (theme: Theme): ThemeActionTypes => ({
  type: SET_THEME,
  theme,
});

export const fetchPublishedThemeAction = (): ThemeActionTypes => ({
  type: FETCH_PUBLISHED_THEME,
});

export const fetchPublishedThemeActionSuccess = (): ThemeActionTypes => ({
  type: FETCH_PUBLISHED_THEME_SUCCESS,
});

export const fetchPublishedThemeActionFailure = (): ThemeActionTypes => ({
  type: FETCH_PUBLISHED_THEME_FAILURE,
});

export const toggleDarkModeAction = (): ThemeActionTypes => ({
  type: TOGGLE_DARK_MODE,
});

export const uploadCustomLogoAction = (): ThemeActionTypes => ({
  type: UPLOAD_CUSTOM_LOGO,
});

export const uploadCustomLogoActionActionSuccess = (
  logo: string
): ThemeActionTypes => ({
  type: UPLOAD_CUSTOM_LOGO_SUCCESS,
  logo,
});

export const uploadCustomLogoActionActionFailure = (): ThemeActionTypes => ({
  type: UPLOAD_CUSTOM_LOGO_FAILURE,
});

export const fetchThemesAction = (): ThemeActionTypes => ({
  type: FETCH_THEMES,
});

export const fetchThemesActionSuccess = (
  themes: SettingsTheme[]
): ThemeActionTypes => ({
  type: FETCH_THEMES_SUCCESS,
  themes,
});

export const fetchThemesActionFailure = (): ThemeActionTypes => ({
  type: FETCH_THEMES_FAILURE,
});

export const updateThemeAction = (): ThemeActionTypes => ({
  type: UPDATE_THEME,
});

export const updateThemeActionSuccess = (
  theme: SettingsTheme,
  defaultThemeUpdated: boolean
): ThemeActionTypes => ({
  type: UPDATE_THEME_SUCCESS,
  theme,
  defaultThemeUpdated,
});

export const updateThemeActionFailure = (): ThemeActionTypes => ({
  type: UPDATE_THEME_FAILURE,
});

export const createThemeAction = (): ThemeActionTypes => ({
  type: CREATE_THEME,
});

export const createThemeActionSuccess = (
  theme: SettingsTheme
): ThemeActionTypes => ({
  type: CREATE_THEME_SUCCESS,
  theme,
});

export const createThemeActionFailure = (): ThemeActionTypes => ({
  type: CREATE_THEME_FAILURE,
});

export const deleteThemeAction = (): ThemeActionTypes => ({
  type: DELETE_THEME,
});

export const deleteThemeSuccessAction = (
  themeId: string,
  isDefaultBranch: boolean
): ThemeActionTypes => ({
  type: DELETE_THEME_SUCCESS,
  themeId,
  isDefaultBranch,
});

export const deleteThemeActionFailure = (): ThemeActionTypes => ({
  type: DELETE_THEME_FAILURE,
});

export const toggleThemeDrawerAction = (): ThemeActionTypes => ({
  type: TOGGLE_THEME_DRAWER,
});
