import React from "react";
import { AppDispatch } from "../../../App.reducer";
import { IconButton } from "@material-ui/core";
import { sidebarToggleAction } from "../../pages/Sidebar/actions";
import {Menu24} from '@carbon/icons-react';

interface HamburgerMenuProps {
  dispatch: AppDispatch;
}

export const HamburgerMenu: React.FC<HamburgerMenuProps> = ({ dispatch }) => {
  return (
    <IconButton
      onClick={() => dispatch(sidebarToggleAction())}
      color="inherit"
      aria-label="menu"
      data-testid="hamburger"
    >
      <Menu24 />
    </IconButton>
  );
};
