import React, { useEffect, useState } from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { fetchAgreementEffect, fetchAgreementsEffect } from "./effects";
import {
  getAgreementSelector,
  getAgreementsLinksSelector,
  getAgreementsLoadingStateSelector,
  getAgreementsSelector,
} from "./selectors";
import {
  getAccountSelector,
  getCompanySelector,
  getUserIsSocAdminSelector,
} from "../SignIn/selectors";
import { RouteComponentProps } from "react-router";
import { getSettingsSelector } from "../Settings/selectors";
import { PENDING, REGISTERED, SUBMITTED } from "../../../utils/models";
import { FormAgreement } from "../../organisms/FormAgreement";
import { PageAgreementLoading } from "../../molecules/PageAgreementLoading";
import { paths } from "../../../utils/common";
import { LoadingState } from "../../../types/enums";

export const AgreementRegistration: React.FC<
  RouteComponentProps<{ id: string }>
> = ({
  match: {
    params: { id },
  },
  location: { pathname },
  history: { push },
}) => {
  const dispatch = useAppDispatch();
  const dispatchEffect = useAppDispatchEffect();
  const state = useAppSelector((state) => state);
  const account = getAccountSelector(state);
  const company = getCompanySelector(state);
  const agreement = getAgreementSelector(state);
  const agreementsList = getAgreementsSelector(state);
  const agreementsListLinks = getAgreementsLinksSelector(state);
  const agreementRevisionEnabled = getSettingsSelector(state).revision
    .agreement;
  const isSocAdmin = getUserIsSocAdminSelector(state);
  const agreementsLoadingState = getAgreementsLoadingStateSelector(state);
  const [isFormDataLoading, setIsFormDataLoading] = useState(Boolean(id));

  const getBreadCrumb = () => {
    const cachedAgreement = agreementsList.find((el) => el.guid === id);
    const status = cachedAgreement?.status || agreement?.status || "";
    if ([SUBMITTED, REGISTERED, PENDING].includes(status)) {
      return "View";
    }
    if (id) {
      return "Edit";
    }
    return "New";
  };

  const breadCrumb = getBreadCrumb();

  useEffect(() => {
    const fetchAgreement = async (id: string) => {
      try {
        if (!agreementsListLinks?.agreement) {
          await dispatchEffect(fetchAgreementsEffect());
        }
        await dispatchEffect(fetchAgreementEffect(id));
      } catch (e) {
        push(paths.AGREEMENTS_LIST);
      }
      setIsFormDataLoading(false);
    };
    if (id) {
      fetchAgreement(id);
    } else {
      setIsFormDataLoading(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const fetchAgreements = async () => {
      if (
        agreementsLoadingState === LoadingState.PRISTINE &&
        !agreementsListLinks
      ) {
        try {
          await dispatchEffect(fetchAgreementsEffect());
        } catch (e) {
          if (pathname === paths.AGREEMENTS_REGISTER) {
            push(paths.AGREEMENTS_LIST);
          }
        }
      }
    };

    fetchAgreements();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [agreementsListLinks, dispatchEffect]);

  return isFormDataLoading || !agreementsListLinks ? (
    <PageAgreementLoading dispatch={dispatch} breadCrumb={breadCrumb} />
  ) : account && company ? (
    <FormAgreement
      dispatch={dispatch}
      dispatchEffect={dispatchEffect}
      breadCrumb={breadCrumb}
      agreement={id ? agreement : undefined}
      account={account}
      company={company}
      agreementRevisionEnabled={agreementRevisionEnabled}
      isSocAdmin={isSocAdmin}
      agreementsListLinks={agreementsListLinks}
    />
  ) : null;
};
