import React from "react";
import { createStyles, Grid, makeStyles, Theme } from "@material-ui/core";
import clsx from "clsx";

export const avatarCellWidth = 68;

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    cell: {
      position: "relative",
    },
    header: {
      paddingBottom: theme.spacing(1.5),
      borderBottom: `1px solid ${theme.palette.divider}`,
      height: theme.spacing(5),
    },
  })
);

interface CellAvatarSummaryProps {
  header?: boolean;
}

export const CellAvatarSummary: React.FC<CellAvatarSummaryProps> = ({
  children,
  header = false,
}) => {
  const classes = useStyles();

  return (
    <Grid
      item
      xs={1}
      className={clsx(classes.cell, { [classes.header]: header })}
    >
      {children}
    </Grid>
  );
};
