import React, { useEffect, useState } from "react";
import { format } from "date-fns";
import {
  Box,
  createStyles,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { IN_PROGRESS, Work, WorkRegistration } from "../../../utils/models";
import { AppDispatchEffect } from "../../../App.reducer";
import { RegistrationsPanel } from "../RegistrationsPanel/component";
import { DialogWorkSummary } from "../../organisms/DialogWorkSummary";
import { ContainerContent } from "../../atoms/ContainerContent";
import { DialogSubmissions } from "../DialogSubmissions";
import { CwrErrorCodeType, CwrErrorMessage } from "../../../types/enums";
import {
  downloadCwrEffect,
  fetchWorkRegistrationsEffect,
  fetchWorkVersionEffect,
} from "../../pages/Works/effects";
import { WorkFormValues } from "../../organisms/FormWorkRegistration";
import { workToFormData } from "../../organisms/FormWorkRegistration/utils";
import { DialogCwrValidationErrors } from "../../organisms/DialogCwrValidationErrors";
import Promise from "promise-polyfill";
import { delay } from "../../../utils/helpers";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dateText: {
      marginBottom: theme.spacing(1),
    },
  })
);

interface TabRegistrationsProps {
  workRegistrations: WorkRegistration[];
  dispatchEffect: AppDispatchEffect;
  isTabOpen?: boolean;
  openRegistrationDialog?: WorkRegistration;
}

export const getRegistrationErrorText = (errorCode: CwrErrorCodeType) =>
  CwrErrorMessage[errorCode as CwrErrorCodeType] || "";

const getRegistrationsSortedByDate = (workRegistrations: WorkRegistration[]) =>
  workRegistrations.reduce(
    (acc: { [key: string]: WorkRegistration[] }, cur) => {
      const dateString = format(new Date(cur.created.iso), "MMMM d, yyyy");
      acc[dateString] = acc[dateString] ? [...acc[dateString], cur] : [cur];
      return acc;
    },
    {}
  );

const hasPendingRegistrations = (registrations: WorkRegistration[]) =>
  registrations.find((el) => el.status === IN_PROGRESS);

export const TabRegistrations: React.FC<TabRegistrationsProps> = ({
  dispatchEffect,
  workRegistrations: workRegistrationsProp,
  isTabOpen,
  openRegistrationDialog,
}) => {
  const classes = useStyles();
  const [work, setWork] = useState<WorkFormValues>();
  const [openSummary, setOpenSummary] = useState<WorkRegistration>();
  const [workRegistrations, setWorkRegistrations] = useState(
    workRegistrationsProp
  );
  const [openRegistration, setOpenRegistration] = useState<WorkRegistration>();
  const [
    openValidationErrorsDialog,
    setOpenValidationErrorsDialog,
  ] = useState<WorkRegistration>();
  const registrationsSortedByDate = getRegistrationsSortedByDate(
    workRegistrations
  );
  const [isRefetching, setIsRefetching] = useState(false);
  const handleDialogOpen = (registration: WorkRegistration) => async () => {
    const { workGuid, workVersion } = registration;
    try {
      const response: Work = await dispatchEffect(
        fetchWorkVersionEffect(workGuid, workVersion)
      );
      setWork(workToFormData(response));
      setOpenSummary(registration);
    } catch (e) {}
  };

  const handleClose = () => {
    setOpenSummary(undefined);
    setWork(undefined);
  };

  const handleSocietySubmissionsDialogOpen = (
    workRegistration: WorkRegistration
  ) => () => {
    setOpenRegistration(workRegistration);
  };

  const handleValidationErrorsDialogOpen = (
    workRegistration: WorkRegistration
  ) => () => {
    setOpenValidationErrorsDialog(workRegistration);
  };

  const handleDownloadCwr = (url: string) => () => {
    dispatchEffect(downloadCwrEffect(url));
  };

  const getDownloadCwr = (registration?: WorkRegistration) => {
    const url = registration?._links.downloadCwr?.href;
    if (url) {
      return handleDownloadCwr(url);
    }
  };

  const fetchRegistrationsEffect: () => Promise<WorkRegistration[]> = () =>
    dispatchEffect(fetchWorkRegistrationsEffect());

  const refetchPendingRegistrations = async () => {
    setIsRefetching(true);
    await delay(5000);
    if (!isTabOpen) {
      return;
    }
    const response = await fetchRegistrationsEffect();
    setWorkRegistrations(response);
    if (hasPendingRegistrations(response)) {
      refetchPendingRegistrations();
    } else {
      setIsRefetching(false);
    }
  };

  const refetchRegistrations = async () => {
    try {
      const response = await fetchRegistrationsEffect();
      setWorkRegistrations(response);
      if (hasPendingRegistrations(response) && !isRefetching) {
        refetchPendingRegistrations();
      }
    } catch (e) {}
  };

  useEffect(() => {
    if (isTabOpen && !isRefetching) {
      refetchRegistrations();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isTabOpen]);

  useEffect(() => {
    setWorkRegistrations(workRegistrationsProp);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [workRegistrationsProp.length]);

  useEffect(() => {
    setOpenRegistration(openRegistrationDialog);
  }, [openRegistrationDialog]);

  return (
    <React.Fragment>
      <ContainerContent>
        <Box pt={5}>
          {Object.keys(registrationsSortedByDate).map((date) => (
            <Box key={date} pb={2}>
              <Typography
                variant="body2"
                color="textSecondary"
                className={classes.dateText}
              >
                {date}
              </Typography>
              {registrationsSortedByDate[date].map((registration, index) => (
                <React.Fragment key={registration.workVersion}>
                  <RegistrationsPanel
                    onViewRegistrationDialogOpen={handleDialogOpen(
                      registration
                    )}
                    onSubmissionsDialogOpen={handleSocietySubmissionsDialogOpen(
                      registration
                    )}
                    onErrorsDialogOpen={handleValidationErrorsDialogOpen(
                      registration
                    )}
                    registration={registration}
                    showBorder={index !== workRegistrations.length}
                    onDownloadCwr={getDownloadCwr(registration)}
                  />
                </React.Fragment>
              ))}
            </Box>
          ))}
        </Box>
      </ContainerContent>
      {work && (
        <DialogWorkSummary
          open={Boolean(openSummary)}
          handleClose={handleClose}
          isSubmitting={false}
          values={work}
          onDownloadCwr={getDownloadCwr(openSummary)}
        />
      )}
      <DialogSubmissions
        hideDialog={() => {
          setOpenRegistration(undefined);
        }}
        showDialog={Boolean(openRegistration)}
        workRegistration={openRegistration}
        dispatchEffect={dispatchEffect}
        onDownloadCwr={(url) => dispatchEffect(downloadCwrEffect(url))}
      />
      <DialogCwrValidationErrors
        dispatchEffect={dispatchEffect}
        showDialog={Boolean(openValidationErrorsDialog)}
        workRegistration={openValidationErrorsDialog}
        hideDialog={() => {
          setOpenValidationErrorsDialog(undefined);
        }}
      />
    </React.Fragment>
  );
};
