import { LocationDescriptor } from "history";
import queryString from "query-string";
import { workFilterStatusAll } from "../../organisms/WorksNavigationBar/utils";
import { initialSortParams } from "../../organisms/HeaderTableWorks";
import { LoadingState } from "../../../types/enums";
import { DRAFT } from "../../../utils/models";
import { AppState } from "../../../App.reducer";
import {
  getWorksListLoadingStateSelector,
  getWorksListSelector,
} from "../ListWorks/selectors";
import {
  getTemplatesLoadingStateSelector,
  getTemplatesSelector,
} from "../Templates/selectors";
import { paths } from "../../../utils/common";

export const setWorksListUrlParams = (
  search: string,
  push: (location: LocationDescriptor) => void
) => {
  const currentQueryParams = queryString.parse(search);
  const newQueryParams = {
    ...currentQueryParams,
    filters: workFilterStatusAll.join(","),
    sortBy: initialSortParams.sortBy,
    sortOrder: initialSortParams.sortOrder,
  };
  push({
    search: queryString.stringify(newQueryParams, { encode: false }),
  });
};

export const isSearchDisabled = (
  search: string,
  pathname: string,
  state: AppState
) => {
  if (search) {
    return false;
  }

  const worksListLoadingState = getWorksListLoadingStateSelector(state);
  const templatesListLoadingState = getTemplatesLoadingStateSelector(state);
  const works = getWorksListSelector(state);
  const templates = getTemplatesSelector(state);
  const isDraftsPage = pathname === paths.WORKS_DRAFT_LIST;
  const isMyWorksPage = pathname === paths.WORKS_LIST;
  const isTemplatesPage = pathname === paths.WORKS_TEMPLATE_LIST;

  if (isDraftsPage) {
    return (
      worksListLoadingState === LoadingState.FETCHED &&
      works.filter((el) => el.status === DRAFT).length === 0
    );
  }

  if (isMyWorksPage) {
    return (
      worksListLoadingState === LoadingState.FETCHED &&
      works.filter((el) => el.status !== DRAFT).length === 0
    );
  }
  if (isTemplatesPage) {
    return (
      templatesListLoadingState === LoadingState.FETCHED &&
      templates.length === 0
    );
  }
  return false;
};
