import React from "react";
import {
  createMuiTheme,
  createStyles,
  makeStyles,
  Theme as MaterialUiTheme,
  ThemeProvider,
} from "@material-ui/core";
import Avatar from "./avatar.png";
import { Theme } from "../../../utils/models";

const previewTheme = (theme: Theme) =>
  createMuiTheme({
    palette: {
      type: "light",
      primary: {
        main: theme.appBarColor,
      },
      secondary: {
        main: theme.appBarColor,
      },
      text: {
        primary: theme.textColor,
      },
    },
  });

const useStyles = makeStyles((materialUiTheme: MaterialUiTheme) =>
  createStyles({
    preview: {
      width: "100%",
      height: "100%",
      cursor: (clickable) => (clickable ? "pointer" : "default"),
      border: (clickable) =>
        clickable
          ? "initial"
          : `1px solid ${materialUiTheme.palette.grey[300]}`,
    },
    themeColor: {
      fill: materialUiTheme.palette.primary.main,
    },
    textContrastColor: {
      fill: materialUiTheme.palette.primary.contrastText,
    },
    backgroundColor: {
      fill: materialUiTheme.palette.background.paper,
    },
    textColor: {
      fill: materialUiTheme.palette.text.primary,
    },
  })
);

interface ThemePreviewProps {
  theme: Theme;
  onClick?: () => void;
}

