import {
  REGISTER_WORK,
  REGISTER_WORK_FAILURE,
  REGISTER_WORK_SUCCESS,
  WorkRegistrationActionTypes,
} from "./actions.types";

export const registerWorkAction = (): WorkRegistrationActionTypes => ({
  type: REGISTER_WORK,
});

export const registerWorkSuccessAction = (): WorkRegistrationActionTypes => ({
  type: REGISTER_WORK_SUCCESS,
});

export const registerWorkFailureAction = (): WorkRegistrationActionTypes => ({
  type: REGISTER_WORK_FAILURE,
});
