import React from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { Redirect, RouteComponentProps } from "react-router";
import { getSettingsSelector } from "./selectors";
import { paths } from "../../../utils/common";
import { SettingsTheme } from "../../organisms/SettingsTheme";
import { GeneralSettings } from "../../organisms/SettingsGeneral";
import { isDevelopment } from "../../../utils/helpers";
import { getThemeDrawerOpenSelector } from "../Theme/selectors";

export const settingsTabs = [
  { label: "Theme", link: paths.SETTINGS_THEME },
  ...(isDevelopment
    ? [{ label: "General", link: paths.SETTINGS_GENERAL }]
    : []),
];

export const Settings: React.FC<RouteComponentProps> = ({
  location: { pathname },
}) => {
  const state = useAppSelector((state) => state);
  const dispatchEffect = useAppDispatchEffect();
  const dispatch = useAppDispatch();
  const isGeneralSettingsPage = pathname === paths.SETTINGS_GENERAL;
  const isThemePage = pathname === paths.SETTINGS_THEME;
  const settings = getSettingsSelector(state);
  const drawerOpen = getThemeDrawerOpenSelector(state);

  if (isDevelopment && isGeneralSettingsPage) {
    return (
      <GeneralSettings
        dispatchEffect={dispatchEffect}
        dispatch={dispatch}
        settings={settings}
      />
    );
  }

  if (isThemePage) {
    return (
      <SettingsTheme
        dispatchEffect={dispatchEffect}
        dispatch={dispatch}
        drawerOpen={drawerOpen}
      />
    );
  }

  return <Redirect to={paths.INDEX} />;
};
