import React, { useRef, useState } from "react";
import { Field, FieldProps, useField } from "formik";
import { FormikAutocompleteSelect } from "../../molecules/FormikAutocompleteSelect";
import { IpiSearchCreatorOption } from "../../../utils/models";
import { FetchIpiCreatorsResponse } from "../../../api.types";
import { IpiSearchOption } from "../../molecules/IpiSearchOption";
import { createStyles, makeStyles, Theme, Typography } from "@material-ui/core";

interface FormikIpiNumberAutocompleteProps {
  apiSource: string;
  disabled: boolean;
}

interface StyleProps {
  ipiNameNumber?: number;
}

const useStyles = makeStyles<Theme, StyleProps>((theme) =>
  createStyles({
    displayNameAdornment: {
      position: "absolute",
      bottom: 2,
      left: ({ ipiNameNumber }) => {
        if (!ipiNameNumber) {
          return 0;
        }
        const length = `${ipiNameNumber}`.length;
        return `${length + 3}ch`;
      },
    },
  })
);

export const FormikIpiNumberAutocomplete: React.FC<FormikIpiNumberAutocompleteProps> = ({
  apiSource,
  disabled,
}) => {
  const inputRef = useRef<HTMLInputElement>();
  const [option] = useField<IpiSearchCreatorOption | undefined>({
    name: "ipiNameNumber",
  });
  const classes = useStyles({
    ipiNameNumber: option.value?.ipiNameNumber,
  });
  const [searchPhrase, setSearchPhrase] = useState("");

  return (
    <Field name="ipiNameNumber">
      {(props: FieldProps) => (
        <FormikAutocompleteSelect<
          IpiSearchCreatorOption,
          FetchIpiCreatorsResponse
        >
          {...props}
          label="Search IPI Name or Full IPI Number"
          apiSource={`${apiSource.replace(/{.*.}/, "")}&searchPhrase={query}`}
          getOptionLabel={(option) => `${option.ipiNameNumber}`}
          setApiResponseToOptions={({ response }) =>
            response.ipiSearchResult.items
          }
          filterOptions={(options) => options}
          renderOption={(option) => (
            <IpiSearchOption searchPhrase={searchPhrase} {...option} />
          )}
          disabled={disabled}
          textFieldProps={{
            inputRef,
            onChange: (event) => setSearchPhrase(event.target.value),
            InputProps:
              inputRef.current?.value === `${option.value?.ipiNameNumber}`
                ? {
                    startAdornment: (
                      <Typography
                        color="textSecondary"
                        className={classes.displayNameAdornment}
                      >
                        - {option.value?.profile.displayName}
                      </Typography>
                    ),
                  }
                : undefined,
          }}
        />
      )}
    </Field>
  );
};
