import {
  hasBeenRecorderCodeNames,
  MusicalCategoryCodeNames,
} from "../../../types/enums";

export const formatDuration = (duration: number | undefined) => {
  const value = duration || 0;
  const minutes = Math.floor(value / 60)
    .toString()
    .padStart(2, "0");
  const seconds = (value % 60).toString().padStart(2, "0");
  return `${minutes}:${seconds}`;
};

export const getHasBeenRecordedLabel = (hasBeenRecorded?: boolean) => {
  switch (hasBeenRecorded) {
    case undefined:
      return hasBeenRecorderCodeNames.U;
    case false:
      return hasBeenRecorderCodeNames.N;
    case true:
      return hasBeenRecorderCodeNames.Y;
    default:
      return hasBeenRecorderCodeNames.U;
  }
};

export const getCategoryLabel = (category: string | undefined) => {
  switch (category) {
    case "UNC":
      return MusicalCategoryCodeNames.UNC;
    case "POP":
      return MusicalCategoryCodeNames.POP;
    case "JAZ":
      return MusicalCategoryCodeNames.JAZ;
    case "SER":
      return MusicalCategoryCodeNames.SER;
    default:
      return MusicalCategoryCodeNames.UNC;
  }
};

export const getYesOrNoLabel = (value: boolean) => {
  if (value) {
    return "Yes";
  }
  return "No";
};
