import React, { useState } from "react";
import {
  Box,
  Chip,
  Grid,
  IconButton,
  ListItem,
  Typography,
} from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { format } from "date-fns";
import { Employee, SetGroupAction } from "../../../utils/models";
import { AppDispatchEffect } from "../../../App.reducer";
import { Group, Role } from "../../../types/enums";
import { patchEmployeeEffect } from "../../pages/Company/effects";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { DialogWarning } from "../../molecules/DialogWarning";
import { AvatarTitle } from "../../atoms/AvatarTitle";
import { ActionMenu } from "../ActionMenu";
import { HeaderListEmployees } from "../HeaderListEmployees";
import { TableList } from "../../molecules/TableList";
import { MenuSelect } from "../../molecules/MenuSelect";
import { Icon } from "../../atoms/Icon";

export const useListEmployeesStyles = makeStyles((theme: Theme) =>
  createStyles({
    listItemRow: {
      flexDirection: "column",
      "&:hover": {
        cursor: "default",
      },
    },
    avatarColumn: {
      minWidth: 80,
      alignSelf: "center",
    },
    nameColumn: {
      position: "relative",
      minWidth: 140,
      width: "100%",
    },
    roleColumn: {
      width: "100%",
      minWidth: 170,
      maxWidth: 231,
    },
    dateColumn: {
      width: "100%",
      minWidth: 170,
      maxWidth: 277,
    },
    optionsColumn: {
      display: "flex",
      justifyContent: "flex-end",
      width: "100%",
      minWidth: 50,
      maxWidth: 50,
    },
    ellipsisOverflow: {
      overflow: "hidden",
      textOverflow: "ellipsis",
      whiteSpace: "nowrap",
    },
    listItemWrapper: {
      alignItems: "center",
    },
    chip: {
      marginRight: theme.spacing(1),
      marginBottom: theme.spacing(0.5),
      marginTop: theme.spacing(0.5),
      color: theme.palette.common.black,
    },
    avatar: {
      padding: theme.spacing(0.5, 0),
    },
  })
);

interface ListEmployeesProps {
  dispatchEffect: AppDispatchEffect;
  employees: Employee[];
  canChangeGroup: boolean;
}

const getRoleChangeCopy = (role: string) => {
  if (role === Role.ADMIN) {
    return " from a regular user to an administrator?";
  }
  if (role === Role.EMPLOYEE) {
    return " from administrator to regular user?";
  }
  return "?";
};

export const ListEmployees: React.FC<ListEmployeesProps> = ({
  employees,
  dispatchEffect,
  canChangeGroup,
}) => {
  const classes = useListEmployeesStyles();
  const [roleChange, setRoleChange] = useState<
    undefined | { role: { value: string; label: string }; employee: Employee }
  >();
  const [deleteEmployee, setDeleteEmployee] = useState<undefined | Employee>();
  const [submittingDialog, setSubmittingDialog] = useState(false);

  const isSelfGroups = employees.find((el) => el.isSelf)?.groups || [];
  const availableGroups = isSelfGroups.filter((el) => el !== Group.ORG_ADMIN);

  return (
    <React.Fragment>
      <TableList header={<HeaderListEmployees />}>
        {employees.map((employee) => {
          const {
            userGuid,
            profile: { displayName, initials },
            email,
            created,
            isSelf,
            groups,
          } = employee;
          return (
            <ListItem
              button
              key={userGuid}
              className={classes.listItemRow}
              disableRipple
            >
              <ContainerListItem
                leftOffset={3.5}
                className={classes.listItemWrapper}
              >
                <Box className={classes.avatarColumn}>
                  <AvatarTitle
                    className={classes.avatar}
                    avatar={initials || ""}
                  />
                </Box>
                <Box className={classes.nameColumn}>
                  <Box display="flex" flexWrap="nowrap">
                    <Typography className={classes.ellipsisOverflow}>
                      {displayName}
                    </Typography>
                    &nbsp;
                    <Typography color="textSecondary" variant="body2">
                      {isSelf ? "(you)" : ""}
                    </Typography>
                  </Box>
                  <Typography
                    color="textSecondary"
                    variant="body2"
                    className={classes.ellipsisOverflow}
                  >
                    {email}
                  </Typography>
                </Box>
                <Box className={classes.roleColumn}>
                  {canChangeGroup ? (
                    <React.Fragment>
                      {isSelf || !isSelfGroups.length ? (
                        <Typography variant="subtitle1" color="textSecondary">
                          {groups?.length ? "Admin" : "User"}
                        </Typography>
                      ) : (
                        <MenuSelect
                          interceptOnChange={(value) => {
                            setRoleChange({
                              role: {
                                value: value,
                                label:
                                  value === "ADMIN" ? "user" : "administrator",
                              },
                              employee: employee,
                            });
                          }}
                          value={groups.length ? "ADMIN" : "USER"}
                          options={[
                            { value: "ADMIN", label: "Admin" },
                            { value: "USER", label: "User" },
                          ]}
                        />
                      )}
                    </React.Fragment>
                  ) : (
                    <Chip
                      label={groups?.length ? "Administrator" : "User"}
                      className={classes.chip}
                    />
                  )}
                </Box>
                <Box className={classes.dateColumn}>
                  <Box
                    justifyContent="space-between"
                    display="inline-flex"
                    width={1}
                  >
                    <Typography variant="subtitle1" color="textSecondary">
                      {format(created.timestamp * 1000, "MMM d, yyyy")}
                    </Typography>
                  </Box>
                </Box>
                <Grid item className={classes.optionsColumn}>
                  {/* BE guys doesn't want to show this in development, fix when they implement some logic */}
                  {false && (
                    <ActionMenu
                      items={[
                        {
                          label: "Delete",
                          onClick: () => {
                            setDeleteEmployee(employee);
                          },
                        },
                      ]}
                    >
                      <IconButton size="small">
                        <Icon name="listItemOptions" />
                      </IconButton>
                    </ActionMenu>
                  )}
                </Grid>
              </ContainerListItem>
            </ListItem>
          );
        })}
      </TableList>
      {roleChange && (
        <DialogWarning
          dialogTitle="Change User Role?"
          labelCancel="Cancel"
          labelConfirm="Change"
          width={450}
          isLoading={submittingDialog}
          onConfirm={() => {
            setSubmittingDialog(true);

            const group: SetGroupAction[] =
              roleChange?.role.value === "ADMIN"
                ? availableGroups.map((el) => ({ op: "add", group: el }))
                : availableGroups.map((el) => ({ op: "remove", group: el }));

            return dispatchEffect(
              patchEmployeeEffect({
                employeeGuid: roleChange.employee.userGuid,
                group: group,
              })
            ).finally(() => {
              setSubmittingDialog(false);
              setRoleChange(undefined);
            });
          }}
          onCancel={() => {
            setRoleChange(undefined);
          }}
          message={
            <Typography>
              Are you sure you want to change the role of&nbsp;
              <strong>{roleChange.employee.profile.displayName}</strong>
              {getRoleChangeCopy(roleChange.role.value)}
            </Typography>
          }
        />
      )}

      {deleteEmployee && (
        <DialogWarning
          dialogTitle="Delete Employee"
          labelCancel="Cancel"
          labelConfirm="Delete"
          width={450}
          onConfirm={() => {
            setDeleteEmployee(undefined);
          }}
          onCancel={() => {
            setDeleteEmployee(undefined);
          }}
          message={
            <Typography>
              Are you sure you want to delete&nbsp;
              <strong>
                {deleteEmployee.profile.displayName || deleteEmployee.email}
              </strong>
              &nbsp; as an employee?
            </Typography>
          }
        />
      )}
    </React.Fragment>
  );
};
