import React from "react";
import { List } from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { AgreementListItem } from "../../../utils/models";
import { useHistory } from "react-router";
import { paths } from "../../../utils/common";
import { ContainerListItem } from "../../atoms/ContainerListItem";
import { ContainerTableList } from "../../molecules/ContainerTableList";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { ListItemAgreementAdmin } from "../../molecules/ListItemAgreementAdmin/component";
import { useWindowSize } from "../../../hooks";
import { GridListHeaderItem } from "../../atoms/GridListHeaderItem";

export const useListAgreementAdminStyles = makeStyles((theme: Theme) =>
  createStyles({
    containerListItem: {
      paddingLeft: theme.spacing(8.5),
      [theme.breakpoints.only("xs")]: {
        paddingLeft: theme.spacing(3),
      },
    },
    listRow: {
      minHeight: 85,
    },
    list: {
      paddingTop: theme.spacing(2),
    },
    statusColumn: {
      minWidth: 50,
      maxWidth: 50,
      textAlign: "center",

      [theme.breakpoints.only("xs")]: {
        minWidth: 40,
        maxWidth: 40,
      },
    },
    agreementColumn: {
      position: "relative",
      width: "100%",
      maxWidth: 245,
      minWidth: 150,
      textOverflow: "ellipsis",
      "& span, & p": {
        overflow: "hidden",
        textOverflow: "ellipsis",
        whiteSpace: "nowrap",
      },
    },
    assignorColumn: {
      width: "100%",
      maxWidth: 245,
      minWidth: 150,
      padding: theme.spacing(0, 2),
    },
    assigneeColumn: {
      width: "100%",
      maxWidth: 245,
      minWidth: 150,
      padding: theme.spacing(0, 2),
    },
    updatedColumn: {
      width: "100%",
      maxWidth: 245,
      minWidth: 150,
      padding: theme.spacing(0, 2),
    },
    createdColumn: {
      width: "100%",
      maxWidth: 245,
      minWidth: 150,
      padding: theme.spacing(0, 2),
    },
    optionsColumn: {
      marginLeft: "auto",
      minWidth: 30,
    },
    buttonColumn: {
      minWidth: 132,
      maxWidth: 160,
      width: "100%",
    },
    button: {
      paddingTop: 0,
      paddingBottom: 0,
      minWidth: 129,
    },
    status: {
      display: "flex",
      justifyContent: "flex-start",
    },
  })
);

interface ListAgreementsAdministratorProps {
  agreements: AgreementListItem[];
}

export const ListAgreementsAdministrator: React.FC<ListAgreementsAdministratorProps> = ({
  agreements,
}) => {
  const classes = useListAgreementAdminStyles();
  const { push } = useHistory();
  const [windowWidth] = useWindowSize({ subtractSidebarWidth: true });

  return (
    <React.Fragment>
      {agreements.length > 0 && (
        <ContainerTableList>
          <ContainerListItem
            data-testid="agreements-list"
            className={classes.containerListItem}
          >
            <GridListHeaderItem className={classes.statusColumn} />
            <GridListHeaderItem className={classes.agreementColumn}>
              <ListColumnTitle>Agreement</ListColumnTitle>
            </GridListHeaderItem>
            <GridListHeaderItem className={classes.assignorColumn}>
              <ListColumnTitle>Assignor</ListColumnTitle>
            </GridListHeaderItem>
            <GridListHeaderItem className={classes.assigneeColumn}>
              <ListColumnTitle>Assignee</ListColumnTitle>
            </GridListHeaderItem>
            <GridListHeaderItem className={classes.assigneeColumn}>
              <ListColumnTitle>Updated</ListColumnTitle>
            </GridListHeaderItem>
            <GridListHeaderItem className={classes.assigneeColumn}>
              <ListColumnTitle>Created</ListColumnTitle>
            </GridListHeaderItem>
            <GridListHeaderItem className={classes.optionsColumn} />
          </ContainerListItem>
          <List className={classes.list}>
            {agreements.map((agreement) => (
              <ListItemAgreementAdmin
                key={agreement.guid}
                agreement={agreement}
                windowWidth={windowWidth}
                onClick={() => {
                  push(`${paths.AGREEMENTS_REGISTER}/${agreement.guid}`);
                }}
              />
            ))}
          </List>
        </ContainerTableList>
      )}
    </React.Fragment>
  );
};
