import React from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { darken, Tooltip } from "@material-ui/core";
import clsx from "clsx";
import {
  ACKNOWLEDGED,
  DRAFT,
  FAILURE,
  IN_PROGRESS,
  INTERNAL_FAILURE,
  NO_ACK_INTEGRATION,
  NO_INTEGRATION,
  NO_RECIPIENTS,
  PARTIALLY_PROCESSED,
  REJECTED,
  SUCCESS,
  VALIDATION_ERROR,
} from "../../../utils/models";
import { Checkmark24, Close24, DotMark24 } from "@carbon/icons-react";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    working: {
      color: theme.palette.warning.main,
    },
    notSubmitted: {
      color: theme.palette.grey.A200,
    },
    submitted: {
      color: theme.palette.text.disabled,
    },
    registered: {
      color: theme.palette.success.main,
    },
    failed: {
      color: theme.palette.error.main,
    },
    failedYellow: {
      color: darken(theme.palette.warning.main, 0.1),
    },
    toolTip: {
      display: "flex",
      justifyContent: "center",
    },
    small: {
      fontSize: 16,
    },
  })
);

type MapStatusToTooltip = { [status: string]: string };

const defaultTooltipMap: { [status: string]: string } = {
  ERROR: "Failed",
  INTERNAL_FAILURE: "Failed",
  REJECTED: "Failed",
  VALIDATION_ERROR: "Failed",
  NOT_SUBMITTED: "Saved",
  DRAFT: "In Progress",
  IN_PROGRESS: "In Progress",
  SUBMITTED: "Submitted",
  REGISTERED: "Registered",
  SUCCESS: "Registered",
  PENDING: "Pending",
};

interface StatusIndicatorProps {
  status: string;
  showTooltip?: boolean;
  mapStatusToTooltip?: MapStatusToTooltip;
  className?: string;
}

export const StatusIndicator: React.FC<StatusIndicatorProps> = ({
  status,
  mapStatusToTooltip,
  showTooltip = true,
  className,
}) => {
  const classes = useStyles();
  const getStatusIcon = (status: string) => {
    switch (status) {
      case DRAFT:
      case IN_PROGRESS:
        return <DotMark24 className={classes.working} />;

      case SUCCESS:
      case ACKNOWLEDGED:
      case NO_ACK_INTEGRATION:
        return <Checkmark24 className={classes.registered} />;

      case NO_RECIPIENTS:
      case PARTIALLY_PROCESSED:
      case NO_INTEGRATION:
        return <DotMark24 className={classes.notSubmitted} />;

      case FAILURE:
      case REJECTED:
      case INTERNAL_FAILURE:
        return <Close24 className={classes.failed} />;

      case VALIDATION_ERROR:
        return <Close24 className={classes.failedYellow} />;
    }
  };

  const toolTipTitleMapped = mapStatusToTooltip
    ? mapStatusToTooltip[status]
    : defaultTooltipMap[status];

  const toolTipTitle = showTooltip ? toolTipTitleMapped : "";

  return (
    <Tooltip title={toolTipTitle || ""}>
      <span className={clsx(className || classes.toolTip)} data-testid="status">
        {getStatusIcon(status)}
      </span>
    </Tooltip>
  );
};
