import React from "react";
import { TableSortLabel as MaterialTableSortLabel } from "@material-ui/core";
import { ListColumnTitle } from "../ListColumnTitle";
import { useHistory } from "react-router";
import { useLocation } from "react-router-dom";
import queryString from "query-string";
import { ArrowDown16 } from "@carbon/icons-react";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import { SortOrder, WorkSortVariant } from "../../../utils/models";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    icon: {
      width: 13,
    },
    activeIcon: {
      "&.MuiTableSortLabel-active.MuiTableSortLabel-root.MuiTableSortLabel-active .MuiTableSortLabel-icon": {
        color: theme.palette.primary.main,
      },
    },
  })
);

export interface TableSortLabelProps {
  sortParams: {
    sortBy: WorkSortVariant;
    sortOrder: SortOrder;
  };
  label: string;
  workColumn: WorkSortVariant;
  className?: string;
  initSortOrder: SortOrder;
}

export const TableSortLabel: React.FC<TableSortLabelProps> = ({
  label,
  sortParams,
  workColumn,
  className,
  initSortOrder,
}) => {
  const classes = useStyles();
  const { push } = useHistory();
  const { search } = useLocation();
  const inverse: (order: SortOrder) => SortOrder = (current: SortOrder) => {
    return current === "asc" ? "desc" : "asc";
  };
  const isActive = sortParams.sortBy === workColumn;
  const direction = isActive ? sortParams.sortOrder : initSortOrder;

  const updateSortParams = (sortBy: WorkSortVariant, sortOrder: SortOrder) => {
    const currentQueryParams = queryString.parse(search);
    const newQueryParams = {
      ...currentQueryParams,
      sortBy,
      sortOrder,
    };
    push({
      search: queryString.stringify(newQueryParams),
    });
  };

  return (
    <MaterialTableSortLabel
      className={className}
      classes={{ active: classes.activeIcon, icon: classes.icon }}
      active={isActive}
      direction={direction}
      data-testid={`direction-${direction}`}
      IconComponent={(props) => <ArrowDown16 {...props} />}
      onClick={() => {
        const sortBy = workColumn;
        const sortOrder = isActive
          ? inverse(sortParams.sortOrder)
          : initSortOrder;
        updateSortParams(sortBy, sortOrder);
      }}
    >
      <ListColumnTitle>{label}</ListColumnTitle>
    </MaterialTableSortLabel>
  );
};
