import React from "react";
import { Field, FieldProps, Form, Formik } from "formik";
import {
  Box,
  Button,
  createStyles,
  Grid,
  makeStyles,
  Paper,
  TextField as MuiTextField,
  Theme,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import { TextField } from "formik-material-ui";
import { Address, AddressPatch, Country } from "../../../utils/models";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import { ContainerContent } from "../../atoms/ContainerContent";
import { FormikAutocompleteSelect } from "../../molecules/FormikAutocompleteSelect";
import { TooltipMap } from "../../atoms/TooltipMap";
import { patchCompanyDetailsEffect } from "../../pages/Company/effects";
import { tooltipFaqLinks } from "../FaqDrawer/utils";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    submitButtonGrid: {
      display: "flex",
      justifyContent: "flex-end",
      alignItems: "flex-end",
      flexGrow: 1,
      padding: theme.spacing(1.5, 1.5, 0, 1.5),
    },
    countriesListContainer: {
      maxHeight: 170,
    },
    inner: {
      paddingRight: theme.spacing(9),
      paddingBottom: theme.spacing(8),

      [theme.breakpoints.only("xs")]: {
        paddingRight: theme.spacing(5),
      },
    },
    row: { marginTop: theme.spacing(3.5) },
    ipiInput: { cursor: "help" },
    paper: {
      flexGrow: 1,
    },
  })
);

interface FormPublisherDetailsProps {
  countries: Country[];
  editable?: boolean;
  dispatchEffect: AppDispatchEffect;
  ipiNameNumber?: number;
  companyData: Address;
  dispatch: AppDispatch;
}

export const FormPublisherDetails: React.FC<FormPublisherDetailsProps> = ({
  countries,
  editable,
  dispatchEffect,
  ipiNameNumber,
  companyData,
  dispatch,
}) => {
  const classes = useStyles();
  const theme = useTheme();
  const breakpointXs = useMediaQuery(theme.breakpoints.down("xs"));

  return (
    <Formik
      enableReinitialize
      initialValues={{
        address1: companyData.address1 || "",
        address2: companyData.address2 || "",
        website: companyData.website || "",
        phoneNumber: companyData.phoneNumber || "",
        city: companyData.city || "",
        countryCode:
          companyData.countryCode && countries.length > 0
            ? countries.find((el) => el.countryCode === companyData.countryCode)
            : null,
        postalCode: companyData.postalCode || "",
      }}
      onSubmit={(values) => {
        const formData: AddressPatch = {
          ...values,
          countryCode: values.countryCode?.countryCode || null,
        };
        return dispatchEffect(patchCompanyDetailsEffect(formData));
      }}
    >
      {({ isSubmitting, handleSubmit, resetForm, dirty }) => (
        <Paper square className={classes.paper}>
          <ContainerContent className={classes.inner}>
            <Form>
              <Box pt={8}>
                {ipiNameNumber && (
                  <React.Fragment>
                    <Grid container spacing={3}>
                      <Grid item xs={12}>
                        <Typography variant="h5">
                          <TooltipMap
                            to="IPINameNumber"
                            faqLink={tooltipFaqLinks.PUBLISHER_DETAILS_IPI}
                            dispatch={dispatch}
                          >
                            IPI
                          </TooltipMap>
                        </Typography>
                      </Grid>
                      <Grid item sm={6} xs={12}>
                        <TooltipMap to="PublisherDetailsIpiNumber">
                          <MuiTextField
                            inputProps={{ className: classes.ipiInput }}
                            label="IPI"
                            variant="filled"
                            fullWidth
                            value={ipiNameNumber}
                            disabled
                          />
                        </TooltipMap>
                      </Grid>
                      <Grid item xs={6} />
                    </Grid>
                  </React.Fragment>
                )}
                <Box pt={ipiNameNumber ? 6 : 0}>
                  <Grid container spacing={3}>
                    <Grid item xs={12}>
                      <Typography variant="h5">Address</Typography>
                    </Grid>
                    <Grid item sm={6} xs={12}>
                      <Field
                        component={TextField}
                        label="Address line 1"
                        name="address1"
                        variant="filled"
                        disabled={!editable}
                        fullWidth
                      />
                    </Grid>
                    {!breakpointXs && <Grid item xs={6} />}
                    <Grid item sm={6} xs={12}>
                      <Field
                        component={TextField}
                        label="Address line 2"
                        name="address2"
                        variant="filled"
                        fullWidth
                        disabled={!editable}
                      />
                    </Grid>
                    {!breakpointXs && <Grid item xs={6} />}
                    <Grid item sm={6} xs={12}>
                      <Field
                        component={TextField}
                        label="City"
                        name="city"
                        variant="filled"
                        fullWidth
                        disabled={!editable}
                      />
                    </Grid>
                    {!breakpointXs && <Grid item xs={6} />}
                    <Grid item sm={6} xs={12}>
                      <Field
                        component={TextField}
                        label="Postal code"
                        name="postalCode"
                        variant="filled"
                        fullWidth
                        disabled={!editable}
                      />
                    </Grid>
                    <Grid item sm={6} xs={12}>
                      <Field name="countryCode">
                        {(props: FieldProps) => (
                          <FormikAutocompleteSelect<Country>
                            {...props}
                            label="Country"
                            options={countries}
                            getOptionLabel={(option) => option.name || ""}
                            getOptionSelected={(option, { countryCode }) =>
                              option.countryCode === countryCode
                            }
                            popupIcon={<SearchIcon />}
                            disabled={!editable}
                            classes={{
                              listbox: classes.countriesListContainer,
                            }}
                          />
                        )}
                      </Field>
                    </Grid>
                    <Grid item sm={6} xs={12}>
                      <Field
                        component={TextField}
                        label="Phone number"
                        name="phoneNumber"
                        variant="filled"
                        fullWidth
                        disabled={!editable}
                      />
                    </Grid>
                    <Grid item sm={6} xs={12}>
                      <Field
                        component={TextField}
                        label="Website"
                        name="website"
                        variant="filled"
                        fullWidth
                        disabled={!editable}
                      />
                    </Grid>
                  </Grid>
                </Box>
                <Grid
                  container
                  justify="flex-end"
                  direction="row"
                  className={classes.row}
                >
                  {dirty && (
                    <Button
                      color="primary"
                      onClick={() => resetForm()}
                      disabled={!editable || isSubmitting}
                    >
                      Cancel
                    </Button>
                  )}
                  <Button
                    type="submit"
                    color="primary"
                    onClick={() => handleSubmit()}
                    disabled={!editable || isSubmitting || !dirty}
                  >
                    Update
                  </Button>
                </Grid>
              </Box>
            </Form>
          </ContainerContent>
        </Paper>
      )}
    </Formik>
  );
};
