import React from "react";
import { ContainerContent } from "../../atoms/ContainerContent";
import { CellMrShare } from "../../atoms/CellMrShare";
import { CellSplit } from "../../atoms/CellSplit";
import { CellAvatar } from "../../atoms/CellAvatar";
import { CellOptions } from "../../atoms/CellOptions";
import { CellRole } from "../../atoms/CellRole";
import { CellCreator } from "../../atoms/CellCreator";
import { CellPrShare } from "../../atoms/CellPrShare";
import { CellCollaboration } from "../../atoms/CellCollaboration";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { RowCreatorListItem } from "../RowCreatorListItem";
import { TooltipMap } from "../../atoms/TooltipMap";
import { tooltipFaqLinks } from "../../organisms/FaqDrawer/utils";
import { AppDispatch } from "../../../App.reducer";
import { createStyles, makeStyles, Theme } from "@material-ui/core";
import { ColumnBreakpoints } from "../../../utils/models";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    cellCreator: {
      position: "relative",
    },
  })
);

interface HeaderCreatorListProps {
  hideSplitColumn?: boolean;
  hideOptionsColumn?: boolean;
  hideCollaborationColumn?: boolean;
  columnBreakPoints: ColumnBreakpoints;
  dispatch: AppDispatch;
}

export const HeaderCreatorList: React.FC<HeaderCreatorListProps> = ({
  hideOptionsColumn,
  hideSplitColumn,
  hideCollaborationColumn,
  columnBreakPoints: bp,
  dispatch,
}) => {
  const classes = useStyles();
  return (
    <ContainerContent>
      <RowCreatorListItem noPadding>
        <CellAvatar header />
        <CellCreator header dense={bp.creator} className={classes.cellCreator}>
          <ListColumnTitle>Party</ListColumnTitle>
        </CellCreator>
        <CellRole header dense={bp.role}>
          <ListColumnTitle>
            <TooltipMap
              to="Role"
              faqLink={tooltipFaqLinks.ROLE}
              dispatch={dispatch}
            >
              Role
            </TooltipMap>
          </ListColumnTitle>
        </CellRole>
        <CellPrShare header dense={bp.prShare}>
          <ListColumnTitle align="left">
            <TooltipMap
              to="PerformingLabel"
              faqLink={tooltipFaqLinks.WORKS_SHARES_PR_SHARE}
              dispatch={dispatch}
            >
              PR Share
            </TooltipMap>
          </ListColumnTitle>
        </CellPrShare>
        <CellMrShare header dense={bp.mrShare}>
          <ListColumnTitle align="left">
            <TooltipMap
              to="MechanicalLabel"
              faqLink={tooltipFaqLinks.WORKS_SHARES_MR_SHARE}
              dispatch={dispatch}
            >
              MR Share
            </TooltipMap>
          </ListColumnTitle>
        </CellMrShare>
        {!(hideSplitColumn || bp.split) && (
          <CellSplit header>
            <ListColumnTitle>
              <TooltipMap to="SplitLabel">Split</TooltipMap>
            </ListColumnTitle>
          </CellSplit>
        )}
        {!(hideCollaborationColumn || bp.collaboration) && (
          <CellCollaboration header>
            <ListColumnTitle>
              <TooltipMap to="CollaborationLabel">Collaboration</TooltipMap>
            </ListColumnTitle>
          </CellCollaboration>
        )}
        {!hideOptionsColumn && <CellOptions header />}
      </RowCreatorListItem>
    </ContainerContent>
  );
};
