import {
  Box,
  createStyles,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import React from "react";
import { AppDispatchEffect } from "../../../App.reducer";
import { Ipi } from "../../../utils/models";
import { Field } from "formik";
import { IpiVerifiedIcon } from "../IpiVerifiedIcon";
import { FormikSelectIpiDropdown } from "../../molecules/FormikSelectIpiDropdown";
import { HighlightText } from "../HighlightText";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      overflow: "hidden",
    },
    secondaryText: {
      paddingTop: theme.spacing(0.5),
      maxWidth: 125,
      display: "inline-flex",
      alignItems: "center",
    },
  })
);

interface CardCreatorProps {
  dispatchEffect?: AppDispatchEffect;
  ipiList?: Ipi[];
  isAuthor?: boolean;
  creatorPath?: string;
  creator: {
    displayName: string;
    ipi: number;
    isni: string;
    ipn: string;
    company: string;
    ipiVerified?: boolean;
  };
  wrapDisplayName?: boolean;
  highlightText?: string;
  multiline?: boolean;
  editable?: boolean;
}

export const CardCreator = ({
  dispatchEffect,
  ipiList,
  isAuthor,
  creatorPath,
  creator: { displayName = "", ipi, ipiVerified, isni, ipn, company },
  wrapDisplayName,
  multiline,
  highlightText = "",
  editable,
}: CardCreatorProps) => {
  const classes = useStyles();
  return (
    <Box
      className={classes.root}
      justifyContent={!ipi && !company ? "center" : undefined}
    >
      <HighlightText
        multiline={multiline}
        wrapText={wrapDisplayName}
        text={displayName}
        highlightText={highlightText}
      />
      {isAuthor && creatorPath && dispatchEffect && ipiList && (
        <Field
          component={FormikSelectIpiDropdown}
          name={creatorPath}
          dispatchEffect={dispatchEffect}
          ipiList={ipiList}
          disabled={!editable}
        />
      )}

      {!isAuthor && ipi && (
        <Typography
          variant="body2"
          color="textSecondary"
          noWrap
          className={classes.secondaryText}
          data-testid="ipi-name-number"
        >
          IPI {ipi} {ipiVerified && <IpiVerifiedIcon />}
        </Typography>
      )}
      {company && (
        <Typography variant="body2" color="textSecondary" noWrap>
          {company}
        </Typography>
      )}
    </Box>
  );
};
