import React from "react";
import {
  Box,
  BoxProps,
  createStyles,
  Grid,
  makeStyles,
} from "@material-ui/core";
import clsx from "clsx";
import { Theme } from "@material-ui/core/styles";

export const optionsCellWidth = 87;
export const optionsCellDenseWidth = 30;

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    cell: {
      maxWidth: optionsCellWidth,
      width: "100%",
      display: "flex",
      justifyContent: "center",
    },
    dense: {
      maxWidth: optionsCellDenseWidth,
      minWidth: optionsCellDenseWidth,
    },
    header: {
      paddingBottom: theme.spacing(1.5),
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
  })
);

interface CellOptionsProps extends BoxProps {
  header?: boolean;
  dense?: boolean;
}

export const CellOptions: React.FC<CellOptionsProps> = ({
  dense,
  children,
  className,
  header,
  ...props
}) => {
  const classes = useStyles();

  return (
    <Box
      {...props}
      component={Grid}
      className={clsx(classes.cell, className, {
        [classes.dense]: dense,
        [classes.header]: header,
      })}
    >
      {children}
    </Box>
  );
};
