import * as yup from "yup";
import { PasswordPolicy } from "../components/pages/SignIn/actions.types";

export const passwordValidation = (
  {
    pattern,
    minimumLength,
    requireNumbers,
    requireLowercase,
    requireUppercase,
    requireSpecialCharacters,
    notSameAsCurrent,
  }: PasswordPolicy,
  ref?: string
) => {
  const requirements = [];

  if (requireNumbers) {
    requirements.push("number");
  }
  if (requireLowercase) {
    requirements.push("lowercase letter");
  }
  if (requireUppercase) {
    requirements.push("uppercase letter");
  }
  if (requireSpecialCharacters) {
    requirements.push("special character");
  }

  const validation = yup
    .string()
    .required("Password is required")
    .min(minimumLength, "Password is too short")
    .matches(
      new RegExp(pattern),
      requirements.length ? `Password is too weak` : ""
    );

  if (notSameAsCurrent && ref) {
    return validation.notOneOf([yup.ref(ref)], "Please provide a new password");
  }

  return validation;
};
