import {
  Box,
  createStyles,
  Drawer,
  Fab,
  makeStyles,
  Typography,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import clsx from "clsx";
import { AppDispatch } from "../../../App.reducer";
import { Border } from "../../atoms/Border";
import { InputSearchFaq } from "../../atoms/InputSearchFaq";
import { FaqState } from "../../pages/Faq/reducer";
import {
  FaqAccess,
  faqItems,
  getDefaultPathByUrl,
  getFaqObjectByPath,
} from "./utils";
import {
  closeDrawerAction,
  openDrawerAction,
  resetItemAction,
  setFaqPathAction,
} from "../../pages/Faq/actions";
import { ExpansionPanelFaq } from "../ExpansionPanelFaq";
import { BreadcrumbsFaq } from "../BreadcrumbsFaq";
import { NavigationItemFaq } from "../NavigationItemFaq";
import { useLocation } from "react-router";
import { Close24, Help24, Search24 } from "@carbon/icons-react";
import { Theme } from "@material-ui/core/styles";
import { useFaqButtonPosition } from "../../../hooks/useFaqButtonPosition";
import { FaqItem, FaqNavigationItem } from "../../../utils/models";

const faqDrawerWidthOpen = 360;

interface StyleProps {
  shiftFabPosition: boolean;
}

const useStyles = makeStyles<Theme, StyleProps>((theme) =>
  createStyles({
    drawer: {
      position: "absolute",
      height: "100%",
      right: 0,
      flexShrink: 0,
      whiteSpace: "nowrap",
    },
    toolbar: {
      display: "flex",
      alignItems: "center",
      justifyContent: "space-between",
      padding: theme.spacing(0, 1),
      // necessary for content to be below app bar
      ...theme.mixins.toolbar,
    },
    optionsText: {
      padding: theme.spacing(0, 1.5),
    },
    drawerContentContainer: {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      overflow: "hidden",
      width: faqDrawerWidthOpen,
    },
    fabButton: {
      position: "fixed",
      height: 40,
      width: 40,
      minWidth: 40,
      bottom: ({ shiftFabPosition }) =>
        theme.spacing(shiftFabPosition ? 14 : 4),
      right: theme.spacing(5),
      padding: 0,
      backgroundColor: theme.palette.common.white,
      zIndex: 10,
      transition: theme.transitions.create(["bottom"], {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.shortest,
      }),
      "& svg": {
        fill: "rgba(0, 0, 0, 0.87)",
      },
    },
    upperContent: {
      padding: theme.spacing(4, 1.5, 0, 1.5),
    },
    helpAndFaqsContainer: {
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      padding: theme.spacing(0, 1.5),
    },
    searchAndBreadcrumbsContainer: {
      marginTop: theme.spacing(2.5),
    },
    breadCrumbsWrapper: {
      display: "flex",
      justifyContent: "space-between",
      padding: theme.spacing(0.75, 1.5, 0, 1.5),
    },
    breadCrumbLink: {
      color: theme.palette.text.secondary,
      cursor: "pointer",
    },
    clickableIcon: {
      cursor: "pointer",
      color: theme.palette.primary.main,
    },
    searchIcon: {
      marginTop: theme.spacing(0.75),
    },
    expansionPanelMainText: {
      maxWidth: "100%",
      whiteSpace: "pre-line",
    },
    lowerContent: {
      overflowY: "auto",
    },
    drawerPaper: {
      overflowX: "hidden",
    },
  })
);

interface FaqDrawerProps {
  dispatch: AppDispatch;
  faqState: FaqState;
  access: FaqAccess;
}

export const FaqDrawer: React.FC<FaqDrawerProps> = ({
  dispatch,
  faqState,
  access,
}) => {
  const { pathname } = useLocation();
  const shiftFabPosition = useFaqButtonPosition();
  const classes = useStyles({ shiftFabPosition });
  const { path, item, open, defaultPath } = faqState;
  const [searchOpen, setSearchOpen] = useState(false);
  const [searchItems, setSearchItems] = useState<Array<FaqItem>>([]);
  const [searchValue, setSearchValue] = useState("");

  useEffect(() => {
    dispatch(setFaqPathAction(defaultPath));
    dispatch(resetItemAction());
    return function unmount() {
      dispatch(closeDrawerAction());
    };
  }, [defaultPath, dispatch]);

  const handleSearchOnChange = (value: string) => {
    setSearchValue(value);
    if (value === "") {
      setSearchItems([]);
    } else {
      setSearchItems(
        Object.values(faqItems).filter((item: FaqItem) =>
          item.title.toLowerCase().includes(value.toLowerCase())
        )
      );
    }
  };

  const handleSearchClose = () => {
    setSearchValue("");
    setSearchItems([]);
    setSearchOpen(false);
  };

  const handleDrawerClose = () => {
    dispatch(closeDrawerAction());
  };

  const handleDrawerOpen = () => {
    dispatch(
      setFaqPathAction(defaultPath || getDefaultPathByUrl(access, pathname))
    );
    dispatch(resetItemAction());
    dispatch(openDrawerAction());
  };

  const faqObject = React.useMemo(() => getFaqObjectByPath(access, path), [
    access,
    path,
  ]);

  return (
    <React.Fragment>
      <Fab
        className={classes.fabButton}
        onClick={handleDrawerOpen}
        variant="round"
      >
        <Help24 />
      </Fab>
      <Drawer
        anchor="right"
        open={open}
        onClose={handleDrawerClose}
        PaperProps={{ elevation: 5 }}
      >
        <Box className={classes.drawerContentContainer}>
          <Box className={classes.upperContent}>
            <Box className={classes.helpAndFaqsContainer}>
              <Typography variant="h6">{"Help & FAQs"}</Typography>
              <Close24
                onClick={handleDrawerClose}
                className={classes.clickableIcon}
              />
            </Box>
            <Box className={classes.searchAndBreadcrumbsContainer}>
              {searchOpen ? (
                <InputSearchFaq
                  onChange={handleSearchOnChange}
                  value={searchValue}
                  onClose={handleSearchClose}
                />
              ) : (
                <Box className={classes.breadCrumbsWrapper}>
                  <BreadcrumbsFaq
                    dispatch={dispatch}
                    path={path}
                    access={access}
                  />
                  <Search24
                    className={clsx(classes.clickableIcon, classes.searchIcon)}
                    onClick={() => setSearchOpen(true)}
                  />
                </Box>
              )}
            </Box>
          </Box>
          <Border relative noPadding />
          <Box className={classes.lowerContent}>
            {faqObject &&
              !searchValue &&
              faqObject.navigation.map((navigationItem: FaqNavigationItem) => (
                <NavigationItemFaq
                  key={navigationItem.id}
                  dispatch={dispatch}
                  item={navigationItem}
                  currentPath={path}
                />
              ))}
            {faqObject &&
              !searchValue &&
              faqObject.items.map((faqItem: FaqItem) => (
                <ExpansionPanelFaq
                  key={faqItem.id}
                  dispatch={dispatch}
                  expandedItemId={item}
                  item={faqItem}
                  defaultOpen={faqObject.items.length === 1}
                />
              ))}
            {searchItems.length > 0 &&
              searchItems.map((faqItem: FaqItem) => (
                <ExpansionPanelFaq
                  key={faqItem.id}
                  dispatch={dispatch}
                  expandedItemId={item}
                  item={faqItem}
                />
              ))}
          </Box>
        </Box>
      </Drawer>
    </React.Fragment>
  );
};
