import React from "react";
import { CarbonIconProps, OverflowMenuVertical24 } from "@carbon/icons-react";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import clsx from "clsx";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    listItemOptions: {
      color: theme.palette.info.main,
    },
  })
);

interface IconProps {
  name: "listItemOptions";
}

export const Icon: React.FC<IconProps & CarbonIconProps> = ({
  name,
  ...args
}) => {
  const classes = useStyles();

  switch (name) {
    case "listItemOptions": {
      return (
        <OverflowMenuVertical24
          {...args}
          className={clsx(classes.listItemOptions, args.className)}
        />
      );
    }

    default:
      return <React.Fragment />;
  }
};
