import Promise from "promise-polyfill";
import { ThunkAction, ThunkDispatch } from "redux-thunk";
import { AppState } from "../../../App.reducer";
import { Action } from "redux";
import {
  createAgreementFromTemplateAction,
  createAgreementFromTemplateFailureAction,
  createAgreementFromTemplateSuccessAction,
  deleteAgreementTemplateAction,
  deleteAgreementTemplateFailureAction,
  deleteAgreementTemplateSuccessAction,
  fetchAgreementTemplateAction,
  fetchAgreementTemplateFailureAction,
  fetchAgreementTemplatesAction,
  fetchAgreementTemplatesFailureAction,
  fetchAgreementTemplatesSuccessAction,
  fetchAgreementTemplateSuccessAction,
  patchAgreementTemplateAction,
  patchAgreementTemplateFailureAction,
  patchAgreementTemplateSuccessAction,
  postAgreementTemplateAction,
  postAgreementTemplateFailureAction,
  postAgreementTemplateSuccessAction,
} from "./actions";
import { AlertType } from "../Alerts/actions.types";
import { addAlertAction } from "../Alerts/actions";
import axios from "axios";
import {
  FetchAgreementResponse,
  FetchAgreementTemplateResponse,
  FetchAgreementTemplatesResponse,
} from "../../../api.types";
import { handleError } from "../../../utils/errorHandler";
import { _agreementsLinks, agreement } from "../../../__mocks__/constants";
import { Agreement } from "../../../utils/models";
import {
  checkIfAttributesChanged,
  formatAgreementPostData,
  getFormattedAgreement,
} from "../AgreementRegistration/effects.utils";
import { Href } from "../../../utils/models";
import { AgreementFormValues } from "../../organisms/FormAgreement";
import { toasts } from "../../../utils/toasts";

export const fetchAgreementTemplatesEffect = (
  query?: string
): ThunkAction<
  Promise<void | unknown>,
  AppState,
  void,
  Action<string>
> => async (dispatch: ThunkDispatch<AppState, void, Action<string>>) => {
  dispatch(fetchAgreementTemplatesAction());

  const mockedRequestPromise = new Promise<FetchAgreementTemplatesResponse>(
    (resolve) => {
      setTimeout(() => {
        resolve({
          response: {
            template: {
              items: [],
              _links: _agreementsLinks,
            },
          },
        });
      }, 1000);
    }
  );

  try {
    const result = await mockedRequestPromise;
    const { items, _links } = result.response.template;
    dispatch(fetchAgreementTemplatesSuccessAction(items, _links));
  } catch (e) {
    const error = handleError(e.response, "agreement templates");
    dispatch(fetchAgreementTemplatesFailureAction());
    dispatch(
      addAlertAction(
        AlertType.ERROR,
        `${toasts.error.agreementTemplate.GET_LIST} ${error.message}`
      )
    );
  }
};

export const fetchAgreementTemplateEffect = (
  guid: string
): ThunkAction<
  Promise<void | unknown>,
  AppState,
  void,
  Action<string>
> => async (
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState
) => {
  const mockedRequestPromise = new Promise<FetchAgreementTemplateResponse>(
    (resolve) => {
      setTimeout(() => {
        resolve({
          response: {
            template: {
              item: agreement,
              _links: undefined,
            },
          },
        });
      }, 1000);
    }
  );

  dispatch(fetchAgreementTemplateAction());
  try {
    const result = await mockedRequestPromise;
    dispatch(
      fetchAgreementTemplateSuccessAction(result.response.template.item)
    );
    return result.response.template;
  } catch (e) {
    const error = handleError(e.response, "agreement template");
    dispatch(fetchAgreementTemplateFailureAction());
    dispatch(
      addAlertAction(
        AlertType.ERROR,
        `${toasts.error.agreementTemplate.GET_SINGLE} ${error.message}`
      )
    );
  }
};

export const postAgreementTemplateEffect = (
  form: AgreementFormValues
): ThunkAction<
  Promise<void | Agreement>,
  AppState,
  void,
  Action<string>
> => async (
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState
) => {
  dispatch(postAgreementTemplateAction());
  const formatted = formatAgreementPostData(form);

  const mockedRequestPromise = new Promise<FetchAgreementTemplateResponse>(
    (resolve) => {
      setTimeout(() => {
        resolve({
          response: {
            template: {
              item: { ...agreement, ...formatted },
              _links: undefined,
            },
          },
        });
      }, 1000);
    }
  );

  try {
    const result = await mockedRequestPromise;
    const { item } = result.response.template;
    const formattedAgreementTemplate = getFormattedAgreement(item);
    dispatch(postAgreementTemplateSuccessAction(formattedAgreementTemplate));
    return formattedAgreementTemplate;
  } catch (e) {
    const error = handleError(e.response, "agreement template");
    dispatch(postAgreementTemplateFailureAction());
    throw error;
  }
};

export const patchAgreementTemplateEffect = (
  agreement: Agreement,
  form: AgreementFormValues
): ThunkAction<
  Promise<void | Agreement>,
  AppState,
  void,
  Action<string>
> => async (
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState
) => {
  dispatch(patchAgreementTemplateAction());
  const formatted = formatAgreementPostData(form);
  if (checkIfAttributesChanged(agreement, formatted)) {
    const mockedRequestPromise = new Promise<FetchAgreementTemplateResponse>(
      (resolve) => {
        setTimeout(() => {
          resolve({
            response: {
              template: {
                item: { ...agreement, ...formatted },
                _links: undefined,
              },
            },
          });
        }, 1000);
      }
    );

    try {
      const result = await mockedRequestPromise;
      const { item } = result.response.template;
      const formattedAgreement = getFormattedAgreement(item);
      dispatch(patchAgreementTemplateSuccessAction(formattedAgreement));
      return formattedAgreement;
    } catch (e) {
      const error = handleError(e.response, "agreement template");
      dispatch(patchAgreementTemplateFailureAction());
      throw error;
    }
  } else {
    return agreement;
  }
};

export const deleteAgreementTemplateEffect = (
  guid: string,
  link: Href
): ThunkAction<Promise<void>, AppState, void, Action<string>> => async (
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState
) => {
  dispatch(deleteAgreementTemplateAction());
  try {
    await axios.request({
      url: link.href,
      method: link.method,
      withCredentials: true,
    });
    dispatch(deleteAgreementTemplateSuccessAction(guid));
  } catch (e) {
    const error = handleError(e.response, "agreement template");
    dispatch(deleteAgreementTemplateFailureAction());
    dispatch(
      addAlertAction(
        AlertType.ERROR,
        `${toasts.error.agreementTemplate.DELETE} ${error.message}`
      )
    );
    throw error;
  }
};

export const createAgreementFromTemplateEffect = (
  link: Href,
  title: string
): ThunkAction<
  Promise<null | string>,
  AppState,
  void,
  Action<string>
> => async (dispatch: ThunkDispatch<AppState, void, Action<string>>) => {
  const mockedRequestPromise = new Promise<FetchAgreementResponse>(
    (resolve) => {
      setTimeout(() => {
        resolve({
          response: {
            agreement: {
              item: agreement,
              _links: undefined,
            },
          },
        });
      }, 1000);
    }
  );
  dispatch(createAgreementFromTemplateAction());
  try {
    const result = await mockedRequestPromise;
    dispatch(createAgreementFromTemplateSuccessAction());
    dispatch(
      addAlertAction(
        AlertType.SUCCESS,
        toasts.success.common.CREATE_DRAFT(title)
      )
    );
    return result.response.agreement.item.guid;
  } catch (e) {
    dispatch(createAgreementFromTemplateFailureAction());
    const error = handleError(e.response, "agreement template");
    throw error;
  }
};
