import { createStyles, makeStyles, Theme } from "@material-ui/core";
import React from "react";
import { TextField } from "formik-material-ui";
import { Field, FieldAttributes } from "formik";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    input: {
      ...theme.typography.h6,
      fontSize: 20,
      fontWeight: 400,
    },
    width: {
      maxWidth: 496,
    },
  })
);

interface InputFieldAppBarProps {
  readonly?: boolean;
}

export const InputFieldAppBar: React.FC<
  InputFieldAppBarProps & FieldAttributes<any>
> = ({ readonly, "data-testid": dataTestId, ...props }) => {
  const classes = useStyles();

  return (
    <Field
      fullWidth
      className={classes.width}
      InputProps={{
        disabled: readonly,
        classes: {
          input: classes.input,
        },
      }}
      color="secondary"
      component={TextField}
      inputProps={{
        style: props.fontSize ? { fontSize: props.fontSize } : {},
        "data-testid": dataTestId,
      }}
      {...props}
    />
  );
};
