import React from "react";
import { ThemeProvider } from "@material-ui/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import { createMuiTheme, withStyles } from "@material-ui/core/styles";
import { useAppSelector } from "../../../App.reducer";

const GlobalCss = withStyles({
  "@global": {
    a: { color: "inherit" },
  },
})(() => null);

export const Theme: React.FC = ({ children }) => {
  const state = useAppSelector((state) => state);
  // const themeHook = useTheme();
  const theme = createMuiTheme(state.theme);
  // const currentTheme = getCurrentThemeSelector(state);
  // const theme = React.useMemo(
  //   () =>
  //     createMuiTheme({
  //       ...themeState,
  //       // palette: {
  //       //   ...themeState.palette,
  //       //   secondary: {
  //       //     main: themeHook.palette.getContrastText(currentTheme.appBarColor),
  //       //   },
  //       // },
  //     }),
  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  //   [themeHook.palette, themeState, currentTheme]
  // );

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <GlobalCss />
      {children}
    </ThemeProvider>
  );
};
