import { AppState } from "../../../App.reducer";
import { LoadingState } from "../../../types/enums";

export const getWorksListSelector = (state: AppState) =>
  state.works.workListItems;

export const getWorksListTypeSelector = (state: AppState) =>
  state.works.workListItemsType;

export const getWorkSelector = (state: AppState) => state.works.work;

export const getWorkCloneSelector = (state: AppState) => state.works.workClone;

export const getWorkRegistrationsSelector = (state: AppState) =>
  state.works.workRegistrations;

export const getWorkRegistrationsLinksSelector = (state: AppState) =>
  state.works._workRegistrationsLinks;

export const getWorksListLoadingStateSelector = (state: AppState) =>
  state.works.fetchWorksLoadingState;

export const getWorkCloneLoadingStateSelector = (state: AppState) =>
  state.works.workCloneLoadingState;

export const isWorkFormDataLoadedSelector = (state: AppState) => {
  const {
    fetchWorkLoadingState,
    fetchWorkRegistrationsLoadingState,
  } = state.works;
  return (
    fetchWorkLoadingState === LoadingState.FETCHED &&
    fetchWorkRegistrationsLoadingState === LoadingState.FETCHED
  );
};

export const isWorkUpdatingSelector = (state: AppState) => {
  const { updateWorkLoadingState } = state.works;
  const { registerWorkLoadingState } = state.workRegistration;
  return (
    updateWorkLoadingState === LoadingState.FETCHING ||
    updateWorkLoadingState === LoadingState.ERROR ||
    registerWorkLoadingState === LoadingState.FETCHING ||
    registerWorkLoadingState === LoadingState.ERROR
  );
};

export const getWorksListCreateLink = (state: AppState) => {
  return state.works._workListItemsLinks?.create;
};

export const getWorksListLinks = (state: AppState) => {
  return state.works._workListItemsLinks;
};

export const getWorkLinksSelector = (state: AppState) =>
  state.works.work?._links;
