import React, { useEffect, useState } from "react";
import { Field, FieldProps } from "formik";
import { FormikAutocompleteSelect } from "../../molecules/FormikAutocompleteSelect";
import { LoadingState } from "../../../types/enums";
import {
  IpiSearchCreatorOption,
  Society,
  SocietyAffiliation,
} from "../../../utils/models";
import { SocietyAffiliationSelect } from "../../organisms/DialogAddIpi";
import {
  Avatar,
  Box,
  Button,
  createStyles,
  makeStyles,
  Paper,
  Theme,
  Typography,
} from "@material-ui/core";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    listItem: {
      height: 64,
    },
    listContainer: {
      maxHeight: 300,
    },
    optionWrapper: {
      width: "100%",
      display: "inline-flex",
      justifyContent: "space-between",
      alignItems: "center",
    },
    logo: {
      objectFit: "scale-down",
    },
    selectedLogo: {
      width: 48,
      height: 32,
    },
    logoEmpty: {
      width: 60,
      height: 40,
    },
    groupLabel: {
      ...theme.typography.caption,
      color: theme.palette.info.light,
      padding: theme.spacing(2, 2, 1.5, 2),
    },
    showAllButton: {
      padding: theme.spacing(0, 1, 3, 1),
      display: "flex",
      justifyContent: "center",
    },
  })
);

const getOptions: (
  suggestedOptions: SocietyAffiliation[],
  societies: Society[],
  showAll: boolean
) => SocietyAffiliationSelect[] = (suggestedOptions, societies, showAll) => {
  const societyOptions = societies.map((el) => ({
    isSuggested: false,
    title: el.acronym,
    value: {
      societyId: Number(el.id),
      acronym: el.acronym,
      country: el.country,
      logo: el.logo,
    },
  }));

  return [
    ...suggestedOptions.map((el) => ({
      title: el.acronym || "",
      value: el,
      isSuggested: true,
    })),
    ...(showAll ? societyOptions : []),
  ];
};

interface FormikSocietyAffiliationAutocompleteProps {
  disabled: boolean;
  societies: Society[];
  loadingState: LoadingState;
  selectedIpiSearchOption: IpiSearchCreatorOption | null;
}

export const FormikSocietyAffiliationAutocomplete: React.FC<FormikSocietyAffiliationAutocompleteProps> = ({
  disabled,
  societies,
  loadingState,
  selectedIpiSearchOption,
}) => {
  const classes = useStyles();
  const prSocietyAffiliations =
    selectedIpiSearchOption?.prSocietyAffiliations || [];
  const [showAll, setShowAll] = useState(prSocietyAffiliations.length === 0);
  const [open, setOpen] = useState(false);
  const options = getOptions(prSocietyAffiliations, societies, showAll);

  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);

  useEffect(() => {
    setShowAll(prSocietyAffiliations.length === 0);
  }, [prSocietyAffiliations.length]);

  return (
    <Field name="societyAffiliation">
      {(props: FieldProps<SocietyAffiliationSelect>) => (
        <FormikAutocompleteSelect<SocietyAffiliationSelect>
          {...props}
          label="Society Affiliation"
          onChange={handleClose}
          open={open}
          options={options}
          loading={loadingState === LoadingState.FETCHING}
          getOptionSelected={(option, { value: { country } }) =>
            option.value.country === country
          }
          getOptionLabel={(option) => option?.title || ""}
          disabled={disabled}
          classes={{
            option: classes.listItem,
            listbox: classes.listContainer,
            groupLabel: classes.groupLabel,
          }}
          groupBy={(option) =>
            option.isSuggested ? "Suggested Society" : "All Societies"
          }
          textFieldProps={{
            InputProps: {
              onClick: handleOpen,
              onBlur: handleClose,
              ...(props.field.value
                ? {
                    endAdornment: (
                      <Avatar
                        className={clsx(classes.selectedLogo, {
                          [classes.logoEmpty]: props.field.value.value.logo,
                        })}
                        classes={{
                          img: classes.logo,
                        }}
                        variant="rounded"
                        src={props.field.value.value.logo}
                      >
                        {/* override default svg icon */}
                        {""}
                      </Avatar>
                    ),
                  }
                : {}),
            },
          }}
          renderOption={(option) => {
            return (
              <Box className={classes.optionWrapper}>
                <Typography variant="body1">{option.title}</Typography>
                <Avatar
                  className={clsx(!option.value.logo && classes.logoEmpty)}
                  classes={{
                    img: classes.logo,
                  }}
                  variant="rounded"
                  src={option.value.logo}
                >
                  {/* override default svg icon */}
                  {""}
                </Avatar>
              </Box>
            );
          }}
          PaperComponent={({ children, ...rest }) => (
            <Paper {...rest}>
              {children}
              {!showAll && (
                <Box
                  className={classes.showAllButton}
                  onMouseDown={(e) => e.preventDefault()}
                >
                  <Button
                    onMouseDown={(e) => e.preventDefault()}
                    onClick={() => setShowAll(true)}
                  >
                    Show all societies
                  </Button>
                </Box>
              )}
            </Paper>
          )}
        />
      )}
    </Field>
  );
};
