import React, { useState } from "react";
import { MenuAddWorkAgreement } from "../../molecules/MenuAddWorkAgreement";
import { FormAddWorkAgreementNumber } from "../FormAddWorkAgreementNumber";
import { FormAddWorkSubPublisherAgreementNumber } from "../FormAddWorkSubPublisherAgreementNumber";
import { WorkAgreements } from "../WorkAgreements/component";
import { WorkFormValues } from "../FormWorkRegistration";
import {
  getAgreementNumbers,
  removeWorkAgreementAssociation,
  WorkAgreementType,
} from "./component.utils";
import { TemplateFormValues } from "../FormWorkTemplate";
import { PageNoContent } from "../../molecules/PageNoContent";
import { Box } from "@material-ui/core";

interface TabWorkAgreementsProps {
  editable: boolean;
  workFormValues: WorkFormValues | TemplateFormValues;
  setFieldValue: (name: string, value: any) => void;
}

export interface WorkAgreementListItem {
  creatorIpi: number;
  publisherIpi: number;
  agreementNumber: string;
  publisher: string;
  creator: string;
}

export const TabWorkAgreements: React.FC<TabWorkAgreementsProps> = ({
  editable,
  workFormValues,
  setFieldValue,
}) => {
  const [openAgreementNumber, setOpenAgreementNumber] = useState(false);
  const [edit, setEdit] = useState<WorkAgreementListItem>();
  const [
    openSubPublishingAgreementNumber,
    setOpenSubPublishingAgreementNumber,
  ] = useState(false);
  const handleOpenAgreementNumber = () => {
    setOpenAgreementNumber(true);
  };
  const handleCloseAgreementNumber = () => {
    setOpenAgreementNumber(false);
    setEdit(undefined);
  };
  const handleOpenSubPublishingAgreementNumber = () => {
    setOpenSubPublishingAgreementNumber(true);
  };
  const handleCloseSubPublishingAgreementNumber = () => {
    setOpenSubPublishingAgreementNumber(false);
    setEdit(undefined);
  };

  const handleEdit = (type: WorkAgreementType) => (
    item: WorkAgreementListItem
  ) => {
    setEdit(item);
    if (type === WorkAgreementType.CREATOR_PUBLISHER) {
      handleOpenAgreementNumber();
    }
    if (type === WorkAgreementType.SUB_PUBLISHER) {
      handleOpenSubPublishingAgreementNumber();
    }
  };

  const handleDelete = (type: WorkAgreementType) => (
    item: WorkAgreementListItem
  ) => {
    const updated = removeWorkAgreementAssociation(
      workFormValues.creators,
      item,
      type
    );
    setFieldValue("creators", updated);
  };

  const agreementNumbers = getAgreementNumbers(
    workFormValues,
    WorkAgreementType.CREATOR_PUBLISHER
  );

  const subPublishingAgreementNumbers = getAgreementNumbers(
    workFormValues,
    WorkAgreementType.SUB_PUBLISHER
  );

  return (
    <React.Fragment>
      {editable && (
        <MenuAddWorkAgreement
          onOpenAgreementNumber={handleOpenAgreementNumber}
          onOpenSubPublishingAgreementNumber={
            handleOpenSubPublishingAgreementNumber
          }
        />
      )}
      <FormAddWorkAgreementNumber
        open={openAgreementNumber}
        onClose={handleCloseAgreementNumber}
        workFormValues={workFormValues}
        setFieldValue={setFieldValue}
        edit={edit}
      />
      <FormAddWorkSubPublisherAgreementNumber
        open={openSubPublishingAgreementNumber}
        onClose={handleCloseSubPublishingAgreementNumber}
        workFormValues={workFormValues}
        setFieldValue={setFieldValue}
        edit={edit}
      />

      {agreementNumbers.length + subPublishingAgreementNumbers.length === 0 ? (
        <Box pt={7}>
          <PageNoContent
            primaryText="No Agreements"
            secondaryText="Click the + button to add an agreement"
          />
        </Box>
      ) : (
        <React.Fragment>
          <WorkAgreements
            sectionLabel="Agreement Numbers"
            editable={editable}
            onEdit={handleEdit(WorkAgreementType.CREATOR_PUBLISHER)}
            onDelete={handleDelete(WorkAgreementType.CREATOR_PUBLISHER)}
            workAgreements={agreementNumbers}
          />
          <WorkAgreements
            sectionLabel="Sub-Publishing Agreement Numbers"
            editable={editable}
            onEdit={handleEdit(WorkAgreementType.SUB_PUBLISHER)}
            onDelete={handleDelete(WorkAgreementType.SUB_PUBLISHER)}
            workAgreements={subPublishingAgreementNumbers}
          />
        </React.Fragment>
      )}
    </React.Fragment>
  );
};
