import React from "react";
import {
  Box,
  BoxProps,
  createStyles,
  Grid,
  makeStyles,
  Theme,
} from "@material-ui/core";
import clsx from "clsx";

export const collaborationCellWidth = 130;

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    cell: {
      minWidth: 130,
      textAlign: "center",
    },

    header: {
      paddingBottom: theme.spacing(1.5),
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
  })
);

interface CellCollaborationProps extends BoxProps {
  header?: boolean;
}

export const CellCollaboration: React.FC<CellCollaborationProps> = ({
  children,
  className,
  header,
  ...props
}) => {
  const classes = useStyles();

  return (
    <Box
      {...props}
      component={Grid}
      className={clsx(classes.cell, className, {
        [classes.header]: header,
      })}
    >
      {children}
    </Box>
  );
};
