import {
  REGISTER_WORK,
  REGISTER_WORK_FAILURE,
  REGISTER_WORK_SUCCESS,
  WorkRegistrationActionTypes,
} from "./actions.types";
import { LoadingState } from "../../../types/enums";

export interface WorkRegistrationState {
  collaboratorsLoadingState: LoadingState;
  registerWorkLoadingState: LoadingState;
}

export const workRegistrationInitialState: WorkRegistrationState = {
  collaboratorsLoadingState: LoadingState.PRISTINE,
  registerWorkLoadingState: LoadingState.PRISTINE,
};

//TODO:: I think we should remove this reducer and move everything to /Works/reducer.ts

export const workRegistrationReducer = (
  state: WorkRegistrationState = workRegistrationInitialState,
  action: WorkRegistrationActionTypes
) => {
  switch (action.type) {
    case REGISTER_WORK: {
      return { ...state, registerWorkLoadingState: LoadingState.FETCHING };
    }

    case REGISTER_WORK_SUCCESS: {
      return { ...state, registerWorkLoadingState: LoadingState.FETCHED };
    }

    case REGISTER_WORK_FAILURE: {
      return { ...state, registerWorkLoadingState: LoadingState.ERROR };
    }

    default:
      return state;
  }
};
