import React from "react";
import {
  Box,
  createStyles,
  makeStyles,
  Theme as MaterialUiTheme,
} from "@material-ui/core";
import { Skeleton } from "@material-ui/lab";

const useStyles = makeStyles((theme: MaterialUiTheme) =>
  createStyles({
    skeletonLoaderContainer: {
      display: "flex",
      flexDirection: "column",
    },
    skeletonBottom: {
      marginTop: theme.spacing(1.25),
    },
    skeleton: {
      boxShadow: `0px 4px 8px ${theme.palette.grey[400]}`,
    },
  })
);

export const SkeletonThemeOption: React.FC = () => {
  const classes = useStyles();
  return (
    <Box className={classes.skeletonLoaderContainer}>
      <Skeleton
        variant="rect"
        width={160}
        height={90}
        className={classes.skeleton}
      />
      <Skeleton
        variant="rect"
        width={160}
        height={20}
        className={classes.skeletonBottom}
      />
    </Box>
  );
};
