export const SET_FAQ_PATH = "SET_FAQ_PATH";
export const SET_DEFAULT_FAQ_PATH = "SET_DEFAULT_FAQ_PATH";
export const SET_ITEM = "SET_ITEM";
export const RESET_ITEM = "RESET_ITEM";
export const OPEN_DRAWER = "OPEN_DRAWER";
export const CLOSE_DRAWER = "CLOSE_DRAWER";

export type SetFaqPathAction = {
  type: typeof SET_FAQ_PATH;
  path?: string;
};

export type SetItemAction = {
  type: typeof SET_ITEM;
  item?: string;
};

export type SetDefaultFaqPathAction = {
  type: typeof SET_DEFAULT_FAQ_PATH;
  path?: string;
};

export type ResetItemAction = {
  type: typeof RESET_ITEM;
};

export type OpenDrawerAction = {
  type: typeof OPEN_DRAWER;
};

export type CloseDrawerAction = {
  type: typeof CLOSE_DRAWER;
};

export type FaqActionTypes =
  | SetFaqPathAction
  | SetDefaultFaqPathAction
  | SetItemAction
  | ResetItemAction
  | OpenDrawerAction
  | CloseDrawerAction;
