import React, { useState } from "react";
import { Field, useFormikContext } from "formik";
import { Box, createStyles, makeStyles, Theme } from "@material-ui/core";
import { IdentityType, RelationType } from "../../../types/enums";
import {
  _AgreementsLinks,
  Agreement,
  AgreementMember,
} from "../../../utils/models";
import { AgreementFormPermissions } from "../FormAgreement/utils";
import { deleteAgreementEffect } from "../../pages/AgreementRegistration/effects";
import { useHistory } from "react-router";
import { addAlertAction } from "../../pages/Alerts/actions";
import { AlertType } from "../../pages/Alerts/actions.types";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";

import { paths } from "../../../utils/common";
import { CellAgreementRelation } from "../../atoms/CellAgreementRelation";
import { DialogWarning } from "../../molecules/DialogWarning";
import { otherPartyWarning } from "../ListAgreements";
import { CellAgreementParty } from "../../atoms/CellAgreementParty";
import { CellAgreementPermissions } from "../../atoms/CellAgreementPermissions";
import { AgreementFormValues } from "../FormAgreement";
import { CellAgreementOptions } from "../../atoms/CellAgreementOptions";
import { RowGridAgreementMember } from "../../molecules/RowGridAgreementMember";
import { FormikAddAgreementMember } from "../../atoms/FormikAddAgreementMember";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { RowCreatorListItem } from "../../molecules/RowCreatorListItem";
import { CellAgreementIpi } from "../../atoms/CellAgreementIPI";
import { TooltipMap } from "../../atoms/TooltipMap";
import { tooltipFaqLinks } from "../FaqDrawer/utils";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    border: {
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
  })
);

const getPartyMembersSortedByOwnership = ({
  assignor,
  assignee,
}: {
  assignor?: AgreementMember;
  assignee?: AgreementMember;
}) =>
  [
    ...(assignor ? [{ member: assignor, type: RelationType.ASSIGNOR }] : []),
    ...(assignee ? [{ member: assignee, type: RelationType.ASSIGNEE }] : []),
  ].sort((a, b) => {
    if (a.member.permissions.owner === b.member.permissions.owner) {
      return 0;
    }
    return a.member.permissions.owner ? -1 : 1;
  });

interface ListAgreementMembersProps {
  formValues: AgreementFormValues;
  permissions: AgreementFormPermissions;
  agreement?: Agreement;
  dispatchEffect: AppDispatchEffect;
  dispatch: AppDispatch;
  agreementsListLinks: _AgreementsLinks;
}

export const ListAgreementMembers: React.FC<ListAgreementMembersProps> = ({
  formValues,
  agreement,
  permissions,
  dispatchEffect,
  dispatch,
  agreementsListLinks,
}) => {
  const classes = useStyles();
  const [showLeaveDialog, setShowLeaveDialog] = useState(false);
  const { assignor, assignee } = formValues;
  const { setFieldValue } = useFormikContext();
  const { push } = useHistory();

  const partyMembers = getPartyMembersSortedByOwnership({ assignor, assignee });

  const restrictAccessOptionChange = (member?: AgreementMember) => () =>
    Boolean(member?.permissions.read || member?.permissions.write);

  const handleAgreementLeave = async () => {
    const guid = agreement?.guid;
    const link = agreement?._links?.delete;
    if (guid && link) {
      try {
        await dispatchEffect(deleteAgreementEffect(guid, link));
        push(paths.AGREEMENTS);
      } catch (e) {
        setShowLeaveDialog(false);
        dispatch(
          addAlertAction(
            AlertType.ERROR,
            `Couldn't leave agreement. ${e.message}`
          )
        );
      }
    }
  };

  const getDisableRelationOptions = (currentMember: AgreementMember) => {
    const hasCreator = partyMembers.find(
      (el) => el.member.type === IdentityType.CREATOR
    );

    if (!hasCreator) {
      return [];
    }

    return currentMember.type === IdentityType.PUBLISHER
      ? [RelationType.ASSIGNOR]
      : [RelationType.ASSIGNEE];
  };

  return (
    <React.Fragment>
      {showLeaveDialog && (
        <DialogWarning
          width={400}
          onConfirm={handleAgreementLeave}
          onCancel={() => setShowLeaveDialog(false)}
          message={otherPartyWarning}
        />
      )}
      {(assignor || assignee) && (
        <React.Fragment>
          <RowCreatorListItem noPadding>
            <CellAgreementParty className={classes.border}>
              <ListColumnTitle>
                <TooltipMap to="AgreementParty" placement="bottom-start">
                  Party
                </TooltipMap>
              </ListColumnTitle>
            </CellAgreementParty>
            <CellAgreementRelation className={classes.border}>
              <ListColumnTitle>
                <TooltipMap
                  to="AgreementRelation"
                  faqLink={tooltipFaqLinks.RELATION}
                  dispatch={dispatch}
                >
                  Relation
                </TooltipMap>
              </ListColumnTitle>
            </CellAgreementRelation>
            <CellAgreementIpi className={classes.border}>
              <ListColumnTitle>
                <TooltipMap
                  to="IPINameNumber"
                  dispatch={dispatch}
                  faqLink={tooltipFaqLinks.IPI}
                >
                  IPI
                </TooltipMap>
              </ListColumnTitle>
            </CellAgreementIpi>
            <CellAgreementPermissions className={classes.border}>
              <ListColumnTitle>
                <TooltipMap
                  to="AgreementPermissions"
                  faqLink={tooltipFaqLinks.AGREEMENT_PERMISSIONS}
                  dispatch={dispatch}
                >
                  Permissions
                </TooltipMap>
              </ListColumnTitle>
            </CellAgreementPermissions>
            <CellAgreementOptions className={classes.border} />
          </RowCreatorListItem>

          {partyMembers.map(({ member, type }) => (
            <RowGridAgreementMember
              disableRelationOptions={getDisableRelationOptions(member)}
              key={member.ipiNameNumber}
              owner={member.permissions.owner}
              parent={type === RelationType.ASSIGNEE ? "assignee" : "assignor"}
              type={type}
              member={member}
              onDelete={(isLeaving) => {
                if (isLeaving) {
                  setShowLeaveDialog(true);
                } else {
                  if (type === RelationType.ASSIGNOR) {
                    setFieldValue("assignor", undefined);
                  }
                  if (type === RelationType.ASSIGNEE) {
                    setFieldValue("assignee", undefined);
                  }
                }
              }}
              restrictAccessChange={restrictAccessOptionChange(member)}
              onRoleChange={() => {
                setFieldValue("assignor", assignee);
                setFieldValue("assignee", assignor);
              }}
              permissions={permissions}
            />
          ))}
        </React.Fragment>
      )}
      {partyMembers.length < 2 && (
        <Box width={1}>
          <Field
            hasAssignor={Boolean(assignor)}
            hasAssignee={Boolean(assignee)}
            component={FormikAddAgreementMember}
            disabled={!permissions.addParty}
            apiSourceCreator={agreementsListLinks.ipiSearchCreators?.href || ""}
            apiSourcePublisher={
              agreementsListLinks.ipiSearchPublishers?.href || ""
            }
          />
        </Box>
      )}
    </React.Fragment>
  );
};
