import React, { useEffect, useState } from "react";
import {
  Box,
  createStyles,
  makeStyles,
  Theme,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import {
  FAILURE,
  IN_PROGRESS,
  PARTIALLY_PROCESSED,
  REJECTED,
  SUCCESS,
  VALIDATION_ERROR,
  WorkListItemStatusType,
} from "../../../utils/models";
import { AccountRoles } from "../../../types/enums";
import {
  getTemplatesListFetchLinkSelector,
  getUserIsSocAdminSelector,
  getWorkDraftsListFetchLinkSelector,
  getWorksListFetchLinkSelector,
} from "../../pages/SignIn/selectors";
import { AppState } from "../../../App.reducer";
import { paths } from "../../../utils/common";
import { StatusIndicator } from "../../atoms/StatusIndicator";
import { InputSearch } from "../../atoms/InputSearch";
import { DropdownSelect } from "../DropdownSelect";
import { TabMenu } from "../../molecules/TabMenu";
import { TooltipMap } from "../../atoms/TooltipMap";
import { doesUserHaveRoles } from "../../../utils/helpers";
import { getWorkFilters, workFilterStatusAll } from "./utils";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    controls: {
      display: "flex",
      alignItems: "center",
      width: "100%",
      paddingTop: theme.spacing(6),
      [theme.breakpoints.only("xs")]: {
        paddingTop: theme.spacing(2),
      },
    },
    statusOption: {
      display: "inline-flex",
      justifyContent: "space-between",
      width: "100%",
      alignItems: "center",
    },
    statusIndicator: {
      display: "flex",
      width: 30,
      justifyContent: "center",
    },
    filterWrapper: {
      minWidth: 150,
      textAlign: "end",
      color: theme.palette.primary.contrastText,
    },
    dropdown: {
      color: theme.palette.primary.contrastText,
    },
  })
);

const getUserTabs = (isPublisher: boolean, isSocAdmin: boolean) => [
  {
    label: isPublisher || isSocAdmin ? "Works" : "My Works",
    link: paths.WORKS_LIST,
    access: getWorksListFetchLinkSelector,
  },
  {
    label: "Templates",
    link: paths.WORKS_TEMPLATE_LIST,
    access: getTemplatesListFetchLinkSelector,
  },
  {
    label: "Drafts",
    link: paths.WORKS_DRAFT_LIST,
    access: getWorkDraftsListFetchLinkSelector,
  },
];

const statusOptions = [
  {
    key: "ALL",
    value: workFilterStatusAll,
    label: "All status",
    tooltip: "StatusOptionALL",
  },
  {
    key: FAILURE,
    value: [REJECTED, FAILURE, VALIDATION_ERROR],
    label: "Failed",
    tooltip: "StatusOptionFAILED",
  },
  {
    key: IN_PROGRESS,
    value: IN_PROGRESS,
    label: "In Progress",
    tooltip: "StatusOptionPENDING",
  },
  {
    key: SUCCESS,
    value: SUCCESS,
    label: "Registered",
    tooltip: "StatusOptionREGISTERED",
  },
  {
    key: PARTIALLY_PROCESSED,
    value: PARTIALLY_PROCESSED,
    label: "Partially Processed",
    tooltip: "StatusOptionPARTIALLY_PROCESSED",
  },
];

interface WorksNavigationBarProps {
  onFilterChange: (filters: WorkListItemStatusType[]) => void;
  pathname: string;
  search: string;
  onQueryChange: (query: string) => void;
  state: AppState;
  isDraftsPage: boolean;
  searchDisabled: boolean;
}

const getOptions = (workFilters: string[]) =>
  workFilters.length === 1
    ? statusOptions
        .slice(1)
        .find(({ key }) => workFilters.includes(key as WorkListItemStatusType))
    : statusOptions[0];

export const WorksNavigationBar: React.FC<WorksNavigationBarProps> = ({
  onFilterChange,
  pathname,
  search,
  onQueryChange,
  state,
  isDraftsPage,
  searchDisabled,
}) => {
  const classes = useStyles();
  const theme = useTheme();
  const hideBreakpoint = useMediaQuery(theme.breakpoints.up("sm"));
  const isSocAdmin = getUserIsSocAdminSelector(state);
  const isPublisher = doesUserHaveRoles(state, [AccountRoles.PUBLISHER]);
  const filteredTabs = getUserTabs(
    isPublisher,
    isSocAdmin
  ).filter(({ access }) => (access ? Boolean(access(state)) : true));
  const urlParams = new URLSearchParams(search);
  const workFilters = getWorkFilters(urlParams.get("filters"), isDraftsPage);
  const [status, setStatus] = useState(getOptions(workFilters));
  const [queryInput, setQueryInput] = useState(urlParams.get("search") || "");

  useEffect(() => {
    const filters = getWorkFilters(urlParams.get("filters"), isDraftsPage);
    setStatus(getOptions(filters));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathname]);

  return (
    <React.Fragment>
      <div className={classes.controls}>
        <TabMenu
          tabs={filteredTabs}
          onChange={() => {
            setQueryInput("");
            setStatus(statusOptions[0]);
          }}
        />

        {hideBreakpoint && (
          <Box ml="auto" mr={1}>
            <InputSearch
              disabled={searchDisabled}
              value={queryInput}
              onChange={(query) => {
                setQueryInput(query);
                onQueryChange(query);
              }}
            />
          </Box>
        )}

        {hideBreakpoint && pathname === paths.WORKS_LIST && (
          <Box className={classes.filterWrapper}>
            <DropdownSelect
              selectedOption={status}
              onChange={(status) => {
                setStatus(status);
                onFilterChange(status.value);
              }}
              options={statusOptions}
              optionLayout={(option) => (
                <Box
                  className={classes.dropdown}
                  display="flex"
                  justifyContent="space-between"
                  alignItems="center"
                  width="100%"
                >
                  <TooltipMap to={option.tooltip}>
                    <Typography color="textPrimary">{option.label}</Typography>
                  </TooltipMap>
                  <div className={classes.statusIndicator}>
                    <StatusIndicator showTooltip={false} status={option.key} />
                  </div>
                </Box>
              )}
            />
          </Box>
        )}
      </div>
    </React.Fragment>
  );
};
