import {
  createStyles,
  makeStyles,
  Tab as MaterialTab,
  Theme,
  Typography,
} from "@material-ui/core";
import { NavLink } from "react-router-dom";
import React from "react";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    tab: {
      minWidth: 134,
      [theme.breakpoints.only("xs")]: {
        minWidth: 118,
      },
    },
    notSelected: {
      color: theme.palette.info.light,
    },
  })
);

interface TabProps {
  label: string;
  value: string;
  to?: string;
  onChange?: () => void;
  onClick?: () => void;
  selected?: boolean;
}

export const Tab: React.FC<TabProps> = ({
  value,
  label,
  to,
  onChange,
  onClick,
  selected,
}) => {
  const classes = useStyles();

  const defaultProps = {
    value,
    onChange,
    onClick,
    className: classes.tab,
    key: value,
    selected,
    label: (
      <Typography
        color="primary"
        variant="button"
        className={clsx({ [classes.notSelected]: !selected })}
      >
        {label}
      </Typography>
    ),
    "data-testid": value,
  };

  return to ? (
    <MaterialTab
      {...defaultProps}
      component={NavLink}
      to={to}
      color="inherit"
    />
  ) : (
    <MaterialTab {...defaultProps} color="inherit" />
  );
};
