import React from "react";
import { AppDispatch } from "../../../App.reducer";
import {
  Box,
  createStyles,
  Grid,
  makeStyles,
  Paper,
  Theme,
} from "@material-ui/core";
import Skeleton from "@material-ui/lab/Skeleton";
import { paths } from "../../../utils/common";
import { ContainerContent } from "../../atoms/ContainerContent";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paper: {
      width: "100%",
      height: "100%",
    },
  })
);

interface PageAgreementLoadingProps {
  dispatch: AppDispatch;
  breadCrumb?: string;
}

export const PageAgreementLoading: React.FC<PageAgreementLoadingProps> = ({
  dispatch,
  breadCrumb = "New",
}) => {
  const classes = useStyles();
  return (
    <React.Fragment>
      <ContainerAppBar
        dispatch={dispatch}
        parentTitle="Agreements"
        parentTo={paths.AGREEMENTS}
        title={breadCrumb}
        secondaryContent={
          <Box pt={2} pb={9}>
            <Grid container spacing={2}>
              <Grid item sm={8} xs={12}>
                <Skeleton variant="text" height={40} />
              </Grid>
            </Grid>
          </Box>
        }
      />
      <Paper
        square
        className={classes.paper}
        data-testid="agreement-loading-page"
      >
        <Box
          component={ContainerContent}
          display="inline-flex"
          pt={3}
          width={1}
        >
          <Grid container spacing={2} alignItems="center">
            <Grid item>
              <Skeleton variant="circle" width={40} height={40} />
            </Grid>
            <Grid item xs={10}>
              <Skeleton variant="text" height={30} />
            </Grid>
          </Grid>
        </Box>
      </Paper>
    </React.Fragment>
  );
};
