import React from "react";
import { FieldProps, getIn } from "formik";
import { format } from "date-fns";
import { ButtonBase, Typography } from "@material-ui/core";
import { DatePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import { AgreementFormValues } from "../FormAgreement";
import { ChevronLeft24, ChevronRight24 } from "@carbon/icons-react";

interface FormikTableDateSelectProps {
  disabled?: boolean;
}

export const FormikTableDateSelect: React.FC<
  FormikTableDateSelectProps & FieldProps<any, AgreementFormValues>
> = ({ form: { values, setFieldValue }, field: { name }, disabled }) => {
  const value: string | undefined = getIn(values, name);

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <DatePicker
        value={value ? new Date(value) : undefined}
        onChange={(value) => {
          value &&
            !isNaN(value?.getTime()) &&
            setFieldValue(name, value.toISOString());

          if (!value) setFieldValue(name, undefined);
        }}
        rightArrowIcon={<ChevronRight24 />}
        leftArrowIcon={<ChevronLeft24 />}
        TextFieldComponent={(props) => (
          <ButtonBase
            disabled={disabled}
            onClick={(e: any) => {
              if (props && props.onClick) {
                props.onClick(e);
              }
            }}
          >
            <Typography variant="body1">
              {value && format(new Date(value), "dd/MM/yyyy")}
            </Typography>
          </ButtonBase>
        )}
      />
    </MuiPickersUtilsProvider>
  );
};
