import { CloudDownload20 } from "@carbon/icons-react";
import { Button, createStyles, makeStyles, Theme } from "@material-ui/core";
import React from "react";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    button: {
      height: 36,
    },
  })
);

interface ButtonDownloadCwrProps {
  onClick?: () => void;
}

export const ButtonDownloadCwr: React.FC<ButtonDownloadCwrProps> = ({
  onClick,
}) => {
  const classes = useStyles();

  if (!onClick) {
    return null;
  }

  return (
    <Button
      className={classes.button}
      variant="outlined"
      startIcon={<CloudDownload20 />}
      onClick={onClick}
    >
      Download CWR
    </Button>
  );
};
