import React, { useState } from "react";
import { AppDispatch, AppDispatchEffect } from "../../../App.reducer";
import {
  _TemplatesListLinks,
  BootstrapAccount,
  BootstrapCompany,
  Ipi,
  Template,
  WorkCreator,
  WorkPublisher,
} from "../../../utils/models";
import {
  hasBeenRecorderCodeNames,
  MusicalCategoryCodeNames,
  WorkRegistrationErrorType,
  WorkTemplateNavigationTabs,
} from "../../../types/enums";
import {
  Box,
  Button,
  createStyles,
  Grid,
  makeStyles,
  Paper,
  Theme,
} from "@material-ui/core";
import { Field, FieldProps, Form, Formik, FormikHelpers } from "formik";
import * as H from "history";
import {
  addUserToCreatorsList,
  convertToValueLabel,
  preventEnterOnKeyDown,
  scrollToFormErrors,
} from "../../../utils/helpers";
import { WorkSchemaJSON } from "../../../validationSchema/models";
import { buildSaveValidationSchema } from "../FormWorkRegistration/component.validator";
import {
  postTemplateEffect,
  updateTemplateEffect,
} from "../../pages/Templates/effects";
import { addAlertAction } from "../../pages/Alerts/actions";
import { AlertType } from "../../pages/Alerts/actions.types";
import { paths } from "../../../utils/common";
import { TabCreators } from "../../molecules/TabCreators";
import { LeavingGuard } from "../LeavingGuard";
import { TabsNavigation } from "../../molecules/TabsNavigation";
import { WorkTitleInput } from "../../molecules/WorkTitleInput";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { TabDetails } from "../../molecules/TabDetails";
import { FormikAddWorkWriter } from "../FormikAddWorkWriter";
import { ContainerAppBar } from "../../atoms/ContainerAppBar";
import { TabWorkAgreements } from "../TabWorkAgreements";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    form: {
      height: "100%",
      display: "flex",
      flexDirection: "column",
    },
    appBarContent: {
      width: "100%",
      marginTop: theme.spacing(5),
      display: "inline-flex",
      alignItems: "baseline",
      justifyContent: "space-between",
      transition: theme.transitions.create(["padding"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    buttonLargeMargin: {
      marginLeft: theme.spacing(3),
    },
    formControl: {
      width: "100%",
    },
    durationPickerFix: {
      position: "relative",
      top: -2,
    },
    buttonsContainer: {
      display: "flex",
      alignItems: "flex-end",
      justifyContent: "flex-end",
      marginBottom: theme.spacing(-1),
      marginLeft: "auto",
    },
    content: {
      position: "relative",
      height: "100%",
      overflow: "auto",
    },
    containerSpacingFix: {
      marginBottom: 0,
    },
    tabWrapper: {
      display: "none",
      height: "100%",
    },
    tabVisible: {
      display: "flex",
      flexDirection: "column",
    },
  })
);

interface TemplateProps {
  dispatch: AppDispatch;
  dispatchEffect: AppDispatchEffect;
  history: H.History;
  template?: Template;
  validationSchema?: WorkSchemaJSON;
  account?: BootstrapAccount;
  ipiList: Ipi[];
  isPublisher?: boolean;
  isCreator?: boolean;
  company?: BootstrapCompany;
  templateListItemsLinks: _TemplatesListLinks;
}

export interface TemplateFormValues {
  title: string;
  creators: WorkCreator[];
  publishers: WorkPublisher[];
  hasBeenRecorded?: boolean;
  grandRightsIndicator?: boolean;
  firstRefusalRecordingIndicator?: boolean;
  musicalDistributionCategory?: string;
  mechanicalSplitSum?: string;
  performanceSplitSum?: string;
}

export const FormWorkTemplate: React.FC<TemplateProps> = ({
  dispatch,
  dispatchEffect,
  history,
  template,
  validationSchema,
  account,
  ipiList,
  isPublisher,
  company,
  isCreator,
  templateListItemsLinks,
}) => {
  const classes = useStyles();
  const isEditPage = Boolean(template?.guid);
  const [activeTab, setActiveTab] = useState<WorkTemplateNavigationTabs>(
    WorkTemplateNavigationTabs.PARTIES
  );
  const editable = Boolean(template?._links.update || !template?.guid);
  const initialValues: TemplateFormValues = {
    title: "",
    creators: isPublisher
      ? []
      : addUserToCreatorsList(
          account,
          ipiList.find((el) => el.default)
        ),
    publishers: [],
    hasBeenRecorded: false,
    grandRightsIndicator: false,
    firstRefusalRecordingIndicator: false,
    musicalDistributionCategory: "UNC",
  };

  const templateToFormData: (template: Template) => TemplateFormValues = (
    template: Template
  ) => {
    return {
      title: template.title,
      creators: template.creators || [],
      publishers: template.publishers || [],
      hasBeenRecorded: template.hasBeenRecorded,
      grandRightsIndicator: template.grandRightsIndicator,
      firstRefusalRecordingIndicator: template.firstRefusalRecordingIndicator,
      musicalDistributionCategory: template.musicalDistributionCategory,
    };
  };

  const ipiSearchCreatorsLink =
    template?._links.ipiSearchCreators?.href ||
    templateListItemsLinks.ipiSearchCreators?.href ||
    "";

  const ipiSearchPublishersLink =
    template?._links.ipiSearchPublishers?.href ||
    templateListItemsLinks.ipiSearchPublishers?.href ||
    "";

  const handleRedirectToTemplate = (guid: string) => {
    history.push({
      pathname: `${paths.WORK_TEMPLATE}/${guid}`,
      search: history.location.search,
    });
  };

  const onSubmit = async (
    values: TemplateFormValues,
    { resetForm, setStatus }: FormikHelpers<TemplateFormValues>,
    forLeavingGuard?: boolean
  ) => {
    try {
      let currentTemplate = template;
      if (!template?.guid) {
        currentTemplate = await dispatchEffect(
          postTemplateEffect({ title: values.title })
        );
        if (currentTemplate?.guid) {
          handleRedirectToTemplate(currentTemplate.guid);
        }
      }
      if (currentTemplate) {
        const newFormData = await dispatchEffect(
          updateTemplateEffect(currentTemplate.guid, values)
        );
        dispatch(
          addAlertAction(
            AlertType.SUCCESS,
            `Template is successfully ${isEditPage ? "updated" : "created"}.`
          )
        );
        await resetForm({ values: templateToFormData(newFormData.template) });
        if (!isEditPage && !forLeavingGuard) {
          handleRedirectToTemplate(currentTemplate.guid);
        }
      }
    } catch (e) {
      if (e === WorkRegistrationErrorType.SAVE_ERROR_IPI) {
        setStatus(WorkRegistrationErrorType.SAVE_ERROR_IPI);
      }
      if (forLeavingGuard) {
        throw e;
      }
    }
  };

  return (
    <Formik
      initialValues={template ? templateToFormData(template) : initialValues}
      validationSchema={buildSaveValidationSchema({
        schema: validationSchema,
      })}
      onSubmit={onSubmit}
    >
      {(formikProps) => {
        const {
          values,
          setFieldValue,
          isSubmitting,
          dirty,
          errors,
        } = formikProps;
        return (
          <Form
            className={classes.form}
            onKeyDown={preventEnterOnKeyDown}
            data-testid="template-form"
          >
            {ipiSearchCreatorsLink && activeTab === "parties" && (
              <Field>
                {(props: FieldProps) => (
                  <FormikAddWorkWriter
                    {...props}
                    apiSource={ipiSearchCreatorsLink}
                  />
                )}
              </Field>
            )}
            <ContainerAppBar
              dispatch={dispatch}
              title={isEditPage ? "Edit Template" : "New Template"}
              parentTitle="Works"
              parentTo={paths.WORKS_TEMPLATE_LIST}
              secondaryContent={
                <Box pt={4} pb={0}>
                  <Grid container className={classes.containerSpacingFix}>
                    <WorkTitleInput placeholder="Template Title" />
                    <Box className={classes.appBarContent}>
                      <TabsNavigation<WorkTemplateNavigationTabs>
                        addToUrl
                        activeTab={activeTab}
                        setActiveTab={setActiveTab}
                        tabs={[
                          {
                            label: "Parties",
                            value: WorkTemplateNavigationTabs.PARTIES,
                          },
                          {
                            label: "Details",
                            value: WorkTemplateNavigationTabs.DETAILS,
                          },
                          {
                            label: "Agreements",
                            value: WorkTemplateNavigationTabs.AGREEMENTS,
                            disable: isCreator,
                          },
                        ]}
                      />
                      <Box display="inline-flex">
                        <Button
                          onClick={() =>
                            history.push(paths.WORKS_TEMPLATE_LIST)
                          }
                        >
                          Cancel
                        </Button>
                        <ButtonSubmit
                          variant="contained"
                          color="primary"
                          className={classes.buttonLargeMargin}
                          disabled={isEditPage && !dirty}
                          submitting={isSubmitting}
                          onClick={() => {
                            scrollToFormErrors(errors);
                          }}
                        >
                          {isEditPage ? "Save" : "Create"}
                        </ButtonSubmit>
                      </Box>
                    </Box>
                  </Grid>
                </Box>
              }
            />
            <Box className={classes.content}>
              <Paper
                square
                className={clsx(classes.tabWrapper, {
                  [classes.tabVisible]:
                    activeTab === WorkTemplateNavigationTabs.PARTIES,
                })}
              >
                <TabCreators
                  ipiSearchPublishersApi={ipiSearchPublishersLink}
                  isTemplate
                  publishers={values.publishers}
                  hideSplitColumn
                  hideCollaborationColumn
                  creators={values.creators}
                  setFieldValue={setFieldValue}
                  isSubmitting={isSubmitting}
                  dispatchEffect={dispatchEffect}
                  ipiList={ipiList}
                  isPublisher={isPublisher}
                  isCreator={isCreator}
                  company={company}
                  account={account}
                  dispatch={dispatch}
                />
              </Paper>
              <Paper
                square
                className={clsx(classes.tabWrapper, {
                  [classes.tabVisible]:
                    activeTab === WorkTemplateNavigationTabs.DETAILS,
                })}
              >
                <TabDetails
                  isTemplatePage
                  editable
                  distributionCategories={convertToValueLabel(
                    MusicalCategoryCodeNames
                  )}
                  hasBeenRecordedOptions={convertToValueLabel(
                    hasBeenRecorderCodeNames
                  )}
                />
              </Paper>
              <Paper
                square
                className={clsx(classes.tabWrapper, {
                  [classes.tabVisible]:
                    activeTab === WorkTemplateNavigationTabs.AGREEMENTS,
                })}
              >
                {!isCreator && (
                  <TabWorkAgreements
                    editable={editable}
                    workFormValues={values}
                    setFieldValue={setFieldValue}
                  />
                )}
              </Paper>
            </Box>
            <LeavingGuard
              when={dirty && !isSubmitting}
              navigate={(path) => history.push(path)}
              shouldBlockNavigation={(location) => {
                // Ignore search params and only check pathname
                return location.pathname !== history.location.pathname;
              }}
              onSubmitPromise={() => onSubmit(values, formikProps, true)}
            />
          </Form>
        );
      }}
    </Formik>
  );
};
