import {
  createStyles,
  Grid,
  ListItem,
  makeStyles,
  Theme,
} from "@material-ui/core";
import React from "react";
import clsx from "clsx";

export const useCreatorRowListItemStyles = makeStyles((theme: Theme) =>
  createStyles({
    listItemRow: {
      position: "relative",
      padding: theme.spacing(2.5, 0),
    },
    noPadding: {
      paddingTop: 0,
      paddingBottom: 0,
    },
    rowGutter: {
      minHeight: 40,
    },
  })
);

interface RowCreatorListItemProps {
  className?: string;
  listClassName?: string;
  button?: boolean;
  onClick?: () => void;
  disableRipple?: boolean;
  noPadding?: boolean;
}

export const RowCreatorListItem: React.FC<RowCreatorListItemProps> = ({
  className,
  listClassName,
  children,
  button,
  onClick,
  disableRipple,
  noPadding,
}) => {
  const classes = useCreatorRowListItemStyles();

  // 'disableRipple' prop can't be passed conditionally when 'button' prop is also conditional
  const disableRippleProp = button
    ? {
        disableRipple,
      }
    : {};

  return (
    <ListItem
      {...disableRippleProp}
      button={button as true} //material-ui accepts only "true" attribute here. So can't pass like this: !!button
      onClick={() => onClick && onClick()}
      className={clsx(listClassName, classes.listItemRow, {
        [classes.noPadding]: noPadding,
      })}
    >
      <Grid
        container
        wrap="nowrap"
        className={clsx(classes.rowGutter, className)}
      >
        {children}
      </Grid>
    </ListItem>
  );
};
