import * as yup from "yup";
import { WorkSchemaJSON } from "../../../validationSchema/models";
import { addValidationKeyForRequiredEnum } from "../../../utils/helpers";
import { BootstrapCompany, WorkCreator } from "../../../utils/models";

const { buildYup } = require("json-schema-to-yup");

const shareValidation = (required?: boolean) => {
  const validation = yup
    .number()
    .min(0, "Min is 0%")
    .max(100, "Max is 100%")
    .transform((value) => (isNaN(value) ? undefined : value));

  if (required) {
    return validation.required("Required");
  }

  return validation;
};

export const maxSharesValidation = ({
  message,
  key,
  rule,
}: {
  message: string;
  key: "mrShare" | "prShare";
  rule: (share: number) => boolean;
}) =>
  yup.mixed().when(["creators"], function (creators: WorkCreator[]) {
    const shareSum = creators.reduce((acc, val) => acc + (val[key] || 0), 0);
    return yup
      .string()
      .test(
        "splitSumError",
        message,
        () => !(!isNaN(shareSum) && rule(shareSum))
      );
  });

const shareInputValidation = yup.object().shape({
  prShare: shareValidation(),
  mrShare: shareValidation(),
  publishers: yup.array().of(
    yup.object().shape({
      prShare: shareValidation(),
      mrShare: shareValidation(),
    })
  ),
});

const companyPublisherAssociation = (company: BootstrapCompany) =>
  yup.mixed().when(["creators"], function (creators: WorkCreator[]) {
    const creatorAssociatedPublishers = creators
      .map(
        (creator) =>
          creator.publishers?.map((publisher) => publisher.ipiNameNumber) || []
      )
      .flat();
    return yup
      .string()
      .test(
        "companyPublisherError",
        "Initial creator must be associated to the company publisher creating the work",
        () =>
          creatorAssociatedPublishers.includes(Number(company.ipiNameNumber))
      );
  });

export const buildSubmitValidationSchema = ({
  schema,
  company,
  isPublisher,
}: {
  schema?: WorkSchemaJSON;
  company?: BootstrapCompany;
  isPublisher?: boolean;
}) => {
  if (!schema) return;

  const configuredSchema = addValidationKeyForRequiredEnum({
    ...schema.properties.body,
  });

  const requiredMsg = { required: "Required" };

  const build = buildYup(configuredSchema, {
    errMessages: {
      title: {
        matches: "Invalid format",
        required: "Title is required",
        max: (props: any) =>
          `Title must be at most ${props.maxLength} characters`,
      },
      altTitle: {
        matches: "Invalid format",
        required: "Alternative title is required",
        max: (props: any) =>
          `Alternative title must be at most ${props.maxLength} characters`,
      },
      firstReleaseDate: {
        matches: "Invalid format",
        required: "Required",
      },
      duration: requiredMsg,
      musicalDistributionCategory: requiredMsg,
      hasBeenRecorded: requiredMsg,
      firstRefusalRecordingIndicator: requiredMsg,
      grandRightsIndicator: requiredMsg,
    },
  });
  return build.shape({
    mechanicalSplitSum: maxSharesValidation({
      message: "Share fields must be equal to 100%",
      key: "mrShare",
      rule: (share: number) => Math.round(share) !== 100,
    }),
    performanceSplitSum: maxSharesValidation({
      message: "Share fields must be equal to 100%",
      key: "prShare",
      rule: (share: number) => Math.round(share) !== 100,
    }),
    ...(schema.properties.body.required.includes("creator") && {
      creators: yup.array().min(1, "Creator required").of(shareInputValidation),
    }),
    ...(isPublisher &&
      company && {
        companyPublisherError: companyPublisherAssociation(company),
      }),
  });
};

export const buildSaveValidationSchema = ({
  schema,
  company,
  isPublisher,
}: {
  schema?: WorkSchemaJSON;
  company?: BootstrapCompany;
  isPublisher?: boolean;
}) => {
  if (!schema) return;

  const { title, altTitle } = schema.properties.body.properties;

  return yup.object().shape({
    ...(title && {
      title: yup
        .string()
        .required("Title is required")
        .matches(new RegExp(title.pattern || ""), "Title is not valid")
        .max(
          title.maxLength,
          `Title must be at most ${title.maxLength} characters`
        ),
    }),
    ...(altTitle && {
      altTitle: yup
        .string()
        .matches(
          new RegExp(altTitle.pattern || ""),
          "Alternative title is not valid"
        )
        .max(
          altTitle.maxLength,
          `Title must be at most ${altTitle.maxLength} characters`
        ),
    }),
    mechanicalSplitSum: maxSharesValidation({
      message: "Share fields must not exceed 100%",
      key: "mrShare",
      rule: (share: number) => Math.round(share) > 100,
    }),
    performanceSplitSum: maxSharesValidation({
      message: "Share fields must not exceed 100%",
      key: "prShare",
      rule: (share: number) => Math.round(share) > 100,
    }),
    ...(schema.properties.body.required.includes("creator") && {
      creators: yup.array().min(1, "Creator required").of(shareInputValidation),
    }),
    ...(isPublisher &&
      company && {
        companyPublisherError: companyPublisherAssociation(company),
      }),
  });
};
