import { AppState } from "../../../App.reducer";

export const getCountriesSelector = (state: AppState) =>
  state.company.countries;

export const getEmployeesSelector = (state: AppState) =>
  state.company.employees;

export const getCountriesLoadingStateSelector = (state: AppState) =>
  state.company.countriesLoadingState;

export const getEmployeesLoadingStateSelector = (state: AppState) =>
  state.company.employeesLoadingState;

export const getInvitesSelector = (state: AppState) => state.company.invites;

export const getInvitesLoadingStateSelector = (state: AppState) =>
  state.company.invitesLoadingState;

export const getInviteEmployeeLinkSelector = (state: AppState) =>
  state.company._linksEmployees?.inviteEmployee ||
  state.company._linksInvites?.inviteEmployee;

export const getDeleteInviteLinkSelector = (state: AppState) =>
  state.company._linksInvites?.deleteInvite;

export const getResendInviteLinkSelector = (state: AppState) =>
  state.company._linksInvites?.resendInvite;

export const getPatchEmployeeGroupLinkSelector = (state: AppState) =>
  state.company._linksEmployees?.patchGroup;
