import { Link } from "react-router-dom";
import {
  createStyles,
  ListItem,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import React from "react";
import clsx from "clsx";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    navItem: {
      padding: theme.spacing(1.5, 0),
      marginBottom: theme.spacing(2),
      borderRadius: 8,
    },
    link: {
      display: "inline-flex",
      paddingLeft: theme.spacing(2),
      color: theme.palette.info.main,
    },
    selected: {
      color: theme.palette.custom.secondary.green,
    },
  })
);

interface LinkListProps {
  selected: boolean;
  to: string;
}

export const LinkList: React.FC<LinkListProps> = ({
  children,
  selected,
  to,
}) => {
  const classes = useStyles();
  return (
    <ListItem
      data-testid={`Nav ${to}`}
      className={classes.navItem}
      selected={selected}
      button
      color="inherit"
      component={Link}
      to={to}
    >
      <Typography
        className={clsx(classes.link, { [classes.selected]: selected })}
        variant="body1"
      >
        {children}
      </Typography>
    </ListItem>
  );
};
