import React from "react";
import { createStyles, Grid, makeStyles, Theme } from "@material-ui/core";
import { ListColumnTitle } from "../../atoms/ListColumnTitle";
import { RowCreatorListItem } from "../RowCreatorListItem";
import { TooltipMap } from "../../atoms/TooltipMap";
import { GridListHeaderItem } from "../../atoms/GridListHeaderItem";
import { CellCreatorSummary } from "../../atoms/CellCreatorSummary";
import { CellRoleSummary } from "../../atoms/CellRoleSummary";
import { CellPrShareSummary } from "../../atoms/CellPrShareSummary";
import { CellMrShareSummary } from "../../atoms/CellMrShareSummary";
import { CellAvatarSummary } from "../../atoms/CellAvatarSummary";

export const useHeaderStyles = makeStyles((theme: Theme) =>
  createStyles({
    rowGutterClass: {
      paddingRight: theme.spacing(8.5),
      paddingLeft: theme.spacing(2),
    },
  })
);

export const HeaderCreatorListSummary: React.FC = () => {
  const classes = useHeaderStyles();

  return (
    <RowCreatorListItem className={classes.rowGutterClass} noPadding>
      <Grid container>
        <CellAvatarSummary header />
        <CellCreatorSummary header>
          <ListColumnTitle>Party</ListColumnTitle>
        </CellCreatorSummary>
        <CellRoleSummary header>
          <ListColumnTitle>Role</ListColumnTitle>
        </CellRoleSummary>
        <CellPrShareSummary header>
          <ListColumnTitle align="left">
            <TooltipMap to="PerformingLabel">PR Share</TooltipMap>
          </ListColumnTitle>
        </CellPrShareSummary>
        <CellMrShareSummary header>
          <ListColumnTitle align="right">
            <TooltipMap to="MechanicalLabel">MR Share</TooltipMap>
          </ListColumnTitle>
        </CellMrShareSummary>
        <GridListHeaderItem xs={1} />
      </Grid>
    </RowCreatorListItem>
  );
};
