import React, { useRef, useState } from "react";
import {
  Box,
  Grid,
  IconButton,
  InputAdornment,
  Typography,
} from "@material-ui/core";
import { Field, FieldProps, useFormikContext } from "formik";
import { TextField } from "formik-material-ui";
import CloseIcon from "@material-ui/icons/Close";
import {
  SalesOrManufacture,
  UsaLicenseIndicatorType,
} from "../../../types/enums";
import { FormikSwitch } from "../FormikSwitch";
import { ContainerContent } from "../../atoms/ContainerContent";
import { DialogWarning } from "../../molecules/DialogWarning";
import { AgreementFormValues } from "../FormAgreement";
import { FormikRadioSelect } from "../FormikRadioSelect";
import { FormikMenuSelect } from "../../molecules/FormikMenuSelect";
import { FormikAddAgreementNumber } from "../../atoms/FormikAddAgreementNumber";
import { TooltipMap } from "../../atoms/TooltipMap";

interface TabGeneralProps {
  formValues: AgreementFormValues;
  disabled?: boolean;
}

export const TabGeneral: React.FC<TabGeneralProps> = ({
  formValues,
  disabled,
}) => {
  const { submitterAgreementNumbers, libraryMusic } = formValues;
  const [showConfirm, setShowConfirm] = useState(false);
  const [deleteAgreementNumberIndex, setDeleteAgreementNumberIndex] = useState<
    undefined | number
  >(undefined);
  const { setFieldValue } = useFormikContext();

  const recordingPrefixRef = useRef<HTMLInputElement>();

  const handleClickDeleteAgreementNumber = (index: number) => () => {
    setShowConfirm(true);
    setDeleteAgreementNumberIndex(index);
  };
  const handleConfirm = () => {
    const newState = submitterAgreementNumbers.filter(
      (_, index) => index !== deleteAgreementNumberIndex
    );
    setFieldValue("submitterAgreementNumbers", newState);
    handleCancel();
  };
  const handleCancel = () => {
    setShowConfirm(false);
    setDeleteAgreementNumberIndex(undefined);
  };

  return (
    <React.Fragment>
      {showConfirm && (
        <DialogWarning
          onConfirm={handleConfirm}
          onCancel={handleCancel}
          message="You are about to delete this Agreement Number"
          width={358}
          labelCancel="Cancel"
          labelConfirm="Remove"
        />
      )}
      <Grid container spacing={3} component={ContainerContent}>
        <Box width={1} pt={8} pb={3} pl={1.5}>
          <Typography variant="h6">General</Typography>
        </Box>

        <Grid item xs={12} sm={6}>
          <TooltipMap to="AgreementSubmitterNumber">
            <Field
              component={TextField}
              disabled={disabled}
              name="submitterAgreementNumbers.0"
              label="Submitter Agreement Number"
              variant="filled"
              fullWidth
            />
          </TooltipMap>
        </Grid>

        {submitterAgreementNumbers.map(
          (el, index) =>
            index !== 0 && (
              <Grid item xs={12} sm={6} key={`agreementNumbers.${index}`}>
                <TooltipMap to="AgreementNumber">
                  <Field
                    component={TextField}
                    name={`submitterAgreementNumbers.${index}`}
                    label="Agreement Number"
                    variant="filled"
                    fullWidth
                    disabled={disabled}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton
                            disabled={disabled}
                            onClick={handleClickDeleteAgreementNumber(index)}
                          >
                            <CloseIcon />
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                  />
                </TooltipMap>
              </Grid>
            )
        )}

        <Box width={1} mt={-1} pb={1.5} pl={1.5}>
          <Field
            component={FormikAddAgreementNumber}
            disabled={disabled}
            name={`submitterAgreementNumbers.${submitterAgreementNumbers.length}`}
          />
        </Box>

        <Grid item xs={12} sm={6}>
          <Field
            component={FormikRadioSelect}
            labelTooltip="AgreementPreviouslyRegisteredWorks"
            name="bulkTransferWorks"
            label="Previously Registered Works"
            disabled={disabled}
            options={[
              { label: "Transfer to Agreement", value: true },
              { label: "Dont Transfer", value: false },
            ]}
          />
        </Grid>

        <Grid item xs={12} sm={3}>
          <Field
            component={FormikRadioSelect}
            name="agreementType"
            labelTooltip="AgreementType"
            label="Type of Agreement"
            disabled={disabled}
            options={[
              { label: "General", value: "general" },
              { label: "Specific", value: "specific", disabled: true },
            ]}
          />
        </Grid>

        <Grid item xs={12} sm={3}>
          <Field
            component={FormikRadioSelect}
            labelTooltip="AgreementRightsAcquiredFor"
            name="salesOrManufacture"
            label="Rights Acquired For"
            disabled={disabled}
            options={[
              { label: "Sales", value: SalesOrManufacture.SALES },
              { label: "Manufacture", value: SalesOrManufacture.MANUFACTURE },
            ]}
          />
        </Grid>

        <Box width={1} pt={5} pl={1.5} pb={2.5}>
          <Typography variant="h5">Additional</Typography>
        </Box>

        <Grid item xs={12} sm={6}>
          <TooltipMap to="AgreementLibraryMusic">
            <Field name="libraryMusic">
              {(props: FieldProps) => (
                <FormikSwitch
                  label="Library Music"
                  disabled={disabled}
                  onChange={(value) => {
                    if (value) {
                      setTimeout(() => {
                        // can't focus elements straight away because it has appearing
                        // animation and textField is still non-focusable
                        recordingPrefixRef.current?.focus();
                      }, 50);
                    }
                  }}
                  {...props}
                />
              )}
            </Field>
          </TooltipMap>
        </Grid>
        <Grid item xs={12} sm={6}>
          <TooltipMap to="AgreementRecordingPrefix">
            <Field
              component={TextField}
              disabled={!libraryMusic || disabled || undefined}
              name="recordingPrefix"
              label="Recording Prefix"
              variant="filled"
              fullWidth
              inputRef={recordingPrefixRef}
            />
          </TooltipMap>
        </Grid>

        <Grid item xs={12} sm={6}>
          <TooltipMap to="AgreementUSALicenseIndicator">
            <Field
              component={FormikMenuSelect}
              name="usaLicenseIndicator"
              label="USA License Indicator"
              hideValues
              disabled={disabled}
              options={[
                { label: "None", value: UsaLicenseIndicatorType.NONE },
                { label: "ASCAP 010", value: UsaLicenseIndicatorType.ASCAP },
                { label: "BMI 021", value: UsaLicenseIndicatorType.BMI },
                { label: "SESAC 071", value: UsaLicenseIndicatorType.CISAC },
              ]}
            />
          </TooltipMap>
        </Grid>
      </Grid>
    </React.Fragment>
  );
};
