import * as yup from "yup";

export const agreementValidationSchema = yup.object().shape({
  title: yup.string().required("Title is required"),
  terms: yup.array().of(
    yup.object().shape({
      shares: yup.array().of(
        yup.object().shape({
          publisherShareRetained: yup
            .number()
            .test(
              "is-100",
              "Share sum must be equal to 100%",
              function (value) {
                return (
                  value +
                    this.parent.writerShareRetained +
                    this.parent.assigneeCollectibleShare ===
                  100
                );
              }
            ),
          writerShareRetained: yup
            .number()
            .test(
              "is-100",
              "Share sum must be equal to 100%",
              function (value) {
                return (
                  this.parent.publisherShareRetained +
                    this.parent.assigneeCollectibleShare +
                    value ===
                  100
                );
              }
            ),

          assigneeCollectibleShare: yup
            .number()
            .test(
              "is-100",
              "Share sum must be equal to 100%",
              function (value) {
                return (
                  this.parent.publisherShareRetained +
                    this.parent.writerShareRetained +
                    value ===
                  100
                );
              }
            ),
        })
      ),
    })
  ),
});
