import { SettingsTheme, Theme } from "../../../utils/models";
import { URL_NAMESPACE } from "../../../api.config";
import { SETTINGS_SCOPE } from "../../../types/enums";
import { AppState } from "../../../App.reducer";
import { getCompanyOrganizationKeySelector } from "./selectors";
import { ThemeState } from "./types";

const THEME_ID_PREFIX = "theme-";

// https://gist.github.com/lopspower/03fb1cc0ac9f32ef38f4
export const getHexColorWithOpacity = (color: string, p: number) => {
  const intValue = Math.round((p / 100) * 255);
  const hexValue = intValue.toString(16);
  return `${color}${hexValue.padStart(2, "0").toUpperCase()}`;
};

export const getNewThemeId = (themes: SettingsTheme[]): string => {
  const ids = themes.map((theme) =>
    Number(theme.data.id.replace(THEME_ID_PREFIX, ""))
  );
  if (ids.length > 0) {
    return `${THEME_ID_PREFIX}${Math.max.apply(null, ids) + 1}`;
  }
  return `${THEME_ID_PREFIX}${0}`;
};

