export const createInitialSettingTheme: (theme: Theme) => SettingsTheme = (
  theme: Theme
) => ({
  data: theme,
  version: "version-placeholder",
  scope: SETTINGS_SCOPE.THEME,
  namespace: URL_NAMESPACE,
});

export const getOrganizationNamespace: (state: AppState) => string | null = (
  state: AppState
) => {
  const orgKey = getCompanyOrganizationKeySelector(state);
  if (!orgKey) {
    return null;
  }
  return `org-${orgKey}`;
};
