import React from "react";
import {
  Button,
  DialogActions,
  DialogContent,
  DialogTitle,
  Typography,
} from "@material-ui/core";
import { Dialog } from "../Dialog";
import { createStyles, makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles(() =>
  createStyles({
    dialog: {
      maxWidth: 560,
    },
  })
);

interface SignUpCheckEmailProps {
  show: boolean;
  onClose: () => void;
}

export const SignUpCheckEmail: React.FC<SignUpCheckEmailProps> = ({
  show,
  onClose,
}) => {
  const classes = useStyles();
  return (
    <Dialog open={show} variant="simple" classes={{ paper: classes.dialog }}>
      <DialogTitle>Email Verification</DialogTitle>
      <DialogContent>
        <Typography variant="body1" color="textSecondary">
          We have sent you an email to confirm your address. Please check your
          inbox and click the link to verify. Once verified, you can sign in.
        </Typography>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Close</Button>
      </DialogActions>
    </Dialog>
  );
};
