import { ThunkAction, ThunkDispatch } from "redux-thunk";
import Promise from "promise-polyfill";
import { AppState } from "../../../App.reducer";
import { Action } from "redux";
import axios, { Method } from "axios";
import {
  confirmEmailChangeAction,
  confirmEmailChangeFailureAction,
  confirmEmailChangeSuccessAction,
  fetchProfileAction,
  fetchProfileFailureAction,
  fetchProfileSuccessAction,
} from "../SignIn/actions";
import { addAlertAction } from "../Alerts/actions";
import { AlertType } from "../Alerts/actions.types";
import { handleError } from "../../../utils/errorHandler";
import { toasts } from "../../../utils/toasts";
import { getLegalProfileLinkSelector } from "../SignIn/selectors";
import { FetchLegalProfileResponse } from "../../../api.types";
import { fetchLegalProfileResponse } from "../../../mocks";

export const confirmEmailChangeEffect =
  (
    url: string,
    method: Method,
    code: string
  ): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, state) => {
    dispatch(confirmEmailChangeAction());
    try {
      await axios.request({
        method,
        url,
        data: {
          code,
        },
        withCredentials: true,
      });
      dispatch(confirmEmailChangeSuccessAction());
      dispatch(
        addAlertAction(AlertType.SUCCESS, toasts.success.auth.CHANGE_EMAIL)
      );
    } catch (e) {
      const error = handleError(e.response);
      dispatch(confirmEmailChangeFailureAction());
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.auth.CHANGE_EMAIL} ${error.message}`
        )
      );
    }
  };

export const fetchLegalProfileEffect =
  (): ThunkAction<Promise<void>, AppState, void, Action<string>> =>
  async (dispatch: ThunkDispatch<AppState, void, Action<string>>, getState) => {
    dispatch(fetchProfileAction());
    const link = getLegalProfileLinkSelector(getState());
    try {
      // const response = await axios.request<FetchLegalProfileResponse>({
      //   url: link?.href,
      //   method: link?.method,
      //   withCredentials: true,
      // });
      dispatch(
        fetchProfileSuccessAction(
          fetchLegalProfileResponse.response.profile.item
        )
      );
    } catch (e) {
      dispatch(fetchProfileFailureAction());
      const error = handleError(e.response);
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.profile.GET_PROFILE} ${error.message}`
        )
      );
    }
  };
