import React, { useState } from "react";
import {
  Button,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@material-ui/core";
import { AppDispatchEffect } from "../../../App.reducer";
import { leaveWorkEffect } from "../../pages/Works/effects";
import { Work } from "../../../utils/models";
import { ButtonSubmit } from "../../atoms/ButtonSubmit";
import { Dialog } from "../Dialog";

interface DialogLeaveWorkProps {
  showDialog: boolean;
  hideDialog: () => void;
  onLeave?: () => void;
  dispatchEffect: AppDispatchEffect;
  workGuid?: string;
  ipiReference?: string;
  work?: Work;
  isPublisher?: boolean;
}

const creatorMessage =
  "By leaving this work you remove your creator data, including\n" +
  " associated shares, and it will no longer appear in your work lists.";
const publisherMessage =
  "By leaving this work you remove your publisher data, including\n" +
  " associated shares, and it will no longer appear in your work lists.";

export const DialogLeaveWork: React.FC<DialogLeaveWorkProps> = ({
  showDialog,
  hideDialog,
  workGuid,
  ipiReference,
  dispatchEffect,
  onLeave,
  work,
  isPublisher,
}) => {
  const [loading, setLoading] = useState(false);

  const handleSuccess = () => {
    setLoading(false);
    hideDialog();
    onLeave && onLeave();
  };
  const handleFail = () => setLoading(false);
  const handleClose = () => hideDialog();

  const handleConfirm = async () => {
    if ((work?.guid || workGuid) && work?._links.leave) {
      setLoading(true);
      try {
        await dispatchEffect(
          leaveWorkEffect({
            link: work._links.leave,
            ipiReference: ipiReference,
            workGuid: work?.guid || workGuid || "",
          })
        );
        handleSuccess();
      } catch (e) {
        handleFail();
      }
    } else {
      handleSuccess();
    }
  };

  return (
    <Dialog variant="simple" open={showDialog} onClose={handleClose}>
      <DialogTitle>Leave work?</DialogTitle>
      <DialogContent>
        <DialogContentText>
          {isPublisher ? publisherMessage : creatorMessage}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
        <ButtonSubmit
          submitting={loading}
          onClick={handleConfirm}
          variant="text"
          data-testid="submit-leave-work"
        >
          Leave Work
        </ButtonSubmit>
      </DialogActions>
    </Dialog>
  );
};
