import React from "react";
import {
  Box,
  createStyles,
  FormControl,
  Grid,
  InputLabel,
  makeStyles,
  MenuItem,
  Theme,
} from "@material-ui/core";
import { Field, FieldProps } from "formik";
import { Select, TextField } from "formik-material-ui";
import { enumKeys } from "../../../utils/helpers";
import { FormikTimePicker } from "../../atoms/FormikTimePicker";
import { ContainerContent } from "../../atoms/ContainerContent";
import { FormikRadioSelect } from "../../organisms/FormikRadioSelect";
import { FormikDatePicker } from "../../organisms/FormikDatePicker";
import { FormikMenuSelect } from "../FormikMenuSelect";
import { TooltipMap } from "../../atoms/TooltipMap";
import { languageCodeNames } from "../../../types/enums";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    emptyGrid: {
      "&.MuiGrid-item": {
        paddingTop: 0,
        paddingBottom: 0,
      },
    },
    spacing: {
      "&.MuiGrid-item": {
        paddingTop: theme.spacing(3.25),
      },
    },
  })
);

interface TabDetailsProps {
  altLanguageCodeRequired?: boolean;
  distributionCategories?: Array<{ value: string; label?: string }>;
  hasBeenRecordedOptions?: Array<{ value: string; label?: string }>;
  languageCodes?: { [index: string]: string };
  isTemplatePage?: boolean;
  editable?: boolean;
}

export const TabDetails: React.FC<TabDetailsProps> = ({
  altLanguageCodeRequired,
  distributionCategories,
  languageCodes,
  isTemplatePage,
  editable,
}) => {
  const classes = useStyles();
  return (
    <React.Fragment>
      <ContainerContent>
        <Box pt={7}>
          <Grid container spacing={3}>
            {!isTemplatePage && (
              <React.Fragment>
                <Grid item sm={6} xs={12}>
                  <TooltipMap to="AltTitle">
                    <Field
                      inputProps={{
                        "data-testid": "Alternative Title",
                      }}
                      component={TextField}
                      label="Alternative Title"
                      name="altTitle"
                      variant="filled"
                      fullWidth
                      disabled={editable ? undefined : !editable}
                    />
                  </TooltipMap>
                </Grid>
                <Grid item sm={3} xs={12}>
                  {altLanguageCodeRequired && (
                    <FormControl variant="filled" fullWidth>
                      <InputLabel>Alt. Title Language</InputLabel>
                      <Field
                        component={Select}
                        name="altTitleLanguageCode"
                        disabled={!editable}
                      >
                        {enumKeys(languageCodes).map((key: string) => (
                          <MenuItem key={key} value={key}>
                            {languageCodeNames[key]}
                          </MenuItem>
                        ))}
                      </Field>
                    </FormControl>
                  )}
                </Grid>

                <Grid
                  item
                  sm={3}
                  xs={12}
                  classes={{ item: classes.emptyGrid }}
                />
              </React.Fragment>
            )}
            {!isTemplatePage && (
              <Grid item sm={3} xs={12} className={classes.spacing}>
                <Field
                  variant="filled"
                  fullWidth
                  inputVariant="filled"
                  label="Duration"
                  name="duration"
                  component={FormikTimePicker}
                  disabled={!editable}
                />
              </Grid>
            )}
            <Grid
              item
              sm={isTemplatePage ? 6 : 3}
              xs={12}
              className={classes.spacing}
            >
              <TooltipMap to="DistributionCategory">
                <Field
                  component={FormikMenuSelect}
                  name="musicalDistributionCategory"
                  label="Category"
                  options={distributionCategories}
                  disabled={editable ? undefined : !editable}
                />
              </TooltipMap>
            </Grid>
            <Grid item sm={3} xs={12} className={classes.spacing}>
              {!isTemplatePage && (
                <Field
                  component={FormikDatePicker}
                  tooltip="FirstReleaseDate"
                  clearable
                  inputVariant="filled"
                  fullWidth
                  name="firstReleaseDate"
                  label="First Release Date"
                  disabled={!editable}
                />
              )}
            </Grid>
            <Grid item xs={12} sm={3} classes={{ item: classes.emptyGrid }} />
            <Grid item xs={3} className={classes.spacing}>
              <Field name="hasBeenRecorded">
                {(props: FieldProps) => (
                  <FormikRadioSelect
                    {...props}
                    label="Recorded"
                    labelTooltip="Recorded"
                    options={[
                      { label: "Yes", value: true },
                      { label: "No", value: false },
                    ]}
                    disabled={!editable}
                  />
                )}
              </Field>
            </Grid>
            <Grid item xs={3} className={classes.spacing}>
              {/*TODO:: This field only visible for PRS creator. */}
              <Field name="grandRightsIndicator">
                {(props: FieldProps) => (
                  <FormikRadioSelect
                    {...props}
                    label="Grand Rights"
                    labelTooltip="GrandRights"
                    options={[
                      { label: "Yes", value: true },
                      { label: "No", value: false },
                    ]}
                    disabled={!editable}
                  />
                )}
              </Field>
            </Grid>
            <Grid item xs={3} className={classes.spacing}>
              {/*TODO:: This field only visible for PRS creator. */}
              <Field name="firstRefusalRecordingIndicator">
                {(props: FieldProps) => (
                  <FormikRadioSelect
                    {...props}
                    label="First Recording Refusal"
                    labelTooltip="FirstRecordingRefusal"
                    options={[
                      { label: "Yes", value: true },
                      { label: "No", value: false },
                    ]}
                    disabled={!editable}
                  />
                )}
              </Field>
            </Grid>
          </Grid>
        </Box>
      </ContainerContent>
    </React.Fragment>
  );
};
