import React from "react";
import { Dialog } from "../../molecules/Dialog";
import {
  Box,
  Button,
  createStyles,
  DialogActions,
  DialogContent,
  DialogTitle,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { Field, FieldProps, Form, Formik } from "formik";
import { FormikAutocompleteSelect } from "../../molecules/FormikAutocompleteSelect";
import {
  getOptions,
  getPublisherCreators,
} from "../FormAddWorkAgreementNumber/component.utils";
import { TextField } from "formik-material-ui";
import { WorkFormValues } from "../FormWorkRegistration";
import {
  createSubPublisherAssociation,
  isSubmitButtonDisabled,
} from "./component.utils";
import { WorkAgreementListItem } from "../TabWorkAgreements";
import { WorkCreator } from "../../../utils/models";
import {
  findCreatorAndFormatToOption,
  findPublisherAndFormatToOption,
  removeWorkAgreementAssociation,
  WorkAgreementType,
} from "../TabWorkAgreements/component.utils";
import { TemplateFormValues } from "../FormWorkTemplate";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dialog: {
      width: 528,
    },
    submitButton: {
      width: 106,
    },
    label: {
      padding: theme.spacing(2, 2, 0),
    },
  })
);

type IpiOption = { label: string; ipi: number };

export interface FormValues {
  publisher?: IpiOption;
  creator?: IpiOption;
  agreementNumber: string;
}

interface FormAddWorkSubPublisherAgreementNumberProps {
  open: boolean;
  onClose: () => void;
  workFormValues: WorkFormValues | TemplateFormValues;
  setFieldValue: (name: string, value: any) => void;
  edit?: WorkAgreementListItem;
}

const getInitialValues: (
  workFormValues: WorkFormValues | TemplateFormValues,
  edit?: WorkAgreementListItem
) => FormValues = (workFormValues, edit) => {
  const creator = findCreatorAndFormatToOption(workFormValues.creators, edit);
  const publisher = findPublisherAndFormatToOption(
    workFormValues,
    WorkAgreementType.SUB_PUBLISHER,
    edit
  );
  return {
    creator,
    publisher: publisher && {
      ipi: publisher.ipi,
      label: publisher.label,
    },
    agreementNumber: publisher?.agreementNumber || "",
  };
};

export const FormAddWorkSubPublisherAgreementNumber: React.FC<FormAddWorkSubPublisherAgreementNumberProps> = ({
  open,
  onClose,
  workFormValues,
  setFieldValue,
  edit,
}) => {
  const classes = useStyles();

  const onSubmit = (values: FormValues) => {
    let updatedCreators: WorkCreator[] = workFormValues.creators;

    if (edit) {
      updatedCreators = removeWorkAgreementAssociation(
        updatedCreators,
        edit,
        WorkAgreementType.SUB_PUBLISHER
      );
    }

    const creators = createSubPublisherAssociation(updatedCreators, values);
    setFieldValue("creators", creators);
    onClose();
  };

  return (
    <Dialog
      variant="complex"
      open={open}
      classes={{ paper: classes.dialog }}
      onClose={onClose}
    >
      <Formik
        onSubmit={onSubmit}
        initialValues={getInitialValues(workFormValues, edit)}
      >
        {({ values }) => (
          <Form>
            <DialogTitle>
              {edit ? "Edit" : "Add"} Sub-Publisher Agreement Number
            </DialogTitle>
            <DialogContent>
              <Field name="publisher">
                {(props: FieldProps) => (
                  <FormikAutocompleteSelect<IpiOption>
                    {...props}
                    label="Publisher"
                    options={getOptions(workFormValues, {
                      onlyPublisher: true,
                    })}
                    getOptionSelected={(option, value) =>
                      option?.ipi === value?.ipi
                    }
                    getOptionLabel={(option) => option?.label}
                  />
                )}
              </Field>
              <Typography
                variant="body2"
                color="textSecondary"
                className={classes.label}
              >
                In relation to
              </Typography>
              <Field name="creator">
                {(props: FieldProps) => (
                  <FormikAutocompleteSelect<IpiOption>
                    {...props}
                    label="Creator"
                    options={getPublisherCreators(
                      workFormValues,
                      values.publisher?.ipi
                    )}
                    getOptionSelected={(option, value) =>
                      option?.ipi === value?.ipi
                    }
                    getOptionLabel={(option) => option?.label}
                  />
                )}
              </Field>
              <Box pt={3} pb={2}>
                <Field
                  component={TextField}
                  label="Sub-Publisher Agreement Number"
                  name="agreementNumber"
                  variant="filled"
                  fullWidth
                />
              </Box>
            </DialogContent>
            <DialogActions>
              <Button onClick={onClose}>Cancel</Button>
              <Button
                type="submit"
                color="primary"
                variant="contained"
                className={classes.submitButton}
                disabled={isSubmitButtonDisabled(values)}
              >
                {edit ? "Edit" : "Add"}
              </Button>
            </DialogActions>
          </Form>
        )}
      </Formik>
    </Dialog>
  );
};
