import React, { useState } from "react";
import { RowCreatorListItem } from "../RowCreatorListItem";
import { createStyles, makeStyles, Typography } from "@material-ui/core";
import {
  IdentityType,
  PartyMemberAccessType,
  PartyMemberAccessTypeLabel,
  RelationType,
  RelationTypeLabel,
} from "../../../types/enums";
import { Field, useFormikContext } from "formik";
import { AgreementMember } from "../../../utils/models";
import { AgreementFormPermissions } from "../../organisms/FormAgreement/utils";
import { CellAgreementRelation } from "../../atoms/CellAgreementRelation";
import { CellAgreementPermissions } from "../../atoms/CellAgreementPermissions";
import { DialogWarning } from "../DialogWarning";
import { CellAgreementOptions } from "../../atoms/CellAgreementOptions";
import { ActionMenu } from "../../organisms/ActionMenu";
import { CellAgreementParty } from "../../atoms/CellAgreementParty";
import { FormikEnumSelect } from "../FormikEnumSelect";
import { CardAgreementMember } from "../CardAgreementMember";
import { CellAgreementIpi } from "../../atoms/CellAgreementIPI";
import { TooltipMap } from "../../atoms/TooltipMap";
import { Icon } from "../../atoms/Icon";
import { Theme } from "@material-ui/core/styles";

const useStyles = makeStyles<Theme>((theme) =>
  createStyles({
    row: {
      alignItems: "baseline",
    },
    rowPadding: {
      padding: theme.spacing(2, 0, 0, 0),
    },
    relationSelect: {
      height: 40,
      maxWidth: 184,
      width: "100%",
    },
    permissionSelect: {
      maxWidth: 140,
      width: "100%",
    },
  })
);

const messageView = "Allowing the other party to view is irreversible.";
const messageEdit =
  "You may revoke edit permissions later but this party will always be able to view once shared.";

interface RowGridAgreementMemberProps {
  parent: string;
  type: RelationType;
  member: AgreementMember;
  onDelete: (isLeaving?: boolean) => void;
  owner: boolean;
  restrictAccessChange: () => boolean;
  onRoleChange: (role: string) => void;
  permissions: AgreementFormPermissions;
  disableRelationOptions: RelationType[];
}

export const RowGridAgreementMember: React.FC<RowGridAgreementMemberProps> = ({
  parent,
  member,
  owner,
  onDelete,
  restrictAccessChange,
  onRoleChange,
  permissions,
  type,
  disableRelationOptions,
}) => {
  const classes = useStyles();
  const { setFieldValue } = useFormikContext();
  const [showConfirm, setShowConfirm] = useState(false);
  const [message, setMessage] = useState("");
  const [tempAccess, setTempAccess] = useState<PartyMemberAccessType>();
  const disableAccessNone = restrictAccessChange();

  const handleConfirm = () => {
    if (tempAccess) {
      const permissions = {
        ...member.permissions,
        read: [
          PartyMemberAccessType.READ,
          PartyMemberAccessType.WRITE,
        ].includes(tempAccess),
        write: tempAccess === PartyMemberAccessType.WRITE,
      };
      setFieldValue(`${parent}.permissions`, permissions);
    }
    setShowConfirm(false);
  };

  const handleCancel = () => {
    setShowConfirm(false);
  };

  const onAccessChange = (value: PartyMemberAccessType) => {
    setTempAccess(value);
    if (value === PartyMemberAccessType.READ) {
      setMessage(messageView);
      setShowConfirm(true);
    } else if (value === PartyMemberAccessType.WRITE) {
      setMessage(messageEdit);
      setShowConfirm(true);
    } else {
      setFieldValue(`${parent}.permissions`, {
        owner: member.permissions.owner,
        read: false,
        write: false,
      });
    }
  };

  const getPermissionValue = ({
    permissions: { read, write },
  }: AgreementMember) => {
    if (write) {
      return PartyMemberAccessType.WRITE;
    }
    if (read) {
      return PartyMemberAccessType.READ;
    }
    return PartyMemberAccessType.NONE;
  };

  return (
    <React.Fragment>
      {showConfirm && (
        <DialogWarning
          onConfirm={handleConfirm}
          onCancel={handleCancel}
          message={message}
        />
      )}
      <RowCreatorListItem
        className={classes.row}
        listClassName={classes.rowPadding}
      >
        <CellAgreementParty>
          <CardAgreementMember member={member} />
        </CellAgreementParty>
        <CellAgreementRelation>
          <Field
            component={FormikEnumSelect}
            className={classes.relationSelect}
            disabled={!permissions.changePartyRoles}
            interceptOnChange
            onChange={onRoleChange}
            name={parent}
            value={type}
            type="text"
            tooltips={{
              [RelationType.ASSIGNEE]: "AgreementAssignee",
              [RelationType.ASSIGNOR]: "AgreementAssignor",
            }}
            options={RelationType}
            optionToLabel={{
              [RelationType.ASSIGNEE]: RelationTypeLabel.ASSIGNEE,
              [RelationType.ASSIGNOR]: RelationTypeLabel.ASSIGNOR,
            }}
            disabledOptions={disableRelationOptions}
            disableUnderline
          />
        </CellAgreementRelation>
        <CellAgreementIpi>
          <Typography variant="subtitle1">{member.ipiNameNumber}</Typography>
        </CellAgreementIpi>
        <CellAgreementPermissions>
          {member.permissions.owner ? (
            <Typography variant="subtitle1">
              <TooltipMap to="AgreementOwner">Owner</TooltipMap>
            </Typography>
          ) : member.type === IdentityType.CREATOR ? (
            <Typography variant="subtitle1">
              {PartyMemberAccessTypeLabel.NONE}
            </Typography>
          ) : (
            <Field
              component={FormikEnumSelect}
              className={classes.permissionSelect}
              disabled={!permissions.updateParty}
              interceptOnChange
              onChange={onAccessChange}
              value={getPermissionValue(member)}
              type="text"
              options={PartyMemberAccessType}
              disabledOptions={
                disableAccessNone ? [PartyMemberAccessType.NONE] : undefined
              }
              tooltips={{
                [PartyMemberAccessType.READ]: "AgreementView",
                [PartyMemberAccessType.WRITE]: "AgreementEdit",
                [PartyMemberAccessType.NONE]: "AgreementNone",
              }}
              optionToLabel={{
                [PartyMemberAccessType.NONE]: PartyMemberAccessTypeLabel.NONE,
                [PartyMemberAccessType.READ]: PartyMemberAccessTypeLabel.READ,
                [PartyMemberAccessType.WRITE]: PartyMemberAccessTypeLabel.WRITE,
              }}
              disableUnderline
            />
          )}
        </CellAgreementPermissions>
        <CellAgreementOptions>
          {!owner && (
            <ActionMenu
              items={[
                {
                  label: permissions.leaveAgreement ? "Leave" : "Remove",
                  disabled: permissions.leaveAgreement
                    ? false
                    : !permissions.deleteParty,
                  // : !permissions.deleteParty && member.partyId !== "",
                  onClick: () => onDelete(permissions.leaveAgreement),
                },
              ]}
            >
              <Icon name="listItemOptions" />
            </ActionMenu>
          )}
        </CellAgreementOptions>
      </RowCreatorListItem>
    </React.Fragment>
  );
};
