import React from "react";
import { DialogWarning } from "../../molecules/DialogWarning";

interface DialogSetThemeProps {
  onConfirm: () => void;
  onCancel: () => void;
}

export const DialogSetTheme: React.FC<DialogSetThemeProps> = ({
  onCancel,
  onConfirm,
}) => (
  <DialogWarning
    width={400}
    onConfirm={onConfirm}
    onCancel={onCancel}
    message="Are you sure you want to set the theme for all society users?"
    dialogTitle="Set Theme"
    labelCancel="Cancel"
    labelConfirm="Set Theme"
  />
);
