import { ThunkAction, ThunkDispatch } from "redux-thunk";
import { AppState } from "../../../App.reducer";
import { Action } from "redux";
import axios from "axios";
import {
  signUpIncompleteIdentityAction,
  signUpIncompleteIdentityFailureAction,
  signUpIncompleteIdentitySuccessAction,
} from "./actions";
import { FinalizeSignUpResponse } from "../../../api.types";
import { Href } from "../../../utils/models";
import { addAlertAction } from "../Alerts/actions";
import { AlertType } from "../Alerts/actions.types";
import { handleError } from "../../../utils/errorHandler";
import Promise from "promise-polyfill";
import {
  ApiErrorCodeMessages,
  ApiErrorCodeTypes,
  IdentityType,
} from "../../../types/enums";
import { toasts } from "../../../utils/toasts";
import { getBootstrapLinksSelector } from "../SignIn/selectors";

export const signUpIncompleteIdentityEffect = (
  { href, method }: Href,
  data: {
    firstName: string;
    lastName: string;
    role: IdentityType;
    companyIpiNameNumber?: string;
  }
): ThunkAction<
  Promise<FinalizeSignUpResponse>,
  AppState,
  void,
  Action<string>
> => async (dispatch: ThunkDispatch<AppState, void, Action<string>>) => {
  dispatch(signUpIncompleteIdentityAction());
  try {
    const response = await axios.request<FinalizeSignUpResponse>({
      url: href,
      method,
      data,
      withCredentials: true,
    });
    dispatch(signUpIncompleteIdentitySuccessAction());
    return response.data;
  } catch (e) {
    dispatch(signUpIncompleteIdentityFailureAction());
    if (
      e.response?.data?.error?.statusReason ===
      ApiErrorCodeTypes.ENTITY_NOT_FOUND
    ) {
      dispatch(
        addAlertAction(
          AlertType.ERROR,
          `${toasts.error.auth.FINISH_SIGNUP} ${ApiErrorCodeMessages.ENTITY_NOT_FOUND}`
        )
      );
      throw e;
    }
    const error = handleError(e.response, "registration");
    dispatch(
      addAlertAction(
        AlertType.ERROR,
        `${toasts.error.auth.FINISH_SIGNUP} ${error.message}`
      )
    );
    throw e;
  }
};

export const signUpCompanyEffect = (data: {
  email: string;
  firstName: string;
  lastName: string;
  ipiNameNumber: string;
}): ThunkAction<Promise<void>, AppState, void, Action<string>> => async (
  dispatch: ThunkDispatch<AppState, void, Action<string>>,
  getState
) => {
  const link = getBootstrapLinksSelector(getState())?.publisherSignUpRequest;
  try {
    await axios.request<FinalizeSignUpResponse>({
      url: link?.href,
      method: link?.method,
      data,
      withCredentials: true,
    });
  } catch (e) {
    const error = handleError(e.response);
    dispatch(
      addAlertAction(
        AlertType.ERROR,
        `${toasts.error.auth.SIGN_UP_COMPANY} ${error.message}`
      )
    );
    throw e;
  }
};
