import React from "react";
import {
  createStyles,
  Grid,
  makeStyles,
  MenuItem,
  Theme,
} from "@material-ui/core";
import { enumKeys } from "../../../utils/helpers";
import { languageCodeNames } from "../../../types/enums";
import { SelectAppBar } from "../../atoms/SelectAppBar";
import { InputFieldAppBar } from "../../atoms/InputFieldAppBar";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    languageCodeSelect: {
      marginLeft: theme.spacing(2),
    },
    menuItem: {
      color: theme.palette.text.primary,
    },
  })
);

interface WorkTitleInputProps {
  languageCodeRequired?: boolean;
  placeholder: string;
  languageCodes?: { [index: string]: string };
  disabled?: boolean;
}

export const WorkTitleInput: React.FC<WorkTitleInputProps> = ({
  languageCodeRequired,
  placeholder,
  languageCodes,
  disabled,
}) => {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Grid item sm={6} xs={9}>
        <InputFieldAppBar
          data-testid="work-title-input"
          placeholder={placeholder}
          name="title"
          disabled={disabled}
        />
      </Grid>
      {languageCodeRequired && (
        <Grid item sm={3} xs={3}>
          <SelectAppBar
            name="titleLanguageCode"
            data-testid="title-language-code-select"
            className={classes.languageCodeSelect}
            disabled={disabled}
          >
            {enumKeys(languageCodes).map((key: string) => (
              <MenuItem className={classes.menuItem} key={key} value={key}>
                {languageCodeNames[key] || key}
              </MenuItem>
            ))}
          </SelectAppBar>
        </Grid>
      )}
    </React.Fragment>
  );
};
