import {
  AgreementActionTypes,
  DELETE_AGREEMENT,
  DELETE_AGREEMENT_FAILURE,
  DELETE_AGREEMENT_SUCCESS,
  FETCH_AGREEMENT,
  FETCH_AGREEMENT_FAILURE,
  FETCH_AGREEMENT_SUCCESS,
  FETCH_AGREEMENTS,
  FETCH_AGREEMENTS_FAILURE,
  FETCH_AGREEMENTS_SUCCESS,
  PATCH_AGREEMENT,
  PATCH_AGREEMENT_FAILURE,
  PATCH_AGREEMENT_SUCCESS,
  POST_AGREEMENT,
  POST_AGREEMENT_DUPLICATE,
  POST_AGREEMENT_DUPLICATE_FAILURE,
  POST_AGREEMENT_DUPLICATE_SUCCESS,
  POST_AGREEMENT_FAILURE,
  POST_AGREEMENT_SUCCESS,
  REGISTER_AGREEMENT,
  REGISTER_AGREEMENT_FAILURE,
  REGISTER_AGREEMENT_SUCCESS,
} from "./actions.types";
import {
  _AgreementsLinks,
  Agreement,
  AgreementListItem,
  ListMeta,
} from "../../../utils/models";

export const fetchAgreementsAction = (
  next?: boolean
): AgreementActionTypes => ({
  type: FETCH_AGREEMENTS,
  next,
});

export const fetchAgreementsSuccessAction = ({
  agreements,
  _links,
  next,
  meta,
}: {
  agreements: AgreementListItem[];
  _links: _AgreementsLinks;
  next?: boolean;
  meta?: ListMeta;
}): AgreementActionTypes => ({
  type: FETCH_AGREEMENTS_SUCCESS,
  agreements,
  _links,
  next,
  meta,
});

export const fetchAgreementsFailureAction = (): AgreementActionTypes => ({
  type: FETCH_AGREEMENTS_FAILURE,
});

export const postAgreementAction = (): AgreementActionTypes => ({
  type: POST_AGREEMENT,
});

export const postAgreementSuccessAction = (
  agreement: Agreement
): AgreementActionTypes => ({
  type: POST_AGREEMENT_SUCCESS,
  agreement,
});

export const postAgreementFailureAction = (): AgreementActionTypes => ({
  type: POST_AGREEMENT_FAILURE,
});

export const patchAgreementAction = (): AgreementActionTypes => ({
  type: PATCH_AGREEMENT,
});

export const patchAgreementSuccessAction = (
  agreement: Agreement
): AgreementActionTypes => ({
  type: PATCH_AGREEMENT_SUCCESS,
  agreement,
});

export const patchAgreementFailureAction = (): AgreementActionTypes => ({
  type: PATCH_AGREEMENT_FAILURE,
});

export const fetchAgreementAction = (): AgreementActionTypes => ({
  type: FETCH_AGREEMENT,
});

export const fetchAgreementSuccessAction = (
  agreement: Agreement
): AgreementActionTypes => ({
  type: FETCH_AGREEMENT_SUCCESS,
  agreement,
});

export const fetchAgreementFailureAction = (): AgreementActionTypes => ({
  type: FETCH_AGREEMENT_FAILURE,
});

export const registerAgreementAction = (): AgreementActionTypes => ({
  type: REGISTER_AGREEMENT,
});

export const registerAgreementSuccessAction = (): AgreementActionTypes => ({
  type: REGISTER_AGREEMENT_SUCCESS,
});

export const registerAgreementFailureAction = (): AgreementActionTypes => ({
  type: REGISTER_AGREEMENT_FAILURE,
});

export const deleteAgreementAction = (): AgreementActionTypes => ({
  type: DELETE_AGREEMENT,
});

export const deleteAgreementSuccessAction = (
  guid: string
): AgreementActionTypes => ({
  type: DELETE_AGREEMENT_SUCCESS,
  guid,
});

export const deleteAgreementFailureAction = (): AgreementActionTypes => ({
  type: DELETE_AGREEMENT_FAILURE,
});

export const postAgreementDuplicateAction = (): AgreementActionTypes => ({
  type: POST_AGREEMENT_DUPLICATE,
});

export const postAgreementDuplicateSuccessAction = (
  agreement: AgreementListItem
): AgreementActionTypes => ({
  type: POST_AGREEMENT_DUPLICATE_SUCCESS,
  agreement,
});

export const postAgreementDuplicateFailureAction = (): AgreementActionTypes => ({
  type: POST_AGREEMENT_DUPLICATE_FAILURE,
});
